import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import { map } from "../../fe/Maybe"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { FileCache } from "../fs/FileCache"
import { readFileType_ } from "../fs/FileType"
import { PosixFile } from "../fs/PosixFile"
import { SimpleFileOrString } from "../fs/SimpleFile"
import { commandTimeoutMs } from "../volumes/VolumeTtls"
import { cachedTags, rawTagsCache } from "./ExifTool"
import { normalizeMimetype } from "./MimeTypes"

export const mimetypeCache = lazy(
  () =>
    new FileCache<Maybe<string>>({
      name: "tags.mimetype",
      maxSize: 2048,
      timeoutMs: commandTimeoutMs()
    })
)

export async function readMimeType(
  pathOrFile: SimpleFileOrString
): PromiseMaybe<string> {
  if (blank(pathOrFile)) return
  const f = PosixFile.for(pathOrFile)
  return mimetypeCache().getOrSetAsync(f.nativePath, async () => {
    return map(
      rawTagsCache().get(f.nativePath)?.MIMEType ??
        (await cachedTags(f))?.MIMEType ??
        (await readFileType_(f.nativePath).catch(() => undefined))?.mime,
      normalizeMimetype
    )
  })
}
