import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { filterAsync, thenMapOr } from "../../core/async/Promise"
import { libraryOriginalsDirPosixFile } from "../../core/dir/LibraryDirs"
import { ee } from "../../core/event/EventEmitter"
import { isReadableDirectory } from "../../core/fs/Stat"
import { Settings } from "../../core/settings/Settings"
import { nativePath2uri } from "../../core/uri/FileURI"
import { isExcludedMountpoint } from "../../core/volumes/PosixVolumes"
import { volumes } from "../../core/volumes/Volumes"
import { later } from "../../fe/Delay"
import { gt0 } from "../../fe/Number"
import { thenCollect } from "../../fe/Promise"
import { toA } from "../../fe/toA"
import { nativePathBlocklist } from "./SyncPaths"
import {
  UriPath,
  nativePathToUriPath,
  posixFileToUriPath,
  toUriPath
} from "./UriPath"

export const logger = lazy(() => mkLogger("sync.PathsToSync"))

const volumeUriPaths = lazy(() =>
  thenCollect(volumes(), vol =>
    vol.ignorable !== true &&
    gt0(vol.size) &&
    !isExcludedMountpoint(vol.mountpoint)
      ? toUriPath(vol.mountpoint, nativePath2uri(vol.mountpoint, vol))
      : undefined
  )
)

export async function scanPaths() {
  const noUriPaths: string[] = []
  const result: UriPath[] = []
  for (const scanDir of toA(Settings.scanPaths.value)) {
    if (!(await isReadableDirectory(scanDir))) {
      logger().warn(
        "Ignoring scanPath element: not readable directory",
        scanDir
      )
    } else {
      const uriPath = await nativePathToUriPath(scanDir)
      if (uriPath == null) {
        noUriPaths.push(scanDir)
        logger().warn(
          "Ignoring scanPath element: failed to create URI path",
          scanDir
        )
      } else {
        result.push(uriPath)
      }
    }
  }
  return { result, noUriPaths }
}

later(() => {
  ee().on("volumesChanged", () => {
    volumeUriPaths.unset()
  })
})

export async function pathsToSync(): Promise<UriPath[]> {
  const arr: UriPath[] = []
  arr.push(...(await scanPaths()).result)
  if (Settings.scanAllDrives.valueOrDefault) {
    arr.push(...(await volumeUriPaths()))
  }

  const lp = await thenMapOr(
    libraryOriginalsDirPosixFile(),
    posixFileToUriPath,
    () =>
      logger().throw("libraryOriginalsDir was null", {
        libraryDir: Settings.libraryDir.valueOrDefault,
        originalsDir: Settings.originalsDir.value
      })
  )

  if (Settings.scanLibraryFirst.valueOrDefault) {
    arr.unshift(lp!)
  }

  if (Settings.scanLibraryLast.valueOrDefault) {
    arr.push(lp!)
  }

  const result = await filterAsync({
    name: "pathsToSync",
    arr,
    f: ea =>
      !nativePathBlocklist.has(ea.nativePath) &&
      isReadableDirectory(ea.nativePath)
  })

  logger().info("pathsToSync", result)
  return result
}
