import { SimpleAppName, SimpleShortAppName } from "./AppName"
import { mkLogger } from "./Logger"
import { isMainService, serviceName } from "./ServiceNames"
import { isElectron } from "./platform/Platform"

// break dep loop on Settings:
let shortProcessNames = !isElectron
export function setShortProcessNames(short: boolean) {
  shortProcessNames = short
}

export function setProcessTitle() {
  try {
    const result = [shortProcessNames ? SimpleShortAppName : SimpleAppName]
    if (!(isMainService() && isElectron)) {
      result.push(serviceName())
    }
    const title = result.join(isElectron ? " " : "-")
    // SITS: using an import doesn't work
    require("process").title = title
  } catch (error) {
    mkLogger("ServiceNames").warn("Failed to set friendly process name", error)
  }
}
