import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"

/**
 * Consistent version formatting.
 *
 * @returns "(missing version)" if the input is blank.
 */
export function ver(s: Maybe<string>) {
  return blank(s)
    ? "(missing version)"
    : s.replace(/^(?:v|ver|version)?\s*/, "version ")
}
