import { compact } from "../../fe/Array"
import { Maybe } from "../../fe/MaybeTypes"
import { clamp, closeTo, fmtPct, gt0, times } from "../../fe/Number"
import { PartiallyNull } from "../../fe/PartiallyNull"
import { Centroid } from "../math/Kmeans"
import { mapGt0 } from "../Number"
import { bestLabhashName } from "./ColorNames"
import { lab2rgbhex, toLabhash, unlabhash } from "./Colorspace"
import { ModeBits } from "./ModeBits"
import { ModeCount } from "./ModeCount"
import { WithModeData } from "./ModeData"
import { Triplet } from "./Triplet"

export function dominantColorsToRgbHex(arr: DominantColor[]) {
  return compact(arr.map(ea => [ea.rgbhex, fmtPct(ea.pct)]))
}

export class DominantColor {
  static fromModeData(md: Partial<WithModeData>): DominantColor[] {
    return compact(
      times(ModeCount, i =>
        DominantColor.fromLabhash({
          labhash: (md as any)[`mode${i}`],
          pct: (md as any)[`mode${i}pct`]
        })
      )
    )
  }

  static toModeData(arr: DominantColor[]): PartiallyNull<WithModeData> {
    const md: PartiallyNull<WithModeData> = {} as any
    times(ModeCount, i => {
      // We _really_ want all the fields here, to make sure we fully overwrite
      // prior dominant colors (especially if there are fewer now than
      // before), hence the `?? null`:
      ;(md as any)["mode" + i] = arr[i]?.labhash ?? null
      ;(md as any)["mode" + i + "pct"] = arr[i]?.pct ?? null
    })
    return md
  }

  static fromLabhash({
    labhash,
    labhashBits = ModeBits,
    pct
  }: {
    labhash: Maybe<number>
    labhashBits?: number
    pct?: number
  }): Maybe<DominantColor> {
    const lab = mapGt0(labhash, ea => unlabhash(ea, labhashBits))
    return lab == null || !gt0(pct) // < drops [0% - 0.5%]
      ? undefined
      : new DominantColor(lab, pct, labhash)
  }

  /**
   * @param c.centroid is in L*a*b space
   */
  static fromCentroid(c: Centroid, totalPixels: number) {
    return new DominantColor(
      c.centroid as Triplet,
      Math.round(100 * (c.size / totalPixels))
    )
  }

  readonly labhash: number

  readonly pct: number

  constructor(
    public readonly lab: Triplet,
    pct: number,
    labhash?: number
  ) {
    this.pct = clamp(0, 100, pct)
    this.labhash = labhash ?? toLabhash(lab, ModeBits)
  }

  get rgbhex() {
    return lab2rgbhex(this.lab)
  }

  get color() {
    return bestLabhashName(this.labhash)
  }

  toGreyscale() {
    return new DominantColor([this.lab[0], 0, 0], this.pct)
  }

  eql(other: DominantColor) {
    return this.labhash === other.labhash && closeTo(this.pct, other.pct, 5)
  }
}
