const logger = lazy(() => mkLogger("img.ffmpeg"))

// This may be something reasonable, like "ffmpeg version 3.2.1", but if it's a
// nightly build, "ffmpeg version N-94833-g9d1e98afee"
const versionRe = /ffmpeg version (?<version>\S+)/i

const _ffmpegVersion_ = lazy<PromiseMaybe<ToolDetails>>(async () => {
  const ffmpegPath = await pathTo({
    tool: "ffmpeg",
    alternativePath: Settings.ffmpegPath.valueOrDefault
  })
  if (ffmpegPath == null) {
    logger().info("failed to find ffmpeg in $PATH", {
      ffmpegPath: Settings.ffmpegPath.valueOrDefault
    })
    return
  }
  const result = await stdoutResult_(ffmpegPath, ["-version"], {
    timeoutMs: commandTimeoutMs(),
    ignoreStderr: true
  })
  const version: Maybe<string> = versionRe.exec(result.result)?.[1]
  logger().debug("ffmpegVersion", {
    version,
    code: result.code,
    stdout: result.result.split("\n", 1)[0]
  })
  return {
    path: ffmpegPath,
    version: version ?? "(unknown)",
    isSupportedVersion:
      map(debianVersionToSemver(version), ea => satisfies(ea, ">=3.2")) ?? false
  }
})

export const ffmpegVersionDescription = lazy(() =>
  thenMapOr(
    _ffmpegVersion_(),
    ver => "version " + ver,
    () => "(not found)"
  )
)

function clearCaches() {
  _ffmpegVersion_.unset()
  ffmpegVersionDescription.unset()
}

later(() => {
  ee().on("clearCache", clearCaches)
  ee().on("clearToolCache", clearCaches)
})

export async function ffmpegVersion_(): PromiseMaybe<ToolDetails> {
  return (await _ffmpegVersion_.prior()) ?? _ffmpegVersion_.refresh()
}

export async function isFFmpegSupported() {
  // TODO: what versions do we *actually* require?
  return (await _ffmpegVersion_()) != null
}

// Example errors:

// [h264 @ 0x5642cf833640] left block unavailable for requested intra mode

// [h264 @ 0x5642cf833640] error while decoding MB 0 16, bytestream 99553

// [mov,mp4,m4a,3gp,3g2,mj2 @ 0x556e03cfb8c0] stream 0, offset 0x184ef: partial file

// Invalid data found when processing input

// Nothing was written into output file 0 (pipe:), because at least one of its streams received no packets.

// Error: [mov,mp4,m4a,3gp,3g2,mj2 @ 0x560b7c7f59a0] Referenced QT chapter track not found

/*
-vframes number (output) Set the number of video frames to output. This is an
alias for "-frames:v".

-f fmt (input/output) Force input or output file format. The format is normally
auto detected for input files and guessed from the file extension for output
files, so this option is not needed in most cases.

pipe:[number] number is the number corresponding to the file descriptor of the
pipe (e.g. 0 for stdin, 1 for stdout, 2 for stderr). If number is not specified,
by default the stdout file descriptor will be used for writing, stdin for
reading.

-ss position (input/output) When used as an input option (before -i), seeks in
this input file to position. Note that in most formats it is not possible to
seek exactly, so ffmpeg will seek to the closest seek point before position.
When transcoding and -accurate_seek is enabled (the default), this extra segment
between the seek point and position will be decoded and discarded. When doing
stream copy or when -noaccurate_seek is used, it will be preserved.

When used as an output option (before an output url), decodes but discards input
until the timestamps reach position.

position must be a time duration specification, see (ffmpeg-utils)the Time
duration section in the ffmpeg-utils(1) manual.
*/
export async function ffmpegFrame_(args: {
  src: PosixFile
  dest: PosixFile
  startAtSec?: number
  width?: number
  height?: number
}) {
  await args.dest.parent().mkdirp_()

  await stdoutResult_(
    Settings.ffmpegPath.valueOrDefault,
    compact([
      // we don't use hwaccel here: it doesn't speed things up.
      "-loglevel",
      "error",
      "-i",
      args.src.nativePath,
      ...(mapGte0f(args.startAtSec, ea => ["-ss", ea.toFixed(1)]) ?? []),
      "-vframes",
      "1",
      // This didn't seem to really help much, and made it slower.
      // "-q:v", // require quality to be <= 2 (range between 1 and 31)
      // "2",
      // This seems to lead to distorted outputs?
      // ...(args.width != null && args.height != null
      //   ? ["-s", `${round(args.width)}x${round(args.height)}`]
      //   : []),
      "-y", // overwrite output files
      args.dest.nativePath
    ]),
    {
      timeoutMs: minuteMs, // extracting a single frame should take less than a second.
      isIgnorableError: isIgnorableValidationError
    }
  )
  if (
    !(await untilTrue(() => args.dest.clear().isNonEmptyFile(), {
      timeoutMs: 3 * secondMs
    }))
  ) {
    throw new Error("Failed to extract frame for " + args.src)
  }
}

function hwaccel(args: { ffmpegHwaccel?: string }) {
  const s = args?.ffmpegHwaccel ?? Settings.ffmpegHwaccel.valueOrDefault
  return blank(s) || isDisabled(s) ? [] : ["-hwaccel", s]
}

export function avcArgs(args: Partial<Dimensions>): string[] {
  const result = [...Settings.ffmpegAvcTranscodeArgs.values] as string[]

  if (
    !gt0(args.width) ||
    !gt0(args.height) ||
    !gt0(Settings.transcodeMaxDim.valueOrDefault)
  ) {
    return result
  }

  // the `-vf scale=720x480` format doesn't seem to work (!?!)
  // see: https://ffmpeg.org/ffmpeg.html#filter_005foption and

  const w = roundEven(args.width)
  const h = roundEven(args.height)

  const dim = w + "x" + h
  switch (Settings.ffmpegScaleType.valueOrDefault) {
    case "s":
      return ["-s", dim, ...result]
    case "vf":
      const vfIdx = result.indexOf("-vf")
      if (vfIdx >= 0) {
        result[vfIdx + 1] += ":scale=" + dim
      } else {
        result.push("-vf", "scale=" + dim)
      }
      return result
  }
}

/**
 * @see https://trac.ffmpeg.org/wiki/Limiting%20the%20output%20bitrate
 */
function videoBitrateArgs(bitrateKbps: Maybe<number>) {
  if (!gt0(bitrateKbps)) return []

  const m = sigFigs(bitrateKbps, 2)
  return [
    "-b:v",
    m + "k",
    "-maxrate",
    m + "k",
    "-bufsize",
    sigFigs(m / 2, 2) + "k"
  ]
}

function framerateArgs(args?: { fps?: number }) {
  const r = Settings.transcodeFrameRate.valueOrDefault
  return !gt0(r) || lte(args?.fps, r) ? [] : ["-r", Math.round(r)]
}

export async function ffmpegTranscode_(args: {
  src: PosixFile
  dest: PosixFile
  width: Maybe<number>
  height: Maybe<number>
  fps?: number
  videoBitrateKbps?: number
  ffmpegHwaccel?: string
}) {
  try {
    await args.dest.parent().mkdirp_()

    return await stdoutResult_(
      Settings.ffmpegPath.valueOrDefault,
      compactBlanks([
        "-loglevel",
        "error",

        ...hwaccel(args),

        "-i", // --input
        args.src.nativePath,

        ...avcArgs(args),

        ...framerateArgs(args),

        // -threads must be AFTER the input!
        // https://ffmpeg-user.ffmpeg.narkive.com/jvDZY1aP/threads-option-has-no-effect#post6
        "-threads",
        toS(ffmpegThreads()),

        ...videoBitrateArgs(args.videoBitrateKbps),

        args.dest.nativePath // < output
      ]),
      {
        timeoutMs: 0,
        isIgnorableError: isIgnorableValidationError
      }
    )
  } catch (error) {
    await unlink(args.dest)
    const s = errorToS(error, { maxLen: 1024 })
    if (
      Settings.ffmpegScaleType.valueOrDefault !== "s" &&
      /option 'scale' not found/i.test(s)
    ) {
      logger().warn(
        "ffmpegTranscode_() failed: scale not found. Switching to old-style ffmpeg resizing and retrying...",
        { args, error }
      )
      // WOAH WOAH WOAH WTH!?!

      // Chiilax, future me.

      // Scaling approaches depend on the version of ffmpeg, which won't be
      // changing.

      // Hardware accelleration, though, may work for some input formats and
      // not others, so it needs to be specific to the input.

      Settings.ffmpegScaleType.envValue = "s"
      return ffmpegTranscode_(args)
    } else if (
      !isDisabled(
        args.ffmpegHwaccel ?? Settings.ffmpegHwaccel.valueOrDefault
      ) &&
      /hwaccel.*error/i.test(s)
    ) {
      logger().warn(
        "ffmpegTranscode_() failed: bad -hwaccel arg? Disabling hwaccel and retrying...",
        { args, error }
      )
      return ffmpegTranscode_({ ...args, ffmpegHwaccel: "disabled" })
    } else {
      throw error
    }
  }
}

/**
 * @throws on errors
 */
export async function ffmpegValidVideo_(src: PosixFile): Promise<StdoutResult> {
  return logger().tap({
    msg: "ffmpegValidVideo",
    meta: { src: src.nativePath },
    result: await stdoutResult_(
      Settings.ffmpegPath.valueOrDefault,
      // `-v error` may also complains about minor encoding issues, so we have to
      // pass the error messages through that RegExp.
      [
        "-v",
        "error",
        "-nostats",
        "-i",
        src.nativePath,
        "-threads",
        toS(ffmpegThreads()),
        "-f",
        "null",
        "-"
      ],
      {
        timeoutMs: 0,
        isIgnorableError: isIgnorableValidationError,
        ignoreExitCode: true,
        quiet: false // < raise the error
      }
    )
  })
}
