import { Knex } from "knex"
import { ending } from "../../core/async/Endable"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { identity } from "../../core/Object"
import { blank } from "../../fe/Blank"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { strEnum, StrEnumKeys } from "../../fe/StrEnum"
import { SyncOrAsync } from "../../fe/SyncOrAsync"
import { PartialPojo } from "../db/PartialPojo"
import { TimestampedModel } from "./TimestampedModel"

// TODO: rebuild, rebuild-assets (just rebuilds assets), sync-asset, ...
export const OperationNames = strEnum(
  "rebuildLibrary",
  "forceRestartSync",
  "enqueueAssetFileUpdates",
  "enqueueAssetUpdates",
  "applyNewTagger",
  // Actually exclude all excluded assets:
  "applyExcluded",
  // Actually delete all assets marked as deleted:
  "applyDeleted"
)
export type OperationName = StrEnumKeys<typeof OperationNames>

const logger = lazy(() => mkLogger("model.Operation"))

// TODO: REPLACE "Operations" with non-transient events?

export class Operation extends TimestampedModel {
  static override readonly $tableName = "Operation"
  static override readonly $uniqueColumnName = "id"
  name!: OperationName
  value?: string
  version?: number
  completedAt?: number

  static incomplete() {
    return this.ops().allf(q =>
      q.whereIn("id", q2 =>
        q2.table(Operation.$tableName).min("id").whereNull("completedAt")
      )
    )
  }

  /**
   * Ensure there's an incomplete/pending Operation with the given attributes
   */
  static ensurePendingOp(Obj: Pick<Operation, "name" | "value">): Operation {
    return Operation.getFirstPendingOp(Obj) ?? Operation.ops().insertOne(Obj)
  }

  static getFirstPendingOp(crit?: PartialPojo<Operation>) {
    return logger().tap({
      msg: "getFirstPendingOp",
      level: "info",
      result: this.ops().firstf(q => {
        // We want the earliest incomplete op (order by id desc)
        const result = q.whereNull("completedAt").orderBy("createdAt", "asc")
        return crit != null ? result.andWhere(crit) : result
      }),
      meta: { crit }
    })
  }

  static markOpCompleted(
    crit: Pick<Operation, "name" | "value" | "version">,
    update?: Pick<Operation, "value">
  ) {
    if (blank(crit?.name))
      return logger().throw("markOpCompleted(): bad query", { crit })
    return this.dbl.runf(q =>
      q
        .whereNull("completedAt")
        .andWhere(crit)
        .update({ completedAt: Date.now(), ...update })
    )
  }

  static async applyIfPending<T>(
    crit: Pick<Operation, "name" | "value" | "version">,
    f: (op: Operation) => Promise<T>
  ): PromiseMaybe<T> {
    const op = this.ops().firstf(q => q.whereNull("completedAt").andWhere(crit))
    logger().info("applyIfPending()", { crit, op })
    return op == null ? undefined : f(op)
  }

  static async applyOnce<T>(
    crit: Pick<Operation, "name" | "value" | "version">,
    f: (op: Operation) => SyncOrAsync<T>,
    qb: (ea: Knex.QueryBuilder) => Knex.QueryBuilder = identity
  ): PromiseMaybe<T> {
    const priorCompleted = this.ops().firstf(q =>
      qb(q.whereNotNull("completedAt").andWhere(crit))
    )

    if (priorCompleted != null) {
      logger().debug("applyOnce(): already done", { priorCompleted })
      return
    }
    const op = this.ops().insertOne(crit)
    const result = await f(op)
    if (!ending()) {
      op.upsert({ completedAt: Date.now() })
    }
    return result
  }
}
