import { Maybe } from "./MaybeTypes"
import { toInt, within } from "./Number"
import { randomInt } from "./Random"
import { toS } from "./toS"

export const PrimesPerBin = 4
export const PrimeBins = 8

export const SeedCount = PrimesPerBin ** PrimeBins

export function prngSeed(): number {
  return randomInt(0, SeedCount)
}

export function toSeed(s: any): Maybe<number> {
  const i = toInt(toS(s))
  return within(0, SeedCount, i) ? i : undefined
}
