import { lazy } from "../../core/Lazy"
import { PosixFile } from "../fs/PosixFile"
import { mkLogger } from "../Logger"
import { sqliteFiles } from "./SQLiteFiles"

const logger = lazy(() => mkLogger("db.DbBackupCold"))

/**
 * SQLite's db backup tries to acquired filesystem locks when it backs up.
 * This won't work if the db is on a remote filesystem.
 *
 * If you don't care about running a "hot" backup, this implementation is
 * simpler.
 */
export async function dbBackupCold_(
  srcDbFile: PosixFile,
  destDir: PosixFile
): Promise<PosixFile[]> {
  await destDir.mkdirp_()
  const wips = []
  try {
    for (const ea of sqliteFiles(srcDbFile)) {
      const wip = destDir.join(ea.base).wip()
      wips.push(wip)
      await ea.copyFile_(wip)
    }
    // OK! All the copies worked! Now we can rename the wips to the final
    // names.
    return logger().tap({
      msg: "completed",
      level: "info",
      result: await Promise.all(wips.map(ea => ea.unwip_())),
      meta: { srcDbFile, destDir }
    })
  } catch (error) {
    await Promise.all(wips.map(ea => ea.unlink()))
    throw error
  }
}
