import { gunzipSync, gzipSync } from "zlib"
import { mapNotBlank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { hammRatioBigInt } from "../Number"

export function hex2b64(hex: string): string {
  return Buffer.from(hex, "hex").toString("base64")
}

export function b64encode(i: number | bigint): string {
  const hex = i.toString(16)
  // Make sure we're padded to even characters. Buffer.from("", "hex") IGNORES ODD LENGTHS !!!WTFOMG!?!
  const pad = hex.length % 2 === 0 ? "" : "0"
  return Buffer.from(pad + hex, "hex").toString("base64")
}

export function b64decode(base64: string): bigint {
  return BigInt("0x0" + Buffer.from(base64, "base64").toString("hex"))
}

// use gzip rather than brotli as a head-fake to licensing
export function gz64encodeString(s: string): string {
  return gzipSync(Buffer.from(s, "utf8")).toString("base64")
}

export function d(s: string): string {
  return gunzipSync(Buffer.from(s, "base64")).toString("utf8")
}

export const gz64decodeString = d

/**
 * hamm...ratio...operator
 */
export function b64hammRatio(
  a: Maybe<string>,
  b: Maybe<string>
): Maybe<number> {
  return mapNotBlank(a, ea1 =>
    mapNotBlank(b, ea2 => hammRatioBigInt(b64decode(ea1), b64decode(ea2)))
  )
}

export function b64encodeBits(m: (0 | 1)[]): string {
  // TODO: don't just use BigInt and Buffer, but actually do the work ourselves.
  return b64encode(BigInt("0b0" + m.map(ea => (ea === 1 ? "1" : "0")).join("")))
}
