import { gte, pct } from "../../fe/Number"
import { ProgressEvtWithoutPct } from "./ProgressEvt"
import { emitProgressEvt } from "./ProgressObserver"

export class PushProgressObserver {
  private readonly start = Date.now()
  private current?: number
  private done?: boolean
  constructor(
    readonly context: ProgressEvtWithoutPct,
    readonly total: number
  ) {}

  toJSON() {
    return undefined // not serializable
  }

  incrProgress(incremental: number) {
    this.onProgress(incremental + (this.current ?? 0))
  }

  onProgress(current?: number) {
    this.current = (current ?? this.current ?? 0) + 1
    if (gte(this.current, this.total)) this.done = true
    this.emit()
  }

  completed() {
    this.done = true
    this.current = this.total
    this.emit()
  }

  emit() {
    emitProgressEvt({
      ...this.context,
      pct: pct(this.current, this.total),
      elapsedMs: Date.now() - this.start,
      done: this.done
    })
  }
}
