import { License } from "../../fe/api/License"
import { S } from "../../fe/api/Subscriptions"
import { compact, uniq } from "../../fe/Array"
import { dayMs } from "../../fe/Date"
import { Maybe } from "../../fe/MaybeTypes"
import { Obj } from "../../fe/Object"
import { cmp, Comparable } from "../../fe/Primitive"
import { intersection } from "../Array"
import { within } from "../Number"
import { k, ParsedLicense } from "./Licensing"
import { getScheme, prefix2scheme } from "./SystemIdSchemes"

export class L implements ParsedLicense {
  static for(opts: {
    str: string
    l: Maybe<License>
    sids: string[]
    src: string
  }) {
    return new this(opts.str, opts.l, opts.sids, opts.src)
  }
  readonly meta: Obj
  /**
   * @param s the actual license string
   * @param _l the parsed License
   */
  private constructor(
    readonly s: string,
    readonly _l: Maybe<License>,
    readonly _sids: string[],
    src: string
  ) {
    this.meta = { src }
  }

  get l(): Maybe<License> {
    return this._l
  }

  get ok(): boolean {
    if (this._l == null) return false
    const matchedUids = intersection(this._sids, this._l.uids)
    const matchedSchemeIds = uniq(matchedUids.map(getScheme))
    const matchedSchemes = compact(
      matchedSchemeIds.map(ea => prefix2scheme(ea as any))
    )
    const uidsOK = matchedSchemes.length >= this._l.mat
    const dateOK = within(
      this._l.iat?.getTime(),
      this._l.exp?.getTime() + dayMs,
      Date.now()
    )

    this.meta[k().o] = dateOK
    this.meta[k().u] = uidsOK
    this.meta[k().m] = matchedSchemes

    // console.log("L.ok()", {
    //   dateOK,
    //   uidsOK,
    //   sids: this._sids,
    //   l_uids: this._l.uids,
    //   matchedSchemes,
    //   matchedUids,
    //   matchedSchemePrefixes
    // })
    return dateOK && uidsOK
  }

  cmpVal(): Comparable[] {
    return [
      this.ok,
      -(S.indexOf(this.l?.[k().T]) ?? 0),
      this.l?.exp?.getTime() ?? 1,
      this.meta.matchedSchemes // more is better
    ]
  }

  /**
   * @return 1 if this > b, -1 if this < b, 0 if this == b.
   */
  cmp(p: L) {
    return cmp(this.cmpVal(), p.cmpVal())
  }
}
