import { findAsync } from "../../core/Array"
import { mkLogger } from "../../core/Logger"
import { PosixFile } from "../../core/fs/PosixFile"
import { acceptParentAndFileAndSimple } from "../../core/fs/PosixFileFilters"
import { HealthCheck } from "../../core/health/HealthCheck"
import { libraryHasSettings } from "../../core/settings/SettingsIO"
import { isEmpty } from "../../fe/Array"
import { li, tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { pathsToSync } from "../sync/PathsToSync"

const logger = defer(() => mkLogger("health.SyncHealthCheck"))

export const syncHealthCheck = defer(() =>
  HealthCheck.for({
    section: "Library",
    id: "library-nothing-to-sync",
    ordinal: 99,
    rejectIsWarn: true,
    settings: [
      "scanAllDrives",
      "scanPaths",
      "scanLibraryFirst",
      "scanLibraryLast"
    ],
    pendingMsg: "Checking synced directories",
    later: async () => {
      if (!libraryHasSettings()) {
        return {
          level: "disabled",
          msg: "Library sync paths test disabled: no library is open"
        }
      }
      const paths = (await pathsToSync()).map(ea => ea.nativePath)
      if (isEmpty(paths)) {
        return {
          level: "warn",
          msg:
            "No paths are configured to scan\n" +
            li([
              tt("PS_SCAN_ALL_DRIVES") + " is false",
              tt("PS_SCAN_PATHS") + " is empty",
              "both " +
                tt("PS_SCAN_LIBRARY_FIRST") +
                " and " +
                tt("PS_SCAN_LIBRARY_LAST") +
                " are false"
            ]),
          links: [
            {
              text: "Read more",
              url: "https://photostructure.com/go/nothing-to-scan"
            }
          ]
        }
      } else {
        // We could run a quick DirectoryIterator here to see if any of the
        // paths are empty, but let's just complain if no files or directories
        // are found in _any_ scanned paths.

        const posixFiles = paths.map(ea => PosixFile.for(ea))
        logger().info("Checking if any paths are empty", { posixFiles })
        const firstNonEmpty = await findAsync(
          posixFiles,
          async dir =>
            null !=
            (await dir.someDescendant(
              ea => ea.isDirectorySync() || acceptParentAndFileAndSimple(ea),
              1
            ))
        )

        if (firstNonEmpty == null) {
          return {
            level: "warn",
            msg: [
              "No eligible files found in scanned paths",
              "Checked paths:",
              li(paths.map(tt))
            ],
            links: [
              {
                text: "Read more",
                url: "https://photostructure.com/go/nothing-to-scan"
              }
            ]
          }
        }

        return {
          level: "ok",
          msg: [
            "At least one directory with eligible files is configured to be synced",
            "Checked paths:",
            li(paths.map(tt))
          ]
        }
      }
    }
  })
)
