import { Command } from "commander"
import { CLI, CommandPlugin } from "../core/cli/CLI"
import { Settings } from "../core/settings/Settings"
import { isTrue } from "../fe/Boolean"
import { Obj } from "../fe/Object"
import { ServiceNames } from "../fe/ServiceNames"
import { MainService } from "./MainService"
import { DaemonArgs } from "./cli/Daemon"
import { ExposeArg } from "./cli/ExposeArg"
import { LogArgs } from "./cli/LogArgs"
import { NoFilterArg } from "./cli/NoFilterArg"
import { PauseSyncArg } from "./cli/PauseSyncArg"

// process.on("SIGINT", () => {
//   why_running?.()
// })

export const MainArgs: CommandPlugin = {
  beforeParse: (cmd: Command) =>
    cmd.option(
      "--quiet",
      "Don't emit lifecycle messages to stdout from the main service."
    ),
  afterParse: (opts: Obj) => {
    if (isTrue(opts.quiet)) {
      Settings.quiet.tmpValue = true
    }
  }
}

async function run() {
  try {
    new CLI(ServiceNames.main)
      .add(MainArgs, ExposeArg, PauseSyncArg, DaemonArgs, NoFilterArg, LogArgs)
      .parse()

    // If we're being daemonized, we'll have exited at this point.

    new MainService()
  } catch (err) {
    console.error("Failed to start:")
  }
}

void run()
