import { Knex } from "knex"
import { zip } from "../../core/Array"
import { escapeRegExp } from "../../core/RegExp"
import { isString } from "../../core/String"
import { notBlank } from "../../fe/Blank"
import { flatten } from "../../fe/Flatten"
import { stringify } from "../../fe/JSON"
import { keys, omit } from "../../fe/Object"
import { isFunction } from "../../fe/isFunction"
import { toS } from "../../fe/toS"
import { DbValued, isDbValued } from "./DbValued"

export function isSqlQuery(obj: any): obj is SqlQuery {
  return (
    obj != null &&
    isString(obj.sql) &&
    (obj.bindings == null || isDbValued(obj.bindings))
  )
}

export interface SqlQuery {
  sql: string
  bindings?: Knex.Value[] | DbValued
  method?: "select" | "raw"
}

export type Queryish = string | Knex.QueryBuilder | SqlQuery | Knex.Raw

export function toSqlQuery(sql: Queryish): SqlQuery {
  if (sql == null) throw new Error("null sql")
  if (isSqlQuery(sql)) return sql
  if (isString(sql)) {
    return { sql }
  } else if (isFunction(sql["toSQL"])) {
    return omit(sql.toSQL() as any, "__knexQueryUid") as any as SqlQuery
  } else {
    throw new Error("not queryish: " + stringify(sql))
  }
}

export function sqlQueryToS(sq: SqlQuery) {
  if (sq.bindings == null) return sq.sql
  else if (Array.isArray(sq.bindings)) {
    return (
      flatten(
        zip(
          sq.sql.split("?"),
          sq.bindings.map(ea => stringify(ea))
        )
      )
        // .map(ea => ea.trim())
        .join("")
    )
  } else {
    let result = sq.sql
    for (const key of keys(sq.bindings)) {
      result = result.replace(
        new RegExp("[@:$]" + escapeRegExp(key), "gi"),
        toS(sq.bindings[key])
      )
    }
    return result
  }
}

export function isQueryBuilder(q: Queryish): q is Knex.QueryBuilder {
  return isFunction((q as any)?.where) && isFunction((q as any)?.limit)
}

/**
 * Assume a "heredoc" with 1 or more queries.
 *
 * - Comment lines that start with `--` will be removed
 * - Individual queries are separated by a semicolon at the end of a line
 * - Whitespace is normalized to a single space.
 * - Empty lines are filtered out
 */
export function prepQueries(s: string) {
  return s
    .replace(/\s*--.*\n/g, "")
    .split(";")
    .map(q => q.replace(/\s+/g, " ").trim())
    .filter(notBlank)
}
