import { Maybe } from "./MaybeTypes"
import { gt0, toGt0 } from "./Number"
import { swappableRotation } from "./Rotation"
import { megapixels, pixels2size } from "./Units"

export interface Dimensions {
  width: number
  height: number
}

export function isDimensions(a: any): a is Dimensions {
  return a != null && gt0(a.width) && gt0(a.height)
}

export function isCloseAspectRatio(
  a: Maybe<number>,
  b: Maybe<number>,
  withinRatio = 0.05
): boolean {
  return (
    gt0(a) &&
    gt0(b) &&
    a > 1 === b > 1 && // both are portrait or both are landscape
    Math.abs(a - b) <= Math.max(a, b) * withinRatio
  )
}

export function dimGte(a: Dimensions, b: Dimensions): boolean {
  return a.width >= b.width && a.height >= b.height
}

export function fmtDim(d: Dimensions, space = " ") {
  return `${d.width}${space}${space}${d.height}`
}

export function dimToSize(d: Dimensions) {
  return pixels2size(d.width * d.height)
}

/**
 * return `{ width: dim.height, height: dim.width }`
 */
export function dimSwap(dim: Dimensions): Dimensions {
  return { width: dim.height, height: dim.width }
}

export function maybeDimSwap(
  dim: Dimensions,
  rotation: Maybe<number>
): Dimensions {
  return swappableRotation(rotation) ? dimSwap(dim) : dim
}

export function maybeFlipInPlace(dim: Dimensions, rotation: Maybe<number>) {
  if (swappableRotation(rotation)) {
    ;[dim.width, dim.height] = [dim.height, dim.width]
  }
}

export function isPortrait(dim: Dimensions): boolean {
  return dim.width / dim.height < 1
}

export function dmegapixels(dim: Dimensions) {
  return megapixels(pixels(dim))
}

/**
 * @return 0 if dim is invalid
 */
export function pixels(dim: Maybe<Dimensions>) {
  return (toGt0(dim?.width) ?? 0) * (toGt0(dim?.height) ?? 0)
}
