import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { isEmpty } from "../../fe/Array"
import { map, orElse } from "../../fe/Maybe"
import { gt0 } from "../../fe/Number"
import { Asset } from "./Asset"
import { ChangedTag } from "./ChangedTag"
import { Model } from "./Model"
import { Tag } from "./Tag"

const logger = lazy(() => mkLogger("model.AssetTag"))

export class AssetTag extends Model {
  static override readonly $tableName = "AssetTag"
  static override readonly $uniqueColumnName = "assetId,tagId"
  static override readonly $useCache = false

  $pk() {
    return [
      orElse(this.assetId, () => map(this.asset, ea => ea.id)),
      orElse(this.tagId, () => map(this.tag, ea => ea.id))
    ].join(":")
  }

  asset!: Asset
  assetId!: number

  tag!: Tag
  tagId!: number

  static addTagsToAsset(assetId: number, tagIds: number[]) {
    if (!gt0(assetId)) {
      logger().warn("addTagsToAsset(): invalid assetId")
      return
    }
    tagIds = tagIds.filter(gt0)
    if (isEmpty(tagIds)) return

    ChangedTag.upsertChangedTagIds(tagIds)
    return this.dbl.runf(q =>
      q
        .insert(tagIds.map(tagId => ({ assetId, tagId })))
        .onConflict(["assetId", "tagId"])
        .ignore()
    )
  }

  static removeTagsFromAsset(assetId: number, tagIds: number[]) {
    tagIds = tagIds.filter(gt0)
    if (isEmpty(tagIds)) return
    ChangedTag.upsertChangedTagIds(tagIds)
    return this.dbl.runf(q =>
      q.whereIn("tagId", tagIds).andWhere({ assetId }).delete()
    )
  }
}
