import bs = require("better-sqlite3")
import _path from "path"
import { diffEql } from "../../core/Array"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { Schema } from "../../core/db/Schemas"
import { PosixFile } from "../../core/fs/PosixFile"
import { ProjectPath } from "../../core/fs/ProjectPath"
import { diff, isNotEmpty } from "../../fe/Array"
import { andList } from "../../fe/FmtList"
import { plur } from "../../fe/Units"
import { dbModelHealthCheck } from "../health/DbModelHealthCheck"
import { Db } from "./Db"
import { TableInfo, localTableInfo } from "./TableInfo"

const logger = lazy(() => mkLogger("db.DbSchemaValid"))

export function schemaJsonFile(schema: Schema) {
  // NOT SORTED!
  return PosixFile.for(_path.join(ProjectPath.Data(), schema + "-schema.json"))
}

interface JsonContents {
  info: string
  tableInfo: TableInfo[]
}

export async function assertValidDbSchema_(db: Db) {
  try {
    await db.migrate_()
    const actualTableInfo = localTableInfo(db.db)
    const actualTableNames = actualTableInfo.map(t => t.tableName)
    const expectedTableInfo = (
      await schemaJsonFile(db.schema).readJson_<JsonContents>()
    ).tableInfo
    {
      const expectedTableNames = expectedTableInfo.map(t => t.tableName)
      const missing = diff(expectedTableNames, actualTableNames)
      if (isNotEmpty(missing)) {
        logger().throw(
          "Db is missing " +
            plur(missing.length, "table") +
            " : " +
            andList(missing)
        )
      }
    }
    for (const table of actualTableInfo) {
      const exp = expectedTableInfo.find(t => t.tableName === table.tableName)
      if (exp == null) {
        logger().warn("Unexpected table in db", table)
      } else {
        const missing = diffEql(exp.columns, table.columns)
        if (isNotEmpty(missing)) {
          logger().error("Missing columns", { expected: exp, actual: table })
          logger().throw(
            "Db is missing " +
              plur(missing.length, "column") +
              " from table " +
              table.tableName +
              " : " +
              andList(missing.map(ea => ea.name))
          )
        }
      }
    }
    logger().info("Validated schema.")
  } catch (err: any) {
    // We can't recover from this!
    dbModelHealthCheck().reset()
    throw err
  }
}

export function writeSchemaJsonFile_(schema: Schema, db: bs.Database) {
  return schemaJsonFile(schema).writeJson_(
    {
      info: "This is used in validating PhotoStructure library databases after repair and restore operations.",
      tableInfo: localTableInfo(db)
    } satisfies JsonContents,
    { spaces: 2 }
  )
}
