import { Maybe } from "./MaybeTypes"
import { isNumber } from "./Number"

// This is ordered by popularity/likelihood of use
export const Rotations = [0, 90, 270, 180] as const
export type Rotation = (typeof Rotations)[number]

export function isRotation(r: any): r is Rotation {
  return isNumber(r) && Rotations.includes(r as Rotation)
}

/** @return if set, rotation, normalized to [0, 360) */
export function normalizeRotation(r: Maybe<number>): Maybe<Rotation> {
  if (!isNumber(r)) return
  const mod = (Math.round(r / 90) * 90) % 360
  // `<= 0` to avoid negative zero as a result
  return (mod <= 0 ? (mod + 360) % 360 : mod) as Rotation
}

/**
 * @return true if the dimensions need to be flipped to respect the given
 * rotation
 */
export function swappableRotation(rotation: Maybe<number>) {
  const r = normalizeRotation(rotation)
  return r === 90 || r === 270
}
