export function romanToInt(s: string): Maybe<number> {
  const index = RomanNumerals.indexOf(s as any)
  return index === -1 ? undefined : index
}

/**
 * @param i an integer between 1 and 60
 * @return the Roman numeral for `i`, or `i` as a string if `i` is not between 1 and 42
 */
export function intToRoman(i: number | string): string {
  return RomanNumerals[toInt(i) as any] ?? toS(i)
}
