import _os from "os"
import { lazy } from "../../core/Lazy"
import { map } from "../../fe/Maybe"
import { firstDefinedLater } from "../async/Later"
import { FatalErrorFlag } from "../error/ErrorFlags"
import { isWin, platformName } from "../platform/Platform"
import { BaseFile } from "./BaseFile"
import { pathIfExists, pathTo } from "./PathTo"
import { ProjectPath } from "./ProjectPath"

const toolsDir = lazy(() => map(ProjectPath.Tools(), ea => BaseFile.for(ea)))

/**
 * @param tool ".exe" is appended if the local platform is windows.
 * @throws if tool is not found
 */
export async function pathToTool_(tool: string): Promise<string> {
  if (true !== (await toolsDir()?.isDirectory())) {
    // Electron builds always have a tools dir:
    throw new Error("Cannot find project path for /tools/" + FatalErrorFlag)
  }

  return firstDefinedLater<string>(
    // See https://github.com/photostructure/base-tools/blob/main/Dockerfile
    () => pathIfExists(toolsDir()?.join("bin", tool)),

    () => devPathToTool(tool),
    // system-installed tool:
    () => systemPathToTool(tool)
  ) as any
}

function devPathToTool(tool: string) {
  return pathIfExists(
    toolsDir()?.join(
      platformName + "-" + _os.arch(),
      tool + (isWin ? ".exe" : "")
    )
  )
}

async function systemPathToTool(tool: string): Promise<string> {
  return firstDefinedLater<string>(
    // system-installed tool:
    () => pathTo({ tool }),
    () => {
      throw new Error("Cannot find path for " + tool)
    }
  ) as any
}

export const dcrawEmuNativePath_ = lazy(() => {
  return pathToTool_("dcraw_emu")
})
export const rawIdentifyNativePath_ = lazy(() => {
  return pathToTool_("raw-identify")
})
export const jpegtranNativePath_ = lazy(() => {
  return pathToTool_("jpegtran")
})
export const sqliteNativePath_ = lazy(() => {
  return pathToTool_("sqlite3")
})
