// How many bits per color for N modes?
// 6 bits shared across 3 channels means 2 bits (4 values) per channel, 4 ** 3 =
// 64 colors, but ends up only being ~21 colors in practice due to the
// non-uniform L*a*b* colorspace
// Total possible uniq labhashes
// for 6: 40
// for 7: 70
// for 8: 107
// for 9: 172
// for 10: 292
// for 11: 460
// for 12: 589
// But in practice, photos only have a subset of the full colorspace:
// for 8, ~35 colors.
// for 9, ~38 colors.
// for 10, ~49 colors
// for 11, ~47 colors (! worse than 10)
// for 12, ~80 colors
// for 15, ~95 colors (still ample collisions)
// HEY FUTURE ME. 12 bits is _fine_. It allows for reasonably good matching (average cie94 delta of 3)

export const ModeBits = 12 // 12 = 4 bits per L*, a*, and b channels
