interface Defer<T> {
  (): T
  prior(): T | undefined
}

/**
 * Just like {@link MemoizedThunk}, but evaluated only once.
 */
export function defer<T>(thunk: () => T): Defer<T> {
  let prior: T
  let caught: Error
  let applied = false
  const result = function DeferImpl() {
    if (applied) {
      if (caught != null) throw caught
      return prior!
    }
    try {
      applied = true
      return (prior = thunk())
    } catch (error: any) {
      caught = error
      throw error
    }
  }
  result.prior = () => prior
  return result
}
