import { lazy } from "../../core/Lazy"
import { sort } from "../../fe/ArraySort"
import { Maybe } from "../../fe/MaybeTypes"
import { times } from "../../fe/Number"
import { CaseInsensitiveMap } from "../CaseInsensitiveMap"

const m = lazy(() => {
  const result = new CaseInsensitiveMap<number>()
  // `undefined` uses the system locale:
  for (const locale of ["en-US", undefined]) {
    // Accept "sep" or "sept" for September:
    result.set("Sept", 9)
    result.set("Sept.", 9)
    for (const flavorOfTheMonth of ["short", "long"] as const) {
      const dtf = new Intl.DateTimeFormat(locale, {
        month: flavorOfTheMonth
      })
      times(12, index => {
        const monthName = dtf.format(new Date(2017, index))
        result.set(monthName, index + 1)
        if (flavorOfTheMonth === "short" && locale === "en-US") {
          // TODO: do other locales add a period after the abbreviated month name?
          result.set(monthName + ".", index + 1)
        }
      })
    }
  }
  return result
})

export function monthName2index(monthName: Maybe<string>): Maybe<number> {
  return monthName == null ? undefined : m().get(monthName)
}

export function monthNames(): string[] {
  return sort([...m().keys()])
}
