const logger = lazy(() => mkLogger("img.Video"))

function mkLog(method: string, src: string | SimpleFile) {
  return mkLogger("img/Video." + method + "(" + src + ")")
}

export async function getVideoToolDetails_(opts?: {
  ignoreffmpeg: any
}): PromiseMaybe<ToolDetails> {
  if (isProd) opts = undefined
  return isTrue(opts?.ignoreffmpeg) ? undefined : await ffmpegVersion_()
}

export const isVideoSupported = lazy(async () => {
  try {
    return (await getVideoToolDetails_())?.isSupportedVersion === true
  } catch (error) {
    logger().warn("isVideoSupported", { error })
    return false
  }
})

later(() => ee().on("clearCache", () => isVideoSupported.unset()))

// These are "high quality" bitrates for a given video resolution.
export const bitrateKps = (pixelCount: number) =>
  sigFigs(
    lerp2d(
      pixelCount,
      { x: 320 * 240, y: Settings.transcodeBitrateQVGA.valueOrDefault },
      { x: 3840 * 2160, y: Settings.transcodeBitrateUHD.valueOrDefault }
    ),
    2
  )

function extractMaxBitrate(src: PosixFile, rawTags: Tags) {
  const log = mkLog("extractMaxBitrate", src)
  // These dimensions will be enforced by PosixFileFilters, before this. We need
  // reasonable dimensions to give to VLC, though, so re-validating them here is
  // excusable. Hopefully.
  const minDim = Settings.minVideoDimension.valueOrDefault
  const width = rawTags.ImageWidth
  if (width != null && !gte(width, minDim)) {
    return log.throw("invalid width: " + width, { ignorable: true })
  }
  const height = rawTags.ImageHeight
  if (height != null && !gte(height, minDim)) {
    return log.throw("invalid height: " + height, { ignorable: true })
  }

  const bitrateKbps =
    extractBitrateKbps(rawTags) ?? Settings.transcodeBitrateUHD.valueOrDefault

  // Don't let video bitrate exceed the encoded bitrate:
  const videoBitrateKbps = mapGt0(width, w =>
    mapGt0(height, h => clamp(0, bitrateKbps, bitrateKps(w * h)))
  )

  const result = { width, height, videoBitrateKbps }
  log.debug("dim()", { src, result })
  return result
}

const cache = lazy(
  () =>
    new FileCache<string>({
      name: "img.videoFrame",
      timeoutMs: minuteMs
    })
)

export async function extractVideoFrame_(file: string | PosixFile) {
  return (await isVideoSupported())
    ? PosixFile.for(await _extractVideoFrame_(toNativePath_(file)))
    : undefined
}

// only exported for shim
export const _extractVideoFrame_ = shim1({
  name: "img.extractVideoFrame",
  cache,
  impl: (nativePath: string) => __extractVideoFrame_(nativePath)
})

// only exported for tests
export async function __extractVideoFrame_(
  nativePath: string,
  forceImpl?: {
    useFfmpeg?: boolean
  }
): Promise<string> {
  const log = mkLog("extractVideoFrame", nativePath)
  const src = PosixFile.for(nativePath)
  const useFfmpeg = isTrue(forceImpl?.useFfmpeg) || (await isFFmpegSupported()) // < try to use ffmpeg if possible
  if (!useFfmpeg) return log.throw("no video implementation")

  // We don't use BaseFile.applyIfEmpty here, because we want to verify the
  // dimensions are reasonable:

  const dest = await cachedImageFile_(src, "frame", ".jpg")
  log.debug("extractVideoFrame(" + dest + ")")

  const srcMtime = await src.mtimeMs()
  if (srcMtime == null) {
    return log.throw("null mtime")
  }

  const rawTags = await readRawTags(src)
  if (rawTags == null) {
    return log.throw("no tags")
  }
  const rot = extractRotation(rawTags)
  log.debug("video orientation:" + rot)

  // Note that width and height may be missing from videos like AVCHD:
  const srcDim = extractSizeInfoFromTags(rawTags, rot)?.dimensions

  const destStat = await dest.stat()
  // If the file doesn't exist, don't tease ExifTool:
  const destDim = destStat == null ? undefined : await dimensions(dest)

  if (
    destStat != null &&
    destStat.mtimeMs > srcMtime &&
    destDim != null &&
    (srcDim == null ||
      (destDim.height === srcDim.height && destDim.width === srcDim.width))
  ) {
    log.debug("prior dest, " + dest + " seems reasonable", { srcDim, destDim })
    return dest.nativePath
  }
  const duration = extractDurationSec(rawTags)
  const startAtSec = Math.min(
    duration ?? 0,
    Settings.videoFrameAtSec.valueOrDefault
  )
  log.info("extracted metadata", {
    startAtSec,
    duration
  })

  await dest.applyIfEmpty_({
    fn_: async destWip => {
      const args = {
        src,
        dest: destWip,
        startAtSec,
        ...srcDim
      }

      await ffmpegFrame_(args)

      if (
        false ===
        (await untilTrue(
          async () =>
            log.tap({
              msg: "frame extracted?",
              result: await destWip.clear().isNonEmptyFile()
            }),
          { timeoutMs: 5 * secondMs, intervalMs: 500 }
        ))
      ) {
        log.throw("ffmpeg failed to extract frame")
      }

      await deleteAllTags_(destWip, destWip)

      // If we have to add VLC support again, remember that it doesn't
      // auto-rotate. FFmpeg does.
      // if (useVlc && rot != null && rot !== 0) {
      //   log.info("rotating in place..." + destWip, { rot })
      //   await rotateInPlace_(destWip, rot)
      // }
    },
    timeoutMs: commandTimeoutMs()
  })
  return dest.nativePath
}

export async function isVideoTranscodingSupported() {
  return Settings.transcodeVideos.valueOrDefault && (await isFFmpegSupported())
}

export async function needsTranscoding(f: PosixFile) {
  const log = mkLog("needsTranscoding", f)

  if (!(await isVideoTranscodingSupported())) {
    return log.tap({
      msg: "videoTranscodingSupported is false",
      result: false
    })
  }

  const t = await readRawTags(f)
  if (t == null) {
    return log.tap({
      msg: "Cannot transcode files that exiftool can't read",
      result: false
    })
  }

  const mimetype = t.MIMEType!

  if (!isVideoMimeType(mimetype)) {
    return log.tap({
      msg: "not transcoding (unsupported mimetype)",
      result: false,
      meta: { mimetype }
    })
  }

  const duration = extractDurationSec(t)

  if (!gt(duration, Settings.minVideoDurationSec.valueOrDefault)) {
    return log.tap({
      msg: "not transcoding (video duration is too short)",
      result: false,
      meta: { duration }
    })
  }

  const audioCodecs = compactBlanks([t.AudioFormat])
  const isSafeAudioCodec = audioCodecs.some(ea =>
    includesIgnoreCase(Settings.doNotTranscodeAudioCodecs.values, ea)
  )

  const videoCodecs = compactBlanks([
    t.VideoCodec,
    t.CompressorID,
    (t as any).CompressorName
  ])
  const isSafeVideoCodec = videoCodecs.some(ea =>
    includesIgnoreCase(Settings.doNotTranscodeVideoCodecs.values, ea)
  )

  const isSafeMimeType = includesIgnoreCase(
    Settings.doNotTranscodeMimeTypes.values,
    mimetype
  )

  return log.tap({
    level: "info",
    msg: "result",
    result: !(isSafeAudioCodec && isSafeVideoCodec && isSafeMimeType),
    meta: {
      mimetype,
      isSafeMimeType,
      audioCodecs,
      isSafeAudioCodec,
      videoCodecs,
      isSafeVideoCodec
    }
  })
}

/**
 * @return true iff `dest` seems to be an up to date transcoded version of
 * `src`
 */
async function isTranscodeOf(src: PosixFile, dest: PosixFile) {
  const destRawTags = await readRawTags(dest)
  const srcRawTags = await readRawTags(src)
  const srcDurationSec = extractDurationSec(srcRawTags)
  const destDurationSec = extractDurationSec(destRawTags)
  return logger().tap({
    msg: "isTranscodeOf()",
    result: closeTo(srcDurationSec, destDurationSec, 1.5),
    meta: {
      srcDurationSec,
      destDurationSec,
      src: src.nativePath,
      dest: dest.nativePath
    }
  })
}

const currentTranscodes = new TTLMap<string, Deferred<void>>(15 * minuteMs)

/**
 * This will no-op if a reasonable transcoded video already exists at dest (unless ctx.force is true)
 */
export async function transcode_(
  src: PosixFile,
  dest: PosixFile,
  ctx?: { force?: boolean }
): PromiseMaybe<PosixFile> {
  if (!(await isVideoTranscodingSupported())) {
    return
  }

  const log = mkLog("transcode", src)

  const prior = currentTranscodes.get(dest.nativePath)
  if (prior != null && !isTrue(ctx?.force)) {
    // TODO: What if src doesn't match the prior src? kill the prior?
    log.info("already transcoding", { dest, priorSrc: prior.name })
    return prior.promise.then(() => dest)
  }
  const d = new Deferred<void>(src.nativePath)
  currentTranscodes.set(dest.nativePath, d)

  if (false === (await needsTranscoding(src))) {
    log.info("no transcoding needed")
    return
  }

  const filesize = await src.size()
  if (!gt0(filesize)) {
    return log.throw("source is empty or cannot read")
  }

  const srcTags = await readTags(src)
  if (srcTags == null) {
    return log.throw("source cannot be read by ExifTool")
  }

  if (true === ctx?.force) {
    await dest.unlink()
  } else {
    if (await isTranscodeOf(src, dest)) {
      log.info("no-op, dest duration looks reasonable", { dest })
      return dest
    }
  }

  return time("video.transcode()", async () => {
    const durationSec = extractDurationSec(srcTags) ?? 60
    const args: any = {
      src,
      ...extractMaxBitrate(src, srcTags)
    }
    const expectedFilesize = guessExpectedSize(
      filesize,
      args.videoBitrateKbps,
      durationSec
    )

    const maxDim = Settings.transcodeMaxDim.valueOrDefault
    const srcMaxDim = max([srcTags.dimensions.height, srcTags.dimensions.width])
    if (gt0(maxDim) && gt(srcMaxDim, maxDim)) {
      const output = fitInside(srcTags.dimensions, {
        width: maxDim,
        height: maxDim
      })
      if (output == null) {
        log.warn(
          "Cannot downsample transcoded video: fitInside() returned null",
          { input: srcTags.dimensions, maxPixels: maxDim }
        )
      } else {
        // libx264 requires even dimensions:
        args.width = roundEven(output.width)
        args.height = roundEven(output.height)
        log.info("Downsampling transcoded video", {
          original: srcTags.dimensions,
          output
        })
      }
    }

    const _transcode_ = async (destWip: PosixFile) => {
      log.info("starting...", { destWip })
      const obs = new PullProgressObserver(
        { path: src.nativePath, op: "Transcoding video" },
        expectedFilesize,
        async () => {
          return (await dest.clear().size()) ?? 0
        }
      )

      args.dest = destWip

      args.fps = toGt0(srcTags.VideoFrameRate)

      const result = await obs.observe(ffmpegTranscode_(args))
      if (result.code !== 0) {
        log.throw("transcode failed with code " + result.code)
      }
    }

    await dest.applyWip_({
      fn_: destWip => _transcode_(destWip),
      skipFsLock: false,
      timeoutMs: 0
    })
    return dest
  })
}

export function guessExpectedSize(
  filesize: number,
  videoBitrateKbps: Maybe<number>,
  durationSec: Maybe<number>
) {
  return Math.min(
    filesize,
    // SITS: these constants are not really defensible
    (durationSec ?? 10) * (videoBitrateKbps ?? 1000)
  )
}

/**
 * @throws on errors
 */
export async function validVideo_(src: PosixFile) {
  // quick pre-check:
  const f = await extractVideoFrame_(src)
  if (f == null) {
    mkLog("validVideo", src).throw("Could not extract a video frame")
  }
  return ffmpegValidVideo_(src)
}
