import { lazy } from "../../core/Lazy"
import { within } from "../../fe/Number"
import { mkLogger } from "../Logger"
import { Average } from "../math/Average"
import { Settings } from "../settings/Settings"
import { clampLab } from "./Colorspace"
import { Triplet } from "./Triplet"

const logger = lazy(() => mkLogger("color.Lab"))

export function isLab(l: any): l is Triplet {
  return (
    Array.isArray(l) &&
    l.length === 3 &&
    within(0, 100, l[0]) &&
    within(-110, 110, l[1]) &&
    within(-110, 110, l[2])
  )
}

/**
 * @param labs [[L* values], [a* values], [b* values]]
 */
export function labsAreGreyscale(labs: Triplet[]) {
  const thresh = Settings.greyscaleColorThreshold.valueOrDefault
  const a = new Average()
  const b = new Average()
  for (const ea of labs) {
    const clamped = clampLab(ea)
    a.push(clamped[1])
    b.push(clamped[2])
  }
  const value = (a.stdDev ?? 0) + (b.stdDev ?? 0)
  const result = value < thresh
  return logger().tap({
    msg: "labsAreGreyscale()",
    result,
    meta: { value, thresh }
  })
}
