import { tt } from "../../fe/BabyMarkdown"
import { minuteMs, secondMs } from "../../fe/Date"
import { defer } from "../../fe/Defer"
import { fmtPct } from "../../fe/Number"
import { EndableInterval } from "../async/EndableInterval"
import { Settings } from "../settings/Settings"
import { CpuUsage, isTooBusy } from "../work/CpuUsage"
import { HealthCheck } from "./HealthCheck"

export const systemLoadHealthCheck = defer(() => {
  const interval = new EndableInterval({
    name: "systemLoadHealthCheck",
    intervalMs: minuteMs,
    callback: async () => systemLoadHealthCheck().refresh()
  })
  return HealthCheck.for({
    section: "System",
    id: "system-load",
    pendingMsg: "Checking system load",
    settings: ["cpuBusyPercent"],
    rejectIsWarn: true,
    later: async () => {
      const busyPct = CpuUsage.instance().busyPct()
      const tooBusy = isTooBusy()
      interval.setIntervalMs((tooBusy ? 6 : 60) * secondMs)
      const level = tooBusy ? "stop-sync" : "ok"
      const msg =
        "CPU utilization is " +
        fmtPct(busyPct, 200) +
        "\n" +
        (tooBusy
          ? "PhotoStructure sync is paused until CPU utilization drops below"
          : "PhotoStructure sync will pause if CPU utilization exceeds") +
        " " +
        tt(Settings.cpuBusyPercent.key) +
        ": " +
        fmtPct(Settings.cpuBusyPercent.valueOrDefault, 200)
      return { level, msg }
    }
  })
})
