import _k, { Knex } from "knex"
import { lazy } from "../../core/Lazy"
import { thenMap } from "../../core/async/Promise"
import { map } from "../../fe/Maybe"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { values } from "../../fe/Object"

export const knex = lazy(() =>
  _k({ client: "sqlite3", useNullAsDefault: true })
)

export async function firstMigratedAt(k: Knex): PromiseMaybe<Date> {
  return thenMap(k("migrations").min("migration_time").first(), d =>
    map(values(d as any)[0], ea => new Date(ea as any))
  )
}
