import { Range, SemVer, parse, satisfies } from "semver"
import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { Comparable } from "../../fe/Primitive"
import { toS } from "../../fe/toS"
import { greatestBy } from "../Array"
import { UpdateChannels, extractUpdateChannel } from "./UpdateChannels"

// Typical debian package versions don't comply with semver:
// 0.11.2-6
// 2:4.15.13+dfsg-0ubuntu1.4
// 4.8.0-2ubuntu1

const DebianVersionRE =
  /^(?:\d+:)?(?<major>\d+)(?<minor>\.\d+)?(?<patch>\.\d+)?/

export function debianVersionToSemver(
  version: Maybe<string | SemVer>
): Maybe<string> {
  if (version instanceof SemVer) return version.version
  const ver = DebianVersionRE.exec(version ?? "")?.groups
  return ver == null
    ? undefined
    : ver.major + (ver.minor ?? ".0") + (ver.patch ?? ".0")
}

export function semverSatisfies(
  version: string | SemVer,
  range: string | Range
): boolean {
  if (blank(version)) return false
  const optionsOrLoose = { loose: true, includePrerelease: true }
  if (satisfies(version, range, optionsOrLoose)) return true
  const v = debianVersionToSemver(version)
  return v == null ? false : satisfies(v, range, optionsOrLoose)
}

export function semverGte(v1: string | SemVer, v2: string | SemVer): boolean {
  return semverLatest(v1, v2) === v1
}

export function toSemver(v: string | SemVer): Maybe<SemVer> {
  try {
    return v instanceof SemVer ? v : parse(v, { loose: true }) ?? undefined
  } catch {
    return
  }
}

function semverSortBy(v: string | SemVer): Maybe<Comparable[]> {
  const s = toSemver(v)
  const ord = UpdateChannels.ordinal(extractUpdateChannel(toS(v)))
  return s == null
    ? undefined
    : [s.major, s.minor, s.patch, ord, ...s.prerelease.slice(1)]
}

export function semverLatest<T extends string | SemVer>(...arr: T[]): Maybe<T> {
  return greatestBy(arr, semverSortBy)
}
