const logger = lazy(() => mkLogger("tags.ExifTool"))

let _addInstanceIdsToTags = false

export function addInstanceIdsToTags(b: boolean) {
  _addInstanceIdsToTags = b
}

export function setExifToolProcs(i: number) {
  // used only in tests, and we want children to inherit this value:
  Settings.exiftoolProcsPerChild.envValue = i
  return shutdownExiftool()
}

function maxProcs() {
  // thread sub-processes only do one thing at a time, so they only need 1 exiftool:
  return isTest
    ? 8
    : isWorkerService()
    ? 2
    : Settings.exiftoolProcsPerChild.value ??
      Math.ceil(maxConcurrentImports() / 3)
}

const _exiftool = lazy(() => {
  // BatchClusterObserver will renice, track and clean up child exiftool processes:
  return new BatchClusterObserver(
    "ExifTool",
    new ExifTool({
      useMWG: Settings.useMWG.valueOrDefault,
      backfillTimezones: Settings.backfillTimezones.valueOrDefault,
      inferTimezoneFromDatestamps: false, // < we'll handle it in InferTimezone
      defaultVideosToUTC: Settings.defaultVideosToUTC.valueOrDefault,
      isIgnorableError: error => {
        return logger().tap({
          msg: "isIgnorableError()",
          result: startsWithIgnoreCase(toS(error).trim(), "warning:"),
          meta: { error, toS: toS(error) }
        })
      },
      // TODO: FINISH: includeImageDataMD5: Settings.useImageDataHashes.valueOrDefault,
      ...batchClusterOptions(maxProcs(), "tags.exiftool")
    })
  ).t
})

export function exiftool(): ExifTool {
  const et = _exiftool()
  return et.ended ? _exiftool.refresh() : et
}

export async function exiftoolVersion_(): PromiseMaybe<string> {
  return thenOrTimeout(exiftool().version(), ShortCommandTimeoutMs, () => {
    throw new Error("ExifTool timed out")
  })
}

/**
 * If this process hasn't started exiftool, don't bother.
 */
export function exiftoolVersionMaybe() {
  return map(_exiftool.prior(), et =>
    et.ended
      ? undefined
      : thenOrTimeout(et.version(), commandTimeoutMs(), () => {
          throw new Error("ExifTool timed out")
        })
  )
}

// Primarily for tests:
export function shutdownExiftool(gracefully: boolean = false) {
  return map(_exiftool.clear(), ea => ea.end(gracefully))
}

/**
 * @throws on error
 */
export function extractBinaryTag(
  tagname: string,
  src: string,
  dest: string
): Promise<void> {
  return exiftool().extractBinaryTag(tagname, src, dest)
}

// Only exported for tests
export const tagsCache = lazy(
  () =>
    new FileCache<Maybe<ExifTags>>({
      name: "tags.parsed",
      maxSize: 256,
      timeoutMs: minuteMs
    })
)

// Only exported for tests
export const rawTagsCache = lazy(
  () =>
    new FileCache<Maybe<TimedTags>>({
      name: "tags.raw",
      maxSize: 256,
      timeoutMs: minuteMs
    })
)

export function clearTagsCache() {
  tagsCache.prior()?.clear()
  rawTagsCache.prior()?.clear()
  mimetypeCache.prior()?.clear()
}

// These are fields that aren't in the Tags interface.
const IgnoredNonTypedFields = {
  ExifToolVersion: undefined,
  RAFVersion: undefined, // ? wha?
  Directory: undefined,
  ExifByteOrder: undefined,
  FileAccessDate: undefined,
  FileCreateDate: undefined,
  FileIndex2: undefined,
  FileInfoVersion: undefined,
  FileInodeChangeDate: undefined,
  FileModifyDate: undefined,
  FilePermissions: undefined
}

export const IgnoredTypedFields: Tags = {
  Duration: undefined, // < See https://forum.photostructure.com/t/videos-duration-wildly-inaccurate/999/2
  MediaDuration: undefined,
  TrackDuration: undefined,
  FileFormat: undefined,
  FileIndex: undefined,
  FileName: undefined,
  FileNumber: undefined,
  FileNumberMemory: undefined,
  FileNumberSequence: undefined,
  FileSize: undefined,
  FileType: undefined,
  FileTypeExtension: undefined,
  FileVersion: undefined,
  MIMEType: undefined,
  SourceFile: undefined
}

export const IgnoredSidecarFields = [
  "errors",
  ...(keys(IgnoredNonTypedFields) as any),
  ...keys(IgnoredTypedFields)
] as const satisfies readonly (keyof ExifTags)[]

async function readSidecarTags(f: PosixFile) {
  return omit(await readRawTags(f, false), ...(IgnoredSidecarFields as any))
}

export async function sidecarEql(a: PosixFile, b: PosixFile): Promise<boolean> {
  return (
    (await eqlAsync(a.sha(), b.sha())) ||
    (await eqlAsync(readSidecarTags(a), readSidecarTags(b)))
  )
}

/**
 * Fetch the ExifTags for the given file, including sidecar and sibling inferred
 * metadata.
 */
export function readTags(
  pathOrFile: Maybe<SimpleFileOrString>
): MaybePromiseMaybe<ExifTags> {
  return pathOrFile == null ? undefined : _readTags(toNativePath_(pathOrFile))
}

export const _readTags = shim1({
  name: "tags.readTags",
  impl: async (nativePath: string) => {
    const f = PosixFile.for(nativePath)
    return parseTags(f, await readRawTags(f))
  },
  cache: tagsCache
})

export async function cachedTags(pathOrFile: SimpleFileOrString) {
  return tagsCache().get(toNativePath_(pathOrFile))
}

// Convenience methods:

export async function readCapturedAt(
  pathOrFile: SimpleFileOrString
): PromiseMaybe<CapturedAt> {
  return (await readTags(pathOrFile))?.capturedAt
}

export async function readRawField<K extends keyof Tags>(
  pathOrFile: SimpleFileOrString,
  tagName: K
): PromiseMaybe<Tags[K]> {
  try {
    const result = await exiftool().readRaw(toNativePath_(pathOrFile), [
      "-" + tagName
    ])
    return pluckCaseInsensitive(result, tagName)
  } catch (err) {
    logger().info("readRawField failed", err)
    return
  }
}

export async function readRotation(
  pathOrFile: SimpleFileOrString
): PromiseMaybe<number> {
  // We want to pick up rotation from a sidecar, if present:
  return extractRotation(await readRawTags(pathOrFile, true))
}

/**
 * ExifTool writes "file.jpg_original" files. Rename them to
 * "file_original_06.jpg" so the file extension isn't bogus.
 */
export async function moveOriginal(
  src: PosixFile,
  suffix = "_original"
): PromiseMaybe<PosixFile> {
  return src
    .sibling(src.base + suffix)
    .saveIfNewOrDelete(src.name + suffix + src.ext)
}

export async function overwriteTags_(src: PosixFile, tags: WriteTags) {
  try {
    await time("tag.overwriteTags()", async () => {
      await exiftool().write(src.nativePath, tags, await writeTagArgs(src))
      src.clearThisAndParent() // < this may be a new file
    })
  } catch (error) {
    logger().throw("failed to overwrite tags to " + src, { error, tags })
  }
}

export async function deleteAllTags_(src: PosixFile, dest: PosixFile) {
  await time("tag.deleteAllTags()", async () => {
    const args = ["-all="]
    if (src.eql(dest)) {
      args.push("-overwrite_original")
    } else {
      args.push("-o", dest.nativePath)
    }
    await exiftool().write(src.nativePath, {}, args)
    dest.clear()
  })
}

export async function writeTagArgs(dest: PosixFile) {
  // See https://forum.photostructure.com/t/manually-editing-capture-time-title-and-description-caption/104/9?u=mrm

  // TODO: https://exiftool.org/ExifTool.html#QuickTimeUTC
  // ("Error: Warning: Tag 'api' is not defined")
  const args = []
  if (Settings.overwriteOriginal.valueOrDefault)
    args.push("-overwrite_original")
  if (gt(await dest.size(), 2 * GB)) {
    // https://exiftool.org/ExifTool.html#LargeFileSupport
    args.push("-api LargeFileSupport=1")
  }
  return args
}

/**
 * Figure out where we should stuff this tag
 */
export async function writeTagDest(
  src: PosixFile,
  tagName: keyof WriteTags,
  srcMimeType?: string
): Promise<PosixFile> {
  // if it's blocklisted, no need to check anything else:
  if (
    includesIgnoreCase(
      Settings.sidecarTagBlocklist.values,
      stripSuffix(tagName, "#") // < handle `Orientation#`
    )
  ) {
    return logger().tap({
      msg: "writeTagDest(): blocklisted tag, not writing to sidecar",
      result: src,
      meta: {
        src,
        tagName
      }
    })
  }
  const mt = srcMimeType ?? (await readMimeType(src))
  const sidecar = await src.sidecar()
  const writeMetadataToSidecarsIfImage =
    isImageMimeType(mt) &&
    Settings.writeMetadataToSidecarsIfImage.valueOrDefault

  const writeMetadataToSidecarsIfVideo =
    isVideoMimeType(mt) &&
    Settings.writeMetadataToSidecarsIfVideo.valueOrDefault

  const writeMetadataToSidecarsIfSidecarExists =
    Settings.writeMetadataToSidecarsIfSidecarExists.valueOrDefault &&
    (await sidecar.exists())

  const useSidecar =
    writeMetadataToSidecarsIfImage ||
    writeMetadataToSidecarsIfVideo ||
    writeMetadataToSidecarsIfSidecarExists

  return logger().tap({
    msg: "writeTagDest()",
    result: useSidecar ? sidecar : src,
    meta: {
      src,
      tagName,
      mimetype: mt,
      writeMetadataToSidecarsIfImage,
      writeMetadataToSidecarsIfVideo,
      writeMetadataToSidecarsIfSidecarExists
    }
  })
}

export function writeTags_(
  src: PosixFile,
  tags: WriteTags,
  srcMimeType?: string
): Promise<void> {
  return time("tags.writeTags", async () => {
    // multiple tags may mean multiple destinations. YAY
    const path2entries = new MultiMap<string, [keyof WriteTags, any]>()
    for (const [k, v] of entries(tags)) {
      const dest = await writeTagDest(src, k, srcMimeType)
      path2entries.add(dest.nativePath, [k, v])
    }

    for (const [dest, tagEntries] of path2entries.entriesArray()) {
      const f = PosixFile.for(dest)
      const t = fromEntries(tagEntries)
      logger().info("writeTags()", { src, dest, t })
      await exiftool().write(f.nativePath, t, await writeTagArgs(f))
      if (!Settings.overwriteOriginal.valueOrDefault) {
        await moveOriginal(f)
      }
      // This may be a new file, or an existing file--either way, the parent
      // directory should be re-scanned:
      f.clearThisAndParent()
    }
  })
}

/**
 * Return the raw, unparsed tags for `bf`, and merge tags from any sidecars.
 */
export async function readRawTags(
  f: SimpleFileOrString,
  includeSidecars = true
): PromiseMaybe<TimedTags & WithSidecars & ParsedTags> {
  const pf = PosixFile.for(f)
  if (pf.isSidecar()) includeSidecars = false

  if (await pf.isEmptyFile(64)) {
    rawTagsCache().delete(pf.nativePath)
    logger().debug(
      "readRawTags(" + pf + "): !nativePathIsFile(), returning null."
    )
    return
  }

  const fileTags = await _readRawTags(pf.nativePath)
  if (fileTags == null || !includeSidecars) return fileTags

  logger().debug("readRawTags()", {
    pf,
    pickedFileTags: pick(
      fileTags,
      "tz",
      "tzSource",
      ...(Settings.capturedAtTags.values as any)
    )
  })

  const file2tags: [PosixFile, PromiseMaybe<TimedTags | JsonSidecarTags>][] = []

  for (const ea of await pf.jsonSidecars()) {
    file2tags.push([ea, readJsonSidecar(ea, fileTags.tz)])
  }

  for (const ea of await pf.existingExifSidecars()) {
    file2tags.push([ea, _readRawTags(ea.nativePath)])
  }

  // we shallow clone here to prevent messing up the _readRawTags cache:
  const result: TimedTags & WithSidecars & ParsedTags = {
    original: {},
    MIMEType: mapNotBlank(fileTags.MIMEType, normalizeMimetype),
    ...fileTags
  }

  const histories: ResourceEvent[] = []

  for (const [sidecar, p] of file2tags) {
    if (p == null) continue
    const sidecarTags = await p
    if (sidecarTags == null) continue
    const safeTags = omit(sidecarTags, ...IgnoredSidecarFields)
    if (isNotEmpty(values(safeTags))) {
      ;(result.sidecars ??= []).push(sidecar.base)

      histories.push(...toA((sidecarTags as any)["History"]))

      // store overwritten values:
      assignNullishFields(result.original, pick(result, ...keys(safeTags)))

      // sidecar values win:
      assignFields(result, safeTags)

      logger().debug("readRawTags() sidecar had values", {
        sidecar: sidecar.base
      })
    } else {
      logger().debug("readRawTags() sidecar was empty", {
        sidecar: sidecar.base
      })
    }
  }

  result.inferred = getInferredHistoricValues(histories)
  assignNullishFields(result, result.inferred)

  logger().debug("readRawTags() final", {
    pf,
    inferred: result.inferred,
    pickedResult: pick(
      result,
      "tz",
      "tzSource",
      ...(Settings.capturedAtTags.values as any)
    )
  })

  return result
}

/**
 * This doesn't handle sidecars. You want `readRawTags`.
 */
export const _readRawTags = shim1({
  name: "tags.readRawTags",
  cache: rawTagsCache,
  impl: async (nativePath: string) => {
    if (!(await nativePathIsFile(nativePath))) {
      logger().debug("_readRawTags(): !nativePathIsFile(), returning null.", {
        nativePath
      })
      rawTagsCache().delete(nativePath)
      return
    }

    logger().debug("_readRawTags() not cached, reading now.", { nativePath })

    // We need to read the whole file to get video duration. [] disables the
    // ["-fast"]:
    const args = isVideoExt(_path.extname(nativePath)) ? [] : undefined

    const t = await time("tag.read()", () =>
      exiftool()
        .read(nativePath, args)
        .catch(error => {
          logger().warn("ExifTool failed to read", { nativePath, error })
          return undefined
        })
    )
    if (t == null) return

    logger().debug("_readRawTags()", {
      nativePath,
      pickedTags: pick(
        t,
        "tz",
        "tzSource",
        ...(Settings.capturedAtTags.values as any)
      )
    })

    t.MIMEType = mapNotBlank(t.MIMEType, normalizeMimetype)

    // PERF: no maps here as there are tons of fields.
    for (const key of keys(t)) {
      const v = t[key]
      if (typeof v === "string") {
        if (v === "0000:00:00" || v === "0000:00:00 00:00:00") {
          delete t[key]
        }
      }
    }

    const problems = compactBlanks(
      [t.Error, ...(t.errors ?? []), t.Warning].map(toS)
    )
    if (isNotEmpty(problems)) {
      ;(t as any)["problems"] = problems
    }
    if (_addInstanceIdsToTags) (t as any)["__instance"] = safeUUID()
    return t as TimedTags
  }
})

/**
 * NOT FOR PUBLIC CONSUMPTION only exposed for tests
 */
export async function parseTags(f: PosixFile, t: Maybe<TimedTags>) {
  return t == null ? undefined : time("tag.parseTags()", () => _parseTags(f, t))
}

async function _parseTags(f: PosixFile, t: TimedTags): PromiseMaybe<ExifTags> {
  try {
    const nativePath = f.nativePath
    const m = (t.MIMEType = mapNotBlank(t.MIMEType, normalizeMimetype))
    if (blank(m)) {
      logger().debug("No mimetype for " + nativePath)
      return
    }
    // easy assigning additional fields from the ExifTags interface.

    // under-lay inferred values as a backstop for missing values in `t`:
    const a = { ...((t as any).inferred ?? {}), ...t } as ExifTags
    a.inferred ??= {}
    a.original ??= {}

    if (Settings.fuzzyDateParsing.valueOrDefault) {
      for (const k of [
        ...Settings.capturedAtTags.values,
        ...Settings.capturedAtTagsFallback.values
      ]) {
        const v = (a as any)[k]
        if (isString(v)) {
          const fuzzy = parseDated({
            input: v,
            includeDate: true,
            includeFuzzyDate: true
          })
          if (fuzzy != null) {
            logger().debug("parseTags() fuzzy date", { fuzzy, k, v })
            ;(a as any)[k] = fuzzy
          }
        }
      }
    }

    const skipSiblingInference = !(await canInferForDir(f.parent()))

    const tzMeta = await maybeInferTimezone(f, a, skipSiblingInference)

    logger().debug("parseTags()", { tzMeta, skipSiblingInference })

    if (tzMeta?.tz != null) {
      const keepLocalTime =
        a.tzSource === defaultVideosToUTC ? false : undefined
      // We don't set timezones for all dates: they may be in a different tz
      // (due to subsequent edits or whatever)
      for (const k of [
        ...Settings.capturedAtTags.values,
        ...Settings.capturedAtTagsFallback.values
      ]) {
        const v = (a as any)[k]
        if (v instanceof ExifDateTime) {
          ;(a as any)[k] = setZone(v, tzMeta.tz, { keepLocalTime }) ?? v
        }
      }

      // save the prior values:
      assignUndefinedFields(a.original, a, "tz", "tzSource")
      // and adopt the new tzMeta values:
      assignFields(a, tzMeta)
      assignFields(a.inferred, tzMeta)
    }

    const mm = skipSiblingInference
      ? extractMakeAndModel(a)
      : await inferMakeAndModel(f, a)

    // Only add "Make" and "Model" to original/inferred if it's not a simple
    // make() and model() cleanup:
    if (mm.Make !== make(a.Make)) {
      a.original.Make = a.Make
      a.inferred.Make = mm.Make
    }
    if (mm.Model !== model(mm.Make, a.Model)) {
      a.original.Model = a.Model
      a.inferred.Model = mm.Model
    }

    if (
      !blank(Settings.defaultCopyright.valueOrDefault) &&
      isExifUnset(a.Copyright)
    ) {
      a.Copyright = Settings.defaultCopyright.valueOrDefault
    }

    const lensMakeModel = extractLensMakeModel(a)

    // CAPTURED-AT MUST BE AFTER TZ INFERENCE!
    const capturedAt = await extractCapturedAt(f, a, skipSiblingInference)
    if (capturedAt == null) {
      logger().info("No capturedAt for " + f)
      return
    }
    if (!skipSiblingInference && capturedAt.isInferred) {
      a.inferred.capturedAt = capturedAt
    }
    const exposureSettings = extractExposureSettings(a)

    const sizeInfo = await extractSizeInfoFromFile(f, a)
    if (sizeInfo == null) {
      logger().info("No size info for " + nativePath)
      return
    }

    const obj = {
      ...mm,
      mimetype: m,
      capturedAt,
      exposureSettings,
      ...extractTitleDescription(a),
      ...lensMakeModel,
      cameraId: cameraIdFromTags(a),
      imageId: imageIdFromTags(a),
      lensId: lensIdFromLensInfo({
        ...lensMakeModel,
        ...pick(a, "LensSerialNumber")
      }),
      ...sizeInfo,
      duration: extractDurationSec(a),
      tz: a.tz,
      rating: extractRating(a)
    }

    if (isTest) {
      ;(obj as any).__parsedBy__ = process.pid
    }
    const result = {
      ...a,
      ...obj // < needs to be after ...a to override ImageHeight and ImageWidth
    }
    logger().debug("parsedTags", {
      nativePath,
      skipInference: skipSiblingInference,
      ...obj,
      inferred: a.inferred,
      original: a.original,
      pickedResult: pick(
        result,
        "capturedAt",
        "tz",
        "tzSource",
        "Make",
        "Model"
      )
    })
    return definedValues(result) as ExifTags
  } catch (error) {
    logger().warn("parseTags() failed", { error })
    return
  }
}
