import { dirname } from "path"
import { clamp } from "../../fe/Number"
import { MiB } from "../../fe/Units"
import { toS } from "../../fe/toS"
import { mkLogger } from "../Logger"
import { isDbJanitorService } from "../ServiceNames"
import { uid } from "../UID"
import { mkdirpSync_, posixPathFromGrandparent, resolve } from "../fs/Path"
import { nativePathSizeSync } from "../fs/StatSync"
import { defaultLogLevel } from "../log/LogFilter"
import { Settings } from "../settings/Settings"
import { maxCpus } from "../work/MaxCpus"
import { DatabaseWithUid } from "./DatabaseWithUid"
import bs = require("better-sqlite3")

const DbCacheScalar = 1.5

// don't cache this
export function mkdb_({
  nativePath,
  timeoutMs = Settings.dbBusyTimeoutMs.valueOrDefault,
  logSql = Settings.logSql.valueOrDefault
}: {
  nativePath: string
  timeoutMs?: any
  logSql?: boolean
}): DatabaseWithUid {
  nativePath = resolve(nativePath)
  mkdirpSync_(dirname(nativePath))

  let verbose: any
  // The better-sqlite logger truncates variables very oddly, so we log in
  // DbRequest instead:
  const partialPath = posixPathFromGrandparent(nativePath)

  if (logSql) {
    const l = mkLogger("SQLite(" + partialPath + ")")
    const ll = defaultLogLevel()
    verbose = (sql: string) => l.log(ll, sql.replace(/\s{2,}/g, " "))
  }

  const dbFileSize = nativePathSizeSync(nativePath)

  const logger = mkLogger("db.mkdb(" + partialPath + ")")

  if (dbFileSize != null) {
    // nuc library (is big!) and is 179M
    const dbFileSizeMb = Math.round(dbFileSize / MiB)
    const minDbCacheSizeMb = Math.ceil(DbCacheScalar * dbFileSizeMb)
    if (minDbCacheSizeMb > Settings.dbCacheSizeMb.valueOrDefault) {
      logger.info(
        "Dynamically setting dbCacheSize to " + Settings.dbCacheSizeMb.value,
        { db: nativePath, dbFileSizeMb }
      )
      Settings.dbCacheSizeMb.envValue = minDbCacheSizeMb
    }

    const minRamMb = Math.round(dbFileSizeMb * DbCacheScalar)

    if (minRamMb > Settings.maxMemoryMb.valueOrDefault) {
      logger.warn("Large database: setting maxMemoryDb to " + minRamMb + "MB", {
        dbFileSizeMb,
        "Settings.maxMemoryMb": Settings.maxMemoryMb.valueOrDefault
      })
      Settings.maxMemoryMb.envValue = minRamMb
    }
  }

  logger.info("Opening " + nativePath + "...")
  const db = new bs(nativePath, {
    fileMustExist: false,
    readonly: false,
    timeout: timeoutMs,
    verbose: 2 < 1 ? ea => console.log("SQLITE: ", ea) : verbose
  })
  logger.info("Open. Setting PRAGMAs...")

  for (const pragma of [
    // List of PRAGMAs: https://sqlite.org/pragma.html#toc
    // https://wiki.mozilla.org/Performance/Avoid_SQLite_In_Your_Next_Firefox_Feature

    'encoding = "UTF-8"',

    // https://sqlite.org/pragma.html#pragma_threads

    // This limit sets an upper bound on the number of auxiliary threads that a
    // prepared statement is allowed to launch to assist with a query.
    "threads = " + maxCpus(),

    // Doesn't default to ON. WTH.
    "foreign_keys = ON",

    // https://sqlite.org/pragma.html#pragma_page_size

    // page_size defaults to 4k, and seems to work fine.
    "page_size = " + pageSizeBytes(),

    // https://sqlite.org/pragma.html#pragma_trusted_schema

    // There are advantages to turning it off, and most applications will be
    // unaffected if it is turned off. For that reason, all applications are
    // encouraged to switch this setting off on every database connection as soon
    // as that connection is opened.
    "trusted_schema = 0",

    // https://sqlite.org/pragma.html#pragma_cache_size

    // If the argument N is negative, then the number of cache pages is adjusted
    // to be a number of pages that would use approximately abs(N*1024) bytes of
    // memory based on the current page size.

    "cache_size = -" +
      Math.round((Settings.dbCacheSizeMb.valueOrDefault * MiB) / 1024),

    // setting this to EXCLUSIVE prevents concurrent read /or/ write access.
    "locking_mode = NORMAL",

    // to improve performance and support concurrent writers:
    "journal_mode = WAL",

    // This PRAGMA may hang if the DB is locked or stored on a remote
    // filesystem:
    "busy_timeout = " + Settings.dbBusyTimeoutMs.valueOrDefault,

    // https://sqlite.org/pragma.html#pragma_synchronous

    // Windows (the worst for File IO) drops latency from ~17ms to ~5ms for
    // upserts if `synchronous = OFF`, but that doesn't end up being substantive
    // to processing rate, and OFF means library DBs can be corrupt from power
    // cuts, so let's just use NORMAL and be safe (and a bit slower)

    // With no dbrpc, synchronous = NORMAL made updates sometimes not "stick",
    // causing Heartbeat to not get updated.

    // With FULL, inserts on linux are 10-20x slower.
    "synchronous = " + Settings.dbSynchronousMode.valueOrDefault,

    // DON'T SET THIS ON! Tags are case-insensitive!
    // This is required for tag path LIKE clauses:
    // (see https://stackoverflow.com/a/8586390/1268016)
    "case_sensitive_like = 0",

    // https://sqlite.org/pragma.html#pragma_wal_autocheckpoint
    "wal_autocheckpoint = " +
      (isDbJanitorService()
        ? toS(Settings.dbWalAutoCheckpoint.valueOrDefault)
        : "0"),

    // https://sqlite.org/pragma.html#pragma_auto_vacuum
    "auto_vacuum = " + Settings.dbAutoVacuumMode.valueOrDefault
  ]) {
    logger.tapThunk({
      msg: "PRAGMA " + pragma,
      result: () => db.pragma(pragma)
    })
  }
  // Used for prepared statement invalidation:
  const result: DatabaseWithUid = db as any
  result.__uid = uid()
  return result
}

export function pageSizeBytes(): number {
  const i = Settings.dbPageSizeBytes.valueOrDefault
  // Must be a power of 2:
  return Math.pow(2, clamp(1, 28, Math.round(Math.log2(i))))
}
