import { lazy } from "../../core/Lazy"
import { map } from "../../fe/Maybe"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { MaybeSyncOrAsync } from "../../fe/SyncOrAsync"
import { WrappedError } from "../error/WrappedError"
import { mkLogger } from "../Logger"
import { PosixFile } from "./PosixFile"

export interface CreatedAt {
  createdAt: number
}

export class JsonFileStore<T extends CreatedAt> {
  constructor(
    readonly file: PosixFile,
    readonly mkObject: () => MaybeSyncOrAsync<T>,
    readonly onWrite?: (pf: PosixFile, object: T) => any
  ) {}

  private readonly prior = lazy(() => this.file.readJson<T>("debug"))

  async read_(): PromiseMaybe<T> {
    return (
      (await this.prior()) ?? map(await this.mkObject(), t => this.write_(t))
    )
  }

  async write_(t: T): Promise<T> {
    try {
      await this.file.writeJson_(t, { spaces: 2 })
      this.prior.set(Promise.resolve(t))
      mkLogger("fs.JsonFileStore").info("wrote to " + this.file, t)
      if (this.onWrite != null) {
        await this.onWrite(this.file, t)
      }
      return t
    } catch (err) {
      throw new WrappedError("Failed to write to " + this.file, {
        cause: err,
        path: this.file.nativePath
      })
    }
  }
}
