import { isSuspended } from "../../core/IsSuspended"
import { Logger, mkLogger } from "../../core/Logger"
import { EndableRanks, ending } from "../../core/async/Endable"
import { EndableInterval } from "../../core/async/EndableInterval"
import { EndableWrapper } from "../../core/async/EndableWrapper"
import { filestamp } from "../../core/date/Filestamp"
import { dbBackupCold_ } from "../../core/db/DbBackupCold"
import { sqliteFiles } from "../../core/db/SQLiteFiles"
import { SqliteExt } from "../../core/db/Schemas"
import { isUpdateReadyToInstall } from "../../core/event/UpdateReadyToInstall"
import { FsLock, withLock_ } from "../../core/fs/FsLock"
import { PosixFile } from "../../core/fs/PosixFile"
import { Settings } from "../../core/settings/Settings"
import { minuteMs } from "../../fe/Date"
import { fmtDuration } from "../../fe/FmtDuration"
import { runMaintenanceTasks } from "../model/MaintenanceTasks"
import { Db } from "./Db"
import { libraryDbInfoFromDbFile, libraryDbObjects_ } from "./DbModelSetup"
import { dbSetupErrors, hasDbSetupErrors } from "./DbSetupErrors"

const endTimeoutMs = 5 * minuteMs

export const NoWriteLock = Symbol("NoWriteLock")
export const NoOp = Symbol("NoOp")
export const Contested = Symbol("Contested")

export class DbModelJanitor extends EndableInterval {
  readonly backupsDir = this.libraryDbFile.sibling("backups")
  readonly logger: Logger = mkLogger(this.name)
  readonly fslockRelease = new EndableWrapper(
    "db.DbModelJanitor.fslockRelease",
    () => this.dbFslock.release(),
    EndableRanks.postdb,
    endTimeoutMs
  )

  #lastUpdatedAt = 0

  constructor(
    readonly db: Db,
    readonly dbFslock: FsLock,
    readonly libraryDbFile: PosixFile = libraryDbObjects_().libraryDbFile,
    readonly retentionCount: number = Settings.dbBackupRetentionCount
      .valueOrDefault
  ) {
    super({
      name: "db.DbModelJanitor",
      callback: () => this.#onInterval(),
      intervalMs: Settings.dbBackupIntervalMs.valueOrDefault,
      initialDelayMs: minuteMs,
      rank: EndableRanks.db,
      endTimeoutMs: 5 * minuteMs, // 200MB to copy, 1MB/s, 3 minutes
      onEnd: () => this.#teardown()
    })
    void this.dbFslock.tryAcquire_()
  }

  async vacuumBackups() {
    const dbFiles =
      (await this.backupsDir.childFiles(ea => ea.ext === SqliteExt)) ?? []
    const victims = dbFiles.slice(0, -this.retentionCount)
    for (const victim of victims) {
      this.logger.info("backup(): removing stale backup " + victim)
      for (const ea of sqliteFiles(victim)) {
        await ea.unlink()
      }
    }
  }

  get useReplica() {
    return !this.db.dbFile.eql(this.libraryDbFile)
  }

  async #onInterval() {
    try {
      await this.backup_()
    } catch (error) {
      this.logger.error("backup_() failed", { error })
    }
  }

  async #teardown() {
    const teardownLock = FsLock.for({
      file: this.libraryDbFile.sibling("db-teardown"),
      timeoutMs: endTimeoutMs,
      endableRank: EndableRanks.postdb,
      noopIfContested: false // we want to wait for acquisition
    })

    await teardownLock.withLock_(async () => {
      // Last one out needs to turn off the lights:
      if (!(await this.dbFslock.iAmOnly_({ vacuum: true }))) {
        this.logger.info(
          "#teardown(): no-op: other processes are still running"
        )
        return
      }
      this.logger.info(
        "#teardown(): I'm the last one in. Running backup and cleanup."
      )

      await this.backup_()

      // Only run replica cleanup if backup_() is successful!
      if (this.useReplica) {
        const dbInfoJson = libraryDbInfoFromDbFile(this.libraryDbFile)
        const victims = [dbInfoJson, ...sqliteFiles(this.db.dbFile)]
        this.logger.info("#teardown(): Removing replica artifacts", { victims })
        for (const ea of victims) {
          await ea.unlink()
        }
      }
    })

    this.db.closeDb()
  }

  /**
   * Are we under a deadline here?
   */
  #needToHurry() {
    return this.ended || ending() || isSuspended() || isUpdateReadyToInstall()
  }

  forceBackup_(): Promise<
    typeof NoOp | typeof NoWriteLock | typeof Contested | PosixFile
  > {
    return this.backup_(true)
  }

  async backup_(
    force: boolean = false
  ): Promise<typeof NoOp | typeof NoWriteLock | typeof Contested | PosixFile> {
    return (
      (await withLock_(
        {
          file: this.libraryDbFile.sibling("backup"),
          timeoutMs: 5 * minuteMs,
          noopIfContested: !force
        },
        async () => {
          // don't skip backup if maxUpdatedAt hasn't changed since last backup:
          if (force) this.#lastUpdatedAt = 0
          if (hasDbSetupErrors()) {
            this.logger.warn(
              "backup_(): db has setup errors, skipping backup",
              {
                errors: dbSetupErrors()
              }
            )
            return NoOp
          }
          if (!(await this.dbFslock.tryAcquire_())) {
            this.logger.info("backup_(): no-op: someone else has the fslock", {
              me: this.dbFslock.lockfile?.base,
              owner: (await this.dbFslock.selfAndSiblingNames_())[0]
            })
            return NoWriteLock
          }

          if (!this.#needToHurry()) {
            await runMaintenanceTasks()
          }
          const newMaxUpdatedAt = this.db.maxUpdatedAt()
          if (
            newMaxUpdatedAt != null &&
            newMaxUpdatedAt > this.#lastUpdatedAt
          ) {
            if (!this.#needToHurry()) {
              await this.db.vacuum()
            } else {
              this.logger.info("backup_(): posthaste: skipping vacuum")
            }
            this.#lastUpdatedAt = newMaxUpdatedAt
            const destDir = this.backupsDir.join(filestamp())
            const destDbFile = await this.db.backup_(destDir, !this.useReplica)
            if (this.useReplica) {
              await dbBackupCold_(destDbFile, this.libraryDbFile.parent())
            }
            this.logger.info("backup_(): backup successful", { destDbFile })
            return destDbFile
          } else {
            this.logger.info(
              "backup_(): no update to the db since last backup",
              {
                ago: fmtDuration(Date.now() - this.#lastUpdatedAt)
              }
            )
            return NoOp
          }
        }
      )) ?? Contested
    )
  }
}
