import { get } from "http"
import { lazy } from "../../core/Lazy"
import { Maybe } from "../../fe/MaybeTypes"
import { mkLogger } from "../Logger"
import { Deferred } from "../async/Deferred"
import { URI } from "../uri/URI"
import { HttpStatusIs } from "../web/HttpStatus"

const logger = lazy(() => mkLogger("net.httping"))
/**
 * @param url must be http, not https.
 * @return the status code of the request. If the site is not reachable, `-1`
 * will be returned
 */
export function httping(
  host: string,
  port: number,
  path: string,
  timeoutMs: number
): Promise<Maybe<number>> {
  const url = URI.from({
    scheme: "http",
    authority: host + ":" + port,
    path
  }).toString()
  const d = new Deferred<Maybe<number>>("ping " + url)
  void d.setTimeout(timeoutMs)
  get(url, { timeout: timeoutMs }, resp => {
    logger().log(
      HttpStatusIs.error(resp.statusCode) ? "warn" : "debug",
      "ping",
      {
        latencyMs: Date.now() - d.startedAt,
        statusCode: resp.statusCode
      }
    )
    // See https://nodejs.org/api/http.html#http_http_get_options_callback
    resp.resume()
    void d.resolve(resp.statusCode)
  }).on("error", e => {
    logger().warn("Failed to connect to " + url, e)
    void d.resolve(undefined)
  })
  return d.promise
}
