import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { stripPrefix } from "../../fe/String"
import { camel2words } from "../StringCase"

export function negateFilterName(s: Maybe<string>) {
  if (blank(s)) return undefined
  // If we have any spaces in the string, assume it's already human-friendly.
  const w = /\s/.test(s)
    ? s
    : camel2words(s).replace(/\bphoto structure\b/gi, "PhotoStructure")
  return w.match(/^not\b/)
    ? w.replace(/^not\b/, "").trim()
    : "not " + stripPrefix(w, "is ")
}
