import { lazy } from "../core/Lazy"
import { isProd } from "../core/NodeEnv"
import { isEnvTrue } from "../core/env/GetEnv"
import { isPacked } from "../core/platform/IsPacked"
import { Settings } from "../core/settings/Settings"
import { later } from "../fe/Delay"

export const sentryEnabled = lazy(
  () =>
    isEnvTrue("ENABLE_SENTRY" as any) || // < allow for manual testing
    (isProd && isPacked() && Settings.reportErrors.valueOrDefault)
)

later(() => {
  Settings.reportErrors.watchLater(() => sentryEnabled.clear())
})
