const clearCaches = () => {
  excludeGlobs.unset()
  excludeBundles.unset()
}

const clearNotExcludedDirs = () => {
  notExcludedDirs.unset()
}

const watchSettings = lazy(() => {
  Settings.excludeGlobsOmitDefaults.watchLater(clearCaches)
  Settings.excludeGlobsAdd.watchLater(clearCaches)
  Settings.excludeGlobsOmit.watchLater(clearCaches)
  Settings.globsCaseInsensitive.watchLater(clearCaches)

  Settings.libraryDir.watchLater(clearNotExcludedDirs)
  Settings.originalsDir.watchLater(clearNotExcludedDirs)
  Settings.scanPaths.watchLater(clearNotExcludedDirs)
  Settings.argvScanPaths.watchLater(clearNotExcludedDirs)
})

// exposed for tests
export const defaultDirPredicates = lazy<PredicateBundle<SimpleFile>>(() => [
  {
    notSymlinkLoop,
    notInHiddenPhotoStructureDir,
    notNoMedia,
    notHidden
    // don't hide "ignorable" mountpoints!
  }
])

// exposed for tests
export const defaultFilePredicates = lazy(() => [
  { notInHiddenPhotoStructureDir }
])

export const excludeGlobs = lazy(() => {
  watchSettings()

  const result = new CaseInsensitiveMap<Glob>()
  function addGlob(glob: Glob) {
    if (glob != null && glob.valid) {
      const k = glob.pattern.trim()
      if (result.has(k)) {
        logger().warn("Duplicate glob:", { glob })
      }
      result.set(k, glob)
    }
  }

  function omitGlob(glob: string) {
    const k = glob.trim()
    if (!result.has(k)) {
      logger().warn(
        "Failed to find and remove glob specified in PS_EXCLUDE_GLOBS_OMIT",
        { glob }
      )
    } else {
      result.delete(k)
    }
  }

  if (!Settings.excludeGlobsOmitDefaults.valueOrDefault) {
    for (const ea of defaultExcludeGlobs()) {
      addGlob(ea)
    }
  }
  for (const glob of Settings.excludeGlobsOmit.values) {
    omitGlob(glob)
  }
  for (const ea of Settings.excludeGlobsAdd.values) {
    addGlob(new Glob(ea, "(from PS_EXCLUDED_GLOBS_ADD)"))
  }
  const arr = sortBy(result.values(), ea =>
    compactBlanks(ea.pattern.toLowerCase().split("/"))
  )
  const [dir, file] = partition(arr, ea => ea.pattern.endsWith("/"))

  return { dir, file }
})

export const excludeBundles = lazy(() => {
  const g = excludeGlobs()
  return {
    dir: [
      ...defaultDirPredicates(),
      ...g.dir.map(ea => ea.bundle)
    ] as PredicateBundle<SimpleFileOrString>,
    file: [
      ...defaultFilePredicates(),
      ...g.file.map(ea => ea.bundle)
    ] as SyncPredicateBundle<SimpleFileOrString>
  }
})

const ifDisabled: SyncPredicateBundle<SimpleFileOrString> = [
  { disableAllFilters: () => true }
]
const ifBlank: SyncPredicateBundle<SimpleFileOrString> = [
  { notBlank: () => false }
]

export function notExcludedFilePredicates(
  file: SimpleFileOrString
): SyncPredicateBundle<SimpleFileOrString> {
  return Settings.disableAllFilters.valueOrDefault
    ? ifDisabled
    : blank(file)
    ? ifBlank
    : excludeBundles().file
}

export function whyExcludedFile(f: SimpleFileOrString): Maybe<string> {
  return SyncPredicates.whyRejected(f, ...notExcludedFilePredicates(f))
}

export function isExcludedFile(f: SimpleFileOrString): boolean {
  return null != whyExcludedFile(f)
}

export function notExcludedDirPredicates(
  dir: SimpleFileOrString
): PredicateBundle<SimpleFileOrString> {
  return Settings.disableAllFilters.valueOrDefault
    ? ifDisabled
    : blank(dir)
    ? ifBlank
    : excludeBundles().dir
}

export async function whyExcludedDirectory(
  dir: SimpleFileOrString
): PromiseMaybe<string> {
  return Predicates.whyRejected(dir, ...notExcludedDirPredicates(dir))
}

export async function isExcludedDirectory(
  f: SimpleFileOrString
): Promise<boolean> {
  return null != (await whyExcludedDirectory(f))
}

export const notExcludedDirs = lazy<Promise<string[]>>(async () => {
  // yes we're uniq'ing twice here but it means we can hit actualPath in
  // parallel and not waste time.
  const arr = uniq(
    compactBlanks([
      libraryDirPosixFile(),
      libraryOriginalsDirPosixFile(),
      Settings.argvScanPaths.values,
      Settings.scanPaths.values
    ])
  )
  // Include both the resolved() path and the actualPath() for this list, just
  // to make sure our bases are covered.
  return logger().tap({
    msg: "notExcludedDirs() (these directories will never be excluded)",
    level: "info",
    result: uniq([
      ...arr,
      ...(await Promise.all(arr.map(ea => actualPath(ea))))
    ])
  })
})

export async function whyExcludedDirectoryRecursive(
  dir: SimpleFile
): PromiseMaybe<string> {
  // We don't want to exclude the library directory or scanned paths for any
  // reason--they might put it in some directory that we normally exclude, and
  // we don't want to just prune out those patterns, as they should normally
  // apply, so if we're in the library dir (or scanned directory), don't
  // descend past that.
  const doNotPassDirs = await notExcludedDirs()

  for (
    let ea: Maybe<SimpleFile> = dir;
    ea != null && !ea.isRoot;
    ea = await ea?.parent()
  ) {
    if (doNotPassDirs.includes(ea?.nativePath)) {
      // Return now: don't check the directory, nor check ancestor directories.
      return
    }
    const why = await whyExcludedDirectory(ea)
    if (why != null) {
      return why
    }
  }
  return
}

function notSymlinkLoop(dir: SimpleFileOrString): boolean {
  return !seemsLikeSymlinkLoop(dir)
}

function notNoMedia(dir: SimpleFileOrString) {
  return thenNot(hasNoMedia(dir))
}

function notHidden(dir: SimpleFileOrString) {
  return thenNot(isHidden(dir))
}

export function notInHiddenPhotoStructureDir(dir: SimpleFileOrString) {
  return !inHiddenPhotoStructureDir(dir)
}
