const rootNamesLowerCase = uniq(
  [
    ...TagRoots.values,
    ...Settings.rootTagKeywordsAliases.values,
    ...Settings.rootTagWhoAliases.values
  ].map(ea => ea.toLowerCase())
)

export function hasTagRoot(path: TagPath): boolean {
  return rootNamesLowerCase.includes(tagRefToS(path[0]).toLowerCase())
}

export function stripTagRoot(path: TagPath): TagPath {
  return hasTagRoot(path) ? path.slice(1) : path
}

const albumRootsLowerCased = uniq(
  [TagRoots.Albums, ...Settings.rootTagAlbumsAliases.values].map(ea =>
    ea.toLowerCase()
  )
)

const kwRootsLowerCased = uniq(
  [TagRoots.Keywords, ...Settings.rootTagKeywordsAliases.values].map(ea =>
    ea.toLowerCase()
  )
)

const whoRootsLowerCased = uniq(
  [TagRoots.Who, ...Settings.rootTagWhoAliases.values].map(ea =>
    ea.toLowerCase()
  )
)

export function normalizeTagRoot(path: TagPath): TagPath {
  const root = tagRefToS(path[0]).toLowerCase()
  if (blank(root)) return path
  if (kwRootsLowerCased.includes(root)) return [KwRoot, ...path.slice(1)]
  if (kwRootsLowerCased.includes(root)) return [KwRoot, ...path.slice(1)]
  if (whoRootsLowerCased.includes(root)) return [WhoRoot, ...path.slice(1)]
  if (albumRootsLowerCased.includes(root)) return [AlbumsRoot, ...path.slice(1)]

  return path
}

/**
 * Determine which TagPaths need to be added and removed to make `before` become
 * `after`.
 */
export async function tagDeltas(
  before: Maybe<Maybe<TagPath>[]>,
  after: Maybe<Maybe<TagPath>[]>
): Promise<{ add: TagPath[]; remove: TagPath[] }> {
  const add = tagDiff(after, before)
  const remove = tagDiff(before, after)

  const excludedRootTags = Settings.excludedRootTags.valueOrDefault

  filterInPlace(add, tagPath => {
    const bad = excludedRootTags.includes(tagRefToS(tagPath[0]).toLowerCase())
    if (bad && !tagPathsInclude(tagPath, remove)) {
      remove.push(tagPath)
      console.log("removing bad tag", tagPath)
    }
    return !bad
  })

  return {
    add,
    remove
  }
}
