import { Maybe } from "../../fe/MaybeTypes"
import { toS } from "../../fe/toS"
import { extractFloat } from "../Number"

export function extractBitrateKbps(tags: {
  AvgBitrate?: string
  MaxDataRate?: string
}): Maybe<number> {
  for (const ea of ["AvgBitrate", "MaxDataRate"] as const) {
    const result = parseBitrateKbps(tags[ea])
    if (result != null) return result
  }
  return
}

// MB/s or Kbps

const units = [
  { pat: "mb/s", fac: 1024 },
  { pat: "mbps", fac: 1024 },
  { pat: "kb/s", fac: 1 },
  { pat: "kbps", fac: 1 }
] as const

function parseBitrateKbps(s: Maybe<string>): Maybe<number> {
  const f = extractFloat(s)
  if (f == null) return
  const lc = toS(s).toLowerCase()
  for (const ea of units) {
    if (lc.includes(ea.pat)) return f * ea.fac
  }
  return
}
