import { lazy } from "../../core/Lazy"
import { mkLogger } from "../Logger"
import { orRegExpPatterns } from "../RegExp"
import { errorToS } from "../error/Error"
import { Settings } from "../settings/Settings"

const logger = lazy(() => mkLogger("img.ValidationError"))

export function isIgnorableValidationError(err: any) {
  const s = errorToS(err)
  return logger().tap({
    msg: "isIgnorableValidationError",
    result:
      validationErrorAllowlist().test(s) || !validationErrorBlocklist().test(s),
    meta: { err }
  })
}
const validationErrorBlocklist = lazy(() =>
  orRegExpPatterns(Settings.validationErrorBlocklist.values, "i")
)
const validationErrorAllowlist = lazy(() =>
  orRegExpPatterns(Settings.validationErrorAllowlist.values, "i")
)
