import { lerp2d } from "../../core/math/Lerp"
import { minuteMs, secondMs } from "../../fe/Date"
import { orElse } from "../../fe/Maybe"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { clamp } from "../../fe/Number"
import { KB, MB } from "../../fe/Units"
import { mapGt0 } from "../Number"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { thenMap } from "../async/Promise"
import { PosixFile } from "../fs/PosixFile"
import { readRawTags } from "../tags/ExifTool"
import { isRawImageExt, isVideoExt } from "../tags/FileTypes"
import { MinIoRate, commandTimeoutMs } from "../volumes/VolumeTtls"

// Handle general system churn/hiccups/external drive spinups:
export const MinSyncFileTimeoutMs = minuteMs

// This number needs to be greater than the slowest computer's file processing
// time under load.

// TODO: LATER: make timeouts more accurate by using the current system's
// processing times.

export const BytesPerSecond = KB

// Sync file time is going to be governed by

// 1. DB insert time (normally less than a second, let's say 5 seconds)
// 2. Metadata extraction (should be less than a second, worst-case, 30 seconds)
// 3. Copy to library (assume 4-5mb/sec transfer speed for a slow nas, should
//    typically be more like 20-50mb/s, though) + SHA, == MaxFileSize / 4mb
// 4. Thumbnail generation (normally less than a second, can be 30 seconds for
//    RAW images)
// 5. Video transcoding (may a no-op, or only be remuxing, which is fast, or
//    actual transcoding, which is ~1-50x longer than the actual duration of the
//    video, and can be limited by disk I/O as well).

// linux timings:
//           ubu mac win
//  7M .MTS:     30s
// 18M .MTS: 23s 60s
// 8M  .AVI: 4s
// 30M .MOV: 18s 40s

// HEVC is ~2MB/second for 1080p/30, and 12Mb/s for 4k/60, but transcode time
// varies _widely_, and ffmpeg doesn't seem to hang, so we'll just use the
// file size as a proxy for transcode time.

// 5,931 ms for speedy to convert 56,637,237 bytes, so roughly 10mb/s, or divide bytes by 10,000 to get millis.

// I suspect RPi to be 6-10x slower, so 1mb/s: divide bytes by 1000.

// These timings are all from the Raspberry Pi 4, the slowest supported CPU.

const ImagePoints = {
  p0: { x: 0.8 * MB, y: 4 * secondMs },
  p1: { x: 20 * MB, y: 12 * secondMs }
}

const RawPoints = {
  p0: { x: 11 * MB, y: 12 * secondMs },
  p1: { x: 26 * MB, y: 24 * secondMs }
}

export function syncFileTimeoutForFileMs(file: PosixFile) {
  return thenMap(syncFileTimeoutForFile(file), ea => ea.result)
}

export function syncFileTimeoutForFile(file: PosixFile) {
  return thenMap(readMeta(file), syncFileTimeout)
}

function readMeta(file: PosixFile): PromiseMaybe<FileTimeoutMeta> {
  return thenMap(file.size(), async bytes => ({
    bytes,
    ext: file.ext,
    durationMs: isVideoExt(file.ext)
      ? await thenMap(readRawTags(file), t =>
          mapGt0(t.Duration, d => d * secondMs)
        )
      : undefined
  }))
}

export interface FileTimeoutMeta {
  bytes: number
  ext: string
  durationMs?: number
}

export function dcrawTimeout(bytes: Maybe<number>): number {
  return lerp2d(
    clamp(11 * MB, 100 * MB, orElse(bytes, 0)),
    RawPoints.p0,
    RawPoints.p1
  )
}

export function syncFileTimeout(meta: FileTimeoutMeta) {
  if (isVideoExt(meta.ext)) return { result: 0 }

  // Time to sync is DB + tags + copy op + sha + thumbs + transcode.

  // db lookup time. Should be ~10ms, but the system might be borked:
  const dbMs = ShortCommandTimeoutMs

  // tag extraction time. Should be normally ~5-50ms, but we may need to fetch
  // tags for sibling variants (5? 10?) over a slow LAN NAS.
  const tagMs = commandTimeoutMs()

  // Copy time will be directly proportional to the asset size. Assume 2mb/s for
  // a really crappy NAS and a 100baseT network, and that we need to do it twice
  // (once to copy, once to SHA-verify the copy):

  const copyMs = meta.bytes / MinIoRate

  const lerpPoints = (p: typeof ImagePoints) =>
    lerp2d(clamp(0.5 * MB, 50 * MB, meta.bytes), p.p0, p.p1)

  const thumbMs = lerpPoints(ImagePoints)

  const rawDecodeMs = isRawImageExt(meta.ext) ? lerpPoints(RawPoints) : 0

  const result = Math.max(
    MinSyncFileTimeoutMs,
    dbMs + tagMs + copyMs + thumbMs + rawDecodeMs
  )

  return {
    result,
    dbMs,
    tagMs,
    copyMs,
    thumbMs,
    rawDecodeMs
  }
}
