import _path from "path"
import { lazy } from "../../core/Lazy"
import { compactBlanks, uniq } from "../../fe/Array"
import { toNotBlank } from "../../fe/Blank"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { gte } from "../../fe/Number"
import { ensureSuffix } from "../../fe/String"
import { KiB } from "../../fe/Units"
import { toA } from "../../fe/toA"
import { toS } from "../../fe/toS"
import { mkLogger } from "../Logger"
import { env } from "../env/Env"
import { getEnv } from "../env/GetEnv"
import { isLinux, isWin } from "../platform/Platform"
import { isDocker } from "../platform/isDocker"
import { isStatRX } from "./Access"
import { actualPath } from "./ActualPath"
import { BaseFile } from "./BaseFile"
import { statMaybe } from "./Stat"

const logger = lazy(() => mkLogger("fs.PathTo"))

// This file is instead of depending on the "which" npm package, which doesn't
// have fallbacks, nor min filesize, nor access validation

// dcraw_emu.exe is the smallest binary, and it's 26K.
const MinBinarySize = KiB

export async function isRXFile(
  nativePath: string,
  minSize = MinBinarySize
): Promise<boolean> {
  const stat = await statMaybe(nativePath, "trace")
  return (
    stat != null &&
    stat.isFile() &&
    gte(stat.size, minSize) &&
    isStatRX(stat, nativePath)
  )
}

function fallbackPathDirectories() {
  if (isWin) {
    const systemRoot = toNotBlank(getEnv("SystemRoot")) ?? "C:\\Windows"
    const systemDrive = toNotBlank(getEnv("SystemDrive")) ?? "C:"
    return [
      systemRoot,
      _path.join(systemRoot, "System32"),
      _path.join(systemRoot, "System32", "Wbem"), // < wmic is hiding in Wbem
      _path.join(systemDrive, "cygwin64", "bin")
    ]
  } else {
    return [
      ...(isDocker()
        ? ["/opt/photostructure/bin", "/opt/photostructure/tools/bin"]
        : []),
      "/usr/local/bin",
      "/usr/bin",
      "/bin",
      "/usr/sbin",
      "/sbin"
    ]
  }
}

function pathDirectories() {
  const paths = toS(env().PATH).split(_path.delimiter)
  return uniq(compactBlanks([...paths, ...fallbackPathDirectories()]))
}

export async function pathTo({
  tool,
  alternativePath,
  paths
}: {
  tool: string
  alternativePath?: string
  paths?: string[]
}): PromiseMaybe<string> {
  const dirs = uniq(compactBlanks([...pathDirectories(), ...toA(paths)]))
  for (const ea of uniq(compactBlanks([tool, alternativePath]))) {
    const base = isWin ? ensureSuffix(ea, ".exe") : ea

    for (const dir of dirs) {
      const nativePath = _path.join(dir, base)
      if (await isRXFile(nativePath)) {
        // only run actualPath for case-insensitive OSes:
        return isLinux ? nativePath : actualPath(nativePath)
      }
    }
  }
  logger().warn("Failed to find tool", { tool, dirs })
  return
}

export async function pathIfExists(f: Maybe<BaseFile>) {
  if (f == null) return
  const s = await f.stat({ logLevel: "debug" })
  return s != null &&
    s.isFile() &&
    s.size > MinBinarySize &&
    isStatRX(s, f.nativePath)
    ? f.nativePath
    : undefined
}

export const wmic = () => "wmic"
export const fsutil = () => "fsutil"
export const nslookupWin = () => "nslookup"
export const pingWin = () => "ping"
export const arpWin = () => "arp"

