const logger = lazy(() => mkLogger("Sentry"))

const MaxBreadcrumbs = 100

export async function installSentry(service: Service) {
  try {
    if (!sentryEnabled()) return false
    Sentry.init({
      // See https://sentry.io/settings/photostructure-inc/projects/photostructure-for-desktop/keys/

      dsn: isElectron
        ? "https://0652cdd351054fe9853ff944b1f54e2c@o130922.ingest.sentry.io/289071"
        : "https://5f52aefec1b3439eaed2e77b2d1210a8@o130922.ingest.sentry.io/1828379",
      shutdownTimeout: serviceExitTimeoutMs(service.name),
      release: release, // Must match webpack and bin/release
      environment: nodeEnv,
      maxBreadcrumbs: MaxBreadcrumbs,
      integrations: [], // we do everything
      beforeSend: eventFilter().beforeSend,
      onFatalError: (error: Error) => onError("sentry.onFatalError", error)
    })
    logger().info("Sentry initialized", { isElectron })
    return true
  } catch (error) {
    logger().warn("Failed to set up sentry", { error })
    return false
  }
}

export function sendToSentry(error: Error) {
  if (sentryEnabled() && error != null && true !== isDoNotSendError(error)) {
    Sentry.captureException(error)
  }
}

export const eventFilter = lazy(() => new EventFilter())

export class EventFilter {
  constructor() {
    ee().on("fatal", sendToSentry)
    ee().on("nonFatal", sendToSentry)
    new EndableWrapper("EventFilter", () => this.end(), EndableRanks.first)
  }

  private end() {
    return map(Sentry.getCurrentHub().getClient(), ea => ea.close(5 * secondMs))
  }

  readonly beforeSend = async (
    event: Event,
    hint?: EventHint
  ): Promise<Event | null> => {
    // < this must satisfy the beforeSend API
    if (!sentryEnabled()) {
      logger().warn("Sentry.beforeSend(): not sending event", event)
      return null
    }

    if (true === (await ErrorStore.instance()?.eventQuotaExceeded(event))) {
      logger().warn("Sentry.beforeSend(): event quota exceeded", event)
      return null
    }

    const message = extractMessage(event, hint)

    if (true === isDoNotSendError(message)) {
      logger().info("Sentry.beforeSend(): event not sendable. vetoing", {
        event,
        hint,
        msg: message
      })
      return null
    }
    if (blank(event.message)) {
      event.message = ellipsize(message, 256)
    }
    const e = await annotateEvent(event as any)
    return (await ErrorStore.instance()?.maybeSendEvent(e)) ?? null
  }
}

export function extractMessage(event: Event, hint?: EventHint): string {
  return uniq(
    compactBlankish([
      event.message,
      ...toA(sentryExceptionsToS(event.exception?.values)),
      errorToS(hint?.originalException)
    ])
  ).join(": ")
}

export function sentryExceptionsToS(
  e: Maybe<Sentry.Exception[]>
): Maybe<string[]> {
  return mapNotEmpty(e, arr => compactBlanks(arr.map(sentryExceptionToS)))
}

export function sentryExceptionToS(e: Maybe<Sentry.Exception>): Maybe<string> {
  return mapNotEmpty(
    compactBlanks(
      [e?.type, e?.value].filter(ea => toS(ea).toLowerCase() !== "error")
    ),
    arr => arr.join(": ")
  )
}

// NOTE: Calling annotateEvent twice on an Event must be idempotent
export async function annotateEvent(
  event: Event & { message: string }
): Promise<Event & StoredError> {
  const email = await getEmail()
  if (notBlank(email)) {
    if (event.user == null) event.user = {}
    event.user.email = email
  }

  if (isEmpty(event.breadcrumbs)) event.breadcrumbs = await mkBreadcrumbs()

  const extra = event.extra ?? {}
  extra.pid = process.pid
  extra.serviceName = serviceName()
  extra.serviceEnding = ending()
  extra.runtimeMs = runtimeMs()
  extra.version = version
  extra.os = osFullName()
  extra.isDocker = isDocker()
  extra.nodeVersion = process.versions.node
  extra.locale = await locale()
  extra.cpus = CPUs()
  extra.memoryUsageMb = memoryUsageMb()
  extra.memoryUsageRssMb = memoryUsageRssMb()
  extra.systemMemory = fmtBytes(os.freemem()) + " / " + fmtBytes(os.totalmem())
  extra.ffmpeg = await ffmpegVersionDescription()
  extra.argv = stringify(process.argv)

  event.extra = extra

  return {
    timestamp: Date.now() / secondMs,
    ...event
  }
}

export async function mkBreadcrumbs() {
  // wait for fs flush:
  await delay(DefaultLogFlushMs * 3)
  const entries = await allRecentLogEntries()
  return compact(entries.map(logEntryToBreadcrumb))
}

export function logEntryToBreadcrumb(le: LogEntry): Maybe<Breadcrumb> {
  return map(logLevelToSeverity(le.l), level => ({
    timestamp: le.ts / secondMs, // sentry is unixtime (seconds)
    level,
    category: le.from ?? processName(),
    message: stripAnsiEsc(le.ctx + ": " + le.msg),
    data:
      // SITS: why does Sentry require a data object?
      typeof le.meta === "object"
        ? le.meta
        : { value: isString(le.meta) ? stripAnsiEsc(le.meta) : le.meta }
  }))
}

const level2sev = new Map<LogLevel, SeverityLevel>([
  [LogLevels.fatal, "fatal"],
  [LogLevels.error, "error"],
  [LogLevels.warn, "warning"],
  [LogLevels.info, "info"],
  [LogLevels.debug, "debug"]
])

export function logLevelToSeverity(l: LogLevel): Maybe<SeverityLevel> {
  return level2sev.get(l)
}
