import { compactBlanks, includesAll } from "../../fe/Array"
import { Dimensions, dmegapixels } from "../../fe/Dimensions"
import { isMimeTypeSupportedByBrowser } from "../tags/MimeTypes"
import { ImageSize } from "./ImageSize"
import { OutputSize } from "./Reducers"

// For tag galleries, we always want the littlest sizes:
const FitAlways = [ImageSize.QVGA, ImageSize.QQVGA]

/**
 * @param dim is final, post-rotated dimension (the caller must flip if rotation
 * is 90 or 270)
 */
export function fitSizes(
  dim: Dimensions,
  mimetype: string
): [OutputSize, ImageSize][] {
  const sizes = ImageSize.fit()

  const results: [OutputSize, ImageSize][] = []
  let smallestDim = isMimeTypeSupportedByBrowser(mimetype) ? dim : undefined

  for (let idx = 0; idx < sizes.length; idx++) {
    const size = sizes[idx]
    const d = size.outputSize(dim)
    if (d == null) continue

    // The goal here is to generate the "correct" number of thumbnails: we
    // want to avoid too many different resolutions (which would waste disk
    // space), and at the same time, we want to be efficient with network
    // bandwidth (which would prefer lots of different resolutions).
    const keep =
      smallestDim == null ||
      FitAlways.includes(size) ||
      dmegapixels(smallestDim) / dmegapixels(d) > 3 ||
      dmegapixels(d) - dmegapixels(smallestDim) > 2.5

    if (keep) {
      smallestDim = d
      results.push([d, size])
    }
  }
  return results
}

function old2new(s: string) {
  // Unfortunately, prior v0.3.8 encoded wvga as qhd, and wvgap as qhdp.
  // As of v0.6.0, portrait sizes went away.
  if (s === "qhd") return "wvga"
  if (s === "uhd") return "uhd4k"
  return s
}

export function equivalentFitSizes(
  oldArr: string[],
  newArr: string[]
): boolean {
  return includesAll(
    compactBlanks(oldArr).map(old2new),
    compactBlanks(newArr).map(old2new)
  )
}
