import { lazy } from "../../core/Lazy"
import { time } from "../../core/async/PromiseTimer"
import { Settings } from "../../core/settings/Settings"
import { hourMs } from "../../fe/Date"
import { later } from "../../fe/Delay"
import { runTagMaintenance } from "../tag/TagSql"
import { normalizeDateTags } from "./DateTagNormalizer"
import { Progress } from "./Progress"

export async function runMaintenanceTasks({ force }: { force?: boolean } = {}) {
  await _normalizeDateTags() // < never needs to be done more than once
  await (force === true ? progressVacuum.refresh() : progressVacuum())
  await (force === true ? runTagMaintenance.force() : runTagMaintenance())
}

const _normalizeDateTags = lazy(() =>
  time("db.normalizeDateTags", normalizeDateTags)
)

const progressVacuum = lazy(
  () => time("db.Progress.vacuum", () => Progress.vacuum()),
  2 * hourMs
)

later(() => {
  Settings.libraryDir.watchLater(() => {
    _normalizeDateTags.unset()
    progressVacuum.unset()
  })
})
