import { lazy } from "../../core/Lazy"
import { map } from "../../fe/Maybe"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { toFloat } from "../../fe/Number"
import { toA } from "../../fe/toA"
import { geohashNumericShort } from "../GeoHash"
import { mkLogger } from "../Logger"
import { shim1 } from "../Shim1"
import { SortableAssetFile } from "../asset-file/AssetFileSorter"
import { DominantColor } from "../color/DominantColor"
import { PosixFile } from "../fs/PosixFile"
import { SimpleFileOrString } from "../fs/SimpleFile"
import { ImageHash, imageHash_ } from "../img/ImageHash"
import { max } from "../math/Vector"
import { Settings } from "../settings/Settings"
import { readTags } from "../tags/ExifTool"
import {
  AssetFileCapturedAtFields,
  AssetFileImageHashFields,
  AssetFileMetadata,
  AssetFileStatFields,
  AssetFileUriFields,
  IAssetFile
} from "./IAssetFile"

const logger = lazy(() => mkLogger("asset-file.AssetFileFactory"))

export async function assetFileUriFields_(
  f: PosixFile
): Promise<AssetFileUriFields> {
  try {
    return {
      nativePath: f.nativePath,
      uri: await f.uri_(),
      mountpoint: (await f.mountpoint())?.nativePath
    }
  } catch (error) {
    return logger().throw("Failed to build URI for " + f, { error })
  }
}

export async function assetFileStatFields_(
  f: PosixFile
): Promise<AssetFileStatFields> {
  const stat = await f.stat_()
  const arr = await f.sidecareStats()
  const mtime = max([stat.mtimeMs, ...toA(arr).map(ea => ea.mtimeMs)])!
  return {
    mtime,
    fileSize: stat.size
  }
}

export function imageHashToAssetFileImageHashFields(
  ih: ImageHash
): AssetFileImageHashFields & ImageHash {
  return {
    ...ih,
    ...DominantColor.toModeData(ih.dominantColors)
  }
}

export async function assetFileImageHashFields_(
  f: PosixFile
): PromiseMaybe<AssetFileImageHashFields & ImageHash> {
  if (!Settings.useImageHashes.valueOrDefault) {
    return
  }
  const ih = await imageHash_(f.nativePath)

  if (ih == null) {
    return logger().throw("Failed to extract image hash for " + f)
  }

  return imageHashToAssetFileImageHashFields(ih)
}

export const assetFileMetadata_ = shim1({
  name: "asset-file.assetFileMetadata",
  impl: _assetFileMetadata_
})

async function _assetFileMetadata_(
  nativePath: SimpleFileOrString
): Promise<AssetFileCapturedAtFields & AssetFileMetadata> {
  const f = PosixFile.for(nativePath)
  const t = await readTags(f)
  if (t == null) {
    return logger().throw("Failed to read tags for " + f)
  }

  const caFields = t.capturedAt?.toAssetFileFields()
  if (caFields == null) {
    return logger().throw("Failed to extract captured-at for " + f)
  }

  // image hash runs readTags, so we shouldn't do this in parallel.

  return {
    ...caFields,
    ...t.exposureSettings,
    ...t.dimensions,
    mimetype: t.mimetype,
    rotation: t.rotation,
    make: t.Make,
    model: t.Model,

    // store the rating so if we lose access to the file we still have the metadata:
    rating: t.rating,

    cameraId: t.cameraId,
    imageId: t.imageId,
    lensId: t.lensId,
    geohash: geohashNumericShort(t.GPSLatitude, t.GPSLongitude),

    durationMs: map(t.duration, ea => Math.round(ea * 1000)),
    fps: map(toFloat(t.VideoFrameRate), Math.round)
  }
}

export async function mkAssetFile_(f: PosixFile) {
  const [saf, ih] = await Promise.all([
    mkSortableAssetFile_(f),
    assetFileImageHashFields_(f)
  ])
  const result = {
    ...saf,
    ...ih
  } satisfies IAssetFile
  return result
}

export async function mkSortableAssetFile_(f: PosixFile) {
  const [a, b, c, sha] = await Promise.all([
    assetFileUriFields_(f),
    assetFileStatFields_(f),
    assetFileMetadata_(f),
    f.sha_()
  ])
  const result = {
    ...a,
    ...b,
    ...c,
    sha
  } satisfies SortableAssetFile
  return result
}
