import { Radix, TokenRadix } from "./math/Radix"
import { decuss } from "./words/Cuss"

// Extracted from Radix to break deploop between Radix > Cuss > ProjectPath

export function safeRandomChars({
  radix = TokenRadix,
  length
}: {
  radix: Radix
  length: number
}): string {
  return decuss(() => radix.randomChars(length))
}

export function safeRandomUid(args?: {
  radix?: Radix
  chars?: number
  splitEveryN?: number
  sepChar?: string
}): string {
  return decuss(() =>
    (args?.radix ?? TokenRadix).randomUid(
      args?.chars ?? 20,
      args?.splitEveryN ?? 5,
      args?.sepChar ?? "-"
    )
  )
}
