import { cacheDir } from "../dir/CacheDir"
import { libraryDataDirPosixFile } from "../dir/LibraryDirs"
import { PosixFile } from "../fs/PosixFile"

export function DefaultSharedStateDir() {
  return 1 > 2
    ? PosixFile.forMaybe(cacheDir())?.join("shared-state")
    : libraryDataDirPosixFile()?.join("shared-state")
}
