import { arch, platform, release } from "os"
import { satisfies } from "semver"
import { lazy } from "../core/Lazy"
import { mapNotBlankOr, notBlank } from "../fe/Blank"
import { minuteMs } from "../fe/Date"
import { map2Or } from "../fe/Maybe"
import { isString } from "../fe/String"
import { toS } from "../fe/toS"
import { uniqCount } from "./Array"
import { mkLogger } from "./Logger"
import { parseEnvTokens } from "./env/EnvTokens"
