import { isDocker } from "./platform/isDocker"
import { debianVersionToSemver } from "./version/SemVer"
import { cpuInfo } from "./work/CpuInfo"

const logger = lazy(() => mkLogger("os"))

export const osArch = lazy(() => `${osName()} on ${arch()}`)
export const osFullName = lazy(() => osArch() + (isDocker() ? " (Docker)" : ""))

function osName() {
  switch (platform()) {
    case "linux":
      return osNameLinux()
    case "darwin":
      return osNameMac()
    case "win32": // doesn't mean 32 bit (BECAUSE WINDOWS)
      return osNameWin()
    default:
      return osNameDefault()
  }
}

function osNameDefault() {
  return platform() + " " + release()
}

