import { Unpick } from "../../fe/Maybe"
import { gt0 } from "../../fe/Number"
import { Obj, omit, pick } from "../../fe/Object"
import { StrEnumKeys, strEnum } from "../../fe/StrEnum"

export interface WithModePcts {
  mode0pct: number
  mode1pct: number
  mode2pct: number
  mode3pct: number
  mode4pct: number
  mode5pct: number
  mode6pct: number
}

export interface WithModes {
  mode0: number
  mode1: number
  mode2: number
  mode3: number
  mode4: number
  mode5: number
  mode6: number
}

export type WithModeData = WithModes & WithModePcts

export function isModeData(ea: any): ea is Partial<WithModeData> {
  // only require mode0
  return gt0(ea.mode0) && gt0(ea.mode0pct)
}

export const ModeDataKeys = strEnum(
  "mode0",
  "mode0pct",
  "mode1",
  "mode1pct",
  "mode2",
  "mode2pct",
  "mode3",
  "mode3pct",
  "mode4",
  "mode4pct",
  "mode5",
  "mode5pct",
  "mode6",
  "mode6pct"
)

export type ModeDataKey = StrEnumKeys<typeof ModeDataKeys>

export function pickModeData(ea: any) {
  return pick(ea, ...ModeDataKeys.values)
}

export function omitModeData<T extends Obj>(ea: T): Unpick<T, ModeDataKey> {
  return omit(ea, ...ModeDataKeys.values)
}
