import { lazy } from "../../core/Lazy"
import { isEmpty } from "../../fe/Array"
import { entries, Valued, values } from "../../fe/Object"
import { Logger, mkLogger } from "../Logger"
import { negateFilterName } from "./PredicateNames"
import { SyncPredicate } from "./SyncPredicates"

const _logger = lazy(() => mkLogger("predicates.Predicates"))

export interface Predicate<A> {
  (a: A): boolean | null | Promise<boolean | null>
}

export type PredicateValued<A> = Valued<Predicate<A> | SyncPredicate<A>>

export type PredicateBundle<A> = PredicateValued<A>[]

export class Predicates {
  static async accepted<A>(
    a: A,
    ...predicates: PredicateValued<A>[]
  ): Promise<boolean> {
    for (const obj of predicates) {
      for (const ea of values(obj)) {
        if (false === (await ea(a))) return false
      }
    }
    return true
  }

  static async rejected<A>(
    a: A,
    ...predicates: PredicateBundle<A>
  ): Promise<boolean> {
    return !(await Predicates.accepted(a, ...predicates))
  }

  static async explain<A>(a: A, ...predicates: PredicateBundle<A>) {
    const accepted: string[] = []
    const rejected: string[] = []
    const notApplicable: string[] = []
    for (const obj of predicates) {
      for (const [name, ea] of entries(obj)) {
        const result = await ea(a)
        if (result === true) {
          accepted.push(name)
        } else if (result === false) {
          rejected.push(name)
        } else {
          notApplicable.push(name)
        }
      }
    }
    return {
      accepted,
      rejected,
      notApplicable
    }
  }

  static async whyRejected<A>(
    a: A,
    ...predicates: PredicateBundle<A>
  ): Promise<string | undefined> {
    return negateFilterName(await Predicates.firstFalse(a, ...predicates))
  }

  static async firstFalse<A>(
    a: A,
    ...predicates: PredicateBundle<A>
  ): Promise<string | undefined> {
    for (const obj of predicates) {
      for (const [k, v] of entries(obj)) {
        try {
          const result = await v(a)
          if (false === result) {
            return k
          }
        } catch (error) {
          _logger().warn("firstFalse() caught from " + k, { error })
        }
      }
    }
    return
  }

  static async logged<A>({
    a,
    logger,
    msg,
    predicates
  }: {
    a: A
    logger: Logger
    msg: string
    predicates: PredicateBundle<A>
  }) {
    const { accepted, rejected } = await Predicates.explain(a, ...predicates)
    return logger.tap({
      msg,
      result: isEmpty(rejected),
      meta: { a, accepted, rejected }
    })
  }
}
