function pad(n?: number, minLength: number = 2): string {
  return n == null ? "" : leftPad(n, minLength, "0")
}

export interface FuzzyDateObject {
  year: number
  month?: number
  day?: number
}

export type FuzzyDateInput = Partial<{
  year: number | string
  month: number | string
  day: number | string
}>

export class FuzzyDate implements FuzzyDateObject {
  constructor(
    readonly year: number,
    readonly month?: number,
    readonly day?: number
  ) {}

  static fromISO(s: Maybe<string>): Maybe<FuzzyDate> {
    if (blank(s)) return
    return this.for(
      (ISO_YMD_RE.exec(s)?.groups ?? ISO_YMD_LAX_RE.exec(s)?.groups) as any
    )
  }

  toJSON(): FuzzyDateObject {
    return {
      _ctor: "FuzzyDate",
      ...this
    }
  }

  static fromJSON(obj: Maybe<FuzzyDateObject>) {
    return this.for(obj)
  }

  static for(obj: Maybe<FuzzyDateInput>): Maybe<FuzzyDate> {
    const year = toGt0(obj?.year)
    if (
      obj == null ||
      year == null ||
      year < Settings.minValidYear.valueOrDefault
    ) {
      return
    }
    const fd =
      obj instanceof FuzzyDate
        ? obj
        : new FuzzyDate(year, toGt0(obj.month), toGt0(obj.day))
    return fd.isValid ? fd : undefined
  }

  static localToday() {
    return toFuzzyDate(new Date())!
  }

  get isValid() {
    // year is required:
    if (!isNumber(this.year)) return false
    // if day is not null, month and year must be defined:
    if (isNumber(this.day) && !isNumber(this.month)) return false
    return validYMD(this.year, this.month, this.day)
  }

  toString() {
    return this.toISOString()
  }

  [inspect.custom]() {
    return this.toISOString()
  }

  toISOString() {
    return compact([this.year, this.month, this.day])
      .map(ea => pad(ea))
      .join("-")
  }

  toLocal() {
    return datedToLocal(this)
  }

  toDateTime() {
    return DateTime.fromObject(pick(this, "year", "month", "day"))
  }

  toDate() {
    return this.toDateTime().toJSDate()
  }

  toMillis() {
    return this.toDateTime().toMillis()
  }

  /**
   * @return the following date to this date:
   * - if this is a year, return the following year
   * - if this has month precision, return the next month
   * - if this has day precision, return the following day
   */
  following() {
    const dt = this.toDateTime()
    const obj: DateObject =
      this.day != null
        ? pick(dt.plus({ day: 1 }), "year", "month", "day")
        : this.month != null
        ? pick(dt.plus({ month: 1 }), "year", "month")
        : pick(dt.plus({ year: 1 }), "year")
    // HEY FUTURE ME: we don't want to use .for() here, because the
    // FuzzyDate.isValid is false for future dates.
    return new FuzzyDate(obj.year!, obj.month, obj.day)
  }
}

export const ignorableSubpaths: string[][] = []

export function addIgnorableSubpath(subpath: string[]) {
  ignorableSubpaths.push(subpath)
}

// Only for tests
export function clearIgnorableSubpaths() {
  ignorableSubpaths.length = 0
}

export function zoneOffsetToName(
  offsetMinutes: Maybe<number>,
  includeUTCPrefix = true
): Maybe<string> {
  if (offsetMinutes == null) return
  if (offsetMinutes === 0) return includeUTCPrefix ? "UTC" : ""
  const sign = offsetMinutes < 0 ? "-" : "+"
  // Round to nearest :15 minute:
  const rounded = round(offsetMinutes / 15) * 15
  const abs = Math.abs(rounded)
  const hours = Math.floor(abs / 60)
  const minutes = Math.floor(Math.abs(abs % 60))
  return `${includeUTCPrefix ? "UTC" : ""}${sign}${pad(hours)}:${pad(minutes)}`
}

/**
 * DateTime doesn't retain timezone offsets being "set" or "unset", so we prefer
 * ExifDateTime.
 *
 * @param zoneName if undefined, the returned ExifDateTime will not have a zone or tzOffset
 */
export function toExifDateTime(
  d: Maybe<Dated>,
  zoneName?: Maybe<string>
): Maybe<ExifDateTime> {
  if (d instanceof ExifDateTime && (zoneName == null || d.zone === zoneName)) {
    return d
  }
  if (d == null || !hasTime(d)) {
    return
  }

  // DON'T USE ExifDateTime.fromDateTime(): it uses UnsetZoneOffsetMinutes.

  const tzoffsetMinutes = mapNotBlank(zoneName, ea =>
    zoneToTzOffsetMinutes(datedToMillis(d), ea)
  )
  if (zoneName != null && tzoffsetMinutes == null) {
    return
  }
  return map(getYear(d), year =>
    map(getMonth(d), month =>
      map(getDay(d), day =>
        map(getHour(d), hour =>
          dateObjectToExifDateTime({
            year,
            month,
            day,
            hour,
            minute: getMinute(d) ?? 0,
            second: getSecond(d) ?? 0,
            millisecond: getMillisecond(d),
            tzoffsetMinutes,
            rawValue: (d as any).rawValue,
            zone: zoneName
          })
        )
      )
    )
  )
}

export function datedToOffsetMinutes(d: Maybe<Dated>): Maybe<number> {
  return map(d, ea =>
    ea instanceof ExifDateTime
      ? ea.tzoffsetMinutes
      : isDateTime(ea)
      ? ea.offset
      : undefined
  )
}

export function datedToISO(
  d: Maybe<Dated>,
  includeOffset?: boolean,
  precisionMs?: number
): Maybe<string> {
  if (d == null) {
    return
  } else if (d instanceof DateInterval) {
    return d.toString({ includeOffset })
  } else if (!hasTime(d) || (precisionMs ?? datedToPrecisionMs(d)) >= dayMs) {
    return datedToYMD(d)
  } else {
    return toExifDateTime(d, getZoneName(d))?.toISOString({ includeOffset })
  }
}

const ExifFmt = "yyyy:MM:dd HH:mm:ss.SSS"
const ExifFmtWithZone = ExifFmt + "ZZ"

export function datedToEXIF(d: Maybe<Dated | number>): Maybe<string> {
  if (d == null) return
  if (d instanceof DateInterval) {
    // EXIF dates don't support date intervals. Just render the middle
    // datetime:
    return datedToEXIF(d.middle)
  }

  const dt = isNumber(d) ? DateTime.fromMillis(d) : datedToDateTime(d)
  return dt == null || !dt.isValid
    ? undefined
    : dt.toFormat(hasZone(d) ? ExifFmtWithZone : ExifFmt)
}

export function datedToYMD(d: Dated, separator = "-"): string {
  return compact([getYear(d), getMonth(d), getDay(d)])
    .map(ea => pad(ea))
    .join(separator)
}

export function toFuzzyDate(d: Maybe<Dated>): Maybe<FuzzyDate> {
  return map(d, ea =>
    map(getYear(ea), y => new FuzzyDate(y, getMonth(ea), getDay(ea)))
  )
}

export function sameDay(dis: Maybe<Dated>, dat: Maybe<Dated>): boolean {
  return lte(diffMillis(dis, dat), dayMs)
}

export function diffMillis(
  a: Maybe<Dated | number>,
  b: Maybe<Dated | number>
): Maybe<number> {
  const [aMs, bMs] = [a, b].map(datedToMillis)
  return aMs == null || bMs == null ? undefined : aMs - bMs
}

export function closeTo(
  a: Maybe<Dated | number>,
  b: Maybe<Dated | number>,
  maxDiffMs: number
): boolean {
  return mapOr(
    diffMillis(a, b),
    ea => Math.abs(ea) < maxDiffMs,
    () => false
  )
}

export function gtDated(a: Maybe<Dated>, b: Maybe<Dated>): boolean {
  if (a == null || b == null) return false
  for (const f of [
    getYear,
    getMonth,
    getDay,
    getHour,
    getMinute,
    getSecond,
    getMillisecond
  ]) {
    if (lte(f(a), f(b))) return false
  }
  return true
}

/**
 * @param index must be [0, splits]
 * @param splits must be >= 1
 */
export function dateBetween(
  a: Maybe<Dated>,
  b: Maybe<Dated>,
  index = 1,
  splits = 1
): Maybe<Dated> {
  if (datedToMillis(a) == null || datedToMillis(b) == null) return
  const [start, end] = [a, b].map(d => datedToMillis(d)!).sort()
  const diff = end - start
  const delta = diff / (splits + 1)
  const zoneA = getZoneName(a!)
  const zoneB = getZoneName(b!)
  const zone = zoneA === zoneB ? zoneA : undefined
  const result = DateTime.fromMillis(start + delta * index, { zone })
  return [a, b].some(ea => !hasTime(ea)) ? toFuzzyDate(result) : result
}

export function setZone(
  d: Maybe<Dated>,
  zone: Maybe<string | number | Zone>,
  opts?: ZoneOptions
) {
  const o = {
    ...compactValues(opts),
    keepLocalTime: opts?.keepLocalTime ?? !hasZone(d)
  }
  const z = normalizeZone(zone)
  if (d == null || z == null || !hasTime(d)) return
  return d instanceof DateInterval
    ? d.setZone(z, o)
    : toExifDateTime(d)?.setZone(z, o)
}

export function toIsoDate(d: Maybe<Dated>): Maybe<string> {
  if (d == null) return
  const arr = [getYear(d), getMonth(d), getDay(d)]
  return gt0(arr[0]) ? compact(arr).map(pad2).join("-") : undefined
}

export function utcToZone(
  dated: Dated,
  zoneName: string | Zone
): Maybe<DateTime> {
  // don't use setZone() here: we want to adjust the local time.
  let date = datedToDateTime(dated)
  if (date == null) return
  const priorZone = getZoneName(dated)
  if (priorZone == null || priorZone === "UTC") {
    date = date.toUTC(0, { keepLocalTime: true })
  }
  return date.setZone(zoneName)
}
