import { isObject } from "../../fe/Object"
import { isString } from "../../fe/String"
import { UpdateChannel, UpdateChannels } from "./UpdateChannels"

export interface UpdateChannelVersion {
  channel: UpdateChannel
  version: string
}

export function isUpdateChannelVersion(x: unknown): x is UpdateChannelVersion {
  return (
    isObject(x) &&
    UpdateChannels.has((x as UpdateChannelVersion).channel) &&
    isString((x as UpdateChannelVersion).version)
  )
}
