import { id2id, IDish } from "./api/ID"
import { compact } from "./Array"
import { blank } from "./Blank"
import { fmtLocalToShort } from "./Date"
import { ReducerName, ReducerNames } from "./ImageReducers"
import { SqWidths } from "./ImageSizes"
import { Maybe } from "./MaybeTypes"
import { gt0, numericOr, toGt0 } from "./Number"
import { StringValued } from "./Object"
import { ThumbSize } from "./ThumbSizes"
import { toS } from "./toS"
import {
  assembleFullPath,
  mergeURLSearchParams,
  URLSearchParamable
} from "./URLSearchParams"
import { ua } from "./UserAgent"
import { isSafari } from "./UserAgents"

function mkSrcSet(url: string, width: number) {
  return url + " " + width + "w"
}

function toQuery(assetId: IDish, params?: Maybe<URLSearchParamable>[]) {
  let result = params == null ? undefined : mergeURLSearchParams(...params)
  const v = toS((assetId as any)["v"])
  if (!blank(v) && v !== "1") {
    ;(result ??= new URLSearchParams()).append("v", v)
  }
  return result
}

export function assetUrl({
  assetId,
  params
}: {
  assetId: IDish
  params?: Maybe<URLSearchParamable>[]
}) {
  return assetId == null
    ? ""
    : assembleFullPath("/asset/" + id2id(assetId), toQuery(assetId, params))
}

export function assetLinkAttrs({
  assetId,
  params
}: {
  assetId: IDish
  params?: Maybe<URLSearchParamable>[]
}) {
  const href = assetUrl({ assetId, params })
  return blank(href) ? {} : { href }
}

/**
 * resized preview image
 */
export function assetImgLink({
  assetId,
  reducer,
  width,
  params
}: {
  assetId: IDish
  reducer: ReducerName
  width?: number
  params?: Maybe<URLSearchParamable>[]
}) {
  // Must match ImgRouter:
  return assembleFullPath(
    `/img/${id2id(assetId)}/${reducer}/${toGt0(width) ?? 320}`,
    toQuery(assetId, params)
  )
}

/**
 * Sends the full "shown" asset
 */
export function assetImgActualLink({
  assetId,
  assetFileId,
  params
}: {
  assetId: IDish
  assetFileId?: number
  params?: Maybe<URLSearchParamable>[]
}) {
  // Must match ImgActualRouter:
  return assembleFullPath(
    `/img/${compact([id2id(assetId), assetFileId]).join("/")}/actual`,
    toQuery(assetId, params)
  )
}

export function assetVideoLink({
  assetId,
  assetFileId,
  params
}: {
  assetId: IDish
  assetFileId: number
  params?: Maybe<URLSearchParamable>[]
}): string {
  return assembleFullPath(
    `/video/${id2id(assetId)}-${assetFileId}`,
    toQuery(assetId, params)
  )
}

export function assetSqImgAttrs({
  assetId,
  params,
  lazyLoad,
  size = "m"
}: {
  assetId: IDish
  params?: Maybe<URLSearchParamable>[]
  lazyLoad?: boolean
  size?: ThumbSize
}) {
  return {
    ...assetImgAttrs({
      assetId,
      params,
      reducer: "sq",
      widths: SqWidths,
      lazyLoad
    }),
    // This is only directionally accurate. It's the max size expected for the
    // given thumb size.
    sizes:
      (size === "s" ? 80 : size === "m" ? 160 : 320) *
        numericOr((globalThis as any)?.window?.devicePixelRatio, 1) +
      "px"
  }
}

export function assetImgAttrs({
  assetId,
  params,
  reducer,
  widths,
  lazyLoad,
  af
}: {
  assetId: IDish
  params?: Maybe<URLSearchParamable>[]
  reducer: ReducerName
  widths?: number[]
  lazyLoad?: boolean
  af?: { assetFileId: number; width: number }
}) {
  const id = id2id(assetId)
  if (id == null) {
    return { src: "/images/clear-64.png" }
  }
  widths = (widths ?? []).filter(gt0)
  if (widths.length === 0) widths.push(320) // backstop
  const width = Math.min(...widths)
  const attrs: StringValued = { width: toS(width) }
  const title = fmtLocalToShort((assetId as any)["capturedAtLocal"])
  if (title != null) {
    attrs.title = title
  }
  const src = assetImgLink({ assetId, params, reducer, width })
  // TODO: safari and safari mobile doesn't support lazy loading.
  lazyLoad ??= true
  const laterLoad = lazyLoad && isSafari(ua())

  if (laterLoad) {
    attrs.src = "/images/clear-64.png"
    attrs["data-src"] = src
  } else {
    attrs.src = src
  }

  if (!isSafari(ua())) {
    // safari doesn't support loading yet.
    attrs.loading = lazyLoad ? "lazy" : "eager"
  }

  if (reducer === ReducerNames.sq) {
    // attrs.sizes is set in the frontend based on the current thumbnail size.
    attrs.height = toS(width)
  }
  const srcSetArr = widths.map(w =>
    mkSrcSet(assetImgLink({ assetId, params, reducer, width: w }), w)
  )
  if (af != null) {
    srcSetArr.push(
      mkSrcSet(
        assetImgActualLink({ assetId, params, assetFileId: af.assetFileId }),
        af.width
      )
    )
  }
  attrs[(laterLoad ? "data-" : "") + "srcSet"] = srcSetArr.join(",")
  return attrs
}
