import { PartiallyNull } from "../../fe/PartiallyNull"
import { ExposureSettings } from "../../fe/api/ExposureSettings"
import { WithModeData } from "../color/ModeData"
import { ImageHash } from "../img/ImageHash"
import { uri2nativePath } from "../uri/FileURI"

// TODO: do we _want_ isGreyscale here (or in the AssetFile model)?

export interface AssetFileUriFields {
  nativePath: string
  uri: string
  mountpoint?: string
}

export interface AssetFileStatFields {
  mtime: number // in millis
  fileSize: number // in bytes
}

export interface AssetFileCapturedAtFields
  extends Pick<AssetFileUriFields, "nativePath">,
    Pick<AssetFileStatFields, "mtime"> {
  capturedAtLocal: number
  capturedAtOffset?: number
  /**
   * Valid values are [0, yearMs]. If null or -1, it means the precision is unknown.
   */
  capturedAtPrecisionMs?: number
  capturedAtSrc: string
}

// image hashing is optional--but mimetype and dimensions are required, so we
// only pull out the hashes here:
export type AssetFileImageHashFields = Pick<
  ImageHash,
  "meanHash" | "dctHash" | "diffHash"
> &
  PartiallyNull<WithModeData> // < not all images have N dominant colors

export interface AssetFileMetadata
  extends Partial<ExposureSettings>,
    AssetFileCapturedAtFields {
  mimetype: string

  width: number
  height: number
  rotation?: number

  make?: string
  model?: string

  rating?: number

  cameraId?: string
  imageId?: string
  lensId?: string
  geohash?: number

  // Optional, as it only applies to videos:
  durationMs?: number
  fps?: number
}

export interface IAssetFile
  extends Partial<AssetFileImageHashFields>, // < image hashing is optional!
    AssetFileUriFields,
    AssetFileStatFields,
    AssetFileCapturedAtFields,
    AssetFileMetadata {
  id?: number
  assetId?: number

  // this is expensive all by itself
  sha: string

  /** truthy if this is the "primary" variation */
  shown?: number | boolean
}

export async function getAssetFileNativePath<T extends AssetFileUriFields>(
  af: T
): Promise<string> {
  return (af.nativePath ??= (await uri2nativePath(af.uri, af.mountpoint))!)
}
