import process from "process"
import { Deferred } from "../core/async/Deferred"
import { end, EndableRanks, ending } from "../core/async/Endable"
import { EndableWrapper } from "../core/async/EndableWrapper"
import { exit } from "../core/async/Exit"
import { setUnrefTimeout } from "../core/async/Timers"
import { ChildService } from "../core/child/ChildService"
import { FatalErrorFlag } from "../core/error/ErrorFlags"
import { onError } from "../core/error/OnError"
import { broadcastPause } from "../core/event/BroadcastEvent"
import { ee } from "../core/event/EventEmitter"
import { BaseFile } from "../core/fs/BaseFile"
import { externalDirsHealthCheck } from "../core/health/ExternalDirsHealthCheck"
import { lazy } from "../core/Lazy"
import { mkLogger } from "../core/Logger"
import { httping } from "../core/net/httping"
import { Settings } from "../core/settings/Settings"
import {
  libraryHasSettings,
  readSystemSettings
} from "../core/settings/SettingsIO"
import { consoleLog } from "../core/Stdout"
import { URI } from "../core/uri/URI"
import { HttpStatusIs } from "../core/web/HttpStatus"
import myWebUrls from "../core/web/WebUrls"
import { isPaused, pause, resume } from "../core/work/IsPaused"
import { blank, mapNotBlank } from "../fe/Blank"
import { mapBoolean, mapTrue } from "../fe/Boolean"
import { secondMs } from "../fe/Date"
import { map } from "../fe/Maybe"
import { MaybePromiseMaybe, PromiseMaybe } from "../fe/MaybeTypes"
import { pick } from "../fe/Object"
import { toS } from "../fe/toS"
import { scheduleFileAndProcCleanups } from "./Cleanup"
import { Service } from "./Service"
import { rmStatDbDirs } from "./StatsDbDir"

/**
 * Launches and maintains web and sync processes. Extracted as a class for use
 * by CLI library and electron.
 *
 * MainService itself isn't currently "endable," as all dependent services (like
 * the pid monitor, web, and sync) will add themselves to the endables chain.
 */
export class MainService {
  static readonly instance = lazy(() => new MainService())

  readonly start = Date.now()
  private readonly logger = lazy(() => mkLogger("MainService"))
  readonly service: Service
  readonly httpPort = new Deferred<number>("MainService.httpPort")
  readonly libraryDir = new Deferred<string>("MainService.libraryDir")
  private web!: ChildService
  private sync: MaybePromiseMaybe<ChildService>

  constructor() {
    this.service = new Service({ name: "main" })
    void this.setup()
    setUnrefTimeout(() => {
      if (this.httpPort.isPending && !ending()) {
        consoleLog("Please wait, setting up...")
      }
    }, 4 * secondMs)
  }

  readonly setup = lazy(async () => {
    // Kick off the webservice as soon as possible so we can show the splash
    // screen:
    this.web = (await ChildService.mk("web", {
      onData: obj => this.onWebData(obj),
      healthy: () => this.webHealthCheck()
    }))!

    await mapNotBlank(Settings.pidFile.value, async ea => {
      const pidfile = BaseFile.for(ea)
      this.logger().info("Writing to pidfile " + pidfile, { pid: process.pid })
      await pidfile.writeText_(toS(process.pid))
      new EndableWrapper(
        "remove pidfile",
        () => pidfile.unlink(),
        EndableRanks.postdb
      )
    })

    await this.service.ready

    // We need to be in the main service to get macOS to prompt for
    // readability. This health check will do that for us.
    await externalDirsHealthCheck()?.result()

    if (Settings.startPaused.valueOrDefault) {
      this.logger().info("Settings.startPaused: pausing on startup.")
      await broadcastPause()
    }

    this.service.setInputHandler("--restart-sync", () =>
      this.restartSync(false)
    )

    if (null == this.web) {
      return onError("Failed to start webservice" + FatalErrorFlag)
    }

    ee().on("resume", () => this.onResume())

    // main doesn't need to know about shared state:
    // SharedState.instance()

    void this.httpPort.promise.then((port: number) => {
      const urls = myWebUrls(port)
      const msg =
        (urls.length === 1 ? " " : "\n  - ") +
        urls.map(ea => `${ea}`).join("\n  - ")
      consoleLog(`PhotoStructure is ready:${msg}`)
      if (!Settings.exposeNetworkWithoutAuth.valueOrDefault) {
        consoleLog(
          `See https://photostructure.com/faq/remote-access/ to enable non-localhost access.`
        )
      }
      // We don't start sync here, because the library may not be set up yet.
      // Web is responsible for sending us a "restartSync" message as soon as
      // the library is ready.
    })

    void this.libraryDir.promise.then((ea: string) => {
      consoleLog(`Your library is at <${URI.file(ea)}>`)
    })

    new EndableWrapper(
      "shutdown notice",
      () => consoleLog("\nShutting down PhotoStructure..."),
      EndableRanks.first
    )

    scheduleFileAndProcCleanups()

    return
  })

  /**
   * @return true if web service is healthy, or undefined if no http port is set yet.
   */
  private async webHealthCheck(): PromiseMaybe<boolean> {
    const httpPort = this.httpPort.value
    if (httpPort == null) {
      this.logger().info(
        "webHealthCheck(): no-op, no http port set (web is starting up still)"
      )
      return
    }

    try {
      const statusCode = await httping(
        "localhost",
        httpPort,
        "/ping",
        3 * secondMs
      )
      if (HttpStatusIs.success(statusCode)) {
        return true
      } else {
        this.logger().warn(
          "webHealthCheck(): web service returned " + statusCode
        )
        return false
      }
    } catch (err) {
      this.logger().warn("webHealthCheck(): httping error: " + err)
      return false
    }
  }

  private async onWebData(o: any) {
    if (blank(o)) return

    this.logger().debug("onWebData", o)

    map(o[Settings.httpPort.key], httpPort => {
      this.logger().info("Got HTTP port number from web service:" + httpPort)
      Settings.httpPort.envValue = httpPort
      void this.httpPort.maybeResolve(httpPort)
    })

    mapBoolean(o.pause, b => (b ? pause() : resume()))

    // Output by ApiSystemRouter:
    await mapTrue(o.shutdown, () =>
      exit({
        reason: "shutdown requested",
        status: 0
      })
    )

    mapTrue(o.shutdownSync, () => this.shutdownSync())

    // See https://forum.photostructure.com/t/docker-environmental-flags-not-working-or-possible-syntax-error/684
    if (!isPaused()) await mapTrue(o.restartSync, () => this.restartSync(false))

    await mapTrue(o.forceRestartSync, () => this.forceRestartSync())

    return
  }

  shutdownSync() {
    this.logger().info("shutdownSync()", { syncWasDefined: this.sync != null })
    void end(this.sync)
    this.sync = undefined
  }

  async rebuildLibrary() {
    const result = (await this.sync)?.write("--force-rebuild-library")
    if (result == null) {
      return this.logger().throw("Cannot schedule library rebuild: no sync!")
    } else if (result === false) {
      return this.logger().throw(
        "Cannot schedule library rebuild: sync process is not writable!"
      )
    }
    return this.logger().info("Scheduled library rebuild")
  }

  async forceRestartSync() {
    return this.restartSync(true)
  }

  async restartSync(force: boolean) {
    const log = mkLogger("MainService.restartSync()")
    if (ending()) {
      log.info("ending, no-op")
      return
    }
    const sync = await this.sync
    log.info(
      "priorSync",
      map(sync, ea => pick(ea, "pid", "name"))
    )

    await end(sync)

    this.sync = undefined

    await readSystemSettings()
    if (!libraryHasSettings()) {
      log.info("restartSync(): no library settings", {
        libraryDir: Settings.libraryDir.value
      })
      return
    }

    // WE DON'T WANT --rebuild or --force, because this will cause a rebuild
    // every N hours (on "bounce"), so we'll clear the cache dirs ourself right
    // here:

    if (force) {
      await rmStatDbDirs()
    }

    const s = await (this.sync = ChildService.mk("sync"))
    log.info(
      "Started sync",
      map(s, ea => pick(ea, "pid", "name"))
    )
    return
  }

  private async onResume() {
    if (null == (await this.sync) && true === (await this.webHealthCheck())) {
      this.logger().info("onResume(): starting sync.")
      await this.restartSync(false)
    }
    return
  }
}
