import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { toS } from "../../fe/toS"

const NotSetRe = /undefined|null|none|n\/a|unknown/i
export function isExifUnset(s: any): s is undefined {
  return s == null || blank(s) || NotSetRe.test(toS(s))
}

export function toExifDefined<T>(s: T): Maybe<T> {
  return isExifUnset(s) ? undefined : s
}
