export interface HealthCheckTest
  extends Partial<Pick<HealthCheckResult, "level" | "links">> {
  // no buttons!
  msg: string | Maybe<string>[]
}

export const HealthCheckLevelToLogLevel: Record<HealthCheckLevel, LogLevel> = {
  error: "error",
  "no-library": "info",
  pending: "debug",
  "stop-sync": "info",
  warn: "warn",
  ok: "info",
  disabled: "debug"
}

const HealthCheckLevelOrder = [
  "error",
  "no-library",
  "pending",
  "stop-sync",
  "warn"
] as const satisfies ReadonlyArray<HealthCheckLevel>

function summaryForLevel(
  level: (typeof HealthCheckLevelOrder)[number]
): Pick<HealthCheckSummary, "level" | "state" | "msg" | "links" | "buttons"> {
  switch (level) {
    case "error":
      return {
        level,
        state: "failed",
        msg: [
          "Some health checks failed",
          "Your library won't be synced until these issues are addressed."
        ],
        buttons: restartResetOrShutdownButtons()
      }
    case "no-library":
      if (
        isDocker() &&
        true !== libraryDirPosixFile()?.isReadWriteExecutableSync()
      ) {
        return {
          level,
          state: "failed",
          msg: [
            "Your docker container is missing a bind mount for " +
              tt(Settings.libraryDir.valueOrDefault)
          ],
          links: [DockerVolumeLink],
          buttons: [
            {
              text:
                "Shut down and add a bind mount for " +
                Settings.libraryDir.valueOrDefault,
              type: "button",
              method: "POST",
              url: "/admin/shutdown",
              icon: "power"
            }
          ]
        }
      } else {
        return {
          level,
          state: "welcome",
          msg: ["No PhotoStructure library is open"],
          links: [WhatIsALibraryLink],
          buttons: [
            {
              text: "Set up your library",
              type: "button",
              method: "GET",
              url: "/welcome",
              icon: "rocket_launch"
            }
          ]
        }
      }

    case "pending":
      return {
        level,
        state: "loading",
        msg: ["Some health checks are still pending"]
      }
    case "stop-sync":
      return {
        level,
        state: "ready",
        msg: [
          "Some health checks are preventing your library from being synced.",
          "You should still be able to view your library, however."
        ]
      }
    case "warn":
      return {
        level,
        state: "ready",
        msg: [
          "Some health checks are not OK",
          "Your library will still be synced, but you may want to address these issues."
        ]
      }
    default:
      throw new UnreachableCaseError(level)
  }
}

const logger = lazy(() => mkLogger("health.HealthCheck"))

export interface RepairTask extends Pick<HealthCheckResult, "id" | "section"> {
  (): Promise<void>
}

export type EnabledHealthCheckLevel = Exclude<HealthCheckLevel, "disabled">

export interface HealthCheckWatcher {
  (result: {
    hcr: HealthCheckResult
    hc: HealthCheck
    priorLevel: HealthCheckLevel
  }): void
}

export class HealthCheck {
  /**
   * Deferred that need to resolve or reject before #isLoading can be set to false
   */
  static #pending: Deferred<any>[] = []

  static #p = new Promises("HealthCheck")

  static #vacuumPending() {
    filterInPlace(this.#pending, ea => !ea.isPending)
    return this.#pending
  }

  static traceLater_<T>(
    desc: string,
    fn: SyncOrAsync<T> | (() => SyncOrAsync<T>)
  ): Deferred<T> | Promise<T> {
    const d = new Deferred<T>(desc)
    void d.catch(error => {
      this.#errors.push(desc + ": failed: " + errorToS(error))
      this.onResultChange()
    })
    this.#vacuumPending().push(d)
    try {
      return d.observe(tot(fn))
    } catch (error) {
      void d.reject(error)
      throw error
    }
  }

  static #errors = new BoundedList<string>(10)
  static #priorUniqSummaryStates = new BoundedList<{
    state: RunStateEnum
    ts: number
  }>(10)

  static priorRunStates() {
    return this.#priorUniqSummaryStates.toA()
  }

  static #loadingMsgs = new BoundedList<{ at: number; msg: string }>(100)
  static #shownMsgs = new CountingSet<string>()
  static #instances: HealthCheck[] = []

  static readonly all = lazy<readonly HealthCheck[]>(() =>
    Object.freeze(sortBy(this.#instances, ea => ea.sortBy))
  )

  static allCritical(): HealthCheck[] {
    return this.all().filter(ea => ea.rejectIsWarn !== true)
  }

  static addError(error: Error | string | any) {
    this.#errors.push(isString(error) ? error : errorToS(error))
    this.onResultChange()
  }

  static addLoadingMsg(msg: string) {
    this.#loadingMsgs.push({ at: Date.now(), msg })
  }

  static normalizeMsg(msg: string | Maybe<string>[]) {
    return compact(toA(msg))
      .map(toS)
      .join("\n")
      .replace(/\n{3,}/g, "\n\n") // no more than 2 blank lines
      .replace(/\n{2,} {2}- /g, "\n  - ") // no blank lines before or between bullets
      .trim() // no blank lines at start or end
      .split("\n")
  }

  static unseenOrOldestPendingTrace() {
    const pending = this.all()
      .map(ea => ea.result())
      .filter(ea => ea.isPending)
    pending.push(...this.#vacuumPending())
    logger().info("unseenOrOldestPendingTrace", {
      pending: pending.map(ea => ({ name: ea.name, id: ea.id })),
      shownCounts: this.#shownMsgs.entriesByCountDesc()
    })

    // Show oldest unshown message first:
    // If there's a message I haven't shown yet, that's great, show that.
    return map(
      leastBy(pending, ea => [this.#shownMsgs.get(ea.name), ea.startedAt]),
      ea => ({
        msg: this.findById(ea.name)?.pendingMsg ?? ea.name,
        at: ea.startedAt
      })
    )
  }

  static loadingMsg() {
    const result =
      this.#loadingMsgs.shift() ?? this.unseenOrOldestPendingTrace()
    map(result, ea => this.#shownMsgs.incr(ea.msg))
    return logger().tap({
      msg: "loadingMsg",
      level: "info",
      result: result?.msg,
      meta: {
        runState: this.summary().state
      }
    })
  }

  static enableAll() {
    return this.disableAll(false)
  }

  static disableAll(b = true) {
    // AKA skipAllHealthChecks
    const skipHealthCheckIds = b ? HealthCheckIds.values : []
    Settings.skipHealthCheckIds.envValue = skipHealthCheckIds
    return HealthCheck.reset()
  }

  static findById(id: string) {
    return this.#instances.find(ea => ea.id === id)
  }

  static testResults(): Deferred<HealthCheckResult>[] {
    return this.all().map(ea => ea.result())
  }

  static testResultsCritical(): Deferred<HealthCheckResult>[] {
    return this.allCritical().map(ea => ea.result())
  }

  static async errorResults(): Promise<HealthCheckResult[]> {
    return (await Promise.all(this.testResults())).filter(
      ea => ea.level === "error"
    )
  }

  static async notOkResults(): Promise<HealthCheckResult[]> {
    return (await Promise.all(this.testResults())).filter(ea =>
      levelIsNotOK(ea.level)
    )
  }

  static runState() {
    return this.summary().state
  }

  static readonly summary = lazy(() => {
    return this.#summary()
  })

  static summaryFromResults(results: HealthCheckResult[]) {
    const result = this.#summary(results)
    this.summary.set(result)
    return result
  }

  static #summary(results?: HealthCheckResult[]): HealthCheckSummary {
    const result = logger().tap({
      level: "info",
      msg: "#summary()",
      result: this.#_summary(results)
    })
    if (result.state !== this.#priorUniqSummaryStates.last?.state) {
      this.#priorUniqSummaryStates.push({ state: result.state, ts: Date.now() })
    }
    return result
  }

  /** DO NOT USE. See #summary() or summary() */
  static #_summary(results?: HealthCheckResult[]): HealthCheckSummary {
    results ??= (
      isWebService() ? this.allCritical() : (this.all() as HealthCheck[])
    ).map(ea => ea.toResultSync())

    const pendingIds = results
      .filter(ea => ea.level === "pending")
      .map(ea => ea.id)
    const pendingCount = pendingIds.length
    const settledCount = results.length - pendingCount
    const settled = pendingCount === 0

    if (this.#errors.length > 0) {
      return {
        ...BaseHealthCheckSummary,
        level: HealthCheckLevels.error,
        settled,
        pendingIds,
        pendingCount,
        settledCount,
        ts: Date.now(),
        state: RunStates.failed,
        msg: [
          "Errors encountered during setup:",
          uniq(this.#errors.map(ea => errorToS(ea))).join("\n---\n")
        ],
        buttons: restartResetOrShutdownButtons()
      }
    }

    for (const level of HealthCheckLevelOrder) {
      const hcWithLevel = results.filter(ea => ea.level === level)
      if (isNotEmpty(hcWithLevel)) {
        // Any other messages we should show?
        const hcNotOK = results.filter(
          ea => !hcWithLevel.includes(ea) && levelIsNotOK(ea.level)
        )
        const args = summaryForLevel(level)

        return logger().tap({
          msg:
            "summary() found " +
            hcWithLevel.length +
            " " +
            args.level +
            " checks",
          level: HealthCheckLevelToLogLevel[args.level],
          result: {
            ...BaseHealthCheckSummary,
            level: args.level,
            state: args.state,
            links: args.links,
            buttons: args.buttons,
            settled,
            pendingIds,
            pendingCount,
            settledCount,
            linkIds: hcWithLevel.map(ea => ea.id),
            ts: Date.now(),
            msg: uniq([
              ...args.msg,
              ...(args.level === HealthCheckLevels.pending
                ? []
                : flatten([...hcWithLevel, ...hcNotOK].map(ea => ea.msg[0])))
            ])
          },
          meta: { checks: hcWithLevel.map(ea => ea.id) }
        })
      }
    }

    return logger().tap({
      msg: "summary(): all are passing",
      result: {
        ...BaseHealthCheckSummary,
        level: HealthCheckLevels.ok,
        state: RunStates.ready,
        settled,
        pendingIds,
        pendingCount,
        settledCount,
        ts: Date.now(),
        msg: [
          "All health checks pass",
          plur(results.length, "health check") + " were run",
          "Last updated: " + fmtDateShort(Date.now())
        ]
      }
    })
  }

  static allResults(): AsyncGenerator<HealthCheckResult> {
    return Deferred.toAsyncIterable(this.all().map(ea => ea.result()))
  }

  static async awaitSettled(): Promise<HealthCheckSummary> {
    const expectedChecks = (isWebService() ? this.allCritical() : this.all())
      .length
    let result = HealthCheck.summary()
    if (result.settledCount < expectedChecks || !result.settled) {
      const results = await Promise.all(
        isWebService()
          ? HealthCheck.testResultsCritical()
          : HealthCheck.testResults()
      )
      result = HealthCheck.summaryFromResults(results)
      logger().info("awaitSettled(): summary result", {
        summary: result,
        results
      })
    }
    return logger().tap({
      msg: "awaitSettled(): done",
      level: "info",
      result
    })
  }

  static rerunSetup() {
    this.reset()
    return this.awaitSettled()
  }

  static onResultChange() {
    HealthCheck.summary.unset()
  }

  static resetDebounced = debounce(() => HealthCheck.reset(), 50)

  static reset() {
    if (ending()) {
      logger().warn("Rejecting reset() when ending()", shortStack())
      return
    }
    this.onResultChange()
    this.#errors.clear()
    this.#loadingMsgs.clear()
    this.#shownMsgs.clear()
    this.#pending.length = 0
    for (const ea of this.all()) {
      ea.reset()
    }
  }

  static for(args: {
    section: HealthCheckSection
    id: HealthCheckId
    pendingMsg: string
    settings: (keyof typeof Settings)[]
    later: Later<HealthCheckTest>
    ordinal?: number
    links?: Link[]
    okLinks?: Link[]
    warnLinks?: Link[]
    noLibraryLinks?: Link[]
    errorLinks?: Link[]
    onReset?: () => void
    ttlMs?: number
    timeoutMs?: number
    rejectIsWarn?: boolean
  }) {
    const prior = this.findById(args.id)
    if (prior != null) {
      throw new Error(
        `Health check with id ${args.id} already exists in section ${prior.section}`
      )
    }
    const result = new HealthCheck(
      args.section,
      args.id,
      args.pendingMsg,
      args.settings,
      args.later,
      args.ordinal,
      args.links,
      args.okLinks,
      args.warnLinks,
      args.noLibraryLinks,
      args.errorLinks,
      args.onReset,
      args.ttlMs,
      args.timeoutMs,
      args.rejectIsWarn
    )
    this.#instances.push(result)
    this.all.unset()
    return result
  }

  readonly logger: Logger
  readonly sortBy: Comparable[]
  readonly result: MemoizedDeferred<HealthCheckResult>

  private constructor(
    readonly section: HealthCheckSection,
    readonly id: HealthCheckId,
    readonly pendingMsg: string,
    readonly settings: (keyof typeof Settings)[],
    later: Later<HealthCheckTest>,
    readonly ordinal?: number,
    private readonly links?: Link[],
    private readonly okLinks?: Link[],
    private readonly warnLinks?: Link[],
    private readonly noLibraryLinks?: Link[],
    private readonly errorLinks?: Link[],
    readonly onReset?: () => void,
    readonly ttlMs?: number,
    readonly timeoutMs: number = commandTimeoutMs(),
    readonly rejectIsWarn = false
  ) {
    this.sortBy = [
      HealthCheckSections.ordinal(this.section),
      ordinal ?? 999,
      // id is included for determinism and as a backstop for lazy health
      // checks that don't have an ordinal:
      this.id
    ]
    this.logger = mkLogger("health.HealthCheck." + this.id)
    this.result = lazyAsync({
      desc: this.pendingMsg,
      later: () =>
        HealthCheck.#p.enqueue({
          name: this.id,
          l: () => this.#wrapLater(later)
        }),
      ttlMs,
      timeoutMs
    })
    for (const ea of this.settings) {
      Settings[ea].watchLater(() => this.reset())
    }
    this.result.watchLater({
      onChange: () => HealthCheck.onResultChange(),
      onError: () => HealthCheck.onResultChange()
    })
  }

  isPending() {
    return this.toResultSyncMaybe() == null
  }

  settledAtLeastOnce() {
    return this.result.settledCount()
  }

  /**
   * Clear the prior test result so that it will be re-evaluated on next
   * access.
   *
   * NOTE: this forces this HealthCheck back to the pending state!
   */
  reset() {
    this.result.unset()
    this.onReset?.()
  }

  /**
   * Rerun the test now
   */
  refresh() {
    return this.result.refresh()
  }

  isSkipped() {
    return Settings.skipHealthCheckIds.values.includes(this.id)
  }

  async #wrapLater(later: Later<HealthCheckTest>): Promise<HealthCheckResult> {
    logger().debug("#wrapLater() starting", { id: this.id })
    try {
      if (this.isSkipped()) {
        return this.#toResult({
          level: "disabled",
          msg: ["Disabled by " + Settings.skipHealthCheckIds.key]
        })
      }
      const timeoutMs = this.timeoutMs ?? commandTimeoutMs()
      const result = await thenOrTimeoutError(
        getDevEnvFlag("PS_SLOMO")
          ? // make .8 to avoid timeout, or 1.5 to exercise timeout error handling:
            delay(randomInt(secondMs, timeoutMs * 0.3)).then(later)
          : later(),
        timeoutMs / 2
      )
      return this.#toResult(result)
    } catch (err) {
      return this.#toErrorResult(err)
    }
  }

  // All results go through this method
  #toResult(test: HealthCheckTest, src = "#toResult"): HealthCheckResult {
    // normalize lines:
    const msg = HealthCheck.normalizeMsg(test.msg)
    const level =
      test.level ??
      (toS(msg).toLowerCase().includes("error")
        ? "error"
        : toS(msg).toLowerCase().includes("warn")
        ? "warn"
        : "ok")
    const links = compact([
      ...(test?.links ??
        (level === "ok" || level === "pending"
          ? this.okLinks
          : level === "warn"
          ? this.warnLinks
          : level === "no-library"
          ? this.noLibraryLinks
          : level === "error"
          ? this.errorLinks
          : []) ??
        []),
      ...(this.links ?? [])
    ])
    const result: HealthCheckResult = {
      section: this.section,
      id: this.id,
      ts: Date.now(),
      level,
      elapsedMs: this.result.elapsedMs(),
      msg,
      ...partitionLinks(links)
    }
    // don't bother to log if we're ending
    return ending()
      ? result
      : this.logger.tap({
          level: HealthCheckLevelToLogLevel[level],
          msg: src,
          result
        })
  }

  #toErrorResult(error: any, level?: HealthCheckLevel): HealthCheckResult {
    this.logger.warn("#toErrorResult", { error, level })
    const msg =
      stripSuffix(this.pendingMsg, "") +
      " failed:\n" +
      (map(error, errorToS) ?? "Unknown error")
    level ??=
      this.rejectIsWarn ||
      (!isFatalError(error) && msg.toLowerCase().includes("warn"))
        ? "warn"
        : "error"
    return this.#toResult({ msg, level }, "#toErrorResult")
  }

  toLevelSync() {
    return this.result.prior()?.value?.level ?? "pending"
  }

  toResultSyncMaybe(): HealthCheckResult | undefined {
    return this.result()?.value ?? this.result.lastValue()
  }

  toResultSync(): HealthCheckResult {
    return (
      this.toResultSyncMaybe() ?? {
        section: this.section,
        id: this.id,
        ts: Date.now(),
        level: "pending",
        msg: [this.pendingMsg]
      }
    )
  }

  async resultOrTimeout(timeoutMs: number): Promise<HealthCheckResult> {
    return (
      (await thenOrTimeoutAs({ f: this.result(), timeoutMs, as: undefined })) ??
      this.#toErrorResult("Timeout", "warn")
    )
  }
}

function partitionLinks(
  arr: Maybe<Link[]>
): Pick<HealthCheckResult, "links" | "buttons"> {
  if (isEmpty(arr)) return {}
  const [buttons, links] = partition(
    arr,
    ea => ea.type === "button" || ea.method === "POST"
  )
  const result = {} as Pick<HealthCheckResult, "links" | "buttons">
  if (isNotEmpty(links)) result.links = uniqBy(links)
  if (isNotEmpty(buttons)) result.buttons = uniqBy(buttons)
  return result
}
