import { hourMs } from "../../fe/Date"
import { defer } from "../../fe/Defer"
import { checkVersion_ } from "../version/VersionCheck"
import { HealthCheck } from "./HealthCheck"

export const versionHealthCheck = defer(() =>
  HealthCheck.for({
    section: "System",
    id: "ps-version",
    pendingMsg: "Checking PhotoStructure version",
    settings: ["autoUpdateCheck", "configDir", "libraryDir", "optOut"],
    ttlMs: hourMs,
    rejectIsWarn: true,
    links: [
      {
        text: "Change auto-update setting",
        icon: "settings",
        url: "/settings#update-check-section"
      },
      {
        text: "What's new?",
        icon: "docs",
        url: "https://photostructure.com/whats-new/"
      },
      // {
      //   text: "Release notes",
      //   icon: "docs",
      //   url: "https://photostructure.com/release-notes/"
      // },
      {
        text: "Update checking & privacy",
        icon: "docs",
        url: "https://forum.photostructure.com/t/new-in-v2023-10-automated-photostructure-version-checking/1956"
      }
    ],
    later: checkVersion_
  })
)
