import { StrEnumKeys, strEnum } from "../../fe/StrEnum"

// these must be in order from least-to-most stable:
export const UpdateChannels = strEnum("prealpha", "alpha", "beta", "stable")
export type UpdateChannel = StrEnumKeys<typeof UpdateChannels>

export function extractUpdateChannel(version: string): UpdateChannel {
  for (const c of UpdateChannels.values) {
    // the minus sign lets "alpha" not spuriously match against "prealpha":
    if (version.includes("-" + c)) return c
  }
  return UpdateChannels.stable
}

/**
 * What channels are equivalent or better than `channel`?
 */
export function eligibleForChannel(channel: UpdateChannel): UpdateChannel[] {
  return UpdateChannels.values.slice(UpdateChannels.ordinal(channel))
}
