import { strEnum, StrEnumKeys } from "../fe/StrEnum"

export const RunStates = strEnum(
  /** starting up: show splash */
  "loading",
  /** no current library: show /welcome page */
  "welcome",
  /** library is open: show /tag home page */
  "ready",
  /** library isn't open: show /status page */
  "failed"
)
export type RunStateEnum = StrEnumKeys<typeof RunStates>

export type ApiSplashEvent = "version" | "msg" | "settled"
