import { Settings as LuxonSettings } from "luxon"
import { lazy } from "../../core/Lazy"
import { locale } from "../Locale"
import { Settings } from "../settings/Settings"

export const setupLuxon = lazy(async () => {
  LuxonSettings.throwOnInvalid = false
  Settings.twoDigitCutoffYear.watch(
    // TODO: fix Luxon types!
    ea => ((LuxonSettings as any).twoDigitCutoffYear = ea)
  )
  LuxonSettings.defaultLocale = await locale()
})
