import { defer } from "../../fe/Defer"
import { ProjectPath } from "../fs/ProjectPath"
import { isElectron, isMac } from "../platform/Platform"
import { HealthCheck } from "./HealthCheck"

export const notInDMGHealthCheck = defer(() =>
  isMac && isElectron
    ? HealthCheck.for({
        section: "System",
        id: "proc-not-in-dmg",
        pendingMsg: "Checking PhotoStructure installation directory",
        settings: [],
        later: async () => {
          if (ProjectPath.isInDMG()) {
            return {
              level: "error",
              msg: "PhotoStructure cannot run directly from a DMG disk image. Please drag and drop the PhotoStructure icon onto your Applications folder to install.",
              checkboxLabel:
                "Open https://photostructure.com/getting-started/installation/ in your browser"
            }
          } else {
            return {
              level: "ok",
              msg: "PhotoStructure isn't running directly from a DMG disk image"
            }
          }
        }
      })
    : undefined
)
