import { uniq } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { defer } from "../../fe/Defer"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { toA } from "../../fe/toA"
import { mkLogger } from "../Logger"
import { hasApt } from "../OperatingSystem"
import { stdoutResult_ } from "../child/ChildProcess"
import { splitLines } from "../fs/CRLF"
import { debianVersionToSemver } from "../version/SemVer"
import { commandTimeoutMs } from "../volumes/VolumeTtls"

const logger = defer(() => mkLogger("platform.Apt"))

export async function aptInstalledVersion(
  filename: string
): PromiseMaybe<
  { pkg: string; version: Maybe<string>; semver: Maybe<string> }[]
> {
  if (!hasApt()) return

  const arr = []
  // package is a reserved word (!!)
  for (const pkg of toA(await dpkgSearch(filename))) {
    const version = await aptCachePolicyVersion(pkg)
    arr.push({ pkg, version, semver: debianVersionToSemver(version) })
  }
  return arr
}

// https://www.debian.org/doc/debian-policy/ch-controlfields.html
const DebPackageNameRe = /^[a-z][a-z\d+\-.]+/

async function dpkgSearch(nativePath: string): PromiseMaybe<string[]> {
  if (!hasApt() || blank(nativePath)) return

  // Case 1:

  // # dpkg -S /usr/bin/heif-convert
  // libheif-examples: /usr/bin/heif-convert

  // Case 2:

  // # dpkg -S /usr/bin/heif-convert
  // (stderr): dpkg-query: no path found matching pattern /usr/bin/heif-convert

  const result = await stdoutResult_("dpkg", ["-S", nativePath], {
    timeoutMs: commandTimeoutMs(),
    ignoreExitCode: true,
    ignoreStderr: true,
    isIgnorableError: () => true
  })

  logger().debug("dpkg -S", { nativePath, result })

  return result.code !== 0
    ? undefined
    : uniq(splitLines(result.result).map(ea => DebPackageNameRe.exec(ea)?.[0]))
}

export async function aptCachePolicyVersion(
  packageName: string
): PromiseMaybe<string> {
  if (!hasApt() || blank(packageName)) return

  // Case 1:

  // # apt-cache policy libheif-examples
  // N: Unable to locate package libheif-examples

  // Case 2:

  // # apt-cache policy libheif-examples
  // libheif-examples:
  //   Installed: 1.15.1-1
  //   Candidate: 1.15.1-1
  //   Version table:
  //  *** 1.15.1-1 100
  //         100 /var/lib/dpkg/status

  const result = await stdoutResult_("apt-cache", ["policy", packageName], {
    timeoutMs: commandTimeoutMs(),
    ignoreExitCode: true,
    ignoreStderr: true,
    isIgnorableError: () => true
  })

  logger().debug("apt-cache policy", { packageName, result })

  if (result.code !== 0) return

  for (const line of splitLines(result.result)) {
    const match = /^\s*Installed: (.*)$/.exec(line)
    if (match != null) {
      return match[1]
    }
  }
  return
}
