import { lazy } from "../../core/Lazy"
import { isNotEmpty } from "../../fe/Array"
import { dayMs, minuteMs } from "../../fe/Date"
import { clamp } from "../../fe/Number"
import { gt } from "../../fe/Primitive"
import { MaybeSyncOrAsync, SyncOrAsync } from "../../fe/SyncOrAsync"
import { Logger, mkLogger } from "../Logger"
import { isTest } from "../NodeEnv"
import { rateLimited } from "../RateLimited"
import { EndableRanks, ending } from "../async/Endable"
import { EndableInterval } from "../async/EndableInterval"
import { fmtFullDuration } from "../date/DurationFormat"
import { ee } from "../event/EventEmitter"
import { DirectoryEntry } from "./DirectoryEntry"
import { isNoMediaName } from "./NoMedia"
import { SimpleFile } from "./SimpleFile"

export class FileCleanup {
  readonly #logger: Logger

  constructor(
    readonly name: string,
    readonly rootNativePath: () => MaybeSyncOrAsync<string | SimpleFile>,
    readonly staleMs: number,
    readonly isPrunable?: (file: DirectoryEntry) => SyncOrAsync<boolean>
  ) {
    this.#logger = mkLogger("fs.FileCleanup(" + name + ")")
  }

  readonly intervalMs = lazy(() =>
    clamp(isTest ? 0 : 5 * minuteMs, dayMs, this.staleMs / 2)
  )

  readonly scheduleInterval = lazy(
    () =>
      new EndableInterval({
        name: this.name,
        callback: () => this.cleanup(),
        intervalMs: this.intervalMs(),
        rank: EndableRanks.first
      })
  )

  // We're using DirectoryEntries here, as we don't want anything that's cached,
  // we want to minimize memory/object creation, and don't want to blow out the
  // PosixFile caches:
  readonly cleanup = rateLimited({
    name: this.name,
    minCallDelayMs: this.intervalMs() / 4,
    f: async () => {
      if (ending()) return
      const r = await this.rootNativePath()
      if (r == null) return []
      const root = await DirectoryEntry.for(r)
      if (root == null) {
        // this.logger.debug("_cleanup(): no-op, root is null", r)
        return []
      }

      this.#logger.info("cleanup(): starting", {
        root,
        staleMs: fmtFullDuration(this.staleMs, " ")
      })

      const minFreshTs = Date.now() - this.staleMs
      const victimDirs = new Set()
      const prunedFiles: string[] = []
      const prunedDirs: string[] = []
      // this.logger.debug("_cleanup(): starting", { root, maxTs })
      await root.clear().visitDescendants(async f => {
        // Don't delete the root .nomedia dir:
        if (f.dir === root.nativePath && isNoMediaName(f.base)) return

        // If we're a victim dir, ignore the mtime: we should still try to delete it.
        const notVictimDir = !victimDirs.has(f.nativePath)
        if (notVictimDir && gt(await f.mtimeMs(), minFreshTs)) {
          return
        }

        if (f.isDirectory()) {
          try {
            // Rather than running readdir, just try unlinking:
            await f.rmdir_()
            prunedDirs.push(f.nativePath)
            victimDirs.add(f.dir)
            // this.logger.debug("_cleanup(): deleted " + f)
          } catch (err: any) {
            if (err.code === "ENOENT") {
              this.#logger.warn("cleanup(): ENOENT " + f, err)
              ee().emit("clearCache")
            }
            if (err.code !== "ENOTEMPTY") {
              this.#logger.warn("cleanup(): failed to rmdir " + f, err)
            }
          }
        } else if (this.isPrunable == null || (await this.isPrunable(f))) {
          try {
            await f.unlink_()
            prunedFiles.push(f.nativePath)
            victimDirs.add(f.dir)
            // this.logger.debug("_cleanup(): unlinked " + f)
          } catch (err) {
            this.#logger.warn("cleanup(): failed to clean up tmpfile " + f, err)
          }
        } else {
          // this.logger.debug("_cleanup(): not prunable: " + f)
        }
      })
      const result = [...prunedFiles, ...prunedDirs]
      if (isNotEmpty(result)) {
        root.clear()
      }
      this.#logger.info("cleanup(): done", {
        root,
        pruned_file_count: prunedFiles.length,
        pruned_dir_count: prunedDirs.length
      })
      return result
    }
  })
}
