import _sharp, { FailOnOptions } from "sharp"
import { lazy } from "../../core/Lazy"
import { blank, notBlank } from "../../fe/Blank"
import { minuteMs } from "../../fe/Date"
import { stringify } from "../../fe/JSON"
import { Maybe } from "../../fe/MaybeTypes"
import { mkLogger } from "../Logger"
import { shim1 } from "../Shim1"
import { includesIgnoreCase } from "../String"
import { WrappedError } from "../error/WrappedError"
import { FileCache } from "../fs/FileCache"
import { PosixFile } from "../fs/PosixFile"
import {
  SimpleFileOrString,
  isSimpleFile,
  toNativePath_
} from "../fs/SimpleFile"
import { Settings } from "../settings/Settings"
import { isVideoMimeType } from "../tags/MimeTypes"
import { readMimeType } from "../tags/ReadMimeType"
import { commandTimeoutMs } from "../volumes/VolumeTtls"
import { isSharp } from "./Sharp"
import { DescribedSharpReadable, sharpReadable_ } from "./SharpReadable"
import { validVideo_ } from "./Video"
import { validJpeg_ } from "./jpegtran"

const logger = lazy(() => mkLogger("img.ValidFile"))

/**
 * @return true if the file is valid, or false if the file is invalid.
 */
export async function isValidFile(
  fileOrPath: SimpleFileOrString
): Promise<boolean> {
  return blank(await whyInvalidFile(toNativePath_(fileOrPath)))
}

/**
 * Try to validate the contents of an image or video are not corrupt
 * @throws on invalid file
 */
export async function throwIfInvalidFile_(
  fileOrPath: SimpleFileOrString
): Promise<void> {
  if (
    // it's turned off:
    !Settings.validateJpegImages.valueOrDefault &&
    !Settings.validateRawImages.valueOrDefault &&
    !Settings.validateVideos.valueOrDefault
  ) {
    logger().debug("file validation is disabled for " + fileOrPath, {
      validateJpegImages: Settings.validateJpegImages.valueOrDefault,
      validateRawImages: Settings.validateRawImages.valueOrDefault,
      validateVideos: Settings.validateVideos.valueOrDefault
    })
    return
  }
  const nativePath = toNativePath_(fileOrPath)

  const why = logger().tap({
    msg: "whyInvalidFile(" + nativePath + ")",
    result: await whyInvalidFile(nativePath)
  })

  if (notBlank(why)) {
    logger().throw(why, {
      path: nativePath,
      retriable: false,
      doNotSend: true, // we expect these to happen.
      ignorable: true // we expect these to happen.
    })
  }
}

const cache = lazy(
  () =>
    new FileCache<string>({
      name: "img.validFile",
      maxSize: 256,
      // https://forum.photostructure.com/t/initial-import-of-library-fails/1643/2
      timeoutMs: Math.max(
        minuteMs,
        Settings.validateVideos.valueOrDefault
          ? 5 * minuteMs
          : commandTimeoutMs()
      )
    })
)

export function clearInvalidFileCache() {
  return cache().clear()
}

export const whyInvalidFile = shim1({
  name: "img.whyInvalidFile",
  cache,
  toKey: toNativePath_,
  impl: async (nativePath: string) => {
    const src = PosixFile.for(nativePath)
    if (!(await src.isFile()) || !(await src.isReadable())) {
      return src + " is not a readable file"
    }
    if (await src.isEmpty()) {
      return src + " is empty"
    }
    // Use the raw tags--if we're missing dimensions or something else, it still
    // might be valid.
    try {
      const mt = await readMimeType(src)
      if (mt == null) {
        throw new Error("Cannot validate, no mimetype")
      }
      if (isVideoMimeType(mt)) {
        if (Settings.validateVideos.valueOrDefault) {
          logger().debug("validating " + src)
          await validVideo_(src)
        }
      } else if (mt.startsWith("image/")) {
        if (mt === "image/jpeg") {
          if (Settings.validateJpegImages.valueOrDefault) {
            await validJpeg_(src)
          }
        } else if (Settings.validateRawImages.valueOrDefault) {
          if (
            includesIgnoreCase(Settings.validateMimetypeSkiplist.values, mt)
          ) {
            logger().info(
              "Skipping validation of " +
                src +
                ": validateMimetypeSkiplist matches",
              { mime: mt }
            )
          } else {
            // heif? what else?
            const sr = await sharpReadable_({ src, skipEmbedded: true })
            const s = await toSharpFailOnError(sr)
            await s.tiff().toBuffer()
          }
        }
      } else {
        throw new Error("Unsupported mimetype " + stringify(mt))
      }
      return ""
    } catch (cause) {
      // The error doesn't need to include the filename: the sync report will
      // already be on that line.
      return new WrappedError("Failed to validate", {
        cause,
        path: nativePath,
        doNotSend: true
      }).toS()
    }
  }
})

async function toSharpFailOnError(
  input: Maybe<DescribedSharpReadable>,
  failOn: FailOnOptions = Settings.imageFailOn.valueOrDefault as FailOnOptions
): Promise<_sharp.Sharp> {
  const a: any = input

  if (isSimpleFile(a.file)) {
    return _sharp(a.file.nativePath, { failOn })
  }

  if (isSharp(a.sharp)) {
    const { data, info } = await a.sharp
      .raw()
      .toBuffer({ resolveWithObject: true })
    return _sharp(data, { ...info, failOnError: true })
  }

  throw new Error("Cannot read input " + input?.desc)
}
