import { Maybe } from "../fe/MaybeTypes"
import { Primitive } from "../fe/Primitive"

export class WeakCache<K extends Primitive, V extends object>
  implements Map<K, V>
{
  readonly [Symbol.toStringTag] = "WeakCache"

  // Maps entries to insertion time values. Entries should expire after ttlMs.
  readonly #delegate = new Map<K, WeakRef<V>>()

  clear(): this {
    this.#delegate.clear()
    return this
  }

  delete(key: K): boolean {
    return this.#delegate.delete(key)
  }

  deleteAll(keys: K[]) {
    for (const ea of keys) {
      this.delete(ea)
    }
  }

  forEach(callbackfn: (value: V, key: K, map: Map<K, V>) => void): void {
    for (const [k, v] of this.entries()) {
      callbackfn(v, k, this)
    }
  }

  findEntry(
    callbackfn: (value: V, key: K, map: Map<K, V>) => boolean
  ): Maybe<V> {
    for (const [k, v] of this.entries()) {
      if (callbackfn(v, k, this)) {
        return v
      }
    }
    return
  }

  deleteValueIf(predicate: (value: V, key: K, map: Map<K, V>) => boolean) {
    for (const [k, v] of this.entries()) {
      if (predicate(v, k, this)) {
        this.#delegate.delete(k)
      }
    }
  }

  get(key: K): V | undefined {
    const w = this.#delegate.get(key)
    const v = w?.deref()
    if (v == null && w != null) {
      this.#delegate.delete(key)
    }
    return v
  }

  getOrMaybeSet(key: K, valueThunk: () => Maybe<V>): Maybe<V> {
    const v = this.#delegate.get(key)?.deref()
    if (v != null) return v
    const value = valueThunk()
    if (value != null) this.set(key, value)
    return value
  }

  set(key: K, value: V): this {
    this.#delegate.set(key, new WeakRef(value))
    return this
  }

  has(key: K): boolean {
    return this.#delegate.get(key)?.deref() != null
  }

  get size() {
    this.#vacuum()
    return this.#delegate.size
  }

  keys(): IterableIterator<K> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<K> {
      for (const [k, w] of self.#delegate.entries()) {
        if (w.deref() == null) {
          self.#delegate.delete(k)
        } else {
          yield k
        }
      }
    }
    return iter()
  }

  values(): IterableIterator<V> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<V> {
      for (const [k, w] of self.#delegate.entries()) {
        const v = w.deref()
        if (v == null) {
          self.#delegate.delete(k)
        } else {
          yield v
        }
      }
    }
    return iter()
  }

  entries(): IterableIterator<[K, V]> {
    // eslint-disable-next-line @typescript-eslint/no-this-alias
    const self = this
    function* iter(): IterableIterator<[K, V]> {
      for (const [k, w] of self.#delegate.entries()) {
        const v = w.deref()
        if (v == null) {
          self.#delegate.delete(k)
        } else {
          yield [k, v]
        }
      }
    }
    return iter()
  }

  [Symbol.iterator](): IterableIterator<[K, V]> {
    return this.entries()
  }

  /**
   * remove all expired entries
   */
  #vacuum() {
    for (const [k, v] of this.#delegate.entries()) {
      if (v.deref() == null) this.#delegate.delete(k)
    }
  }
}
