import { ResizeOptions } from "sharp"
import { lazy } from "../../core/Lazy"
import { Dimensions, fmtDim, pixels } from "../../fe/Dimensions"
import { fitInside } from "../../fe/Fit"
import { ReducerName, ReducerNames } from "../../fe/ImageReducers"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { StrEnumKeys, strEnum } from "../../fe/StrEnum"
import { mkLogger } from "../Logger"
import { ltBoth, lteBoth } from "./Dimensions"

// See http://sharp.pixelplumbing.com/en/stable/api-resize/ and
// https://github.com/lovell/sharp/issues/1135
export const SharpFits = strEnum(
  "cover",
  "contain", // this adds background to fill the expected dim, instead of cropping
  "fill",
  "inside",
  "outside"
)
export type SharpFit = StrEnumKeys<typeof SharpFits>

export type OutputSize = ResizeOptions & Dimensions

export interface Reducer {
  name: ReducerName
  fit: SharpFit
  reduce(max: Dimensions, input: Dimensions): Maybe<OutputSize>
}

/**
 * Reduce the smaller dimension and crop the larger dimension to fill the given
 * constraints. As this is the default behavior of sharp, the implementation is
 * the identity function.
 */
export namespace Square {
  const logger = lazy(() => mkLogger("img.Reducers.Square"))
  export const name = ReducerNames.sq
  export const fit = "cover"
  export function reduce(
    max: Dimensions,
    input: Dimensions
  ): Maybe<OutputSize> {
    const result = !lteBoth(max, input)
      ? undefined
      : {
          ...max,
          fit: SharpFits.cover
        }

    return logger().tap({
      msg: "reduce()",
      result,
      meta: {
        max,
        input
      }
    })
  }
}

/**
 * Reduce `input` to match `aspectRatio`'s aspect ratio.
 */
export function fitInsideToAspectRatio(
  input: Dimensions,
  aspectRatio: number
): Dimensions {
  return input.width / input.height >= aspectRatio
    ? {
        width: Math.floor(input.height * aspectRatio),
        height: input.height
      }
    : {
        width: input.width,
        height: Math.floor(input.width / aspectRatio)
      }
}

/**
 * Reduce `input` to match `aspectRatio`'s aspect ratio.
 */
export function fitInsideToMaxPixels(
  input: Dimensions,
  maxPixels: number
): Maybe<Dimensions> {
  if (input == null || !gt0(input.width) || !gt0(input.height)) return
  if (pixels(input) < maxPixels) return // no need to reduce
  const aspectRatio = input.width / input.height
  const height = Math.floor(Math.sqrt(maxPixels / aspectRatio))
  const width = Math.floor(aspectRatio * height)
  return { width, height }
}

/**
 * Reduce the entire image to fit given constraints
 */
export namespace Fit {
  const logger = lazy(() => mkLogger("img.Reducers.Fit"))

  export const name = ReducerNames.fit
  export const fit = "inside"
  export function reduce(
    max: Dimensions,
    input: Dimensions
  ): Maybe<OutputSize> {
    // if input height < max AND input width < max, max is too big.
    if (ltBoth(input, max)) {
      logger().trace(
        `reduce(): input ${fmtDim(input)} is too small for ${fmtDim(max)}`
      )
      return
    }
    return fitInside(input, max)
  }
}

