import { notBlank } from "../../fe/Blank"
import { Settings } from "../settings/Settings"
import { m } from "./Licensing"

export async function getEmail() {
  const s = Settings.email.value
  if (notBlank(s)) return s
  for (const ea of await m()) {
    if (notBlank(ea.l?.sub)) return ea.l?.sub
  }
  return
}
