import { Tags } from "exiftool-vendored"
import { uniq } from "../../fe/Array"
import { mapNotBlank, notBlank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { compactValues } from "../../fe/Object"
import { toS } from "../../fe/toS"
import { ExifTags } from "./ExifTags"

export function extractTitleDescription(t: ExifTags) {
  // from https://exiftool.org/forum/index.php?topic=1464.msg6370#msg6370

  // Olympus sometimes adds OLYMPUS DIGITAL CAMERA as the ImageDescription, but
  // also includes that string as the "CameraID". In any event, if the title or
  // description matches the camera id, or "OLYMPUS DIGITAL CAMERA", it's not
  // valid, and we should look elsewhere for the title or description.

  const not = uniq([
    "OLYMPUS DIGITAL CAMERA",
    t.original?.Make,
    t.original?.Model,
    t.Model,
    t.Make,
    t.CameraID
  ]).map(ea => mapNotBlank(ea, s => s.trim().toLowerCase().normalize()))

  const first = (...fieldNames: (keyof Tags)[]): Maybe<string> => {
    for (const fieldName of fieldNames) {
      const s = toS(t[fieldName]).trim()
      if (notBlank(s) && !not.includes(s.toLowerCase().normalize())) {
        return s
      }
    }
    return
  }

  return compactValues({
    title: first("XPTitle", "Title", "ObjectName"),
    description: first(
      "XPSubject",
      "Description",
      "ImageDescription",
      "Caption-Abstract"
    )
  })
}
