import { li, tt } from "../../fe/BabyMarkdown"
import { minuteMs } from "../../fe/Date"
import { defer } from "../../fe/Defer"
import { Link } from "../../fe/HealthChecks"
import { userDesc } from "../UserInfo"
import { libraryDirPosixFile, setupLibraryDirs_ } from "../dir/LibraryDirs"
import { errorToS } from "../error/Error"
import { isWin } from "../platform/Platform"
import { isDocker } from "../platform/isDocker"
import { Settings } from "../settings/Settings"
import { libraryHasSettings } from "../settings/SettingsIO"
import { HealthCheck, HealthCheckTest } from "./HealthCheck"
import { DockerVolumeLink } from "./LibraryAdminLinks"

export const libraryDirectoriesCheck = defer(() =>
  HealthCheck.for({
    section: "Library",
    id: "library-directories",
    ordinal: 0,
    pendingMsg: "Checking library directories",
    settings: ["libraryDir", "originalsDir", "previewsDir", "cacheDir"],
    ttlMs: minuteMs,
    links: [
      {
        text: `What's a "PhotoStructure library?"`,
        icon: "docs",
        url: "https://photostructure.com/faq/library/"
      },
      ...(isDocker()
        ? [
            {
              text: `Read how to setup up Docker`,
              icon: "docs",
              url: "https://photostructure.com/server/photostructure-for-docker/"
            }
          ]
        : [])
    ] as Link[],

    okLinks: [
      {
        type: "button",
        text: "Run library maintenance jobs",
        url: "/admin/run-maintenance",
        method: "POST",
        icon: "handyman"
      }
    ],
    later: isDocker() ? testDockerLibraryDir : testLibraryDir
  })
)

async function verifyLibraryDirs_(): Promise<HealthCheckTest> {
  const dirs = await setupLibraryDirs_()
  if (libraryHasSettings()) {
    return {
      level: "ok",
      msg: [
        "Library directories are OK",
        `Verified directory permissions for the following directories:`,
        li(...dirs.map(d => tt(d)))
      ]
    }
  } else {
    return {
      level: "no-library",
      msg: ["Your PhotoStructure library hasn't been set up yet"]
    }
  }
}

async function testDockerLibraryDir(): Promise<HealthCheckTest> {
  const libraryDir = libraryDirPosixFile()
  if (libraryDir == null) {
    return {
      level: "error", // < we can't recover from this. They didn't set up the bind mount correctly.
      msg: [
        `Something's amiss: ${tt("PS_LIBRARY_DIR")} is blank`,
        `The default is ${tt(
          Settings.libraryDir.toEnvLine(Settings.libraryDir.defaultValue)
        )}`
      ]
    }
  }

  // We can't just mkdir the library dir--it has to be a bind mount.
  try {
    if (!(await libraryDir.isReadWriteExecutable_())) {
      return {
        level: "error",
        msg: [
          `Your library directory, ${tt(libraryDir)}, is not read/write${
            isWin ? "" : "/execute"
          } by ${await userDesc()}`,
          "Please fix the permissions for this bind mount and restart the container."
        ],
        links: [DockerVolumeLink]
      }
    }
    return await verifyLibraryDirs_()
  } catch (error: any) {
    return {
      level: "error",
      msg: [
        `Your library directory, ${tt(
          libraryDir
        )}, can't be set up for ${await userDesc()}.`,
        errorToS(error, { maxLen: 80 }),
        "Please fix the permissions for this bind mount and restart the container."
      ],
      links: [DockerVolumeLink]
    }
  }
}

async function testLibraryDir(): Promise<HealthCheckTest> {
  const libraryDir = libraryDirPosixFile()
  if (libraryDir == null) {
    return {
      level: "no-library",
      msg: "No PhotoStructure library is open"
    }
  }

  // if we got to here, the directory is either missing or is read/write.
  // If we have settings, we're all good:
  if (!libraryHasSettings()) {
    return {
      level: "no-library",
      msg: [
        "No PhotoStructure library is open",
        Settings.libraryDir.hasValue()
          ? tt(Settings.libraryDir.toEnvLine())
          : undefined
      ]
    }
  }
  try {
    if (!(await libraryDir.isReadWriteExecutable_())) {
      return {
        level: "no-library",
        msg: [
          `Your library directory, ${tt(libraryDir)}, is not read/write${
            isWin ? "" : "/execute"
          } by ${await userDesc()}`,
          "Please fix the permissions or pick a new library directory."
        ]
      }
    }
    return await verifyLibraryDirs_()
  } catch (error: any) {
    return {
      level: "no-library",
      msg: [
        `Your library directory, ${tt(
          libraryDir
        )}, can't be set up for ${await userDesc()}`,
        errorToS(error, { maxLen: 80 }),
        "Please fix the permissions for this bind mount and restart the container."
      ]
    }
  }
}
