import tz_lookup from "@photostructure/tz-lookup"
import { defaultVideosToUTC, ExifDateTime, Tags } from "exiftool-vendored"
import { Zone } from "luxon"
import { lazy } from "../../core/Lazy"
import { compactBlanks } from "../../fe/Array"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { toDated } from "../date/Dated"
import { datedToISO } from "../date/FuzzyDate"
import { parseDateTime } from "../date/FuzzyDateParser"
import {
  isValidZone,
  normalizeZone,
  normalizeZoneOffsetMinutes
} from "../date/Timezone"
import { isValidDate } from "../date/ValidDate"
import { PosixFile } from "../fs/PosixFile"
import { validLat, validLon } from "../GeoHash"
import { mkLogger } from "../Logger"
import { eqlSubset } from "../Object"
import { Settings } from "../settings/Settings"
import { bname } from "./Bname"
import { DefaultCapturedAtTagsConst } from "./DefaultCapturedAtTags"
import { nearestSiblingTzOffset } from "./TagInference"

const logger = lazy(() => mkLogger("tags.InferTimezone"))

export async function maybeInferTimezone(
  f: PosixFile,
  t: Tags,
  skipSiblingInference: boolean
): PromiseMaybe<Pick<Tags, "tz" | "tzSource">> {
  return logger().tap({
    msg: "maybeInferTimezone()",
    result: await _maybeInferTimezone(f, t, skipSiblingInference)
  })
}

async function _maybeInferTimezone(
  f: PosixFile,
  t: Tags,
  skipSiblingInference: boolean
): PromiseMaybe<Pick<Tags, "tz" | "tzSource">> {
  {
    // If we have GPS, let that override everything else.
    const zone = zoneFromLatLon(t)
    if (zone != null && isValidZone(zone)) {
      const result = {
        tz: zone.name,
        tzSource: "GPSLatitude/GPSLongitude"
      }
      return eqlSubset(result, t) ? undefined : result
    }
  }

  if (isValidZone(t.tz) && t.tzSource !== defaultVideosToUTC) {
    return
  }

  if (Settings.inferTimezoneFromDatestamps.valueOrDefault) {
    for (const tagname of compactBlanks([
      ...Settings.capturedAtTags.values,
      ...Settings.capturedAtTagsFallback.values
    ])) {
      const edt = (t as any)[tagname]
      // some timestamps include "+00:00" _but they don't mean it_
      if (
        edt instanceof ExifDateTime &&
        isValidZone(edt.zone) &&
        edt.zone !== "UTC"
      ) {
        return {
          tz: edt.zone,
          tzSource: "infer:tag." + tagname + ".zone"
        }
      }
    }
  }
  if (!skipSiblingInference) {
    const result = inferTzOffsetMinutesFromFilename(f, t)
    if (result != null) return result
  }
  if (!skipSiblingInference) {
    const result = await nearestSiblingTzOffset(f)
    if (result != null) {
      return {
        tz: result.zoneName,
        tzSource: "infer:nearestSibling:" + result.base
      }
    }
  }
  return
}

export function zoneFromLatLon(
  tags: Pick<Tags, "GPSLatitude" | "GPSLongitude"> = {}
): Maybe<Zone> {
  const lat = tags.GPSLatitude
  const lon = tags.GPSLongitude
  if (!validLat(lat) || !validLon(lon)) {
    return logger().tap({
      msg: "zoneFromLatLon(): invalid lat/lon",
      result: undefined,
      meta: {
        lat,
        lon
      }
    })
  }
  try {
    const zoneName = tz_lookup(lat, lon)
    const zone = normalizeZone(zoneName)
    return logger().tap({
      msg: "zoneFromLatLon()",
      result: zone,
      meta: {
        lat,
        lon,
        zoneName
      }
    })
  } catch (error) {
    return logger().tap({
      msg: "zoneFromLatLon(): invalid lat/lon",
      result: undefined,
      meta: {
        lat,
        lon,
        error
      }
    })
  }
}

export type CapturedAtTags = Pick<
  Tags,
  Exclude<(typeof DefaultCapturedAtTagsConst)[number], "photoTakenTime">
>

export function inferTzOffsetMinutesFromFilename(
  f: PosixFile,
  tags: Pick<Tags, "tzSource" | "tz"> & CapturedAtTags
): Maybe<{ tz: string; tzSource: string }> {
  const basenameDT = parseDateTime([bname(f.name, true), bname(f.name, false)])
  if (basenameDT == null || !isValidDate(basenameDT)) {
    return
  }

  // It's unclear if the duration always perturbs the filename.

  // const durationMin = Math.round((extractDurationSec(tags) ?? 0) / 60)
  // if (durationMin > 0) {
  //   basenameDT = basenameDT.minus({ minutes: durationMin })
  // }

  for (const tagname of Settings.capturedAtTags.values) {
    const dt = toDated((tags as any)[tagname])
    if (dt == null || isValidDate(dt)) continue

    const rawTzoffsetMinutes = Math.round(
      (basenameDT.diff(dt, "minutes").as("minutes") * 15) / 15
    )
    // Sometimes the filename is off by a minute or two, so rawTzoffsetMinutes is ~7 or something.
    const normalizedOffsetMinutes =
      normalizeZoneOffsetMinutes(rawTzoffsetMinutes)

    // If the offset minutes is 0, we can't safely assume it was taken in UTC
    // (some tags are always stored in UTC, regardless of the
    // local-at-capture-time timezone)
    const z =
      normalizedOffsetMinutes === 0
        ? undefined
        : normalizeZone(normalizedOffsetMinutes)
    if (z != null) {
      return logger().tap({
        msg: "inferTzOffsetMinutesFromFilename(" + f.name + ")",
        result: {
          tz: z.name,
          tzSource: "infer:filename (diff with " + tagname + ")"
        },
        meta: {
          dt: datedToISO(dt),
          rawTzoffsetMinutes,
          normalizedOffsetMinutes
        }
      })
    }
  }
  return
}

