import { Tags } from "exiftool-vendored"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { clamp, mapNumeric, toInt } from "../../fe/Number"
import { Settings } from "../settings/Settings"
import { JsonSidecarTags } from "./JsonSidecar"

export function clampRating(rating: any): Maybe<number> {
  const r = toInt(rating)
  return r == null ? undefined : clamp(-1, 5, r)
}

export function extractRating(
  tags: Maybe<Tags & Pick<JsonSidecarTags, "favorited">>
): Maybe<number> {
  return (
    clampRating(tags?.Rating) ??
    mapNumeric(toInt(tags?.RatingPercent), pct => Math.ceil((pct * 5) / 100)) ??
    // Only assert a rating if the JSON sidecar is favorited:
    map(tags?.favorited, ea =>
      ea === true ? Settings.likeRating.valueOrDefault : undefined
    )
  )
}

export function extractLiked(tags: Maybe<Tags>): Maybe<boolean> {
  const r = extractRating(tags)
  return r == null ? undefined : r >= Settings.likeRating.valueOrDefault
}
