import {
  PrimitiveRevision,
  RevisionValue,
  SetAddRevision,
  SetDeleteRevision
} from "../../core/tags/MergedTags"
import { stringify } from "../../fe/JSON"
import { Maybe } from "../../fe/MaybeTypes"
import { parseJSON } from "../../fe/ParseMaybe"
import { Model } from "./Model"

export class AssetRevision extends Model {
  static override readonly $tableName = "AssetRevision"

  static insert(
    ...arr: (Omit<
      PrimitiveRevision | SetAddRevision | SetDeleteRevision,
      "createdAt"
    > & {
      assetId: number
    })[]
  ) {
    return AssetRevision.ops().insert(
      arr.map(obj => ({
        assetId: obj.assetId,
        createdAt: Date.now(),
        field: obj.field,
        op: (obj as any).op,
        _priorValueJson: stringify((obj as any).priorValue),
        _newValueJson: stringify((obj as any).newValue)
      }))
    )
  }

  assetId?: number
  createdAt!: number
  field!: string
  op?: "add" | "delete"
  _priorValueJson?: string | null
  _newValueJson?: string | null

  get priorValue(): Maybe<RevisionValue> {
    return parseJSON(this._priorValueJson)
  }

  get newValue(): Maybe<RevisionValue> {
    return parseJSON(this._newValueJson)
  }
}
