import _sharp from "sharp"
import { inspect } from "util"
import { lazy } from "../../core/Lazy"
import {
  Dimensions,
  dimSwap,
  dmegapixels,
  isPortrait
} from "../../fe/Dimensions"
import { ImageSizeName } from "../../fe/ImageSizes"
import { Maybe } from "../../fe/MaybeTypes"
import { megapixels } from "../../fe/Units"
import { greatestBy } from "../Array"
import { Settings } from "../settings/Settings"
import { Fit, OutputSize, Reducer, Square } from "./Reducers"
import { sharpRenderOptions } from "./Sharp"

// https://www.bhphotovideo.com/FrameWork/charts/resolutionChartPopup.html
// says 2240 x 1680, or 4 Megapixels, is "Photo Quality" 8x10 prints. That's
// ~210 pixels per inch, which is fine for some continuous tone images, but
// high-contrast line art should see 300 at least.

// We want to render assets on a 5k display quickly.

// If we can cheaply stream the original file, we don't need to save a
// 5K UHD-resized version.

// "cheaply stream" requires the following to be true:

// * not RAW
// * not enormous (> 20MP)

// x2 image resolutions results in a 20MB, 318 file previews directory.
// x3 image resolutions results in a 17MB, 210 file previews directory.

// Possible resize strategies:

// megapixel-based (.5MP, 1MP, 2MP, 3MP, ...)

// file size based (2k, 8k, 32k, 128k, ...)

// use popular display sizes, k-means grouping with 4 sizes

// there are *many* display resolutions from 720p through 5k--we'll get it
// wrong--so what are we trying to do here?

// * We want to make the UI fast by reducing the bytes needed to render a page,
//   so we want different sizes of previews.

// * We want to minimize the work the browser and webserver need to do to per
//   request, so if we're going to resize previews, it needs to be < 100ms of
//   work per image.

// * We want a previews directory that is only as large as it needs to be.

// We could probably serve smaller image resizes on the fly if the source image
// was small enough (< 2 MP)? But that won't save much disk space.

// A page of several hundred thumbnails would crush the webserver's CPU if the
// per-request expense is sufficiently high.

// Each halving of width quarters the size of the image, of course, so size
// drops geometrically.

// https://docs.google.com/spreadsheets/d/18j8oBFwPu9lwQKk2RgpeDIZ5k2bpb5WF7rbHFQwP_4s/edit?usp=sharing

// Display sizes:
// 1136  640: iPhone SE
// 1280  720: HD, cheap android phones
// 1334 x 750: iPhone 6, iPhone 7
// 1920 x 1080: FHD: TVs, iPhone 6 Plus, iPhone 7 Plus, Google Pixel, Nexus 5x, Chromebooks
// 2048 x 1536: iPads, Nexus 9
// 2436 x 1125: iPhone X
// 2560 x 1440: QHD: Nexus 6P, Google Pixel XL, Samsung Note 5
// 3120 x 1440: OnePlus 7 Pro
// 2560 x 1600: 13" Retina Macbook
// 2880 x 1440: QHD+: Pixel 2 XL
// 2732 x 2048: iPad Pro
// 2880 x 1800: 15" Retina Macbook
// 3840  2160: UHD 4k monitor
// 5120  2880: iMac 5k

// Camera resolutions:
// First(ish) camera with selfie cam: iPhone 4: front: VGA (480P) rear: 2592x1936

// srcset only works with widths, so images fit to common native screen
// resolutions will most likely not be the native resolution width, hence the
// name

// https://en.wikipedia.org/wiki/Graphics_display_resolution
// https://en.wikipedia.org/wiki/Pixel#/media/File:Sensoraufl%C3%B6sungen.svg

export class ImageSize {
  static readonly #sizes: ImageSize[] = []

  static sq(): ImageSize[] {
    return this.#sizes.filter(ea => ea.reducer === Square)
  }
  static largestSq(): ImageSize {
    return greatestBy(this.sq(), ea => ea.megapixels())!
  }

  static fit(): ImageSize[] {
    const arr = Settings.previewResolutions.valueOrDefault
    return this.#sizes.filter(ea => ea.reducer === Fit && arr.includes(ea.name))
  }
  static largestFit(): ImageSize {
    return greatestBy(this.fit(), ea => ea.megapixels())!
  }

  static all(): ImageSize[] {
    return [...this.#sizes]
  }

  // Landscape fullscreen native resolution sizes:

  // These images are fit to these boundaries without any cropping

  // static readonly UHD5K = new ImageSize("uhd5k", 5120, 5120, Fit)

  // fit-* files are reduced in size until the entire image fits in the given
  // dimensions. No cropping is done.

  // Given that we always serve the original as a width, UHD and QHD isn't
  // needed. FHD is only used to reduce network load.

  // 5K UHD: (14.7 MP)
  // UHD: (8.3 MP) ~1MiB
  // FHD: (2 MP) ~500KiB
  // FWXGA: (1.2 MP) ~300K
  // HD: (.9 MP) ~100K
  // SD: (.3 MP) 46K
  // w240 (.1 MP) 13K

  // These aren't rotational (as of 2020):
  static readonly UHD8k = new ImageSize("uhd8k", 7680, 4320, Fit, false)
  static readonly UHD5k = new ImageSize("uhd5k", 5120, 2880, Fit, false) // < 15 MP

  // As of 2020, 4k displays are *normally* landscape.
  static readonly UHD = new ImageSize("uhd4k", 4096, 2160, Fit)

  // Flagship smartphone screens. Rotational.
  static readonly QHD = new ImageSize("qhd", 3120, 1440, Fit)

  // 1920x1080 is 1080p. fit to 1920x1920 to fill a 1080p display (most smartphones)
  // Only one of these will be built, and it depends on the orientation of the image:
  static readonly FHD = new ImageSize("fhd", 1920, 1080, Fit)

  // FWXGA is so close to FHD, just send them the fhd and save time and space.
  // // 1366x768 (FWXGA) is (at least as of 2018) the most popular screen
  // // resolution, according to
  // // http://gs.statcounter.com/screen-resolution-stats/desktop/worldwide
  // but 720 video is 720x1280, so this makes a better poster image:
  static readonly HD = new ImageSize("hd", 1280, 720, Fit)

  // 720x480 is WVGA or 480p (aka "EDTV"). Presumably will be used if the window
  // isn't full screen.
  static readonly WVGA = new ImageSize("wvga", 720, 480, Fit)

  // old videos are 320x240, so 256 is slightly too big, and made the thumbnails horrible.
  static readonly QVGA = new ImageSize("qvga", 320, 240, Fit)

  // 160x100 is the wonderfully descriptive QQVGA standard.
  static readonly QQVGA = new ImageSize("qqvga", 160, 120, Fit)

  // Anything smaller than qqvga is silly, because sending <1k JPEGs vs ~1.5k
  // JPEGs, it's still just one TCP packet. Note that each additional file
  // also makes scanning the previews directory slower.

  // sq-* are constrained to a square:

  // Typical file sizes:
  // sq-w480: ~40-100k
  // sq-w240: 10-30k
  // sq-w120: 4-8k
  // sq-w60: 1-3k

  static readonly S480 = new ImageSize("s480", 480, 480, Square)
  static readonly S240 = new ImageSize("s240", 240, 240, Square)
  static readonly S120 = new ImageSize("s120", 120, 120, Square)
  static readonly S60 = new ImageSize("s60", 60, 60, Square)

  // See the note about about f80 being silly.

  // static readonly S32 = new ImageSize("s32", 32, 32, Square, "s64")

  readonly max: Dimensions

  private constructor(
    readonly name: ImageSizeName,
    readonly maxWidth: number,
    readonly maxHeight: number,
    readonly reducer: Reducer,
    readonly rotational = true
  ) {
    this.max = { width: maxWidth, height: maxHeight }
    ImageSize.#sizes.push(this)
  }

  [inspect.custom]() {
    return {
      ctor: "ImageSize",
      name: this.name + " (" + this.reducer.name + ")",
      maxDim: this.maxWidth + "" + this.maxHeight,
      maxMP: this.megapixels()
    }
  }

  get aspectRatio() {
    return this.maxWidth / this.maxHeight
  }

  get maxPixels() {
    return this.maxWidth * this.maxHeight
  }

  get minDimension() {
    return Math.min(this.maxWidth, this.maxHeight)
  }

  readonly megapixels = lazy(() => megapixels(this.maxWidth * this.maxHeight))

  outputSize(inputSize: Dimensions): Maybe<OutputSize> {
    return this.reducer.reduce(
      this.rotational && isPortrait(inputSize) ? dimSwap(this.max) : this.max,
      inputSize
    )
  }

  resize(outputSize: Dimensions, s: _sharp.Sharp): _sharp.Sharp {
    // SITS: SHARP TYPINGS ARE CRAP
    s = (s as any).resize({
      ...outputSize,
      fit: this.reducer.fit,
      withoutEnlargement: true
    })

    return s
  }

  toJpeg({
    path,
    sh,
    outputSize
  }: {
    path: string
    sh: _sharp.Sharp
    outputSize: Dimensions
  }): Promise<_sharp.OutputInfo> {
    // we don't need to add colorspace or metadata, as sRGB and tag stripping is
    // on by default.

    const mp = dmegapixels(outputSize)
    if (mp < 2 || Settings.previewSharpen.valueOrDefault) {
      sh = sh.sharpen()
    }
    return sh.jpeg(sharpRenderOptions()).toFile(path)
  }

  toString() {
    return this.name
  }
}
