import { mapNotEmpty } from "../../fe/Array"
import { minuteMs } from "../../fe/Date"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { compressWhitespace } from "../../fe/String"
import { AssetTag } from "./AssetTag"
import { Model } from "./Model"

export class ChangedTag extends Model {
  static override readonly $tableName = "ChangedTag"
  tagId!: number
  updatedAt?: number

  static upsertChangedTagIds(tagIds: number[]) {
    const updatedAt = Date.now()
    return mapNotEmpty(
      tagIds.filter(gt0).map(tagId => ({ tagId, updatedAt })),
      arr =>
        ChangedTag.dbl.runf(q =>
          q.insert(arr).onConflict("tagId").merge({ updatedAt })
        )
    )
  }

  static onAssetRemoval(assetId: number) {
    const tagIds = AssetTag.dbl.pluckAllf<number>(q =>
      q.select("tagId").where({ assetId })
    )
    return ChangedTag.upsertChangedTagIds(tagIds)
  }

  static getAllChangedTags(lagtimeMs?: number): ChangedTagPojo[] {
    const updatedAt = Date.now() - (lagtimeMs ?? minuteMs)
    return ChangedTag.dbl.all({
      sql: compressWhitespace(
        " WITH RECURSIVE ancestors(id) AS (",
        "   SELECT",
        "     tagId",
        "   FROM",
        "     ChangedTag",
        "   WHERE",
        "     updatedAt <= " + updatedAt,
        "   UNION",
        "   SELECT",
        "     Tag.parentId",
        "   FROM",
        "     Tag,",
        "     ancestors",
        "   WHERE",
        "     Tag.id = ancestors.id",
        " )",
        " SELECT DISTINCT",
        "   id as tagId,",
        "   ct.updatedAt as updatedAt",
        " FROM",
        "   ancestors",
        " LEFT JOIN ChangedTag AS ct on ct.tagId = ancestors.id"
      )
    }) as any
  }

  static deleteChangedTag(t: Maybe<ChangedTagPojo>) {
    return t == null || !gt0(t.tagId)
      ? undefined
      : ChangedTag.dbl.runf(q => q.delete().where(t))
  }
}

export type ChangedTagPojo = Pick<ChangedTag, "tagId" | "updatedAt">
