import { Command } from "commander"
import { CommandPlugin } from "../../core/cli/CLI"
import { disableAllFilters } from "../../core/settings/MetaSettings"
import { isFalse } from "../../fe/Boolean"
import { Obj } from "../../fe/Object"

/**
 * Used by sync and sync-file
 */
export const NoFilterArg: CommandPlugin = {
  beforeParse: (cmd: Command) =>
    cmd.option(
      "--no-filter",
      "Disables import filters. All paths will try to be imported, even if they are too small or are missing tags. See https://photostructure.com/faq/why-is-my-file-missing/ ."
    ),
  afterParse: async (opts: Obj) => {
    if (isFalse(opts.filter)) {
      disableAllFilters()
    }
  }
}
