import { SortedSet } from "./SortedSet"

export class BoundedGreatestSet<T> {
  readonly sortedArray: SortedSet<T>

  constructor(
    readonly maxLength: number,
    readonly toValue: (t: T) => number
  ) {
    this.sortedArray = new SortedSet(toValue)
  }

  toArray() {
    this.vacuum()
    return this.sortedArray.store
  }

  vacuum() {
    return this.sortedArray.retainLastN(this.maxLength)
  }

  add(...arr: T[]) {
    this.sortedArray.addAll(...arr)
    if (this.sortedArray.length >= 2 * this.maxLength) this.vacuum()
  }
}
