export class AssetFile
  extends TimestampedModel
  implements IAssetFile, PreviewAssetFile
{
  static override readonly $tableName = "AssetFile"
  static override readonly $uniqueColumnName = "uri"
  static override readonly $booleanFields = ["shown"] as const
  static override readonly $transientFields = [
    "nativePath",
    "syncState"
  ] as const

  static outdatedQuery(args: {
    q?: Knex.QueryBuilder
    maxUpdatedAt?: number
    uris: string[]
    mountpoints: string[]
  }) {
    let q = (args.q ?? this.query()).where("version", "<", AssetFileVersion)
    if (args.maxUpdatedAt != null) {
      q = q.andWhere("updatedAt", "<", args.maxUpdatedAt)
    }
    return q
      .andWhere(q2 => {
        if (isNotEmpty(args.mountpoints)) {
          q2 = q2.whereIn("mountpoint", args.mountpoints)
        }
        for (const ea of args.uris) {
          q2 = q2.orWhereLike("uri", ea + "%")
        }
        return q2
      })
      .orderBy("id")
  }

  static async nextOutdated(
    q: (qb: Knex.QueryBuilder) => Knex.QueryBuilder = identity
  ) {
    return this.ops().findOne(q(this.outdatedQuery(await currentVolumeRoots())))
  }

  static async outdatedCount(
    q: (qb: Knex.QueryBuilder) => Knex.QueryBuilder = identity
  ): Promise<number> {
    const qb = this.outdatedQuery(await currentVolumeRoots())
    return this.dbl.pluckFirst(q(qb.count("id"))) as any // SITS TYPING
  }

  static shownAssetFileId(assetId: number) {
    return this.dbl.pluckFirstf<number>(q =>
      q.where({ assetId, shown: 1 }).limit(1)
    )
  }

  static primaryForAssetId(assetId: Maybe<number>) {
    return !gt0(assetId)
      ? undefined
      : AssetFile.ops().first(
          Asset.shownUnhidden()
            .join("AssetFile", "AssetFile.assetId", "Asset.id")
            .select("AssetFile.*")
            .where({ assetId, "AssetFile.shown": 1 })
        )
  }

  static shownUnhidden({
    uriRoot
  }: { uriRoot?: string } = {}): Knex.QueryBuilder {
    let q = Asset.shownUnhidden().join(
      "AssetFile",
      "AssetFile.assetId",
      "Asset.id"
    )
    if (!blank(uriRoot)) {
      q = q.andWhereLike("AssetFile.uri", uriRoot + "%")
    }
    return q
  }

  /**
   * Most recent 64 assetURIs, mapped to their asset id. Given we need at most
   * 32 per progress bar, and there may be the end of one progress and the start
   * of another, 64 should do it.
   */
  // static readonly uri2assetId = new BoundedMap<string, number>(
  //   5 * minuteMs,
  //   64,
  //   true
  // )
  // static clear() {
  //   this.uri2assetId.clear()
  // }
  static recentAssetIdsByUriRoot(
    uriRoot: Maybe<string>,
    ttlMs: number,
    limit = 64
  ): number[] {
    const since = Date.now() - ttlMs
    return this.dbl.pluckAll(
      AssetFile.shownUnhidden({ uriRoot })
        .distinct("AssetFile.assetId")
        .where("AssetFile.updatedAt", ">", since)
        .orderBy("AssetFile.updatedAt", "desc")
        .limit(limit)
    ) as number[]
  }

  static assetFileCountByMimeType(
    uriRoot?: string
  ): { mimetype: string; assetFileCount: number; assetCount: number }[] {
    const qb = AssetFile.shownUnhidden({ uriRoot })
      .select("mimetype")
      .countDistinct({
        assetFileCount: "AssetFile.id",
        assetCount: "Asset.id"
      })
      .groupBy("mimetype")
      .orderBy("mimetype")
    return this.dbl.all(qb) as any
  }

  static assetFileCountWithMimeType(
    mimeRootOrType: string,
    uriRoot?: string
  ): { assetFileCount: number; assetCount: number } {
    let qb = AssetFile.shownUnhidden({ uriRoot }).countDistinct({
      assetFileCount: "AssetFile.id",
      assetCount: "Asset.id"
    })
    qb = mimeRootOrType.endsWith("/")
      ? qb.andWhereLike("mimetype", mimeRootOrType + "%")
      : qb.andWhere("mimetype", mimeRootOrType)
    return this.dbl.first(qb) as any
  }

  /**
   * Aggregate by "image/" and "video/"
   */
  static assetFileCountByMimeTypeRoots(
    mimeRootOrTypes: string[] = ["image/", "video/"],
    uriRoot?: string
  ): { mimeTypeRoot: string; assetFileCount: number }[] {
    return mimeRootOrTypes.map(mimeTypeRoot => ({
      mimeTypeRoot,
      assetFileCount:
        AssetFile.assetFileCountWithMimeType(mimeTypeRoot, uriRoot)
          ?.assetFileCount ?? 0
    }))
  }

  static children(
    uriParent: string,
    q?: (qb: Knex.QueryBuilder) => Knex.QueryBuilder
  ) {
    const qb = this.query()
      // the uri like allows the index to be used:
      .whereLike("uri", uriParent + "/%")
      .andWhere("uri", "regexp", `^${escapeRegExp(uriParent)}/[^/]+$}`)
    return this.ops().all(q == null ? qb : q(qb))
  }

  static async sameShaInLibrary(f: PosixFile): PromiseMaybe<AssetFile[]> {
    if (this.db() == null) return
    const sha = await f.sha()
    const fileSize = await f.size()
    if (sha == null || fileSize == null) return
    return this.ops().allf(q =>
      q.where({ sha, fileSize }).andWhereLike("uri", PS_LIBRARY_PROTOCOL + "%")
    )
  }

  static sameAssetFileInLibrary(af: AssetFile): AssetFile[] {
    return this.ops().allf(q =>
      q
        .where(pick(af, "sha", "fileSize"))
        .andWhere(q2 => q2.whereLike("uri", PS_LIBRARY_PROTOCOL + "%"))
        .andWhere(q2 => q2.whereNot({ id: af.id }))
    )
  }

  assetId?: number
  asset?: Asset

  /** shown is true iff it's the Asset's preview source */
  shown!: number | boolean

  uri!: string

  mountpoint?: string // < not set for pslib: URIs. Helps reconstitute URIs for unmounted volumes.

  mtime!: number
  fileSize!: number
  mimetype!: string
  sha!: string

  /**
   * deci-second resolution in YYYYMMddHHmmssSS
   */
  capturedAtLocal!: number
  capturedAtOffset?: number
  capturedAtPrecisionMs?: number
  capturedAtSrc!: string

  focalLength?: string // 35 mm
  aperture?: number // 4.8
  shutterSpeed?: string // 1/100 (does this need to be cleaned up?)
  iso?: number // 100
  durationMs?: number
  fps?: number

  width!: number
  height!: number
  rotation?: number

  make?: string
  model?: string
  cameraId?: string
  imageId?: string
  lensId?: string
  geohash?: number
  rating?: number

  /**
   * nullable because image hashing is optional
   */
  meanHash?: ImageHash["meanHash"]
  diffHash?: ImageHash["diffHash"]
  dctHash?: ImageHash["dctHash"]
  mode0?: number
  mode1?: number
  mode2?: number
  mode3?: number
  mode4?: number
  mode5?: number
  mode6?: number
  mode0pct?: number
  mode1pct?: number
  mode2pct?: number
  mode3pct?: number
  mode4pct?: number
  mode5pct?: number
  mode6pct?: number

  version!: number
  updateCount?: number

  // transient
  nativePath!: string
  syncState?: AssetFileSyncState

  // afterUpsert() {
  //   map(this.assetId, assetId =>
  //     map(this.uri, uri => AssetFile.uri2assetId.set(uri, assetId))
  //   )
  // }

  librarySiblings() {
    return this.getAsset()
      ?.getAssetFiles()
      .filter(ea => ea.id !== this.id && ea.uri.startsWith(PS_LIBRARY_PROTOCOL))
  }

  capturedAtLocale(locale?: string) {
    return fmtLocalDateShort(this.capturedAtLocal, locale)
  }

  get dominantColors(): DominantColor[] {
    return DominantColor.fromModeData(this)
  }

  set dominantColors(dominantColors: DominantColor[]) {
    // we want to assign both values and nulls here (in case there are fewer
    // than 7 dominant colors)
    assignAllFields(this as AssetFile, DominantColor.toModeData(dominantColors))
  }

  readonly uriObj = lazy(() => URI.parse(this.uri))

  get posixPathFromGrandparent() {
    return map(this.uriObj()?.fsPath, posixPathFromGrandparent)
  }

  /**
   * You probably want checkForNoop instead -- this only checks if this row in
   * the database looks OK.
   */
  async whyNotMatchesFile(file?: PosixFile): PromiseMaybe<string> {
    const outdated = this.#whyOutdated()
    if (outdated != null) return outdated

    if (this.fileSize == null || this.mtime == null) {
      return "missing file metadata"
    }

    if (file != null) {
      const fileUri = await file.uri()
      if (fileUri == null) {
        return "file has no URI"
      }
      if (!(await uriIsEquivalent(this.uri, fileUri))) {
        return "file URI mismatch"
      }
    }

    try {
      const pf = file ?? (await this.posixFile_())
      const ff = await this.fileFields_(pf)
      if (ff == null) {
        if (true === (await pf?.isDeleted())) {
          return "file is deleted"
        } else {
          return "file is unreadable"
        }
      }
      if (ff.fileSize !== this.fileSize) {
        return "file size changed since last sync"
      }
      if (!closeTo(ff.mtime, this.mtime, 2 * secondMs)) {
        return "file mtime changed since last sync"
      }
      return
    } catch (error) {
      return errorToS(error)
    }
  }

  async matchesFile(file?: PosixFile): Promise<boolean> {
    const whyNot = await this.whyNotMatchesFile(file)
    return this.logger().tap({
      msg: "matchesFile()",
      result: null == whyNot,
      meta: { whyNot }
    })
  }

  /**
   * Respects `forceSync` and AssetFileVersion, and then delegates to @link whyNotMatchesFile
   */
  async whyNotInSyncWithFile(file?: PosixFile): PromiseMaybe<string> {
    if (Settings.forceSync.valueOrDefault) {
      return "forceSync"
    }
    if (this.version == null || this.version < AssetFileVersion) {
      return "outdated AssetFileVersion"
    }
    return this.whyNotMatchesFile(file)
  }

  async inSyncWithFile(file?: PosixFile): Promise<boolean> {
    const whyNotInSync = await this.whyNotInSyncWithFile(file)
    return this.logger().tap({
      msg: "inSyncWithFile()",
      result: null == whyNotInSync,
      meta: { whyNotInSync }
    })
  }

  async fileFields_(file?: PosixFile): PromiseMaybe<AssetFileStatFields> {
    const pf = file ?? (await this.posixFile_())
    return pf == null ? undefined : await assetFileStatFields_(pf)
  }

  #whyOutdated() {
    if (blank(this.uri)) {
      return "blank uri"
    }

    if (blank(this.sha)) {
      return "blank sha"
    }

    if (!gt0(this.width)) {
      return "missing width"
    }

    if (!gt0(this.height)) {
      return "missing height"
    }

    if (!gt0(this.capturedAtLocal)) {
      return "missing capturedAtLocal"
    }

    if (!gte0(this.capturedAtPrecisionMs)) {
      return "missing capturedAtPrecisionMs"
    }

    if (
      Settings.useImageHashes.valueOrDefault &&
      (blank(this.meanHash) || blank(this.diffHash) || blank(this.dctHash))
    ) {
      return "missing image hashes"
    }

    if (!this.isVersionUpToDate()) {
      return "outdated version"
    }

    return
  }

  async isNoop(
    ctx: ForceContext = {
      forceSync: Settings.forceSync.valueOrDefault
    }
  ) {
    const whyNotNoop = await this.whyNotNoop(ctx)
    return this.logger().tap({
      msg: "isNoop()",
      result: null == whyNotNoop,
      meta: { whyNotNoop }
    })
  }

  /**
   * Verifies that both this row in the database is in sync with the current
   * running code, and that the file doesn't need to be copied into the
   * library (if that setting is enabled)
   */
  async whyNotNoop(
    ctx: ForceContext = {
      forceSync: Settings.forceSync.valueOrDefault
    }
  ) {
    if (true === ctx.forceSync) {
      return "forceSync is true"
    }

    // SQLite ROWID starts at 1: https://www.sqlite.org/autoinc.html
    if (!gt0(this.id)) {
      return "missing .id"
    }

    if (!gt0(this.assetId)) {
      return "missing .assetId"
    }

    const whyNotMatches = await this.whyNotMatchesFile()
    if (whyNotMatches != null) {
      return whyNotMatches
    }

    if (await mayCopyAssetsToLibrary()) {
      const repo = Library.instanceRequired().assetFileRepository()
      const why = repo.whyNotCopyFile(await this.posixFile_())
      if (blank(why)) {
        const af = await repo.existingLibraryAssetFile(this)
        if (af == null) {
          return "need to copy into library"
        }
      }
    }

    this.syncState = AssetFileSyncStates.noop
    return
  }

  isVersionUpToDate() {
    return gte(this.version, AssetFileVersion)
  }

  async siblingCount(): Promise<number> {
    return AssetFile.dbl.pluckFirstf<number>(q =>
      q.count().where({ assetId: this.assetId }).andWhereNot({ id: this.id })
    )
  }

  async upsertIfNeeded_(
    file?: PosixFile,
    forceSync = Settings.forceSync.valueOrDefault
  ): PromiseMaybe<this> {
    if (file != null) {
      await this.setFile_(file)
    }
    if ((await this.matchesFile()) && gt0(this.id) && !forceSync) {
      this.upsert() // < update the updatedAt
      this.syncState = AssetFileSyncStates.noop
      return
    }
    const isDeleted = await this.isFileDeleted()
    if (isDeleted === true) {
      this.syncState = AssetFileSyncStates.deleted
      this.delete()
      return this
    } else if (isDeleted === false) {
      const updated = await this.updateFromFile_(file)
      this.syncState ??=
        updated == null
          ? AssetFileSyncStates.skipped
          : AssetFileSyncStates.synced
      return this.upsert()
    } else {
      // we don't have this volume currently mounted.
      this.syncState ??= AssetFileSyncStates.skipped
      return this
    }
  }

  async updateFileFields_(file?: Maybe<PosixFile>) {
    assignFields(this as AssetFile, await this.fileFields_(file))
  }

  /**
   * NOTE: DOES NOT UPSERT. See `upsertIfNeeded()`.
   * @return if this assetFile should not be upserted (due to already being in sync, or source file missing)
   */
  async updateFromFile_(file?: PosixFile): PromiseMaybe<this> {
    const start = Date.now()
    const l = this.logger().addContext(".updateFromFile_()")
    if (this.id != null && (await this.matchesFile(file))) {
      l.info(
        "updateFromFile() no-op: up-to-date version and file stat matches since last update"
      )
      return
    }
    if (file != null) {
      await this.setFile_(file)
    }
    if (file == null) {
      file = await this.posixFile_()
    }

    // only decide we're a no-op if we flush cache and it's still not there:
    if (file == null || (await file.clear().notExists())) {
      this.syncState = AssetFileSyncStates.deleted
      l.info("no-op, " + file + " is missing")
      return
    }

    // this may throw!
    const fields = await mkAssetFile_(file)

    if (fields.uri !== this.uri) {
      // It's OK to upgrade the URI from file: but not vice versa.
      if (blank(this.uri) || true === this.uri?.startsWith("file:")) {
        this.logger().warn("updateFromFile() upgrading uri", {
          prior: this.uri,
          new: fields.uri
        })
        this.uri = fields.uri
      }
    }

    assignAllFields<AssetFile>(this, omit(fields, "uri"))

    this.version = AssetFileVersion
    this.syncState = SyncFileStates.synced

    l.elapsed("finished", Date.now() - start, this.toJSON())
    return this
  }

  /**
   * Skip exiftool and image hashing ops if the sibling is relevant and
   * up-to-date.
   */
  async updateFromShaSibling_(sibling: AssetFile) {
    const whySiblingIsInvalid = sibling.#whyOutdated()
    if (whySiblingIsInvalid != null) {
      this.logger().warn("updateFromShaSibling() sibling is invalid", {
        whySiblingIsInvalid
      })
      return
    }
    this.logger().debug("updateFromShaSibling", sibling)
    if (
      sibling.sha != null &&
      (await sibling.matchesFile()) &&
      this.sha === sibling.sha
    ) {
      assignFields(
        this as AssetFile,
        omit(
          sibling,
          "id",
          "posixFile",
          "uriObj",
          "shown",
          "uri",
          "mtime",
          "fileSize",
          "mountpoint",
          "createdAt",
          "updatedAt",
          "updateCount",
          ...AssetFile.$transientFields // includes nativePath
        )
      )
      return this.updateFileFields_()
    } else {
      return this.updateFromFile_()
    }
  }

  getAsset() {
    return (this.asset ??= gt0(this.assetId)
      ? Asset.ops().findById(this.assetId)
      : undefined)
  }

  exists(): Promise<boolean> {
    return uriExists(this.uri, this.mountpoint)
  }

  /**
   * True if the volume is currently mounted and this file is missing.
   */
  async isFileDeleted(): PromiseMaybe<boolean> {
    return (await this.posixFile_())?.isDeletedUri(this.uri)
  }

  whyRejected = lazy(async () => {
    // if the SHA is bad, we don't care about anything else:
    if (true === isShaBlockslisted(this.sha)) return "blocklisted SHA"
    const pf = await this.posixFile_()
    if (pf == null) return "invalid URI"
    if (true === (await this.isFileDeleted())) return "deleted"
    return whyRejectFile(pf)
  })

  async accepted() {
    return blank(await this.whyRejected())
  }

  async setFile_(file: PosixFile): Promise<this> {
    const uri = await file.uri_()
    if (uri == null) {
      return this.logger().throw("setFile(): no URI for " + file)
    }
    // THIS MAY BE VALIDLY NULL if file lives in the originals or library
    // directories!
    const mountpoint = await file.mountpoint()

    if (this.uri != null) {
      // Verify the prior URI is equivalent:
      if (!(await uriIsEquivalent(uri, this.uri))) {
        return this.logger().throw(
          "setFile(): cannot reassign non-equivalent URI",
          { prior: this.uri, new: uri, file: file.nativePath }
        )
      }
    }

    const u = await file.uriObject_()
    if (u == null) {
      return this.logger().throw("failed to create URI for " + file)
    }

    this.uri = uri
    this.uriObj.set(u)
    this.mountpoint = mountpoint?.nativePath
    this.nativePath = file.nativePath

    this.posixFile_.set(Promise.resolve(file))
    return this
  }

  get basename() {
    return blank(this.uri)
      ? undefined
      : decodeURIComponent(this.uri.slice(this.uri.lastIndexOf("/") + 1))
  }

  readonly posixFile_ = lazy(async () =>
    PosixFile.for(await this.getNativePath())
  )

  posixFile() {
    return this.posixFile_().catch(() => undefined)
  }

  async getNativePath() {
    if (blank(this.nativePath)) {
      // DON'T BE GRUMPY IF MOUNTPOINT IS NULL. mountpoint will be null for
      // library asset files!
      if (this.uri == null) {
        this.logger().error(
          ".getNativePath() called before .setFile_()" + InternalErrorFlag
        )
      } else {
        this.nativePath = await getAssetFileNativePath(this)
      }
    }
    return this.nativePath
  }

  async capturedAt() {
    if (this.nativePath == null) await this.getNativePath()
    return CapturedAt.fromAssetFile(this)
  }

  async originalDownloadable({
    brief = false
  }: { brief?: boolean } = {}): PromiseMaybe<Downloadable> {
    const basename = this.basename
    if (brief === true) {
      // GO FAST
      return basename == null
        ? undefined
        : {
            href: `/dl/${this.assetId}/${this.id}`,
            size: "original",
            title: `Download ${basename}`,
            basename,
            description: "Download original"
          }
    }
    const posixFile = await this.posixFile_()
    return this.logger().tap({
      msg: "originalDownloadable()",
      level: "info",
      result:
        posixFile == null || (await posixFile.isEmpty())
          ? undefined
          : {
              href: `/dl/${this.assetId}/${this.id}`,
              size: "original",
              title: brief
                ? `Download ${posixFile.base}`
                : mkDownloadableTitle(
                    posixFile,
                    "original",
                    this.isVideo ? "video" : "image",
                    { width: this.width, height: this.height }
                  ),
              basename: posixFile.base,
              description: "Download original",
              details: `(${fmtDim(this)} ${posixFile.ext})`
            },
      meta: {
        posixFile
      }
    })
  }

  async downloadables(
    previews: Previews,
    shownSha?: string
  ): Promise<Downloadable[]> {
    try {
      const posixFile = await this.posixFile_()
      if (posixFile == null) return []

      const ap = previews.ap(this.assetId!)

      // If we can't get dimensions, the file is missing

      const result: Downloadable[] = []
      await thenMap(this.originalDownloadable(), ea => result.push(ea))

      if (this.isVideo) return result

      const shown = isTrue(this.shown) || this.sha === shownSha

      if (!shown) return result

      const arr = compact(
        await Promise.all(
          [...(await ap.previewInfos())]
            .reverse()
            .filter(ea => ea.reducer === ReducerNames.fit)
            .map(ea => previewToDownloadable(posixFile.base, ea))
        )
      )

      result.push(
        ...uniqBy(
          arr,
          // arr.filter(ea => ["large", "small"].includes(ea.size)),
          ea => ea.size
        )
      )
      return result
    } catch (err) {
      onError("AssetFile.downloadables failed for " + this.uri, err)
      return []
    }
  }

  /**
   * @see AssetFilePathInfo
   */
  async pathInfo(): PromiseMaybe<AssetFilePathInfo> {
    try {
      const uri = URI.parse(this.uri)
      const tag = map(uriToTagPath({ uri, isFile: true }), tagPath =>
        Tag.findByPath(tagPath)
      )
      return this._pathInfo_(await tag?.toApiPathElements())
    } catch (err) {
      this.logger().warn("pathInfo(): failed", err)
      return
    }
  }

  /**
   * only exposed for tests
   */
  async _pathInfo_(
    tagLineage: Maybe<PathTag[]>
  ): PromiseMaybe<AssetFilePathInfo> {
    // See https://forum.photostructure.com/t/image-filepath-not-correct-synology-docker-compose-alpha-branch/545

    const posixFile = await this.posixFile_()
    if (posixFile == null) {
      return this.logger().throw("pathInfo(): no posixFile", {
        uri: this.uri
      })
    }

    // for pslib://, it'll be the path from the library.
    // for psfile://, it'll be the path from the mountpoint.

    const nativePath = posixFile.nativePath
    const u = URI.parse(this.uri)
    const exists = await posixFile.exists()

    if (tagLineage == null || isEmpty(tagLineage)) {
      this.logger().warn(
        "pathInfo(): failed to find existing Tag. Returning a simple path.",
        {
          uri: this.uri,
          nativePath,
          tagLineage
        }
      )
      return {
        nativePath,
        pathElements: [],
        nativePathSuffix: nativePath,
        pathSep: sep,
        exists
      }
    }

    // The first tag element is "fs", we don't need that:
    // (remember that slice returns a shallow copy of the array)
    if (eql(tagLineage[0]?.tagPath, [TagRoots.fs])) {
      tagLineage = tagLineage.slice(1)
    }

    function lastTagName() {
      return at(at(tagLineage, -1)?.tagPath, -1)
    }

    const isLibraryUri = u.scheme === PS_LIBRARY_SCHEME

    const filePaths = isLibraryUri
      ? u.path.split("/").slice(1)
      : posixFile.pathnames

    const nativePathSuffix: string = filePaths.pop()!

    const pathElements: PathTag[] = []

    // We expect almost all the paths to match, but there's shenanigans at the
    // beginning of the paths due to the volsha and the initial library path:
    while (
      filePaths.length > 0 &&
      equalsIgnoreCase(lastTagName(), at(filePaths, -1))
    ) {
      filePaths.pop()!
      const t = tagLineage.pop()!
      pathElements.unshift(t)
      this.logger().debug("added matching tag", t)
    }

    // fs/XXX tags can represent
    if (isLibraryUri && lastTagName() === LibraryTagName) {
      filePaths.pop()
      const t = tagLineage.pop()!
      pathElements.unshift(t)
      this.logger().debug("pslib: added matching library tag", t)
    }

    // Can we pop off the "root" of this volume?
    if (u.scheme === PS_LOCAL_FILE_SCHEME) {
      const thisVolSha = URI.parse(this.uri).authority
      this.logger().debug("psfile: pop last?", {
        lastTagName: lastTagName(),
        thisVolSha
      })
      if (lastTagName() === thisVolSha) {
        filePaths.pop()
        const t = tagLineage.pop()!
        pathElements.unshift(t)
      } else {
        this.logger().warn(
          "weird: I expected to be able to include the volsha tag element, but it doesn't seem to match",
          {
            thisVolSha,
            tagVolSha: lastTagName()
          }
        )
      }
    }

    return this.logger().tap({
      msg: "pathInfo()",
      result: {
        nativePath,
        pathElements,
        nativePathSuffix,
        pathSep: sep,
        exists
      }
    })
  }

  async toApi(
    previews: Previews,
    shownSha?: string
  ): PromiseMaybe<ApiAssetFile> {
    return thenMap(this.pathInfo(), async pi =>
      reqValuedOrElse({
        assetId: this.assetId,
        assetFileId: this.id,
        ...pi,
        mimetype: this.mimetype,
        shown: isTrue(this.shown),
        name: this.basename,
        width: this.width,
        height: this.height,
        rotation: orElse(this.rotation, 0),
        fileSize: this.fileSize,
        mtime: this.mtime,
        downloadables: await this.downloadables(previews, shownSha),
        createdAtLocale: fmtMillis(this.createdAt!),
        updatedAtLocale: fmtMillis(this.updatedAt!)
      })
    )
  }

  get isVideo(): boolean {
    return isVideoMimeType(this.mimetype)
  }
}

function fmtMillis(ms: Maybe<number>): Maybe<string> {
  return map(ms, ea => fmtDuration(Date.now() - ea, 1) + " ago")
}
