import { isDated } from "../../core/date/Dated"
import { datedToMillis } from "../../core/date/DatedToMillis"
import { includes } from "../../fe/Array"
import { entries, mapFields } from "../../fe/Object"

// SQLite can only store numbers or strings, but let's also take booleans to
// simplify constructors
export type DbValue = number | string | boolean | Date

export interface DbValued {
  [key: string]: DbValue | undefined | null
}

export function isDbValue(obj: any): obj is DbValue {
  return obj === null || includes(["number", "string"], typeof obj)
}

export function isDbValued(obj: any): obj is DbValued {
  return obj != null && Array.isArray(obj)
    ? obj.every(isDbValued)
    : entries(obj).every(([, value]) => isDbValue(value))
}

export function toDbValued(obj: any): DbValued {
  return mapFields(obj, (key, value) => {
    if (key.startsWith("$")) return
    if (typeof value === "boolean") return [key, value ? 1 : 0]
    if (isDbValue(value)) return [key, value]
    if (isDated(value)) return [key, datedToMillis(value)]
    return
  }) as DbValued
}
