import { Tags } from "exiftool-vendored"
import { includes } from "../../fe/Array"
import { blank, notBlank } from "../../fe/Blank"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { gt0, gte, lte, toInt } from "../../fe/Number"
import { toS } from "../../fe/toS"
import { allNotBlank } from "../Array"
import { thenMapOr } from "../async/Promise"
import {
  Predicate,
  PredicateBundle,
  Predicates
} from "../predicates/Predicates"
import { Settings } from "../settings/Settings"
import { extractDurationSec } from "../tags/Duration"
import { readRawTags, readTags } from "../tags/ExifTool"
import { rawTagKeywords } from "../tags/KeywordTags"
import { isAssetFileMimeType, isVideoMimeType } from "../tags/MimeTypes"
import { readMimeType } from "../tags/ReadMimeType"
import { extractSizeInfoFromFile } from "../tags/SizeInfo"
import { leafIsExcluded } from "../tags/TagPaths"
import { isBaseFile } from "./BaseFile"
import { isAssetFileExtension } from "./BaseFilePredicates"
import {
  isExcludedDirectory,
  notExcludedFilePredicates,
  whyExcludedDirectoryRecursive
} from "./Excluded"
import { PosixFile } from "./PosixFile"
import { SimpleFile } from "./SimpleFile"
import { exists } from "./Stat"

function mkTagFilter(f: (tags: Tags) => boolean): Predicate<PosixFile> {
  return async (file: PosixFile) =>
    thenMapOr(readRawTags(file, false), f, () => false)
}

export function requiredTagsFilter(
  requiredExifTags: (keyof Tags)[]
): Predicate<PosixFile> {
  return mkTagFilter((t: Tags) =>
    allNotBlank(...requiredExifTags.map(ea => t[ea]))
  )
}

export async function notDimensionsTooSmall(f: PosixFile) {
  if (
    !gt0(Settings.minVideoDimension.valueOrDefault) &&
    !gt0(Settings.minImageDimension.valueOrDefault)
  ) {
    return
  }
  const m = await readMimeType(f)
  if (m == null || (!m.startsWith("image/") && !m.startsWith("video/")))
    return false
  const min = m.startsWith("video/")
    ? Settings.minVideoDimension.valueOrDefault
    : Settings.minImageDimension.valueOrDefault
  if (!gt0(min)) return
  return thenMapOr(
    extractSizeInfoFromFile(f),
    dim => gte(dim.ImageWidth, min) && gte(dim.ImageHeight, min),
    () => false
  )
}

async function notVideoTooShort(f: PosixFile) {
  if (!gt0(Settings.minVideoDurationSec.valueOrDefault)) return
  const m = await readMimeType(f)
  if (m == null) return false
  if (!isVideoMimeType(m)) return
  const t = await readRawTags(f, false)
  if (t == null) return false
  return gte(extractDurationSec(t), Settings.minVideoDurationSec.valueOrDefault)
}

async function notVideoTooLong(f: PosixFile) {
  if (!gt0(Settings.maxVideoDurationSec.valueOrDefault)) return
  const m = await readMimeType(f)
  if (m == null) return false
  if (!isVideoMimeType(m)) return
  const t = await readRawTags(f, false)
  if (t == null) return false
  return lte(extractDurationSec(t), Settings.maxVideoDurationSec.valueOrDefault)
}

async function notMissingMimeType(f: PosixFile): Promise<boolean> {
  return !blank(await readMimeType(f))
}

// A rating of -1 is considered "rejected"
async function notRejectedRating(f: PosixFile): Promise<boolean | undefined> {
  if (Settings.rejectRatingsLessThan.valueOrDefault === -100) return undefined
  const t = await readTags(f)
  const rating = toInt(t?.Rating)
  return (
    rating == null || rating >= Settings.rejectRatingsLessThan.valueOrDefault
  )
}

// Any keywords matching excludedKeywords?
async function notExcludedKeyword(f: PosixFile): Promise<boolean | undefined> {
  if (Settings.keywordBlocklist.values.length === 0) return undefined
  const tagPaths = rawTagKeywords(await readTags(f))
  return tagPaths.every(
    ea => leafIsExcluded(ea, Settings.keywordBlocklist.values) !== true
  )
}

export const hasBrowserImgMimeType = mkTagFilter((t: Tags) =>
  includes(["image/jpeg", "image/png"], toS(t.MIMEType))
)

async function notMissingMakeAndModelTags(f: PosixFile) {
  const skip = !Settings.requireMakeModel.valueOrDefault
  if (skip) return
  const t = await readRawTags(f)
  if (t == null || blank(t.MIMEType)) return
  if (t.MIMEType.startsWith("image/")) {
    return t != null && notBlank(t.Make) && notBlank(t.Model)
  } else {
    return // not applicable
  }
}

export async function notFileMissing(file: SimpleFile) {
  return isBaseFile(file) ? file.exists() : exists(file.nativePath)
}

// Skip over files that are < 50kb or > 1 gb, assuming that > 1 gb files are
// downloaded movies. (1 gb is 10-20 minutes of FHD video, or > 5 minutes of
// UHD video). kb = 1e3, mb = 1e6, gb = 1e9

// If we're copying a file from a NAS to an external drive, we might be looking
// at ~5-10mb/s. Transferring 1.5gb @ 5 mb/s is 5 minutes.

// TODO: if we make asset import timeouts based on filesize, we can make this
// limit much larger.

// (Note that a 1600x1200 JPEG at 50% quality is ~150k, so 50k should be very
// conservative.)
export async function notFileTooSmall(
  file: SimpleFile
): Promise<boolean | null> {
  const min = Settings.minAssetFileSizeBytes.valueOrDefault
  if (min <= 0) return null // disabled
  const size = await file.size()
  return gte(size, min)
}

export async function notFileTooBig(file: SimpleFile): Promise<boolean | null> {
  const max = Settings.maxAssetFileSizeBytes.valueOrDefault
  if (max <= 0) return null
  const size = await file.size()
  return lte(size, max)
}

export function simpleFileFiltersFor(
  file: SimpleFile
): PredicateBundle<SimpleFile> {
  return [
    { isSupportedFileExtension: isAssetFileExtension },
    ...notExcludedFilePredicates(file),
    { notFileMissing },
    { notFileTooSmall, notFileTooBig }
  ] as PredicateBundle<SimpleFile>
}

export function whyRejectFileSimple(file: SimpleFile): PromiseMaybe<string> {
  return Predicates.whyRejected(file, ...simpleFileFiltersFor(file))
}

export function acceptFileSimple(file: SimpleFile): Promise<boolean> {
  return Predicates.accepted(file, ...simpleFileFiltersFor(file))
}

export async function acceptParentAndFileAndSimple(
  file: SimpleFile
): Promise<boolean> {
  const parent = await file.parent()
  return (
    parent != null &&
    !(await isExcludedDirectory(parent)) &&
    (await Predicates.accepted(file, ...simpleFileFiltersFor(file)))
  )
}

// NOTE 1: These are named to make PredicateNames.negateFilterName simple (so,
// "notMissingMimeType" becomes "missing mime type")

export function expensiveFileFiltersFor(
  file: PosixFile
): PredicateBundle<PosixFile> {
  return [
    ...simpleFileFiltersFor(file),
    { notMissingMimeType },
    { supportedMimeType: isAssetFileMimeType },
    { notMissingMakeAndModelTags },
    { notDimensionsTooSmall },
    { notVideoTooShort },
    { notVideoTooLong },
    { notRejectedRating },
    { notExcludedKeyword }
  ] as PredicateBundle<PosixFile> // SITS TYPING (why is the as cast necessary?)
}

export async function whyRejectFile(
  file: PosixFile,
  bundle?: PredicateBundle<PosixFile>
): PromiseMaybe<string> {
  return (
    // file predicate first as it may be the most specific rejection reason
    (await Predicates.whyRejected(
      file,
      ...(bundle ?? expensiveFileFiltersFor(file))
    )) ?? (await whyExcludedDirectoryRecursive(file.parent()))
  )
}

export async function acceptFile(file: PosixFile): Promise<boolean> {
  return null == (await whyRejectFile(file))
}
