import { isNotEmpty } from "../../fe/Array"
import { li } from "../../fe/BabyMarkdown"
import { fmtAgo, minuteMs } from "../../fe/Date"
import { defer } from "../../fe/Defer"
import { Timeout, thenOrTimeout } from "../../fe/ThenOrTimeout"
import { plur } from "../../fe/Units"
import { partition } from "../Array"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { isWin } from "../platform/Platform"
import { dfPosixRawLocal_ } from "../volumes/DfPosixRaw"
import { getLocalVolumesWin_ } from "../volumes/DfWin"
import { commandTimeoutMs } from "../volumes/VolumeTtls"
import { volumes } from "../volumes/Volumes"
import { HealthCheck } from "./HealthCheck"

export const volumeHealthCheck = defer(() =>
  HealthCheck.for({
    section: "System",
    id: "system-volumes",
    ordinal: 0,
    pendingMsg: "Checking volumes",
    settings: [],
    rejectIsWarn: true,
    // ttlMs: we're relying on mountpointsChanged to trigger a recheck
    links: [
      {
        text: `What's a volume?`,
        url: "https://photostructure.com/faq/what-is-a-volume/",
        icon: "docs"
      }
    ],
    warnLinks: isWin
      ? [
          {
            text: "Check this page for troubleshooting tips",
            url: "https://photostructure.com/faq/windows-troubleshooting/#unhealthy-network-shares"
          }
        ]
      : undefined,
    later: async () => {
      const start = Date.now()
      const vols = await thenOrTimeout(volumes(), commandTimeoutMs())
      if (vols === Timeout) {
        // are local volumes ok?
        const localVolumes = await thenOrTimeout<any>(
          isWin ? getLocalVolumesWin_() : dfPosixRawLocal_(),
          ShortCommandTimeoutMs
        )
        // try again after 5 minutes?
        volumeHealthCheck().logger.warn("volumes() timed out, retrying in 5m")
        setTimeout(() => volumeHealthCheck().refresh(), 5 * minuteMs)
        return {
          level: "warn",

          msg: [
            localVolumes === Timeout
              ? "Something seems wrong with your computer's volumes"
              : "Something seems wrong with one or more remote filesystem mounts",
            "Gathering volume metadata took longer than " +
              fmtAgo(start, "") +
              "."
          ]
        }
      }

      const notOk = vols.filter(ea => ea.ok === false)
      if (isNotEmpty(notOk)) {
        return {
          level: "warn",
          msg: [
            "One or more volumes are not healthy",
            "Check the following mountpoints:",
            li(notOk.map(ea => ea.mountpoint))
          ]
        }
      }

      const [remote, local] = partition(vols, ea => ea.remote === true)
      return {
        level: "ok",
        msg: [
          "Volumes seem OK",
          plur(local.length, "local volume") +
            " and " +
            plur(remote.length, "remote volume") +
            " checked."
        ]
      }
    }
  })
)
