import { lazy } from "../../core/Lazy"
import { notBlank } from "../../fe/Blank"
import { minuteMs } from "../../fe/Date"
import { later } from "../../fe/Delay"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { KB } from "../../fe/Units"
import { mkLogger } from "../Logger"
import { stdoutResult_ } from "../child/ChildProcess"
import { ee } from "../event/EventEmitter"
import { pathTo } from "../fs/PathTo"
import { PosixFile } from "../fs/PosixFile"
import { Settings } from "../settings/Settings"
import { dimensions } from "./FileDimensions"
import { withImageCache_ } from "./ImageCache"

const logger = lazy(() => mkLogger("img.HeifConvert"))

export const heifConvertPath = lazy(async () => {
  const result = logger().tap({
    msg: "heifConvertPath",
    result: await pathTo({
      tool: "heif-convert",
      alternativePath: Settings.heifConvertPath.valueOrDefault
    })
  })
  // They may install heif-convert after we check. If it's missing, try again in a minute:
  heifConvertPath.setTTL(result == null ? minuteMs : undefined)
  return result
})

function clearCache() {
  heifConvertPath.unset()
}

later(() => {
  ee().on("clearCache", clearCache)
  ee().on("clearToolCache", clearCache)
})

export async function isHeifConvertSupported() {
  return notBlank(await heifConvertPath())
}

/**
 * Returns a file holding the binary stream associated to the given tag.
 * Orientation is copied from the src to the result.
 */
export async function heif2png_(src: PosixFile): PromiseMaybe<PosixFile> {
  if (!(await isHeifConvertSupported())) return
  const dims = await dimensions(src)
  if (dims == null) {
    logger().throw("heif2png_() failed: no dimensions for " + src)
  }

  // PNG (vs JPG) IS A BUG WORKAROUND FOR
  // https://forum.photostructure.com/t/incorrectly-rotated-heic-images/34/15?u=mrm

  return withImageCache_(src, "heif", ".png", dest => _heif2png_(src, dest))
}

async function _heif2png_(src: PosixFile, dest: PosixFile) {
  try {
    await stdoutResult_(
      Settings.heifConvertPath.valueOrDefault,
      [
        // No need to specify quality: the output is a lossless PNG.
        // "-q",
        // String(Settings.previewQuality.valueOrDefault),
        src.nativePath,
        // heif-convert does TERRIBLE things to the output path.
        dest.base
      ],
      {
        cwd: dest.dir,
        timeoutMs: minuteMs, // should only take a couple seconds
        ignoreStderr: true // heif-convert may grump about "Can't open -urn:com:apple:photo:2020:aux:hdrgainmap.WIP-heif.png: Invalid argument\ncould not write auxiliary image"
      }
    )
    if (await dest.clear().isEmptyFile(KB)) {
      logger().throw("heif2png_() failed: empty output", { src, dest })
    }
  } catch (error) {
    logger().throw("heif2png() failed", { error, src: src.nativePath })
  }
}
