import _sharp, { JpegOptions } from "sharp"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../Logger"
import { Settings } from "../settings/Settings"

// SITS: why isn't this exported in the typing?
export interface OutputInfo {
  format: string
  size: number
  width: number
  height: number
  channels: number
}

const logger = lazy(() => mkLogger("img.Sharp"))

export function isSharp(object: any): object is _sharp.Sharp {
  return (
    typeof object === "object" &&
    typeof object.jpeg === "function" &&
    typeof object.raw === "function" &&
    typeof object.toBuffer === "function"
  )
}

export async function sharpClone(s: _sharp.Sharp): Promise<_sharp.Sharp> {
  try {
    return s.clone()
  } catch (err) {
    logger().info("sharp.clone() failed", err)
  }
  try {
    const { data, info } = await s.raw().toBuffer({ resolveWithObject: true })
    return sharpFromRawBuffer(data, info)
  } catch (error) {
    return logger().throw("clone via sharp.raw().toBuffer() failed", {
      error
    })
  }
}

export function sharpFromRawBuffer(
  b: Buffer,
  info: Pick<OutputInfo, "width" | "height" | "channels">
) {
  return _sharp(b, {
    raw: info as any
  })
}

export function sharpRenderOptions(): JpegOptions {
  return {
    quality: Settings.previewQuality.valueOrDefault,
    progressive: Settings.previewProgressive.valueOrDefault,
    mozjpeg: Settings.previewMinimized.valueOrDefault
  }
}
