import os from "os"
import { lazy } from "../../core/Lazy"
import { li } from "../../fe/BabyMarkdown"
import { minuteMs, secondMs } from "../../fe/Date"
import { defer } from "../../fe/Defer"
import { HealthCheckLevel } from "../../fe/HealthChecks"
import { gt0 } from "../../fe/Number"
import { GB, MB, fmtBytes } from "../../fe/Units"
import { memoryUsageBytes, memoryUsageMb } from "../Memory"
import { serviceName } from "../ServiceNames"
import { Settings } from "../settings/Settings"
import { HealthCheck, HealthCheckTest } from "./HealthCheck"

function asTest({
  desc,
  bytes,
  level,
  thresholdBytes
}: {
  desc: string
  bytes: number
  level: HealthCheckLevel
  thresholdBytes: number
}): HealthCheckTest {
  return {
    level,
    msg: [
      desc +
        " from " +
        serviceName() +
        " is " +
        (level === "ok" ? "OK" : "high"),
      li([
        "Current: " + fmtBytes(bytes, 2),
        "Threshold: " + fmtBytes(thresholdBytes, 2)
      ])
    ]
  }
}

function memoryUsageTest(): HealthCheckTest {
  if (os.totalmem() < GB) {
    return {
      level: "warn",
      msg: "PhotoStructure may need more than a GB of RAM to run, especially if your library is large."
    }
  } else if (
    gt0(Settings.maxMemoryMb.valueOrDefault) &&
    memoryUsageMb() > Settings.maxMemoryMb.valueOrDefault
  ) {
    return asTest({
      level: "error",
      desc: "Used memory",
      bytes: memoryUsageBytes(),
      thresholdBytes: Settings.maxMemoryMb.valueOrDefault * MB
    })
  } else if (
    gt0(Settings.maxMemoryMb.valueOrDefault) &&
    memoryUsageMb() > Settings.maxMemoryMb.valueOrDefault * 0.75
  ) {
    return asTest({
      level: "warn",
      desc: "Used memory",
      bytes: memoryUsageBytes(),
      thresholdBytes: Settings.maxMemoryMb.valueOrDefault * MB
    })
  } else {
    return asTest({
      level: "ok",
      desc: "Used memory",
      bytes: memoryUsageBytes(),
      thresholdBytes: Settings.maxMemoryMb.valueOrDefault * MB
    })
  }
}

export const memoryUsageIsHigh = lazy(
  () => memoryUsageTest().level !== "ok",
  secondMs
)

export const memoryHealthCheck = defer(() =>
  HealthCheck.for({
    section: "System",
    id: "proc-memory-use",
    pendingMsg: "Checking memory usage",
    settings: ["maxMemoryMb"],
    errorLinks: [
      {
        text: "Restart web service",
        type: "button",
        method: "POST",
        url: "/admin/bounce",
        icon: "refresh"
      }
    ],
    ttlMs: minuteMs,
    later: async () => memoryUsageTest()
  })
)
