import { Database } from "better-sqlite3"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { countChars, stripSuffix } from "../../core/String"
import {
  TagSep,
  joinTagPath,
  splitTagPath,
  tagPathLeaf
} from "../../core/tags/TagPaths"
import { compactBlanks, isEmpty } from "../../fe/Array"
import { sortBy } from "../../fe/ArraySort"
import { MultiMap } from "../../fe/MultiMap"
import { gt0 } from "../../fe/Number"
import { toS } from "../../fe/toS"

const logger = lazy(() => mkLogger("db.DedupeTags"))

export function mergeTags_(db: Database, tagIds: number[]) {
  if (isEmpty(tagIds)) {
    logger().warn("mergeTags(): empty tagIds", { tagIds })
  }

  logger().info("mergeTags() starting", { tagIds })

  function run(sql: string) {
    return db.prepare(sql).run()
  }

  const arr = db
    .prepare(
      "SELECT id, _path, parentId FROM Tag WHERE id IN (" +
        tagIds.join(",") +
        ") ORDER BY ID"
    )
    .all() as { id: number; _path: string; parentId: number | null }[]

  // With the v0.8 test db, we have a {id: 1, parentId: null, path: "kw"} and {id: 400,
  // parentId: 1, path: "kw/"}. If id:400 wins, it must assume a null parentId.
  // BAD THINGS happen if we pick a winner whose parent id is one of the
  // losers (the id === parentId!), so we need to avoid that case. We avoid
  // this by assuming null for the parentId if any of the tags have a null parentId.
  // Capitalized base should win. sort is asciibetical, so capitals come first.
  const leafs = compactBlanks(arr.map(ea => tagPathLeaf(ea._path)).sort())

  // It doesn't matter which tag wins, because we're about to fix the
  // parentId and path. Let's arbitrarily pick the lowest id.
  const winner = arr[0]
  const losers = arr.slice(1)
  const winnerId = winner?.id
  if (isEmpty(leafs) || isEmpty(losers) || winner == null || !gt0(winnerId)) {
    logger().warn("mergeTags(): missing winner", {
      winner,
      winnerId,
      leafs,
      losers
    })
    return
  }

  const parentTagPath = splitTagPath(winner._path).slice(0, -1)
  const winnerTagPath = [...parentTagPath, leafs[0]]
  winner._path = joinTagPath(winnerTagPath)
  winner.parentId =
    parentTagPath.length === 0
      ? null
      : (db
          .prepare("SELECT id from Tag where _path LIKE ?")
          .pluck()
          .get(joinTagPath(parentTagPath)) as number) ?? winner.parentId

  logger().info("mergeTags()", { winner, losers, winnerTagPath, leafs })

  const loserIds = losers.map(ea => ea.id).join(",")
  run(
    `UPDATE OR IGNORE AssetTag SET tagId = ${winnerId} WHERE tagId in (${loserIds})`
  )
  run(
    `UPDATE OR IGNORE Tag SET parentId = ${winnerId} WHERE parentId in (${loserIds})`
  )
  run(
    `UPDATE OR IGNORE Tag SET parentId = ${winnerId} WHERE parentId in (${loserIds})`
  )
  run(`DELETE FROM AssetTag WHERE tagId in (${loserIds})`)
  run(`DELETE FROM Tag WHERE id in (${loserIds})`)

  db.prepare(
    "UPDATE Tag SET parentId = :parentId, _path = :path WHERE id = :id"
  ).run({ parentId: winner.parentId, path: winner._path, id: winnerId })

  const children = db
    .prepare(
      "SELECT id, _path FROM Tag WHERE id != :winnerId AND _path LIKE :like COLLATE NOCASE"
    )
    .all({
      winnerId,
      like: winner._path + "%"
    }) as { id: number; _path: string }[]

  for (const ea of children) {
    const priorChildPath = splitTagPath(ea._path)
    const fromParentPath = priorChildPath.slice(winnerTagPath.length)
    const newPath = joinTagPath([...winnerTagPath, ...fromParentPath])
    if (ea._path !== newPath) {
      logger().info("mergeTags(): repairing tag path", { ea, newPath })
      db.prepare(
        // the OR IGNORE should not be needed:
        "UPDATE OR IGNORE Tag SET _path = :path, updatedAt = :updatedAt WHERE id = :id"
      ).run({
        id: ea.id,
        updatedAt: Date.now(),
        path: newPath
      })
    }
  }

  return logger().tap({
    msg: "mergeTags()",
    level: "info",
    result: { winner, losers: arr }
  })
}

export function dedupeTags_(db: Database) {
  // Unfortunately, there isn't a way to do unicode normalization in SQLite, so we need to do this:
  const mm = new MultiMap<string, number>()
  for (const { id, _path } of db
    .prepare("SELECT id, _path FROM Tag ORDER BY id")
    .iterate() as IterableIterator<{ id: number; _path: string }>) {
    mm.add(stripSuffix(toS(_path).trim(), TagSep).toLowerCase().normalize(), id)
  }
  const dupes = sortBy(
    mm.entriesArray().filter(([, ids]) => ids.length > 1),
    // Do longest paths first, or child repair isn't going to work (unique
    // constraints will fail!)
    ([path]) => [-countChars(path, TagSep), -path.length, path]
  )
  for (const [, ids] of dupes) {
    mergeTags_(db, ids)
  }
}
