import { satisfies } from "semver"
import { lazy } from "./Lazy"
import { isElectron } from "./platform/Platform"

export const isSupportedNode = lazy(_isSupportedNode)

export const requiredNodeRange = lazy(() =>
  isElectron ? "16||18||20" : "18||20"
)
export function _isSupportedNode(nodeVersion = process.versions.node) {
  return satisfies(nodeVersion, requiredNodeRange())
}
