import { PosixFile } from "../fs/PosixFile"
import { SQLiteSuffixes } from "./SQLiteSuffixes"

/**
 * @return an empty array if dbfile doesn't exist
 */
export function sqliteFiles(dbFile: PosixFile): PosixFile[] {
  return [
    dbFile,
    ...SQLiteSuffixes.map(ea => dbFile.sibling(dbFile.base + ea))
  ].filter(ea => ea.existsSync({ refresh: true }))
}

/**
 * Upper bound for the size of the db
 */
export async function sqliteSizeBytes(dbFile: PosixFile): Promise<number> {
  return (
    ((await dbFile.size({ refresh: true })) ?? 0) +
    ((await dbFile.sibling(dbFile.base + "-wal").size({ refresh: true })) ?? 0)
  )
}
