import { compact } from "../../fe/Array"
import { Dimensions, dmegapixels } from "../../fe/Dimensions"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0, lt, lte, sigFigs, toInt } from "../../fe/Number"
import { Rotation, Rotations } from "../../fe/Rotation"
import { toS } from "../../fe/toS"
import { ExifTags } from "../tags/ExifTags"
import { roundAspectRatio } from "./Ratio"

export function tmegapixels(t: ExifTags) {
  if (t.dimensions != null) return dmegapixels(t.dimensions)
  if (gt0(t.Megapixels)) return t.Megapixels
  return
}

/**
 * @return true if `lhs` is wholly contained by `rhs`.
 */
export function ltBoth(lhs: Partial<Dimensions>, rhs: Partial<Dimensions>) {
  return lt(lhs.width, rhs.width) && lt(lhs.height, rhs.height)
}

/**
 * @return true if `lhs` is wholly contained by `rhs`.
 */
export function lteBoth(lhs: Partial<Dimensions>, rhs: Partial<Dimensions>) {
  return lte(lhs?.width, rhs?.width) && lte(lhs?.height, rhs?.height)
}

/**
 * @return true if `lhs` is wholly contained by `rhs`.
 */
export function ltEither(lhs: Partial<Dimensions>, rhs: Partial<Dimensions>) {
  return lt(lhs.width, rhs.width) || lt(lhs.height, rhs.height)
}

export function parseDimensions(s: Maybe<string>): Maybe<Dimensions> {
  const arr = compact(
    toS(s)
      .split(/[x]/)
      .map(ea => toInt(ea))
  )
  return arr.length === 2 ? { width: arr[0], height: arr[1] } : undefined
}

export function fitToResolution(
  dim: Dimensions,
  pixelCount: number
): Dimensions {
  if (dim.width * dim.height <= pixelCount) return dim
  const aspect_ratio = dim.width / dim.height
  const widthF = Math.round(Math.sqrt(pixelCount * aspect_ratio))
  return {
    width: widthF,
    height: Math.round(widthF / aspect_ratio)
  }
}

export function aspectRatio(
  d: Maybe<Partial<Dimensions>>,
  thresh?: number
): Maybe<number> {
  return d != null && gt0(d.width) && gt0(d.height)
    ? sigFigs(roundAspectRatio(d.width / d.height, thresh), 3)
    : undefined
}

export function rotateDimensions(d: Dimensions, r: Rotation): Dimensions {
  return r === 0 || r === 180 ? d : { width: d.height, height: d.width }
}

export function validRotations(
  src: Dimensions,
  dest: Dimensions,
  thresh = 0.2
): Rotation[] {
  const targetRatio = aspectRatio(src, thresh)
  return targetRatio == null
    ? []
    : Rotations.filter(
        r => aspectRatio(rotateDimensions(dest, r), thresh) === targetRatio
      )
}
