import { compactBlankish } from "./Array"
import { blank } from "./Blank"
import { flatten } from "./Flatten"
import { replaceAllMatches } from "./RegExp"
import { toS } from "./toS"

export function md2html(markdown: string) {
  let result = markdown
    // escape html, then add code blocks, bold, and list items
    .replace(/</g, "&lt;")
    // horizontal rule:
    .replace(/^---$/gm, "<hr/>")
    // Handle lists:
    .replace(/^ *- (.*)$/gm, (_, s) => "<li>" + s.trim() + "</li>")

  result = replaceAllMatches(
    result,
    /```([^`]+)```/g,
    m => `<code>${m[1]}</code>`
  )
  result = replaceAllMatches(result, /\*\*([^*]+)\*\*/, m => `<b>${m[1]}</b>`)

  return result
}

export function tt(str: any) {
  return blank(str) ? "" : "```" + toS(str) + "```"
}

export function b(str: any) {
  return "**" + toS(str) + "**"
}

export function li(...arr: any[]) {
  return compactBlankish(flatten(arr))
    .map((s: any) => "- " + toS(s))
    .join("\n")
}
