import { lazy } from "../../core/Lazy"
import { compact, pushUniq } from "../../fe/Array"
import { getOrSet } from "../../fe/Map"
import { Maybe } from "../../fe/MaybeTypes"
import { gte0 } from "../../fe/Number"
import { leastBy } from "../Array"
import { mkLogger } from "../Logger"
import { capitalize } from "../String"
import {
  lab2rgbhex,
  rgb2lab,
  rgbhex2Lab,
  rgbhex2Labhash,
  toLabhash,
  unlabhash
} from "./Colorspace"
import { CrayonColors, CrayonSupplements } from "./CrayonColors"
import { DarkWikipediaColors } from "./DarkWikipediaColors"
import { ciede2000_delta_e } from "./DeltaE"
import { Greys } from "./Greys"
import { LightWikipediaColors } from "./LightWikipediaColors"
import { ModeBits } from "./ModeBits"
import { normalizeRgbHex } from "./RGB"
import { Triplet } from "./Triplet"

const logger = lazy(() => mkLogger("color.ColorNames"))

/* 
https://en.wikipedia.org/wiki/List_of_colors:_A%E2%80%93F
https://en.wikipedia.org/wiki/List_of_colors:_G%E2%80%93M
https://en.wikipedia.org/wiki/List_of_colors:_N%E2%80%93Z

https://en.wikipedia.org/wiki/List_of_colors_by_shade

Try not to include any color names that are names of people, organizations, or
places. Flower names are fine.

*/

export const ColorNames = lazy<LabNamedRgbHexColor[]>(() =>
  compact(
    [
      ...CrayonColors,
      ...CrayonSupplements,
      ...DarkWikipediaColors,
      ...LightWikipediaColors,
      ...Greys
    ].map(colorToTuple)
  )
)

export type NamedRgbHexColor = {
  name: string
  rgb: string
}

export type LabNamedRgbHexColor = NamedRgbHexColor & {
  lab: Triplet

  /**
   * Best-matching labhash for this RGB color
   */
  labhash: number

  /**
   * All the labhashes that map to this color
   */
  closeLabhashes: number[]

  closeRgb: string[]
}

export function bestRgbTripletName<T extends LabNamedRgbHexColor>(
  rgb: Triplet,
  colors = ColorNames() as T[]
) {
  return bestLabName(rgb2lab(rgb), colors)
}

export function colorToTuple(
  color: NamedRgbHexColor
): Maybe<LabNamedRgbHexColor> {
  const rgb = normalizeRgbHex(color.rgb)
  if (rgb == null) {
    logger().warn(`skipping invalid entry (bad rgb): ${color.rgb}`, color)
    return
  }
  const lab = rgbhex2Lab(rgb)
  if (lab == null) {
    logger().warn(
      `skipping invalid entry (failed to convert to L*a*b): ${rgb}`,
      color
    )
    return
  }

  const labhash = toLabhash(lab, ModeBits)
  return {
    ...color,
    rgb,
    lab,
    labhash,
    // This is set later by labhashToColor()
    closeLabhashes: [labhash],
    closeRgb: [rgb],
    name: capitalize(color.name)
  }
}

export function bestRgbName(rgb: string): Maybe<LabNamedRgbHexColor> {
  return bestLabhashName(rgbhex2Labhash(rgb))
}

export function bestLabName(
  lab: Maybe<Triplet>,
  colors?: LabNamedRgbHexColor[]
): Maybe<LabNamedRgbHexColor> {
  return lab == null
    ? undefined
    : leastBy(colors ?? ColorNames(), ea => ciede2000_delta_e(ea.lab, lab))
}

const labhashToColor = new Map<number, LabNamedRgbHexColor>()

export function bestLabhashName(
  labhash: Maybe<number>
): Maybe<LabNamedRgbHexColor> {
  return gte0(labhash)
    ? getOrSet(labhashToColor, labhash, () => {
        const exact = ColorNames().find(ea => ea.labhash === labhash)
        if (exact != null) return exact
        const lab = unlabhash(labhash)
        const result = bestLabName(lab)
        if (lab != null && result != null) {
          pushUniq(result.closeLabhashes, labhash)
          pushUniq(result.closeRgb, lab2rgbhex(lab))
        }
        return result
      })
    : undefined
}
