// No need for the -shm file:  The WAL-index or "shm" file is used to
// coordinate access to the database by multiple clients, and as a cache to
// help clients quickly locate frames within the wal file.

// See https://sqlite.org/walformat.html#the_wal_index_file_format

// Because the shm file is not involved in recovery, the shm file does not
// need to be machine byte-order independent. Hence, numeric values in the shm
// file are written in the native byte order of the host computer, rather than
// being converted into a specific cross-platform byte order as is done with
// the main database file and the wal file.

// The -journal file may be needed if it is "hot": Before reading from a
// database file, SQLite always checks to see if that database file has a hot
// journal. If the file does have a hot journal, then the journal is rolled
// back before the file is read. In this way, we ensure that the database file
// is in a consistent state before it is read.

// See https://sqlite.org/lockingv3.html and
// https://www.sqlite.org/howtocorrupt.html

export const SQLiteSuffixes = ["-wal", "-journal"]
