import { readdirSync } from "fs"
import { dirname } from "path"
import { Maybe } from "../../fe/MaybeTypes"

/**
 * @return nativePath to all ancestors of a given path, in order from nearest
 * to farthest.
 */
export function ancestors(path: string): string[] {
  return [...ancestorG(path)]
}

export function* ancestorG(path: string): Generator<string> {
  while (path !== dirname(path)) {
    path = dirname(path)
    yield path
  }
  return
}

export function* selfAndAncestorG(path: string): Generator<string> {
  yield path
  yield* ancestorG(path)
}

export function childrenSync(path: string): string[] {
  try {
    return readdirSync(path)
  } catch (err) {
    return []
  }
}
export function hasChildrenSync(path: string, childNames: string[]): boolean {
  const actual = childrenSync(path)
  return childNames.every(ea => actual.includes(ea))
}

export function ancestorWithChildren(
  path: string,
  childNames: string[]
): Maybe<string> {
  return ancestors(path).find(ea => hasChildrenSync(ea, childNames))
}
