import { FileTypeResult, fromBuffer } from "file-type"
import { lazy } from "../../core/Lazy"
import { blank, mapNotBlank } from "../../fe/Blank"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { mkLogger } from "../Logger"
import { WrappedError } from "../error/WrappedError"
import { normalizeExt } from "../fs/FileExt"
import { readRawField } from "../tags/ExifTool"
import { mimetypeExt } from "../tags/FileTypes"
import { normalizeMimetype } from "../tags/MimeTypes"
import { PosixFile } from "./PosixFile"
import { readFilePart_ } from "./ReadFilePart"
import { exists } from "./Stat"

const logger = lazy(() => mkLogger("fs.FileType"))

// file-type doesn't handle these correctly:
const BadMimes = ["image/tiff", "audio/mpeg"]

/**
 * You probably want readMimeType(), which tries to use existing ExifTool
 * caches first before falling back to calling this function.
 */
export async function readFileType_(
  nativePath: string
): PromiseMaybe<FileTypeResult> {
  try {
    // PERF: don't stat then read. Just hit it.
    // We need to fetch at least 197 bytes to work with AVCHD properly:
    const { buffer } = await readFilePart_({
      nativePath,
      position: 0,
      length: 256
    })
    const result = await fromBuffer(buffer)
    if (result != null && !BadMimes.includes(result.mime)) {
      return {
        ext: normalizeExt(result.ext) ?? result.ext,
        mime: normalizeMimetype(result.mime)
      } as FileTypeResult
    }

    // Don't bother exiftool if the file doesn't exist:
    if (!(await exists(nativePath))) return

    const mime = mapNotBlank(
      await readRawField(nativePath, "MIMEType"),
      normalizeMimetype
    )
    const ext = normalizeExt(PosixFile.for(nativePath)) ?? mimetypeExt(mime)
    return blank(mime) || blank(ext)
      ? undefined
      : ({ ext, mime } as FileTypeResult)
  } catch (cause: any) {
    if (cause?.code === "EISDIR") {
      return undefined
    } else {
      logger().warn("readFileType() failed for " + nativePath, cause)
      throw new WrappedError("Failed to read mimetype for " + nativePath, {
        cause,
        ignorable: true,
        retriable: false,
        fatal: false,
        path: nativePath
      })
    }
  }
}
