import { tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { later } from "../../fe/Delay"
import { map } from "../../fe/Maybe"
import { ee } from "../event/EventEmitter"
import { getHeifSupportDetails } from "../img/Heif"
import { isDocker } from "../platform/isDocker"
import { ver } from "../version/Ver"
import { HealthCheck } from "./HealthCheck"

export const heifHealthCheck = defer(() =>
  HealthCheck.for({
    section: "Tools",
    id: "tools-heif",
    pendingMsg: "Checking HEIF/HEIC tooling",
    settings: ["heifConvertPath"],
    links: [
      {
        text: "Read how to set up HEIF support with PhotoStructure",
        url: "https://photostructure.com/getting-started/heif-support/",
        icon: "docs"
      }
    ],
    warnLinks: isDocker()
      ? [] // can't fix docker!
      : [
          {
            text: "Re-check for HEIF tooling",
            icon: "refresh",
            method: "POST",
            type: "button",
            url: "/admin/recheck-tools"
          }
        ],
    // if the HEIF tooling is not installed or explodes when checking, we
    // don't want to block the server from starting:
    rejectIsWarn: true,

    later: async () => {
      const td = await getHeifSupportDetails()
      return td != null && false !== td.isSupportedVersion
        ? {
            level: "ok",
            msg: [
              "HEIF images will be imported",
              map(td.version, ver),
              map(td.path, tt)
            ]
          }
        : {
            level: "warn",
            msg: [
              "HEIF images will not be imported",
              "No HEIF tooling was found."
            ]
          }
    }
  })
)

later(() => {
  ee().on("clearToolCache", () => heifHealthCheck.prior()?.reset())
})
