/**
 * licensing keys
 */
export const k = defer<{
  n: "SystemIds"
  i: "ioreg"
  pc: "/proc/cpuinfo"
  ia: ["-rd1", "-c", "IOPlatformExpertDevice"]
  lm: ["/etc/machine-id", "/var/lib/dbus/machine-id"]
  lp: ["/sys/class/dmi/id/product_uuid"]
  lb: ["/sys/devices/virtual/dmi/id/board_serial"]
  bm: [
    "/sys/devices/virtual/dmi/id/board_vendor",
    "/sys/devices/virtual/dmi/id/board_name"
  ]
  w: "Get-ItemProperty -Path hklm:\\SOFTWARE\\Microsoft\\Cryptography -Name MachineGuid | Select-Object -Property MachineGuid"
}>(() =>
  j(
    ""
  )
)

const logger = defer(() => mkLogger(k().n))

export async function readInfo(
  f: string | SimpleFile = k().pc
): PromiseMaybe<string[]> {
  // This is really only found on Raspberry Pis, but certainly not on Windows:
  if (!isLinux) return
  try {
    const info = await fileGrep_(
      f,
      /^\s*(?:hardware|revision|serial|model)\s*[:=]\s*[a-z\d]{6}.*$/i
    )
    // normalize whitespace and order:
    const lines = compactBlanks(
      splitLines(info).map(ea => ea.replace(/\s+/g, " "))
    ).sort()
    return lines.some(ea => ea.match(/^serial/i) != null) ? lines : undefined
  } catch {
    //
    return
  }
}

const cpuinfoSerial = defer(() =>
  thenMap(readInfo(), arr => toUID(S.lc, arr.join(",")))
)

const oemRe = /o\.e\.m\./i

async function readFiles(key: keyof ReturnType<typeof k>) {
  return uniq(
    await Promise.all(
      (k()[key] as string[]).map(ea => readFileMaybe(ea, LogLevels.trace))
    )
  )
    .map(toS)
    .filter(ea => notBlank(ea) && null == ea.match(oemRe))
}

async function linuxMachineUid() {
  return isLinux
    ? (await readFiles("lm")).map(ea => toUID(S.lm, ea))
    : undefined
}

async function linuxProductUid() {
  return isLinux
    ? (await readFiles("lp")).map(ea => toUID(S.lp, ea))
    : undefined
}

async function linuxBoardSerial() {
  return isLinux
    ? (await readFiles("lb")).map(ea => toUID(S.lb, ea))
    : undefined
}

async function mac_IOPlatformUUID_() {
  const out = await stdout_(k().i, k().ia, {
    timeoutMs: 5 * secondMs
  })
  // "IOPlatformUUID" = "123ABC45-67DE-8F90-ABC1-23456DEF7890"
  const mp = out.match(/uuid" = "([a-z\d-]{12,})"/i)?.[1]

  // "IOPlatformSerialNumber" = "ABC0123XYZ45"
  const ms = out.match(/SerialNumber" = "([a-z\d-]{10,})"/i)?.[1]
  return { mp, ms }
}

async function macUids() {
  if (!isMac) return
  try {
    return entries(await mac_IOPlatformUUID_()).map(([pfx, id]) =>
      toUID(pfx, id)
    )
  } catch (err) {
    logger().warn(k().i + " failed", err)
    return
  }
}

// exposed for tests
export async function win_machineGuid_() {
  return (await PowerShell.instance().executeJson(k().w))?.MachineGuid
}

async function winMachineUid() {
  return isWin ? toUID(S.wm, await win_machineGuid_()) : undefined
}

const cpuModel = defer(() => toUID(S.cm, cpuInfo()[0].model))

async function boardModel() {
  return isLinux
    ? toUID(
        S.bm,
        compactBlanks(await readFiles("bm"))
          .map(ea => ea.trim())
          .join("\n")
      )
    : []
}

async function libraryUid() {
  return toUID(S.li, await LibraryUIDStore()?.readUid_())
}

async function systemUid() {
  return toUID(S.si, await SystemUIDStore()?.readUid_())
}

function networkMacUids() {
  return networkMacAddresses().map(ea => toUID(S.nm, ea))
}

async function volumeIds() {
  return toA(await volumes()).map(v => toUID(S.vl, v.uuid))
}

const priorSids = new Set<string>()

export async function sids({ timeoutMs }: { timeoutMs: number }) {
  const startTs = Date.now()
  const sidFuncs: ThunkOrLater<Maybe<string | Maybe<string>[]>>[] = [
    // easiest/most important first:
    cpuModel,
    cpuinfoSerial,
    macUids,
    libraryUid,
    systemUid,
    boardModel,
    linuxMachineUid,
    linuxProductUid,
    linuxBoardSerial,
    winMachineUid,
    networkMacUids,
    volumeIds
  ]
  const flat = flatten(await someOrTimeout(sidFuncs, { timeoutMs }))
  const arr = flat.filter(isString)
  for (const ea of arr) {
    priorSids.add(ea)
  }
  return logger().tap({
    msg: "sids()",
    result: sortUids([...priorSids, ...arr]),
    meta: {
      timeoutMs,
      elapsedMs: Date.now() - startTs
    }
  })
}

export function addSids(arr: string[]) {
  if (isTest) {
    for (const ea of arr) {
      priorSids.add(ea)
    }
  }
}

export function clearSids() {
  if (isTest) priorSids.clear()
}
