import { ExifDate, ExifDateTime } from "exiftool-vendored"
import { DateTime } from "luxon"
import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import {
  dayMs,
  hourMs,
  minuteMs,
  monthMs,
  secondMs,
  yearMs
} from "../../fe/Date"
import { Json } from "../../fe/JSON"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0, isNumber } from "../../fe/Number"
import { maybeCall } from "../../fe/Object"
import { isString } from "../../fe/String"
import { mkLogger } from "../Logger"
import { DateObject, isDateObject } from "./Date"
import { DateInterval } from "./DateInterval"
import { datedToMillis } from "./DatedToMillis"
import { dateObjectToExifDateTime, parseExifDateTime } from "./ExifDateTime"
import { FuzzyDate, setZone } from "./FuzzyDate"
import {
  getDay,
  getHour,
  getMillisecond,
  getMinute,
  getMonth,
  getSecond
} from "./GetDateField"
import { hasTime } from "./HasTime"
import { isDateTime } from "./IsDateTime"
import { getZoneName } from "./getZoneName"

const logger = lazy(() => mkLogger("date.Dated"))

// can't IDate because IMarried HUR HUR LOL DADJOKES FTW

// references to exiftool-vendored must only be in this type signature to
// prevent account from depending on exiftool-vendored.

export type Dated =
  | FuzzyDate
  | ExifDate
  | ExifDateTime
  | Date
  | DateInterval
  | DateTime
  | DateObject

// we don't include luxon.Interval here because our DateInterval supports
// slots and index.

export function isDated(d: any): d is Dated {
  // My proudest moment in coding:

  // oh wait this is all just garbage bailing wire and duct tape *tear*.
  return (
    d != null &&
    (d instanceof FuzzyDate ||
      d instanceof ExifDate ||
      d instanceof ExifDateTime ||
      d instanceof Date ||
      d instanceof DateInterval ||
      isDateTime(d) ||
      isDateObject(d))
  )
}

export function toDated(obj: any): Maybe<Dated> {
  return isDated(obj) ? obj : undefined
}

export function mapDated<T>(obj: any, f: (d: Dated) => T): Maybe<T> {
  return isDated(obj) ? f(obj) : undefined
}

export function toDate(d: Maybe<Dated>): Maybe<Date> {
  if (d == null) return
  if (typeof d === "number") {
    return new Date(d)
  }
  if (d instanceof Date) {
    return d
  }
  return maybeCall(d, "toJSDate") ?? maybeCall(d, "toDate")
}

export function datedToStartTs(d: Maybe<Dated>): Maybe<number> {
  if (d == null) return
  else if (d instanceof DateInterval) {
    return d.start.toMillis()
  } else if (d instanceof ExifDate) {
    return d.toMillis(0) // < defaults to 12 hours!
  } else {
    return datedToMillis(d)
  }
}

export function datedToStartDateTime(d: Dated): Maybe<DateTime> {
  return d instanceof DateInterval
    ? d.start.toDateTime()
    : d instanceof ExifDate
    ? DateTime.fromObject(d) // < defaults to noon, but we want midnight.
    : datedToDateTime(d)
}

/**
 * If you don't care about timezone offsets (you're just wanting `.isValid`,
 * right?), otherwise, use `toExifDateTime`.
 */
export function datedToDateTime(d: Maybe<Dated>): Maybe<DateTime> {
  try {
    if (d == null || isString(d) || isNumber(d)) return
    if (isDateTime(d)) return d
    if (d instanceof ExifDateTime || d instanceof FuzzyDate)
      return d.toDateTime()
    if (d instanceof DateInterval) return d.middle.toDateTime()
    if (d instanceof Date) return DateTime.fromJSDate(d)
    return dateObjectToExifDateTime(d as any)?.toDateTime()
  } catch (error) {
    logger().warn("datedToDateTime() failed", { d, error })
    return
  }
}

export function datedToPrecisionMs(d: Dated): number {
  if (d instanceof DateInterval) {
    return d.precisionMs
  }

  if (hasTime(d)) {
    if (gt0(getMillisecond(d))) return 0
    if (gt0(getSecond(d))) return secondMs - 1
    if (gt0(getMinute(d))) return minuteMs - 1
    if (gt0(getHour(d))) return hourMs - 1
  }

  return gt0(getDay(d))
    ? dayMs - 1
    : gt0(getMonth(d))
    ? monthMs - 1
    : yearMs - 1
}

export function isoToPrecisionMs(iso: string): Maybe<number> {
  return map(isoToDated(iso), datedToPrecisionMs)
}

const NoOverlap = {
  aContainsB: false,
  aOverlapsB: false
}

export function datedOverlap({
  a,
  b,
  aPrecisionMs,
  bPrecisionMs
}: {
  a: Maybe<Dated>
  b: Maybe<Dated>
  aPrecisionMs?: number
  bPrecisionMs?: number
}): typeof NoOverlap {
  if (a == null || b == null) return NoOverlap

  aPrecisionMs ??= datedToPrecisionMs(a)
  bPrecisionMs ??= datedToPrecisionMs(b)

  // Eek: if one of the dates (but not the other) are missing a timezone, we
  // want to assume the other is in the same timezone (or it'll be incorrectly
  // wrong by the timezone offset)
  const aZone = getZoneName(a)
  const bZone = getZoneName(b)

  if (aZone == null && bZone != null) {
    a = setZone(a, bZone) ?? a
  }

  if (bZone == null && aZone != null) {
    b = setZone(b, aZone) ?? b
  }

  const a0 = datedToStartTs(a)
  const b0 = datedToStartTs(b)

  if (a0 == null || b0 == null) return NoOverlap

  const a1 = a0 + aPrecisionMs
  const b1 = b0 + bPrecisionMs

  const adjacent = a0 !== b0 && (a1 === b0 || a0 === b1)
  const aOverlapsB = !adjacent && !(a1 < b0 || a0 > b1)
  const aContainsB = aOverlapsB && a0 <= b0 && a1 >= b1
  return logger().tap({
    msg: "datedOverlap()",
    result: {
      aOverlapsB,
      aContainsB
    },
    meta: {
      a,
      b,
      aPrecisionMs,
      bPrecisionMs
    }
  })
}

export function isoToDated(iso: Maybe<string>, defaultZone?: Maybe<string>) {
  return blank(iso)
    ? undefined
    : DateInterval.fromISO(iso, defaultZone) ??
        parseExifDateTime(iso, defaultZone) ??
        FuzzyDate.fromISO(iso)
}

export function datedToJson(d: Dated): Json {
  const j = (d as any).toJSON?.()
  if (j != null) return j
  if (d instanceof DateTime)
    return {
      ...d.toObject(),
      zone: d.zoneName
    }
  return d as Json
}
