import { mapAsync } from "../core/async/Promise"
import { libraryPreviewsDirPosixFile } from "../core/dir/LibraryDirs"
import { isSharedStateFileOrDir } from "../core/event/SharedState"
import { FileCleanup } from "../core/fs/FileCleanup"
import { defaultFsAdvisoryLockDir } from "../core/fs/FsAdvisoryLocks"
import { isPsLockFileOrDir } from "../core/fs/FsLockDir"
import { PosixFile } from "../core/fs/PosixFile"
import { readdirCacheDir, ReadDirCacheName } from "../core/fs/Readdir"
import { isWip } from "../core/fs/WIP"
import { imageCacheDir_, ImageCacheName } from "../core/img/ImageCache"
import { lazy } from "../core/Lazy"
import { LogDirCleanup } from "../core/log/LogDirCleanup"
import { mkLogger } from "../core/Logger"
import { Pids, ProcCleaner } from "../core/Pids"
import { isDbJanitorService } from "../core/ServiceNames"
import { Settings } from "../core/settings/Settings"
import { compact } from "../fe/Array"
import { hourMs } from "../fe/Date"
import { Library } from "./Library"
import { vacuumStatDbDirs } from "./StatsDbDir"
import { forceRunTagMaintenance } from "./tag/TagSql"

const logger = lazy(() => mkLogger("Cleanup"))

export async function cleanup_() {
  const library = Library.instance()
  if (library == null) {
    logger().info("No library to vacuum.")
  } else {
    logger().info("Running tag and search maintenance...")
    await forceRunTagMaintenance()
    logger().info("Vacuuming db...")
    await (await library.dbModelSetup_()).db.vacuum()
    if (isDbJanitorService()) {
      logger().info("Running database maintenance...")
      await library.runDbBackup()
    }
  }

  logger().info("Cleaning child processes...")
  await Pids.instance()?.killOldProcs()

  logger().info("Cleaning stale stat db directories...")
  await vacuumStatDbDirs()

  logger().info("Cleaning stale and temp files...")
  await mapAsync({
    name: "fileCleanup",
    arr: fileCleanups(),
    f: ea => ea.cleanup()
  })
}

export const imageCacheCleanup = lazy(
  () =>
    new FileCleanup(
      "imageCacheCleanup",
      imageCacheDir_,
      Settings.imageCacheMs.valueOrDefault,
      f => f.pathnames.includes(ImageCacheName)
    )
)

export const readdirCleanup = lazy(
  () =>
    new FileCleanup(
      "readdirCacheCleanup",
      async () => PosixFile.for(readdirCacheDir()),
      Settings.readdirCacheMs.valueOrDefault,
      f => f.pathnames.includes(ReadDirCacheName)
    )
)

export const sharedFileCleanup = lazy(
  () =>
    new FileCleanup(
      "sharedFileCleanup",
      () => PosixFile.forMaybe(Settings.sharedStateDir.valueOrDefault),
      hourMs,
      isSharedStateFileOrDir
    )
)

export const previewWipCleanup = lazy(
  () =>
    new FileCleanup(
      "previewWipCleanup",
      libraryPreviewsDirPosixFile,
      hourMs,
      f => isPsLockFileOrDir(f) || isWip(f)
    )
)

export const advisoryLockWipCleanup = lazy(
  () =>
    new FileCleanup(
      "advisoryLockWipCleanup",
      defaultFsAdvisoryLockDir,
      hourMs,
      isPsLockFileOrDir
    )
)

export function fileCleanups() {
  return compact([
    imageCacheCleanup(),
    readdirCleanup(),
    sharedFileCleanup(),
    previewWipCleanup(),
    advisoryLockWipCleanup(),
    LogDirCleanup()
  ])
}

export const scheduleFileAndProcCleanups = lazy(() => {
  ProcCleaner()
  for (const ea of fileCleanups()) {
    ea.scheduleInterval()
  }
})
