import { strEnum, StrEnumKeys } from "../../fe/StrEnum"
import { TimestampedModel } from "./TimestampedModel"

export const ProgressMetaNames = strEnum(
  "lastScannedDirectory",
  "scannedDirectoryCount",
  "completedDirectoryScan",
  "enqueuedStaleFiles",
  "processedImageCount",
  "processedVideoCount",
  "completedDirectorySync"
)
export type ProgressMetaName = StrEnumKeys<typeof ProgressMetaNames>

export class ProgressMeta extends TimestampedModel {
  static override readonly $tableName = "ProgressMeta"
  static override readonly $uniqueColumnName = "progressId,name"
  name!: ProgressMetaName
  value!: string
}

export type ProgressMetaRecord = Record<ProgressMetaName, string>
