import { RunResult } from "better-sqlite3"
import { Knex } from "knex"
import { batches } from "../../core/Array"
import { lazy } from "../../core/Lazy"
import { Logger, mkLogger } from "../../core/Logger"
import { equalsIgnoreCase } from "../../core/String"
import { WeakCache } from "../../core/WeakCache"
import {
  InternalErrorFlag,
  NonRetriableErrorFlag
} from "../../core/error/ErrorFlags"
import { ee } from "../../core/event/EventEmitter"
import { Settings } from "../../core/settings/Settings"
import { compact, diff, isNotEmpty, uniq } from "../../fe/Array"
import { sortBy } from "../../fe/ArraySort"
import { blank, toNotBlank } from "../../fe/Blank"
import { flatten } from "../../fe/Flatten"
import { stringify } from "../../fe/JSON"
import { map, mapOr } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0, toGt0, toInt } from "../../fe/Number"
import { assignFields, isEmptyObj, keys, pick } from "../../fe/Object"
import { toA } from "../../fe/toA"
import { Db } from "../db/Db"
import { DbRequest } from "../db/DbRequest"
import { DbValue, toDbValued } from "../db/DbValued"
import { PartialPojo } from "../db/PartialPojo"
import { Queryish } from "../db/SqlQuery"
import { Model, ModelClass } from "./Model"
import { addModelClass } from "./ModelJson"
import { TableName } from "./TableName"

export class ModelOps<M extends Model> {
  readonly dbl: DbRequest
  readonly #logger: Logger
  #cacheEnabled: boolean = true
  readonly #cache = new WeakCache<string, M>()
  readonly #deletedIds = new Set<number>()

  constructor(
    readonly model: ModelClass<M>,
    readonly db: () => Db
  ) {
    this.#logger = mkLogger(`ModelOps(${model.$tableName})`)
    addModelClass(model)
    this.#cacheEnabled = model.$useCache ?? true
    this.dbl = new DbRequest(this.db, model.$tableName)
    ee().on("clearCache", () => this.#cache.clear())
    ee().on("clearDbCache", () => this.#cache.clear())
  }

  #assign(dest: PartialPojo<M> | M, src: Maybe<PartialPojo<M> | M>) {
    assignFields(dest, src, { omitKeys: ["$instanceId"], assignNullish: true })
  }

  clearDbCache() {
    this.#cache.clear()
  }

  get $cache() {
    return this.#cache
  }

  #maybeCache(m: M) {
    if (this.#cacheEnabled) {
      const prior = this.$cachedByJson(m)
      if (prior != null) {
        this.#assign(prior, m)
        return prior
      } else {
        const k = this.$pickModelUcn(m) ?? m.id
        if (k != null) {
          this.#cache.set(stringify(k), m)
        }
      }
    }
    return m
  }

  $cachedByJson(json: M | Record<keyof M, DbValue>) {
    if (json == null || !this.#cacheEnabled) return
    const ucn = this.$pickModelUcn(json as any)
    return (
      (ucn == null ? undefined : this.#cache.get(stringify(ucn))) ??
      this.$cachedById((json as any).id)
    )
  }

  $cachedById(id: Maybe<number>) {
    return gt0(id) && this.#cacheEnabled
      ? this.#cache.get(stringify(id)) ??
          this.#cache.findEntry(ea => ea.id === id)
      : undefined
  }

  get dbOpen(): boolean {
    return mapOr(
      this.db(),
      ea => ea.open,
      () => false
    )
  }

  readonly columnNames = lazy<string[]>(() =>
    this.db()
      .tableInfo_()
      .find(ea => equalsIgnoreCase(ea.tableName, this.tableName))!
      .columns.map(ea => ea.name)
  )

  readonly updatableColumnNames = lazy<string[]>(() =>
    diff(this.columnNames(), this.immutableColumnNames)
  )

  private get tableName(): TableName {
    return this.model.$tableName
  }

  query() {
    return this.model.query()
  }

  toDbValued(obj: PartialPojo<M> | M) {
    const m = this.model.fromJSON(obj)
    const dbv = toDbValued(m)
    return pick(dbv, ...this.columnNames())
  }

  private run(qb: Queryish) {
    return this.dbl.run(qb)
  }

  fromJSON(obj: Maybe<any>): Maybe<M> {
    if (isEmptyObj(obj)) return
    const result = this.model.fromJSON(obj)
    return this.#maybeCache(result)
  }

  fromJSONs(pojos: any[]): M[] {
    return compact(pojos.map(ea => this.fromJSON(ea as any)))
  }

  first(qb: Knex.QueryBuilder): Maybe<M> {
    return this.fromJSON(this.dbl.first(qb))
  }

  firstf(f: (qb: Knex.QueryBuilder) => Knex.QueryBuilder): Maybe<M> {
    return this.first(f(this.query()))
  }

  all(qb: Queryish = this.query()): M[] {
    return this.fromJSONs(this.dbl.all(qb))
  }

  allf(f: (qb: Knex.QueryBuilder) => Knex.QueryBuilder): M[] {
    return this.all(f(this.query()))
  }

  findOne(qb: Knex.QueryBuilder): Maybe<M> {
    return this.fromJSON(this.dbl.first(qb))
  }

  findOneBy(constraint: PartialPojo<M>): Maybe<M> {
    return this.findOne(this.model.query().where(constraint))
  }

  findById(id: Maybe<number>, constraint?: PartialPojo<M>): Maybe<M> {
    return this.findOneBy({ ...constraint, id } as any)
  }

  findByIds(ids: number[]): M[] {
    ids = toA(ids).filter(gt0)
    const cached: M[] = []
    const uncachedIds: number[] = []
    for (const id of ids) {
      const prior = this.$cachedById(id)
      if (prior != null) cached.push(prior)
      else uncachedIds.push(id)
    }
    const results = batches(
      uncachedIds,
      Settings.dbBatchSelectSize.valueOrDefault
    ).map(arr => this.dbl.all(this.query().whereIn("id", arr)))
    return sortBy([...cached, ...this.fromJSONs(flatten(results))], ea =>
      ids.indexOf(ea.id!)
    )
  }

  findBy(constraint: PartialPojo<M>): M[] {
    return this.all(this.model.queryBy(constraint))
  }

  findWhereIn<K extends keyof M>(column: K, values: M[K][]): M[] {
    // SITS TODO: remove the as any cast
    return this.all(this.model.query().whereIn(column as any, values as any))
  }

  rows(qb?: Knex.QueryBuilder): number {
    // don't apply count to qb (so countDistinct() will be respected):
    return this.count(qb ?? this.model.query().count())
  }

  /**
   * Count the rows in the given query,
   */
  count(qb: Knex.QueryBuilder): number {
    return toGt0(this.dbl.pluckFirst(qb)) ?? 0
  }

  countf(f: (qb: Knex.QueryBuilder) => Knex.QueryBuilder): number {
    return this.count(f(this.query()))
  }

  insert(arr: (M | PartialPojo<M>)[]): M[] {
    return toA(arr).map(ea => this.insertOne(ea))
  }

  upsertOne(t: M | PartialPojo<M>) {
    return this.modelSupportsUpsert
      ? this.upsert([t])![0]
      : this.insertOrUpdateOne(t)
  }

  insertOrUpdateOne(t: M | PartialPojo<M>) {
    return t.id == null ? this.insertOne(t) : this.updateOne(t)
  }

  insertOne(t: M | PartialPojo<M>): M {
    if (t.id != null) {
      throw new Error(
        "insert called for " + stringify(t) + NonRetriableErrorFlag
      )
    }
    const m = this.fromJSON(t)
    if (m == null) {
      return this.#logger.throw(
        "insertOne(): fromJSON returned null" + InternalErrorFlag,
        { t }
      )
    }

    // we want this instance to be cached:
    if (m instanceof this.model) this.#maybeCache(m)

    m.$beforeUpsert()
    const dbJson = m.$toDbJSON(this.columnNames())
    const runInfo = this.dbl.runf(q => q.insert(dbJson))
    m.id = toInt(runInfo.lastInsertRowid)
    m.$afterUpsert()
    return m
  }

  unsetField(id: number, fieldName: keyof M) {
    map(this.$cachedById(id), (ea: any) => {
      ea[fieldName] = null
    })
    return this.dbl.runf(q => q.where({ id }).update(fieldName as string, null))
  }

  updateOne(t: M | PartialPojo<M>): M {
    if (t.id == null) {
      throw new Error(
        "update called for " +
          stringify(t) +
          NonRetriableErrorFlag +
          InternalErrorFlag
      )
    }
    const m = this.model.fromJSON(t)
    m.$beforeUpsert()
    const updateRecord = m.$toDbJSON(this.updatableColumnNames())
    this.dbl.runf(q => q.where({ id: t.id }).update(updateRecord))
    // There shouldn't be anything to assign back to `t`.
    m.$afterUpsert()
    return this.#maybeCache(m) ?? m
  }

  /**
   * unique column name (not ID): NOTE THAT THIS MAY BE MULTIPLE COLUMNS (see
   * ProgressMeta: "progressId,name" or AssetTag: "assetId,tagId")
   */
  get ucn(): string {
    return toNotBlank(this.model.$uniqueColumnName) ?? "id"
  }

  // TODO: make this return `(keyof M)[]`
  ucnFieldNames = lazy(() => this.ucn.split(","))

  get modelSupportsUpsert() {
    return this.ucn != null && this.ucn !== "id"
  }

  get ucnConstraint(): string {
    return `(${this.ucn})`
  }

  get immutableColumnNames(): string[] {
    return uniq([...this.ucnFieldNames(), "id", "createdAt"])
  }

  onConflictClause(cols: string[]) {
    const skip = this.immutableColumnNames
    const doUpdate = cols
      .filter(ea => !skip.includes(ea))
      .sort()
      .map(ea => `${ea}=excluded.${ea}`)
      .join(",")
    // https://sqlite.org/lang_UPSERT.html
    const onConflictArr = ["ON CONFLICT", this.ucnConstraint]
    if (blank(doUpdate)) {
      onConflictArr.push("DO NOTHING")
    } else {
      onConflictArr.push("DO UPDATE SET", doUpdate)
    }
    return onConflictArr.join(" ")
  }

  #upsertDbJson(jsons: Record<keyof M, DbValue>[]) {
    const insert = this.query().insert(jsons).toSQL()
    const setKeys = new Set<string>()
    for (const json of jsons) {
      for (const key of keys(json)) {
        setKeys.add(key)
      }
    }
    const sql = insert.sql + " " + this.onConflictClause([...setKeys.values()])
    const upsert = { sql, bindings: insert.bindings as Knex.Value[] }
    // NOTE: lastInsertRowid DOES NOT WORK HERE, as the ID will be from a
    // prior insert if the ON CONFLICT clause runs.
    this.run(upsert)
    for (const json of jsons) {
      const prior = this.$cachedByJson(json)
      if (prior != null) {
        this.#assign(prior, this.model.fromJSON(json as any))
      }
    }
  }

  $pickModelUcn(m: Model): Maybe<PartialPojo<M>> {
    const result: any = {}
    for (const key of this.ucnFieldNames()) {
      const v = (m as any)[key]
      if (v == null) return
      result[key] = v
    }
    return result
  }

  reloadOne(m: M): Maybe<M> {
    if (this.#deletedIds.has((m as any).id)) return
    const bk = this.$pickModelUcn(m)
    if (bk != null) {
      // m should win
      const result = this.#maybeCache(m)
      const fromDb = this.findOneBy(bk)
      this.#assign(result, fromDb)
      if (result !== m) {
        this.#assign(m, result)
      }
      return result
    } else {
      this.#logger.warn(".reloadOne(): failed to reload " + stringify(m))
      return // no business key: TODO: throw?
    }
  }

  reload(arr: M[]): M[] {
    return compact(arr.map(ea => this.reloadOne(ea)))
  }

  upsert(arr: (M | PartialPojo<M>)[], skipReturn = false): M[] {
    if (!this.modelSupportsUpsert) {
      return arr.map(ea => this.insertOrUpdateOne(ea))
    }

    const cols = this.columnNames()
    const result: M[] = []
    for (const batchArr of batches(
      toA(arr),
      Settings.dbBatchUpsertSize.valueOrDefault
    )) {
      const models = compact(batchArr.map(ea => this.fromJSON(ea)))
      for (const ea of models) {
        ea.$beforeUpsert()
      }
      this.#upsertDbJson(models.map(ea => ea.$toDbJSON(cols)))

      for (const ea of models) {
        const m = skipReturn ? ea : this.reloadOne(ea) ?? ea
        m.$afterUpsert()
        result.push(m)
      }
    }
    return result
  }

  delete(ids: number[]): Maybe<RunResult> {
    const arr = toA(ids).filter(gt0)
    for (const ea of arr) {
      this.#deletedIds.add(ea)
    }
    this.#cache.deleteValueIf(ea => arr.includes(ea.id!))
    return this.run(this.model.query().delete().whereIn("id", arr))
  }

  /**
   * @param q the query will have an .orderBy("id", "asc") added.
   */
  batched(opts: {
    onResults: (arr: M[]) => any
    qb: (q: Knex.QueryBuilder) => Knex.QueryBuilder
  }) {
    const limit = Settings.dbBatchSelectSize.valueOrDefault
    let prior: Maybe<M[]>
    let maxId: Maybe<number>
    do {
      prior = this.allf(q => {
        q = opts.qb(q)
        if (maxId != null) q = q.andWhere("id", ">", maxId)
        return q.orderBy("id", "asc").limit(limit)
      })
      if (isNotEmpty(prior)) {
        maxId = Math.max(...prior.map(ea => ea.id!))
        opts.onResults(prior)
      }
    } while (isNotEmpty(prior))
  }
}
