import { isTrue } from "../../fe/Boolean"
import { Maybe } from "../../fe/MaybeTypes"
import { fromEntries, pick } from "../../fe/Object"
import { StrEnumKeys, strEnum } from "../../fe/StrEnum"
import { Settings } from "./Settings"

// Hey, so, why don't we just use Settings.forceSync?

// BECAUSE, future me, sync might be doing multiple tasks simultaneously: the
// call stack is the only place we can ensure tasks don't pollute any other
// concurrent tasks.

const ForceContextKeys = strEnum(
  /**
   * If true, ignore all short-circuiting paths if we think the file or asset
   * is already in sync.
   */
  "forceSync",
  /**
   * If true, don't build previews: we'll do it later.
   */
  "skipPreviews",
  /**
   * If true, always rebuild previews
   */
  "forceRebuildPreviews",
  /**
   * If true, don't build previews: we'll do it later.
   */
  "skipAssetTagging",
  "recountAllTags"
)
export type ForceContextKey = StrEnumKeys<typeof ForceContextKeys>

export type ForceContext = Partial<Record<ForceContextKey, boolean>> & {
  retries?: number
}

export type RequiredForceContext = Required<ForceContext>

export function pickForceContext<T extends ForceContext>(o: T): ForceContext {
  return pick(o, ...ForceContextKeys.values)
}

export function forceContextOrSetting(
  ctx?: Maybe<ForceContext>
): RequiredForceContext {
  return fromEntries(
    ForceContextKeys.values.map(k => [
      k,
      // defer to Settings if ctx is undefined, but remember that not all keys
      // are settings!
      ctx?.[k] ?? isTrue((Settings as any)[k]?.valueOrDefault)
    ])
  )
}
