const logger = lazy(() => mkLogger("child.ChildService"))

/**
 * Find the path the given command.
 *
 * If we're running from mocha, we want callerDirname + cmdBase. If this is
 * running in packaged electron, the script will live in the asar bundle.
 */
export async function pathToService(cmdBase: ServiceName): Promise<BaseFile> {
  const cmd = ensureSuffix(cmdBase, ".js")
  const root = BaseFile.projectRoot()
  const currDir = BaseFile.for(process.cwd())
  const paths = isPacked()
    ? [
        // Expected path for PhotoStructure for Servers:
        root.join("bin", cmd),
        // Expected electron production path is in the root of app.asar:
        isElectron ? root.join("app.asar", cmd) : undefined
      ]
    : [
        // library tests:
        currDir.join("dist", "library", cmd),
        // core tests (worker, logtail, and logcat are in core):
        currDir.join("dist", "core", cmd),
        // desktop tests:
        currDir.join("lib", "library", cmd)
      ]

  // for running out of src/desktop:
  paths.push(currDir.join("dist", "app", cmd))

  for (const ea of paths) {
    if (
      ea != null &&
      true === (await ea.isNonEmptyFile(isPacked() ? KB : 128))
    ) {
      return logger().tap({
        msg: "pathToService()",
        level: "info",
        result: ea,
        meta: { cmd, isPacked, paths: paths.map(toS) }
      })
    }
  }
  return logger().throw("Failed to find path to " + cmdBase, {
    paths,
    fatal: true
  })
}

export interface ChildServiceOpts {
  nodeArgs?: string[]
  args?: string[]
  onData?(obj: Obj): void
  healthy?: () => SyncOrAsync<Maybe<boolean>>
  onPreRestart?: () => any
  /** only for the test service: */
  pathToService?: BaseFile
}

export function inspectPort(cmd: ServiceName): number {
  switch (cmd) {
    case "web":
      return 9223
    case "sync":
      return 9224
    default:
      return randomInt(9225, 9600)
  }
}

/**
 * ChildService configures a WatchedChild for use by a PhotoStructure service,
 * like `web` or `sync`, and adds health checks.
 */
export class ChildService implements Endable {
  static async mk(
    cmd: ServiceName,
    opts: Partial<WatchedChildOpts & WatchedChildListener> &
      ChildServiceOpts &
      Partial<child_process.SpawnOptions> = {}
  ): PromiseMaybe<ChildService> {
    const pathTo = opts.pathToService ?? (await pathToService(cmd))
    if (blank(pathTo)) throw new Error("Failed to find path to " + cmd)
    opts.nodeArgs = toA(opts.nodeArgs)
    return new ChildService(cmd, pathTo, opts)
  }
  readonly name: string
  restartCount = 0

  private readonly logger: Logger
  readonly wc: WatchedChild
  readonly spawnOpts: SpawnOptionsWithLocale

  readonly endTimeoutMs: Maybe<number>

  private constructor(
    readonly serviceName: ServiceName,
    readonly cmd: BaseFile,
    readonly opts: Partial<WatchedChildOpts & WatchedChildListener> &
      ChildServiceOpts &
      Partial<SpawnOptionsWithLocale>
  ) {
    this.name = "ChildService(" + serviceName + ")"
    this.endTimeoutMs = serviceExitTimeoutMs(serviceName)
    this.logger = mkLogger(this.name)
    const spawnArgs = [...toA(opts.nodeArgs), cmd.nativePath]
    if (isNotEmpty(opts.args)) {
      spawnArgs.push(...opts.args)
    }
    this.spawnOpts = pick(
      this.opts,
      "argv0",
      "cwd",
      "detached",
      "env",
      "gid",
      "shell",
      "stdio",
      "timeout",
      "uid",
      "windowsHide",
      "windowsVerbatimArguments",
      "forceCLocale"
    )
    // We don't want to force the locale for photostructure services
    this.spawnOpts.forceCLocale = false
    const maxAgeMs = -1
    this.wc = new WatchedChild({
      name: serviceName,
      childFactory: async () => {
        if (this.restartCount > 0 && this.opts.onPreRestart != null) {
          await this.opts.onPreRestart()
        }
        this.restartCount++
        return spawn(process.execPath, spawnArgs, maxAgeMs, this.spawnOpts)
      },
      // other things may rely on the child to get cleanup work done:
      endableRank: EndableRanks.service,
      endTimeoutMs: this.endTimeoutMs,
      onStdout: this.onStdout.bind(this),
      onStderr: this.onStderr.bind(this),
      onError: orElse<WatchedChildListener["onError"]>(
        this.opts.onError,
        () => (ea: any) => true !== isIgnorableError(ea) // < restart on any error emitted to stderr or stdout
      ),
      ignoreStopErrors: false,
      exitCommand: ChildServiceExitCommand,
      ...opts
    })

    addEndable(EndableRanks.first, this)
  }

  get startMs() {
    return this.wc.startTs
  }

  private onStderr(data: string) {
    const obj = parseJSON(data)
    this.logger.warn(".onStderr()", obj ?? data)
    return obj?.fatal === true
  }

  private onStdout(data: string) {
    if (blank(data)) return
    try {
      const obj = parseJSON(data)
      if (obj == null) {
        this.logger.info(".onStdout(): non-JSON payload", data)
        return
      }
      if (true === obj?.fatal) {
        void this.wc.onError(this.name + ".onStdout()", obj)
      } else if (null != this.opts.onData) {
        this.opts.onData(obj)
      }
    } catch (err) {
      // Pass it on:
      console.log(data)
      // Don't grump too much:
      // this.logger.warn("invalid JSON: " + err, data)
      // this.wc.onError("onStdout()", err)
    }
  }

  start() {
    return this.wc.start()
  }

  stop() {
    return this.wc.stop()
  }

  restart(force?: boolean) {
    return this.wc.restart(force)
  }

  get ended() {
    return this.wc.ended
  }

  async end() {
    if (!this.wc.ended) this.write(ChildServiceExitCommand)
    return this.wc.end()
  }

  get pid() {
    return this.wc.pid
  }

  running() {
    return this.wc.running()
  }

  notRunning() {
    return this.wc.notRunning()
  }

  write(toStdin: string, retries = 2): boolean {
    if (retries < 0) {
      this.logger.warn("write(): no more retries", { toStdin })
      return false
    }

    // Don't check for ended, because end() uses this.
    try {
      const childProc = this.wc.proc
      if (true !== childProc?.stdin?.writable) {
        this.logger.warn("write(): childProc isn't open, ignoring", { toStdin })
        return false
      }
      return childProc.stdin.write(ensureSuffix(toStdin, "\n"))
    } catch (err) {
      this.logger.warn("write(): caught " + err)
      // The only reason for an error here is if the proc is gone.
      // onError returns the result of restart--if that is successful,
      // try again.
      this.wc.onError("onStdout()", toErr(err)!)
      return this.write(toStdin, retries - 1)
    }
  }
}
