import { map2Or } from "../Maybe"
import { Maybe } from "../MaybeTypes"
import { isNumber } from "../Number"
import { AssetId } from "./Asset"

export interface ID {
  id: number
  v: number
}

export type IDish = ID | number | AssetId | { tagId: number }

/**
 * @returns numeric id
 */
export function id2id(id: Maybe<IDish>): Maybe<number> {
  const a = id as any
  // ids may be negative for stream placeholders, so don't use gt0:
  return isNumber(id)
    ? id
    : isNumber(a.id)
    ? a.id
    : isNumber(a.assetId)
    ? a.assetId
    : isNumber(a.tagId)
    ? a.tagId
    : undefined
}

/**
 * An ID is "equal" to a number if the number equals the `id`.
 *
 * If both are numbers, or both are instances of ID, use `eql`.
 */
export function idEql(a: IDish, b: IDish): boolean {
  return map2Or(
    id2id(a),
    id2id(b),
    (i, j) => i === j,
    () => false
  )
}
