import { DateTime, Zone } from "luxon"
import { lazy } from "../../core/Lazy"
import { localToDate } from "../../fe/Date"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { isNumber, toGt0 } from "../../fe/Number"
import { Settings } from "../settings/Settings"
import { DateObject, dateObjectToDateTime } from "./Date"
import { Dated, datedToDateTime, isoToDated } from "./Dated"
import { datedToMillis } from "./DatedToMillis"
import { shortDateTimeFormat } from "./ExtendedDate"
import { FuzzyDate } from "./FuzzyDate"
import {
  getCentisecond,
  getDay,
  getHour,
  getMinute,
  getMonth,
  getSecond,
  getYear
} from "./GetDateField"
import { hasTime } from "./HasTime"
import { normalizeZone } from "./Timezone"

const datesBeforeAreEstimated = lazy(
  () =>
    isoToLocal(Settings.datesBeforeAreEstimated.valueOrDefault) ??
    isoToLocal(Settings.datesBeforeAreEstimated.defaultValue)!
)

export function localIsFuzzy(localCs: Maybe<number>): boolean {
  return (
    localCs == null ||
    // If no minute, second, or centisecond, it's fuzzy
    localCs % 100_00_00 === 0 ||
    localCs < datesBeforeAreEstimated()
  )
}

export function isoToLocal(iso: Maybe<string>): Maybe<number> {
  return map(isoToDated(iso), datedToLocal)
}

export function maybeDatedToLocal(d: Maybe<Dated | number>): Maybe<number> {
  return map(d, datedToLocal)
}

export function datedToLocal(d: Dated | number): number {
  if (isNumber(d)) return tsToLocal(d)
  const s = datedToLocalSec(d)
  return s * 100 + (getCentisecond(d) ?? 0)
}

export function datedToLocalSec(d: Dated | number): number {
  if (isNumber(d)) return Math.floor(tsToLocal(d) / 100)
  let result = 0
  for (const f of [getYear, getMonth, getDay, getHour, getMinute, getSecond]) {
    result = result * 100 + (f(d) ?? 0)
  }
  return result
}

export function localToDateObject(
  local: Maybe<number>,
  offset?: Maybe<string | number | Zone>
): Maybe<DateObject> {
  if (local == null || local < 0) return
  let i = local
  const pop2 = () => {
    const result = i % 100
    i = Math.floor(i / 100)
    return result
  }
  const millisecond = 10 * pop2() // < because centiseconds
  const second = pop2()
  const minute = pop2()

  // DateTime freaks out if hour, day, or month is 0.
  const hour = toGt0(pop2())
  const day = toGt0(pop2())
  const month = toGt0(pop2())

  // the remainders are the year
  const year = i

  return {
    year,
    month,
    day,
    hour,
    minute,
    second,
    millisecond,
    zone: normalizeZone(offset)
  }
}

export function fmtLocalDateShort(
  local: number,
  locale: Maybe<string>
): string {
  return shortDateTimeFormat(locale).format(localToDate(local))
}

export function localToDateTime(
  local: Maybe<number>,
  offset?: Maybe<number>
): Maybe<FuzzyDate | DateTime> {
  const obj = localToDateObject(local, offset)
  return hasTime(obj) ? dateObjectToDateTime(obj) : FuzzyDate.for(obj)
}

/**
 * Convert local numeric to millis-from-common-epoch timestamp
 */
export function localToTs(
  local: Maybe<number>,
  offset?: Maybe<number>
): Maybe<number> {
  return map(localToDateTime(local, offset), datedToMillis)
}

export function nowLocal(zone?: Maybe<string | Zone>) {
  return agoLocal(0, zone)
}

export function agoLocal(agoMs: number, zone?: Maybe<string | Zone>) {
  return tsToLocal(Date.now() - agoMs, zone)
}

/**
 * This should only be used by tests, as a timestamp has already lost it's
 * relevant timezone offset.
 * @param ts is a millis-from-common-epoch timestamp
 */
export function tsToLocal(ts: number, zone?: string | Zone): number {
  return datedToLocal(DateTime.fromMillis(ts, { zone }))!
}

/**
 * @return local numeric of `local` plus `ms` millis
 */
export function localPlusMs(local: number, ms: number): number {
  return datedToLocal(datedToDateTime(localToDateTime(local))!.plus(ms))!
}
