const logger = lazy(() => mkLogger("date.FuzzyDateParser"))

export interface FuzzyDateParserOptions {
  fuzzyDateParsing?: boolean
  fuzzyYearParsing?: boolean
}

export function extractDateFromPath(path: string[]): Maybe<Dated> {
  return logger().tap({
    msg: "extractDateFromPath",
    result: FuzzyDateParser.instance().extractDateFromPath(path),
    meta: { path }
  })
}

export function parseYMD(s: string): Maybe<FuzzyDate | ExifDateTime> {
  return FuzzyDateParser.instance().parseYMD(s)
}

export function parseFuzzyDate(s: string): Maybe<FuzzyDate> {
  return FuzzyDateParser.instance().parse(s)
}

// We're using literal RegExps here so syntax highlighting can help find
// issues:

// negative numeric lookbehind to prevent "1992 AUG 1971" from being parsed as
// "2 AUG 1971":
export const nonNumericLookbackRE = /(?<!\d)/

export const yearRE = /(?<year>(?:19|20)\d{2})/ // 1975, 2014
export const yearishRE = /(?<year>(?:19|20)?\d{2})/ // 1975, 2014

export const monthRE = /(?<month>[0-3]\d)/ // < requires 0-padded month
export const monthishRE = /(?<month>[0-3]?\d)/

export const dayRE = /(?<day>[1-3]\d|0[1-9])/ // < requires 0-padded day
export const dayishRE = /(?<day>[1-3]\d|0?[1-9])/

export const hourRE = /(?<hour>1\d|2[0-3]|0\d)/

export const minuteRE = /(?<minute>[0-5]\d)/

export const secondRE = /(?<second>[0-5]\d)/

export const seps = /([-.,:_/ |])/
export const optSeps = new RegExp(seps.source + "?")

export const subsecRE = /(?<subsec>\.\d+)?/

const PatternToRE = lazy(() =>
  entries({
    " ": /\s+/,
    "-": /-/,
    _: /_/,
    ".": /\./,
    ",": /,/,
    yyyy: /\d{4}/,
    yy: /\d+/,
    y: /\d+/,
    MMMM: /\S{3,12}/,
    MM: /\d{2}/,
    M: /\d+/,
    dd: /\d{2}/,
    d: /\d+/,
    HH: /\d{2}/,
    H: /\d+/,
    mm: /\d{2}/,
    m: /\d+/,
    ss: /\d{2}/,
    s: /\d+/,
    ZZ: TimezoneOffsetRE(),
    "'Z'": /Z/,
    "'at'": /at/
  }).map(
    ([token, re]) =>
      // we need to remove the names from the capture groups--we don't need
      // them, and we may get spurious duplicate group name errors otherwise:
      [token, new RegExp(re.source.replace(/\(\?<.+?>/g, "("))] as const
  )
)

function token2re(s: string) {
  for (const [token, re] of PatternToRE()) {
    if (s.startsWith(token)) return { token, re }
  }
  return
}

const formatRegexCache = new FifoCache<RegExp | null>(256)

export function regexFromDateFormat(dateFormat: string): RegExp | null {
  let s = dateFormat
  return formatRegexCache.getOrSet(dateFormat, () => {
    const arr: RegExp | string | RegExpEscaped[] = []
    while (!blank(s)) {
      const ea = token2re(s)
      if (ea == null) {
        logger().debug(
          "regexFromDateFormat(): unknown token starting with " +
            s +
            ", bailing",
          { dateFormat }
        )
        return null
      }
      arr.push(ea.re)
      s = s.slice(ea.token.length)
    }
    const result = concatRegexp([
      new RegExpEscaped("("),
      ...arr,
      new RegExpEscaped(")")
    ])
    logger().trace("regexFromDateFormat()", {
      dateFormat,
      result: result.source
    })
    return result
  })
}

export function dateTimeBetween(a: DateTime, b: DateTime): DateTime {
  // When would this ever be invalid (and therefor null)?

  // Assuming it can't ever be invalid, we're bang-ing the end. Yeah. We're
  // end bangers over here. JOIN US WE KNOW YOU ARE BANGCURIOUS
  return a.until(b).divideEqually(2)[0].end!
}

export function agoMs(dated: Maybe<Dated>): Maybe<number> {
  return mapNumeric(datedToMillis(dated), ea => Date.now() - ea)
}

export const ISO_YMD_RE = concatRegexp([
  /^/,
  yearRE,
  RegExpOptional.from(/-/, monthRE, RegExpOptional.from(/-/, dayRE)),
  /$/
])

export const ISO_YMD_LAX_RE = concatRegexp([
  /^/,
  yearishRE,
  RegExpOptional.from(/-/, monthishRE, RegExpOptional.from(/-/, dayishRE)),
  /$/
])

export function parseDateTime(
  s: Maybe<string> | Maybe<string>[],
  defaultZone?: string
): Maybe<DateTime> {
  const d = parseDated({
    input: s,
    defaultZone,
    descPredicate: ea => ea.match(/DateTime/i) != null
  })

  return isDateTime(d) ? d : isExifDateTime(d) ? d.toDateTime() : undefined
}

function extractDateTime(
  input: string,
  format: string,
  defaultZone?: string
): Maybe<ExifDateTime> {
  const d = parseExifDateTimeFormat({
    input,
    format,
    defaultZone
  })
  if (d != null) return d
  const str = regexFromDateFormat(format)?.exec(input)?.[0]
  if (str != null) {
    return parseExifDateTimeFormat({
      input: str,
      format,
      defaultZone
    })
  }
  return
}

function* parsers(args: {
  defaultZone: Maybe<string>
  includeDate?: boolean
  includeFuzzyDate?: boolean
}) {
  yield {
    desc: "DateInterval.fromISO",
    f: (ea: string) => DateInterval.fromISO(ea)
  }

  // date and time:
  yield {
    desc: "parseExifDateTime",
    // this calls ExifDateTime.fromISO and ExifDateTime.fromEXIF
    f: (ea: string) => parseExifDateTime(ea, args.defaultZone)
  }

  yield { desc: "ExifDateTime.fromRFC2822", f: parseExifDateTimeFromRFC2822 }

  for (const format of compactBlanks(Settings.extraDateTimeFormats.values)) {
    yield {
      desc: `extractDateTime(${format})`,
      f: (ea: string) => extractDateTime(ea, format, args.defaultZone)
    }
  }

  if (args.includeDate === true) {
    // HEY FUTURE ME DO NOT RE-ADD JS DATE PARSING it forces everything to UTC
    yield {
      desc: "ExifDate.fromEXIF",
      f: (ea: string) => ExifDate.fromEXIF(ea)
    }
  }

  if (args.includeFuzzyDate === true) {
    for (const parser of FuzzyDateParser.instance().allParsers) {
      yield {
        desc: "FuzzyDateParser " + parser.source,
        f: (ea: string) => parser.apply(ea)
      }
    }
  }
}

/**
 * Parses ISO, EXIF, and screenshot-formatted dates.
 */
export function parseDated({
  input,
  defaultZone,
  includeDate = true,
  includeFuzzyDate = true,
  descPredicate = () => true
}: {
  input: MaybeNull<Dated | string | Maybe<string>[]>
  defaultZone?: string
  /**
   * Include ISO-compliant YMD parsers (no time component)
   */
  includeDate?: boolean
  /**
   * Include fuzzy YMD parsers (no time component)
   */
  includeFuzzyDate?: boolean
  descPredicate?: (desc: string) => boolean
}): Maybe<Dated> {
  if (input == null) return
  if (isDated(input)) return input
  const arr = uniq(compactBlanks(toA(input)).map(stripDSC))
  if (arr.length === 0) return
  // date interval (must be first):
  for (const { desc, f } of parsers({
    defaultZone,
    includeDate,
    includeFuzzyDate
  })) {
    if (descPredicate(desc)) {
      for (const s of arr) {
        const result = f(s)
        if (result == null || !isValidDate(result)) continue
        // Don't set zone here: that was the parser's job.
        return logger().tap({
          msg: "parseDated()",
          result,
          meta: {
            s,
            desc,
            defaultZone
          }
        })
      }
    }
  }
  return
}

export class FuzzyDateParser {
  static readonly instance = lazy(() => new FuzzyDateParser())

  readonly ymdParsers: Parser[] = []
  readonly ymParsers: Parser[] = []
  readonly yParsers: Parser[] = []
  readonly allParsers: Parser[] = []

  constructor(readonly opts: FuzzyDateParserOptions = {}) {
    if (opts.fuzzyDateParsing == null) {
      Settings.fuzzyDateParsing.watchLater(() => this.#setup())
    }
    if (opts.fuzzyYearParsing == null) {
      Settings.fuzzyYearParsing.watchLater(() => this.#setup())
    }
    this.#setup()
  }

  get fuzzyDateParsing() {
    return (
      this.opts.fuzzyDateParsing ?? Settings.fuzzyDateParsing.valueOrDefault
    )
  }

  get fuzzyYearParsing() {
    return (
      this.opts.fuzzyYearParsing ?? Settings.fuzzyYearParsing.valueOrDefault
    )
  }

  parse(input: string, arr = this.allParsers): Maybe<FuzzyDate> {
    for (const parser of arr) {
      const result = parser.apply(input)
      if (result != null) {
        return result
      }
    }
    return
  }

  parseYMD(input: string): Maybe<FuzzyDate> {
    return this.parse(input, this.ymdParsers)
  }

  parseYM(input: string): Maybe<FuzzyDate> {
    return this.parse(input, this.ymParsers)
  }

  parseY(input: string): Maybe<FuzzyDate> {
    return this.parse(input, this.yParsers)
  }

  #addParser(arr: RegExp | (RegExp | string | RegExpEscaped)[]) {
    const p = new Parser(arr)
    if (p.hasDay) {
      this.ymdParsers.push(p)
    } else if (p.hasMonth) {
      if (this.fuzzyDateParsing) {
        this.ymParsers.push(p)
      }
    } else if (this.fuzzyYearParsing) {
      this.yParsers.push(p)
    }
  }

  #setup() {
    try {
      this.ymdParsers.length = 0
      this.ymParsers.length = 0
      this.yParsers.length = 0
      this.allParsers.length = 0

      const allMonthsRE = new RegExpEscaped(
        "(?<monthname>" + monthNames().map(escapeRegExp).join("|") + ")"
      )

      // Very conservative/restrictive patterns to guard against mmddyy or ddmmyy:
      // ISO 8601: 1999-11-11 (the /\2/ ensures consistent seps).
      // Seps are required if month and day aren't padded:
      // eslint-disable-next-line regexp/strict
      this.#addParser([yearRE, seps, monthishRE, /\2/, dayishRE])

      // If month and day are padded, seps are optional:
      // eslint-disable-next-line regexp/strict
      this.#addParser([yearRE, optSeps, monthRE, /\2/, dayRE])

      // 2011:Feb:6
      // eslint-disable-next-line regexp/strict
      this.#addParser([yearRE, optSeps, allMonthsRE, /\2/, dayishRE])

      // August 6, 1994
      // eslint-disable-next-line regexp/strict
      this.#addParser([allMonthsRE, optSeps, dayishRE, /,?\2/, yearRE])

      // 6 August 1994
      this.#addParser([
        nonNumericLookbackRE,
        dayishRE,
        optSeps,
        allMonthsRE,
        // eslint-disable-next-line regexp/strict
        /,?\2/,
        yearRE
      ])

      // This needs to be the last ymdParser, as m and d are optional:
      // See https://forum.photostructure.com/t/photostructure-ignored-the-date-metadata-of-some-photos-marked-them-as-taken-in-2002/340/17?u=mrm
      this.#addParser([
        /^(?<year>\d\d\d\d):(?<month> {2}|\d\d):(?<day> {2}|\d\d)( ( {2}|00):( {2}|00):( {2}|00))?$/
      ])

      // The following are only year + month (no date):
      // 1994-08
      this.#addParser([yearRE, seps, monthishRE])

      // Sept 1994 or September 1994
      this.#addParser([allMonthsRE, seps, yearRE])

      // 2001 August or 2001 AUG
      this.#addParser([yearRE, seps, allMonthsRE])

      this.#addParser([nonNumericLookbackRE, yearRE])

      this.allParsers.push(
        // order matters! We want the most specific parsers first:
        ...this.ymdParsers,
        ...this.ymParsers,
        ...this.yParsers
      )
    } catch (e) {
      console.error(e)
    }
  }

  extractDateFromPath(path: string[]): Maybe<Dated> {
    if (!Settings.usePathsToInferDates.valueOrDefault) return

    // https://en.wikipedia.org/wiki/Design_rule_for_Camera_File_system
    ignorableSubpaths.forEach(ea => {
      if (startsWith(path, ea)) {
        path.splice(0, ea.length)
      }
    })

    // No one (!!) will ever encode only the year in the filename--we want to either pull the date from the basename, or from the dirname, but not both.

    path = path.filter(
      // If it's a camera-provided serial number, ignore that part of the path.
      ea => notBlank(ea) && path[0].match(ignorableDCF) == null
    )
    // Don't modify path!
    const reversePath = [...path].reverse()

    // First try to find a YMD in one of the path elements individually:
    for (const ea of reversePath) {
      const result =
        parseDated({
          input: ea,
          includeDate: false,
          includeFuzzyDate: false
        }) ?? this.parseYMD(ea)
      if (result != null) return result
    }
    return (
      // Then try to find YMD by joining the paths (reverse first, so nearer
      // datestamps get picked first)
      this.parseYMD(reversePath.slice(1, 4).join(" ")) ??
      // is it a path like 2019/08/01?
      this.parseYMD(path.slice(0, -1).join(" ")) ??
      // the ymParsers will be empty if fuzzy date parsing is off
      first(reversePath, ea => this.parseYM(ea)) ??
      this.parseYM(path.join(" ")) ??
      // we're at the bottom of the barrel here. Does any path element contain a valid year?
      first(reversePath.slice(1), ea => this.parseY(ea))
    )
  }
}

class Parser {
  readonly regex: RegExp
  constructor(arr: RegExp | Maybe<RegExp | string | RegExpEscaped>[]) {
    // We always add a negative lookahead to the end of the regex to avoid
    // matching against long numeric strings:
    this.regex =
      arr instanceof RegExp ? arr : concatRegexp([...arr, /(?:$|\D)/], "i")
  }

  get source() {
    return this.regex.source
  }

  get hasDay() {
    return this.source.includes("(?<day>")
  }

  get hasMonth() {
    return (
      this.source.includes("(?<month>") || this.source.includes("(?<monthname>")
    )
  }

  apply(s: string) {
    const g = this.regex.exec(s)?.groups
    if (g == null) return
    return FuzzyDate.for({
      year: toInt(g.year),
      month: toInt(g.month) ?? monthName2index(g.monthname),
      day: toInt(g.day)
    })
  }
}

const ignorableDCF =
  /^((DCIM)|(DSC[_F]?|IMG[-_]|GOPRO|MOV[-_]|MVI[-_]|P_?)\d+)$/i
