import sqlite from "better-sqlite3"
import { max } from "../../core/Array"
import { lazy } from "../../core/Lazy"
import { isSyncService } from "../../core/ServiceNames"
import { EndableRanks, ending } from "../../core/async/Endable"
import { EndableWrapper } from "../../core/async/EndableWrapper"
import { time } from "../../core/async/PromiseTimer"
import { filestamp } from "../../core/date/Filestamp"
import { DatabaseWithUid } from "../../core/db/DatabaseWithUid"
import { dbBackupCold_ } from "../../core/db/DbBackupCold"
import { handleDbRetries } from "../../core/db/DbRetries"
import { mkdb_ } from "../../core/db/MkDb"
import { isDirSQLiteReadWrite } from "../../core/db/SQLiteReadWrite"
import { Schema } from "../../core/db/Schemas"
import { WrappedError } from "../../core/error/WrappedError"
import { PosixFile } from "../../core/fs/PosixFile"
import { HealthCheck } from "../../core/health/HealthCheck"
import { Settings } from "../../core/settings/Settings"
import { minuteMs, secondMs } from "../../fe/Date"
import { Maybe } from "../../fe/MaybeTypes"
import { fmtPct, gt0 } from "../../fe/Number"
import { pick } from "../../fe/Object"
import { randomChars } from "../../fe/Random"
import { Migration } from "./Migration"
import { checkpoint_, escStr, repairDbFile_, verifyDb_ } from "./SQLite"
import { localTableInfo } from "./TableInfo"

/**
 * Manages a SQLite database connection. If the connection closes (due to
 * external vacuuming), it will be automatically re-opened.
 */
export class Db extends EndableWrapper {
  #count = 0
  #db: Maybe<DatabaseWithUid>
  readonly endTimeoutMs = minuteMs

  /**
   * @param _dataDir if not given, use `libraryDataDir()`
   */
  constructor(
    readonly schema: Schema,
    readonly dbFile: PosixFile
  ) {
    super("db.Db(" + schema + ")", () => this.closeDb(), EndableRanks.db)
  }

  toJSON() {
    return pick(this, "schema", "dbFile")
  }

  get open() {
    return this.#db != null && this.#db.open
  }

  get inTransaction() {
    return this.open && true === this.#db?.inTransaction
  }

  prepare(source: string) {
    return this.db.prepare(source)
  }

  pragma(pragma: string, options?: sqlite.PragmaOptions): any {
    return this.logger.tapThunk({
      msg: "PRAGMA " + pragma,
      result: () => this.db.pragma(pragma, options)
    })
  }

  get db(): DatabaseWithUid {
    const priorWasNull = this.#db == null
    if (!this.open) {
      this.logger.info("setting up new db connection to " + this.dbFile, {
        priorWasNull
      })
      try {
        this.#db = mkdb_({ nativePath: this.dbFile.nativePath })
      } catch (cause) {
        throw new WrappedError("Failed to set up " + this.dbFile, {
          cause,
          path: this.dbFile.nativePath
        })
      }
    }
    if (
      isSyncService() &&
      Settings.dbAutoVacuumMode.valueOrDefault === "INCREMENTAL" &&
      gt0(Settings.dbWalAutoCheckpoint.valueOrDefault) &&
      ++this.#count % Settings.dbWalAutoCheckpoint.valueOrDefault === 0
    ) {
      this.#db?.pragma("incremental_vacuum")
    }
    return this.#db!
  }

  verify_() {
    return verifyDb_(this.db)
  }

  assertHeartbeat_() {
    const str = randomChars(64)
    const now = Date.now()
    try {
      this.db
        .prepare(
          "INSERT INTO heartbeat (name, createdAt, updatedAt) VALUES (?,?,?)"
        )
        .run(str, now, now)

      const row = this.db
        .prepare("SELECT * FROM heartbeat WHERE name = ?")
        .get(str) as any

      if (row?.name !== str) {
        throw new Error("heartbeat INSERT/SELECT roundtrip failed")
      }
    } finally {
      try {
        this.db.prepare("DELETE FROM heartbeat WHERE name = ?").run(str)
      } catch {
        // ignore
      }
    }
  }

  closeDb() {
    try {
      if (true === this.#db?.open) {
        this.logger.info("closing db", this.#db)
        this.#db?.close()
      }
    } catch (err) {
      this.logger.warn("closeDb(): .close() failed", err)
    }
    this.#db = undefined
  }

  readonly onRetry = lazy(
    () => this.closeDb(),
    Settings.maxBusyDbMs.valueOrDefault / 4
  )

  #maxUpdatedAt(...tablenames: string[]): Maybe<number> {
    const arr: number[] = []
    for (const table of tablenames) {
      try {
        arr.push(
          this.db
            .prepare("SELECT MAX(updatedAt) FROM " + escStr(table))
            .pluck()
            .get() as number
        )
      } catch (error) {
        this.logger.warn(
          `#maxUpdatedAt(): failed to get max updatedAt for ${table}`,
          error
        )
      }
    }
    return max(arr)
  }

  readonly maxUpdatedAt = lazy(
    () =>
      this.schema === "models"
        ? this.#maxUpdatedAt("Asset", "AssetFile", "Example")
        : 0,
    5 * secondMs
  )

  async vacuum() {
    try {
      await handleDbRetries(
        () => time("db.checkpoint", () => checkpoint_({ db: this.db })),
        this.onRetry
      )
      await handleDbRetries(
        () => time("db.optimize", () => this.db.pragma("OPTIMIZE")),
        this.onRetry
      )
      await handleDbRetries(
        () => time("db.vacuum", () => this.db.exec("VACUUM")),
        this.onRetry
      )
    } catch (error) {
      this.logger.warn("vacuum(): failed", { error })
      throw error
    }
  }

  /**
   * Caller should have an exclusive lock on the database to prevent
   * concurrent access.
   */
  async repair_() {
    this.logger.warn("repair_(): Attempting restore of " + this.dbFile)
    this.closeDb()
    await repairDbFile_(this.dbFile)
    this.logger.info("repair_(): Database restoration was successful!")
  }

  async #backupHot_(destFile: PosixFile) {
    await destFile.parent().mkdirp_()
    await this.db.backup(destFile.nativePath, {
      progress: ea => {
        this.logger.log(
          ending() ? "info" : "debug",
          "backup() " +
            fmtPct(100 - (ea.remainingPages / ea.totalPages) * 100) +
            " completed"
        )
        // This is the number of pages to backup per callback
        return ending() ? 1000 : 100
      }
    })
  }

  async backup_(destDir: PosixFile, isDestDirRW?: boolean): Promise<PosixFile> {
    const isRW = isDestDirRW ?? (await isDirSQLiteReadWrite(destDir))
    const hotDestDb = (
      isRW ? destDir : this.dbFile.parent().join("backups", filestamp())
    ).join(this.dbFile.base)
    await this.#backupHot_(hotDestDb)
    if (!isRW) {
      await dbBackupCold_(hotDestDb, destDir)
    }
    return destDir.join(this.dbFile.base)
  }

  readonly migrate_ = lazy(async () => {
    let repaired = false
    const file = this.dbFile
    if (this.dbFile == null) {
      throw new Error("Cannot migrate database: missing db")
    }
    let dbSetupError
    if (await this.dbFile.isNonEmptyFile()) {
      try {
        verifyDb_(this.db) // < this will fail if the db is borked
      } catch (error) {
        this.logger.warn("verifyDb failed before attempting Migration.", {
          error
        })
        dbSetupError = error
      }
    }

    if (dbSetupError != null || Settings.dbForceRecover.valueOrDefault) {
      HealthCheck.addLoadingMsg("Repairing database...")
      await this.repair_()
      repaired = true
    }

    const migration = new Migration(this.schema, this.db, file)
    const appliedMigrations = await migration.apply_()
    return this.logger.tap({
      msg: "migrate_",
      result: { appliedMigrations, migration, repaired }
    })
  })

  readonly tableInfo_ = lazy(() => localTableInfo(this.db))
}
