import { pathToDb } from "../../core/db/Schemas"
import { libraryDataDirPosixFile } from "../../core/dir/LibraryDirs"
import { SimpleFileOrString } from "../../core/fs/SimpleFile"
import { lazy } from "../../core/Lazy"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { Db } from "../db/Db"

export const modelDb = lazy<Db>(() => undefined as any)

export function libraryModelDbFile(libraryDir?: Maybe<SimpleFileOrString>) {
  return map(libraryDataDirPosixFile(libraryDir), ldd =>
    pathToDb(ldd, "models")
  )
}
