import { CountingSet } from "../CountingSet"
import { Settings } from "../settings/Settings"
import { toPathnames } from "./Path"
import { SimpleFileOrString } from "./SimpleFile"

/**
 * nave detection of symlink loops
 */
export function seemsLikeSymlinkLoop(s: SimpleFileOrString): boolean {
  const cs = new CountingSet()
  for (const ea of toPathnames(s)) {
    cs.incr(ea.toLowerCase().normalize())
  }
  return cs.max() > Settings.maxDuplicatePathElements.valueOrDefault
}
