export const tagFtsRebuild = lazy(
  () => time("db.rebuildTagFts", () => TagFts.rebuild()),
  minuteMs
)

/**
 * This should only be run by integration tests or at the end of a sync.
 */
export async function forceRunTagMaintenance(
  args: ForceContext = { forceSync: true, recountAllTags: true }
) {
  logger().info("forceRunTagMaintenance()")
  return await runTagMaintenance.force(args)
}

const MinCallDelayMs = 5 * secondMs

export const runTagMaintenance = rateLimited({
  name: "runTagMaintenance",
  minCallDelayMs: MinCallDelayMs,
  f: async (_ctx?: ForceContext) => {
    const ctx = forceContextOrSetting(_ctx)
    const start = Date.now()
    const log = logger().addContext(".runTagMaintenance()")
    log.info("starting", { ctx })
    try {
      log.info("validating all tags...")
      Tag.validate_()

      log.info("updating tag mountpoints...")
      await updateTagMountpoints()

      log.info("vacuuming orphan tags...")
      await vacuumOrphanTags()

      let countedTags = false
      if (ctx.recountAllTags) {
        if (ctx.forceSync || updateAllTagCounts.isStale()) {
          log.info("recounting all tags...")
          updateAllTagCounts.refresh()
          countedTags = true
        }
      }
      if (!countedTags) {
        log.info("recounting changed tags...")
        const updatedTagIds = updateChangedTagCounts(ctx)
        countedTags = isNotEmpty(updatedTagIds)
      }

      if (countedTags) {
        log.info("rebuilding tag search index...")
        await tagFtsRebuild()
      }
    } catch (err: any) {
      logger().warn("runTagMaintenance() failed", err)
    }
    const elapsedMs = Date.now() - start
    // We want to run this often, but not so often as to cause no other work
    // to be done.
    const newMinCallDelayMs = clamp(MinCallDelayMs, minuteMs, elapsedMs * 5)
    runTagMaintenance.setMinCallDelayMs(newMinCallDelayMs)
    logger().info("runTagMaintenance(): complete.", {
      newMinCallDelayMs,
      elapsedMs
    })
  }
})

function updateChangedTagCounts(ctx: RequiredForceContext, lagtimeMs?: number) {
  // return timeSync("db.updateChangedTagCounts", () => {
  const nullTagIds = Tag.withNullAssetCount()
  logger().info("updateChangedTagCounts()", { nullTagIds })
  const changedTags = ChangedTag.getAllChangedTags(0)
  logger().info("updateChangedTagCounts()", { changedTags })
  const changedTagsById = new Map<number, ChangedTagPojo | null>(
    nullTagIds.map(ea => [ea, null])
  )
  for (const ct of changedTags) {
    changedTagsById.set(ct.tagId, ct)
  }
  const maxUpdatedAt =
    Date.now() - (lagtimeMs ?? (ctx.forceSync ? 0 : minuteMs))

  // TODO: figure out what the correct value is (not just "20")
  if (changedTagsById.size > 20 && updateAllTagCounts.isStale()) {
    logger().warn(
      "updateChangedTagCounts(): too many changed tags. Rebuilding everything."
    )
    updateAllTagCounts()
  } else {
    for (const tagId of changedTagsById.keys()) {
      Tag.updateAssetCount(tagId)
    }
  }

  for (const changedTag of compact(changedTagsById.values())) {
    if (changedTag != null && lt(changedTag.updatedAt, maxUpdatedAt)) {
      const result = ChangedTag.deleteChangedTag(changedTag)
      logger().debug("updateChangedTagCounts(): removing updated tag", {
        changedTag,
        result
      })
    }
  }
  return [...changedTagsById.keys()]
  // })
}

// only exported for tests
export function updateTagCountSql(rnd = randomChars(6)) {
  // closure tree table without a depth/level column:
  const ct = `temp.closure_tree_${rnd}`

  // tag counts table
  const tc = `temp.tag_counts_${rnd}`

  // This takes 6 seconds on nuc test library:
  // CREATE TABLE ${ct} AS
  // SELECT
  // DISTINCT t.id AS parentId,
  // kid.id AS childId
  // FROM
  // Tag t
  // JOIN Tag kid ON kid._path LIKE (t._path || '%');

  // The CTE takes 0.161 seconds on nuc test library (!!)

  return prepQueries(`
CREATE TABLE ${ct} AS 
WITH RECURSIVE cte AS (
  SELECT
    id AS anc_id,
    id AS id
    -- ,0 AS LEVEL
  FROM
    Tag
  UNION
  ALL
  SELECT
    prev.anc_id,
    curr.id AS id
    -- ,prev.level + 1 AS LEVEL
  FROM
    Tag curr
    JOIN cte AS prev
  WHERE
    curr.id <> prev.id
    AND curr.parentId = prev.id
)
SELECT
  *
FROM
  cte;

CREATE INDEX ${ct}_idx ON ${ct.replace(/^temp\./, "")}(id);

-- NOTE: creating a temp table for shown assets with a covering index didn't speed up the next create table

CREATE TABLE ${tc} AS
SELECT
  anc_id AS tagId,
  count(DISTINCT assetId) AS assetCount
FROM
  ${ct}
  JOIN AssetTag ON AssetTag.tagId = ${ct}.id
  JOIN Asset ON Asset.id = AssetTag.assetId
WHERE
  Asset.shown = 1
  AND Asset.hidden = 0
  AND Asset.excludedAt IS NULL
  AND Asset.deletedAt IS NULL
GROUP BY
  1;

CREATE INDEX ${tc}_idx ON ${tc.replace(/^temp\./, "")}(tagId, assetCount);

-- We now have a comprehensive set of counts. Wipe prior counts:

UPDATE Tag SET 
  assetCount = NULL;

UPDATE Tag
SET
  assetCount = ${tc}.assetCount
FROM ${tc}
WHERE ${tc}.tagId = Tag.id;

-- Clean up:

DELETE FROM ChangedTag; -- we just rebuilt everything!

DROP INDEX IF EXISTS ${ct}_idx;
DROP INDEX IF EXISTS ${tc}_idx;
DROP TABLE ${ct};
DROP TABLE ${tc};
`)
}

export const updateAllTagCounts = lazy(
  () =>
    timeSync("db.updateAllTagCounts", () => {
      if (dbr().db() == null) {
        logger().throw("updateAllTagCounts(): modelDb is unset", {
          retriable: false
        })
      }
      try {
        const rnd = randomChars(6)
        const ignorable = "_" + rnd
        const start = Date.now()
        dbr().runScript(updateTagCountSql(rnd), ignorable)
        const elapsedMs = Date.now() - start
        const newTTL = clamp(7 * secondMs, 5 * minuteMs, elapsedMs * 20)
        logger().info("updateAllTagCounts() done", {
          elapsedMs,
          newTTL
        })
      } catch (err) {
        logger().warn("updateAllTagCounts() failed", err)
      }
    }),
  minuteMs // < we shouldn't have to rebuild everything more than once a minute
)

export async function vacuumOrphanTags(
  minCreatedAt = Date.now() - 10 * minuteMs
) {
  const deleted: number[] = []
  for (let iter = 0; iter < 6; iter++) {
    const arr = await vacuumLeafTags(minCreatedAt)
    if (isEmpty(arr)) break
    else deleted.push(...arr)
  }
  return uniq(deleted)
}

export async function vacuumLeafTags(minCreatedAt: number) {
  const candidates = Tag.dbl.pluckAllf<number>(
    q =>
      q
        .select("Tag.id")
        .leftOuterJoin("AssetTag", "AssetTag.tagId", "Tag.id")
        .leftOuterJoin(knex().raw("Tag AS child ON child.parentId = Tag.id"))
        .whereNull("AssetTag.assetId")
        .andWhere(q2 => q2.whereNull("child.id"))
        .andWhere(q2 => q2.whereNull("Tag.assetCount"))
        .andWhere("Tag.createdAt", "<", minCreatedAt)
        .orderBy("Tag._path", "asc") // leaves first
  )
  logger().debug("vacuumLeafTags()", { candidates })
  for (const ea of candidates) {
    try {
      Tag.dbl.runf(q => q.where({ id: ea }).delete())
    } catch (err) {
      logger().info("vacuumLeafTags(): failed to delete", { tagId: ea, err })
    }
  }
  logger().info("vacuumLeafTags() complete.", { candidates })
  return candidates
}

const updateTagMountpoints = lazyFsAsync("updateTagMountpoints", async () => {
  const fileRoot = Tag.findByPath([TagRoots.fs])
  if (fileRoot == null) {
    mkLogger("updateTagMountpoints()").info(
      "No root filesystem tag (new db, I hope?)"
    )
    return
  }

  fileRoot.maybeUpsertDisplayName(Settings.tagDisplayNameFS.valueOrDefault)

  for (const t of fileRoot.getChildren()) {
    await fixFileRootTag(t)
  }
})

function _reset() {
  tagFtsRebuild.unset()
  // if (!isProd) updateAllTagCounts.clear()
  updateTagMountpoints.unset()
}

later(() => {
  ee().on("clearCache", _reset)
  Settings.libraryDir.watchLater(_reset)
})

export async function fixFileRootTag(t: Maybe<Tag>) {
  // Is this an actual file root tag?
  if (t == null || t.depth !== 2 || !t._path.startsWith(TagRoots.fs + TagSep)) {
    // no-op
    return
  }

  const log = mkLogger("fixFileRootTag(" + t + ")")
  if (t.name === LibraryTagName) {
    if (t.ordinal !== 1) {
      log.info("fixing library ordinal", { id: t.id, path: t.path })
      t.upsert({ ordinal: 1 })
      Tag.clear()
    }
    return
  }

  // Was this updated recently?
  if (
    forceContextOrSetting().forceSync &&
    notBlank(t.displayName) &&
    gt(t.updatedAt, Date.now() - volumeMetadataTtlMs())
  ) {
    log.info("no-op: tag recently updated", {
      id: t.id,
      path: t.path,
      updated: fmtDuration(Date.now() - t.updatedAt!) + " ago"
    })
    return
  }

  const displayName = await displayNameForVolsha(t.name)

  if (notBlank(displayName)) {
    t.maybeUpsertDisplayName(displayName)
    log.info("updated tag", { id: t.id, path: t.path, displayName })
    Tag.clear()
  } else {
    log.debug("cannot update tag: no current volume.", {
      id: t.id,
      path: t.path
    })
  }
}

export const displayNameForVolsha = memoizeAsync(
  async (volumeSHA: string) => {
    const log = mkLogger("displayNameForVolsha(" + volumeSHA + ")")
    const vols = await volumes()
    if (vols != null) {
      const vol = vols.find(ea => volsha(ea.uuid) === volumeSHA)
      log.info("find()", { vol })
      if (vol != null) {
        if (
          Settings.tagDisplayNameFSLabels.valueOrDefault &&
          notBlank(vol.label)
        ) {
          return vol.label.trim()
        }

        if (vol.mountpoint === "/") {
          return (
            Settings.tagDisplayNameFSRoot.valueOrDefault +
            (Settings.tagDisplayNameFSRootWithHostname.valueOrDefault
              ? " on " + hostname()
              : "")
          )
        }

        return notBlankOr(
          // We don't want "C:\" (because the UI renders the path separators)
          stripPrefixSuffix(vol.mountpoint, {
            prefix: _path.sep,
            suffix: _path.sep
          }),
          _path.sep // < handle "/"
        )
      } else {
        return AssetFile.dbl.pluckFirstf<string>(q =>
          q
            .select("mountpoint")
            .whereLike("uri", "psfile://" + volumeSHA + "/%")
            .limit(1)
        )
      }
    }
    return
  },
  {
    name: "tag.displayNameForVolsha",
    maxSize: 64,
    timeoutMs: volumeMetadataTtlMs()
  }
)

later(() => {
  mountpoints.watch(() => displayNameForVolsha.clear())
})
