import { Downloadable } from "../../fe/api/Asset"
import { assetImgLink } from "../../fe/AssetUrls"
import { Dimensions, dimToSize, fmtDim } from "../../fe/Dimensions"
import { ReducerName, ReducerNames } from "../../fe/ImageReducers"
import { map2 } from "../../fe/Maybe"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { gt0, toInt } from "../../fe/Number"
import { SizeDescription } from "../../fe/Units"
import { thenMap } from "../async/Promise"
import { extname } from "../fs/Path"
import { PosixFile } from "../fs/PosixFile"
import { mkLogger } from "../Logger"
import { extractInt } from "../Number"
import { stripSuffix } from "../String"
import { dimensions } from "./FileDimensions"

export interface PreviewInfo {
  file: PosixFile
  assetId: number
  reducer?: ReducerName
  width?: number
}

const epilog = mkLogger("extractPreviewInfo")

export function extractPreviewInfo(
  previewsRoot: PosixFile,
  file: PosixFile
): Maybe<PreviewInfo> {
  // For ".photostructure/previews/000/000/21-sq-w480.jpg"
  const posixPath = file.posixPathFrom(previewsRoot)
  // "000/000/21-sq-w480.jpg"
  const arr = posixPath.replace(/\//g, "").split("-")
  // ["00000021", "sq", "w480.jpg"]
  const assetId = toInt(arr[0])
  const reducer = ReducerNames.getCI(arr[1])
  const width = extractInt(arr[2])
  if (!gt0(assetId)) {
    epilog.warn("Failed to extract preview info", {
      file,
      previewsRoot,
      arr,
      assetId,
      reducer,
      width
    })
    return
  } else {
    return { file, assetId, reducer, width }
  }
}

export async function previewToDownloadable(
  basename: string,
  pi: PreviewInfo
): PromiseMaybe<Downloadable> {
  return map2(pi.assetId, pi.width, (assetId, width) =>
    thenMap(dimensions(pi.file), d => {
      const size = dimToSize(d)
      const name = stripSuffix(basename, extname(basename))
      return {
        size,
        basename: `${name}-${size}${pi.file.ext}`,
        title: mkDownloadableTitle(pi.file, size, "image", d),
        description: `Download ${size}`,
        details: `(${fmtDim(d)} ${pi.file.ext})`,
        href: assetImgLink({ assetId, reducer: ReducerNames.fit, width })
      }
    })
  )
}

export function mkDownloadableTitle(
  f: PosixFile,
  s: SizeDescription,
  imageOrVideo: string,
  d: Dimensions
): string {
  return `Download ${s} ${f.ext} ${imageOrVideo} (${fmtDim(d)})`
}
