import { li, tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { sqliteVersion_ } from "../db/SQLiteVersion"
import { errorToS } from "../error/Error"
import { ver } from "../version/Ver"
import { HealthCheck } from "./HealthCheck"

export const sqliteHealthCheck = defer(() =>
  HealthCheck.for({
    section: "Tools",
    id: "tools-sqlite-version",
    ordinal: 0,
    pendingMsg: "Checking SQLite",
    settings: [],
    // ttlMs: minuteMs, // it's either or not at startup, no need to recheck.
    links: [
      {
        text: "Read about PhotoStructure SQLite support",
        url: "https://forum.photostructure.com/t/whats-ps-force-local-db-replica/837",
        icon: "docs"
      }
    ],
    async later() {
      try {
        const v = await sqliteVersion_.refresh()
        if (v == null)
          return {
            level: "error",
            msg: "SQLite version could not be found"
          }

        return {
          level: "ok",
          msg: [
            "SQLite is OK",
            li([
              "embedded: " + ver(v.libraryVersion),
              tt(v.sqliteNativePath) + ": " + ver(v.toolVersion)
            ])
          ]
        }
      } catch (error) {
        return {
          level: "error",
          msg: "Something's amiss with SQLite: " + errorToS(error as any)
        }
      }
    }
  })
)
