import { map } from "../../fe/Maybe"
import { thenOrTimeout } from "../../fe/ThenOrTimeout"
import { sortIgnoreCase } from "../String"
import { shortFsStringSha } from "../StringHash"
import { configDir } from "../dir/ConfigDir"
import { FsLock } from "./FsLock"
import { PosixFile } from "./PosixFile"

export function defaultFsAdvisoryLockDir() {
  return map(configDir(), ea => PosixFile.for(ea).join("advisory-locks"))
}

export async function withFsAdvisoryLocks_<T>({
  name,
  lockNames,
  f,
  timeoutMs,
  lockDir = defaultFsAdvisoryLockDir()
}: {
  name: string
  lockNames: string[]
  f: () => Promise<T>
  timeoutMs: number
  lockDir?: PosixFile
}): Promise<T> {
  if (lockDir == null) return f()
  await lockDir.mkdirp_()
  const fsSafeLockNames = sortIgnoreCase(lockNames)
  const locks = fsSafeLockNames.map(lockName =>
    FsLock.for({
      name: lockName,
      file: lockDir.join(shortFsStringSha(lockName)),
      timeoutMs
    })
  )
  try {
    for (const lock of locks) {
      if (true !== (await thenOrTimeout(lock.acquire_(), timeoutMs))) {
        throw new Error(
          "Failed to acquire lock for " +
            name +
            ": " +
            lock.opts.name +
            " was busy"
        )
      }
    }
    return await f()
  } finally {
    for (const lock of locks) {
      lock.release()
    }
  }
}
