import { lazy } from "../../core/Lazy"
import { Maybe } from "../../fe/MaybeTypes"
import { setupLibraryDataDir_ } from "../dir/LibraryDirs"
import { PosixFile } from "../fs/PosixFile"
import { SimpleFileOrString } from "../fs/SimpleFile"
import { mkLogger } from "../Logger"
import { Settings } from "../settings/Settings"
import { cacheDbDir_ } from "./CacheDbDir"
import { pathToDb, Schema, Schemas, SqliteBase } from "./Schemas"
import { assertDirSQLiteReadWrite_ } from "./SQLiteReadWrite"

const logger = lazy(() => mkLogger("db.CheckLocalDbReplica"))

export async function cacheDbFile_({
  libraryDir = Settings.libraryDir.valueOrDefault,
  schema = Schemas.models
}: { libraryDir?: Maybe<SimpleFileOrString>; schema?: Schema } = {}) {
  return (await cacheDbDir_(libraryDir, schema)).join(SqliteBase)
}

/**
 * @throw if the library is blank
 */
export async function getLiveDbDir_(
  libraryDir: Maybe<SimpleFileOrString> = Settings.libraryDir.valueOrDefault
): Promise<{ db: PosixFile; useReplica: boolean }> {
  // If the user has explicitly set this to true, we can't use the library dir.
  if (!Settings.forceLocalDbReplica.valueOrDefault) {
    // If this throws, the library isn't going to spin up in any event.
    const libraryDb = pathToDb(
      await setupLibraryDataDir_(libraryDir),
      Schemas.models
    )
    try {
      await assertDirSQLiteReadWrite_(libraryDb.parent())
      return { db: libraryDb, useReplica: false }
    } catch (error) {
      logger().info(
        "SQLite cannot directly write to the library: trying to use a local db replica.",
        {
          libraryDb,
          error
        }
      )
    }
  }

  const cacheDbFile = await cacheDbFile_({ libraryDir, schema: Schemas.models })

  try {
    await assertDirSQLiteReadWrite_(cacheDbFile.parent())
    return { db: cacheDbFile, useReplica: true }
  } catch (error) {
    return logger().throw(
      "SQLite cannot directly write to the library or the cacheDir.",
      {
        cacheDbFile,
        error
      }
    )
  }
}
