/**
 * Verify token, but don't validate against current system IDs
 */
export async function v_(str: Maybe<string>) {
  const o = (await V(ensurePrefix(toS(str).trim(), k().p))) as Obj
  const iat = utcIsoToTs(o[k().i])
  if (iat == null) {
    throw new Error("bad " + k().i + ": " + o[k().i] + " (" + str + ")")
  }
  const exp = utcIsoToTs(o[k().r])
  if (exp == null) {
    throw new Error("bad " + k().r + ": " + o[k().r] + " (" + str + ")")
  }

  // Set PS_FORCE_TRIAL=1 to force all licenses into trial mode
  if (getDevEnvFlag(k().y)) {
    o[k().t] = true
  }

  o[k().a] = toGte0(o[k().a]) ?? 2
  o[k().i] = new Date(iat)
  o[k().r] = new Date(exp)
  o[k().b] = toS(o.uids).split(",")
  return o as License
}

export interface ParsedLicense {
  ok: boolean
  /**
   * the actual license string
   */
  s: string
  l?: License
  meta: Obj
}

/**
 * Verify and validate the given stringified license
 */
export async function v(
  str: Maybe<string>,
  src: string,
  timeoutMs: number,
  _sids?: string[]
): PromiseMaybe<ParsedLicense | L> {
  try {
    return blank(str)
      ? undefined
      : L.for({
          str,
          l: await v_(str),
          sids: _sids != null ? _sids : await sids({ timeoutMs }),
          src
        })
  } catch (err) {
    return logger().tap({
      msg: k().v,
      result: {
        s: str!,
        ok: false,
        meta: { err }
      }
    })
  }
}

/**
 * Only return an L if the license string validates and is currently good.
 */
export async function vok(
  str: Maybe<string>,
  src: string,
  timeoutMs: number
): PromiseMaybe<L> {
  if (blank(str)) return
  const ea = await v(str, src, timeoutMs)
  return isTrue(ea?.ok) && ea instanceof L ? ea : undefined
}

/**
 * Return all available licenses and metadata from either the PS_LICENSE
 * environment variable, or licenses stored on the filesystem.
 *
 * The first element will be the "best" license.
 */
export const m = rolazy<Promise<ParsedLicense[]>>(async () => {
  // Set PS_FORCE_LITE=1 to ignore all licenses
  if (getDevEnvFlag(k().x)) {
    return []
  }
  const _sids = await sids({ timeoutMs: UserTimeoutMs })
  const ud = map(configDir(), ea => BaseFile.for(ea))
  const result = sorted(
    flatten(
      (
        await someOrTimeout<ParsedLicense | ParsedLicense[]>(
          [
            // Read from env, system, and library. sb() will sort the best one to be the first
            () => rawLicenseFromSettings(_sids),
            () => rawFromDir(ud?.join(k().d), _sids),
            () => rawFromDir(libraryDataDirPosixFile()?.join(k().d), _sids),
            () =>
              rawFromDir(
                ud?.sibling(AppName().toLowerCase()).join(k().d),
                _sids
              )
          ],
          { timeoutMs: UserTimeoutMs }
        )
      ).filter(ea => {
        if (ea instanceof Error) {
          return false
        } else {
          return true
        }
      })
    )
  )
  return logger().tap({
    msg: k().d + "()",
    result
  })
})

function rawLicenseFromSettings(_sids: string[]) {
  return v(
    Settings[k().L].value,
    "Settings",
    UserTimeoutMs,
    _sids
  ) as Promise<ParsedLicense>
}

async function rawFromDir(dir: Maybe<BaseFile>, _sids: string[]) {
  const results: ParsedLicense[] = []
  const children = await sortByAsync({
    name: "rawLicenseFromDir(" + dir + ")",
    arr: dir?.childFiles(),
    f: ea => ea.mtimeMs()
  })

  // youngest files first:
  for (const ea of children.reverse()) {
    const result = await v(
      toA(await ea.readLines())
        .map(str => str.trim())
        .join(""),
      ea.nativePath,
      UserTimeoutMs,
      _sids
    )
    if (result != null) results.push(result)
  }
  return results
}
/**
 * @return ParsedLicense[], best license first.
 */
function sorted(arr: Maybe<Partial<ParsedLicense> | Error>[]): ParsedLicense[] {
  return sortBy(
    // If .l (the License object) is null, it couldn't be parsed:
    arr.filter(ea => {
      if (ea == null) return false
      if (isError(ea)) {
        logger().warn(k().d + ": ", ea)
      }
      return ea != null && !isError(ea) && ea?.l != null
    }) as ParsedLicense[],
    ea => [
      !ea.ok,
      S.indexOf(ea.l?.[k().T]) ?? S.length + 1,
      -(ea.l?.exp?.getTime() ?? 1) // later expiration is better
    ]
  )
}

later(() => {
  ee().on("clearCache", () => m.unset())
  ee().on(k().c, () => m.unset()) // reloadLicenses
})

/**
 * @return the best license available
 */
export async function b() {
  const arr = await m()
  return arr[0]?.ok ? arr[0] : undefined
}

/**
 * @return the current subscription tier
 */
export async function t() {
  return (await b())?.l?.[k().T] ?? k().f
}

/**
 * @return true if the license is "lite"
 */
export async function l() {
  try {
    return (await t()) === k().f
  } catch {
    return true
  }
}

/**
 * @return true if the license is "plus"
 */
export async function p() {
  try {
    return (await t()) === k().g
  } catch {
    return false
  }
}
