import { hostname } from "os"
import { pid } from "process"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { InternalErrorFlag } from "../../core/error/ErrorFlags"
import { isRaspberryPi } from "../../core/platform/isRaspberryPi"
import { Settings } from "../../core/settings/Settings"
import { maxCpus } from "../../core/work/MaxCpus"
import { compactBlanks, isNotEmpty } from "../../fe/Array"
import { dayMs, secondMs } from "../../fe/Date"
import { stringify } from "../../fe/JSON"
import { map } from "../../fe/Maybe"
import { Maybe, MaybePromiseMaybe } from "../../fe/MaybeTypes"
import { fromEntries, omit } from "../../fe/Object"
import { parseJSON } from "../../fe/ParseMaybe"
import { thenCollect } from "../../fe/Promise"
import { compressWhitespace } from "../../fe/String"
import { ProgressState, SyncStatus } from "../../fe/api/ProgressState"
import { toA } from "../../fe/toA"
import { assignFromJSON } from "./ModelJson"
import { Operation, OperationNames } from "./Operation"
import {
  ProgressMeta,
  ProgressMetaName,
  ProgressMetaRecord
} from "./ProgressMeta"
import { TableName } from "./TableName"
import { TimestampedModel } from "./TimestampedModel"

const logger = lazy(() => mkLogger("model.Tag"))

export interface ProgressPojo extends ProgressState {
  id: number
  uri: string
  completedAt?: number
}

export type ProgressUpdatePojo = Omit<ProgressState, "volume" | "uri"> &
  Pick<Progress, "updatedAt" | "completedAt">

// Slower progress rate if we don't have free CPUs:
export const ProgressRateMs = lazy(
  () => secondMs * (isRaspberryPi() || maxCpus() <= 3 ? 5 : 2)
)

export interface ProgressTimes {
  uri: string
  lastProgressId: number
  lastStartedAt: number
  lastUpdatedAt: number
  lastCompletedAt: Maybe<number>
}

const ProgressTimesQuery = compressWhitespace(`
SELECT
  p1.uri,
  p1.id as lastProgressId,
  p1.createdAt as lastStartedAt,
  p1.updatedAt as lastUpdatedAt,
  p3.lastCompletedAt
FROM
  Progress AS p1
  JOIN (
    SELECT
      uri,
      max(updatedAt) AS lastUpdatedAt
    FROM
      Progress
    WHERE createdAt > :minCreatedAt
    GROUP BY
      1
  ) AS p2 ON p1.uri = p2.uri
  AND p1.updatedAt = p2.lastUpdatedAt
  LEFT JOIN (
    SELECT
      uri,
      max(completedAt) AS lastCompletedAt
    FROM
      Progress
    WHERE
      createdAt > :minCreatedAt
      AND CAST(completedAt AS int) > 0
    GROUP BY
      1
  ) AS p3 ON p1.uri = p3.uri
WHERE p1.createdAt > :minCreatedAt
GROUP BY
  p1.uri`)

function stringifyDek(dek: string[]) {
  return stringify(compactBlanks(toA(dek)))
}

export function defaultMinCreatedAt() {
  const d = Settings.progressStaleDays.valueOrDefault
  return d <= 0 ? 0 : Date.now() - d * dayMs
}

/**
 * Holds per-volume information rendered to the UI by progress-panel. We delete
 * records older than 90 days, so we expect max ~ new sync job every hour: 2,160
 * rows (so we only bother with an index on updatedAt to help performance of
 * ProgressRouter)
 */
export class Progress
  extends TimestampedModel
  implements Omit<ProgressPojo, "id">
{
  static override readonly $tableName: TableName = "Progress"
  static override readonly $uniqueColumnName = "id" as const
  uri!: string
  hostname!: string
  pid!: number
  volume!: string
  state?: SyncStatus
  hed?: string
  dekJSON?: string
  completePct?: number
  incompletePct?: number
  scanningPct?: number
  completedAt?: number

  // we only look back 30 days for last-started-at times
  static vacuum(
    staleMs = Settings.progressRetentionDays.valueOrDefault * dayMs
  ) {
    const staleAt = Date.now() - staleMs
    const victimIds = this.dbl.pluckAllf<number>(q =>
      q.select("id").where("createdAt", "<=", staleAt)
    )

    const metaVictimIds = ProgressMeta.dbl.pluckAllf<number>(q =>
      q.select("id").whereIn("progressId", victimIds)
    )
    ProgressMeta.ops().delete(metaVictimIds)
    Progress.ops().delete(victimIds)
  }

  // don't cache this! Operations can be completed!

  static minCreatedAt() {
    const op = Operation.getFirstPendingOp({
      name: OperationNames.forceRestartSync
    })
    return logger().tap({
      msg: "minCreatedAt",
      level: "info",
      result: op?.createdAt ?? defaultMinCreatedAt(),
      meta: { op }
    })
  }

  static times(): Promise<ProgressTimes[]> {
    const minCreatedAt = this.minCreatedAt()
    return logger().tap({
      msg: "times()",
      result: this.dbl.all({
        sql: ProgressTimesQuery,
        bindings: { minCreatedAt }
      }) as any,
      meta: { minCreatedAt }
    })
  }

  static async saveSyncState(sync: {
    progress: () => MaybePromiseMaybe<Progress | Progress[]>
  }): Promise<Progress[]> {
    return thenCollect(toA(await sync.progress()), ea => {
      if (ea.state === "done" && ea.completedAt == null) {
        ea.completedAt = Date.now()
      }
      if (ea.id == null) {
        return logger().throw(
          "saveSyncState() returned an unsaved Progress" + InternalErrorFlag,
          ea
        )
      }
      return ea.upsert()
    })
  }

  static insertNew(
    obj: { uri: string; volume: string } & Partial<
      Omit<ProgressPojo, "uri" | "volume">
    >
  ) {
    const dekJSON = map(obj.dek, stringify)
    return this.ops().insertOne({
      ...omit(obj, "dek"),
      pid,
      hostname: hostname(),
      dekJSON
    })
  }

  // TODO: save recent asset ids to the db in this table, instead of requiring a
  // query?

  toSyncState(): ProgressPojo {
    return {
      id: this.id!,
      uri: this.uri,
      volume: this.volume,
      state: this.state,
      hed: this.hed,
      dek: this.dek,
      completePct: this.completePct,
      incompletePct: this.incompletePct,
      scanningPct: this.scanningPct
    }
  }

  get dek(): string[] {
    return compactBlanks(parseJSON<string>(this.dekJSON))
  }

  set dek(arr: string[]) {
    this.dekJSON = stringifyDek(arr)
  }

  assignFromPojo(Obj: ProgressUpdatePojo) {
    return assignFromJSON(this, omit(Obj as any, "volume", "uri"))
  }

  getThisMeta() {
    return ProgressMeta.ops().allf(q =>
      q.where({ progressId: this.id! }).orderBy("createdAt")
    )
  }

  getPriorIncompleteProgress() {
    const minCreatedAt = Progress.minCreatedAt()
    const prior = Progress.ops().firstf(q =>
      q
        .where({ hostname: this.hostname, uri: this.uri })
        .andWhere("createdAt", ">", minCreatedAt)
        .orderBy("updatedAt", "desc")
        .limit(1)
    )
    if (prior?.completedAt != null) {
      return this.logger().tap({
        msg: "priorIncompleteProgress(): last Progress was complete",
        result: undefined,
        meta: { prior }
      })
    } else {
      return this.logger().tap({
        msg: "priorIncompleteProgress()",
        result: prior
      })
    }
  }

  getPriorIncompleteMeta() {
    return this.getPriorIncompleteProgress()?.getThisMeta()
  }

  getMeta() {
    const thisMeta = this.getThisMeta()
    if (isNotEmpty(thisMeta)) {
      return thisMeta
    } else {
      const priorMeta = this.getPriorIncompleteMeta()
      if (isNotEmpty(priorMeta)) {
        this.logger().info("getMeta(): inheriting prior progress", priorMeta)
        return ProgressMeta.ops().upsert(
          priorMeta.map(ea => ({
            progressId: this.id,
            name: ea.name,
            value: ea.value
          })),
          false
        )
      } else {
        this.logger().info("getMeta(): no relevant prior progress")
        return []
      }
    }
  }

  setMeta(name: ProgressMetaName, value: string) {
    this.logger().debug("setMeta()", { progressId: this.id, name, value })
    return ProgressMeta.ops().upsertOne({ progressId: this.id, name, value })
  }

  deleteMeta(...names: ProgressMetaName[]) {
    this.logger().debug("deleteMeta()", { progressId: this.id, names })
    return ProgressMeta.dbl.runf(q =>
      q.whereIn("name", names).andWhere({ progressId: this.id }).delete()
    )
  }

  deleteAllMeta() {
    this.logger().debug("deleteAllMeta()", { progressId: this.id })
    return ProgressMeta.dbl.runf(q => q.where({ progressId: this.id }).delete())
  }

  getMetaAsRecord(): ProgressMetaRecord {
    return this.logger().tap({
      msg: "getMetaAsRecord()",
      result: fromEntries(this.getMeta().map(ea => [ea.name, ea.value])) ?? {}
    })
  }
}
