const logger = lazy(() => mkLogger("color.DominantColors"))

export interface DominantColorsResult {
  dominantColors: DominantColor[]
  description?: string
  dominantColorScore?: number
}

export function extractDominantColors(
  labs: Triplet[],
  k = ModeCount
): DominantColorsResult {
  return timeSync("img.extractDominantColors", () =>
    _extractDominantColors(labs, k)
  )
}

export function centroidsScore(labs: Triplet[], centroids: Centroid[]) {
  const avg = new Average()
  for (const lab of labs) {
    const closest = leastBy(centroids, ea =>
      ciede2000_delta_e(ea.centroid as Triplet, lab)
    )
    if (closest != null)
      avg.push(ciede2000_delta_e(closest.centroid as Triplet, lab))
  }
  return avg
}

// // This is a stable set of seeds so modes are consistent across runs,
// // generated by

// // [...Array(128)].map(() => Math.round(Math.random() * Number.MAX_SAFE_INTEGER))

