import { DateTime } from "luxon"
import { lazy } from "../../core/Lazy"
import { compactBlanks } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { replaceAll, stripPrefix } from "../../fe/String"
import { mkLogger } from "../Logger"
import { countChars, splitKeep } from "../String"
import { Dated, datedToDateTime } from "../date/Dated"
import { datedToISO } from "../date/FuzzyDate"
import { hasZone } from "../date/getZoneName"
import { grandParentBasename, parentBasename } from "../fs/Path"
import { SimpleFile } from "../fs/SimpleFile"
import { Settings } from "./Settings"

const logger = lazy(() => mkLogger("settings.PathToLibraryAsset"))

const SingleQuote = ""

function esc(str: Maybe<string>): string {
  return blank(str) ? "" : "'" + replaceAll(str, "'", SingleQuote) + "'"
}

function unesc(str: string) {
  return replaceAll(str, SingleQuote, "'")
}

export function pathToLibraryAsset(
  capturedAt: Maybe<Dated>,
  src: SimpleFile
): Maybe<string[]> {
  // If we can directly convert to DateTime (with toDateTime) we should do that,
  // instead of converting back and forth with an ISO date string.
  const dt =
    datedToDateTime(capturedAt) ??
    map(datedToISO(capturedAt), iso => DateTime.fromISO(iso))

  if (dt == null || !dt.isValid) {
    return logger().tap({
      msg: "pathToLibraryAsset(): failed to convert to a valid date",
      result: undefined,
      meta: { capturedAt, src, dt }
    })
  }

  const withZone = hasZone(capturedAt)

  // A strung-together set of .replace may trip over prior substitutions, so we
  // split the tokens into an array and handle each token exactly once.

  // We don't want to strip quotes from this, as it will remove single-quoted
  // literals:
  const assetPathnameFormat = Settings.assetPathnameFormat.valueOrDefault

  const tokens = splitKeep(
    assetPathnameFormat,
    /((?:GRAND)?PARENT|BASE|NAME|EXT|ISO)/g
  )

  for (let i = 0; i < tokens.length; i++) {
    switch (tokens[i]) {
      case "GRANDPARENT":
        tokens[i] = esc(grandParentBasename(src))
        break
      case "PARENT":
        tokens[i] = esc(parentBasename(src))
        break
      case "BASE":
        tokens[i] = esc(src.base)
        break
      case "NAME":
        tokens[i] = esc(src.name)
        break
      case "EXT":
        tokens[i] = esc(stripPrefix(src.ext, "."))
        break
      case "ISO":
        tokens[i] = "yyyy-MM-dd'T'HH:mm:ss.SSS" + (withZone ? "ZZ" : "")
        break
      default:
        if (countChars(tokens[i], "'") % 2 === 1) {
          tokens[i] = replaceAll(tokens[i], "'", SingleQuote)
        }
    }
  }

  const formatted = unesc(dt.toFormat(tokens.join("")))
  const paths = formatted.split("/")
  return logger().tap({
    msg: "pathToLibraryAsset()",
    result: compactBlanks(paths),
    meta: { capturedAt, src, dt, assetPathnameFormat, tokens }
  })
}
