import { sort } from "../ArraySort"
import { notBlank } from "../Blank"
import { stringify } from "../JSON"
import { mapNumeric } from "../Number"
import { Valued } from "../Object"
import { strEnum, StrEnumKeys } from "../StrEnum"
import { isString } from "../String"
import { ThumbFit } from "../ThumbFit"
import { assembleFullPath, URLSearchParamable } from "../URLSearchParams"
import { AssetId } from "./Asset"

// TODO: this should be adaptive based on size of the client window
export const ThumbsPerSample = 128

export const BeforeAfterStreamLimit = 8 // NOTE: must match gallery-grid SCSS

/**
 * Response object from /api/tag/:tagId.json
 */
export interface ApiTag {
  tagId: number // root is 0
  tagPath: string[]
  description?: string
  displayPath: string[]
  assetCount: number
  releasedAt?: number // millis timestamp, used for sorting albums
}

// we used to page via capturedAtLt, but for tags that include > ThumbsPerSample
// of the same capturedAtLocal, paging broke.
// See https://forum.photostructure.com/t/thumbnails-missing-server-install/675/16?u=mrm
export interface TagAssetsCriteria {
  offset: number
  // don't need seed here: we're just paging through.
}

// Returned from /api/t/:tagId/assets?capturedAtLt=456
export interface TagAssets {
  tagId: number
  assetIds: AssetId[] // may be paged, not prng'ed
  // TODO: prevAssets?: TagAssetsCriteria // /api/tag/:tagId/assets.json?capturedAtGt=123
  nextAssets?: TagAssetsCriteria // /api/tag/:tagId/assets.json?capturedAtLt=123
}

export function nextAssetsUrl(tagId: number, crit: TagAssetsCriteria): string {
  const params: Valued<string> = {}
  mapNumeric(crit.offset, lt => (params.offset = lt.toString()))
  // we don't call it "/api/tag/$tagId" because it's ambiguous with the tag-path
  // endpoint:
  return assembleFullPath(`/api/t/${tagId}/assets`, params)
}

export interface ChildTagCriteria {
  tagId: number
  seed: number
  limit?: number
  offset?: number
}

export type ChildTag = ApiTag & TagAssets

// Returned from /api/t/:tagId/child-tags?seed=123&offset=0
export interface ChildTags {
  tagId: number
  childTagCount: number
  childTags: ChildTag[]
  nextChildTags?: ChildTagCriteria
}

export function nextChildTagsUrl(
  tagId: number,
  crit: ChildTagCriteria
): string {
  const params: URLSearchParamable = {}
  mapNumeric(crit.seed, ea => (params.seed = ea.toString()))
  mapNumeric(crit.limit, ea => (params.limit = ea.toString()))
  mapNumeric(crit.offset, ea => (params.offset = ea.toString()))
  // we don't call it "/api/tag/$tagId" because it's ambiguous with the tag-path
  // endpoint:
  return assembleFullPath(`/api/t/${tagId}/child-tags`, params)
}

// Returned from /api/tag/:tagPath
export interface ApiTagGallery extends ApiTag, TagAssets, ChildTags {
  fullPath: string
  emptyLibrary?: boolean
  startingUp?: boolean
  fit: ThumbFit
}

export const TagGalleryProps: (keyof ApiTagGallery)[] = [
  "tagId",
  "tagPath",
  "assetCount",
  "assetIds",
  "nextAssets",
  "childTags",
  "nextChildTags"
]

export interface TagGalleryContext {
  seed: number
  tagIds: number[]
}

export function ctxToS(ctx: TagGalleryContext): string {
  return stringify({ seed: ctx.seed, tagIds: sort([...ctx.tagIds]) })
}

export interface TagRef {
  name: string
  displayName?: string
  ordinal?: number
  description?: string
  releasedAt?: number // millis timestamp
}

export function isTagRef(t: any): t is TagRef {
  return t != null && !isString(t) && notBlank(t.name)
}

export type TagPath = (string | TagRef)[]

export const TagRoots = strEnum(
  "When",
  "Who",
  "Where",
  "What",
  "Camera",
  "Lens",
  "Albums",
  "Keywords",
  "Type",
  "fs"
)
export type TagRoot = StrEnumKeys<typeof TagRoots>

export const LibraryTagName = "Library"

export const NotInfoPanelTags: string[] = [
  TagRoots.When,
  TagRoots.Camera,
  TagRoots.Lens,
  TagRoots.Type,
  TagRoots.fs
]

export function isInfoTag(rootNameOrTag: string | ApiTag) {
  if (rootNameOrTag == null) return false
  const root = isString(rootNameOrTag)
    ? rootNameOrTag
    : rootNameOrTag.tagPath?.[0]
  return !NotInfoPanelTags.includes(root)
}
