export interface PreviewAssetFile extends SortableAssetFile {
  id?: number
  assetId?: number
  rotation?: number
}

export async function mkPreviewAssetFile_(
  f: PosixFile
): Promise<PreviewAssetFile> {
  const t = (await readTags(f))!
  return {
    nativePath: f.nativePath,
    fileSize: (await f.size())!,
    mtime: (await f.thisOrSidecareMaxMtimeMs())!,
    uri: (await f.uri_())!,
    mountpoint: (await f.mountpoint())?.nativePath,
    mimetype: t.mimetype,
    rotation: t.rotation,
    ...t.dimensions,
    sha: await f.sha_()
  }
}

const badShaCache = lazy(
  () =>
    new FileCache<boolean>({
      name: "img.badShaCache",
      maxSize: 256,
      timeoutMs: minuteMs
    })
)

// PERF: minimize GC
const fp = Promise.resolve(false)
const impl = (_sha: string) => fp

export const isBadSha = shim1({
  name: "isBadSha",
  impl,
  cache: badShaCache
})

export class AssetPreviewBuilder {
  private readonly logger: Logger
  private readonly ctx: RequiredForceContext
  constructor(
    readonly ap: AssetPreviews,
    readonly assetFiles: PreviewAssetFile[],
    ctx: ForceContext
  ) {
    this.logger = mkLogger("AssetPreviewBuilder(" + ap.assetId + ")")
    this.ctx = forceContextOrSetting(ctx)
    this.assetFiles = sortAssetFiles(this.assetFiles)
  }

  [inspect.custom]() {
    return {
      ctor: "AssetPreviewBuilder",
      assetId: this.ap.assetId,
      assetFiles: this.assetFiles
    }
  }

  /**
   * The caller is assumed to have a per-asset advisory lock to prevent
   * concurrent runs for the same asset.
   */
  async build_(): Promise<AssetPreviewInfo> {
    this.logger.info(
      "build_(): asset file candidates:",
      this.assetFiles.map(ea => pick(ea, "id", "uri"))
    )
    const causes: any[] = []
    for (const af of this.assetFiles) {
      const pf = PosixFile.forMaybe(await getAssetFileNativePath(af))
      if (pf == null) {
        this.logger.info("skipping: failed to get PosixFile", { af })
        causes.push(
          new Error(
            "Cannot get path from URI " +
              stringify(pick(af, "uri", "mountpoint", "nativePath"))
          )
        )
        continue
      }
      {
        const whyRejected = await whyRejectFile(pf)
        if (!blank(whyRejected)) {
          this.logger.info("skipping: rejected", { pf, whyRejected })
        }
      }
      const sha = af.sha ?? (await pf.sha())
      if (sha == null) {
        this.logger.info("skipping: cannot read SHA", pf)
        causes.push(new Error("Cannot read " + pf.nativePath))
        continue
      }
      if (true === (await isBadSha(sha))) {
        this.logger.info("skipping: bad SHA", pf)
        continue
      }
      try {
        return await this._build(pf, af)
      } catch (error) {
        causes.push(error)
        badShaCache().set(sha, true)
        this.logger.warn("Failed to set shown file to ", { best: af, error })
      } finally {
        // Make sure we don't have a stale view of the parent contents:
        this.ap.parent.clear()
      }
    }
    return this.logger.throw(
      "Cannot build previews: none of the files are valid",
      {
        doNotSend: true, // < don't send this to Sentry
        retriable: false,
        files: this.assetFiles.map(ea => ea.nativePath ?? ea.uri),
        causes
      }
    )
  }

  private async infoForBest_(
    pf: PosixFile,
    best: PreviewAssetFile
  ): Promise<AssetPreviewInfo> {
    await throwIfInvalidFile_(pf)
    const filesize = await pf.size()
    const mtime = await pf.thisOrSidecareMaxMtimeMs()
    this.logger.debug("_build", { filesize, mtime, best: best.uri })
    if (filesize == null || mtime == null) {
      return this.logger.throw("build(): missing stat info for best file", {
        ignorable: true,
        best
      })
    }

    const { uri, width, height, sha, mimetype } = best

    if (
      uri == null ||
      width == null ||
      height == null ||
      mimetype == null ||
      sha == null
    ) {
      return this.logger.throw(
        "build(): internal error: missing fields for best file " + pf,
        { ignorable: true, best }
      )
    }

    const fits = fitSizes(best as Dimensions, best.mimetype)

    return {
      assetId: best.assetId!,
      assetFileId: best.id!,
      uri,
      uris: this.assetFiles.map(ea => ea.uri),
      path: pf.nativePath,
      mtime,
      filesize,
      width,
      height,
      sha,
      mimetype,
      fitSizes: fits.map(([, ea]) => ea.name).join(","),
      sqWidths: compact(ImageSize.sq().map(ea => ea.outputSize(best)?.width)),
      fitWidths: fits.map(([ea]) => ea.width)
    }
  }

  private async priorIfValid_(
    best: PreviewAssetFile,
    info: AssetPreviewInfo
  ): PromiseMaybe<AssetPreviewInfo> {
    if (this.ctx.forceSync || this.ctx.forceRebuildPreviews) return
    // Make sure we fetch the latest json with .refresh():
    const priorInfo = await this.ap.readInfo.refresh()
    if (priorInfo != null) {
      if (priorInfo.assetId !== info.assetId) {
        throw new InternalError(
          "priorIfValid_(): mismatched asset IDs for " +
            best +
            ": " +
            stringify({ priorInfo, info })
        )
      }
      const priorFits = priorInfo.fitSizes.split(",")
      const currFits = info.fitSizes.split(",")

      if (
        priorInfo.sha != null &&
        priorInfo.sha === info.sha &&
        priorInfo.rotation === info.rotation &&
        equivalentFitSizes(priorFits, currFits)
      ) {
        // This is a no-op as far as the previews are concerned, but in order to
        // be deterministic, we should change the metadata to make it point to
        // (possibly) the library version.

        const fits = fitSizes(best as Dimensions, best.mimetype)

        // Are there previews that were orphaned?
        const leftovers = diff(priorFits, currFits)
        if (isNotEmpty(leftovers)) {
          this.logger.info(
            "priorIfValid_(): removing previews that aren't required anymore.",
            { leftovers }
          )
          for (const fitname of leftovers) {
            const fitsize = fits.find(([, ea]) => ea.name === fitname)
            if (fitsize == null) {
              this.logger.warn(
                "priorIfValid_(): Failed to clean up: missing fit size",
                { fitsize, fits }
              )
            } else {
              const victim = this.ap.fileForWidth(
                fitsize[1].reducer.name,
                fitsize[0].width
              )
              this.logger.debug(
                "priorIfValid_(): unlinking unwanted preview " + victim,
                { fitsize }
              )
              await victim.unlink("warn")
            }
          }
        }

        this.logger.debug(
          "priorIfValid_(): SHA and rotation match, and previews exist.",
          {
            info,
            priorInfo
          }
        )
        await this.ap.writeInfo(info)
        return info
      }
    }
    return
  }

  private async _build(
    srcFile: PosixFile,
    best: PreviewAssetFile
  ): Promise<AssetPreviewInfo> {
    this.logger.info("_build(" + srcFile + ")", { uri: best.uri })

    const info = await this.infoForBest_(srcFile, best)

    // Is the prior preview sufficient?
    const prior = await this.priorIfValid_(best, info)
    if (prior != null) return prior

    // By rebuilding all sizes we ensure all the previews are from the same
    // source (especially noticeable with assets are rotated!)

    const pe = new PushProgressObserver(
      { path: srcFile.nativePath, op: "Building previews" },
      ImageSize.sq().length + ImageSize.fit().length
    )

    this.logger.debug("Rebuilding all previews from " + best.uri, {
      assetId: best.assetId,
      info
    })

    if (null == (await this.ap.parent.mkdirp())) {
      throw new Error(
        "build(): Failed to create previews directory, " +
          this.ap.parent +
          " for " +
          best
      )
    }

    // It'd be easier to remove these files now, but it means there aren't
    // previews available while we rebuild them, and that makes the UI 404.
    // We'll delete them after we finish rebuilding the previews.
    const priorExistingFiles = await this.ap.existingFiles()

    const origSharpReadable = await sharpReadable_({
      src: srcFile,
      minDim: ImageSize.largestFit().max
    })
    if (origSharpReadable == null) {
      throw new WrappedError(
        "AssetPreviewBuilder.build(): " +
          best.uri +
          ", " +
          srcFile.nativePath +
          ", is not readable by sharp.",
        {
          retriable: false
        }
      )
    }

    const origSharp = toSharp(origSharpReadable)
    // TODO: should I restore this? DID IT WORK?
    // const origSharp = await maybeApplyColorspace(pf, toSharp(origSharpReadable))

    // videos are auto-rotated by Video.extractVideoFrame
    if (
      best.mimetype.startsWith("image/") &&
      !isHeifMimeType(best.mimetype) &&
      best.rotation != null &&
      best.rotation !== 0
    ) {
      origSharp.rotate(best.rotation) // we don't use auto-rotate.
      this.logger.debug("build(): rotating " + best.rotation + "")
    }

    if (
      isBaseFile(origSharpReadable) &&
      origSharpReadable.desc.toLowerCase().includes("thumbnail")
    ) {
      // JPEG thumbnails are regularly "letterboxed". Delete that:
      try {
        origSharp.trim(1)
      } catch {
        // don't care
      }
    }

    const fits = fitSizes(best as Dimensions, best.mimetype)
    // These are the thumbnail/preview images we're generating:
    const files: PosixFile[] = []
    // This is a subset of files, which should be tagged:
    const previewTagFiles: PosixFile[] = []
    let sqDim: Maybe<Dimensions> // = origDim
    let sqSharp: Maybe<_sharp.Sharp> // = origSharp

    // Pick the smallest fit size that's larger than the largest square:
    const lsq = ImageSize.largestSq()
    const bestFitNameForSq = leastBy(fits, ([outputSize, fit]) =>
      map(lsq.outputSize(outputSize), () => fit.megapixels())
    )?.[1].name

    const origDim = pick(best, "width", "height") as Dimensions
    {
      let fitSharp = origSharp.clone()
      let fitDim = origDim

      for (const [outputSize, fit] of fits) {
        const start = Date.now()
        const inputDim = fitDim
        const dest = this.ap
          .fileForWidth(fit.reducer.name, outputSize.width)
          .wip()
        fitSharp = fit.resize(outputSize, fitSharp)
        fitDim = outputSize

        if (fit.name === bestFitNameForSq) {
          sqSharp = fitSharp.clone()
          sqDim = outputSize
        }

        if (
          dmegapixels(outputSize) >= 1 &&
          fit.reducer === Fit &&
          Settings.includePreviewTags.valueOrDefault
        ) {
          previewTagFiles.push(dest)
        }

        await fit.toJpeg({
          path: dest.nativePath,
          sh: fitSharp,
          outputSize
        })
        pe.onProgress()

        this.logger.debug(
          "resize(" +
            fit.name +
            ") " +
            fmtDim(inputDim) +
            " -> " +
            fmtDim(outputSize) +
            " in " +
            (Date.now() - start) +
            " ms"
        )
        files.push(dest)
      }
    }

    // sq
    {
      if (sqSharp == null) {
        this.logger.debug("square resize(): resorting to ", {
          origDim,
          bestFitNameForSq
        })
        sqSharp = origSharp
        sqDim = origDim
      } else {
        this.logger.debug("square resize(): using to ", {
          sqDim,
          bestFitNameForSq
        })
      }

      let positioned = false

      // We do square resizing synchronously to ensure the crop is consistent:

      for (const sq of ImageSize.sq()) {
        const start = Date.now()
        const inputDim = sqDim!
        const outputSize = sq.outputSize(sqDim ?? origDim)
        if (outputSize == null) {
          this.logger.debug("skipping square output for " + sq.max)
          continue
        }

        // Positioning the crop takes time, even if it's a no-op, so only do it if
        // the input isn't square already.
        if (!positioned) {
          outputSize.position = Settings.squareThumbStrategy.valueOrDefault
          positioned = true
        }

        this.logger.debug("Applying ", { outputSize })

        const dest = this.ap
          .fileForWidth(sq.reducer.name, outputSize.width)
          .wip()

        sq.resize(outputSize, sqSharp!)

        sqDim = outputSize
        await sq.toJpeg({
          path: dest.nativePath,
          sh: sqSharp!,
          outputSize
          // no srcFile, no need to embed previews for tiny squares.
        })
        pe.onProgress()
        files.push(dest)
        this.logger.debug(
          "resize(" +
            sq.name +
            ") " +
            fmtDim(inputDim) +
            " -> " +
            fmtDim(outputSize) +
            " in " +
            (Date.now() - start) +
            " ms"
        )
      }
    }

    try {
      await includePreviewTags_(srcFile, previewTagFiles).catch(error => {
        this.logger.warn(
          "Failed to restore metadata tags into previews for " + srcFile,
          {
            error
          }
        )
      })
      // don't unlink just-unwipped files!
      const oldPaths = new Set(priorExistingFiles.map(ea => ea.nativePath))
      for (const ea of files) {
        const unwipped = await ea.unwip_()
        oldPaths.delete(unwipped.nativePath)
      }
      await unlink(oldPaths)
      await this.ap.writeInfo(info)
      this.logger.debug("Previews unwipped and info written", { info })
    } catch (cause) {
      await unlink(files)
      throw new WrappedError("Failed to create previews from " + srcFile, {
        cause,
        path: srcFile.nativePath,
        fatal: false
      })
    }
    return info
  }
}
