import { lazy } from "../core/Lazy"
import { mkLogger } from "../core/Logger"
import { AssetFileVersion, AssetVersion } from "../core/PhotoStructureVersions"
import { shortStringSha } from "../core/StringHash"
import { SqliteBase } from "../core/db/Schemas"
import { cacheDir_ } from "../core/dir/CacheDir"
import { PosixFile } from "../core/fs/PosixFile"
import { LibraryUIDStore, SystemUIDStore } from "../core/fs/UIDStore"
import { writeTextfile_ } from "../core/fs/WriteFile"
import { TokenRadix } from "../core/math/Radix"
import { max } from "../core/math/Vector"
import { Settings } from "../core/settings/Settings"
import { libraryHasSettings } from "../core/settings/SettingsIO"
import { dayMs } from "../fe/Date"
import { stringify } from "../fe/JSON"
import { PromiseMaybe } from "../fe/MaybeTypes"

const logger = lazy(() => mkLogger("library.StatsDbDir"))

export const CacheDirPrefix = "sync-state-"

export function statDbDirs(): PromiseMaybe<PosixFile[]> {
  return PosixFile.for(cacheDir_())
    .clear()
    .children(ea => ea.name.startsWith(CacheDirPrefix))
}

// TCBH: By including the settings in the cache dir, we reset the cache dir if
// the settings change. Note that this is a short SHA, as there won't be many
// items (therefore not many chances for collisions). 3 chars of SHA goodness
// would be most likely be fine.
/**
 * @throws if the directory can't be created.
 */
export async function statsDbDir_(
  createIfMissing = true
): PromiseMaybe<PosixFile> {
  if (!libraryHasSettings()) return

  // Really only library settings should invalidate a cache dir:
  // Rather than push the version, we'll push db versions, which only change
  // when something in the asset processing pipeline changes to warrant
  // re-syncing everything.
  const values = {
    AssetVersion,
    AssetFileVersion,
    LibraryUID: await LibraryUIDStore()?.readUid_(),
    SystemUID: await SystemUIDStore()?.readUid_()
  }
  const suffix = shortStringSha(stringify(values), 10, TokenRadix)
  const dir = PosixFile.for(cacheDir_()).join(CacheDirPrefix + suffix)
  if (!createIfMissing) return dir
  await writeTextfile_(
    dir.join("README.txt").nativePath,
    "",
    "This folder holds state for library synchronization of",
    "",
    Settings.libraryDir.value!,
    "",
    "Don't edit the contents of this directory while PhotoStructure is running.",
    "",
    "If you have any questions, please visit https://forum.photostructure.com",
    "",
    stringify(values, undefined, 2)
  )
  logger().info("Set up statsDbDir dir " + dir)
  return dir
}

async function rmStatDbDir(dir: PosixFile) {
  if (true === dir.name.startsWith(CacheDirPrefix)) {
    await dir.rmrf()
  } else {
    logger().warn("rmStatDbDir(): refusing to rm " + dir)
  }
}

export async function vacuumStatDbDirs() {
  const currentCacheDir = await statsDbDir_()
  for (const dir of (await statDbDirs()) ?? []) {
    if (dir.eql(currentCacheDir)) continue
    // don't delete the directory if it's been recently touched:
    const newestTs = max(
      await Promise.all([
        dir.join("README.txt").mtimeMs(),
        dir.join(SqliteBase).mtimeMs()
      ])
    )
    if (newestTs == null || newestTs < Date.now() - dayMs)
      await rmStatDbDir(dir)
  }
}

export async function rmStatDbDirs() {
  for (const dir of (await statDbDirs()) ?? []) {
    logger().warn("deleting cache dir " + dir)
    await rmStatDbDir(dir)
  }
}
