import { memoryUsage } from "process"
import { sigFigs } from "../fe/Number"
import { MB } from "../fe/Units"
import { sum } from "./math/Vector"

export function memoryUsageBytes() {
  const mem = memoryUsage()
  // These are sometimes not defined. We don't want to return NaN.
  return sum([mem.external, mem.heapUsed, mem.arrayBuffers])
}

export function memoryUsageMb() {
  return sigFigs(memoryUsageBytes() / MB, 2)
}

export function memoryUsageRssBytes() {
  return memoryUsage().rss
}

export function memoryUsageRssMb() {
  return sigFigs(memoryUsageRssBytes() / MB, 2)
}
