// TODO: This whole thing is SITS, because we should be able to just give the
// random function in SQLite a seed

// but we can't

// because everything is terrible

// The score for a given row is (ax^2 + bx)(cx + d)

// X^2 is required for reasonable diff distribution between X_ks.

// Related: https://en.wikipedia.org/wiki/Linear_congruential_generator

export function prngOrderByClause(
  seed: number,
  idColumn: string,
  max: number
): string {
  const [a, b, c, d, e] = factors(seed)
  const x = idColumn
  const i = `(${a}*${x}*${x}+${b}*${x})%${max}`
  const j = `(${c}*${x}+${d})%${max}`
  return `((${i})*(${j})+${e})%${max}`
}

export function prng(seed: number, x: number, max: number): number {
  const [a, b, c, d, e] = factors(seed)
  const y1 = (a * x * x + b * x) % max
  const y2 = (c * x + d) % max
  return Math.round((y1 * y2 + e) % max)
}

