import { version, versionMajor, versionMinor, versionPatch } from "../Version"
import { extractUpdateChannel } from "./UpdateChannels"

export function channel() {
  return extractUpdateChannel(version)
}

/**
 * @return "1.2.3", even if `version` is "1.2.3-alpha.0" or "1.2.3-beta.32"
 */
export const baseVersion = () =>
  [versionMajor, versionMinor, versionPatch].join(".")

/**
 * Used for version documentation at https://photostructure.com/v${versionMajorMinor}/
 */
export const versionMajorMinor = versionMajor + "." + versionMinor
