import { ExifDateTime, UnsetZoneOffsetMinutes } from "exiftool-vendored"
import { DateTime, Info, Zone } from "luxon"
import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { toFloat, toInt, within } from "../../fe/Number"
import { isEmptyObj } from "../../fe/Object"
import { toS } from "../../fe/toS"
import { mapEntries } from "../Object"
import { RegExpOptional, concatRegexp } from "../RegExp"
import { Settings } from "../settings/Settings"
import { DateObject } from "./Date"
import { zoneOffsetToName } from "./FuzzyDate"
import {
  dayRE,
  hourRE,
  minuteRE,
  monthRE,
  nonNumericLookbackRE,
  secondRE,
  subsecRE,
  yearRE
} from "./FuzzyDateParser"
import { hasTime } from "./HasTime"
import {
  TimezoneOffsetRE,
  timezoneOffsetFromRegExpMatch,
  zoneToTzOffsetMinutes
} from "./Timezone"

// If `exiftool-vendored` and PhotoStructure's versions of Luxon are
// different, Luxon doesn't work correctly unless we do this:

export const UnsetZone = Info.normalizeZone(UnsetZoneOffsetMinutes)

export function parseExifDateTimeFormat({
  input,
  format,
  defaultZone
}: {
  input: Maybe<string>
  format: string
  defaultZone?: string | Zone
}): Maybe<ExifDateTime> {
  return blank(input)
    ? undefined
    : // use ExifDateTime to clear the UnsetZone:
      ExifDateTime.fromDateTime(
        DateTime.fromFormat(input, format, {
          zone: defaultZone ?? UnsetZone,
          setZone: true
        }),
        { rawValue: input, unsetMilliseconds: true }
      )
}

export function parseExifDateTimeFromRFC2822(
  input: Maybe<string>
): Maybe<ExifDateTime> {
  return blank(input)
    ? undefined
    : ExifDateTime.fromDateTime(
        DateTime.fromRFC2822(input, { zone: UnsetZone, setZone: true })
      )
}

export function parseExifDateTime(
  input: Maybe<string>,
  defaultZone?: string
): Maybe<ExifDateTime> {
  return blank(input)
    ? undefined
    : // fromEXIF also calls fromISO, and only returns valid instances.
      ExifDateTime.from(input, defaultZone) ??
        parseExifDateTimeRe(input, ymd_hmsRegExp(), defaultZone)
}

export function parseExifDateTimeRe(
  input: string,
  re: RegExp,
  defaultZone?: string
): Maybe<ExifDateTime> {
  const m = re.exec(input)
  if (m == null || isEmptyObj(m.groups)) return

  const { year, month, day, hour, minute, second } = mapEntries(
    m.groups,
    (_k, v) => toInt(v)
  )

  if (year == null || month == null || day == null) {
    return
  }
  if (
    !Settings.fuzzyDateParsing.valueOrDefault &&
    (hour == null || minute == null)
  ) {
    return
  }

  const millisecond = map(toFloat(m.groups.subsec), f =>
    within(0, 1, f) ? Math.round(f * 1000) : undefined
  )

  const tzoffsetMinutes = timezoneOffsetFromRegExpMatch(m)
  const zone = zoneOffsetToName(tzoffsetMinutes) ?? defaultZone
  const obj = {
    year,
    month,
    day,
    hour: hour ?? 0,
    minute: minute ?? 0,
    second: second ?? 0,
    millisecond,
    zone,
    rawValue: input,
    tzoffsetMinutes
  }
  return dateObjectToExifDateTime(obj)
}

const sep = /[-:_\s]?/

const ymd_hmsRegExp = lazy(() =>
  concatRegexp([
    nonNumericLookbackRE,
    yearRE,
    sep,
    monthRE,
    sep,
    dayRE,
    /[T\s]/,
    hourRE,
    sep,
    minuteRE,
    sep,
    secondRE,
    subsecRE,
    /\s?/,
    RegExpOptional.from(TimezoneOffsetRE())
  ])
)

export function dateObjectToExifDateTime(
  obj: Maybe<DateObject>
): Maybe<ExifDateTime> {
  if (obj == null || !hasTime(obj)) return
  if (obj instanceof ExifDateTime && obj.isValid) return obj
  try {
    const tzoffsetMinutes =
      obj.tzoffsetMinutes ??
      zoneToTzOffsetMinutes(
        new Date(obj.year, obj.month - 1, obj.day).getTime(),
        obj.zone
      )
    const edt = ExifDateTime.fromJSON({
      ...obj,
      tzoffsetMinutes,
      zoneName: map(obj.zone, toS)
    } as any)

    return edt.isValid ? edt : undefined
  } catch {
    return
  }
}
