import { Dimensions } from "./Dimensions"

export function fitInside(input: Dimensions, max: Dimensions) {
  const inputAspectRatio = input.width / input.height
  const maxAspectRatio = max.width / max.height

  if (inputAspectRatio >= maxAspectRatio) {
    // too wide, height takes aspect ratio into account.
    const width = max.width
    return {
      width,
      height: Math.floor(width / inputAspectRatio)
    }
  } else {
    // too tall, width takes aspect ratio into account.
    const height = max.height
    return {
      width: Math.floor(height * inputAspectRatio),
      height
    }
  }
}
