import { PathTag } from "../../fe/api/Asset"
import { TagPath, TagRef } from "../../fe/api/Tag"
import { compact, isEmpty, isNotEmpty, last, uniqBy } from "../../fe/Array"
import { sortBy } from "../../fe/ArraySort"
import { blank, notBlank } from "../../fe/Blank"
import { mapOr } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { isString, replaceAll } from "../../fe/String"
import { toA } from "../../fe/toA"
import { toS } from "../../fe/toS"
import { ancestry } from "../Array"
import { Settings } from "../settings/Settings"
import { equalsIgnoreCase, includesIgnoreCase } from "../String"

/**
 * ASCII unit separator. We're using an ASCII-separated path, rather than a
 * JSON-encoded path, to make "getDescendants" by path easier.
 */
export const TagSep = String.fromCharCode(31)

export function tagRefToS(tagref: Maybe<string | TagRef>): string {
  // normalize() is required to make macOS paths match other platforms:
  return toS(isString(tagref) ? tagref : tagref?.name).normalize()
}

// export function tagPathToTagRef(ref: TagPath): TagRef[] {
//   return ref.map(ea => (isTagRef(ea) ? ea : { name: toS(ea) }))
// }

export function tagPathToPathTag(tp: TagPath): PathTag[] {
  return ancestry(tagPathToStringArray(tp)).map((tagPath, i) => ({
    tagPath,
    displayName: (tagPath[i] as any)["displayName"]
  }))
}

export function tagPathToStringArray(tagPath: TagPath): string[] {
  return compact(tagPath.map(tagRefToS))
}

export function joinTagPath(tagPath: TagPath, _sep = TagSep): string {
  return (
    tagPathToStringArray(tagPath)
      .map(ea => replaceAll(ea, _sep, ""))
      .join(_sep) + (_sep === TagSep ? _sep : "")
  )
}

export function joinedTagPathBasename(tagPath: string) {
  return last(splitTagPath(tagPath))
}

export function splitTagPath(tagPath: string, _sep = TagSep): string[] {
  // filter(notBlank) removes the empty final path element due to suffixing with
  // TagSep:
  return toS(tagPath).split(_sep).filter(notBlank)
}

export function normalizeTagPath(tagPath: string): string {
  return joinTagPath(splitTagPath(tagPath))
}

export function uniqTagPaths(arr: Maybe<Maybe<TagPath>[]>) {
  return uniqBy(toA(arr), t => (isEmpty(t) ? undefined : joinTagPath(t)))
}

function tagPathToLowerStr(tagPath: TagPath): string {
  return tagPathToStringArray(tagPath).join(TagSep).toLowerCase() // case insensitive
}

export function tagPathEql(a: TagPath, b: TagPath): boolean {
  return a != null && b != null && tagPathToLowerStr(a) === tagPathToLowerStr(b)
}

/**
 * @return elements in `a` that are not in `b`
 */
export function tagDiff(
  a: Maybe<Maybe<TagPath>[]>,
  b: Maybe<Maybe<TagPath>[]>
): TagPath[] {
  const bPaths = new Set(compact(b).map(tag => tagPathToLowerStr(tag)))
  return compact(a).filter(tag => !bPaths.has(tagPathToLowerStr(tag)))
}

export function tagPathsInclude(needle: TagPath, haystack: TagPath[]) {
  const s = tagPathToLowerStr(needle)
  return haystack.some(ea => s === tagPathToLowerStr(ea))
}

export function tagPathLeaf(tagPath: Maybe<TagPath | string>): Maybe<string> {
  return blank(tagPath)
    ? undefined
    : Array.isArray(tagPath)
    ? tagRefToS(last(tagPath))
    : last(splitTagPath(tagPath))
}

export function leafIsExcluded(
  tagPath: Maybe<TagPath | string>,
  excluded: string[]
): boolean {
  return mapOr(
    tagPathLeaf(tagPath),
    leaf => includesIgnoreCase(excluded, leaf),
    () => false
  )
}

/**
 * @return true if ancestor is equivalent to or is an ancestor of `descendant`
 */
export function isTagPathDescendant(
  ancestor: TagPath,
  descendant: TagPath
): boolean {
  if (
    isEmpty(ancestor) ||
    isEmpty(descendant) ||
    ancestor.length > descendant.length
  ) {
    return false
  }
  const des = tagPathToStringArray(descendant)
  return tagPathToStringArray(ancestor).every((ea, i) =>
    equalsIgnoreCase(ea, des[i])
  )
}

export function omitAncestorTags(tagPaths: TagPath[]): TagPath[] {
  if (!Settings.omitAncestorTags.valueOrDefault) return tagPaths

  // longest tags should "win". Omit all tags that are ancestors of winning tags.
  const result: TagPath[] = []

  for (const tagPath of sortBy(tagPaths.filter(isNotEmpty), ea => -ea.length)) {
    if (!result.some(ea => isTagPathDescendant(tagPath, ea))) {
      result.push(tagPath)
    }
  }
  // restore the prior sort order:
  return sortBy(result, ea => tagPaths.indexOf(ea))
}
