const logger = lazy(() => mkLogger("tags.CapturedAt"))

export interface CapturedAtWithTz extends Pick<Tags, "tz" | "tzSource"> {
  capturedAt: CapturedAt
}

/**
 * Should a captured-at source be considered high-quality enough to be stored
 * with copies?
 */
export function capturedAtSrcFromTags(src: string): boolean {
  return toS(src).toLowerCase().includes("tags")
}

export function capturedAtSrcFromStat(src: string): boolean {
  src = toS(src).toLowerCase()
  // We're copying over the tags, so we don't need those:
  return src.includes("stat") && !src.includes("+") // < not "bname+stat"
}

export type CapturedAtJson = Omit<ReturnType<CapturedAt["toJSON"]>, "_ctor">

export class CapturedAt {
  readonly nativePath: string
  readonly date: Dated
  readonly src: string
  readonly local: Maybe<number>
  readonly offset: Maybe<number>
  readonly precisionMs: Maybe<number>
  readonly mtime: Maybe<number>

  static fromAssetFile(af: AssetFileCapturedAtFields) {
    return map(
      localToDateTime(af.capturedAtLocal, af.capturedAtOffset),
      date =>
        new CapturedAt({
          nativePath: af.nativePath,
          date,
          src: af.capturedAtSrc,
          mtime: af.mtime,
          precisionMs: af.capturedAtPrecisionMs,
          offset: af.capturedAtOffset,
          local: af.capturedAtLocal
        })
    )
  }

  static for(args: CapturedAtJson) {
    return new CapturedAt(args)
  }

  private constructor(args: CapturedAtJson) {
    this.nativePath = toNativePath_(args.nativePath)
    this.date = args.date
    this.src = args.src
    this.local =
      (args.local === 0 ? undefined : args.local) ?? datedToLocal(this.date)
    this.offset = args.offset ?? datedToOffsetMinutes(this.date)
    let precisionMs =
      (args.precisionMs === 0 ? undefined : args.precisionMs) ??
      datedToPrecisionMs(this.date)
    if (capturedAtSrcFromStat(this.src)) {
      precisionMs = Math.max(
        Settings.minCapturedAtPrecisionStat.valueOrDefault,
        precisionMs
      )
    }
    this.precisionMs = precisionMs
    this.mtime = datedToMillis(args.mtime) ?? statSync(this.nativePath)?.mtimeMs
  }

  toJSON() {
    return {
      // "_ctor" is magick! see ../fe/JSON.
      _ctor: CapturedAt.name,
      ...pick(
        this,
        "nativePath",
        "date",
        "src",
        "local",
        "offset",
        "precisionMs",
        "mtime"
      )
    }
  }

  static fromJSON(obj: ReturnType<CapturedAt["toJSON"]>): CapturedAt {
    return new CapturedAt(obj)
  }

  spread(p: Partial<CapturedAt>): CapturedAt {
    return new CapturedAt({ ...this.toJSON(), ...p })
  }

  toISOString() {
    return datedToISO(this.date)
  }

  toExifString() {
    return datedToEXIF(this.date)
  }

  exifOffsetMinutes() {
    return this.offset == null ? undefined : fmtOffsetMinutes(this.offset)
  }

  get isFromTags() {
    return capturedAtSrcFromTags(this.src)
  }

  get isInferred() {
    return !capturedAtSrcFromTags(this.src) && !capturedAtSrcFromStat(this.src)
  }

  get isFuzzy() {
    const l = this.local
    return !this.isFromTags || l == null || localIsFuzzy(l)
  }

  toString() {
    return stringify({
      nativePath: this.nativePath,
      date: this.toISOString(),
      src: this.src
    })
  }

  toISOStringWithPrecision() {
    return (
      datedToISO(this.date, true, this.precisionMs) +
      (this.precisionMs == null ? "" : " (" + fmtMs(this.precisionMs) + ")")
    )
  }

  hasTz() {
    return hasZone(this.date) || this.offset != null
  }

  isUTC() {
    return this.offset === 0
  }

  hasTime() {
    return hasTime(this.date)
  }

  hasMillis() {
    return gt0(getMillisecond(this.date))
  }

  toMillis() {
    return datedToMillis(this.date)
  }

  get isValid() {
    return isValidDate(this.date)
  }

  get zoneName() {
    return offsetMinutesToZoneName(this.offset) ?? getZoneName(this.date)
  }

  toAssetFileFields(): Maybe<AssetFileCapturedAtFields> {
    const capturedAtLocal = this.local
    return capturedAtLocal == null || this.mtime == null
      ? undefined
      : {
          nativePath: this.nativePath,
          mtime: this.mtime,
          capturedAtLocal,
          capturedAtOffset: this.offset,
          capturedAtPrecisionMs: this.precisionMs, // < NOT clamped or fuzzy!
          capturedAtSrc: this.src
        }
  }

  asExifTag(): {
    SubSecDateTimeOriginal?: string
    DateTimeOriginal?: string
    OffsetTime?: string
  } {
    const OffsetTime = map(this.offset, fmtOffsetMinutes)
    const value = this.toExifString()
    return this.hasMillis()
      ? { SubSecDateTimeOriginal: value, OffsetTime }
      : { DateTimeOriginal: value, OffsetTime }
  }

  fuzzyPrecisionMs() {
    return (
      (this.isFuzzy ? Settings.fuzzyDatePrecisionCoeff.valueOrDefault : 1) *
      (this.precisionMs ?? secondMs)
    )
  }

  /**
   * Return the localtime, minus the precision of this current time.
   */
  localBoundaries(args?: { delta?: number }): Maybe<{
    start: number
    end: number
  }> {
    const delta = (args?.delta ?? 0) + this.fuzzyPrecisionMs()
    const d = datedToStartDateTime(this.date) ?? datedToDateTime(this.date)
    if (d == null) {
      logger().warn("localBoundaries() failed", {
        date: this.date,
        args,
        d,
        path: this.nativePath
      })
      return
    }
    return {
      start: datedToLocal(d.minus(delta)),
      end: datedToLocal(d.plus(delta))
    }
  }

  /**
   * Taking into account `precisionMsIs `other`
   */
  overlaps(that: Maybe<CapturedAt>) {
    if (that == null) return false
    if (this.local === that.local) return true

    const thisMime = extMimetype(this.nativePath)
    const thatMime = extMimetype(that.nativePath)
    const min =
      thisMime === thatMime
        ? 0
        : Settings.minCapturedAtPrecisionDifferentMimetypes.valueOrDefault

    const aPrecisionMs = Math.max(min, this.fuzzyPrecisionMs())
    const bPrecisionMs = Math.max(min, that.fuzzyPrecisionMs())
    return logger().tap({
      msg: "overlaps()",
      result:
        this.local === that.local ||
        datedOverlap({
          a: this.date,
          b: that.date,
          aPrecisionMs,
          bPrecisionMs
        }).aOverlapsB,
      meta: { a: this, b: that, aPrecisionMs, bPrecisionMs }
    })
  }
}

export function bestCapturedAt(arr: Maybe<CapturedAt>[]): Maybe<CapturedAt> {
  // Always take the value from the newest file (in case the date was updated).
  // Prefer tags, then paths. Ignore stat, which is not trustworthy.
  const sorted = sortBy(arr, ea => -(ea?.mtime ?? 0))
  return (
    sorted.find(ea => ea.src.includes("tags")) ??
    sorted.find(ea => ea.src.includes("bname+stat")) ??
    sorted.find(ea => ea.src.includes("path+stat")) ??
    sorted.find(ea => ea.src.includes("bname")) ??
    sorted.find(ea => ea.src.includes("path")) ??
    sorted.find(ea => ea.src.includes("siblings")) ??
    sorted.find(ea => ea.src.includes("stat")) ??
    sorted[0] // should never happen
  )
}

function cannotUseDateFromPath(f: PosixFile, skipInference: boolean): boolean {
  return (
    f == null ||
    skipInference ||
    !Settings.usePathsToInferDates.valueOrDefault ||
    (!Settings.useLibraryDirsToInferDates.valueOrDefault &&
      containedByNativePath({
        ancestor: Settings.libraryDir.value,
        descendant: f.nativePath,
        acceptSelf: true
      }))
  )
}

/**
 * @param skipInference if the file is a temporary scratch file (like a screenshot from a
 * video or a raw image capture), and more expensive extraction heuristics
 * should be skipped.
 */
// no deps on posixfile because posixfile depends on tag parsing
export async function extractCapturedAt(
  f: PosixFile,
  tags: Maybe<ParsedTags & Partial<WithCapturedAt>>,
  skipInference: boolean = false
): PromiseMaybe<CapturedAt> {
  const mtime = await f.mtimeMs()

  async function from(
    src: string,
    result: MaybeSyncOrAsync<{
      src?: string
      date: Maybe<Dated>
      precisionMs?: number
    }>
  ): PromiseMaybe<CapturedAt> {
    const r = await result
    return r == null || r.date == null || !isValidDate(r?.date)
      ? undefined
      : CapturedAt.for({
          nativePath: f.nativePath,
          date: r.date,
          local: datedToLocal(r.date),
          src: uniqPath([src, r.src]),
          mtime,
          precisionMs: r.precisionMs,
          offset: datedToOffsetMinutes(r.date)
        })
  }

  const skipPathInference = cannotUseDateFromPath(f, skipInference)

  const result = await firstDefinedLater<CapturedAt>(
    () => tags?.capturedAt,
    () => from("tags", capturedAtFromTags(tags)),
    () => tags?.inferred?.capturedAt,

    () =>
      skipPathInference
        ? undefined
        : from("bname+stat", extractStatBnameTime(f)),
    () =>
      skipPathInference ? undefined : from("path+stat", extractStatPathTime(f)),
    () =>
      skipInference
        ? undefined
        : from("siblings", inferCapturedAtFromSiblings(f)),
    () =>
      skipPathInference
        ? undefined
        : from("bname", capturedAtFromBasename(f, tags)),
    () => (skipPathInference ? undefined : from("path", capturedAtFromPath(f))),
    () =>
      !Settings.useStatToInferDates.valueOrDefault
        ? undefined
        : from("stat", capturedAtFromStat(f))
  )

  return logger().tap({
    level: LogLevels.debug,
    msg: "extractCapturedAt()",
    result
  })
}

export function capturedAtFromTags(
  t: Maybe<Tags>
): Maybe<{ date: Dated; src: string; precisionMs?: number }> {
  // Try to use a "good" tag first, but if that fails, try the "problematic"
  // tags in order
  return (
    _capturedAtFromTags(t, Settings.capturedAtTags.values) ??
    _capturedAtFromTags(t, OverrideUTCZoneTags) ??
    _capturedAtFromTags(t, Settings.capturedAtTagsFallback.values)
  )
}

const OverrideUTCZoneTags = ["DateTimeUTC", "GPSDateTime"]

export function _capturedAtFromTags(
  t: Maybe<Tags>,
  keys: string[]
): Maybe<{ date: Dated; src: string; precisionMs?: number }> {
  if (t == null || isEmpty(keys)) return

  const arr: {
    localSec: number
    date: Dated
    src: string
    precisionMs: number
    rawValue: Maybe<string>
  }[] = []

  // We want the earliest date with the best precision. To do that, we get the
  // least date (which may be quite coarse, like year-only). We then find the
  // least date that overlaps with that date with the best precision and least
  // local value.

  for (const key of keys) {
    let date = (t as any)[key]
    let src = key
    if (isValidDate(date)) {
      if (
        isValidZone(t.tz) &&
        (!hasZone(date) || OverrideUTCZoneTags.includes(src))
      ) {
        date = setZone(date, t.tz) ?? date
        const tzSource = blank(t.tzSource)
          ? ""
          : " from " + stripPrefix(t.tzSource, "from ")
        src = src + ` (TZ ${t.tz}${tzSource})`
      }
      arr.push({
        localSec: datedToLocalSec(date),
        date,
        src,
        precisionMs: datedToPrecisionMs(date),
        rawValue: (t as any)[key]?.rawValue
      })
    }
  }

  const least = leastBy(arr, ea => datedToStartTs(ea.date))
  if (least == null) return

  const overlapsWithLeast = arr.filter(
    ea =>
      eql(ea.date, least.date) ||
      (ea.precisionMs <= least.precisionMs &&
        datedOverlap({
          a: ea.date,
          b: least.date,
          aPrecisionMs: ea.precisionMs,
          bPrecisionMs: least.precisionMs
        }).aOverlapsB)
  )

  // We want the earliest date with the best precision.

  const earliest = leastBy(overlapsWithLeast, ea => [
    // Strip off the seconds, minutes, and hours (to handle timezones)
    Math.floor(ea.localSec / 1_00_00_00),

    // prefer dates with zones
    hasZone(ea.date) ? 0 : 1,

    // best precision wins
    ea.precisionMs,

    // prefer longer dates (!!): they are expected to be more precise, as they
    // will include either/and milliseconds and/or timezone. This should stay
    // at pretty much the bottom of the heuristics, though, as it is fuzzy.
    -1 * (ea.rawValue?.length ?? 0),

    ea.src // < for determinism
  ])

  logger().debug("capturedAtFromTags()", { least, overlapsWithLeast, earliest })

  return earliest
}

export function capturedAtFromBasename(
  f: PosixFile,
  t: Maybe<Pick<Tags, "tz">>
): Maybe<{ date: Dated; precisionMs?: number }> {
  if (!Settings.usePathsToInferDates.valueOrDefault) return
  for (const input of uniq([bname(f, true), bname(f, false)])) {
    const date = parseDated({ input, defaultZone: t?.tz })
    if (date != null) {
      return {
        date,
        precisionMs: max([datedToPrecisionMs(date), secondMs])
      }
    }
  }
  return
}

export function capturedAtFromPath(
  f: PosixFile
): Maybe<{ date: Maybe<Dated> }> {
  if (!Settings.usePathsToInferDates.valueOrDefault) return
  return {
    date: extractDateFromPath(f.pathsForDateParsing)
  }
}

export async function capturedAtFromStat(
  f: PosixFile
): PromiseMaybe<{ date: Maybe<Dated> }> {
  if (!Settings.useStatToInferDates.valueOrDefault) return
  return { date: await f.minStatDate() }
}
