import { lazy } from "../core/Lazy"
import { ancestorWithChildren } from "./fs/Ancestors"
import { joinNativePath } from "./fs/Path"

// This was broken out of ExamplesDir so assertEqlPrior could avoid a deploop
export const examplesNativePath_ = lazy(() => {
  const exampleParentDir = ancestorWithChildren(__dirname, ["examples"])
  if (exampleParentDir == null) {
    throw new Error("Couldn't find the examples directory")
  }
  return joinNativePath([exampleParentDir, "examples"])
})
