import { Command } from "commander"
import { CommandPlugin } from "../../core/cli/CLI"
import { Settings } from "../../core/settings/Settings"
import { isTrue } from "../../fe/Boolean"
import { Obj } from "../../fe/Object"

export const PauseSyncArg: CommandPlugin = {
  beforeParse: (cmd: Command) =>
    cmd.option(
      "--pauseSync",
      `Pause sync processing until manually resumed via the system tray or nav menu.`
    ),
  afterParse: (opts: Obj) => {
    if (isTrue(opts.pauseSync)) {
      Settings.startPaused.envValue = true
    }
  }
}
