import {
  BinaryField,
  ExifDate,
  ExifDateTime,
  ExifTime
} from "exiftool-vendored"
import { DateTime } from "luxon"
import { lazy } from "../core/Lazy"
import { defer } from "../fe/Defer"
import { JsonReviver, addJsonRevivers } from "../fe/JSON"
import { compactValues } from "../fe/Object"
import { DateInterval } from "./date/DateInterval"
import { FuzzyDate } from "./date/FuzzyDate"
import { WrappedError } from "./error/WrappedError"
import { CapturedAt } from "./tags/CapturedAt"

const FixDateTimeToJSON = defer(() => {
  ;(DateTime as any).prototype["toJSON"] = function toJSON() {
    return compactValues({
      _ctor: DateTime.name,
      ts: this.toMillis(),
      zone: this.zone?.type === "system" ? undefined : this.zoneName
    })
  }
})

const DateTimeReviver: JsonReviver = {
  name: DateTime.name,
  fromJSON(ea: any) {
    // DateTime requires the zone to be thrown in as a second options hash (!!!)
    return DateTime.fromMillis(ea.ts, compactValues({ zone: ea.zone }))
  }
}

export const addDefaultJsonRevivers = lazy(() => {
  FixDateTimeToJSON()
  // FixBinaryFieldToJSON()
  addJsonRevivers(
    ExifDateTime,
    ExifDate,
    ExifTime,
    DateTimeReviver,
    CapturedAt,
    DateInterval,
    FuzzyDate,
    BinaryField,
    // no need to revive binary field
    WrappedError
  )
})
