import { LibraryTagName, TagPath, TagRoots } from "../../fe/api/Tag"
import { compactBlanks } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { PS_LIBRARY_SCHEME, PS_LOCAL_FILE_SCHEME } from "../../fe/URI"
import { toURI, URI } from "../uri/URI"

/**
 * @param isFile if true, don't include the last path element in `uri` (as
 * it's the filename, and that's not a tag)
 */
export function uriToTagPath({
  uri,
  isFile
}: {
  uri: Maybe<URI | string>
  isFile: boolean
}): Maybe<TagPath> {
  if (uri == null) return
  const u = toURI(uri)
  const authority =
    u.scheme === PS_LIBRARY_SCHEME
      ? LibraryTagName
      : u.scheme === PS_LOCAL_FILE_SCHEME
      ? u.authority
      : undefined

  if (blank(authority)) return

  // simple file URIs won't have a valid authority.
  // slice off the filename:
  const arr = u.path.split("/")

  return compactBlanks([
    TagRoots.fs,
    authority,
    ...(isFile ? arr.slice(0, -1) : arr)
  ])
}
