import fs from "fs"
import { Transform, TransformCallback } from "stream"
import { pipeline } from "stream/promises"
import { toS } from "../../fe/toS"
import { LineReader } from "./LineReader"
import { SimpleFile, toNativePath_ } from "./SimpleFile"
import { WritableToBuffer } from "./WritableToBuffer"

export class GrepReader extends Transform {
  constructor(
    readonly pattern: RegExp,
    readonly sep = "\n"
  ) {
    super({ objectMode: false, autoDestroy: true })
  }

  _transform(chunk: any, _encoding: BufferEncoding, done: TransformCallback) {
    const str = toS(chunk)
    const m = str.match(this.pattern)
    if (m != null) {
      this.push(m[1] ?? m[0] + this.sep)
    }
    done()
  }
}

export async function fileGrep_(file: SimpleFile | string, pattern: RegExp) {
  const output = new WritableToBuffer()
  await pipeline([
    fs.createReadStream(toNativePath_(file)),
    new LineReader(),
    new GrepReader(pattern),
    output
  ])
  return (await output.buffer).toString()
}
