import { tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { errorToS } from "../error/Error"
import { exiftool, exiftoolVersion_ } from "../tags/ExifTool"
import { ver } from "../version/Ver"
import { HealthCheck } from "./HealthCheck"

export const exiftoolHealthCheck = defer(() =>
  HealthCheck.for({
    section: "Tools",
    id: "tools-exiftool",
    pendingMsg: "Checking ExifTool",
    settings: [],
    later: async () => {
      try {
        const ev = await exiftoolVersion_()
        return {
          level: "ok",
          msg: [
            "ExifTool is OK",
            "ExifTool " + ver(ev),
            tt(exiftool().options.exiftoolPath)
          ],
          meta: { exiftoolVersion: ev }
        }
      } catch (error) {
        return {
          level: "error", // < bad enough to not run!
          msg: "Something's amiss with ExifTool: " + errorToS(error as any)
        }
      }
    }
  })
)
