import { sortBy } from "../../fe/ArraySort"

const ml_kmeans = require("ml-kmeans")

export interface KMeansOptions {
  maxIterations: number
  tolerance: number
  withIterations: boolean
  distanceFunction: (a: number[], b: number[]) => number
  seed: number
  initialization: "kmeans++" | "random" | "mostDistant" | number[][]
  description?: string
  id?: string
}

export interface Centroid {
  centroid: number[]
  error?: number
  size: number
}

export interface KMeansResult {
  clusters: number[][]
  centroids: Centroid[]
  iterations: number
  converged: boolean
}

export function kmeans(
  data: number[][],
  k: number,
  options?: Partial<KMeansOptions>
): KMeansResult {
  const result: KMeansResult = ml_kmeans(data, k, options)
  return {
    ...result,
    centroids: sortBy(
      result.centroids.filter(ea => ea.size > 0),
      ea => -ea.size
    )
  }
}

