import { getDevEnvFlag } from "../../core/env/DevEnvFlags"
import { errorToS } from "../../core/error/Error"
import { HealthCheck } from "../../core/health/HealthCheck"
import { libraryHasSettings } from "../../core/settings/SettingsIO"
import { li, tt } from "../../fe/BabyMarkdown"
import { minuteMs } from "../../fe/Date"
import { defer } from "../../fe/Defer"
import { Link } from "../../fe/HealthChecks"
import { Library } from "../Library"
import { Db } from "../db/Db"
import { assertValidDbSchema_ } from "../db/DbSchemaValid"
import {
  addDbSetupErrorListener,
  clearDbSetupErrors,
  dbSetupErrors,
  hasDbSetupErrors
} from "../db/DbSetupErrors"
import { Heartbeat } from "../model/Heartbeat"

const dbRepairLink: Link = {
  text: "Attempt database repair",
  title: "Run a dump and reload of your library database to try to repair it",
  type: "button",
  method: "POST",
  url: "/admin/repair-db",
  icon: "database"
}

export const dbModelHealthCheck = defer(() => {
  const result = HealthCheck.for({
    section: "Library",
    id: "library-db",
    ordinal: 1,
    pendingMsg: "Checking library database",
    settings: ["libraryDir", "forceLocalDbReplica"],
    ttlMs: minuteMs,
    onReset: () => clearDbSetupErrors({ notifyListeners: false }),
    links: [
      {
        text: "Read about PhotoStructure and SQLite",
        icon: "docs",
        url: "https://forum.photostructure.com/t/whats-ps-force-local-db-replica/837"
      }
    ],
    later: async () => {
      if (!libraryHasSettings()) {
        return {
          level: "disabled",
          msg: [`Library database tests disabled: no library is open`]
        }
      }
      let tryRepair = false
      let db: Db | undefined
      let l: Library | undefined
      try {
        const start = Date.now()

        // if we can't open the library, we can't repair it.
        tryRepair = false
        l = Library.instanceRequired()
        // if we can't open db , we can't repair it.
        // const priorError = unrepairableLibraryDirs.get(j.db.dbFile.nativePath)
        // if (priorError != null) {
        //   return mkLibraryErrorResult({
        //     libraryDir: l.rootDir.nativePath,
        //     errorMsg: errorToHumanString(priorError),
        //     error: priorError
        //     // NO REPAIR
        //   })
        // }
        if (hasDbSetupErrors()) {
          return {
            level: "error",
            msg: [
              "Something is wrong with your library database.",
              li(dbSetupErrors())
            ],
            links: [dbRepairLink]
          }
        }
        await l.ready
        // This won't resolve until _after_ migration (whose errors we can't recover from)
        db = await l.modelDb()

        if (getDevEnvFlag("PS_FAIL_DB_HEALTH_CHECK")) {
          throw new Error("PS_FAIL_DB_HEALTH_CHECK")
        }

        // if the schema is missing tables or columns, shit's gone real bad,
        // yo. Don't attempt repair.
        await assertValidDbSchema_(db)

        // OK, so if db verify fails, or ping fails, _maybe_ verify will fix it?
        tryRepair = true
        db.verify_()

        await db.vacuum()

        Heartbeat.assertPing_()

        const info = await l.dbModelSetup_()

        return {
          level: "ok",
          msg: [
            "Library database is OK",
            li([
              "Schema validation, vacuum, optimize, and upsert round-trip took " +
                (Date.now() - start) +
                "ms",
              "Live db is " + tt(db.dbFile.nativePath),
              "Local replica mode is " + tt(info.useReplica),
              info.useReplica
                ? "Cold library db is " + tt(info.libraryDbFile)
                : undefined
            ])
          ]
        }
      } catch (error: any) {
        return {
          level: "error",
          msg: [
            "Library database validation failed",
            (tryRepair ? "" : "Unrecoverable: ") + errorToS(error),
            db == null ? undefined : "Live db is " + tt(db.dbFile.nativePath),
            l == null
              ? undefined
              : "Local replica mode is " + tt(await l.useReplica())
          ],
          links: tryRepair ? [dbRepairLink] : undefined
        }
      }
    }
  })
  addDbSetupErrorListener(() => result.refresh())
  return result
})
