import { eql } from "../fe/Eql"
import { map2Or } from "../fe/Maybe"
import { Maybe } from "../fe/MaybeTypes"
import { pick } from "../fe/Object"
import { MaybeSyncOrAsync } from "../fe/SyncOrAsync"

// TODO: INLINE
export { eql } from "../fe/Eql"
export function definedAndEql<T>(a: Maybe<T>, b: Maybe<T>): boolean {
  return a == null || b == null ? false : eql(a, b)
}

export function definedAndNotEql<T>(a: Maybe<T>, b: Maybe<T>): boolean {
  return a != null && b != null && !eql(a, b)
}

export async function eqlAsync<T>(
  a: MaybeSyncOrAsync<T>,
  b: MaybeSyncOrAsync<T>
): Promise<boolean> {
  return map2Or(await a, await b, eql, () => false)
}

export function eqlPicked<T, K extends keyof T>(
  a: Maybe<T>,
  b: Maybe<T>,
  ...keys: K[]
): boolean {
  return a == null || b == null
    ? false
    : eql(pick(a, ...keys), pick(b, ...keys))
}

export async function eqlAsyncPicked<T, K extends keyof T>(
  a: MaybeSyncOrAsync<T>,
  b: MaybeSyncOrAsync<T>,
  ...keys: K[]
): Promise<boolean> {
  return map2Or(
    await a,
    await b,
    (a1, a2) => eql(pick(a1, ...keys), pick(a2, ...keys)),
    () => false
  )
}
