import { mapGt0 } from "../../core/Number"
import { isEmpty } from "../../fe/Array"
import { gt0, toGt0 } from "../../fe/Number"
import { toA } from "../../fe/toA"
import { PartialPojo } from "../db/PartialPojo"
import { Model, ModelClass } from "./Model"

export class TimestampedModel extends Model {
  static touch<M extends TimestampedModel>(
    this: ModelClass<M>,
    ids: number[],
    Obj: PartialPojo<M> = {}
  ) {
    Obj.updatedAt ??= Date.now()
    delete Obj.id
    const arr = toA(ids).filter(gt0)
    return isEmpty(arr)
      ? undefined
      : this.dbl.runf(q => q.whereIn("id", arr).update(Obj))
  }

  createdAt?: number
  updatedAt?: number

  get createdAtDate() {
    return mapGt0(this.createdAt, ea => new Date(ea))
  }

  get updatedAtDate() {
    return mapGt0(this.updatedAt, ea => new Date(ea))
  }

  touch(Obj: PartialPojo<this> = {}) {
    Obj.updatedAt ??= Date.now()
    const id = this.id
    if (id == null) {
      this.upsert()
    } else {
      this.class().dbl.runf(q => q.where({ id }).update(Obj))
    }
  }

  override $beforeUpsert() {
    // overrides should always call super!
    super.$beforeUpsert()
    if (this.createdAt == null) {
      this.createdAt = Date.now()
    }
    this.updatedAt = Date.now()
    if ("updateCount" in this) {
      const obj = this as any
      const prior = obj.updateCount
      obj.updateCount = (toGt0(obj.updateCount) ?? 0) + 1
      this.logger().debug("$beforeUpsert(): incr updateCount", {
        prior,
        now: obj.updateCount
      })
    }
  }
}
