import { TagSep } from "../../core/tags/TagPaths"
import { toS } from "../../fe/toS"

export function tag_fts_root(tag_path: string) {
  const s = toS(tag_path)
  const idx = s.indexOf(TagSep)
  return idx === -1 ? "" : tag_path.substring(0, idx)
}

export function tag_fts_path(tag_path: string) {
  return toS(tag_path)
    .split(TagSep)
    .filter(ea => ea != null && ea.trim() !== "")
    .join(" ")
}
