import p from "process"
import { tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { isSupportedNode, requiredNodeRange } from "../NodeVersion"
import { ver } from "../version/Ver"
import { HealthCheck } from "./HealthCheck"

export const nodejsHealthCheck = defer(() =>
  HealthCheck.for({
    section: "Tools",
    id: "nodejs-version",
    pendingMsg: "Checking Node.js",
    settings: [],
    // no ttlMs: this won't change at runtime
    links: [
      // this should only apply for PhotoStructure for Node users:
      {
        text: "Read about PhotoStructure NodeJS support",
        icon: "docs",
        url: "https://photostructure.com/server/photostructure-for-node/#prerequisites"
      }
    ],
    later: async () => {
      const msg =
        "You're running Node.js " +
        ver(p.versions.node) +
        ".\nPhotoStructure requires " +
        tt(requiredNodeRange()) +
        "."
      return isSupportedNode()
        ? {
            level: "ok",
            msg: ["Node.js is OK", msg]
          }
        : {
            level: "warn",
            msg: ["Node.js is not a supported version", msg]
          }
    }
  })
)
