import { lazy } from "../../core/Lazy"
import { b, li, tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { HealthCheckLevels } from "../../fe/HealthChecks"
import { mkLogger } from "../Logger"
import { isProd } from "../NodeEnv"
import { env } from "../env/Env"
import { ee } from "../event/EventEmitter"
import { libraryHasSettings } from "../settings/SettingsIO"
import { exiftoolHealthCheck } from "./ExiftoolHealthCheck"
import { externalDirsHealthCheck } from "./ExternalDirsHealthCheck"
import { HealthCheck } from "./HealthCheck"
import { heifHealthCheck } from "./HeifHealthCheck"
import { libraryDirectoriesCheck } from "./LibraryDirectoriesCheck"
import { libraryFreeSpaceCheck } from "./LibraryFreeSpaceCheck"
import { memoryHealthCheck } from "./MemoryHealthCheck"
import { nodejsHealthCheck } from "./NodeHealthCheck"
import { notInDMGHealthCheck } from "./NotInDMGHealthCheck"
import { osHealthCheck } from "./OSHealthCheck"
import { powershellHealthCheck } from "./PowerShellHealthCheck"
import { sqliteHealthCheck } from "./SQLiteVersionHealthCheck"
import { securityHealthCheck } from "./SecurityHealthCheck"
import { settingsEnvHealthCheck } from "./SettingsEnvHealthCheck"
import { settingsLibraryHealthCheck } from "./SettingsLibraryHealthCheck"
import { settingsSystemHealthCheck } from "./SettingsSystemHealthCheck"
import { systemLoadHealthCheck } from "./SystemLoadHealthCheck"
import { versionHealthCheck } from "./VersionHealthCheck"
import { videoHealthCheck } from "./VideoHealthCheck"
import { volumeHealthCheck } from "./VolumeHealthCheck"

const logger = defer(() => mkLogger("health.HealthCheckSetup"))

export const testHealthCheck = defer(() => {
  return HealthCheck.for({
    section: "System",
    id: "test-health-check",
    ordinal: 0,
    settings: [],
    pendingMsg: "Test check",
    later: async () => {
      const level = HealthCheckLevels.validOrElse(
        env().PS_TEST_HEALTH_CHECK_LEVEL,
        HealthCheckLevels.ok
      )
      return {
        level,
        msg: [
          `Test health check: ${level.toUpperCase()}`,
          `These are ${b("details")}:`,
          li("alpha", tt("bravo"), "charlie")
        ]
      }
    }
  })
})

export const healthCheckSetup = lazy(() => {
  exiftoolHealthCheck()
  heifHealthCheck()
  libraryDirectoriesCheck()
  libraryFreeSpaceCheck()
  settingsEnvHealthCheck()
  settingsSystemHealthCheck()
  settingsLibraryHealthCheck()
  externalDirsHealthCheck()
  memoryHealthCheck()
  nodejsHealthCheck()
  notInDMGHealthCheck()
  osHealthCheck()
  powershellHealthCheck()
  securityHealthCheck()
  sqliteHealthCheck()
  systemLoadHealthCheck()
  videoHealthCheck()
  volumeHealthCheck()
  versionHealthCheck()

  if (!isProd) {
    testHealthCheck()
  }

  let lastLibraryHasSettings = libraryHasSettings()

  libraryHasSettings.watchLater(newValue => {
    if (newValue != null && lastLibraryHasSettings !== newValue) {
      logger().info("libraryHasSettings changed. Resetting health checks.", {
        newValue
      })
      lastLibraryHasSettings = newValue
      HealthCheck.resetDebounced()
    }
  })
  for (const ea of ["volumesChanged", "mountpointsChanged", "error"] as const) {
    ee().on(ea, () => {
      logger().debug("Clearing health check cache", { event: ea })
      HealthCheck.resetDebounced()
    })
  }

  ee().on("fatal", err => HealthCheck.addError(err))
})
