import {
  diceCoeff,
  greatestBy,
  retainFirstN,
  retainLastN
} from "../../core/Array"
import { mkLogger } from "../../core/Logger"
import { Settings } from "../../core/settings/Settings"
import { ApiAssetStream, AssetId } from "../../fe/api/Asset"
import { pushUniqBy } from "../../fe/Array"
import { sortByInPlace } from "../../fe/ArraySort"
import { gtOrElse, times } from "../../fe/Number"
import { cmp } from "../../fe/Primitive"
import { thenCollect } from "../../fe/Promise"
import { toA } from "../../fe/toA"
import { Asset } from "./Asset"
import { Tag } from "./Tag"

export const cmpAssetAsc = (a: Asset, b: Asset) =>
  cmp([a.capturedAtLocal, a.id], [b.capturedAtLocal, b.id])

export const cmpAssetDesc = (a: Asset, b: Asset) =>
  cmp([b.capturedAtLocal, b.id], [a.capturedAtLocal, a.id])

export class TaggedAssetStream {
  readonly logger = () =>
    mkLogger("model.TaggedAssetStream(" + this.toString() + ")")

  constructor(
    readonly tags: Tag[],
    readonly before: AssetId[],
    readonly after: AssetId[],
    readonly limit: number
  ) {
    this.logger().info("new", this.toJSON())
  }

  vacuum() {
    sortByInPlace(this.before, a => [-a.capturedAtLocal, -a.assetId])
    retainFirstN(this.before, this.limit)
    sortByInPlace(this.after, a => [-a.capturedAtLocal, -a.assetId])
    retainLastN(this.after, this.limit)
  }

  toString() {
    return this.tags.map(ea => ea.path.join("/")).join(",")
  }

  valueOf() {
    return this.toString()
  }

  get length() {
    return toA(this.before).length + toA(this.after).length
  }

  leftPad(ids: AssetId[]): AssetId[] {
    return [
      ...times(this.limit - ids.length, ea => ({
        assetId: -(ea + 1),
        capturedAtLocal: -1,
        v: 0
      })),
      ...ids
    ]
  }

  rightPad(ids: AssetId[]): AssetId[] {
    return [
      ...ids,
      ...times(this.limit - ids.length, ea => ({
        assetId: -(ea + this.limit + 1),
        capturedAtLocal: -1,
        v: 0
      }))
    ]
  }

  toJSON() {
    return {
      tags: this.tags.map(tag => tag.toString()),
      assetIdsAfter: this.after,
      assetIdsBefore: this.before
    }
  }

  mergeWith(tas: TaggedAssetStream) {
    // this.logger().info("mergeWith()", {
    //   tas: tas.toJSON(),
    //   beforeIds: this.before.map(briefAsset),
    //   afterIds: this.after.map(briefAsset)
    // })

    pushUniqBy(this.tags, tas.tags, ea => ea.path)

    // These are the thumbnails to the right of the current asset:
    pushUniqBy(this.before, tas.before, ea => ea.assetId!)

    // These are the thumbnails to the left of the current asset:
    pushUniqBy(this.after, tas.after, ea => ea.assetId!)
    this.logger().debug("mergeWith() complete", {
      tas: tas.toJSON(),
      tags: this.tags.map(ea => ea.path),
      beforeIds: this.before,
      afterIds: this.after
    })
  }

  async toApi(): Promise<ApiAssetStream> {
    this.vacuum()
    return this.logger().tap({
      msg: "toApi()",
      result: {
        tags: await thenCollect(this.tags, ea => ea.toApiTag()),
        assetIdsAfter: this.leftPad(this.after),
        assetIdsBefore: this.rightPad(this.before)
      }
    })
  }

  streamCoeff(tas: TaggedAssetStream) {
    return this.logger().tap({
      msg: "streamCoeff",
      meta: {
        this: this.tags.map(ea => ea.path),
        tas: tas.tags.map(ea => ea.path)
      },
      result: diceCoeff(
        [...this.before, ...this.after],
        [...tas.before, ...tas.after],
        ea => ea.assetId!
      )
    })
  }
}

export function coalesceStreams(
  streams: TaggedAssetStream[]
): TaggedAssetStream[] {
  const minStreamCorr = Settings.minStreamCorrPct.valueOrDefault / 100
  const result: TaggedAssetStream[] = []
  const prefilter = streams.filter(ea => ea.length > 5)
  for (const stream of prefilter) {
    const nearest = greatestBy(result, ea =>
      gtOrElse(ea.streamCoeff(stream), minStreamCorr)
    )
    if (nearest == null) {
      result.push(stream)
    } else {
      nearest.mergeWith(stream)
    }
  }
  return result
}
