import { Emoji } from "./Emoji"
import { IconName } from "./IconName"
import { Maybe } from "./MaybeTypes"
import { PrimitiveValued } from "./Primitive"
import { RunStateEnum } from "./RunStateEnum"
import { ServiceName, ServiceNames } from "./ServiceNames"
import { StrEnumKeys, strEnum } from "./StrEnum"

export const HealthCheckSections = strEnum(
  "Summary",
  "Library",
  // "Storage",
  "System",
  "Tools"
)
export type HealthCheckSection = StrEnumKeys<typeof HealthCheckSections>

const HealthCheckEmoji: Record<HealthCheckSection, Emoji> = {
  Summary: "bookmark-tabs",
  Library: "library",
  // Storage: "dvd",
  System: "computer",
  Tools: "wrench"
} as const

export const HealthCheckLevels = strEnum(
  "error",
  "no-library",
  "pending",
  "stop-sync",
  "warn",
  "ok",
  "disabled"
)
export type HealthCheckLevel = StrEnumKeys<typeof HealthCheckLevels>

const OkLevels = [
  HealthCheckLevels.ok,
  HealthCheckLevels.disabled,
  HealthCheckLevels.pending
] as HealthCheckLevel[]

export function levelIsNotOK(level: HealthCheckLevel) {
  return !OkLevels.includes(level)
}

export function isHealthCheckLevelFatal(
  level: HealthCheckLevel,
  serviceName: ServiceName
) {
  if (
    ([ServiceNames.web, ServiceNames.main] as ServiceName[]).includes(
      serviceName
    )
  ) {
    // main and web should never be in a fatal state:
    return false
  }
  // Only keep main and web running if HealthChecks are in error state:
  if (level === HealthCheckLevels.error) return true
  if (
    serviceName === ServiceNames.sync &&
    level === HealthCheckLevels["no-library"]
  ) {
    // sync requires a library to do work:
    return true
  }
  return false
}

export const HealthCheckLevelToEmoji: Record<HealthCheckLevel, Emoji> = {
  pending: "busy-dots",
  "no-library": "new-yellow",
  ok: "green-check",
  warn: "warning",
  "stop-sync": "pause",
  error: "error",
  disabled: "sleeping-face-desat"
} as const

export function healthCheckLevelToEmoji(level: HealthCheckLevel): Emoji {
  return HealthCheckLevelToEmoji[level]
}

export const HealthCheckLevelDescription = {
  pending: "Health checks are running...",
  "no-library": "No library is open",
  ok: "OK",
  warn: "Warning",
  "stop-sync": "Some health checks are preventing sync from running",
  error: "Some health checks failed: sync will not run",
  disabled: "Some health checks are disabled" // < shouldn't ever show
} as const

export interface Link {
  text: string
  method?: "POST" | "GET"
  type?: "button" | "link"
  title?: string
  url: string
  icon?: IconName
}

export interface HealthCheckResult {
  section: HealthCheckSection
  id: string
  level: HealthCheckLevel
  msg: string[]
  /**
   * When this result was completed
   */
  ts: number
  /**
   * How long it took to complete this result
   */
  elapsedMs?: number
  meta?: PrimitiveValued
  links?: Maybe<Link[]>
  buttons?: Maybe<Link[]>
}

export interface HealthChecksForSection {
  section: HealthCheckSection
  emoji: string
  results: HealthCheckResult[]
}

export interface HealthCheckSummary extends HealthCheckResult {
  settled: boolean
  settledCount: number
  pendingCount: number
  pendingIds: string[]
  state: RunStateEnum
  /**
   * If we're not OK, what's the first HealthCheck.id that caused this level?
   */
  linkIds?: string[]
}

export const BaseHealthCheckSummary = {
  id: "summary",
  section: HealthCheckSections.Summary
} as const

export function newHealthCheckSections(): HealthChecksForSection[] {
  return HealthCheckSections.values.map(section => ({
    section,
    emoji: HealthCheckEmoji[section],
    results:
      section === "Summary"
        ? [
            {
              ...BaseHealthCheckSummary,
              level: "pending",
              settled: false,
              ts: Date.now(),
              msg: [" Requesting health check results..."]
            }
          ]
        : []
  }))
}
