import { uniqBy } from "../../fe/Array"
import { Dimensions, maybeDimSwap } from "../../fe/Dimensions"
import { Maybe } from "../../fe/MaybeTypes"
import { absdiff, isNumber, sigFigs, toGt0 } from "../../fe/Number"
import { lt } from "../../fe/Primitive"
import { flatMap, leastBy, leastByCtx } from "../Array"
import { mapGt0f } from "../Number"

export const Ratios = uniqBy(
  flatMap(
    // Common image ratios (hopefully in order of popularity):
    [
      [3, 2],
      [4, 3],
      [16, 9],
      [1, 1],
      [10, 8],
      [7, 5],
      // panoramas:
      [2, 1],
      [3, 1],
      [4, 1],
      [5, 1]
    ],
    ([w, h]) => [
      { i: w / h, s: w + ":" + h },
      { i: h / w, s: h + ":" + w }
    ]
  ),
  ea => ea.s
)

export const StandardAspectRatios = Ratios.map(ea => ea.i)
export const RatioEpsilon = 0.045

export function roundToNearestRatio(
  ratioOrDimension: number | Dimensions,
  delta: number = RatioEpsilon * 2
): Maybe<number> {
  return mapGt0f(
    isNumber(ratioOrDimension)
      ? ratioOrDimension
      : (toGt0(ratioOrDimension.width) ?? 0) /
          (toGt0(ratioOrDimension.height) ?? 0),
    ratio => {
      const closest = leastBy(StandardAspectRatios, ea => {
        const diff = Math.abs(ratio - ea)
        return diff > delta ? undefined : diff
      })
      return closest ?? ratio
    }
  )
}

export function similarRatios(
  a: number | Dimensions,
  b: number | Dimensions,
  delta: number = RatioEpsilon
): boolean {
  return lt(absdiff(roundToNearestRatio(a), roundToNearestRatio(b)), delta)
}

export function similarRatiosWithRotation(
  a: { dimensions: Dimensions; rotation?: number },
  b: { dimensions: Dimensions; rotation?: number },
  delta: number = 0.05
) {
  return similarRatios(
    maybeDimSwap(a.dimensions, a.rotation),
    maybeDimSwap(b.dimensions, b.rotation),
    delta
  )
}

function nearestAspectRatio(n: number, thresh: number) {
  return leastByCtx(Ratios, ea => {
    const diff = Math.abs(ea.i - n)
    return diff < thresh ? diff : undefined
  })
}

export function roundAspectRatio(n: number, thresh = 0.05) {
  return nearestAspectRatio(n, thresh)?.result.i ?? n
}

export function fmtAspectRatio(n: number, thresh = 0.05): string {
  return nearestAspectRatio(n, thresh)?.result.s ?? String(sigFigs(n, 3))
}
