import { Maybe } from "../../fe/MaybeTypes"
import { actualPath_ } from "./ActualPath"
import { SimpleFileOrString, toNativePath_ } from "./SimpleFile"

export function nativePathsEqlSync(
  a: Maybe<SimpleFileOrString>,
  b: Maybe<SimpleFileOrString>
): boolean {
  if (a == null || b == null) return false
  if (a === b) return true
  try {
    // toNativePath does a resolve() for us, we don't need to!
    const ap = toNativePath_(a)
    const bp = toNativePath_(b)
    return ap != null && bp != null && ap === bp
  } catch {
    return false
  }
}

export async function nativePathsEql(
  a: Maybe<SimpleFileOrString>,
  b: Maybe<SimpleFileOrString>
): Promise<boolean> {
  try {
    // toNativePath does a resolve() for us, we don't need to!
    return (
      a != null &&
      b != null &&
      (await actualPath_(toNativePath_(a))) ===
        (await actualPath_(toNativePath_(b)))
    )
  } catch {
    return false
  }
}
