const logger = defer(() => mkLogger("VersionCheck"))

export function currentChannel() {
  const least = [Settings.updateChannel.valueOrDefault, channel()]
    .map(ea => UpdateChannels.ordinal(ea))
    .sort()[0]
  // we want the least-stable value:
  return UpdateChannels.values[least] ?? Settings.updateChannel.valueOrDefault
}

export type CheckVersionResult = HealthCheckTest & {
  latestVersion?: string
  latestChannel?: UpdateChannel
}

export async function checkVersion_(args?: {
  installedVersion: string
  latestVersion: string
}): Promise<CheckVersionResult> {
  if (true !== Settings.autoUpdateCheck.value) {
    const msg = ["PhotoStructure version checking is disabled"]
    if (Settings.optOut.valueOrDefault === true) {
      msg.push("Set " + tt(Settings.optOut.toEnvLine(false)) + " to enable.")
    } else if (Settings.autoUpdateCheck.value === false) {
      msg.push(
        "Set " + tt(Settings.autoUpdateCheck.toEnvLine(true)) + " to enable."
      )
    } else {
      msg.push(
        "Finish setup, or set " +
          tt(Settings.autoUpdateCheck.toEnvLine(true)) +
          " to enable."
      )
    }
    return { level: "disabled", msg }
  }

  const dir = configDir()
  if (dir == null || !(await isDirectory(dir))) {
    return {
      level: "warn",
      msg: [
        "PhotoStructure version checking is disabled",
        "No suitable configuration directory exists."
      ]
    }
  }

  const channelVersions: Maybe<ChannelVersionJson> = await getChannelVersions_()
  if (channelVersions == null) {
    return {
      level: "warn",
      msg: ["Failed to fetch PhotoStructure version information"]
    }
  }
  const installedVersion = args?.installedVersion ?? version
  const myChannel = extractUpdateChannel(installedVersion)

  // This shouldn't be necessary--we should be copying versions to less-stable channels
  const channelCandidates = eligibleForChannel(myChannel)
  const versionCandidates = channelVersions.versions
    .filter(ea => channelCandidates.includes(ea.channel))
    .map(ea => ea.version)

  const latestVersion =
    args?.latestVersion ?? semverLatest(...versionCandidates)

  if (latestVersion == null) {
    return {
      level: "warn",
      msg: [
        "PhotoStructure version checking failed",
        "No suitable version was found for " +
          Settings.updateChannel.toEnvLine()
      ]
    }
  }

  const latestChannel = extractUpdateChannel(latestVersion)

  const currentVerMsg =
    "You are running " + EditionName() + " " + ver(installedVersion)

  const latestVerMsg =
    "The latest published " +
    tt(latestChannel) +
    " build is " +
    ver(latestVersion)

  const advice =
    latestChannel === myChannel
      ? undefined
      : "You should consider switching to the " +
        tt(latestChannel) +
        " channel."

  const agoMs = Date.now() - channelVersions.updatedAt
  const lastCheckedMsg =
    "Last checked " +
    (agoMs < minuteMs ? "just now" : fmtDuration(agoMs, 1) + " ago") +
    " (" +
    new Date(channelVersions.updatedAt).toISOString() +
    ")"

  const msgs = compact([currentVerMsg, latestVerMsg, advice, lastCheckedMsg])

  // During a release, it's plausible that users will be running a version
  // that is newer than what is in the channel-versions.json if they update
  // manually because I announced it on discord.

  // **Don't freak out**
  if (semverGte(installedVersion, latestVersion)) {
    return {
      level: "ok",
      msg: ["PhotoStructure is up to date", ...msgs],
      latestVersion,
      latestChannel
    }
  } else {
    return {
      level: "warn",
      msg: ["PhotoStructure is out of date", ...msgs],
      latestVersion,
      latestChannel
    }
  }
}

export async function userAgent() {
  return Settings.allowUserAgent.valueOrDefault
    ? systemUserAgent()
    : SimpleAppName
}

export async function systemUserAgent() {
  return (
    SimpleAppName +
    "/" +
    version +
    " (" +
    osFullName() +
    ") " +
    EditionType().toLowerCase() +
    "/" +
    (await t())
  )
}

interface ChannelVersionJson {
  versions: UpdateChannelVersion[]
  updatedAt: number
  updatedByVersion: string
}

const ChannelVersionsUrl = "https://photostructure.com/channel-versions.json"

// lazy for tests (and timeout)
export const getChannelVersions_: MemoizedDeferred<Maybe<ChannelVersionJson>> =
  lazyAsync({
    timeoutMs: commandTimeoutMs(),
    ttlMs: minuteMs,
    later: async () => {
      const cache = channelVersionsCache()!
      const response = await httpGetJson_({
        url: ChannelVersionsUrl,
        config: {
          headers: {
            "User-Agent": await userAgent()
          }
        },
        cache,
        ttlMs: dayMs,
        preCacheTransform: versions => ({
          versions,
          updatedAt: Date.now(),
          updatedByVersion: version
        })
      })

      const result = response.data as any as ChannelVersionJson

      // Is the response valid?
      if (
        result == null ||
        !Array.isArray(result.versions) ||
        result.versions.length === 0 ||
        result.versions.some(ea => !isUpdateChannelVersion(ea)) ||
        result.updatedByVersion !== version
      ) {
        if (response.fromCache) {
          logger().info(
            "getChannelVersions_(): Invalid or stale cached response. Retrying after wiping cache",
            {
              result,
              cache
            }
          )
          await cache.unlink_()
          return getChannelVersions_.refresh()
        }
        logger().throw("getChannelVersions_(): Invalid response", { result })
      }

      return logger().tap({
        msg: "getChannelVersions_()",
        result
      })
    }
  })
