import { Tags } from "exiftool-vendored"
import { lazy } from "../../core/Lazy"
import { compactBlanks, uniq } from "../../fe/Array"
import { blank, notBlank } from "../../fe/Blank"
import { map, mapOr } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { sigFigs, toFloat } from "../../fe/Number"
import { entries } from "../../fe/Object"
import { toS } from "../../fe/toS"
import { first, greatestBy } from "../Array"
import { mkLogger } from "../Logger"
import { Settings } from "../settings/Settings"
import { make } from "./MakeModel"

export interface LensInfo {
  lensMake: string
  lensModel: string
  lensInfo?: string
}

const logger = lazy(() => mkLogger("tags.LensMakeModel"))

const KnownLensBrands = {
  nikkor: "Nikon",
  "m.zuiko": "Olympus",
  zuiko: "Olympus"
} as const

export type LensTags = Pick<
  Tags,
  | "Make"
  | "Lens"
  | "LensType"
  | "LensType2"
  | "LensType3"
  | "LensID"
  | "LensSpec"
  | "LensInfo"
  | "LensMake"
  | "LensModel"
> & { DNGLensInfo?: string }

const ZeissBatisHackRe = /^zeiss batis (?<f>[\d.]+)\/(?<mm>[\d.]+)(?<etc>.*)/i

export function extractLensMakeModel(t: LensTags): Maybe<LensInfo> {
  const lensMake = make(t.LensMake)
  const Make = make(t.Make)

  const results: LensInfo[] = []

  // "LensID" can be quite descriptive, like "Olympus M.Zuiko Digital ED
  // 12-100mm F4.0 IS Pro", but it's also sometimes "Unknown 7-21mm"

  // "LensModel" is typically shorter than "LensID", but still has some
  // branding, ** and may be different between JPG and RAW images **

  // "LensInfo" is typically perfect for short: "4-6mm f/1.8-2.4" but sometimes is "24-105mm f/?"

  // "Lens" doesn't always include aperture: "28.0 - 135.0 mm"

  // "LensType" is a grab bag of randomness: "E-Mount, T-Mount, Other Lens or no
  // lens", "n/a", "G VR", "Canon EF 50mm f/1.4 USM or Other Lens". I'll only
  // use if it we can extract a length and aperture from the value.

  const lensInfo = first(
    [t.LensInfo, t.DNGLensInfo, t.LensSpec, t.LensModel, t.LensID],
    justLengthAndAperture
  )

  const lensModels = uniq([
    t.LensModel,
    t.LensID,
    t.LensSpec,
    t.LensInfo,
    t.LensType,
    t.LensType2,
    t.LensType3,
    t.Lens
  ])

  logger().debug("extractLensMakeModel", { lensMake, lensInfo, lensModels })

  for (const lensModel of lensModels) {
    {
      // Hack for "ZEISS Batis 2/40 CF", which means "40mm f/2"
      const g = ZeissBatisHackRe.exec(lensModel)?.groups as {
        f: string
        mm: string
        etc: string
      }
      if (g != null) {
        results.push({
          lensMake: "Zeiss",
          lensModel: `Batis ${g.mm}mm F${g.f} ${g.etc.trim()}`.trim(),
          lensInfo: `${g.mm}mm f/${g.f}`
        })
      }
    }

    const hasLengthAndAperture = justLengthAndAperture(lensModel) != null

    if (!hasLengthAndAperture) continue
    if (notBlank(lensMake)) {
      results.push({
        lensMake,
        lensModel,
        lensInfo
      })
    }

    const lcLensModel = lensModel.toLowerCase()

    for (const [k, replacementLensMake] of entries(KnownLensBrands)) {
      if (lcLensModel.includes(k)) {
        results.push({
          lensMake: replacementLensMake,
          lensModel,
          lensInfo
        })
      }
    }

    for (const mfg of compactBlanks([lensMake, ...Settings.lensMakes.values])) {
      // Some camera bodies don't encode a LensMake, and include the make in the
      // model name. If a lens includes a make, use that for lensMake, and strip of lensMake from the lensModel:
      if (lensModel.toLowerCase().includes(mfg.toLowerCase())) {
        results.push({
          lensMake: mfg,
          lensModel,
          lensInfo
        })
      }
    }

    // Is it a Nikon "VR" lens?
    if (lcLensModel.startsWith("vr ") && Make === "Nikon") {
      results.push({
        lensMake: "Nikon",
        lensModel,
        lensInfo
      })
    }

    // mrm 20211229: I don't think this is safe:
    // if (lcLensModel.startsWith("unknown") && notBlank(t.Make)) {
    //   // Assume the lens is built-in
    //   makes.push("unknown")
    //   return {
    //     lensMake: t.Make,
    //     lensModel: cleanUpModel(lensModel),
    //     lensInfo
    //   }
    // }
  }

  for (const ea of results) {
    const replacement = (KnownLensBrands as any)[ea.lensMake.toLowerCase()]
    ea.lensMake = replacement ?? ea.lensMake
    ;(ea.lensModel = cleanBogusPrecision(
      ea.lensModel.replace(new RegExp("\\b" + ea.lensMake + "\\b", "i"), "")
    )),
      (ea.lensInfo = map(ea.lensInfo, cleanBogusPrecision))
  }

  return greatestBy(results, ea => [
    // Avoid, if we can, model names like "RF 50mm F1.2L USM or other Canon RF
    // Lens" or "EF 17-40mm f/4L USM or Sigma Lens"
    null == ea.lensModel.match(/\bor\b/i),
    ea.lensModel.length
  ])
}

function nonZeroish(s: Maybe<string>): s is string {
  return s != null && s.match(/^0+$/) == null
}

const mmRangeRE = /([\d. -]+)\s?mm\b/i
const apertureRE = /\bf\/?([\d.\-]+)/i

export function justLengthAndAperture(s: Maybe<string>): Maybe<string> {
  if (blank(s) || toS(s).toLowerCase().includes("unknown")) return
  // normalizeLensModel(
  //   s.replace(/\b(AF-S|CF|DG|Digital|DI|DX|E|ED|HSM|LM|OIS|Pro|R|XF)\b/gi, "")
  // )

  // Handle "22mm" and "EF 12 - 123.3 mm" and remove any spaces:
  s = s.replace(/\s+/g, " ").trim()
  const mm = mmRangeRE.exec(s)?.[1].replace(/\s/g, "")
  const aperture = apertureRE.exec(s)?.[1]
  return nonZeroish(mm) && nonZeroish(aperture)
    ? cleanBogusPrecision(`${mm}mm f/${aperture}`)
    : undefined
}

export function cleanBogusPrecision(s: string, significant = 3): string {
  return s
    .replace(/\d+\.\d+/g, m =>
      mapOr(toFloat(m), ea => String(sigFigs(ea, significant)), m)
    )
    .replace(/\s+/g, " ")
    .trim()
}

export function normalizeLensModel(model: string): string {
  return cleanBogusPrecision(model)
    .replace(/(\d) - (\d)/g, (_, m1, m2) => `${m1}-${m2}`)
    .replace(/(\d)\s+mm\b/, (_, m1) => m1 + "mm")
    .replace(/\s+f\/?(\d)/i, (_, m1) => " f/" + m1)
}
