import { lazy } from "../../core/Lazy"
import { compact, uniq } from "../../fe/Array"
import { sortBy } from "../../fe/ArraySort"
import { blank } from "../../fe/Blank"
import { minuteMs, weekMs } from "../../fe/Date"
import { Dimensions } from "../../fe/Dimensions"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { groupBy } from "../../fe/MultiMap"
import { gt0, map2Numeric } from "../../fe/Number"
import { toReqValued } from "../../fe/Object"
import { CmpReverseFlag, Comparable, Primitive } from "../../fe/Primitive"
import { strEnum } from "../../fe/StrEnum"
import {
  PS_LIBRARY_SCHEME,
  PS_LOCAL_FILE_SCHEME,
  PS_NETWORK_FILESYSTEM_SCHEME
} from "../../fe/URI"
import { mkLogger } from "../Logger"
import { includesIgnoreCase } from "../String"
import { IAssetFile } from "../asset-file/IAssetFile"
import { copySuffixCountFromName } from "../fs/FuzzyNames"
import { parentBasename, parsePosixPath } from "../fs/Path"
import { AssetFileSortCriterion } from "../settings/AssetFileSortCriteria"
import { Settings } from "../settings/Settings"
import { isSupportedByCurrentBrowserExt } from "../tags/FileTypes"
import { uriExists } from "../uri/FileURI"
import { URI } from "../uri/URI"

const logger = lazy(() => mkLogger("img.AssetFileSorter"))

// goodness ascending:
const UriSchemes = strEnum(
  "file",
  PS_NETWORK_FILESYSTEM_SCHEME,
  PS_LOCAL_FILE_SCHEME,
  PS_LIBRARY_SCHEME
)

export type SortableAssetFile = Pick<
  IAssetFile,
  | "uri"
  | "mountpoint"
  | "nativePath"
  | "mtime"
  | "fileSize"
  | "mimetype"
  | "sha"
  | "width"
  | "height"
  | "capturedAtPrecisionMs"
  | "make"
  | "model"
  | "rating"
  | "cameraId"
  | "imageId"
  | "lensId"
  | "geohash"
>

/**
 * Rough comparable number such that substantively larger resolution files are
 * considered "better"
 */
export function dim2sort(d: Partial<Dimensions>): Maybe<number> {
  return map2Numeric(d?.width, d?.height, (w, h) => sortScale(w * h))
}

export function sortScale(i: Maybe<number>): Maybe<number> {
  if (!gt0(i)) return
  // 1/6 is the strongest fractional exponent such that ImageSize values are
  // still distinct.
  return Math.round(
    Math.pow(i, Settings.variantSortCriteriaPower.valueOrDefault)
  )
}

export function mtime2sort(ms: number): number {
  // ignore smaller than 5 minute differences
  return Math.round(ms / (5 * minuteMs))
}

const MetadataFields: (keyof SortableAssetFile)[] = [
  "make",
  "model",
  "rating",
  "cameraId",
  "imageId",
  "lensId",
  "geohash"
]

/**
 * A pleasing melange of heuristics to pick the "best" file from a set of
 * files to show to the user.
 *
 * Sorted by best to worst (or "goodness descending").
 *
 * Files that are missing, less than a KB, or have no URI will be removed from
 * the returned array.
 */
export function sortAssetFiles<T extends SortableAssetFile>(files: T[]): T[] {
  // For each uniq SHA, pretend everyone has the youngest (biggest!) mtime.
  // This lets scheme win over mtime for the same SHA.
  for (const sameSha of groupBy(files, ea => ea.sha).values()) {
    if (sameSha.length > 1) {
      const maxMtime = Math.max(...compact(sameSha.map(ea => ea.mtime)))
      for (const ea of sameSha) {
        ea.mtime = maxMtime
      }
    }
  }
  const goodnessAscending = sortBy(files, assetFileSortCriteria)
  return goodnessAscending.reverse()
}

export type AssetFileSortPojo = Record<AssetFileSortCriterion, Comparable>

// the Obj is easier to log:
export function assetFileSortCriteriaPojo(
  af: SortableAssetFile
): Maybe<AssetFileSortPojo> {
  const parsedURI = URI.parse(af.uri)

  if (blank(parsedURI?.scheme) || blank(parsedURI?.path)) {
    logger().warn("assetFileSortCriteriaPojo(): skipping (invalid URI)", {
      uri_scheme: parsedURI?.scheme,
      uri_path: parsedURI?.path,
      af,
      parsedURI
    })
    return
  }

  const schemeIdx = UriSchemes.indexOf(parsedURI.scheme)

  if (schemeIdx == null) {
    logger().warn(
      "assetFileSortCriteriaPojo(): skipping (invalid URI scheme)",
      {
        uri_scheme: parsedURI.scheme,
        af,
        parsedURI
      }
    )
    return
  }

  const mtimeMs = af.mtime ?? 0
  const parsedFile = parsePosixPath(parsedURI.path)

  // 5-minute-resolution, plus the scheme index to resolve close ties

  const metadataFields = []
  for (const ea of MetadataFields) {
    if (!blank(af[ea])) {
      metadataFields.push(ea)
    }
  }
  // assume later counts may be newer edits, and pick those:

  // Note that we don't fetch tags here, as that is done for only the winning
  // file by AssetPreviewBuilder.

  // bigger values win:
  const fields = {
    resolution: dim2sort(af),
    schemeIdx,

    capturedAtPrecision: -(af.capturedAtPrecisionMs ?? weekMs), // < smaller is better, so negate
    metadataCoverage: metadataFields.length, // < bigger is better
    metadataFields: metadataFields.join(","),

    // booleans sort [false, true], and we want supported files to win:
    isBrowserSupported: isSupportedByCurrentBrowserExt(parsedFile.ext),

    mtime: mtime2sort(mtimeMs), // we want more recent to be better
    fileSize: sortScale(af.fileSize), // bigger is better?

    isCover: includesIgnoreCase(["cover"], parsedFile.base),

    count: Math.abs(copySuffixCountFromName(parsedFile.name) ?? 0),

    // uno reverse card these strings so A > B
    basename: CmpReverseFlag + parsedFile.base,
    parentBasename:
      CmpReverseFlag + parentBasename(af.nativePath ?? parsedURI.path),
    uri: CmpReverseFlag + af.uri
  }

  return logger().tap({
    msg: "assetFileSortCriteriaPojo()",
    result: toReqValued(fields)
  })
}

export function assetFileSortFields(): AssetFileSortCriterion[] {
  return uniq<AssetFileSortCriterion>([
    ...Settings.variantSortCriteria.values,
    // Always include URI for determinism:
    "uri"
  ] as any)
}

/**
 * @return undefined if any assetFileSortCriteriaPojo fields are undefined
 */
export function assetFileSortCriteria(
  af: SortableAssetFile
): Maybe<Primitive[]> {
  const obj = assetFileSortCriteriaPojo(af)
  if (obj == null) return
  const result = []
  for (const field of assetFileSortFields()) {
    result.push((obj as any)[field])
  }
  return result
}

export async function bestExistingAssetFile<T extends SortableAssetFile>(
  files: T[]
): PromiseMaybe<T> {
  for (const ea of sortAssetFiles(files)) {
    if (await uriExists(ea.uri, ea.nativePath)) {
      return logger().tap({
        msg: "bestExistingAssetFile()",
        result: ea,
        meta: { files: files.map(af => af.uri) }
      })
    }
  }
  return logger().tap({
    msg: "bestExistingAssetFile(): no existing uris",
    result: undefined,
    meta: { files: files.map(ea => ea.uri) }
  })
}
