import { nativePath2uri } from "../uri/FileURI"
import { lazyFsAsync } from "./LazyFsAsync"
import { volumes } from "./Volumes"

export const currentVolumeRoots = lazyFsAsync(
  "currentVolumeRoots",
  _currentVolumeRoots
)

async function _currentVolumeRoots(): Promise<{
  uris: string[]
  mountpoints: string[]
}> {
  const uris: string[] = []
  const mountpoints: string[] = []
  for (const vol of await volumes()) {
    mountpoints.push(vol.mountpoint)
    const uri = (await nativePath2uri(vol.mountpoint, vol))?.toString()
    if (uri != null) uris.push(uri)
  }
  return { uris, mountpoints }
}
