export interface AssetTagsParams {
  assetId: number
  tagPaths: TagPath[]
}

export type RemovableColumn = "deletedAt" | "excludedAt"

const logger = lazy(() => mkLogger("model.Asset"))

const Unhidden = {
  "Asset.shown": 1,
  "Asset.hidden": 0,
  "Asset.excludedAt": null,
  "Asset.deletedAt": null
} as const

export class Asset extends TimestampedModel {
  static override readonly $tableName = "Asset"
  static override readonly $uniqueColumnName = "id"
  static override readonly $booleanFields = ["shown", "hidden"]

  capturedAtLocal!: number
  // shown is set by sync, and is false during import or if all files are
  // missing. It should be renamed to something like "ready":
  shown?: boolean
  rating?: number | null // [-1, 5]
  // hidden is set by the user
  hidden?: boolean
  excludedAt?: number | null
  deletedAt?: number | null

  /** de-normalized from the shown assetfile */
  durationMs?: number

  /** This is the value of the AssetVersion constant at time of last sync. */
  version?: number

  /** This monotonically increases every time the primary asset file updates. */
  updateCount?: number

  // TODO: ADD so we don't have to hit json files at serving time:
  // fitWidths: number[]
  // sqWidths: number[]

  assetFiles?: AssetFile[]
  tags?: Tag[]

  // For UI use (transient/not persisted):
  streams?: TaggedAssetStream[]
  attrs = {}
  afterId?: AssetId
  beforeId?: AssetId

  readonly assetIdish = lazy(() => ({
    assetId: this.id!,
    v: this.updateCount,
    capturedAtLocal: this.capturedAtLocal
  }))
  imgAttrs?: Map<ReducerName, StringValued>
  /**
   * The "poster" image for a video assets
   */
  poster?: string

  static shownUnhidden(qb?: Knex.QueryBuilder): Knex.QueryBuilder {
    return qb == null ? Asset.query().where(Unhidden) : qb.andWhere(Unhidden)
  }

  static findUnhiddenById(id: Maybe<number>) {
    return gt0(id)
      ? Asset.ops().firstf(qb => Asset.shownUnhidden(qb.where({ id })))
      : undefined
  }

  static shownRemovable(
    col: RemovableColumn,
    qb?: Knex.QueryBuilder
  ): Knex.QueryBuilder {
    return (qb ?? Asset.query())
      .whereNotNull(col)
      .andWhere({ "Asset.shown": 1 })
  }

  // CAREFUL: if this is cached, be sure to invalidate regularly or else the
  // "library is empty" message will show to first-time users.
  static shownCount() {
    return Asset.ops().count(Asset.shownUnhidden().countDistinct("Asset.id"))
  }

  static #outdatedQuery() {
    return Asset.shownUnhidden().andWhere("version", "<", AssetVersion)
  }

  static deletePreviews(assetId: number) {
    return Library.instance()!.previews().ap(assetId).deleteAll()
  }

  static async remove({
    assetId,
    blocklistShas,
    unlinkAssetFiles
  }: {
    assetId: number
    blocklistShas: boolean
    unlinkAssetFiles: boolean
  }) {
    const l = mkLogger("Asset(" + assetId + ").remove()")
    l.info("starting", { blocklistShas, unlinkAssetFiles })
    const afs = AssetFile.dbl.allf(q =>
      q.select("sha", "uri", "mountpoint").where({ assetId })
    ) as Pick<AssetFile, "sha" | "uri" | "mountpoint">[]
    if (blocklistShas) {
      upsertToShaBlockslist(...afs.map(ea => ea.sha))
    }
    AssetTag.dbl.runf(q => q.delete().where({ assetId }))
    AssetFile.dbl.runf(q => q.delete().where({ assetId }))
    AssetRevision.dbl.runf(q => q.delete().where({ assetId }))
    Asset.dbl.runf(q => q.delete().where({ id: assetId }))
    try {
      await Asset.deletePreviews(assetId)
    } catch (error) {
      l.error("Failed to delete previews", {
        error
      })
    }
    const deletedMeta = unlinkAssetFiles
      ? await trashOrUnlinkFileUris_(afs)
      : []

    return { deletedMeta, assetFileMeta: afs }
  }

  static async exclude(assetId: number) {
    logger().info("Excluding asset " + assetId)
    return {
      dbResult: Asset.dbl.runf(q =>
        q.update({ excludedAt: Date.now() }).where({ id: assetId })
      ),
      deletedPreviews: await Asset.deletePreviews(assetId)
    }
  }

  static nextOutdated(
    q: (qb: Knex.QueryBuilder) => Knex.QueryBuilder = identity
  ) {
    return this.ops().findOne(q(this.#outdatedQuery()))
  }

  static nextOutdateds(q: (qb: Knex.QueryBuilder) => Knex.QueryBuilder) {
    return this.ops().all(q(this.#outdatedQuery()))
  }

  static outdatedCount(
    q: (qb: Knex.QueryBuilder) => Knex.QueryBuilder = identity
  ) {
    return this.dbl.pluckFirst<number>(q(this.#outdatedQuery()).count())
  }

  static findFirstByFile(f: (qb: Knex.QueryBuilder) => Knex.QueryBuilder) {
    return this.ops().findOne(
      f(
        this.query()
          .select("Asset.*")
          .join("AssetFile", "AssetFile.assetId", "Asset.id")
      )
    )
  }

  static addTags(assetId: number, tagPaths: TagPath[]) {
    const uniqTagPaths = uniqBy(tagPaths.filter(isNotEmpty), t =>
      joinTagPath(t)
    )
    logger().debug("addTags()", { assetId, tagPaths, uniqTagPaths })
    if (isEmpty(uniqTagPaths)) return
    const tags = uniqTagPaths.map(tp => Tag.findOrCreate(tp))
    const tagIds = tags.map(ea => ea.id!)
    return AssetTag.addTagsToAsset(assetId, tagIds)
  }

  static removeTags(assetId: number, tagPaths: TagPath[]) {
    if (isEmpty(tagPaths)) return
    const tags = compact(tagPaths.map(ea => Tag.findByPath(ea)))
    logger().info("removeTags()", {
      assetId,
      tags: tags.map(ea => pick(ea, "id", "path")),
      tagPaths
    })
    return AssetTag.removeTagsFromAsset(assetId, compact(tags.map(ea => ea.id)))
  }

  static unshownAssetIds() {
    return this.dbl.pluckAll<number>(`
        SELECT DISTINCT af1.assetId
        FROM AssetFile af1
          LEFT JOIN (SELECT DISTINCT assetId FROM AssetFile WHERE shown = 1) AS af2 
            ON af1.assetId = af2.assetId
        WHERE af2.assetId IS NULL`)
  }

  static archive(assetId: number) {
    const a = Asset.ops().findById(assetId)
    if (a != null) {
      AssetTag.dbl.runf(q => q.where({ assetId }).delete())
      AssetFile.dbl.runf(q => q.where({ assetId }).delete())
      Asset.dbl.runf(q => q.where({ id: assetId }).delete())
    }
  }

  markUnshownAndUpsert() {
    this.shown = false
    this.version = AssetVersion // don't touch with modelDbUpdater.
    this.upsert()
  }

  markShownAndUpsert_(shownAssetFile?: AssetFile) {
    const priorShownId = this.getShown()?.id
    if (shownAssetFile != null) {
      const previouslyShown = shownAssetFile.shown
      this.setShown_(shownAssetFile, { skipUpsert: true })
      if (previouslyShown !== true) shownAssetFile.upsert()
    }
    this.shown = true
    this.version = AssetVersion
    if (priorShownId !== this.getShown()?.id) {
      this.updateCount = (this.updateCount ?? 0) + 1
    }
    this.upsert()
  }

  readonly mergedTags = lazy(async () => {
    // TODO: add other fields (like title, description, ...)
    const tags: ExifTagsWithSource[] = []
    for (const af of this.getAssetFiles()) {
      const pf = await af.posixFile()
      const t = await readTags(pf)
      const mtime = await pf?.mtimeMs()
      if (pf != null && t != null && mtime != null) {
        tags.push({
          ...t,
          SourceFile: pf.nativePath,
          sourceModifiedTime: mtime,
          sourceIsPrimary: isTrue(af.shown)
        })
      }
    }
    const revisions = AssetRevision.ops().allf(q =>
      q.where({ assetId: this.id })
    )
    // TODO: THIS NEEDS MORE TESTS!
    const mt = new MergedTags("Asset:" + this.id)
    mt.addTag(...tags, ...this.assetFiles!)
    mt.addRevision(...revisions)
    return mt
  })

  async updateFromFiles() {
    const mt = await this.mergedTags()
    map(mt.get("rating"), rating => {
      this.logger().info("updateFromFiles()", { rating })
      this.rating = rating.value
    })
    // TODO: add title, description, keywords, ...
  }

  toAssetId(): AssetId {
    return toAssetId(this)!
  }

  // get fitWidths() {
  //   return mapNotBlank(this._fitWidths, ea => JSON.parse(ea) as number[])
  // }

  // set fitWidths(arr: number[]) {
  //   this._fitWidths = stringify(arr)
  // }

  renderCaption(locale?: string) {
    return map(
      this.capturedAtLocal,
      ea => "Taken " + fmtLocalDateShort(ea, locale)
    )
  }

  // async whenApiTag(): PromiseMaybe<ApiTag> {
  //   const ca = this.capturedAt
  //   const path = await dateTag(ca)
  //   const tag = path == null ? undefined : await Tag.findOrCreate(path)
  //   return tag == null ? undefined : tag.toApiTag()
  // }

  async whenApiTag(): PromiseMaybe<ApiTag> {
    const t = dateTag(localToDateObject(this.capturedAtLocal))
    return t == null ? undefined : Tag.findOrCreate(t).toApiTag()
  }

  addTagPaths(tagPaths: TagPath[]) {
    return Asset.addTags(this.id!, tagPaths)
  }

  findAssetFileByUri(uri: string): Maybe<AssetFile> {
    return this.getAssetFiles()?.find(ea => ea.uri === uri)
  }

  addAssetFile(af: AssetFile): void {
    // no dupe entries:
    filterInPlace(
      af.asset?.assetFiles ?? [],
      ea => !(ea.id === af.id || ea.uri === af.uri)
    )
    af.assetId = this.id
    af.asset = this
    af.shown = false
    this.getAssetFiles().push(af)
    this.clear() // mergedTags will need to be reset
    // Only set if we haven't set it yet:
    this.capturedAtLocal ??= af.capturedAtLocal
    this.rating ??= af.rating
    this.durationMs ??= af.durationMs
  }

  /**
   * @return prior AssetFile instance, or a new AssetFile that needs to be
   * updated and upserted
   */
  async assetFileForFile_(
    f: PosixFile,
    opts?: { skipUpsert?: boolean }
  ): Promise<AssetFile> {
    const uri = await f.uri()
    if (uri == null)
      return this.logger().throw("assetFileForFile_(): null uri", { f })
    const prior = this.getAssetFiles().find(ea => ea.uri === uri)
    if (prior != null) return prior
    const af = new AssetFile()
    this.addAssetFile(af)
    if (opts?.skipUpsert !== true) {
      await af.upsertIfNeeded_(f)
    } else {
      await af.setFile_(f)
    }
    return af
  }

  static getTags(assetId: number) {
    return Tag.ops().all(
      Tag.query()
        .select("Tag.*")
        .join("AssetTag", "AssetTag.tagId", "Tag.id")
        .where("AssetTag.assetId", assetId)
        .orderByRaw("COALESCE(Tag.ordinal, Tag._path)")
    )
  }

  getTags() {
    return (this.tags ??= Asset.getTags(this.id!))
  }

  /**
   * Tags will be strings concatenated by `Tag.sep`
   */
  static getTagPaths(assetId: number) {
    const q = Tag.query()
      .select("Tag._path")
      .join("AssetTag", "AssetTag.tagId", "Tag.id")
      .where("AssetTag.assetId", assetId)
      .orderByRaw("COALESCE(Tag.ordinal, Tag._path)")
    return Tag.dbl.pluckAll(q) as MaybeSyncOrAsync<string[]>
  }

  tagPaths() {
    const tags = this.getTags()
    return tags.map(t => t.path.join("/")).sort()
  }

  getStreams(limit: number): TaggedAssetStream[] {
    if (this.streams == null) {
      // const tags = (await this.getTags()).filter(ea => !isFsTag(ea.path))
      const tags = this.getTags()
      this.logger().info("getStreams(): fetched tags " + tags, { limit })
      const streams = tags.map(tag => tag.getAssetStream(this, limit))
      this.streams = coalesceStreams(compact(streams))
      // Backfill the ancestors:
      for (const stream of this.streams) {
        for (const tag of stream.tags) {
          tag.getAncestors()
        }
      }
    }
    return this.streams
  }

  getBeforeAfterId() {
    // Do we have multiple assets with the same capturedAt?
    const sameIds = Asset.dbl.all(
      Asset.shownUnhidden()
        .clearSelect()
        .select({ assetId: "id", v: "updateCount" })
        .andWhere("capturedAtLocal", this.capturedAtLocal)
        .orderBy("assetId")
    ) as any as AssetId[]
    this.afterId = sameIds.find(ea => ea.assetId > this.id!)
    // use id as a secondary sort for capturedAt.
    this.beforeId = sameIds.reverse().find(ea => ea.assetId < this.id!)
    if (this.beforeId == null) this.#getBeforeId()
    if (this.afterId == null) this.#getAfterId()
  }

  #getBeforeId() {
    return (this.beforeId = Asset.dbl.first(
      Asset.shownUnhidden()
        .clearSelect()
        .select({ assetId: "id", v: "updateCount" })
        // This needs to be <, not <=. See getBeforeAfterId.
        .andWhere("capturedAtLocal", "<", this.capturedAtLocal)
        .orderBy([
          { column: "capturedAtLocal", order: "desc" },
          // we have to add this if the prev is a batch of same-time assets:
          { column: "assetId", order: "desc" }
        ])
    ) as any)
  }

  #getAfterId() {
    return (this.afterId = Asset.dbl.first(
      Asset.shownUnhidden()
        .clearSelect()
        .select({ assetId: "id", v: "updateCount" })
        // This needs to be >, not >=. See getBeforeAfterId.
        .andWhere("capturedAtLocal", ">", this.capturedAtLocal)
        .orderBy([
          { column: "capturedAtLocal", order: "asc" },
          // we have to add this if the prev is a batch of same-time assets:
          { column: "assetId", order: "asc" }
        ])
    ) as any)
  }

  getTagPaths() {
    const tags = this.getTags()
    return tags.map(tag => tag.path)
  }

  getAssetFiles({ refresh = false }: { refresh?: boolean } = {}) {
    // If we have files, then return them (even if we're not saved!)
    if (this.id != null) {
      if (refresh) {
        this.assetFiles = undefined
      }
      this.assetFiles ??= sortBy(
        AssetFile.ops().findBy({ assetId: this.id }),
        ea => [!isTrue(ea.shown), -ea.mtime]
      )
    }
    return this.assetFiles ?? []
  }

  setShown_(af: number | AssetFile, opts?: { skipUpsert?: boolean }): void {
    if (af instanceof AssetFile) {
      // replace our copy with `af`.
      this.getAssetFiles()
      this.assetFiles?.some((ea, i) => {
        const result = ea.id === af.id || ea.uri === af.uri
        if (result) this.assetFiles![i] = af
        return result
      })
    }
    const afId = af instanceof AssetFile ? af.id : af
    if (this.getShown()?.id === afId) {
      this.logger().debug("setShown_(): no-op, already set", { afId })
      return
    }
    this.logger().info("setShown_()", { assetId: this.id, assetFileId: afId })

    if (!gt0(this.id) || !gt0(afId)) {
      this.logger().throw(
        "setShown_(): invalid IDs: " +
          stringify({ assetId: this.id, assetFileId: afId }) +
          InternalErrorFlag
      )
    }

    // This is in lieu of

    // UPDATE AssetFile SET shown = CASE id WHEN $assetFileId THEN 1 ELSE 0
    // END, updatedAt = $updatedAt, WHERE assetId = $assetId

    // but lets stay with knex here, it only adds one more db roundtrip.

    const updatedAt = Date.now()

    // Avoid fighting the unique constraint:
    const r1 = AssetFile.dbl.runf(q =>
      q.update({ shown: 0, updatedAt }).where({ assetId: this.id })
    )
    if (r1.changes <= 0) {
      this.logger().warn("setShown_(): all asset files were already unshown", {
        afId
      })
    }
    const r2 = AssetFile.dbl.runf(q =>
      q.update({ shown: 1, updatedAt }).where({ id: afId })
    )
    if (r2.changes !== 1) {
      this.logger().throw(
        "setShown_(): failed to update asset file: unexpected changed rows",
        { runResult: r2 }
      )
    }
    const shownAF =
      this.assetFiles?.find(ea => ea.id === afId) ??
      this.getAssetFiles({ refresh: true }).find(ea => ea.id === afId)
    if (shownAF == null) {
      this.logger().throw("setShown_(): failed to find shown asset file", {
        af
      })
      return
    }

    // Override the prior field values with the show value:
    this.capturedAtLocal = shownAF.capturedAtLocal
    this.rating = shownAF.rating
    this.durationMs = shownAF.durationMs

    // Update the in-memory models:
    for (const ea of this.assetFiles ?? []) {
      ea.shown = ea.id === afId
    }
    if (opts?.skipUpsert !== true) {
      this.upsert()
    }
    return
  }

  getShown(opts?: { refresh?: boolean }): Maybe<AssetFile> {
    return this.getAssetFiles(opts)?.find(af => isTrue(af.shown))
  }

  getShownNativePath(): MaybePromiseMaybe<string> {
    return this.getShown()?.getNativePath()
  }

  async getCapturedAts(): Promise<CapturedAt[]> {
    return thenCollect(this.getAssetFiles(), ea => ea.capturedAt())
  }

  link(): string {
    return "/asset/" + this.id
  }

  sqAttrs(lazyLoad: boolean = true): any {
    return {
      ...assetSqImgAttrs({ assetId: this.assetIdish(), lazyLoad }),
      title: this.renderCaption(),
      ...this.attrs
    }
  }

  async getImgAttrs(previews: Previews, reducer: ReducerName, skipFs = false) {
    if (this.imgAttrs == null)
      this.imgAttrs = new Map<ReducerName, StringValued>()
    if (this.imgAttrs.get(reducer) == null) {
      const ap = previews.ap(this.id!)
      await thenMap(ap.readInfo(), async info => {
        if (info.mimetype.startsWith("video/")) {
          this.poster = await ap.posterLink()
        }
      })
      const stat = true
      this.imgAttrs.set(reducer, await ap.imgAttrs(reducer, stat, skipFs))
    }
    return this.imgAttrs.get(reducer)
  }

  async fitAttrs(previews: Previews): Promise<StrBoolValued> {
    return {
      ...(await this.getImgAttrs(previews, ReducerNames.fit)),
      // title: this.renderCaption(),
      ...this.attrs
    }
  }

  isVideo(): boolean {
    if (this.assetFiles == null) {
      throw new Error(".video called before getFiles()")
    }
    return this.assetFiles.some(ea => ea.isVideo)
  }

  videoAttrs(): StrBoolValued {
    return {
      controls: true,
      autoplay: true,
      poster: this.poster,
      ...this.attrs
    }
  }

  async videoSources(): Promise<{ src: string; type: string }[]> {
    // We need at least one video to be an mp4.
    // 1. prefer the shown asset file if it's an mp4
    // 2. if any siblings are an mp4, use that
    // 3. otherwise, videolink the shown or first existing asset file with an "mp4="

    // Only take the first existing file for a given mimetype.
    const result: { src: string; type: string }[] = []
    let firstExistingAF: AssetFile | undefined
    for (const af of this.getAssetFiles()) {
      if (result.some(ea => ea.type === af.mimetype)) continue
      if (await af.exists()) {
        firstExistingAF ??= af
        result.push({
          src: assetVideoLink({
            assetId: this.assetIdish(),
            assetFileId: af.id!
          }),
          type: af.mimetype
        })
      }
    }

    if (
      !result.some(ea => ea.type === "video/mp4") &&
      firstExistingAF != null
    ) {
      result.unshift({
        src:
          assetVideoLink({
            assetId: this.assetIdish(),
            assetFileId: firstExistingAF.id!
          }) + ".mp4",
        type: "video/mp4"
      })
    }
    return result
  }

  async getPosixFiles(): PromiseMaybe<PosixFile[]> {
    return thenCollect(this.getAssetFiles(), ea => ea.posixFile_())
  }

  async getExistingAssetFiles() {
    return filterAsync({
      name: "getExistingAssetFiles",
      arr: this.getAssetFiles(),
      f: af => af.exists()
    })
  }

  async findOrCreateByFile(f: PosixFile) {
    const uri = await f.uri_()
    if (uri == null) return
    const prior = toA(AssetFile.ops().findBy({ assetId: this.id!, uri }))[0]
    return orElse<SyncOrAsync<AssetFile>>(prior, async () => {
      const af = new AssetFile()
      af.asset = this
      af.assetId = this.id
      await af.setFile_(f)
      return af
    })
  }

  partialSimpleAsset() {
    return {
      assetId: this.id!,
      liked: gte(this.rating, Settings.likeRating.valueOrDefault),
      hidden: isTrue(this.hidden),
      excluded: this.excludedAt != null,
      deleted: this.deletedAt != null
    }
  }

  clear() {
    // this _does not_ delete .assetFiles, as that may be un-upserted
    // information.
    this.imgAttrs = undefined
    this.streams = undefined
    this.tags = undefined
    this.mergedTags.unset()
    return this
  }
}
