import { b, tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { groupid, isRootUser, userid, username } from "../UserInfo"
import { getDevEnvFlag } from "../env/DevEnvFlags"
import { env } from "../env/Env"
import { DevEnvFlags } from "../env/EnvKeys"
import { isWin } from "../platform/Platform"
import { HealthCheck } from "./HealthCheck"

export const securityHealthCheck = defer(() => {
  const desc = isWin ? "an administrator" : "root"

  return HealthCheck.for({
    section: "System",
    id: "proc-not-superuser",
    pendingMsg: "Checking user permission level",
    settings: [],
    rejectIsWarn: true,
    // no need to ever expire!
    later: async () => {
      if (env().PUID === "0") {
        return {
          level: "disabled",
          msg: `Admin health check is disabled (${tt("PUID=0")}).`
        }
      }
      const msg = [
        "Current user id: " + b(tt(userid())),
        "Current group id: " + b(tt(groupid())),
        "Current user name: " + b(tt((await username()) ?? "(unknown)"))
      ]

      if (
        true === (await isRootUser()) ||
        getDevEnvFlag(DevEnvFlags.PS_FAIL_SECURITY_HEALTH_CHECK)
      ) {
        return {
          level: "warn",
          msg: ["PhotoStructure should not be run as " + desc, ...msg],
          links: [
            {
              text: "Read why PhotoStructure should not be run as " + desc,
              url: "https://photostructure.com/server/photostructure-for-docker/#why-not-run-as-root"
            }
          ]
        }
      } else {
        return {
          msg: ["PhotoStructure is not running as " + desc, ...msg],
          level: "ok"
        }
      }
    }
  })
})
