import { minuteMs } from "../../fe/Date"
import { defer } from "../../fe/Defer"
import { errorToS } from "../error/Error"
import { isWin } from "../platform/Platform"
import { checkPowerShell_ } from "../pwsh/PowerShell"
import { ver } from "../version/Ver"
import { HealthCheck } from "./HealthCheck"

export const powershellHealthCheck = defer(() =>
  isWin
    ? HealthCheck.for({
        section: "Tools",
        id: "tools-powershell",
        pendingMsg: "Checking PowerShell",
        settings: ["powerShellArgs"],
        ttlMs: minuteMs,
        links: [
          {
            text: "How to fix PowerShell issues",
            url: "https://photostructure.com/fix-pwsh/"
          }
        ],

        later: async () => {
          try {
            const version = await checkPowerShell_()
            return {
              level: "ok",
              msg: ["PowerShell is OK", ver(version)],
              meta: {
                version
              }
            }
          } catch (error) {
            return {
              level: "error",
              msg: "PowerShell failed: " + errorToS(error as any)
            }
          }
        }
      })
    : undefined
)
