import { ExifDateTime, ResourceEvent } from "exiftool-vendored"
import { lazy } from "../../core/Lazy"
import { pushUniq, remove } from "../../fe/Array"
import { sortBy } from "../../fe/ArraySort"
import { blank, mapNotBlank, notBlank } from "../../fe/Blank"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { isNumber } from "../../fe/Number"
import { isEmptyObj, pick } from "../../fe/Object"
import { parseJSON } from "../../fe/ParseMaybe"
import { toA } from "../../fe/toA"
import { toS } from "../../fe/toS"
import { AppName, AppNameVersion } from "../AppName"
import { Logger, mkLogger } from "../Logger"
import { stringifySorted } from "../StringifySorted"
import { IAssetFile } from "../asset-file/IAssetFile"
import { datedToMillis } from "../date/DatedToMillis"
import { parseDated } from "../date/FuzzyDateParser"
import { ExifTags } from "./ExifTags"

const logger = lazy(() => mkLogger("tags.MergedTags"))

export type RevisionValue = number | string | string[]

export interface BaseRevision {
  createdAt: number
  field: string
}

export interface PrimitiveRevision extends BaseRevision {
  priorValue?: Maybe<RevisionValue>
  newValue: Maybe<RevisionValue>
}

/**
 * Handles revisions to `Set` fields, like `Keywords`
 */
export interface SetAddRevision extends BaseRevision {
  op: "add"
  newValue: RevisionValue
}

export interface SetDeleteRevision extends BaseRevision {
  op: "delete"
  priorValue: RevisionValue
}

export function isBaseRevision(o: any): o is BaseRevision {
  return isNumber(o?.createdAt) && notBlank(o?.field)
}

export function isPrimitiveRevision(o: any): o is PrimitiveRevision {
  return o?.op == null && isBaseRevision(o)
}

export function isSetRevision(o: any): o is SetAddRevision | SetDeleteRevision {
  return ["add", "delete"].includes(o?.op) && isBaseRevision(o)
}

export type Revision = PrimitiveRevision | SetAddRevision | SetDeleteRevision

export function resourceEventToRevision(
  re: Maybe<ResourceEvent>
): Maybe<Revision> {
  if (re == null || !toS(re.SoftwareAgent).startsWith(AppName())) {
    return logger().tap({
      msg: "resourceEventToRevision() invalid",
      result: undefined,
      meta: { resourceEvent: re }
    })
  }
  const field = re.Changed

  if (blank(field)) {
    return logger().tap({
      msg: "resourceEventToRevision() missing Changed field name",
      result: undefined,
      meta: { resourceEvent: re }
    })
  }

  const fields = pick(
    mapNotBlank(re.Parameters, parseJSON) ?? ({} as any),
    "newValue",
    "priorValue"
  )

  if (isEmptyObj(fields)) {
    return logger().tap({
      msg: "resourceEventToRevision() missing Parameter fields",
      result: undefined,
      meta: { resourceEvent: re }
    })
  }

  const createdAtTs = datedToMillis(parseDated({ input: re.When }))

  if (createdAtTs == null) {
    return logger().tap({
      msg: "resourceEventToRevision() missing createdAt (When)",
      result: undefined,
      meta: { resourceEvent: re }
    })
  }

  if (blank(re.Action)) {
    return logger().tap({
      msg: "resourceEventToRevision() missing op (Action)",
      result: undefined,
      meta: { resourceEvent: re }
    })
  }

  // TODO: Do we need to verify newValue and priorValue are set properly for
  // add, delete, and assign?

  return logger().tap({
    msg: "resourceEventToRevision()",
    result: {
      createdAt: createdAtTs,
      field,
      op: re.Action === "assign" ? undefined : re.Action,
      ...fields
    },
    meta: { resourceEvent: re }
  })
}

export function revisionToResourceEvent(revision: Revision): ResourceEvent {
  return {
    Action: (revision as any).op ?? "assign",
    Changed: revision.field,
    InstanceID: map((revision as any).id, toS),
    Parameters: stringifySorted(
      pick(revision as any, "newValue", "priorValue")
    ),
    SoftwareAgent: AppNameVersion(),
    When: ExifDateTime.fromMillis(revision.createdAt ?? Date.now())
  }
}

export interface ExifTagsWithSource extends Partial<ExifTags> {
  sourceModifiedTime: number
  sourceIsPrimary: boolean // is this tag from the "primary" asset file variation?
  SourceFile: string
}

function isPrimary(ea: ExifTagsWithSource | IAssetFile): boolean {
  return (ea as any)?.sourceIsPrimary ?? (ea as any)?.shown ?? false
}

function mtime(ea: ExifTagsWithSource | IAssetFile): number {
  return (ea as any)?.sourceModifiedTime ?? (ea as any)?.mtime ?? 0
}

function sourceFile(ea: Maybe<ExifTagsWithSource | IAssetFile>): Maybe<string> {
  return (ea as any)?.SourceFile ?? (ea as any)?.uri
}

export class MergedTags {
  private readonly logger: Logger
  constructor(readonly name: string) {
    this.logger = mkLogger(`MergedTags(${name})`)
  }
  private readonly revisions: Revision[] = []
  private readonly tags: (ExifTagsWithSource | IAssetFile)[] = []

  addRevision(...r: Revision[]) {
    this.revisions.push(...r)
  }

  addTag(...t: (ExifTagsWithSource | IAssetFile)[]) {
    this.tags.push(...t)
    // sort so first one found wins (so first look at all "primary" sources, and then look at the newest sources first)
    sortBy(this.tags, ea => [!isPrimary(ea), -mtime(ea)])
  }

  get(field: string): Maybe<{ value: any; source: string[] }> {
    // only look at relevant Revisions:
    const revs = sortBy(
      this.revisions.filter(ea => ea.field === field),
      ea => ea.createdAt
    )
    const t = this.tags.find(ea => (ea as any)[field] != null)
    let value = (t as any)?.[field] // SITS TSC
    const source: string[] = []
    if (value != null) mapNotBlank(sourceFile(t), ea => source.push(ea))

    // Are we an array?
    const isPrimitive =
      revs.every(isPrimitiveRevision) &&
      this.tags.every(ea => !Array.isArray((ea as any)[field]))

    if (isPrimitive) {
      for (const revision of revs as PrimitiveRevision[]) {
        if (
          value != null &&
          value !== revision.priorValue &&
          value !== revision.newValue
        ) {
          this.logger.info(
            "get(" +
              field +
              "): skipping revision because expected prior or new value didn't match current value",
            { revision }
          )
        } else {
          value = revision.newValue as any
          source.push(`Revision@${revision.createdAt}`)
        }
      }
      return value == null ? undefined : { value, source }
    } else {
      // This is an array field
      // Start with the newest value from tags:
      const arr: (number | string | boolean)[] = toA(value as any)

      // Ensure all revisions are applied:
      for (const revision of revs as (SetAddRevision | SetDeleteRevision)[]) {
        if (revision.op === "add" && revision.newValue != null) {
          source.push(`Revision@${revision.createdAt}`)
          pushUniq(arr, ...toA(revision.newValue as any))
        } else if (revision.op === "delete" && revision.priorValue != null) {
          source.push(`Revision@${revision.createdAt}`)
          for (const val of toA(revision.priorValue as any)) {
            remove(arr, val)
          }
        }
      }

      return {
        value: arr as any, // SITS TSC
        source
      }
    }
  }
}
