import { lazy } from "../../core/Lazy"
import { later } from "../../fe/Delay"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { safeRandomUid } from "../Random"
import { version } from "../Version"
import { configDir } from "../dir/ConfigDir"
import { ee } from "../event/EventEmitter"
import { TokenRadix } from "../math/Radix"
import { Settings } from "../settings/Settings"
import { CreatedAt, JsonFileStore } from "./JsonFileStore"
import { PosixFile } from "./PosixFile"
import { SimpleFileOrString } from "./SimpleFile"

export interface UidFile extends CreatedAt {
  uid: string
  version: string
  type: "library" | "system"
}

export function mkuid() {
  return safeRandomUid({ radix: TokenRadix, chars: 24, splitEveryN: 4 })
}

export class UIDStore extends JsonFileStore<UidFile> {
  constructor(
    readonly rootDir: PosixFile,
    readonly type: UidFile["type"]
  ) {
    // we hide it, because why not:
    super(
      // system-uid.json and library-uid.json
      rootDir.join("." + type + "-uid.json"),
      () => ({
        uid: mkuid(),
        version,
        type: this.type,
        createdAt: Date.now()
      }),
      f => f.hide()
    )
  }

  /**
   * @return just the UUID, not the UidFile struct
   */
  readonly readUid_ = lazy(async () => (await this.read_())?.uid)
}

// /**
//  * We store this so the system UID stays consistent even if the local hostname
//  * changes
//  */
export const SystemUIDStore = lazy(() =>
  map(configDir(), ea => new UIDStore(PosixFile.for(ea), "system"))
)

export const LibraryUIDStore = lazy(libraryUidStore)

export function libraryUidStore(
  libraryDir: Maybe<SimpleFileOrString> = Settings.libraryDir.valueOrDefault
) {
  return map(PosixFile.forMaybe(libraryDir), ea => new UIDStore(ea, "library"))
}

later(() => {
  ee().on("clearCache", () => {
    SystemUIDStore.unset()
    LibraryUIDStore.unset()
  })
  Settings.libraryDir.watchLater(() => LibraryUIDStore.unset())
})
