import { SyncPredicate } from "../predicates/SyncPredicates"
import { Settings } from "../settings/Settings"
import { ExtType, ExtTypes } from "../tags/ExtType"
import { isExtType } from "../tags/FileTypes"
import { toPathnames } from "./Path"
import { SimpleFile } from "./SimpleFile"

/**
 * case-insensitive basename matching
 */
export function excludedPathFilter<T extends SimpleFile>(
  excludedBasenames: string[]
): SyncPredicate<T> {
  const s = new Set(excludedBasenames.map(ea => ea.toLowerCase()))
  return (file: T) =>
    toPathnames(file.nativePath).every(ea => !s.has(ea.toLowerCase()))
}

/**
 * @param includedExtensions case-insensitive file extensions.
 * Note these must be prefixed with a '.'
 */
export function extFilter<T extends SimpleFile>(
  extType: ExtType
): SyncPredicate<T> {
  return (file: T) =>
    Settings.respectFileExtensions.valueOrDefault
      ? isExtType(file?.ext, extType)
      : null
}

export const isBrowserExtension = extFilter(ExtTypes.SupportedByCurrentBrowser)
export const isVideoExtension = extFilter(ExtTypes.Video)
export const isAssetFileExtension = extFilter(ExtTypes.AssetFile)
