import { lazy } from "../../core/Lazy"
import { compact, isEmpty } from "../../fe/Array"
import {
  assetImgAttrs,
  assetImgLink,
  assetSqImgAttrs
} from "../../fe/AssetUrls"
import { ReducerName, ReducerNames } from "../../fe/ImageReducers"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { StringValued, pick } from "../../fe/Object"
import { leftPad } from "../../fe/Pad"
import { ID, id2id } from "../../fe/api/ID"
import { greatestBy, leastBy } from "../Array"
import { Logger, mkLogger } from "../Logger"
import { splitEvery } from "../String"
import { sortByAsync } from "../async/Promise"
import { InternalErrorFlag } from "../error/ErrorFlags"
import { PosixFile } from "../fs/PosixFile"
import { max } from "../math/Vector"
import { PreviewInfo, extractPreviewInfo } from "./AssetPreviewInfo"

/**
 * Contents of the JSON metadata files stored alongside the previews:
 */
export interface AssetPreviewInfo {
  assetId: number
  assetFileId: number
  uri: string
  uris: string[]
  path: string
  mimetype: string
  width: number
  height: number
  /**
   * If filesize and maxStatMs matches, we can assume this metadata is still correct:
   */
  mtime: number
  filesize: number
  sha: string
  rotation?: number
  fitSizes: string
  sqWidths: number[]
  fitWidths: number[]
}

export class AssetPreviews {
  readonly logger: Logger
  readonly assetId: number
  readonly parent: PosixFile
  readonly basename: string

  constructor(
    readonly previewsRoot: PosixFile,
    id: number | ID
  ) {
    this.logger = mkLogger("AssetPreviews(assetId:" + id2id(id) + ")")
    this.assetId = id2id(id)!
    // To simplify debugging, we use the base-10 (rather, than, say, geohash
    // radix encoded) ID in the path.

    // MacOS finder and Windows Explorer crashes with folders > 1000, so that
    // (ish) should be considered the largest allowable number of items in a
    // given directory. dir_stat reads in chunks of 32k, so smaller than that is
    // better.

    // FAT and FAT32 maximum number of files on disk: 65,517

    // As there will be ~10 files per image, we can have ~100 assets per
    // directory

    // See discussion:
    // https://docs.google.com/document/d/1UzrntF9-gQhvmvKgecMI3Em623hsMP5iKBdkPHR5mC0/edit#heading=h.kyfjnq6c2j4i

    // If we pad to 8 digits and split every 3 (assuming 99 million is an upper
    // bound, which is ridiculously large), we get /000/000/01-fit-w480.jpg

    // We could make this leftPad(5 or 8 or 11) based on the size of assetId,
    // but let's just make this simple and only support assetIds < 100 million.
    const paddedId = leftPad(this.assetId, 8, "0")
    const assetPath = splitEvery(paddedId, 3)
    // NOTE: make sure the "-" stays here, or else removeAll may remove other
    // asset's files!
    this.basename = assetPath.pop() + "-"
    this.parent = previewsRoot.join(...assetPath)
  }

  parentNotExists() {
    return this.parent.clear().notExists()
  }

  async existingFiles() {
    return (
      (await this.parent
        .clear()
        .childFiles(ea => ea.base.startsWith(this.basename))) ?? []
    )
  }

  async existingJpgs() {
    return (await this.existingFiles()).filter(ea => ea.ext === ".jpg")
  }

  async #existingFilesWithInfo() {
    const result = []
    for (const ea of await this.existingFiles()) {
      const info = extractPreviewInfo(this.previewsRoot, ea)
      if (info != null) result.push(info)
    }
    return result
  }

  // Sorted by size ascending
  async previewInfos(): Promise<PreviewInfo[]> {
    return sortByAsync({
      name: "img.AssetPreviews.previews",
      arr: this.#existingFilesWithInfo(),
      f: ea => ea?.file.size()
    })
  }

  async deleteAll() {
    const arr = await this.existingFiles()
    if (arr.length > 20) {
      throw new Error(
        "deleteAll(): rejected: > 20 existing files?!" + InternalErrorFlag
      )
    }
    for (const ea of arr) {
      await ea.unlink()
    }
    return arr
  }

  fileForSuffix(suffix: string): PosixFile {
    return this.parent.join(this.basename + suffix)
  }

  mp4(): PosixFile {
    return this.fileForSuffix("video.mp4")
  }

  infoJson(): PosixFile {
    return this.fileForSuffix("info.json")
  }

  readonly readInfo = lazy<PromiseMaybe<AssetPreviewInfo>>(() =>
    // PERF: don't stat then read--just try to read.
    this.infoJson().readJson<AssetPreviewInfo>("debug")
  )

  writeInfo(info: AssetPreviewInfo): PromiseMaybe<PosixFile> {
    this.readInfo.unset()
    return this.infoJson().writeJson(
      // make sure we only write out expected fields
      pick(
        info,
        "assetId",
        "assetFileId",
        "uri",
        "uris",
        "path",
        "mimetype",
        "width",
        "height",
        "mtime",
        "filesize",
        "sha",
        "rotation",
        "fitSizes",
        "fitWidths"
      )
    )
  }

  fileForWidth(reducer: ReducerName, width: number): PosixFile {
    return this.fileForSuffix(reducer + "-w" + width + ".jpg")
  }

  async filesForReducer(reducer: ReducerName): Promise<PosixFile[]> {
    const prefix = this.basename + reducer + "-"
    return this.logger.tap({
      msg: "filesForReducer(" + reducer + ")",
      result: (await this.existingFiles()).filter(ea =>
        ea.base.startsWith(prefix)
      )
    })
  }

  async smallestFileForReducer(reducer: ReducerName): PromiseMaybe<PosixFile> {
    return leastBy(
      await this.filesForReducer(reducer),
      f => extractPreviewInfo(this.previewsRoot, f)?.width
    )
  }

  async largestFileForReducer(reducer: ReducerName): PromiseMaybe<PosixFile> {
    return greatestBy(
      await this.filesForReducer(reducer),
      f => extractPreviewInfo(this.previewsRoot, f)?.width
    )
  }

  async widths(reducer: ReducerName): PromiseMaybe<number[]> {
    const info = await this.readInfo()
    const field = reducer === ReducerNames.sq ? "sqWidths" : "fitWidths"
    const result = info?.[field]
    if (!isEmpty(result)) return result

    // We have to scan the filesystem to find the widths
    const widths = compact(
      (await this.previewInfos())
        .filter(ea => ea.reducer === reducer)
        .map(ea => ea.width)
    )
    if (info != null) {
      info[field] = widths
      await this.writeInfo(info)
    }
    return widths
  }

  async posterLink(): Promise<string> {
    const widths = await this.widths(ReducerNames.fit)
    return assetImgLink({
      assetId: this.assetId,
      reducer: ReducerNames.fit,
      width: max(widths)
    })
  }

  /**
   * @param stat if true, always look at the filesystem
   */
  async imgAttrs(
    reducer: ReducerName,
    stat = false,
    lazyLoad = true
  ): Promise<StringValued> {
    if (!stat && reducer === ReducerNames.sq) {
      return assetSqImgAttrs({ assetId: this.assetId, lazyLoad })
    } else {
      const info =
        reducer === ReducerNames.fit ? await this.readInfo() : undefined
      return assetImgAttrs({
        assetId: this.assetId,
        reducer,
        widths: await this.widths(reducer),
        lazyLoad,
        af: info
      })
    }
  }
}
