import { Sharp } from "sharp"
import { lazy } from "../../core/Lazy"
import { Dimensions } from "../../fe/Dimensions"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { map2Numeric } from "../../fe/Number"
import { mkLogger } from "../Logger"
import { thenMap } from "../async/Promise"
import { PosixFile } from "../fs/PosixFile"
import { extractSizeInfoFromFile } from "../tags/SizeInfo"
import { isSharp } from "./Sharp"

const logger = lazy(() => mkLogger("img.FileDimensions"))

export async function dimensions(
  file: Maybe<PosixFile | string | Sharp>
): PromiseMaybe<Dimensions> {
  try {
    if (file == null) return
    if (isSharp(file)) {
      const m = await file.metadata()
      return map2Numeric(m.width, m.height, (width, height) => ({
        width,
        height
      }))
    } else {
      return thenMap(extractSizeInfoFromFile(file), ea => ({
        width: ea.ImageWidth,
        height: ea.ImageHeight
      }))
    }
  } catch (error) {
    logger().warn("dimensions() failed", { error })
    return
  }
}
