import { lazy } from "../../core/Lazy"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { stdoutResult_ } from "../child/ChildProcess"
import { pathTo } from "../fs/PathTo"
import { PosixFile } from "../fs/PosixFile"
import { isMac } from "../platform/Platform"
import { Settings } from "../settings/Settings"
import { commandTimeoutMs } from "../volumes/VolumeTtls"
import { withImageCache_ } from "./ImageCache"

// No one knows nor cares about the version: we just hope they're all compatible with eachother, ish.

export const sipsPath = lazy(async () => {
  return isMac ? pathTo({ tool: "sips" }) : undefined
})

// sips -s format png oldpic.jpg --out newpic.png

export async function sips2jpeg_(src: PosixFile): PromiseMaybe<PosixFile> {
  return withImageCache_(src, "heif", ".jpeg", dest =>
    stdoutResult_(
      "sips",
      [
        "-s",
        "format",
        "jpeg",
        "-s",
        "formatOptions",
        String(Settings.previewQuality.valueOrDefault),
        src.nativePath,
        "--out",
        dest.base
      ],
      {
        cwd: dest.dir,
        timeoutMs: commandTimeoutMs()
      }
    )
  )
}
