const logger = lazy(() => mkLogger("tags.TagInference"))

const MaxSiblingsChecked = 7

export async function canInferForDir(d: PosixFile) {
  if (
    Settings.siblingInference.valueOrDefault === TagInferenceSettingValues.never
  ) {
    logger().debug(
      "canInferForDir(" +
        d +
        "): disabled: " +
        Settings.siblingInference.toEnvLine()
    )
    return false
  }

  if (true === inHiddenPhotoStructureDir(d)) {
    logger().debug(
      "canInferForDir(" + d + "): disabled (PhotoStructure directory)"
    )
    return false
  }

  if (
    Settings.siblingInference.valueOrDefault ===
      TagInferenceSettingValues.auto &&
    (await isSlowDir(d.nativePath)) &&
    !(await isCachedDir(d.nativePath))
  ) {
    logger().debug(
      "canInferForDir(" + d + "): disabled (slow uncached directory)"
    )
    return false
  }

  return true
}

/**
 * HEY GUESS WHAT HERE ARE SOME HEURISTICS OO BOY OO BOY WHAT COULD
 * POSSIBLY GO WRONG, AMIRITE?
 *
 * Unfortunately, many smartphones don't encode the make and model into their
 * video headers (like the Google Pixel!). They are stinky, stinky poopy
 * pants, but we can try to clean up their mess. With a different stinky poopy
 * mess.
 *
 * Also unfortunately, `jpegtrans` from days of yore (2000s) didn't copy
 * tags to rotated images--so the image rotation was lossless, but the
 * resulting file lost its tags (like Make, Model, and capture time).
 *
 * If there are other files in the directory, and they have a consistent make
 * and model, assume the video shares the same make and model.
 */
export async function inferMakeAndModel(
  f: PosixFile,
  tags: Maybe<RareTags>
): Promise<{
  Make?: string
  Model?: string
}> {
  const mm = extractMakeAndModel(tags)

  if (mm.Make != null && mm.Model != null) {
    return mm
  }

  if (!(await canInferForDir(f.parent()))) {
    logger().debug(
      "inferMakeAndModel(" + f + "): cannot infer for this directory",
      f.parent()
    )
    return mm
  }

  // If a nearby file in the current directory have the same make and
  // model, or a filename has the same prefix as our file, use that.
  const nearestN = zipSibling(await nearestSiblings(f)).slice(0, 10)

  logger().debug(
    "inferMakeAndModel(" + f + "): looking at nearby siblings",
    nearestN.map(ea => ea.base)
  )

  for (const sibling of nearestN) {
    // DON'T readTags HERE! We'll go into inference infinite loopage!
    const fromSibling = extractMakeAndModel(await readRawTags(sibling))
    // If we have a Make or a Model, don't infer a different one from the sibling:
    if (definedAndNotEql(fromSibling.Make, mm.Make)) {
      logger().debug(
        "inferMakeAndModel(" + f + "): skipping sibling (mismatching Make)",
        { mm, fromSibling }
      )
      continue
    }
    // this shouldn't ever happen:
    if (definedAndNotEql(fromSibling.Model, mm.Model)) {
      logger().debug(
        "inferMakeAndModel(" + f + "): skipping sibling (mismatching Model)",
        { mm, fromSibling }
      )
      continue
    }
    if (fromSibling.Make != null && fromSibling.Model != null) {
      return logger().tap({
        msg: "inferMakeAndModel(" + f + ")",
        level: LogLevels.info,
        result: fromSibling,
        meta: { sibling: sibling.base }
      })
    }
  }
  return mm
}

export async function inferCapturedAtFromSiblings(
  file: PosixFile
): PromiseMaybe<{ date: Dated; src: string }> {
  if (!(await canInferForDir(file.parent()))) {
    return
  }

  return thenMap(
    beforeAfterCapturedAt(file),
    ({ before, after, index, slots }) => {
      if (!closeTo(before.date, after.date, 2 * dayMs)) {
        // This "same day" heuristic means we don't apply this general heuristic
        // when the siblings are really different.
        return
      }
      return map(
        DateInterval.for(
          toExifDateTime(before.date, getZoneName(before.date)),
          toExifDateTime(after.date, getZoneName(after.date)),
          index,
          slots
        ),
        date => ({
          date,
          src: `before:${before.src},after:${after.src}`
        })
      )
    }
  )
}

async function firstWithCapturedAt(
  arr: Maybe<PosixFile[]>
): PromiseMaybe<{ date: Dated; src: string; index: number }> {
  arr = toA(arr)
  for (
    let index = 0;
    index < Math.min(arr.length, MaxSiblingsChecked);
    index++
  ) {
    const sib = arr[index]
    const tags = await readRawTags(sib)
    const ca = capturedAtFromTags(tags)
    if (ca != null) return { ...ca, index }
  }
  return
}

async function firstWithZoneName(
  arr: PosixFile[]
): PromiseMaybe<{ zoneName: string; path: string; base: string; src: string }> {
  arr = toA(arr).slice(0, MaxSiblingsChecked)
  for (const sib of arr) {
    const t = await readRawTags(sib) // < NOT readTags--that would cause an infinite loop!
    logger().debug("firstWithZoneName() readRawTags", { sib, t })
    if (t != null && notBlank(t.tz) && t.tzSource !== defaultVideosToUTC) {
      return logger().tap({
        msg: "firstWithZoneName()",
        result: {
          zoneName: t.tz,
          path: sib.nativePath,
          base: sib.base,
          src: t.tzSource ?? "tags.tz"
        },
        meta: {
          tzSource: t.tzSource
        }
      })
    }
  }
  logger().debug("firstWithZoneName(): no zone name found", { arr })
  return
}

export type BeforeAfterResult = PromiseMaybe<{
  before: { date: Dated; src: string }
  after: { date: Dated; src: string }
  index: number
  slots: number
}>

const bacaCache = lazy(
  () => new FileCache<BeforeAfterResult>({ name: "tags.beforeAfterCapturedAt" })
)

export function beforeAfterCapturedAt(file: PosixFile): BeforeAfterResult {
  return bacaCache().getOrSet(file.nativePath, async () => {
    const sibs = await nearestSiblings(file)
    const before = await firstWithCapturedAt(sibs?.younger)
    const after = await firstWithCapturedAt(sibs?.older)
    return before == null || after == null
      ? undefined
      : {
          before,
          after,
          index: before.index,
          slots: before.index + after.index + 1
        }
  })
}

const isAssetFileExtension: SyncPredicate<SimpleFile> = extFilter(
  ExtTypes.AssetFile
)

type NearestSiblings = { younger: PosixFile[]; older: PosixFile[] }

function zipSibling(s: Maybe<NearestSiblings>): PosixFile[] {
  return s == null ? [] : compact(flatZip(toA(s.younger), toA(s.older)))
}

/**
 * Return `{younger: PosixFile[], older: PosixFile[]}`, ordered by basename
 * similarity to `f`.
 *
 * Both `younger` and `older` will be truncated after `maxLength` entries, to
 * prevent large directories from causing exiftool to visit every entry in large
 * directories.
 */
export async function nearestSiblings(
  f: PosixFile,
  maxLength = 7
): PromiseMaybe<NearestSiblings> {
  if (!(await canInferForDir(f.parent()))) return

  const dirents = await time("tags.nearestSiblings", async () => {
    const parent = await f.parent().directoryEntry()
    const arr = await parent?.childFiles()
    return arr?.filter(
      async ea =>
        // no hidden files:
        ea.isFile() &&
        !ea.isNameHidden() &&
        // any file with EXIF:
        (isAssetFileExtension(ea) ?? (await isAssetFileMimeType(ea)))
    )
  })

  if (dirents == null) {
    logger().info("nearestSiblings(): can't readdir parent, " + f.parent())
    return
  }
  const sorted = sortBy(dirents, ea => bname(ea))
  // WE WANT BASE, NOT NAME, because we want the entry for `f`:
  const myIndex = findFileIndex(f, sorted)
  if (myIndex < 0) {
    logger().warn("nearestSiblings(): can't find self in siblings: " + f)
    return
  }

  const minMatch = clamp(
    0,
    1,
    Settings.siblingInferenceBasenameCoeff.valueOrDefault
  )

  const [youngerDE, olderDE] = [
    sorted.slice(myIndex - maxLength * 2, myIndex),
    sorted.slice(myIndex + 1, myIndex + 1 + maxLength * 2)
  ]

  const younger: PosixFile[] = []
  const older: PosixFile[] = []

  while (isNotEmpty(youngerDE) && younger.length < maxLength) {
    const ea = PosixFile.forDirectoryEntry(youngerDE.pop()!)
    if (diceCoeff(f.name, ea.name) >= minMatch) {
      younger.push(ea)
    }
  }

  while (isNotEmpty(olderDE) && older.length < maxLength) {
    const ea = PosixFile.forDirectoryEntry(olderDE.shift()!)
    if (diceCoeff(f.name, ea.name) >= minMatch) {
      older.push(ea)
    }
  }

  return logger().tap({
    msg: "nearestSiblings()",
    result: {
      younger,
      older
    },
    meta: { f }
  })
}

export async function nearestSiblingTzOffset(
  f: PosixFile
): PromiseMaybe<{ zoneName: string; path: string; base: string; src: string }> {
  if (!(await canInferForDir(f.parent()))) return

  const sibs = zipSibling(await nearestSiblings(f))
  const result = await firstWithZoneName(sibs)
  return logger().tap({
    msg: "nearestSiblingTzOffset()",
    result,
    meta: { f, sibs: sibs.map(ea => ea.base) }
  })
}

/**
 * If the basename encodes a date and time, and it matches any file stat time,
 * use that.
 */
export async function extractStatBname(
  f: PosixFile
): PromiseMaybe<{ src: string; date: Dated }> {
  const input = uniq([bname(f, true), bname(f, false)])
  const fromName = parseDated({ input })

  if (fromName == null) {
    logger().debug("extractStatBname(): bname isn't dated", {
      base: f.base,
      input
    })
    return
  }

  const stat = await f.stat()
  if (stat == null) {
    logger().debug("extractStatBname(): no stat", {
      path: f.nativePath
    })
    return
  }

  for (const src of ["birthtime", "atime", "mtime", "ctime"] as const) {
    if (datedOverlap({ a: fromName, b: stat[src] }).aOverlapsB) {
      return logger().tap({
        msg: "extractStatBname()",
        result: { src, date: fromName },
        meta: { input, stat }
      })
    }
  }
  return logger().tap({
    msg: "extractStatBname(): no stat overlap with bname",
    result: undefined,
    meta: { input, fromName, stat }
  })
}

/**
 * If the basename encodes a date and time, and it matches any file stat time,
 * use that.
 */
export async function extractStatPathTime(
  f: PosixFile
): PromiseMaybe<{ src: string; date: Dated }> {
  const dateFromPath = extractDateFromPath(f.pathsForDateParsing)
  if (dateFromPath == null) return

  // The date encoded in the path may be quite course. If the stat time
  // matches path-encoded date, let's use the stat time as a refinement.

  const stat = await f.stat()
  if (stat == null) return

  // I'll take _any_ stat time (other than access time) that overlaps.
  for (const src of ["birthtime", "mtime", "ctime"] as const) {
    const d = stat[src]
    if (
      d != null &&
      datedOverlap({ a: dateFromPath, b: d })?.aContainsB === true
    ) {
      return logger().tap({
        msg: "extractStatPathTime()",
        result: { src: "path+" + src, date: DateTime.fromJSDate(d) },
        meta: { dateFromPath, stat, src }
      })
    }
  }
  return logger().tap({
    msg: "extractStatPathTime(): no match",
    result: undefined,
    meta: { dateFromPath, stat }
  })
}
