import { Later } from "../../fe/Later"
import { Maybe, MaybePromiseMaybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { MaybeSyncOrAsync } from "../../fe/SyncOrAsync"

export interface LaterMaybe<T> {
  (): PromiseMaybe<T>
}

export interface MaybeLaterMaybe<T> {
  (): MaybePromiseMaybe<T>
}

/**
 * Wrap a Later to get a promise that will be resolved or rejected when the
 * returned thunk is called.
 */
export function laterPromise<T>(later: () => T | Promise<T>): {
  promise: Promise<T>
  later: Later<T>
} {
  // We could use Deferred here, but I want to minimize cross-dependencies.
  let resolve: (t: T | Promise<T>) => void
  let reject: (error: any) => void
  const promise = new Promise<T>((res, rej) => {
    resolve = res
    reject = rej
  })
  return {
    promise,
    later: async () => {
      try {
        const r = await later()
        resolve(r)
        return r
      } catch (err) {
        reject(err)
        throw err
      }
    }
  }
}

export async function firstDefinedLater<T>(
  ...arr: Maybe<() => MaybeSyncOrAsync<T>>[]
): PromiseMaybe<T> {
  if (arr == null) return
  for (const ea of arr) {
    if (ea == null) continue
    const result = await ea()
    if (result != null) return result
  }
  return
}
