import process from "process"
import { blank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"

export interface DaemonOptions {
  daemon?: boolean
  pidfile?: string
  stop?: boolean
}

export function isDaemon(opts?: DaemonOptions) {
  return (
    isTrue(process.env.__is_daemon) ||
    isTrue(opts?.daemon) ||
    !blank(opts?.pidfile)
  )
}
