import { Tags } from "exiftool-vendored"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { normalizeRotation } from "../../fe/Rotation"
import { toS } from "../../fe/toS"
import { isVideoMimeType } from "./MimeTypes"

