// TODO: support https://github.com/recurser/exif-orientation-examples

// See: https://www.daveperrett.com/articles/2012/07/28/exif-orientation-handling-is-a-ghetto/

export function extractRotation(tags: Maybe<Tags>): Maybe<number> {
  return tags == null
    ? undefined
    : orientationToRotation(tags.Orientation) ??
        orientationToRotation(tags.CameraOrientation) ??
        normalizeRotation(tags.Rotation) // < used by videos
}

const ExifOrientationToRotation = new Map<string | number, number>([
  ["Horizontal (normal)", 0],
  ["Rotate 90 CW", 90],
  ["Rotate 180", 180],
  ["Rotate 270 CW", 270],
  [1, 0],
  [6, 90],
  [3, 180],
  [8, 270]
  // TODO: FIXME: Mirror orientations are not currently mirrored properly.
  // ["Mirror horizontal", 0], // 2
  // ["Mirror vertical", 180], // 4
  // ["Mirror horizontal and rotate 270 CW", 270], // 5
  // ["Mirror horizontal and rotate 90 CW", 90], // 7
  // [2, 0],
  // [4, 180],
  // [5, 270],
  // [7, 90]
])

export function orientationRequiresMirroring(
  orientation: Maybe<string> | number
) {
  return (
    [2, 4, 5, 7].includes(orientation as any) ||
    toS(orientation).startsWith("Mirror")
  )
}

/**
 * @return clockwise rotation, or undefined if mirroring is involved or the
 * orientation string is undefined or unknown
 */
export function orientationToRotation(
  orientation: Maybe<string | number>
): Maybe<number> {
  return map(orientation, ea => ExifOrientationToRotation.get(ea))
}

const Rotation2ExifOrientation = new Map<number, number>([
  [0, 1],
  [90, 6],
  [180, 3],
  [270, 8]
  // [0, 2],
  // [180, 4],
  // [270, 5],
  // [90, 7]
])

/**
 * @param rotation clockwise rotation (0, 90, 180, 270)
 * @return if rotated is defined and a known value, return the ExifTool
 * Orientation string
 */
export function rotationToExifOrientation(
  rotation: Maybe<number>
): Maybe<number> {
  return Rotation2ExifOrientation.get(normalizeRotation(rotation) as any)
}

export type ImageDimensions = Required<Pick<Tags, "ImageHeight" | "ImageWidth">>

export function rotationToWriteTag(rotation: number, mimetype: string) {
  return map(normalizeRotation(rotation), nr =>
    isVideoMimeType(mimetype)
      ? { Rotation: nr as any }
      : map(rotationToExifOrientation(nr), ea => ({
          "Orientation#": ea
        }))
  )
}
