const logger = lazy(() => mkLogger("img.SharpReadable"))

// Sharp can cause RSS bloat with high-CPU-count machines. See
// https://github.com/lovell/sharp/issues/955
const setupSharp = lazy(() => {
  _sharp.simd(Settings.enableSIMD.valueOrDefault)
  _sharp.cache(Settings.enableVipsCache.valueOrDefault)
  _sharp.concurrency(sharpThreadsPerProcess())
})

export async function _imgFromExif_(
  src: PosixFile,
  tag: keyof Tags,
  useBuffer: boolean
): PromiseMaybe<string | Buffer> {
  if (useBuffer) {
    return await exiftool().extractBinaryTagToBuffer(tag, src.nativePath)
  } else {
    return (await extractImageForThumbs_(src, tag))?.nativePath
  }
}

/**
 * If the file is rotated (in camera or post), there aren't good assumptions to
 * be made regarding the embedded image, so we can't use it.
 *
 * If orientation of the original image is normal, and EXIF thumb aspect ratio
 * agrees with source image, EXIF thumb is valid. In all other jpeg cases, EXIF
 * thumb needs to be ignored. For raw and video, which are much less likely to
 * be edited, if both raw images are landscape, use the orientation.
 */
export async function imgFromExif_(
  src: PosixFile,
  tags: Maybe<Tags>,
  tag: keyof Tags,
  minDim: Dimensions,
  fileAspectRatio: Maybe<number>
): PromiseMaybe<SharpReadable> {
  const val = tags?.[tag] as Maybe<BinaryField>
  if (!(val instanceof BinaryField)) return

  const useBuffer = val.bytes < Settings.maxEmbeddedBuffer.valueOrDefault

  try {
    const input = await _imgFromExif_(src, tag, useBuffer)
    if (input == null) return

    const s = _sharp(input, {
      failOn: Settings.imageFailOn.valueOrDefault as SharpFailOn
    })
    const m = await s.metadata()

    // This is the aspect ratio without taking EXIF orientation into account:
    const renderedAspectRatio = aspectRatio(m)

    // TODO: when is m.width or m.height not set?

    // If the fileAspectRatio  the aspect ratio incorrect?
    if (
      fileAspectRatio != null &&
      renderedAspectRatio != null &&
      !closeTo(fileAspectRatio, renderedAspectRatio, 0.2)
    ) {
      // (ignore square dimension requests)
      logger().info(
        "imgFromExif(" +
          src +
          ", " +
          tag +
          "): rejecting (aspect ratio mismatch)",
        {
          fileAspectRatio,
          renderedAspectRatio
        }
      )
      return
    }

    const valid = lteBoth(minDim, m)

    logger().debug("imgFromExif(" + src + ", " + tag + ")", {
      valid,
      minDim,
      useBuffer,
      fileAspectRatio,
      renderedAspectRatio,
      dim: pick(m, "width", "height")
    })
    return valid
      ? {
          sharp: s,
          file: isString(input) ? PosixFile.for(input) : undefined
        }
      : undefined
  } catch (error) {
    logger().info("Failed to read image from EXIF tag " + tag, { src, error })
    return
  }
}

export function toSharp(s: SharpReadable): Sharp {
  const a: any = s // TODO SITS TS TYPING
  if (isSharp(a.sharp)) {
    return a.sharp
  } else if (isSimpleFile(a.file)) {
    return _sharp(a.file.nativePath, {
      failOn: Settings.imageFailOn.valueOrDefault as SharpFailOn
    })
  } else {
    throw new Error("toSharp(): not a Sharp object: " + stringify(s))
  }
}

type HasPosixFile = { file: SimpleFile }
type HasSharp = { sharp: _sharp.Sharp }

export type SharpReadable = HasPosixFile | HasSharp | (HasPosixFile & HasSharp)

export type Described = { desc: string; rot?: number; mimetype: string }

export type DescribedSharpReadable = SharpReadable & Described

/**
 * @throws if we can't get `src` into a format that sharp can read.
 */
export function sharpReadable_(args: {
  src: PosixFile
  minDim?: Dimensions
  skipEmbedded?: boolean
}): PromiseMaybe<DescribedSharpReadable> {
  return time(`img.sharpReadable${args.src.ext.toUpperCase()}`, () =>
    _sharpReadable_(args)
  )
}

async function _sharpReadable_({
  src,
  minDim,
  skipEmbedded
}: {
  src: PosixFile
  minDim?: Dimensions
  skipEmbedded?: boolean
}): PromiseMaybe<DescribedSharpReadable> {
  const log = logger().addParameterContext(src.nativePath)
  setupSharp()
  const mt = await readMimeType(src)
  if (blank(mt)) {
    // this shouldn't happen: PosixFileFilters should have rejected this file already!
    return log.throw(src + " is not supported (missing MIMEType)", {
      doNotSend: true
    })
  }

  const sizeInfo = await extractSizeInfoFromFile(src)
  if (sizeInfo == null) {
    log.warn("Failed to extract size info from " + src)
    return
  }

  const _strategies: ({ desc: string } & LaterMaybe<SharpReadable>)[] = []

  function addStrategy(desc: string, f: LaterMaybe<SharpReadable>) {
    ;(f as any).desc = desc
    _strategies.push(f as any)
  }

  const rot = sizeInfo.rotation
  const isVideo = isVideoMimeType(mt)
  const isRaw = isLibrawMimeType(mt)

  // libRaw is _really_ expensive, and doesn't always render correct images
  // (like with Canon .cr3 and newer Sony cameras): if an embedded preview is
  // 90% of the desired dimension, and we don't have rotation, just use that.

  const sid = sizeInfo.dimensions

  const dim: Maybe<Dimensions> =
    minDim ??
    map2Gt0(sid.width, sid.height, (w, h) => ({
      width: Math.round(w * 0.9),
      height: Math.round(h * 0.9)
    }))

  if (dim?.width == null || dim?.height == null) {
    return log.throw(src + " is not supported (missing dimensions)", {
      dim,
      doNotSend: true,
      mimetype: mt,
      isRaw,
      isVideo
    })
  }

  // Don't include metadata thumbnails if:

  // 1. src is a video, because some thumbnails are cutesy and add filmstrip
  //    borders, which we don't really want to try to "trim"

  // 2. the on-file dimensions are portrait--this means the image has been
  //    rotated/edited, and the preview thumbnails are no longer trustworthy

  const fileAspectRatio = aspectRatio(sizeInfo.fileDimensions)
  const isFilePortrait = lt(fileAspectRatio, 1)
  const isSharpMimeAndNoMinDim = isSharpMimeType(mt) && minDim == null
  const isMirrorOrientation = orientationRequiresMirroring(sizeInfo.orientation)

  if (
    skipEmbedded === true ||
    isVideo ||
    isFilePortrait ||
    isSharpMimeAndNoMinDim ||
    isMirrorOrientation
  ) {
    log.debug("not pulling previews from tags", {
      isVideo,
      isFilePortrait,
      isSharpMimeAndNoMinDim,
      isMirrorOrientation
    })
  } else {
    const includeSidecars = false
    const t = await readRawTags(src, includeSidecars)
    if (t == null) {
      log.warn("Cannot pull previews from tags: readRawTags() failed")
    } else {
      const tagsToExtract = [...Settings.embeddedPreviews.values] as string[]
      if (minDim != null && dmegapixels(minDim) < 1) {
        tagsToExtract.unshift(...Settings.embeddedThumbnails.values)
      }
      // We're trying to fetch the nearest thumbnail to deliver at least minDim.
      // Previews compressed more than 95% will look terrible:
      const minBytes = dim.width * dim.height * 0.05
      const sizeSortedTagNames = sortBy(
        tagsToExtract.filter(key => (t as any)[key] instanceof BinaryField),
        key => {
          const bytes = (t as any)[key]?.bytes
          return gt(bytes, minBytes) ? bytes : undefined
        }
      )
      if (!isEmpty(sizeSortedTagNames)) {
        log.debug("checking previews from tags", { sizeSortedTagNames })
        for (const ea of sizeSortedTagNames) {
          addStrategy("imgFromExif." + ea, () =>
            imgFromExif_(src, t, ea as any, dim, fileAspectRatio)
          )
        }
      }
    }
  }

  if (isSharpMimeType(mt)) {
    addStrategy("sharp", async () => ({ file: src }))
  }

  if (isHeifMimeType(mt) && (await isHeifSupported())) {
    addStrategy("heif", () => thenMap(heif2img(src), file => ({ file })))
  }

  if (isRaw) {
    addStrategy("raw2tiff", () => thenMap(raw2tiff_(src), file => ({ file })))
  }

  if (isVideo) {
    addStrategy("extractVideoFrame", async () => {
      if (!(await isVideoSupported())) {
        log.warn("video file, but video support is missing", {
          src: src.nativePath,
          mimetype: mt,
          minDim
        })
        return
      } else {
        try {
          return await thenMap(extractVideoFrame_(src), file => ({ file }))
        } catch (error: any) {
          throw new WrappedError(
            "Failed to extract video frame for " + src,
            error
          )
        }
      }
    })
  }

  const causes: Error[] = []

  for (const s of _strategies) {
    try {
      const result = await time("img.read." + s.desc, s)
      if (result != null) {
        log.debug(s.desc + " strategy succeeded", {
          src,
          minDim,
          rot,
          img: (result as any).file?.nativePath
        })
        return {
          desc: s.desc,
          rot,
          mimetype: mt,
          ...result
        }
      } else {
        log.debug(s.desc + " returned null", {
          src,
          minDim,
          rot
        })
      }
    } catch (error) {
      log.warn(s.desc + " failed", error)
      causes.push(toErr(error)!)
    }
  }

  throw new WrappedError("Failed to render " + src, {
    causes,
    path: String(src),
    fatal: false,
    doNotSend: true,
    retriable: false
  })
}

/**
 * Returns a file holding the binary stream associated to the given tag.
 * Orientation is copied from the src to the result.
 */
export async function extractImageForThumbs_(
  src: PosixFile,
  tag: keyof Tags
): PromiseMaybe<PosixFile> {
  const suffix = tag.toLowerCase().endsWith("tiff") ? ".tiff" : ".jpg"
  try {
    // extracting a binary tag takes millis. No need to re-use between
    // processes, because handling errors and lock contention is something we
    // can just skip here.
    const f = await tmpImageFile_({ src, tag, ext: suffix })
    return await f.applyIfEmpty_({
      retries: 2,
      fn_: dest => extractBinaryTag(tag, src.nativePath, dest.nativePath),
      timeoutMs: commandTimeoutMs()
    })
  } catch (error) {
    // if this returns undefined, applyIfEmpty_ will wait for a timeout.
    logger().throw(
      "Failed to extract embedded " + tag + " from " + src.nativePath,
      { error }
    )
    return
  }
}
