import { readdir } from "fs/promises"
import * as pf from "platform-folders"
import { uniq } from "../../fe/Array"
import { li, tt } from "../../fe/BabyMarkdown"
import { notBlank } from "../../fe/Blank"
import { defer } from "../../fe/Defer"
import { MultiMap } from "../../fe/MultiMap"
import { sortByCaseInsensitive } from "../String"
import { defaultApplePhotosLibrary } from "../dir/DefaultApplePhotosLibrary"
import { homeDir } from "../dir/HomeDir"
import { isDocker } from "../platform/isDocker"
import { HealthCheck } from "./HealthCheck"

export const externalDirsHealthCheck = defer(() =>
  isDocker()
    ? undefined
    : HealthCheck.for({
        section: "System",
        id: "media-directories",
        pendingMsg: "Checking default photo directories",
        settings: [],
        rejectIsWarn: true,
        later: async () => {
          const arr = uniq([
            homeDir(),
            pf.getHomeFolder(),
            pf.getPicturesFolder(),
            pf.getVideosFolder(),
            pf.getDesktopFolder(),
            await defaultApplePhotosLibrary()
          ]).sort()

          const errs = new MultiMap<string, string>()
          const dirs = uniq(arr.filter(notBlank)).sort()
          for (const dir of dirs) {
            try {
              await readdir(dir, { withFileTypes: true })
            } catch (err: any) {
              if (notBlank(err?.code)) {
                errs.add(err.code, dir)
              }
            }
          }
          errs.delete("ENOTDIR") // don't care
          errs.delete("ENOENT") // don't care

          if (errs.valueCount() > 0) {
            return {
              level: "warn",
              msg: [
                "Problem trying to read the following directories:",
                li(
                  sortByCaseInsensitive(errs.tuples(), ([, v]) => v).map(
                    ([k, v]) => `${tt(v)}: ${k}`
                  )
                )
              ]
            }
          } else {
            return {
              level: "ok",
              msg: [
                "Default media directories are readable\nChecked the following directories:",
                li(dirs.map(tt))
              ]
            }
          }
        }
      })
)
