import { clamp, round, toGt0 } from "../../fe/Number"
import { lazy } from "../Lazy"
import { Settings } from "../settings/Settings"
import { maxCpus } from "../work/MaxCpus"

// Don't hammer the system too hard--we're doing work too!
export const ffmpegThreads = lazy(
  () => toGt0(Settings.ffmpegThreads.value) ?? clamp(1, 6, round(maxCpus() / 4))
)
