export const ImageNumberSynonyms = [
  // ImageNumber and ShutterCount are synonyms (and used interchangeably):
  "ImageNumber",
  "ShutterCount",
  "ShutterCount2",
  "ShutterCount3"
] as const

// See https://forum.photostructure.com/t/jpeg-raw-bundles/265/5?u=mrm

export const CameraSerialNumberSynonyms = [
  "CameraSerialNumber",
  "SerialNumber",
  "BodySerialNumber",
  "InternalSerialNumber"
] as const

// export const TagSynonyms = [ImageNumberSynonyms, CameraSerialNumberSynonyms]

const TagSynonymGroups = [ImageNumberSynonyms, CameraSerialNumberSynonyms]

export const Tag2SynonymGroup = lazy(() => {
  const result = new Map<string, string>()
  for (const arr of TagSynonymGroups) {
    const name = arr[0]
    for (const ea of arr) {
      result.set(ea, name)
    }
  }
  return result
})
export const Tag2Synonyms = lazy(() => {
  const result = new Map<string, readonly string[]>()
  for (const arr of TagSynonymGroups) {
    for (const ea of arr) {
      result.set(ea, arr)
    }
  }
  return result
})

export const zeroesRe = /^[\s0]*1?$/

/**
 * Ignore values that are blank or like "000000000000" or "000000000001"
 */
function rubbish(o: any) {
  return (
    o == null ||
    blankish(o) ||
    (isNumber(o) && (o === 0 || o === 1)) ||
    zeroesRe.exec(toS(o)) != null
  )
}

export function stringifyExifUid(
  result: [string, number | string][]
): Maybe<string> {
  const arr = result == null ? [] : result.filter(([, v]) => !blank(v))
  return arr.length === 0 ? undefined : stringify(fromEntries(arr))
}

function normalizeString(s: any): string {
  // some values look bonkers, like "            LENS    123"
  return toS(s).trim().replace(/\s+/g, " ")
}
/**
 * Remove rubbish values and only retain uniq values for tag synonym groups
 *
 * Only exposed for tests
 */
export function renderExifUidId(obj: any) {
  const uniqSynonymValues = new MultiMap<string, string>()
  const result: [string, number | string][] = []
  for (const key of sortIgnoreCase(keys(obj))) {
    if (rubbish(obj[key])) continue
    const v = obj[key]
    const s = normalizeString(obj[key])
    // normalize all values to a trimmed string
    // first value for a synonym group?
    const group = Tag2SynonymGroup().get(key)
    if (group != null) {
      if (
        uniqSynonymValues.includes(group, v) ||
        uniqSynonymValues.includes(group, s)
      ) {
        continue
      } else {
        uniqSynonymValues.add(group, v)
      }
    }
    result.push([key, gt0(v) ? v : s])
  }
  return stringifyExifUid(result)
}

function splitToEntry(keyColonValue: string): Maybe<[string, string]> {
  // v1.1 encoded tuples as `${key}:${value}` strings.
  const arr = splitFirst(keyColonValue, ":")
  return arr.length === 2 ? arr : undefined
}

export function decodeExifUid(
  s: Maybe<string>
): Maybe<Valued<number | string>> {
  if (blank(s)) return
  const obj = parseJSON(s)
  if (obj == null) {
    // single values may be a colon-separated string
    return fromEntries([splitToEntry(s)])
  }
  if (Array.isArray(obj)) {
    // v1.1 encoded multiple values as a JSON-encoded array
    return fromEntries(obj.map(splitToEntry))
  }
  // v2.1 encodes tuples as an object
  return compactBlankValues(obj)
}

export type InequalFields = {
  aKey: string
  aValue: number | string
  bKey: string
  bValue: number | string
}

export function findInequalFields(
  a: Maybe<string>,
  b: Maybe<string>
): Maybe<InequalFields> {
  const aObj = decodeExifUid(a)
  const bObj = decodeExifUid(b)
  if (aObj == null || bObj == null || a === b) return

  // check non-synonym'ed fields:
  const commonSimpleFields = intersection(keys(aObj), keys(bObj)).filter(
    ea => !Tag2SynonymGroup().has(ea)
  )

  // any direct non-matches?
  for (const key of commonSimpleFields) {
    // allow either aVal or bVal to be coerced to string (so disable eqeqeq)
    const aValue = aObj[key]
    const bValue = bObj[key]
    if (normalizeString(aValue) !== normalizeString(bValue)) {
      return { aKey: key, aValue, bKey: key, bValue }
    }
  }

  // For each synonym group, as long as there's one match, or one side has no values, we're fine.
  for (const group of TagSynonymGroups) {
    const aPick = pick(aObj, ...group)
    if (isEmptyObj(aPick)) continue
    const bPick = pick(bObj, ...group)
    if (isEmptyObj(bPick)) continue

    const aValues = values(aPick).map(toS)
    const bValues = values(bPick).map(toS)

    if (includesAny(aValues, bValues)) continue

    // Pick the least-valued key for each for determinism
    const aKey = least(keys(aPick))!
    const bKey = least(keys(bPick))!
    return { aKey, aValue: aObj[aKey], bKey, bValue: bObj[bKey] }
  }

  return
}

function whyDefinedNotApproximate({
  a,
  b,
  field,
  desc,
  parser
}: {
  a: Partial<ExposureSettings>
  b: Partial<ExposureSettings>
  field: keyof ExposureSettings
  desc: string
  parser: (input: any) => Maybe<number>
}): Maybe<string> {
  const i = parser(a[field])
  const j = parser(b[field])
  const minCoeff = Settings.minExposureSettingsCoeffPct.valueOrDefault / 100
  return i == null || j == null || approximates(i, j, minCoeff)
    ? undefined
    : "Different " + desc + ": " + a[field] + "  " + b[field]
}

/**
 * @return a message with a mismatch if both a and b have a defined field but
 * they don't match.
 */
export function whyExposureSettingsNotSimilar(
  a: Maybe<Partial<ExposureSettings>>,
  b: Maybe<Partial<ExposureSettings>>
): Maybe<string> {
  if (a == null || b == null) return
  return firstDefinedThunk([
    () =>
      whyDefinedNotApproximate({
        a,
        b,
        field: "focalLength",
        desc: "focal length",
        parser: extractFloat
      }),
    () =>
      whyDefinedNotApproximate({
        a,
        b,
        field: "aperture",
        desc: "aperture",
        parser: extractFloat
      }),
    () =>
      whyDefinedNotApproximate({
        a,
        b,
        field: "shutterSpeed",
        desc: "shutter speed",
        parser: extractFraction
      }),
    () =>
      whyDefinedNotApproximate({
        a,
        b,
        field: "iso",
        desc: "ISO",
        parser: extractFloat
      })
  ])
}

export function cameraIdFromTags(t: Tags): Maybe<string> {
  return renderExifUidId(pick(t, ...CameraSerialNumberSynonyms))
}

export function lensIdFromTags(t: LensTags & Pick<Tags, "LensSerialNumber">) {
  return lensIdFromLensInfo({
    ...extractLensMakeModel(t),
    ...t
  })
}

/**
 * Prevents re-extracting lens make and model
 */
export function lensIdFromLensInfo(
  info: Partial<LensInfo> & Pick<Tags, "LensSerialNumber">
): Maybe<string> {
  // DON'T USE LensID! It doesn't match between JPG and RAW images!
  const obj: any = pick(info, "LensSerialNumber")
  if (info != null && !blank(info.lensMake) && !blank(info?.lensInfo)) {
    // Lens models are TERRIBLE: Nikon gives TOTALLY different values between
    // JPG and RAW pairs: so if something terse, like lensInfo, is available,
    // use that instead. "mli" means "make+lensInfo". It's our own tag, so we
    // can call it whatever we want.
    obj.mli = `${info.lensMake.toLowerCase()}/${info.lensInfo}`
  }
  return renderExifUidId(obj)
}

export function imageIdFromTags(t: Tags): Maybe<string> {
  return renderExifUidId(
    pick(t, ...ImageNumberSynonyms, "BurstUUID", "RunTimeValue")
  )
}

export function uidGeoHash(
  lat: Maybe<number>,
  lon: Maybe<number>
): Maybe<string> {
  // Note that 52 bits gives ~.5m resolution. Unfortunately, some cameras (like
  // Google Pixels) will add slightly different GPS values to JPG/RAW pairs, so we
  // downsample the geohash here.

  // https://en.wikipedia.org/wiki/Decimal_degrees says 4 decimals get me to ~10m.
  // Good enough.
  return geohash(toFixed(lat, 4), toFixed(lon, 4))
}
