import { tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { later } from "../../fe/Delay"
import { map } from "../../fe/Maybe"
import { ee } from "../event/EventEmitter"
import { getVideoToolDetails_ } from "../img/Video"
import { isDocker } from "../platform/isDocker"
import { ver } from "../version/Ver"
import { HealthCheck } from "./HealthCheck"

export const videoHealthCheck = defer(() =>
  HealthCheck.for({
    section: "Tools",
    id: "tools-video",
    pendingMsg: "Checking video tooling",
    settings: ["ffmpegPath"],
    links: [
      {
        text: "Read how to set up video support with PhotoStructure",
        url: "https://photostructure.com/getting-started/video-support/",
        icon: "docs"
      }
    ],
    warnLinks: isDocker()
      ? [] // can't fix docker!
      : [
          {
            text: "Re-check for video tooling",
            icon: "refresh",
            method: "POST",
            type: "button",
            url: "/admin/recheck-tools"
          }
        ],
    // if the video tooling is not installed, we don't want to block the server from starting:
    rejectIsWarn: true,
    later: async () => {
      const td = await getVideoToolDetails_()
      return td != null && false !== td.isSupportedVersion
        ? {
            level: "ok",
            msg: [
              "Videos will be imported",
              map(td.version, ver),
              map(td.path, tt)
            ]
          }
        : {
            level: "warn",
            msg: ["Videos will not be imported", "No video tooling was found."]
          }
    }
  })
)

later(() => {
  ee().on("clearToolCache", () => videoHealthCheck.prior()?.reset())
})
