import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import {
  isRetriableError,
  isSqliteBusyError
} from "../../core/error/ErrorTypes"
import { Settings } from "../../core/settings/Settings"
import { delay } from "../../fe/Delay"
import { randomInt } from "../../fe/Random"
import { SyncOrAsync } from "../../fe/SyncOrAsync"

const logger = lazy(() => mkLogger("db.DbRetries"))

/**
 * @param f MUST BE IDEMPOTENT
 */
export async function handleDbRetries<T>(
  f: () => SyncOrAsync<T>,
  onRetry?: () => any
): Promise<T> {
  const start = Date.now()
  let retries = 0
  const timeoutAt = start + Settings.maxBusyDbMs.valueOrDefault
  while (Date.now() < timeoutAt) {
    try {
      return await f()
    } catch (error) {
      if (
        isRetriableError(error) === false ||
        !isSqliteBusyError(error) ||
        Date.now() >= timeoutAt
      ) {
        logger().error("Caught db error. Not retrying.", { error })
        throw error
      } else {
        const ms = randomInt(500, 1500) * ++retries
        logger().error("Caught db error. Retrying in " + ms + "ms.", { error })
        onRetry?.()
        // give the other process time to unlock:
        await delay(ms)
      }
    }
  }
  throw new Error(
    "handleDbRetries(): timeout after " +
      Settings.maxBusyDbMs.valueOrDefault +
      "ms"
  )
}
