import { Knex } from "knex"
import { inspect } from "util"
import { Logger, mkLogger } from "../../core/Logger"
import { isProd, isTest } from "../../core/NodeEnv"
import { Constructor } from "../../core/Object"
import { uid } from "../../core/UID"
import { Schema } from "../../core/db/Schemas"
import { includes, notEmptyOr } from "../../fe/Array"
import { boolToInt, isTrue } from "../../fe/Boolean"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { fromEntries } from "../../fe/Object"
import { isPrimitive } from "../../fe/Primitive"
import { toS } from "../../fe/toS"
import { Db } from "../db/Db"
import { DbRequest } from "../db/DbRequest"
import { DbValue, DbValued } from "../db/DbValued"
import { knex } from "../db/Knex"
import { PartialPojo } from "../db/PartialPojo"
import { modelDb } from "./ModelDb"
import { ModelInterface } from "./ModelInterface"
import { assignFromJSON, fromJSON } from "./ModelJson"
import { ModelOps } from "./ModelOps"
import { TableName } from "./TableName"

export interface ModelClass<M extends Model> extends Constructor<M> {
  db(): Db
  dbl: DbRequest
  // NOTE TO FUTURE ME: .ops() can't be a getter, as I need to capture the
  // <this> for typing. :(
  ops(): ModelOps<M>
  fromJSON(json: M | PartialPojo<M>): M
  toJSON(json: M | PartialPojo<M>): DbValued
  // TODO: remove these methods and make everything go through .ops()?
  query(): Knex.QueryBuilder
  queryBy(constraint: PartialPojo<M>): Knex.QueryBuilder
  queryOneBy(constraint: PartialPojo<M>): Knex.QueryBuilder

  $tableName: TableName
  $schema: Schema
  $ctor: string
  $uniqueColumnName: string
  $booleanFields?: string[] | readonly string[]
  $transientFields?: string[] | readonly string[]
  $useCache?: boolean
}
// let id = 0

export class Model implements ModelInterface {
  // readonly $id = ++id
  static readonly $tableName: TableName
  static readonly $schema: Schema = "models"
  static readonly db = modelDb
  static readonly $uniqueColumnName: string
  static readonly $booleanFields?: string[] | readonly string[]
  static readonly $transientFields?: string[] | readonly string[]
  static readonly $useCache?: boolean

  static get $ctor() {
    return this.$schema + "." + this.$tableName
  }

  static query() {
    return knex()(this.$tableName)
  }

  static queryBy<M extends Model>(
    this: ModelClass<M>,
    constraint: PartialPojo<M>
  ) {
    return this.query().where(constraint)
  }

  static queryOneBy<M extends Model>(
    this: ModelClass<M>,
    constraint: PartialPojo<M>
  ) {
    return this.queryBy(constraint)
  }

  static ops<M extends Model>(this: Constructor<M>): ModelOps<M> {
    return ((this as any)._ops ??= new ModelOps(this as any, (this as any).db))
  }

  static get dbl(): DbRequest {
    return this.ops().dbl
  }

  static rows(): number {
    return this.ops().rows()
  }

  static truncate() {
    if (isProd) {
      throw new Error("rejecting attempt to truncate in prod")
    }
    this.dbl.run("DELETE FROM " + this.$tableName)
  }

  $ops() {
    return this.class().ops()
  }

  id?: number

  // Can't be a getter because getters can't have args and we need to give it
  // the this:
  class<M extends Model>(this: M): ModelClass<M> {
    return this.constructor as any
  }

  $pk(): string {
    return toS(this.id)
  }

  $ucn(): DbValue {
    return (this as any)[this.class().$uniqueColumnName]
  }

  $tableName(): string {
    return this.class().$tableName
  }

  readonly $instanceId = isTest ? uid() : undefined

  $uid(): string {
    return `${this.$tableName()}(${this.id ?? this.$ucn()})${
      (this as any).shown === true ? "!" : ""
    }`
  }

  protected logger(): Logger {
    return mkLogger(this.class().$schema + "." + toS(this.valueOf()))
  }

  [inspect.custom]() {
    return { ...this.$toShallowJSON(), $instanceId: this.$instanceId }
  }

  toString(): string {
    return this.$uid()
  }

  // We have to return Object to follow es5
  // eslint-disable-next-line @typescript-eslint/ban-types
  valueOf(): Object {
    return this.$uid()
  }

  /**
   * Supports both db and rpc JSON representations
   */
  static fromJSON<M extends Model>(
    this: Constructor<M>,
    Obj: M | PartialPojo<M>
  ): M {
    return fromJSON(this as ModelClass<M>, Obj)
  }

  static toJSON<M extends Model>(
    this: ModelClass<M>,
    m: M | PartialPojo<M>
  ): DbValued {
    return this.fromJSON(m).toJSON()
  }

  $toShallowJSON() {
    return this.$_toJSON({ encodeBool: isTrue })
  }

  $toDbJSON(colNames?: string[]): Record<keyof this, DbValue> {
    return this.$_toJSON({
      encodeBool: boolToInt,
      includeCtor: false,
      includeTransient: false,
      colNames
    }) as any
  }

  $clone(): this {
    return fromJSON(this.class(), this.toJSON())
  }

  // used for RPC (millis dates to reduce unnecessary parsing work)
  toJSON(): PartialPojo<this> {
    // the m.valueOf removes circular reference errors.
    // we want date in millis to make date parsing simpler.
    return this.$_toJSON({ encodeBool: isTrue })
  }

  _get(
    key: keyof this & string,
    encodeBool: (b: any) => any,
    includeTransient = true
  ): Maybe<[string, DbValue | null]> {
    const value = this[key]
    if (value === null) {
      return [key, null]
    }
    if (!isPrimitive(value)) {
      // unexpected!
      return
    }
    if (!includeTransient && includes(this.class().$transientFields, key)) {
      return
    }
    if (includes(this.class().$booleanFields, key)) {
      return [key, encodeBool(value)]
    }
    return [key, value]
  }

  $_toJSON({
    encodeBool,
    includeCtor = true,
    includeTransient = true,
    colNames
  }: {
    encodeBool: (b: any) => any
    includeCtor?: boolean
    includeTransient?: boolean
    colNames?: string[]
  }): PartialPojo<this> {
    const mc = this.class()
    const columnNames = notEmptyOr(colNames, mc.ops().columnNames())
    return fromEntries(
      columnNames.map(ea => this._get(ea as any, encodeBool, includeTransient)),
      includeCtor ? { $ctor: mc.$ctor } : {}
    ) as any
  }

  insert(): this {
    this.logger().debug("insert()", { self: this.toJSON() })
    return this.class().ops().insertOne(this)
  }

  upsert(arg?: PartialPojo<this>): this {
    if (arg != null) assignFromJSON(this, arg)
    this.logger().debug("upsert()", { self: this.toJSON(), arg })
    const result = this.class().ops().upsertOne(this)
    this.$afterUpsert()
    return result
  }

  $beforeUpsert() {
    // no-op, but Timestamped overrides.
  }

  $afterUpsert() {
    // no-op, but Tag overrides.
  }

  reload() {
    // reloadOne already assigns fetched values to `this`.
    return this.class().ops().reloadOne(this)
  }

  delete() {
    if (gt0(this.id)) {
      this.class().ops().delete([this.id])
    }
  }
}
