import { Maybe } from "../../fe/MaybeTypes"
import { isNumber } from "../../fe/Number"
import { isString } from "../String"
import { Dated } from "./Dated"

// CAREFUL! Don't reference exiftool-vendored here, because it will cause
// account to depend on it.

export function datedToMillis(
  d: Maybe<Dated | number | string>
): Maybe<number> {
  // invalid tag from ExifTool:
  if (d == null || isString(d)) return

  // Assume we're already in milliseconds (!! :hope:)
  if (isNumber(d)) return d

  return d instanceof Date
    ? d.getTime()
    : // FuzzyDate, ExifDateTime, ExifDate, DateTime all have a .toMillis() method:
      (d as any).toMillis?.()
}
