import { Maybe } from "../../fe/MaybeTypes"
import { lazyAsync } from "../async/LazyAsync"
import { stdout_ } from "../child/ChildProcess"
import { sqliteNativePath_ } from "../fs/Tools"
import { commandTimeoutMs } from "../volumes/VolumeTtls"
import bs = require("better-sqlite3")

export const sqliteVersion_ = lazyAsync({
  later: async function () {
    let db: Maybe<bs.Database>
    try {
      db = new bs(":memory:")
      const libraryVersion = db
        .prepare("select sqlite_version()")
        .pluck()
        .get() as string

      const sqliteNativePath = await sqliteNativePath_()
      const fullToolVersion = (
        await stdout_(sqliteNativePath, ["-version"], {
          timeoutMs: commandTimeoutMs()
        })
      ).trim()

      // expected: "3.31.1 2020-01-27 19:55:54 3bfa9cc97da1059852...16eaa837bb4d6"
      const toolVersion = fullToolVersion.split(/\s+/, 1)[0]
      return { libraryVersion, toolVersion, fullToolVersion, sqliteNativePath }
    } finally {
      db?.close()
    }
  },
  timeoutMs: commandTimeoutMs()
})
