import { lazy } from "../../core/Lazy"
import { minuteMs } from "../../fe/Date"
import { isRotation } from "../../fe/Rotation"
import { mkLogger } from "../Logger"
import { stdoutResult_, stdout_ } from "../child/ChildProcess"
import { WrappedError } from "../error/WrappedError"
import { PosixFile } from "../fs/PosixFile"
import { jpegtranNativePath_ } from "../fs/Tools"
import { isWin } from "../platform/Platform"
import { commandTimeoutMs } from "../volumes/VolumeTtls"
import { isIgnorableValidationError } from "./ValidationErrors"

const devnull = isWin ? "NUL" : "/dev/null"

const logger = lazy(() => mkLogger("img.jpegtran"))

/**
 * @throws if the jpeg is not valid.
 */
export async function validJpeg_(
  src: PosixFile,
  isIgnorableError = isIgnorableValidationError
): Promise<void> {
  const cmd = await jpegtranNativePath_()
  try {
    await stdoutResult_(cmd, ["-outfile", devnull, src.nativePath], {
      timeoutMs: commandTimeoutMs(),
      isIgnorableError,
      ignoreExitCode: true
    })
  } catch (cause: any) {
    logger().warn("validJpeg caught invalid file", { src, cause })
    throw new WrappedError("Invalid JPEG: " + src, {
      cause,
      path: src.nativePath,
      doNotSend: true,
      fatal: false,
      retriable: false
    })
  }
}

/**
 * @throws if error
 */
export async function rotateInPlace_(
  src: PosixFile,
  degrees: number
): Promise<void> {
  logger().info("rotateInPlace(" + src + ")", degrees)
  await src.applyWip_({
    fn_: dest => rotate_(src, dest, degrees),
    skipFsLock: false,
    minSizeBytes: 512,
    timeoutMs: minuteMs
  })
}

/**
 * @throws if error
 */
export async function rotate_(
  src: PosixFile,
  dest: PosixFile,
  degrees: number
): Promise<void> {
  const cmd = await jpegtranNativePath_()
  if (!isRotation(degrees)) {
    throw new Error("refusing to rotate(" + src + ", " + degrees + ")")
  }
  await stdout_(
    cmd,
    [
      "-copy",
      "all",
      "-trim",
      "-rotate",
      degrees.toFixed(0),
      "-outfile",
      dest.nativePath,
      src.nativePath
    ],
    { timeoutMs: minuteMs }
  )
  dest.clear() // < emit fileChanged
  return
}
