import { RunResult, Statement } from "better-sqlite3"
import { Knex } from "knex"
import { FifoCache } from "../../core/FifoCache"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { isTest } from "../../core/NodeEnv"
import { ending } from "../../core/async/Endable"
import { mkElapsed } from "../../core/async/PromiseTimer"
import { defaultLogLevel } from "../../core/log/LogFilter"
import { Settings } from "../../core/settings/Settings"
import { isNotEmpty } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { flatten } from "../../fe/Flatten"
import { Maybe } from "../../fe/MaybeTypes"
import { ellipsize, replaceAll } from "../../fe/String"
import { TableName } from "../model/TableName"
import { Db } from "./Db"
import { DbValue, DbValued } from "./DbValued"
import { knex } from "./Knex"
import { Queryish, SqlQuery, sqlQueryToS, toSqlQuery } from "./SqlQuery"

// SQLite max params is 100 and max variables is 1000.

// This limit is just to prevent memory bloat. It can be larger than SQLite
// batch size. It's small in test to prove we do windowing correctly:
export const MaxBatchSize = isTest ? 25 : 1000

const logger = lazy(() => mkLogger("db.DbRequest"))

export class DbRequest {
  private readonly cachedStatements = new FifoCache<Statement>(256)
  constructor(
    readonly db: () => Db,
    private readonly tableName?: TableName
  ) {}

  qb() {
    return knex()(this.tableName)
  }

  private prep(q: Queryish, pluck = false): { sq: SqlQuery; stmt: Statement } {
    const sq = toSqlQuery(q)
    try {
      const stmt = this.cachedStatements.getOrSet(
        this.db().db.__uid + ":" + (pluck === true ? "pluck:" : "") + sq.sql,
        () => {
          const result = this.db().prepare(sq.sql)
          return pluck === true ? result.pluck() : result
        }
      )
      if (!stmt.database.open) {
        this.cachedStatements.clear()
        return this.prep(q, pluck)
      } else {
        return {
          sq,
          stmt
        }
      }
    } catch (error) {
      return logger().throw("prep() failed", {
        error,
        sqlQuery: sq,
        retriable: false
      })
    }
  }

  // private caller() {
  //   return stack().filter(
  //     ea =>
  //       ea.match(
  //         /DbRequest|transactions|dbRetries|asyncRetry|ModelOps|Promise/i
  //       ) == null
  //   )
  // }

  private wrap({
    q,
    pluck,
    m
  }: {
    q: Queryish
    pluck?: boolean
    m: "run" | "get" | "all"
  }) {
    try {
      // HEY FUTURE ME: don't put the catch /in/ the tx: that can prevent errors
      // from being retried correctly.
      const { sq, stmt } = this.prep(q, pluck)
      if (Settings.logSql.valueOrDefault)
        logger().log(
          defaultLogLevel(),
          m + "(): " + sqlQueryToS(sq)
          // this.caller()
        )
      const meth = stmt[m].bind(stmt) as any
      // PERF: INLINED FROM mapOr
      return sq.bindings == null ? meth() : meth(sq.bindings)
    } catch (err: any) {
      if (true === err.message?.includes("AdvisoryLock")) {
        // expect errors from AdvisoryLock!
        throw err
      } else {
        logger().throw(err, { method: m, ...toSqlQuery(q) })
      }
    }
  }

  run(q: Queryish): RunResult {
    return this.wrap({ q, m: "run" })
  }

  runScript(lines: string[], ignorable: string = "") {
    const e = mkElapsed("db.runScript()")
    // Don't block anything with a big transaction boundary:
    for (const sql of lines) {
      if (blank(sql) || sql.trim().startsWith("--")) continue
      this.run({ sql })
      // Only fetch the first part of the query (so temp tables don't pollute
      // the times):
      const msg = replaceAll(ellipsize(sql, 60), ignorable, "")
      e.elapsed(msg)
    }
  }

  mapRun(queries: Queryish[]): RunResult[] {
    return queries.map(q => {
      const { sq, stmt } = this.prep(q)
      return sq.bindings == null ? stmt.run() : stmt.run(sq.bindings)
    })
  }

  runf(f: (q: Knex.QueryBuilder) => Knex.QueryBuilder): RunResult {
    return this.wrap({ q: f(this.qb()), m: "run" })
  }

  upsert(f: (q: Knex.QueryBuilder) => Knex.QueryBuilder): RunResult {
    const q = toSqlQuery(f(this.qb()))
    // https://sqlite.org/lang_insert.html
    // https://sqlite.org/lang_update.html
    q.sql = q.sql.replace(
      /^(?:update|insert)\b/i,
      ea => ea.toUpperCase() + " OR REPLACE "
    )
    return this.wrap({ q, m: "run" })
  }

  first(q: Queryish): DbValued {
    return this.wrap({ q, m: "get" })
  }

  firstf(f: (q: Knex.QueryBuilder) => Knex.QueryBuilder): Promise<DbValued> {
    return this.wrap({ q: f(this.qb()), m: "get" })
  }

  mapAll(queries: Queryish[]): DbValued[] {
    return flatten(
      queries.map(q => {
        const { sq, stmt } = this.prep(q)
        return stmt.all(sq.bindings) as DbValued[]
      })
    )
  }

  all(q: Queryish): DbValued[] {
    return this.wrap({ q, m: "all" })
  }

  allf(f: (q: Knex.QueryBuilder) => Knex.QueryBuilder): DbValued[] {
    return this.wrap({ q: f(this.qb()), m: "all" })
  }

  batched<T>(opts: {
    onResults: (ids: T[]) => any
    qb: (q: Knex.QueryBuilder, prior: Maybe<T[]>) => Knex.QueryBuilder
  }) {
    let prior: Maybe<T[]>
    do {
      prior = this.wrap({
        q: opts.qb(this.qb(), prior).limit(MaxBatchSize),
        m: "all"
      })
      if (isNotEmpty(prior)) {
        opts.onResults(prior)
      }
    } while (isNotEmpty(prior) && !ending())
  }

  pluckFirst<T = DbValue>(q: Queryish): T {
    return this.wrap({ q, pluck: true, m: "get" })
  }

  pluckFirstf<T extends DbValue = DbValue>(
    f: (q: Knex.QueryBuilder) => Knex.QueryBuilder
  ): T {
    return this.wrap({ q: f(this.qb()), pluck: true, m: "get" })
  }

  pluckAll<T = DbValue>(q: Queryish): T[] {
    return this.wrap({ q, pluck: true, m: "all" })
  }

  pluckAllf<T = DbValue>(f: (q: Knex.QueryBuilder) => Knex.QueryBuilder): T[] {
    return this.wrap({ q: f(this.qb()), pluck: true, m: "all" })
  }

  pluckBatched<T extends number | string>(opts: {
    onResults: (ids: T[]) => any
    qb: (q: Knex.QueryBuilder, prior: Maybe<T[]>) => Knex.QueryBuilder
  }) {
    let prior: Maybe<T[]>
    do {
      prior = this.wrap({
        q: opts.qb(this.qb(), prior).limit(MaxBatchSize),
        pluck: true,
        m: "all"
      })
      if (isNotEmpty(prior)) {
        opts.onResults(prior)
      }
    } while (isNotEmpty(prior))
  }
}
