import { lazy } from "../../core/Lazy"
import { PosixFile } from "../../core/fs/PosixFile"
import { map } from "../../fe/Maybe"
import { ID } from "../../fe/api/ID"
import { mkLogger } from "../Logger"
import { shim1 } from "../Shim1"
import { libraryPreviewsDirPosixFile } from "../dir/LibraryDirs"
import { ForceContext } from "../settings/ForceContext"
import { AssetPreviewBuilder, PreviewAssetFile } from "./AssetPreviewBuilder"
import { AssetPreviews } from "./AssetPreviews"

export class Previews {
  static readonly instance = lazy(() => {
    mkLogger("Previews").info(".instance()", {
      libraryPreviewsDir: libraryPreviewsDirPosixFile()
    })
    return map(libraryPreviewsDirPosixFile(), dir => new Previews(dir))
  })

  constructor(readonly root: PosixFile) {}

  apb(
    assetId: number,
    assetFiles: PreviewAssetFile[],
    opts: ForceContext
  ): AssetPreviewBuilder {
    return new AssetPreviewBuilder(this.ap(assetId), assetFiles, opts)
  }

  ap(assetId: number | ID) {
    return new AssetPreviews(this.root, assetId)
  }
}

export interface BuildAssetPreviewsArgs extends ForceContext {
  assetId: number
  assetFiles: PreviewAssetFile[]
  validate?: boolean | undefined
}

export const buildAssetPreviews_ = shim1({
  name: "img.buildAssetPreviews",
  impl: (opts: BuildAssetPreviewsArgs) =>
    Previews.instance()!.apb(opts.assetId, opts.assetFiles, opts).build_()
})
