type K = {
  // Sort order:
  cu: "customerUid"

  // linux
  lc: "linuxCpuUid" // cpu serial from `cpuid` (not available on docker)
  lm: "linuxMachineUid" // /etc/machine-id or /var/lib/dbus/machine-id
  lp: "linuxProductUid" // /sys/class/dmi/id/product_uuid (root-only)
  lb: "linuxBoardSerial" // /sys/devices/virtual/dmi/id/board_serial (root-only).

  // mac
  mp: "macOsPlatformUid" // IOPlatformUUID
  ms: "macOsPlatformSerial" // IOPlatformSerialNumber

  // windows
  wm: "windowsMachineUid"

  // less-good hardware ids:
  cm: "cpuModel" // SHA of os.cpus()[0].model
  bm: "boardModel" // SHA of /sys/devices/virtual/dmi/id/board_[vendor,name] (grep -iv o.e.m)
  nm: "networkMac" // SHA of all network interface MAC addresses

  // filesystem ids:
  li: "libraryUid"
  si: "systemUid"
  vl: "volumeUid"
}

