/**
 * System ID schemes, in order of importance
 */
export const S = strEnum(
  ...([
    "cu",
    "lc",
    "lm",
    "lp",
    "lb",
    "mp",
    "ms",
    "wm",
    "cm",
    "bm",
    "nm",
    "li",
    "si",
    "vl"
  ] as const satisfies readonly (keyof K)[])
)
export type SchemePrefix = StrEnumKeys<typeof S>

export function getScheme(uid: string): Maybe<SchemePrefix> {
  const scheme = uid.split(":", 1)[0]
  return S.has(scheme) ? scheme : undefined
}

export function sortUids(uids: string[]) {
  return sortBy(uniq(uids.filter(isValidUid)), ea => [
    S.indexOf(getScheme(ea)) ?? S.length + 1,
    ea
  ])
}

export function prefix2scheme(prefix: SchemePrefix) {
  return k()[prefix]
}

const GenericUUIDs = [
  // https://www.freedesktop.org/software/systemd/man/machine-id.html#Initialization
  "uninitialized",
  // https://github.com/Kicksecure/dist-base-files/blob/master/etc/machine-id
  "b08dfa6083e7567a1921a715000001fb"
]

export const UidLength = 15

export function toUID(
  pfx: SchemePrefix,
  id: Maybe<string | Buffer>
): Maybe<string> {
  try {
    const str = toS(id).replace(/[^a-z\d=/+]+/gi, "")
    return blank(str) ||
      GenericUUIDs.includes(str.toLowerCase()) ||
      str.match(/^[\s0:_/-]*$/) != null
      ? undefined
      : pfx + ":" + shortStringSha(str, UidLength, Radix58)
  } catch (err) {
    mkLogger("toUID").warn("failed", { pfx, err })
    return
  }
}

const uidRE = defer(
  () => new RegExp(`^(?:${S.values.join("|")}):[0-9a-zA-Z]{${UidLength}}$`)
)

export function isValidUid(s: string) {
  return s != null && uidRE().exec(s) != null
}

export class SchemeCounts {
  readonly #uids = new Set<string>()

  constructor(uids?: string[]) {
    if (uids != null) this.addUids(uids)
  }

  get size() {
    return this.#uids.size
  }

  addUids(uids: string[]) {
    for (const ea of uids) {
      if (getScheme(ea) != null && !this.#uids.has(ea)) {
        this.#uids.add(ea)
        this.schemeCounts.unset()
      }
    }
  }

  count(scheme: SchemePrefix): number {
    return this.schemeCounts().get(scheme)
  }

  readonly schemeCounts = lazy(() => {
    const cs = new CountingSet<SchemePrefix>()
    for (const ea of this.#uids) {
      const s = getScheme(ea)
      if (s != null) cs.incr(s)
    }
    return cs
  })
}
