export const AssetFileSyncStates = strEnum(
  "noop",
  "deleted",
  "skipped",
  "synced",
  "unknown"
)
export type AssetFileSyncState = StrEnumKeys<typeof AssetFileSyncStates>

export const SyncFileStates = strEnum(
  ...AssetFileSyncStates.values,
  "rejected",
  "failed",
  "timeout",
  "enqueued",
  "started",
  "copied",
  "note"
)
export type SyncFileState = StrEnumKeys<typeof SyncFileStates>

export const FileDoneStates = diff<SyncFileState>(SyncFileStates.values, [
  "enqueued",
  "started",
  "note"
])

export const SyncDirStates = strEnum(
  "scanning",
  "canceled",
  "failed",
  "timeout",
  "skipped",
  "scanned"
)
export type SyncDirState = StrEnumKeys<typeof SyncDirStates>

export type SyncReportState = SyncDirState | SyncFileState

export function isRejectedState(state: Maybe<SyncReportState>): boolean {
  return (
    state == null ||
    [
      "canceled",
      "deleted",
      "failed",
      "rejected",
      "timeout",
      "unknown"
    ].includes(state)
  )
}

const FileStateDescriptions: Record<SyncFileState, string> = {
  enqueued:
    "the file looks promising, and will be attempted to be imported soon.",
  rejected:
    "the file did not pass all import filters. The details column will explain why.",
  started:
    "the file was dequeued from the work queue, and is now going to be processed.",
  noop: `the current file metadata already matches your library database, so no operation was needed to sync this file.`,
  deleted:
    "the file was determined to be deleted (the prior mountpoint exists, but the file doesn't exist anymore)",
  skipped: "the file lives on a volume that isn't currently mounted.",
  synced: "the file was imported.",
  failed: "something went wrong. The details column will explain why.",
  timeout: "the file wasn't processed in a reasonable amount of time.",
  copied: `"automatic organization" is enabled (copyAssetsToLibrary=true), and the photo or video was copied into your library originals directory. The details column will contain the source file path.`,
  note: "sidecars will be referenced here. The details column will specify which source file(s) it will be associated with.",
  unknown: "an internal error occurred during processing."
}

const DirStateDescriptions: Record<SyncDirState, string> = {
  scanning: "the directory contents are about to be read.",
  scanned: "the directory contents were completely processed.",
  skipped: "the directory was excluded. The details column will explain why.",

  canceled: "PhotoStructure was shut down before the directory was processed.",
  failed: "reading the directory contents failed.",
  timeout: "reading the directory contents took too long."
}

function recordToBullets(obj: Record<string, string>): string {
  return entries(obj)
    .map(([k, v]) => `  - "${k}": ${v}`)
    .join("\n")
}

const WarnStates = new Set<string>([
  AssetFileSyncStates.deleted,
  SyncFileStates.failed,
  SyncFileStates.timeout,
  SyncDirStates.canceled
])

// only used for typing, and so we can get a non-runtime-erased set of keys for
// the header:
class SyncReportClass {
  constructor(
    readonly ts: number,
    readonly at: string,
    readonly path: string,
    readonly state: SyncReportState,
    readonly from: string,
    readonly elapsedMs?: number,
    readonly details?: string,
    readonly url?: string
  ) {}
}

export const SyncReportHeaders = strEnum(
  ...keys(
    new SyncReportClass(
      Date.now(), // ts
      "", // at
      "", // path
      "" as any, //state
      "", // from
      0, // elapsedMs
      "", // details
      "" // url
    )
  )
)

// eslint-disable-next-line @typescript-eslint/no-empty-interface
export interface SyncReportRow extends SyncReportClass {}
export type SyncProgress = Omit<SyncReportClass, "ts" | "at">
export type WrapSyncProgress = Partial<Omit<SyncProgress, "state">> &
  Pick<SyncProgress, "state"> & { error?: string }

export function syncReportDir() {
  // in production we shouldn't need a sync report unless we have a library, but
  // when testing within core, we won't always have a library set up.
  return librarySyncReportsDir() ?? PosixFile.for(logDir()).join("sync-reports")
}

export function syncReportReadme(): PosixFile {
  return syncReportDir().join("README.txt")
}

export const ensureSyncReportReadme = lazy(() =>
  writeTextfile_(
    syncReportReadme().nativePath,
    wrap(
      `

Howdy from PhotoStructure!

This directory contains your recent "sync reports". 

These files are written as your scan paths are examined for photos and videos.

PhotoStructure creates a new file when it restarts and when it completes importing a given scan path directory.

These CSV files can be opened with most spreadsheet applications, like LibreOffice.


Column descriptions
===================

The "ts" column is the timestamp for the row, in millis from 1970-01-01. Most spreadsheet applications dont know how to parse these values, though, so we also add the at column.

The "at" column is ts in ISO format with only second resolution, and should be parsable by most spreadsheet software.

The "path" column is the native path of the directory or file.

The "state" column explains why that row was added.

The "from" column specifies which codepath added the sync report row.

The "elapsedMs" column is only added to rows completing a given path, and records how long that process took.

The "details" column will include information about the path, like why a given file or folder were rejected.

The "url" column is only added to rows when a file or directory is imported. You may need to adjust the domain name of the URL to make it work correctly (it defaults to localhost).


Directory state values
======================

The "state" column for directories will be

${recordToBullets(DirStateDescriptions)}


File state values
=================

The "state" column for files will be

${recordToBullets(FileStateDescriptions)}


See https://photostructure.com/go/sync-reports

`,
      { maxLineLen: 80 }
    ).join("\n")
  )
)

function mkSyncReportPath() {
  const dt = DateTime.now()
  return syncReportDir().join(
    dt.toFormat("y-MM-dd"),
    filestamp() + "-sync-report.csv"
  )
}

export async function recentSyncReports(agoMs = dayMs) {
  const min_mtime = Date.now() - agoMs
  return syncReportDir().descendants(
    async ea => isCsvExt(ea) && gte(await ea.mtimeMs(), min_mtime)
  )
}

export const syncReport = lazy(() => new SyncReport())

export class SyncReport extends EndableWrapper {
  #outputFile: Maybe<PosixFile>
  #output: Maybe<Writable>
  #rowCount = 0
  readonly outputFiles = new BoundedList<string>(64)

  constructor() {
    super(
      `fs.SyncReport()`,
      () => {
        return this.close()
      },
      EndableRanks.service
    )
    void ensureSyncReportReadme()
  }

  get outputNativePath() {
    return this.#outputFile?.nativePath
  }

  get rowCount() {
    return this.#rowCount
  }

  onProgress(s: SyncProgress) {
    if (s == null || blank(s.path) || blank(s.state)) {
      return this.logger.error(".onProgress(): invalid input", {
        s,
        from: stack()
      })
    } else {
      this.logger.log(
        WarnStates.has(s.state) ? LogLevels.warn : LogLevels.info,
        "onProgress()",
        s
      )
      const ts = Date.now()
      const rowObj: SyncReportRow = {
        ts,
        at: new Date(ts).toISOString(),
        path: s.path,
        state: s.state,
        from: s.from,
        details: toS(s.details).trim().replace(/\r?\n/g, "; "),
        url: s.url, // OpenOffice doesn't auto-link angle-bracket-wrapped urls SAD
        elapsedMs: s.elapsedMs
      }
      const row = unparse([rowObj], {
        header: false,
        columns: SyncReportHeaders.values
      })
      this.output.write(row + Newline, err => {
        if (err != null) {
          this.logger.error("Failed to write", err)
        }
      })
      if (++this.#rowCount > Settings.syncReportMaxRows.valueOrDefault) {
        void this.close()
      }
    }
  }

  /**
   * Run the given function `fn_`, ensuring both start and finish rows are
   * written
   *
   * @throws any error thrown by `fn_` after writing an error report row
   */
  async wrap_<T extends WrapSyncProgress>({
    path,
    from,
    fn_
  }: {
    path: string
    from: string
    fn_: () => Promise<T>
  }): Promise<T> {
    const start = Date.now()
    this.onProgress({
      path,
      from,
      state: SyncFileStates.started
    })
    try {
      const result = await fn_()
      const state =
        result.state ??
        (result.error != null ? SyncFileStates.failed : SyncFileStates.unknown)
      if (state === SyncFileStates.unknown) {
        this.logger.error(
          from + " returned result with no state!" + InternalErrorFlag,
          { path, result }
        )
      }
      syncReport().onProgress({
        path,
        from,
        elapsedMs: Date.now() - start,
        ...result,
        state
      })
      return result
    } catch (error: any) {
      this.logger.warn("wrap_() caught error", { error, stack: error.stack })
      this.onProgress({
        path,
        from,
        state:
          error instanceof TimeoutError
            ? SyncFileStates.timeout
            : SyncFileStates.failed,
        details: errorToS(error),
        elapsedMs: Date.now() - start
      })

      throw error
    }
  }

  get output() {
    return this.#output ?? this.#setupOutput()
  }

  // must be sync
  #setupOutput(): Writable {
    this.#outputFile = mkSyncReportPath().ensureNewSync_({ emptyIsNew: false })
    this.outputFiles.push(this.#outputFile.nativePath)
    this.logger.info("Opening new report: " + this.#outputFile)
    if (isTest) stdoutWrite({ syncReport: this.#outputFile.nativePath }, false)
    this.#output = createWriteStream(this.#outputFile.nativePath)
    this.#output.write(
      unparse([SyncReportHeaders.values], {
        header: false
      }) + Newline
    )
    return this.#output
  }

  /**
   * Close the current sync report. The next onProgress() will create a new
   * report.
   */
  async close(): PromiseMaybe<PosixFile> {
    const output = this.#output
    this.#output = undefined
    this.#rowCount = 0
    await endStream(output)
    return output == null ? undefined : this.#outputFile
  }
}
