import * as fsp from "node:fs/promises"
import trash from "trash"
import { lazy } from "../../core/Lazy"
import { uniqBy } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { OneOrMore, toA } from "../../fe/toA"
import { mkLogger } from "../Logger"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { mapAsyncSerial, thenNot } from "../async/Promise"
import { untilTrue } from "../async/until"
import { ee } from "../event/EventEmitter"
import { Settings } from "../settings/Settings"
import { uri2nativePath } from "../uri/FileURI"
import { commandTimeoutMs } from "../volumes/VolumeTtls"
import { SimpleFileOrString, toNativePath_ } from "./SimpleFile"
import { nativePathIsFile } from "./Stat"

const logger = lazy(() => mkLogger("fs.Unlink"))

export interface TrashOrUnlinkResult {
  nativePath: Maybe<string>
  unlinked: boolean
}

export async function unlink(arr: OneOrMore<SimpleFileOrString>) {
  for (const ea of toA(arr)) {
    try {
      if (ea != null) await fsp.unlink(toNativePath_(ea))
    } catch (error) {
      logger().warn("Failed to unlink " + ea, { error })
    }
  }
}

export async function trashOrUnlinkFileUris_(
  arr: { uri: string; mountpoint?: Maybe<string> }[]
) {
  // macOS soft-deletions are _not great_. Only do one at a time to work
  // around https://github.com/sindresorhus/trash/issues/24

  return mapAsyncSerial({
    name: "trashOrUnlinkFileUris_",
    arr: uniqBy(arr, ea => ea.uri),
    f: async ({ uri, mountpoint }) => {
      const nativePath = await uri2nativePath(uri, mountpoint)
      if (blank(nativePath)) {
        return logger().tap({
          msg: "trashOrUnlink: Failed to resolve URI to delete",
          result: undefined,
          meta: { uri, mountpoint }
        })
      }

      try {
        return logger().tap({
          msg: "trashOrUnlink",
          result: await trashOrUnlinkNativePath_(nativePath),
          meta: { nativePath, uri, mountpoint }
        })
      } catch (error) {
        logger().error("Failed to unlink()", {
          nativePath,
          uri,
          mountpoint,
          error
        })
      }
      return
    },
    timeoutMs: commandTimeoutMs()
  })
}

/**
 * @return true if the file was unlinked, false if the file was missing
 * @throws error on file permission error
 */
export async function trashOrUnlinkFileUri_(
  uri: string,
  mountpoint?: Maybe<string>
): Promise<TrashOrUnlinkResult> {
  const nativePath = await uri2nativePath(uri, mountpoint)
  if (blank(nativePath)) {
    return logger().tap({
      msg: "trashOrUnlinkFileUri_: null nativePath",
      result: { nativePath, unlinked: false },
      meta: { uri, mountpoint }
    })
  } else {
    return logger().tap({
      msg: "trashOrUnlinkFileUri_()",
      result: await trashOrUnlinkNativePath_(nativePath),
      meta: {
        uri,
        mountpoint
      }
    })
  }
}

export async function trashOrUnlinkNativePath_(
  nativePath: string
): Promise<TrashOrUnlinkResult> {
  if (!(await nativePathIsFile(nativePath))) {
    return logger().tap({
      level: "warn",
      msg: "trashOrUnlinkFileUri_: path is not a file, refusing to unlink.",
      result: { nativePath, unlinked: false }
    })
  }

  if (Settings.trySoftDeletes.valueOrDefault) {
    try {
      await trash(nativePath, { glob: false })
      if (
        await untilTrue(() => thenNot(nativePathIsFile(nativePath)), {
          timeoutMs: ShortCommandTimeoutMs
        })
      ) {
        logger().info("soft-delete(" + nativePath + ") successful")
        return { nativePath, unlinked: true }
      } else {
        logger().warn(
          "soft-delete(" +
            nativePath +
            ") didn't seem to work: the file is still there?"
        )
      }
    } catch (error) {
      logger().warn(
        "Tried to soft-delete, but that failed. We'll try to hard-delete.",
        { error }
      )
    }
  }

  await unlink(nativePath)
  ee().emit("fileChanged", nativePath)
  logger().info("unlink(" + nativePath + ") successful")
  return { nativePath, unlinked: true }
}
