export const l = lazy<{
  c: "crypto"
  m: "createPublicKey"
  d: "2RHuDkCTP" // default license if k is missing
  o: [
    {
      k: "2RHuDkCTP"
      format: "pem"
      type: "spki"
      key: "-----BEGIN PUBLIC KEY-----\nMCowBQYDK2VwAyEA5XK+l4rxUbUfFOUkFN3S5kk0xpQo4zA3bGNY9SdL1XE=\n-----END PUBLIC KEY-----\n"
    },
    {
      k: "2SoVY4nUP"
      format: "pem"
      type: "spki"
      key: "-----BEGIN PUBLIC KEY-----\nMCowBQYDK2VwAyEAb3JzxWd0rNW8Sft1X9FgEXcbPmCtKT/Eqmh+qx3wczk=\n-----END PUBLIC KEY-----\n"
    }
  ]
  w: "verify"
}>(
  () =>
    j(
      ""
    ) as any
)

/**
 * V2.verify the given token string
 */
export function V(s: string) {
  const d = decode(s)
  const payload: any = d.payload
  if (payload == null) throw new Error("Missing payload")
  // Tokens generated in 2021 didn't have a public key sha. Default to the 2021 key:
  const sha = notBlankOr(payload.k, l().d)
  const k = l().o.find(ea => ea.k === sha)
  if (k == null) throw new Error("Unknown key")
  return V2.verify(s, k)
}
