import { isEmpty } from "../../fe/Array"
import { tag_fts_path, tag_fts_root } from "../tag/TagFtsFns"
import { Model } from "./Model"
import { Tag } from "./Tag"

// FTS === Full Text Search

export class TagFts extends Model {
  static override readonly $tableName = "tag_fts"
  static override readonly $uniqueColumnName = "rowid"
  static override readonly $booleanFields = []

  root?: string
  path?: string

  static insertFts(tags: Tag[]) {
    const arr = tags
      // never need to index root tags:
      .filter(tag => tag.parentId != null)
      .map(tag => ({
        rowid: tag.id,
        root: tag_fts_root(tag._path),
        path: tag_fts_path(tag._path)
      }))
    return isEmpty(arr) ? undefined : this.dbl.runf(q => q.insert(arr))
  }

  static async rebuild() {
    const tn = TagFts.$tableName
    this.dbl.runScript([`INSERT INTO ${tn}(${tn}) VALUES('delete-all')`])
    Tag.ops().batched({
      qb: q => q,
      onResults: arr => this.insertFts(arr)
    })
  }
}
