import { NoLibraryErrorFlag } from "../../core/error/ErrorFlags"
import { secondMs } from "../../fe/Date"
import { lt } from "../../fe/Number"
import { TimestampedModel } from "./TimestampedModel"

export class Heartbeat extends TimestampedModel {
  static override readonly $tableName = "Heartbeat"
  static override readonly $uniqueColumnName = "name"
  static override readonly $useCache = false

  name?: string

  static ping(name: string): Heartbeat {
    return Heartbeat.ops().upsertOne({ name })
  }

  static assertPing_(name: string = "ping-" + Date.now()) {
    if (Heartbeat.db() == null) {
      throw new Error("no library is open" + NoLibraryErrorFlag)
    }
    // Don't check if the db is open here. If it's not, the db will throw an
    // error from this.ping().
    try {
      Heartbeat.ping(name)
      const fromDb = Heartbeat.ops().findOneBy({ name })
      if (
        fromDb == null ||
        fromDb.name !== name ||
        lt(fromDb.updatedAt, Date.now() - 20 * secondMs)
      ) {
        throw new Error("Heartbeat row wasn't inserted")
      }
    } finally {
      try {
        Heartbeat.dbl.runf(q => q.delete().where({ name }))
      } catch {
        // don't care, we're just trying to clean up
      }
    }
  }
}
