import { PosixFile } from "../../core/fs/PosixFile"
import { nativePath2uri } from "../../core/uri/FileURI"
import { URI } from "../../core/uri/URI"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { MaybeSyncOrAsync } from "../../fe/SyncOrAsync"

export interface UriPath {
  nativePath: string
  uri: string
}

export async function toUriPath(
  nativePath: string,
  maybeUri: MaybeSyncOrAsync<URI>
): PromiseMaybe<UriPath> {
  const uri = await maybeUri
  if (uri == null) return
  return {
    nativePath,
    uri: uri.toString()
  }
}

export async function posixFileToUriPath(f: PosixFile): PromiseMaybe<UriPath> {
  return toUriPath(f.nativePath, await f.uriObject_())
}

export async function nativePathToUriPath(
  nativePath: string
): PromiseMaybe<UriPath> {
  return toUriPath(nativePath, nativePath2uri(nativePath))
}
