import { Database } from "better-sqlite3"
import { batches, greatestBy } from "../../core/Array"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { mapGt0 } from "../../core/Number"
import { escapeRegExp } from "../../core/RegExp"
import { countChars, isString } from "../../core/String"
import { toLabhash, unlabhash } from "../../core/color/Colorspace"
import { ModeBits } from "../../core/color/ModeBits"
import { ModeCount } from "../../core/color/ModeCount"
import { Triplet } from "../../core/color/Triplet"
import { datedToPrecisionMs, isoToDated } from "../../core/date/Dated"
import { isoToLocal } from "../../core/date/Localtime"
import { isoToOffsetMinutes } from "../../core/date/Timezone"
import { validYMD } from "../../core/date/ValidDate"
import { splitBits } from "../../core/math/Bits"
import { TagSep, normalizeTagPath } from "../../core/tags/TagPaths"
import { normalizeURI } from "../../core/uri/UriNormalization"
import { compact } from "../../fe/Array"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { times, toInt } from "../../fe/Number"
import { opt } from "../../fe/Opt"
import { PS_NETWORK_FILESYSTEM_SCHEME } from "../../fe/URI"
import { toS } from "../../fe/toS"
import { dedupeTags_, mergeTags_ } from "./DedupeTags"

const logger = lazy(() => mkLogger("db.Migrations"))

const psnetRe = new RegExp(
  `^(${escapeRegExp(PS_NETWORK_FILESYSTEM_SCHEME)}://.*?/)(.*)$`
)

// exported for tests
export const lowercase_psnet_function = (s: any) =>
  opt(s)
    .filter(isString)
    .flatMap(ea => psnetRe.exec(ea))
    .map(m => m[1].toLowerCase() + m[2])
    .getOrElse(() => s)

function splitLabs(labModes: number, bits = ModeBits): Triplet[] {
  return splitBits(labModes, bits).map(ea => unlabhash(ea, bits)) as Triplet[]
}

export function plucklabmodeb6(labModes: number, index: number): Maybe<number> {
  return mapGt0(labModes, () =>
    map(splitLabs(labModes, 6)[index], ea => toLabhash(ea, ModeBits))
  )
}

export function migrationNameToDate(name: string): Maybe<Date> {
  const [y, m, d] = [name.slice(0, 4), name.slice(4, 6), name.slice(6, 8)].map(
    s => toInt(s)!
  )
  return validYMD(y, m, d) ? new Date(y, m - 1, d) : undefined
}

export function tag_path_suffix(path: string): string {
  return path.endsWith(TagSep) ? path : path + TagSep
}

export function fix_root_tags(db: Database) {
  for (const broken of db
    .prepare("SELECT id, parentId, _path FROM Tag WHERE id = parentId")
    .iterate() as IterableIterator<{
    id: number
    parentId: number
    _path: string
  }>) {
    logger().warn("fix_root_tags(): broken tag", { broken })
    if (countChars(broken._path, TagSep) < 2) {
      db.prepare("UPDATE Tag set parentId = null where id = ?").run(broken.id)
    }
  }
}

export function isoToPrecisionMs(iso: string): number {
  return map(isoToDated(iso), datedToPrecisionMs) ?? -1 // 0 is valid
}

/**
 * The functions exported in this namespace may be used by migrations that share
 * the same name as the function. "Applying" that migration will run that
 * function.
 */
export const Migrations = {
  lowercase_psnet_hostname: (db: Database) => {
    db.function(
      "lowercase_psnet_hostname",
      { deterministic: true },
      lowercase_psnet_function
    )
    db.exec("DROP INDEX IF EXISTS assetfile_uri_udx")
    db.exec("UPDATE AssetFile SET uri = lowercase_psnet_hostname(uri)")
    db.exec("CREATE UNIQUE INDEX assetfile_uri_udx ON AssetFile(uri)")
  },

  numeric_captured_at: (db: Database) => {
    db.function("isoToLocal", { deterministic: true }, isoToLocal as any)
    db.function(
      "isoToOffsetMinutes",
      { deterministic: true },
      isoToOffsetMinutes as any
    )
    db.function(
      "isoToPrecisionMs",
      { deterministic: true },
      isoToPrecisionMs as any
    )
    db.exec("ALTER TABLE AssetFile ADD COLUMN capturedAtLocal bigint")
    db.exec("ALTER TABLE AssetFile ADD COLUMN capturedAtOffset integer")
    db.exec("ALTER TABLE AssetFile ADD COLUMN capturedAtPrecisionMs integer")
    db.exec("UPDATE AssetFile SET capturedAtLocal = isoToLocal(capturedAtISO)")
    db.exec(
      "UPDATE AssetFile SET capturedAtOffset = isoToOffsetMinutes(capturedAtISO)"
    )
    db.exec(
      "UPDATE AssetFile SET capturedAtPrecisionMs = isoToPrecisionMs(capturedAtISO)"
    )
    db.exec("DROP INDEX asset_captured_at_geohash_idx")
    db.exec("DROP INDEX assetfile_exifuid_idx")
    db.exec(
      "CREATE INDEX assetfile_capturedAtLocal_idx ON AssetFile(capturedAtLocal)"
    )
  },

  spread_modes: (db: Database) => {
    db.function(
      "plucklabmodeb6",
      { deterministic: true },
      plucklabmodeb6 as any
    )

    times(ModeCount, i =>
      db.exec("ALTER TABLE AssetFile ADD COLUMN " + `mode${i} integer`)
    )
    db.exec(
      "UPDATE AssetFile SET " +
        times(ModeCount, i => `mode${i} = plucklabmodeb6(mode8b6, ${i})`).join(
          ", "
        )
    )
  },

  normalize_asset_file_uris: (db: Database) => {
    db.function("normalizeURI", { deterministic: true }, normalizeURI as any)
    const rows = db
      .prepare(
        "SELECT normalizeURI(uri) AS nuri, count(*) AS cnt, GROUP_CONCAT(id) AS ids FROM AssetFile GROUP BY nuri HAVING cnt > 1"
      )
      .all() as { nuri: string; cnt: number; ids: string }[]
    const loserIds = []
    for (const row of rows) {
      const ids = toS(row.ids).split(",")
      const afs = db
        .prepare(
          "SELECT id, shown, version, updatedAt FROM AssetFile WHERE id in (" +
            ids.join(",") +
            ")"
        )
        .all() as {
        id: number
        shown: number
        version: number
        updatedAt: number
      }[]
      const winner = greatestBy(afs, ea => [ea.shown, ea.version, ea.updatedAt])
      const losers = afs.filter(ea => ea.id !== winner?.id)
      loserIds.push(...losers.map(ea => ea.id))
    }
    for (const ids of batches(loserIds, 256)) {
      db.exec("DELETE FROM AssetFile WHERE id in (" + ids.join(",") + ")")
    }
    db.exec("UPDATE AssetFile SET uri = normalizeURI(uri)")
  },

  dedupe_tag_paths: dedupeTags_,

  suffix_tag_paths: (db: Database) => {
    db.function(
      "tag_path_suffix",
      { deterministic: true },
      tag_path_suffix as any
    )
    dedupeTags_(db)
    db.exec("UPDATE Tag SET _path = tag_path_suffix(_path)")
  },

  // fixes tags with empty interstitial paths (like `aaa//bbb/ccc/`)
  normalize_tag_paths: (db: Database) => {
    const badTags = db
      .prepare(
        "SELECT id, _path FROM Tag WHERE _path LIKE '%' || char(31) || char(31) || '%'"
      )
      .all() as { id: number; _path: string }[]
    logger().info("normalize_tag_paths():", { badTags })
    for (const tag of badTags) {
      const correctPath = normalizeTagPath(tag._path)
      mergeTags_(
        db,
        compact([
          tag.id,
          ...(db
            // handle case insensitivity with LIKE
            .prepare("SELECT id FROM Tag WHERE _path LIKE ?")
            .pluck()
            .all(correctPath) as number[])
        ])
      )
    }
  },

  drop_tc_tables: (db: Database) => {
    const re = /^(ct|taf|tag_counts)_[a-z\d]{6}$/i
    const tables = db
      .prepare("SELECT name FROM sqlite_master WHERE type='table'")
      .pluck()
      .all() as string[]
    const victims = tables.filter(ea => re.exec(ea) != null)
    logger().info("drop_tc_tables()", { tables, victims })
    for (const ea of victims) {
      db.exec("DROP TABLE " + ea)
    }
  },

  // https://forum.photostructure.com/t/incorrectly-rotated-heic-images/34/15?u=mrm
  rebuild_rotated_heic: (db: Database) => {
    db.exec(
      "UPDATE AssetFile SET version = 0 WHERE mimetype = 'image/heif' AND rotation <> 0"
    )
  },

  rebuild_videos: (db: Database) => {
    db.exec("UPDATE AssetFile SET version = 0 WHERE mimetype LIKE 'video/%'")
    db.exec(
      "UPDATE Asset SET version = 0 WHERE id IN (SELECT DISTINCT assetId from AssetFile where mimetype LIKE 'video/%')"
    )
  },

  copy_asset_duration: (db: Database) => {
    db.exec("CREATE INDEX tmp_af_idx ON AssetFile (assetId, durationMs)")
    db.exec(
      "UPDATE Asset SET durationMs = (SELECT durationMs FROM AssetFile WHERE assetId = Asset.id AND durationMs IS NOT NULL LIMIT 1) WHERE durationMs IS NULL"
    )
    db.exec("DROP INDEX tmp_af_idx")

    db.exec(
      "UPDATE AssetFile SET version = 0 WHERE mimetype LIKE 'video/%' and durationMs IS NULL"
    )
    db.exec(
      "UPDATE Asset SET version = 0 WHERE id IN (SELECT DISTINCT assetId from AssetFile where version = 0)"
    )
  }
}
