import { Command } from "commander"
import { CommandPlugin } from "../../core/cli/CLI"
import { Settings } from "../../core/settings/Settings"
import { isTrue } from "../../fe/Boolean"
import { Obj } from "../../fe/Object"

/**
 * Used by main and web
 */
export const ExposeArg: CommandPlugin = {
  beforeParse: (cmd: Command) =>
    cmd.option(
      "--expose",
      "The web service is only accessible to the computer running PhotoStructure by default. Providing this option will expose your library to all computers on your network. See https://photostructure.com/faq/remote-access/ ."
    ),
  afterParse: (opts: Obj) => {
    if (isTrue(opts.expose)) {
      Settings.exposeNetworkWithoutAuth.envValue = true
    }
  }
}
