import {
  ExifDateTime,
  UnsetZoneName,
  UnsetZoneOffsetMinutes
} from "exiftool-vendored"
import { Maybe } from "../../fe/MaybeTypes"
import { isNumber } from "../../fe/Number"
import { DateInterval } from "./DateInterval"
import { Dated } from "./Dated"
import { isDateTime } from "./IsDateTime"

export function hasZone(dated: Maybe<Dated | number>): boolean {
  if (dated == null || isNumber(dated)) {
    return false
  }

  if (dated instanceof DateInterval || dated instanceof ExifDateTime) {
    return dated.hasZone
  }

  if (isDateTime(dated)) {
    return (
      dated.zone != null &&
      dated.zone.isValid &&
      dated.zone.type !== "local" &&
      dated.zone.name !== UnsetZoneName &&
      dated.zone.offset(Date.now()) !== UnsetZoneOffsetMinutes
    )
  }

  return false
}

// Used for creating DateTime. "UTC", or something like "UTC+07:30"

export function getZoneName(d: Maybe<Dated>): Maybe<string> {
  if (d == null || d instanceof Date) {
    return
  } else if (isDateTime(d)) {
    return d.zoneName === UnsetZoneName ? undefined : d.zone?.name
  } else if (d instanceof ExifDateTime) {
    return d.zone
  } else {
    // don't retain the timezone offset of the source date.
    return
  }
}
