import { Maybe } from "./MaybeTypes"

// file deepcode ignore reDOS: <these RE can't cause a DOS>

const chromeRE = /\bChrome\b/
const safariRE = /\bSafari\b/
const firefoxRE = /\bFirefox\b/
const ipadRE = /\biPad\b/
const iphoneRE = /\biPhone\b/

function matches(ua: Maybe<string>, re: RegExp): boolean {
  return ua != null && String(ua).match(re) != null
}

export function isChrome(ua: Maybe<string>): boolean {
  return matches(ua, chromeRE)
}

export function isSafari(ua: Maybe<string>): boolean {
  return matches(ua, safariRE) && !isChrome(ua) && !isFirefox(ua)
}

export function isFirefox(ua: Maybe<string>): boolean {
  return matches(ua, firefoxRE)
}

// NOPE https://developer.apple.com/forums/thread/119186
export function isIpad(ua: Maybe<string>): boolean {
  return matches(ua, ipadRE)
}

export function isIphone(ua: Maybe<string>): boolean {
  return matches(ua, iphoneRE)
}
