import { ExifDateTime, Tags, UnsetZone } from "exiftool-vendored"
import { compactBlanks, mapNotEmpty } from "../../fe/Array"
import { toNotBlank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { map } from "../../fe/Maybe"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { isNumber, mapInt, toInt } from "../../fe/Number"
import { compactValues } from "../../fe/Object"
import { thenMap } from "../../fe/Promise"
import { toA } from "../../fe/toA"
import { first } from "../Array"
import { validLat, validLon } from "../GeoHash"
import { Dated } from "../date/Dated"
import { parseDated } from "../date/FuzzyDateParser"
import { normalizeZone } from "../date/Timezone"
import { PosixFile } from "../fs/PosixFile"

export type JsonSidecarTags = Pick<
  Tags,
  "Title" | "Description" | "GPSLatitude" | "GPSLongitude" | "GPSAltitude"
> & {
  /**
   * This is when the photo was uploaded to Google Photos:
   */
  creationTime?: Dated
  /**
   * This is the time the photo was last modified within Google Photos:
   */
  modificationTime?: Dated
  /**
   * This isn't remotely correct. I'm expecting 2019:01:08 17:28:44.740313
   * (local, UTC-10:00) but I get something like Jan 9, 2019, 3:14:38 AM UTC
   * (expecting 3:28:44 UTC)
   */
  photoTakenTime?: Dated
  imageViews?: number
  peopleNames?: string[]
  favorited?: boolean
  // https://forum.photostructure.com/t/handle-egregious-google-takeout-metadata-butchering/1029/2
  googlePhotosOrigin?: boolean
}

