function parseTimestamp(
  ea: Maybe<{ timestamp?: string; formatted?: string }>,
  defaultZone: Maybe<string>
): Maybe<Dated> {
  if (ea == null) return

  const unixtime = toInt(ea?.timestamp)
  if (isNumber(unixtime)) {
    const zone = normalizeZone(ea?.formatted)
    const edt = ExifDateTime.fromMillis(unixtime * 1000, {
      rawValue: ea.formatted,
      zone: zone ?? defaultZone ?? UnsetZone
    })
    if (true === edt?.isValid) return edt
  }

  return parseDated({ input: ea.formatted, defaultZone })
}

/**
 * JSON sidecar from Google Takeouts.
 */
export async function readJsonSidecar(
  jsonFile: PosixFile,
  defaultZone?: Maybe<string>
): PromiseMaybe<JsonSidecarTags> {
  return thenMap(jsonFile.readJson<any>("info"), j => {
    return compactValues({
      Title: toNotBlank(j.title),
      Description: toNotBlank(j.description),
      GPSLatitude: first([j.geoData?.latitude, j.geoDataExif?.latitude], lat =>
        validLat(lat) ? lat : undefined
      ),
      GPSLongitude: first(
        [j.geoData?.longitude, j.geoDataExif?.longitude],
        lon => (validLon(lon) ? lon : undefined)
      ),
      GPSAltitude: first([j.geoData?.altitude, j.geoDataExif?.altitude], alt =>
        isNumber(alt) ? alt : undefined
      ),
      favorited: map(j.favorited, isTrue),
      peopleNames: mapNotEmpty(
        [...toA(j.people), ...toA(j.person)],
        (arr: any[]) => compactBlanks(arr.map(ea => ea.name))
      ),
      creationTime: parseTimestamp(j.creationTime, defaultZone),
      modificationTime: parseTimestamp(j.modificationTime, defaultZone),
      photoTakenTime: parseTimestamp(j.photoTakenTime, defaultZone),
      imageViews: mapInt(j.imageViews, i => i),
      // https://forum.photostructure.com/t/handle-egregious-google-takeout-metadata-butchering/1029
      googlePhotosOrigin: true
    })
  })
}
