import { WriteTags } from "exiftool-vendored"
import { lazy } from "../../core/Lazy"
import { isEmpty } from "../../fe/Array"
import { assignFields, isEmptyObj, pick } from "../../fe/Object"
import { mkLogger } from "../Logger"
import { time } from "../async/PromiseTimer"
import { PosixFile } from "../fs/PosixFile"
import { Settings } from "../settings/Settings"
import { ExifTags } from "../tags/ExifTags"
import { overwriteTags_, readTags } from "../tags/ExifTool"

const logger = lazy(() => mkLogger("img.includePreviewTags"))

export async function includePreviewTags_(src: PosixFile, files: PosixFile[]) {
  if (
    false === Settings.includePreviewTags.valueOrDefault ||
    Settings.includedPreviewTags.isEmpty() ||
    isEmpty(files)
  ) {
    // no-op
    return
  }

  logger().info("Re-adding metadata to previews...")
  await time("img.includePreviewTags", async () => {
    const writeTags: Partial<ExifTags> & WriteTags = pick(
      (await readTags(src)) as any,
      ...Settings.includedPreviewTags.values
    )
    // also handle the captured-at and exposure settings
    const ca = writeTags?.capturedAt
    if (ca != null) {
      delete writeTags.capturedAt
      assignFields(writeTags, ca.asExifTag())
    }
    const exp = writeTags?.exposureSettings
    if (exp != null) {
      delete writeTags.exposureSettings
      // These tag locations make Eye of Gnome and Apple Previews happy:
      writeTags.FocalLength = exp.focalLength
      writeTags.FNumber = exp.aperture
      writeTags.ExposureTime = exp.shutterSpeed
      writeTags.ISO = exp.iso
    }
    if (Settings.includedPreviewTags.has("Source")) {
      writeTags.Source = src.nativePath
    }
    if (isEmptyObj(writeTags)) {
      logger().warn(
        "not adding tags to previews: no values for tags in source file",
        {
          src,
          includedPreviewTags: Settings.includedPreviewTags.values
        }
      )
    } else {
      logger().info("Overwriting preview tags...", {
        src,
        writeTags
      })
      await Promise.all(files.map(ea => overwriteTags_(ea, writeTags)))
    }
  })
}
