import { Maybe } from "../../fe/MaybeTypes"
import { clamp } from "../../fe/Number"
import { leftPad } from "../../fe/Pad"
import { ensurePrefix } from "../../fe/String"
import { toS } from "../../fe/toS"
import { Triplet } from "./Triplet"

const RgbHexRe = /^#?([\da-f]{2})([\da-f]{2})([\da-f]{2})$/i

export function normalizeRgbHex(rgbhex: string): Maybe<string> {
  const s = toS(rgbhex).trim()
  return RgbHexRe.test(s) ? ensurePrefix(s, "#").toUpperCase() : undefined
}

export function rgbhex2triplet(rgbHex: string): Maybe<Triplet> {
  const m = toS(rgbHex).trim().match(RgbHexRe)
  if (m == null) return
  try {
    // parseInt throws if the string is not a valid number, but the regex
    // should only allow valid hex values.
    return [m[1], m[2], m[3]].map(
      ea => clamp(0, 255, parseInt(ea, 16)) // < no need to round
    ) as Triplet
  } catch {
    return
  }
}

export function rgbTriplet2hex(rgb: Triplet): string {
  return (
    "#" +
    clampRGB(rgb)
      .map(ea => leftPad(Math.round(ea).toString(16).toUpperCase(), 2, "0"))
      .join("")
  )
}

export function clampRGB(rgb: Triplet): Triplet {
  return (
    // the [0], [1], [2] allows rgb to be a Buffer.slice:
    [rgb[0], rgb[1], rgb[2]]
      // clamp values to expected reasonable [0, 255] range:
      .map(ea => clamp(0, 255, Math.round(ea))) as Triplet
  )
}
