import os from "os"
import { lazy } from "../../core/Lazy"
import { minuteMs } from "../../fe/Date"
import { clamp } from "../../fe/Number"
import { mkLogger } from "../Logger"

const logger = lazy(() => mkLogger("work.CpuInfo"))

// cpus() can take 500ms!
export const cpuInfo = lazy(() => os.cpus(), 5 * minuteMs)

export const cpuCount = lazy(() => {
  try {
    const availableParallelism = os.availableParallelism?.()
    const cpuInfoLength = cpuInfo().length
    return logger().tap({
      msg: "cpuCount()",
      result: clamp(1, 128, Math.ceil(availableParallelism ?? cpuInfoLength)),
      meta: {
        availableParallelism,
        cpuInfoLength
      }
    })
  } catch (error) {
    logger().warn("availableParallelism()/cpuCount() failed", { error })
    return 1
  }
})

// Don't look at cgroup info--the algorithm for determining what "1024" means
// in cpu.shares relies on a sum of all cgroup cpu.shares values, and we don't
// have access to that within docker. See
// https://bugs.openjdk.org/browse/JDK-8281181
