import { Tags } from "exiftool-vendored"
import { lazy } from "../../core/Lazy"
import { firstNotBlank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0, toFloat, toInt } from "../../fe/Number"
import { reqValuedOrElse } from "../../fe/Object"
import { ExposureSettings } from "../../fe/api/ExposureSettings"
import { toS } from "../../fe/toS"
import { mkLogger } from "../Logger"
import { firstNonZero } from "../Number"

const logger = lazy(() => mkLogger("tags.ExposureSettings"))

export function extractExposureSettings(t: Tags): Maybe<ExposureSettings> {
  const exposureSettings = {
    focalLength: t.FocalLength,
    iso: firstNonZero(t.ISO, t.ISOSpeed, t.SonyISO),
    aperture: firstNonZero(
      t.FNumber,
      t.Fnumber,
      t.ApertureValue,
      t.Aperture,
      t.SonyFNumber
    ),
    // firstNotBlank rejects numbers, so we have to cast to string:
    shutterSpeed: firstNotBlank(
      toS(t.ExposureTime),
      toS(t.ShutterSpeed),
      toS(t.ShutterSpeedValue),
      toS(t.SonyExposureTime)
    )
  }
  logger().debug("extracted from " + t.FileName, { exposureSettings })
  return reqValuedOrElse(exposureSettings)
}

export function exposureTimeToFloat(i: number | string) {
  if (gt0(i)) return i
  // Handle 1/100 or 1/32.4
  const [j, k] = toS(i)
    .split("/")
    .map(ea => toInt(ea))
  return gt0(j) && gt0(k) ? j / k : undefined
}

export function focalLengthToInt(i: number | string) {
  const f = toFloat(i)
  return gt0(f) ? Math.round(f) : undefined
}
