import { pidExists } from "batch-cluster"
import { spawn } from "child_process"
import { Command } from "commander"
import p from "process"
import { killPid } from "../../core/Pids"
import { childEnv } from "../../core/child/ChildEnv"
import { CommandPlugin } from "../../core/cli/CLI"
import { BaseFile } from "../../core/fs/BaseFile"
import { Settings } from "../../core/settings/Settings"
import { readSystemSettings } from "../../core/settings/SettingsIO"
import { mapNotBlank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { gt0, toInt } from "../../fe/Number"
import { Obj } from "../../fe/Object"

export const DaemonArgs: CommandPlugin = {
  beforeParse: (cmd: Command) =>
    cmd
      .option("-p, --pidfile PIDFILE", `Write a pidfile of the main process.`)
      .option(
        "--stop",
        "Shutdown daemonized PhotoStructure. Requires --pidfile"
      )
      .option(
        "-d, --daemon",
        "Run PhotoStructure as a daemon. Enabled by default when --pidfile is provided."
      ),
  afterParse: async (opts: Obj) => {
    mapNotBlank(opts.pidfile, ea => (Settings.pidFile.envValue = ea))

    if (isTrue(opts.stop)) {
      if (!Settings.pidFile.hasValue()) {
        await readSystemSettings()
        if (!Settings.pidFile.hasValue()) {
          console.error("--stop requires --pidfile to be set.")
          return p.exit(1)
        }
      }
      const f = BaseFile.for(Settings.pidFile.value!)
      if (await f.notExists()) {
        console.error("Pidfile " + f + " does not exist.")
        return p.exit(1)
      }
      const pid = toInt(await f.readTextFile())
      if (!gt0(pid)) {
        console.error("Pidfile " + f + " is invalid.")
        return p.exit(1)
      }
      if (!pidExists(pid)) {
        console.error("Pid " + pid + " does not exist.")
        return p.exit(1)
      }

      await killPid(pid)
      return p.exit(0)
    }

    const daemonize =
      !isTrue(p.env.__is_daemon) &&
      (isTrue(opts.daemon) || Settings.pidFile.hasValue())

    if (daemonize) {
      const idx = p.argv.findIndex(
        ea => !ea.includes("npx") && !ea.includes("node")
      )
      const args = p.argv.slice(idx === -1 ? 0 : idx)
      const command = process.execPath
      const env = { ...childEnv(), __is_daemon: "1" }
      // const out = openSync("./out.log", "a")
      // const err = openSync("./err.log", "a")
      const child = spawn(command, args, {
        env,
        detached: true,
        shell: true // < cmd might be a shebanged script
      })
      child.unref()
      console.log("Daemonized PhotoStructure.", {
        pid: child.pid,
        args
      })
      return p.exit(0)
    }
    return
  }
}
