import { Database, Statement } from "better-sqlite3"
import { randomInt } from "crypto"
import { lazy } from "../../core/Lazy"
import { Logger, mkLogger } from "../../core/Logger"
import { isProd } from "../../core/NodeEnv"
import { gitDate } from "../../core/Version"
import { thenOrTimeout } from "../../core/async/ThenOrTimeout"
import { Schema } from "../../core/db/Schemas"
import { getDevEnvFlag } from "../../core/env/DevEnvFlags"
import { InternalErrorFlag } from "../../core/error/ErrorFlags"
import { onError } from "../../core/error/OnError"
import { WrappedError } from "../../core/error/WrappedError"
import { ee } from "../../core/event/EventEmitter"
import { BaseFile } from "../../core/fs/BaseFile"
import { withLock_ } from "../../core/fs/FsLock"
import { PosixFile } from "../../core/fs/PosixFile"
import { ProjectPath } from "../../core/fs/ProjectPath"
import { HealthCheck } from "../../core/health/HealthCheck"
import { compactBlanks, isNotEmpty } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { minuteMs, secondMs } from "../../fe/Date"
import { delay } from "../../fe/Delay"
import { stringify } from "../../fe/JSON"
import { map } from "../../fe/Maybe"
import { lt } from "../../fe/Primitive"
import { isFunction } from "../../fe/isFunction"
import { addDbSetupError } from "./DbSetupErrors"
import { Migrations, migrationNameToDate } from "./Migrations"
import { foreignKeyCheck_ } from "./SQLite"

const CreateMigration = `
CREATE TABLE IF NOT EXISTS migrations (
  id integer NOT NULL PRIMARY KEY, 
  name varchar(255) NOT NULL, 
  migration_time integer NOT NULL 
)
`

// MAGICK NAMING CONVENTIONS:

// "_nofk" migrations are applied with foreign keys disabled.

function isDisabledForeignKeysMigration(migrationFile: BaseFile) {
  return migrationFile.name.includes("_nofk")
}

export class Migration {
  private readonly logger: Logger
  readonly migrationsDir: PosixFile
  constructor(
    readonly schema: Schema,
    readonly db: Database,
    readonly dbFile: PosixFile
  ) {
    this.logger = mkLogger(
      "db.Migration(" +
        stringify({
          schema,
          db: dbFile.nativePath
        }) +
        ")"
    )
    this.migrationsDir = PosixFile.for(ProjectPath.Migrations()).join(
      this.schema
    )
  }

  async pendingMigrationFiles_(): Promise<BaseFile[]> {
    const inDb = this.migrationsInDatabase_()
    const inFs = await this.fsMigrations_()
    return inFs.filter(ea => !inDb.includes(ea.name))
  }

  readonly fsMigrations_ = lazy(() =>
    // .children_ gives us sorted results!
    this.migrationsDir
      .children_(ea => ea.isFile && ea.basename.endsWith(".sql"))
      .catch(error =>
        this.logger.throw(
          "Failed to read migration directory, " +
            this.migrationsDir +
            InternalErrorFlag,
          { error }
        )
      )
  )

  readonly ensureMigrationTable_ = lazy(() => {
    this.db.exec(CreateMigration)
  })

  migrationsInDatabase_(): string[] {
    this.ensureMigrationTable_()
    return this.db
      .prepare("SELECT name from migrations")
      .pluck()
      .all() as string[]
  }

  readonly unknownMigrations_ = lazy(() =>
    this.migrationsInDatabase_().filter(
      ea => !lt(migrationNameToDate(ea), gitDate)
    )
  )

  readonly assertKnownMigrations_ = lazy(() => {
    // If any migrations are from after this version was built, the library is
    // from a newer version.
    const unknownMigrations = this.unknownMigrations_()
    if (isNotEmpty(unknownMigrations)) {
      const err = new WrappedError(
        "Please upgrade PhotoStructure to a newer version to open this library: Unknown migrations: " +
          unknownMigrations.join(",")
      )
      onError(err)
      throw err
    }
  })

  async apply_(onBeforeMigrate?: (migrationFile: BaseFile) => Promise<any>) {
    return withLock_(
      {
        file: this.dbFile.sibling("migrate"),
        timeoutMs: 2 * minuteMs
      },
      () => this.#apply(onBeforeMigrate)
    )
  }

  async #apply(
    onBeforeMigrate?: (migrationFile: BaseFile) => Promise<any>
  ): Promise<string[]> {
    try {
      this.ensureMigrationTable_()

      if (isProd) this.assertKnownMigrations_()

      const appliedMigrations: string[] = []

      const p = this.db.prepare(
        "INSERT INTO migrations (name, migration_time) VALUES (?, ?)"
      )

      for (const migrationFile of await this.pendingMigrationFiles_()) {
        await HealthCheck.traceLater_(
          "Upgrading database (" + migrationFile.name + ")",
          async () => {
            if (getDevEnvFlag("PS_SLOMO")) {
              // prove that splash is showing migrations:
              await delay(randomInt(secondMs, 3 * secondMs))
            }
            await map(onBeforeMigrate, ea => ea(migrationFile))
            await thenOrTimeout(
              this.applyMigration_(migrationFile, p),
              minuteMs,
              () => {
                throw new WrappedError(
                  "Migration " + migrationFile.name + " timed out.",
                  {
                    fatal: true,
                    ignorable: false
                  }
                )
              }
            )
          }
        )
        appliedMigrations.push(migrationFile.base)
      }

      this.logger.info("Migration complete", { appliedMigrations })
      return appliedMigrations
    } catch (cause: any) {
      const err = new WrappedError(
        "Migrating the " + this.schema + " db failed",
        { cause }
      )
      addDbSetupError(err)
      HealthCheck.addError(err)
      throw err
    }
  }

  //
  // Migrations that name-collide with a function in the Migrations namespace
  // assume to be a javascript-based migration, rather than a SQL migration.
  //
  async applyMigration_(f: BaseFile, addMigrationStmt: Statement) {
    try {
      const start = Date.now()
      const m = (Migrations as any)[f.name.replace(/^[\d_-]+/, "")]
      const codeMigration = isFunction(m)
      if (codeMigration) {
        await m.bind(Migrations)(this.db)
      } else {
        await this.#applySqlMigration_(f)
      }
      this.logger.debug("applyMigration() completed", {
        elapsedMs: Date.now() - start,
        codeMigration,
        migration: f.baseWithParent
      })
      return addMigrationStmt.run(f.name, Date.now())
    } catch (error) {
      return this.logger.throw(
        "Failed to apply migration " + f.baseWithParent,
        { error }
      )
    } finally {
      ee().emit("clearDbCache")
    }
  }

  async #applySqlMigration_(f: BaseFile) {
    const disableForeignKeys = isDisabledForeignKeysMigration(f)
    try {
      const sql = await f.readTextFile()
      if (blank(sql)) {
        this.logger.error("Empty migration: " + f)
        return
      }
      if (disableForeignKeys) {
        // See https://sqlite.org/lang_altertable.html
        this.db.pragma("foreign_keys = OFF")
      }

      // We don't run these inside a transaction, because SQLite doesn't respect
      // boundaries:

      // split DDL into statements to highlight what part was bad:
      for (const ea of compactBlanks(sql.split(";"))) {
        try {
          this.db.exec(ea)
        } catch (cause) {
          throw new WrappedError(`Failed to apply: ${ea}`, {
            cause,
            fatal: true
          })
        }
      }
      if (disableForeignKeys) {
        foreignKeyCheck_(this.db)
      }
    } finally {
      if (disableForeignKeys) {
        this.db.pragma("foreign_keys = ON")
      }
    }
  }
}
