import { keys } from "../../fe/Object"
import { equalsIgnoreCase } from "../String"
import { env } from "../env/Env"
import { IgnorableEnvKeys } from "../env/EnvKeys"
import { ciSettings } from "./Settings"

export function verifyPsEnvSettings(
  e = env(),
  minDiceCoeff = 0.25
): { envKey: string; msg: string }[] {
  const result = []
  for (const envKey of keys(e).filter(ea => {
    const key = ea.toUpperCase()
    return key.startsWith("PS_") && !IgnorableEnvKeys.includes(key)
  })) {
    const envValue: any = e[envKey]
    const exact = ciSettings().lookup(envKey)
    if (exact != null) {
      // verify that the value will be used:
      const parsed = exact.value.opts.fromEnv(envValue)
      if (parsed == null) {
        result.push({
          envKey,
          msg: `The value "${e[envKey]}" isn't valid for this ${exact.value.constructor.name}.`
        })
      }
    } else {
      const closest = ciSettings().lookupNearest(envKey, minDiceCoeff)
      if (closest == null) {
        result.push({ envKey, msg: `No setting was found like "${envKey}"` })
      } else if (!equalsIgnoreCase(closest.key, envKey)) {
        result.push({
          envKey,
          msg: `Did you mean setting "${closest.value.key}"?`
        })
      }
    }
  }
  return result
}
