import { Database } from "better-sqlite3"
import { mkdb_ } from "../../core/db/MkDb"
import { BaseFile } from "../../core/fs/BaseFile"
import { Settings } from "../../core/settings/Settings"
import { toS } from "../../fe/toS"

/**
 * Should only be used by tests
 */
export function withDbSync<T>(
  dbfile: BaseFile | string,
  f: (db: Database) => T,
  timeoutMs = Settings.dbBusyTimeoutMs.valueOrDefault
): T {
  const d = mkdb_({ nativePath: toS(dbfile), timeoutMs })
  try {
    return f(d)
  } finally {
    d.close()
  }
}

/**
 * Should only be used by tests
 */
export async function withDb<T>(
  dbfile: BaseFile | string,
  f: (db: Database) => Promise<T>,
  timeoutMs = Settings.dbBusyTimeoutMs.valueOrDefault
): Promise<T> {
  const d = mkdb_({ nativePath: toS(dbfile), timeoutMs })
  try {
    return await f(d)
  } finally {
    d.close()
  }
}
