import cluster from "cluster"
import process from "process"
import { addDefaultJsonRevivers } from "../core/JsonSetup"
import { lazy } from "../core/Lazy"
import { logStartup } from "../core/LogStartup"
import { Logger, mkLogger } from "../core/Logger"
import { setProcessTitle } from "../core/ProcessTitle"
import {
  isLibraryRequiredService,
  isMainService,
  isServiceThatAcceptsFatalErrors
} from "../core/ServiceNames"
import { setServiceName } from "../core/SetServiceName"
import { stdoutWrite } from "../core/StdoutWrite"
import { version } from "../core/Version"
import { ending } from "../core/async/Endable"
import { exit } from "../core/async/Exit"
import { PromiseTimer, time } from "../core/async/PromiseTimer"
import { Promises } from "../core/async/Promises"
import { ChildServiceExitCommand } from "../core/child/ChildServiceExitCommand"
import { setupLibraryDirs_ } from "../core/dir/LibraryDirs"
import { getEnv } from "../core/env/GetEnv"
import { errorToS } from "../core/error/Error"
import { FatalErrorFlag } from "../core/error/ErrorFlags"
import { addErrorFlags } from "../core/error/ErrorTypes"
import { onError } from "../core/error/OnError"
import { WrappedError } from "../core/error/WrappedError"
import { ee } from "../core/event/EventEmitter"
import { LineReader } from "../core/fs/LineReader"
import { HealthCheck } from "../core/health/HealthCheck"
import { mergeUserDataDirs_ } from "../core/settings/MergeConfigDirs"
import { Settings } from "../core/settings/Settings"
import { setSettingsDefaults } from "../core/settings/SettingsDefaults"
import { readSettings } from "../core/settings/SettingsIO"
import { loadCuss } from "../core/words/Cuss"
import { pause, resume } from "../core/work/IsPaused"
import { mapNotBlank } from "../fe/Blank"
import { secondMs } from "../fe/Date"
import { later } from "../fe/Delay"
import { Latch } from "../fe/Latch"
import { map } from "../fe/Maybe"
import { ServiceName } from "../fe/ServiceNames"
import { toErr } from "../fe/toErr"
import { toS } from "../fe/toS"
import { installSentry } from "./SentrySetup"

export const setupEventHandlers = lazy(() => {
  // listen for timing information:
  PromiseTimer.instance()
  ee().on("resume", () => resume())
  ee().on("pause", () => pause())
  ee().on("exit", () => exit({ reason: "exit() event", status: 0 }))
})

export interface ServiceOptions {
  name: ServiceName
  stdinReceiver?: (stdin: string) => any
  maxErrorsPerMinute?: number
}

/**
 * Framework for creating services that accept async commands from stdin and
 * emit results to stdout. Every process will have a Service instance.
 *
 * The initial service will only be the webservice, propped up in "libraryless"
 * mode.
 *
 * The constructor doesn't take an interface with ready and shutdown fields
 * because the services depend on the library that this service creates, which
 * creates a circular dependency.
 */
export class Service {
  readonly name: ServiceName
  readonly #logger: Logger
  readonly #ready = new Latch()
  readonly promises: Promises
  #exitted = false

  private readonly inputHandlers = new Map<string, (input: string) => any>()

  constructor(readonly opts: ServiceOptions) {
    this.name = this.opts.name

    // This must not be called by the billing service--it requires ExifTool.
    setSettingsDefaults()

    // TODO: FIXME: SITS: service name is being set in both CLI and here:
    setServiceName(this.name)
    this.#logger = mkLogger("Service(" + this.name + ")")

    this.addDefaultInputHandlers()

    // NOTE: if we re-use the ChunkedWorkRunner's promises instance, we wedge
    // randomly :\
    this.promises = new Promises(this.name)

    // By pushing the setup job onto this.jobs, we get two things:
    // 1) exit() can wait for setup()
    // 2) onLine() will implicitly wait for setup.

    // Note also: we're not enqueuing: we're pushing: no need to wait for anything!
    void this.promises.push("Service.setup()", () => this.setup())
  }

  get ready(): Promise<void> {
    return this.#ready.promise
  }

  get isReady(): boolean {
    return this.#ready.isResolved()
  }

  get exitted() {
    return this.#exitted
  }

  readonly setup = lazy<Promise<void>>(() =>
    time("Service.setup()", () => this.#setup())
  )

  async #setup() {
    const canContinue = () => !this.#exitted && !ending()

    try {
      // set up crash handlers
      {
        // Test crash handler (see https://gitlab.com/photostructure/photostructure/issues/163)
        mapNotBlank(getEnv(("PS_FATAL_" + this.name) as any), message => {
          throw new WrappedError(message, { fatal: true })
        })
        mapNotBlank(getEnv(("PS_CRASH_" + this.name) as any), message => {
          later(() => {
            throw new WrappedError(message)
          }, 5 * secondMs)
        })
      }

      setProcessTitle()

      addDefaultJsonRevivers()

      if (isMainService()) {
        await mergeUserDataDirs_()
      }

      await readSettings()

      // set up error handling as early as we can, but only after we've read the
      // library settings:
      await this.#setupErrorHandling()

      // log after we've read settings:
      logStartup()

      setupEventHandlers()

      if (isLibraryRequiredService()) {
        await setupLibraryDirs_()
      }

      // run in background
      void loadCuss()

      this.#stdinReadline()

      if (Settings.startPaused.valueOrDefault) pause()

      const reject = !canContinue()
      if (reject) {
        void this.#ready.reject()
      } else {
        void this.#ready.resolve()
      }
    } catch (err) {
      console.error(errorToS(err))
      void this.#ready.reject(toErr(err)!)
      // We couldn't start up. No need to wait for jobs:
      void exit({
        reason: addErrorFlags(
          this.name + " setup failed: " + errorToS(err),
          FatalErrorFlag
        ),
        status: 14
      })
    }
  }

  async #setupErrorHandling() {
    if (isServiceThatAcceptsFatalErrors()) {
      ee().on("fatal", error => exit({ error, status: 12 }))
    }

    process.on("unhandledRejection", err =>
      map(err, ea => onError("unhandledRejection", ea as any))
    )
    process.on("uncaughtException", err =>
      map(err, ea => onError("uncaughtException", ea))
    )

    process.on("SIGINT", () => exit({ reason: "SIGINT", status: 0 }))
    process.on("SIGTERM", () => exit({ reason: "SIGTERM", status: 0 }))
    // If the Node.js process is spawned with an IPC channel (see the Child
    // Process and Cluster documentation), the 'disconnect' event will be
    // emitted when the IPC channel is closed.
    if (cluster.isWorker) {
      process.on("disconnect", () => exit({ reason: "disconnect", status: 0 }))
    }

    // 20220425 don't bother listening to stdout/stdin/stderr disconnections!

    await installSentry(this)

    return
  }

  readonly #stdinReadline = lazy(() => {
    const rl = process.stdin.pipe(new LineReader())
    rl.on("data", line => this.onLine(toS(line)))
    return rl
  })

  setInputHandler(command: string, f: (input: string) => any) {
    this.inputHandlers.set(command.trim().toLowerCase(), f)
  }

  private addDefaultInputHandlers() {
    this.setInputHandler("--version", () => stdoutWrite({ version }))
    {
      // if the commands are via the stdin, it's safe to assume they want the
      // jobs to complete, so the following should waitForJobs:
      this.setInputHandler(ChildServiceExitCommand, () =>
        // later this so we aren't considered a job to wait for (which causes exit() to hang):
        later(() =>
          exit({
            reason: ChildServiceExitCommand + " from stdin",
            status: 0
          })
        )
      )
    }

    this.setInputHandler("--times", () =>
      stdoutWrite(PromiseTimer.instance().report())
    )

    this.setInputHandler("--health", () => stdoutWrite(HealthCheck.summary()))
  }

  // This shouldn't be async, so this.stdinReadline().pause() can take effect.
  private onLine(line: string) {
    this.#logger.debug("onLine()", { line, ending: this.#exitted || ending() })
    return this.promises.serial("Service.onLine()", async () => {
      await this.setup()
      if (line.startsWith("--")) {
        const cmd = line.split(" ", 1)[0]
        const f = this.inputHandlers.get(cmd)
        if (f == null) {
          this.#logger.error("onLine(): unknown command", {
            line,
            knownCommands: [...this.inputHandlers.keys()]
          })
          console.warn("unknown command " + line)
        } else {
          await f(line.slice(cmd.length).trim())
        }
      } else {
        try {
          await map(this.opts.stdinReceiver, f => f(line))
        } catch (error) {
          this.#logger.error("onLine(): failed to process", { line, error })
        }
      }
      return
    })
  }
}
