import { lazy } from "../../core/Lazy"
import { dayMs, secondMs } from "../../fe/Date"
import { fmtDuration } from "../../fe/FmtDuration"
import { map } from "../../fe/Maybe"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { within } from "../../fe/Number"
import { Obj, omit } from "../../fe/Object"
import { toA } from "../../fe/toA"
import { toS } from "../../fe/toS"
import { mkLogger } from "../Logger"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { shortStringSha } from "../StringHash"
import { ending } from "../async/Endable"
import { configDir } from "../dir/ConfigDir"
import { PleaseSendErrorFlag } from "../error/ErrorFlags"
import { isJsonExt } from "../fs/FileExt"
import { isHiddenBasename } from "../fs/Path"
import { PosixFile } from "../fs/PosixFile"
import { GeoRadix } from "../math/Radix"
import { Settings } from "../settings/Settings"

const logger = lazy(() => mkLogger("error.EventStore"))

export const ExtraEventsForPlease = 7

export type StoredError = Obj & {
  message: string
  timestamp: number // UNIX TIMESTAMP, not millis!
}

function event2log(event: StoredError) {
  // Skip breadcrumbs (all recent logs) because they are enormous.
  return omit(event, "breadcrumbs")
}

/**
 * We don't want the same event spammed N-thousand times from a given host. Send
 * the first, throttle the next N for the next time period.
 *
 * Everything is FS based (rather than using sqlite) to minimize deps and have
 * fewer moving parts.
 *
 * Files are placed in YYYY/MM/DD subdirectories so the current day's events are
 * in one place.
 */
export class ErrorStore<T extends StoredError> {
  static readonly instance = lazy(() =>
    map(configDir(), ea => new ErrorStore(PosixFile.for(ea).join("events")))
  )

  constructor(readonly root: PosixFile) {}

  // we only want to send the same message at most once a month per user.
  datedRoot(d = new Date()) {
    return this.root.join(toS(d.getFullYear()), toS(d.getMonth() + 1))
  }

  rmrf() {
    return this.root.rmrf()
  }

  msg2file(e: StoredError): PosixFile {
    return this.datedRoot(new Date(e.timestamp * secondMs)).join(
      shortStringSha(e.message, 8, GeoRadix) + ".json"
    )
  }

  async eventsFrom(when = new Date(), deltaMs = dayMs) {
    const at = when.getTime()
    const lt = at - deltaMs
    const gt = at + deltaMs
    return logger().tap({
      msg: "eventsFrom()",
      result: toA(
        await this.datedRoot(when)
          .clear()
          .childFiles(async ea =>
            logger().tap({
              msg: "eventsFrom().childFiles() accept " + ea.base,
              result:
                isJsonExt(ea) &&
                !isHiddenBasename(ea.base) && // no WIPs
                within(lt, gt, await ea.mtimeMs())
            })
          )
      ),
      meta: { when, deltaMs: fmtDuration(deltaMs) }
    })
  }

  async eventCount(when = new Date()) {
    const arr = await this.eventsFrom(when)
    return arr.length
  }

  async eventQuotaExceeded(event: T | any) {
    const pleaseSend =
      toS(event).includes(PleaseSendErrorFlag) ||
      toS(event.message).includes(PleaseSendErrorFlag)
    const recentEventCount = await this.eventCount()
    const maxErrorsPerDay =
      Settings.maxErrorsPerDay.valueOrDefault +
      (pleaseSend ? ExtraEventsForPlease : 0)
    return logger().tap({
      msg: "eventQuotaExceeded()",
      result: recentEventCount >= maxErrorsPerDay,
      meta: {
        event: event2log(event),
        recentEventCount,
        maxErrorsPerDay,
        pleaseSend
      }
    })
  }

  async maybeSendEvent(event: T): PromiseMaybe<T> {
    event.timestamp = Math.floor(event.timestamp)
    if (ending()) {
      logger().error("maybeSendEvent(): REJECT: we're ending.", {
        event: event2log(event)
      })
      return
    }
    if (await this.eventQuotaExceeded(event)) {
      logger().error("maybeSendEvent(): REJECT: too many recent events.", {
        event: event2log(event)
      })
      return
    }
    try {
      const eventFile = await this.writeEvent_(event)
      return logger().tap({
        msg:
          "maybeSendEvent(): " + eventFile == null
            ? "REJECT: previously sent this event already this month"
            : "ACCEPT",
        result: eventFile != null ? event : undefined,
        meta: { eventFile }
      })
    } catch (err) {
      logger().error("maybeSendEvent(): REJECT: failed to write event.", err)
      return
    }
  }

  /**
   * Only send one of the same event
   */
  writeEvent_(event: T) {
    const returnUndefinedIfNotEmpty = true
    return this.msg2file(event).applyIfEmpty_({
      fn_: ea => ea.writeJson_(event),
      minSizeBytes: 3,
      returnUndefinedIfNotEmpty,
      timeoutMs: ShortCommandTimeoutMs
    })
  }
}
