import { lazy } from "../../core/Lazy"
import { Dimensions } from "../../fe/Dimensions"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { toGt0 } from "../../fe/Number"
import { FifoCacheAsync } from "../FifoCacheAsync"
import { mkLogger } from "../Logger"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { stdout_ } from "../child/ChildProcess"
import { ee } from "../event/EventEmitter"
import { PosixFile } from "../fs/PosixFile"
import { statTimeoutMs } from "../fs/StatTimeout"
import { rawIdentifyNativePath_ } from "../fs/Tools"

const logger = lazy(() => mkLogger("img.RawInfo"))

export type RawInfo = {
  Filename: string
  Make: string
  Model: string
  ImageSize: Dimensions
}

const cache = lazy(
  () =>
    new FifoCacheAsync<RawInfo>({
      maxSize: 512,
      name: "img.RawInfoCache",
      timeoutMs: ShortCommandTimeoutMs
    })
)

ee().on("clearCache", () => cache.prior()?.clear())
ee().on("fileChanged", ea =>
  ea == null ? cache.prior()?.clear() : cache.prior()?.delete(ea)
)

export async function rawInfo(src: PosixFile): PromiseMaybe<RawInfo> {
  return rawInfo_(src).catch(error => {
    logger().warn("rawInfo() failed", { error, src })
    return undefined
  })
}

export async function rawInfo_(src: PosixFile): Promise<RawInfo> {
  return cache().getOrSetAsync(src.nativePath, async () => {
    const cmd = await rawIdentifyNativePath_()
    // -u -f returns something that _isn't_ the rendered TIFF dimensions.
    return parseRawInfoOutput_(
      await stdout_(cmd, ["-s", src.nativePath], {
        timeoutMs: statTimeoutMs()
      })
    )
  })
}

export function parseRawInfoOutput_(input: string): RawInfo {
  const [Filename, Make, Model, w, h] = input.split("\t")
  const width = toGt0(w)
  const height = toGt0(h)
  if (width == null || height == null) {
    return logger().throw("parseRawInfoOutput_ failed (missing dimensions)", {
      cause: input
    })
  } else {
    return logger().tap({
      msg: "parseRawInfoOutput()",
      result: {
        Filename,
        Make,
        Model,
        ImageSize: { width, height }
      },
      meta: { input }
    })
  }
}
