const logger = lazy(() => mkLogger("db.SQLite"))

/**
 * Feed the given commands to the sqlite binary tool
 */
export async function sqlite({
  dbFile,
  sql,
  args
}: {
  dbFile: PosixFile
  sql: string
  args?: string[]
}): Promise<string> {
  const path = await sqliteNativePath_()
  const r = await thenElapsed(
    // backups can take a while (hopefully never longer than a minute!)
    stdout_(path, [...toA(args), dbFile.nativePath, sql], {
      timeoutMs: 1 * minuteMs
    })
  )
  logger().debug(
    "sqlite(" + sql + ") on " + dbFile + " in " + r.elapsedMs + "ms",
    r.result
  )
  return r.result
}

export function backupDbFile_(srcDbFile: PosixFile, destDb: PosixFile) {
  return retryOnReject(() => _backupDbFile_(srcDbFile, destDb), {
    maxRetries: 3,
    retryDelay: secondMs,
    timeoutMs: minuteMs,
    errorIsRetriable: () => true
  })
}

async function _backupDbFile_(srcDbFile: PosixFile, destDb: PosixFile) {
  try {
    await destDb.parent().mkdirp_()
    await destDb.unlink("trace")
    const bytes = await srcDbFile.size()
    if (!gt0(bytes)) {
      logger().warn("backupDbFile_(): " + srcDbFile + " is empty")
      return
    }
    await modelDb()?.end()
    await withDb(
      srcDbFile,
      async db => db.exec("VACUUM INTO " + escStr(destDb.nativePath)),
      2 * minuteMs
    )
    logger().info("backupDbFile_(): Finished " + srcDbFile + " -> " + destDb)
  } catch (cause) {
    throw new WrappedError(`Could not back up db ${srcDbFile} -> ${destDb}`, {
      cause
    })
  }
}

function pragmaOrThrow_({
  db,
  pragma,
  okResult
}: {
  db: bs.Database
  pragma: string
  okResult: any
}): void {
  const result = isString(okResult)
    ? db.pragma(pragma, { simple: true })
    : db.pragma(pragma)
  if (!eql(result, okResult)) {
    throw new Error(
      `${pragma} failed for ${dbToPath(db)}: ${stringify(result)}`
    )
  }
}

function dbToPath(db: bs.Database): string {
  return ellipsizePath({ p: db.name, maxLength: 80 })
}

export function quickCheck_(db: bs.Database): void {
  return pragmaOrThrow_({ db, pragma: "quick_check", okResult: "ok" })
}

export function integrityCheck_(db: bs.Database): void {
  return pragmaOrThrow_({ db, pragma: "integrity_check", okResult: "ok" })
}

export function foreignKeyCheck_(db: bs.Database): void {
  const badRows = toA(db.pragma("foreign_key_check")) as {
    table: string
    rowid: number
  }[]

  if (badRows.length > Settings.dbInvalidFKThreshold.valueOrDefault) {
    logger().throw(
      "foreignKeyCheck_(): too many invalid foreign keys to repair",
      {
        dbInvalidForeignKeyThreshold:
          Settings.dbInvalidFKThreshold.valueOrDefault,
        badRowsCount: badRows.length,
        badRows
      }
    )
  }
  for (const row of badRows) {
    logger().warn("repairDbFile_(): deleting row with invalid foreign key", {
      row
    })
    db.prepare(`DELETE FROM ${escStr(row.table)} WHERE rowid = ?`).run(
      row.rowid
    )
  }
}

export function verifyDb_(db: bs.Database) {
  quickCheck_(db)
  integrityCheck_(db)
  foreignKeyCheck_(db)
}

/**
 * @throw if the db is not valid
 */
export function verifyDbFile_(dbFile: PosixFile | string) {
  return withDbSync(dbFile, verifyDb_, commandTimeoutMs())
}

type CheckpointResult = { busy: number; log: number; checkpointed: number }

/**
 * @see https://sqlite.org/pragma.html#pragma_wal_checkpoint
 *
 */
export async function checkpoint_({
  db,
  retries = 3,
  checkpointType = Settings.dbWalCheckpointType.valueOrDefault as CheckpointType
}: {
  db: bs.Database
  retries?: number
  checkpointType?: CheckpointType
}): PromiseMaybe<CheckpointResult> {
  checkpointType = CheckpointTypes.validOrElse(
    checkpointType,
    CheckpointTypes.AUTO
  )
  if (checkpointType === CheckpointTypes.AUTO) return
  const pragma = "wal_checkpoint(" + checkpointType + ")"
  const [result] = toA(db.pragma(pragma)) as CheckpointResult[]
  logger().info("checkpoint()", { pragma, result })
  if (gt0(result?.busy) && retries > 0) {
    await delay(250) // let things settle down?
    return checkpoint_({ db, retries: retries - 1 })
  } else {
    return result
  }
}

const IgnorableDumpErrorRe = /no such table: sqlite_\w+/i

/**
 * Dump and restore to srcDb-new, then move src to ./invalid/, and rename the
 * new db to srcDb.
 *
 * The final db will have passed verification.
 *
 * @return where srcDb was moved to
 */
export async function repairDbFile_(
  srcDbPath: SimpleFileOrString,
  op: RepairMode = RepairModes.dump
): Promise<PosixFile> {
  const srcDbFile = PosixFile.for(srcDbPath)
  const destDir = await srcDbFile
    .parent()
    .join("db-" + op, filestamp())
    .mkdirp_()
  const destDbFile = destDir.join(srcDbFile.base)
  logger().info(`repairDbFile_(${srcDbFile} -> ${destDbFile})`)
  try {
    const cmd = await sqliteNativePath_()
    // SQL dumps are written as SQL INSERTs, which are < 2x the size of the
    // db. 4x is certainly sufficient.
    const maxBuffer = Math.min(20 * MiB, await sqliteSizeBytes(srcDbFile)) * 4
    const obs = new PushProgressObserver(
      { path: toS(srcDbFile), op: "Repairing your database" },
      maxBuffer
    )
    const l = new Latch()

    const dumpProc = execFile(
      cmd,
      [srcDbFile.nativePath, "." + op],
      5 * minuteMs,
      {
        encoding: "buffer",
        maxBuffer
      }
    )
    dumpProc.stdout!.on("error", cause => {
      const err = toS(cause)
      if (IgnorableDumpErrorRe.test(err)) {
        logger().info("repairDbFile_(): ignoring stderr", { err })
      } else {
        void l.reject(new Error("sqlite " + op + " failed: " + err))
      }
    })
    const load = execFile(cmd, [destDbFile.nativePath], 5 * minuteMs, {
      encoding: "buffer",
      maxBuffer
    })
    load.on("exit", () => l.resolve())
    dumpProc.stdout!.on("end", () => {
      load.stdin!.end(null)
    })
    dumpProc.stdout!.on("data", buf => {
      const len = (buf as string | Buffer).length
      logger().info("repairDbFile_(): dump", { len })
      obs.incrProgress(len)
      load.stdin!.write(buf)
    })

    await l
    logger().info(`repairDbFile_(): recovered to ${destDbFile}.`)

    verifyDbFile_(destDbFile)

    logger().info(`repairDbFile_(): ${destDbFile} is valid, finishing repair.`)
    const result = await thenCollect(sqliteFiles(srcDbFile), ea =>
      ea.renameYMDHMS_({ subdir: "needed-repair" })
    )
    await thenCollect(sqliteFiles(destDbFile), ea => ea.mv_(srcDbFile.parent()))

    return result.find(ea => ea.ext === ".db" || ea.ext.startsWith(".sqlite"))!
  } catch (error) {
    return logger().throw(
      "failed to recover DB via dump and restore. Please see https://photostructure.com/faq/restore-db-from-backup/ " +
        FatalErrorFlag,
      { error }
    )
  }
}

export function escStr(s: DbValue): string {
  return `'${toS(s).replace(/'/g, "''")}'`
}
