import { ServiceName } from "../../fe/ServiceNames"
import { isSyncService } from "../ServiceNames"
import { Settings } from "../settings/Settings"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"

export function serviceExitTimeoutMs(name?: ServiceName) {
  return isSyncService(name)
    ? Settings.syncExitTimeoutMs.valueOrDefault
    : ShortCommandTimeoutMs
}
