import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { Dated } from "./Dated"
import { hasTime } from "./HasTime"

export function getYear(d: Maybe<Dated>): Maybe<number> {
  return d instanceof Date ? d.getFullYear() : d?.year
}

/**
 * @return 1-12, like normal humans
 */
export function getMonth(d: Maybe<Dated>): Maybe<number> {
  return d instanceof Date ? d.getMonth() + 1 : d?.month
}

export function getDay(d: Maybe<Dated>): Maybe<number> {
  return d instanceof Date ? d.getDate() : d?.day
}

export function getHour(d: Maybe<Dated>): Maybe<number> {
  return d instanceof Date ? d.getHours() : (d as any)?.hour
}

export function getMinute(d: Maybe<Dated>): Maybe<number> {
  return d instanceof Date ? d.getMinutes() : (d as any)?.minute
}

export function getSecond(d: Maybe<Dated>): Maybe<number> {
  return d instanceof Date ? d.getSeconds() : (d as any)?.second
}

export function getMillisecond(d: Maybe<Dated>): Maybe<number> {
  return d instanceof Date ? d.getMilliseconds() : (d as any)?.millisecond
}

export function getCentisecond(d: Maybe<Dated>): Maybe<number> {
  // NOTE: we floor here rather than rounding as we don't want to deal with
  // rounding up to a different minute or hour (or day or year)
  // HONEST IT'S OK IT'S ONLY +/- 5 milliseconds.
  return map(getMillisecond(d), ea => Math.floor(ea / 10))
}

export function hasSeconds(d: Maybe<Dated>): boolean {
  return (
    hasTime(d) &&
    (gt0(getMinute(d)) || gt0(getSecond(d)) || gt0(getMillisecond(d)))
  )
}
/**
 * ".###" or ".#####" from the milliseconds in `d`.
 */

export function getSecMs(d: Maybe<Dated>): Maybe<string> {
  return map(getSecond(d), sec => {
    const ms = getMillisecond(d) ?? 0
    let s = (sec + ms / 1000).toString()
    if (sec < 10) s = "0" + s
    if (ms === 0) s = s + "."
    while (s.length < 6) {
      s = s + "0"
    }
    return s
  })
}
