import { li, tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { plur } from "../../fe/Units"
import { allSettings } from "../settings/Settings"
import { verifyPsEnvSettings } from "../settings/VerifyPsEnvSettings"
import { HealthCheck } from "./HealthCheck"

export const settingsEnvHealthCheck = defer(() =>
  HealthCheck.for({
    section: "Library",
    id: "settings-env",
    pendingMsg: "Checking environment settings",
    settings: [],
    rejectIsWarn: true,
    links: [
      {
        text: "Read about PhotoStructure environment settings",
        url: "https://photostructure.com/faq/environment-variables/",
        icon: "docs"
      }
    ],

    later: async () => {
      const overrides: string[] = []
      for (const ea of allSettings()) {
        if (ea.hasValue() && ea._envValue() != null) {
          const v =
            ea.opts.sensitive === true
              ? "********"
              : ea.toEnvValue(ea._envValue())
          if (v != null) overrides.push(tt(ea.key + "=" + v))
        }
      }
      const msg = [plur(overrides.length, "environment setting"), li(overrides)]
      const warnings = verifyPsEnvSettings()
      if (warnings.length > 0) {
        return {
          level: "warn",
          msg: [
            "Some environment settings may be misconfigured:",
            li(warnings.map(ea => tt(ea.envKey) + ": " + ea.msg)),
            "---",
            ...msg
          ]
        }
      }
      return {
        level: "ok",
        msg
      }
    }
  })
)
