import sharp from "sharp"
import { flatten } from "../../fe/Flatten"
import { rgb2labArray } from "../color/Colorspace"

// From https://github.com/btd/sharp-phash/blob/master/index.js

const SAMPLE_SIZE = 32

function initSQRT(len: number) {
  const c = new Array(len)
  for (let i = 1; i < len; i++) {
    c[i] = 1
  }
  c[0] = 1 / Math.sqrt(2.0)
  return c
}

const SQRT = initSQRT(SAMPLE_SIZE)

function initCOS(len: number) {
  const cosines = new Array(len)
  for (let k = 0; k < len; k++) {
    cosines[k] = new Array(len)
    for (let n = 0; n < len; n++) {
      cosines[k][n] = Math.cos(((2 * k + 1) / (2 * len)) * n * Math.PI)
    }
  }
  return cosines
}

const COS = initCOS(SAMPLE_SIZE)

function applyDCT(matrix: number[][], size: number) {
  const N = size

  const F = new Array(N)
  for (let u = 0; u < N; u++) {
    F[u] = new Array(N)
    for (let v = 0; v < N; v++) {
      let sum = 0
      for (let i = 0; i < N; i++) {
        for (let j = 0; j < N; j++) {
          sum += COS[i][u] * COS[j][v] * matrix[i][j]
        }
      }
      sum *= (SQRT[u] * SQRT[v]) / 4
      F[u][v] = sum
    }
  }
  return F
}

const LOW_SIZE = 8

export async function dctHash(img: sharp.Sharp): Promise<(0 | 1)[]> {
  return dctPixels(
    await img
      .greyscale()
      .resize({ width: SAMPLE_SIZE, height: SAMPLE_SIZE, fit: sharp.fit.fill })
      .raw()
      .toBuffer()
  )
}

export async function dctHashCIELAB(img: sharp.Sharp): Promise<(0 | 1)[]> {
  const rgb = await img
    .resize({ width: SAMPLE_SIZE, height: SAMPLE_SIZE, fit: sharp.fit.fill })
    .raw()
    .toBuffer()
  const lab = rgb2labArray(rgb)
  return flatten(lab.map(dctPixels))
}

export function dctPixels(pixels: Buffer | number[]): (0 | 1)[] {
  // copy signal
  const s: number[][] = new Array(SAMPLE_SIZE)
  for (let x = 0; x < SAMPLE_SIZE; x++) {
    s[x] = new Array(SAMPLE_SIZE)
    for (let y = 0; y < SAMPLE_SIZE; y++) {
      s[x][y] = pixels[SAMPLE_SIZE * y + x]
    }
  }

  // apply 2D DCT II
  const dct = applyDCT(s, SAMPLE_SIZE)

  // get AVG on high frequencies
  let totalSum = 0
  for (let x = 0; x < LOW_SIZE; x++) {
    for (let y = 0; y < LOW_SIZE; y++) {
      totalSum += dct[x + 1][y + 1]
    }
  }

  const avg = totalSum / (LOW_SIZE * LOW_SIZE)

  // compute hash
  const result: (0 | 1)[] = [] // not new Array(): we're just using push().

  for (let x = 0; x < LOW_SIZE; x++) {
    for (let y = 0; y < LOW_SIZE; y++) {
      result.push(dct[x + 1][y + 1] > avg ? 1 : 0)
    }
  }

  return result
}
