import { end, Endable, EndableRanks, ending } from "../core/async/Endable"
import { EndableWrapper } from "../core/async/EndableWrapper"
import { exit } from "../core/async/Exit"
import { setupLuxon } from "../core/date/SetupLuxon"
import { pathToDb } from "../core/db/Schemas"
import {
  libraryDataDirPosixFile,
  libraryOriginalsDirPosixFile,
  libraryPreviewsDirPosixFile,
  setupLibraryDirs_
} from "../core/dir/LibraryDirs"
import { FatalErrorFlag } from "../core/error/ErrorFlags"
import { WrappedError } from "../core/error/WrappedError"
import { BaseFile } from "../core/fs/BaseFile"
import { nativePathsEqlSync } from "../core/fs/NativePathsEql"
import { PosixFile } from "../core/fs/PosixFile"
import { HealthCheck } from "../core/health/HealthCheck"
import { imageCacheDir_ } from "../core/img/ImageCache"
import { Previews } from "../core/img/Previews"
import { lazy } from "../core/Lazy"
import {
  isDbService,
  isStatsDbService,
  isSyncService
} from "../core/ServiceNames"
import { Settings } from "../core/settings/Settings"
import {
  _libraryHasSettings,
  readLibrarySettings
} from "../core/settings/SettingsIO"
import { bestVolumeForPath } from "../core/volumes/Volumes"
import { commandTimeoutMs } from "../core/volumes/VolumeTtls"
import { clearMaxWorkCaches } from "../core/work/MaxCpus"
import { blank } from "../fe/Blank"
import { isTrue } from "../fe/Boolean"
import { minuteMs } from "../fe/Date"
import { Latch } from "../fe/Latch"
import { Maybe } from "../fe/MaybeTypes"
import { tap } from "../fe/Object"
import { DbModelJanitor } from "./db/DbModelJanitor"
import { dbModelSetup_ } from "./db/DbModelSetup"
import { libraryHealthCheckSetup } from "./health/LibraryHealthChecks"
import { statsDbJanitor } from "./stats/StatsDbJanitor"
import { statsDbDir_ } from "./StatsDbDir"
import { AssetFileRepository } from "./sync-file/AssetFileRepository"

// DON'T DO THIS! LibrarySettings will take care of this.
// Settings.libraryDir.addListener(() => Library.onLibraryDirChange())

/**
 * Given a library root directory, provides paths and factory methods for
 * library services, like the SeenFileFilter, Previews, and database setup.
 *
 * Note that a given process only supports a single library, so this should
 * be a singleton. Most likely the singleton will be managed by a singleton
 * instance of `Service`.
 */
export class Library extends EndableWrapper implements Endable {
  readonly start = Date.now()
  static #priorInstance: Maybe<Library>

  // This must not be async to ensure only one instance is currently live:
  static instance(): Maybe<Library> {
    const libraryDir = Settings.libraryDir.valueOrDefault
    const priorDir = this.#priorInstance?.rootDir.nativePath
    const priorWasOpen = this.#priorInstance?.isReadySync() === true
    const priorEnded = this.#priorInstance?.ended === true

    if (nativePathsEqlSync(priorDir, libraryDir) && !priorEnded) {
      // Prior instance is valid!
      return this.#priorInstance
    }

    if (isSyncService() && priorWasOpen) {
      void exit({
        reason: `Library changed mid-run. Shutting down to pick up new library at ${libraryDir}.`,
        status: 0
      })
      return
    }

    // Prior instance is wrong: end it and spin up a new one.
    void end(this.#priorInstance) // < (we don't need to wait for it to end)

    return (this.#priorInstance =
      blank(libraryDir) || !_libraryHasSettings(libraryDir)
        ? undefined
        : new Library(libraryDir))
  }

  static get priorInstance() {
    return this.#priorInstance
  }

  static endPriorInstance() {
    const prior = this.#priorInstance
    this.#priorInstance = undefined
    return end(prior)
  }

  static async instanceReady() {
    const i = Library.instance()
    await i?.ready
    return i
  }

  static instanceRequired() {
    const l = Library.instance()
    if (l == null) {
      throw new Error("Library is undefined" + FatalErrorFlag)
    } else {
      return l
    }
  }

  readonly readyLatch = new Latch()
  readonly rootDir: PosixFile
  readonly dataDir: PosixFile

  constructor(rootDir: string | BaseFile) {
    super(`library.Library(${rootDir})`, () => this.onEnd(), EndableRanks.predb)
    this.rootDir = PosixFile.for(rootDir)
    this.dataDir = libraryDataDirPosixFile(this.rootDir)!
    this.logger.info("new()")
    void this.#setup()
  }

  get isPendingSetup() {
    return this.readyLatch.isPending()
  }

  readonly #setup = lazy(async () => {
    const throwIfEnding = () => {
      if (this.ended || ending()) throw new Error()
    }
    try {
      this.logger.debug("setup() started")

      throwIfEnding()

      await readLibrarySettings(this.rootDir.nativePath)
      await setupLibraryDirs_(this.rootDir)

      // pre-emptively cache the locale, so that we don't have to wait for it
      // later
      await setupLuxon()

      // Prevent health checks from initially failing by setting up all these
      // directories in setup():
      await this.statsDbDir_()
      await imageCacheDir_()

      throwIfEnding()

      const previewsVol = await bestVolumeForPath(
        libraryPreviewsDirPosixFile(this.rootDir)
      )
      if (isTrue(previewsVol?.remote)) {
        const max = Settings.maxConcurrentImportsWhenRemote.valueOrDefault
        if (!Settings.maxConcurrentImports.hasValue()) {
          Settings.maxConcurrentImports.tmpValue = max
          clearMaxWorkCaches()
        }
        this.logger.warn(
          `Library previews are on a remote volume. Setting maxSyncFileJobsMax to ${max}.`
        )
      }

      throwIfEnding()

      if (isDbService()) {
        await this.dbModelJanitor()
      }

      this.previews()

      throwIfEnding()

      if (isStatsDbService()) {
        await this.statsDb()
      }
      if (this.readyLatch.isPending()) {
        this.logger.info("Library.setup() finished.")
        void this.readyLatch.resolve()
      }
      await libraryHealthCheckSetup()
      setImmediate(() => HealthCheck.awaitSettled())
    } catch (error) {
      this.logger.error("setup() failed:", { error })
      if (!this.ended && !ending()) {
        HealthCheck.addError(error)
        void this.readyLatch.reject(
          new WrappedError("Failed to start library", {
            cause: error
          })
        )
      }
    }
  })

  get ready() {
    return this.readyLatch.promise
  }

  isReadySync() {
    return this.readyLatch.isResolved()
  }

  readonly statsDbDir_ = lazy(() => statsDbDir_() as Promise<PosixFile>)

  readonly previews = lazy(() =>
    tap(new Previews(libraryPreviewsDirPosixFile(this.rootDir)!), ea =>
      Previews.instance.set(ea)
    )
  )

  readonly originalsDir = lazy(
    () => libraryOriginalsDirPosixFile(this.rootDir)!
  )

  readonly assetFileRepository = lazy(() => {
    return new AssetFileRepository(this.originalsDir())
  })

  readonly libraryDbFile = lazy(() => pathToDb(this.dataDir, "models"))

  readonly dbModelSetup_ = lazy(async () => {
    if (isDbService()) {
      return dbModelSetup_(this.rootDir)
    } else {
      throw new Error("Not a db service")
    }
  })
  readonly dbFsLock = lazy(async () => {
    return (await this.dbModelSetup_()).fslock
  })
  readonly useReplica = lazy(async () => {
    return (await this.dbModelSetup_()).useReplica
  })
  readonly modelDb = lazy(async () => {
    return (await this.dbModelSetup_()).db
  })
  readonly dbModelJanitor = lazy(async () => {
    const info = await this.dbModelSetup_()
    return new DbModelJanitor(info.db, info.fslock, info.libraryDbFile)
  })
  async runDbBackup() {
    return (await this.dbModelJanitor())?.backup_()
  }

  readonly statsDb = lazy(async () => statsDbJanitor(await this.statsDbDir_()))
  readonly statsDbFile = lazy(() => this.statsDb().then(ea => ea.dbFile))

  // Used when library path changes and by tests:
  private readonly onEnd = lazy(async () => {
    for (const { ea, t } of [
      { ea: this.statsDb.clear(), t: commandTimeoutMs() },
      { ea: this.dbModelJanitor.clear(), t: 5 * minuteMs },
      { ea: this.dbFsLock.clear(), t: commandTimeoutMs() }
    ]) {
      await end(ea, t)
    }
    this.logger.info("onEnd(): finished.")
  })
}
