import { DateTime, DateTimeFormatOptions } from "luxon"
import { blank, mapNotBlank, notBlank } from "../../fe/Blank"
import { secondMs } from "../../fe/Date"
import { getOrSet } from "../../fe/Map"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { mapNumeric } from "../../fe/Number"
import { opt } from "../../fe/Opt"
import { isString } from "../String"
import { closeTo, isRecentMs } from "./Date"
import { DateInterval } from "./DateInterval"
import { Dated } from "./Dated"
import { datedToMillis } from "./DatedToMillis"
import { isValidDate, mapValidDate } from "./ValidDate"

export function recent(d: Maybe<Date | number>, delta = 5 * secondMs): boolean {
  return isRecentMs(datedToMillis(d), delta)
}

export function fmtDateTime(
  dateTime?: DateTime,
  locale?: string,
  // Don't use FULL, because the friendly human offset name isn't known.
  opts: DateTimeFormatOptions = DateTime.DATETIME_MED
) {
  return mapValidDate(dateTime, dt => {
    mapNotBlank(locale, ea => (dt = dt.setLocale(ea)))
    return dt.toLocaleString(opts)
  })
}

const BadMsRE = /(\.\d\d\d)\d+/

export function isoToDateTime(iso: Maybe<string>): Maybe<DateTime> {
  if (blank(iso)) return
  if (iso.includes("/")) {
    // DateInterval will only build if .middle is valid.
    const m = DateInterval.fromISO(iso)?.middle.toDateTime()
    if (m != null) return m
  }
  const dt = DateTime.fromISO(
    iso.replace(BadMsRE, (_, m) => m),
    { setZone: true }
  )
  return dt.isValid ? dt : undefined
}

const locale2dtf = new Map<string, Intl.DateTimeFormat>()

export function shortDateTimeFormat(locale = "en-US"): Intl.DateTimeFormat {
  return getOrSet(
    locale2dtf,
    locale,
    () =>
      new Intl.DateTimeFormat(locale, {
        year: "numeric",
        month: "short",
        day: "numeric",
        hour: "numeric",
        minute: "numeric"
      })
  )
}

export function fmtDateShort(d: number | Dated, locale = "en-US"): string {
  return shortDateTimeFormat(locale).format(datedToMillis(d))
}

export function fmtDateIso(
  isoDate: string,
  locale?: string,
  // Don't use FULL, because the friendly human offset name isn't known.
  opts: DateTimeFormatOptions = DateTime.DATETIME_MED
): string {
  return opt(DateTime.fromISO(isoDate, { setZone: true }))
    .filter(isValidDate)
    .orElse(() =>
      map(DateInterval.fromISO(isoDate), ea => ea.middle.toDateTime())
    )
    .map(ea => fmtDateTime(ea, locale, opts))
    .getOrElse(() => isoDate)!
}

export function parseJsonDate(
  d: Maybe<string | { formatted: string; timestamp: number }>
) {
  if (d == null) return
  if (isString(d)) return isoToDateTime(d)?.toJSDate()
  const ts = d?.timestamp
  const fmt = d?.formatted
  const tsDate = mapNumeric(ts, ea => new Date(ea * secondMs))
  if (notBlank(fmt)) {
    const fmtDate = new Date(fmt)
    if (!closeTo(tsDate, fmtDate, secondMs)) return
  }
  return tsDate
}
