import axios, { AxiosRequestConfig } from "axios"
import { defer } from "../../fe/Defer"
import { Json } from "../../fe/JSON"
import { gt0, gte } from "../../fe/Number"
import { mkLogger } from "../Logger"
import { PosixFile } from "../fs/PosixFile"
import { HttpStatusIs } from "./HttpStatus"

const logger = defer(() => mkLogger("web.HttpGet"))

export type HttpGetJsonResult = {
  data: Json
  updatedAt: number
  fromCache: boolean
}

export async function httpGetJson_(args: {
  url: string
  config?: AxiosRequestConfig
  cache: PosixFile
  ttlMs: number
  preCacheTransform?: (data: Json) => Json
}): Promise<HttpGetJsonResult> {
  const f = args.cache
  const updatedAt = await f.mtimeMs()

  if (
    updatedAt != null &&
    gt0(await f.size()) &&
    gte(updatedAt, Date.now() - args.ttlMs)
  ) {
    return {
      data: await f.readJson_(),
      updatedAt,
      fromCache: true
    }
  }

  const response = await axios.get(args.url, args.config)
  const data = response.data
  if (!HttpStatusIs.success(response.status)) {
    logger().throw("Failed", { url: args.url, response })
  }

  const json = args.preCacheTransform?.(data) ?? data
  logger().info("Fetch success. Caching", {
    args,
    json,
    cache: f
  })
  await f.writeJson_(json)

  return {
    data: json,
    updatedAt: Date.now(),
    fromCache: false
  }
}
