import { greatestBy, leastBy } from "../../core/Array"
import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { isSyncService } from "../../core/ServiceNames"
import { ShortCommandTimeoutMs } from "../../core/ShortCommandTimeoutMs"
import { TTLSet } from "../../core/TTLSet"
import { tsToLocal } from "../../core/date/Localtime"
import { libraryOriginalsDirPosixFile } from "../../core/dir/LibraryDirs"
import { ee } from "../../core/event/EventEmitter"
import { LogLevels } from "../../core/log/LogLevel"
import { Settings } from "../../core/settings/Settings"
import { compact } from "../../fe/Array"
import { minuteMs, secondMs } from "../../fe/Date"
import { later } from "../../fe/Delay"
import { fmtDuration } from "../../fe/FmtDuration"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { closeTo, gt } from "../../fe/Number"
import { Operation, OperationNames } from "../model/Operation"
import { Progress, ProgressTimes } from "../model/Progress"
import { pathsToSync } from "./PathsToSync"
import { UriPath } from "./UriPath"

const logger = lazy(() => mkLogger("sync.SyncPaths"))

export const nativePathBlocklist = new TTLSet(10 * minuteMs)

export type UriPathTimes = UriPath & Partial<Exclude<ProgressTimes, "uri">>

export async function pathsAndTimesToSync(): Promise<UriPathTimes[]> {
  const todo = await pathsToSync()
  const times = Settings.forceSync.valueOrDefault ? [] : await Progress.times()
  return logger().tap({
    msg: "pathsAndTimesToSync()",
    level: LogLevels.info,
    result: todo.map(path => ({
      ...path,
      ...times.find(ea => ea.uri === path.uri)
    }))
  })
}

export async function restartSyncAt() {
  const arr = await pathsAndTimesToSync()
  return (
    Math.min(...compact([Date.now(), ...arr.map(ea => ea.lastCompletedAt)])) +
    Settings.syncNewIntervalMs.valueOrDefault
  )
}

export async function bestPathToSync() {
  return bestStable(await pathsAndTimesToSync())
}

export const getStaleCompletedAt = lazy(async () => {
  // A sync is "stale" if the user has asked for a full-resync and it hasn't
  // completed:
  const op = Operation.getFirstPendingOp({
    name: OperationNames.forceRestartSync
  })

  return (
    op?.createdAt ??
    // Or it's been more than syncIntervalHours since now since the last sync:
    Date.now() - Settings.syncNewIntervalMs.valueOrDefault
  )
}, ShortCommandTimeoutMs)

later(() => ee().on("clearCache", () => getStaleCompletedAt.unset()))

/**
 * This algorithm tries to keep a stable "best" pick, until the best is done.
 */
export async function bestStable(arr: UriPathTimes[]) {
  const staleCompletedAt = await getStaleCompletedAt()
  const incomplete = arr.filter(
    ea => ea.lastCompletedAt == null || ea.lastCompletedAt < staleCompletedAt
  )

  logger().info("bestStable()", {
    paths: arr.map(ea => ({
      ...ea,
      lastStarted: map(ea.lastStartedAt, tsToLocal),
      lastCompleted: map(ea.lastCompletedAt, tsToLocal),
      lastUpdated: map(ea.lastUpdatedAt, tsToLocal),
      incomplete:
        ea.lastCompletedAt == null || ea.lastCompletedAt < staleCompletedAt,
      staleMs: staleCompletedAt - (ea.lastCompletedAt ?? 0)
    })),
    stalePaths: incomplete.map(ea => ea.nativePath),
    stale: tsToLocal(staleCompletedAt)
  })

  // 1. If we're currently syncing a volume, keep at that until it's done.
  {
    const mostRecentlyUpdated = greatestBy(incomplete, ea => ea.lastUpdatedAt)
    // Only return this if we touched it in the last 15 minutes.
    if (
      mostRecentlyUpdated != null &&
      gt(mostRecentlyUpdated.lastUpdatedAt, Date.now() - 15 * minuteMs)
    ) {
      logger().info(
        "bestStable(): returning most recently updated",
        mostRecentlyUpdated
      )
      return { ...mostRecentlyUpdated, why: "recently updated" }
    }
    // Otherwise, there's no need to be "stable":
  }

  // ** Assume order of arr matters **

  // If there are any volumes we've never done before, start those:
  {
    const neverStartedBefore = incomplete.find(ea => ea.lastStartedAt == null)
    if (neverStartedBefore != null) {
      logger().info(
        "bestStable(): returning neverStartedBefore",
        neverStartedBefore
      )
      return { ...neverStartedBefore, why: "never synced before" }
    }
  }

  {
    // If any volumes haven't been completed, start those:
    const neverCompletedBefore = incomplete.find(
      ea => ea.lastCompletedAt == null
    )
    if (neverCompletedBefore != null) {
      logger().info(
        "bestStable(): returning neverCompletedBefore",
        neverCompletedBefore
      )
      return { ...neverCompletedBefore, why: "never completed before" }
    }
  }

  {
    // They've all been completed at least once. Return the least completed:
    const leastRecentlyCompleted = leastBy(incomplete, ea => ea.lastCompletedAt)
    if (leastRecentlyCompleted != null) {
      logger().info(
        "bestStable(): returning leastRecentlyCompleted",
        leastRecentlyCompleted
      )
      return { ...leastRecentlyCompleted, why: "least recently synced" }
    }
  }

  logger().info(
    "bestPathToSync(): all paths have been recently completed (yay)."
  )

  // This is called by `info`: don't mark completed if we're not `sync`!
  if (isSyncService()) {
    logger().info("bestPathToSync(): Marking sync as complete.")
    Operation.markOpCompleted({ name: OperationNames.forceRestartSync })
  }

  return
}

function uptToStatus(info: UriPathTimes, staleCompletedAt: number) {
  return info.lastStartedAt == null
    ? "new"
    : gt(info.lastUpdatedAt, Date.now() - 10 * secondMs) &&
      !closeTo(info.lastCompletedAt, info.lastUpdatedAt, 5 * secondMs)
    ? "syncing"
    : gt(info.lastCompletedAt, staleCompletedAt)
    ? "synced"
    : "stale"
}

function tsToISO(ts: Maybe<number>) {
  return ts == null ? undefined : new Date(ts).toISOString()
}

function tsToAgo(ts: Maybe<number>) {
  if (ts == null) return
  const dur = Date.now() - ts
  return dur < 2 * secondMs ? "just now" : fmtDuration(dur) + " ago"
}

export async function syncPathsForUI() {
  const arr = await pathsAndTimesToSync()
  const best = await bestStable(arr)
  const staleCompletedAt = await getStaleCompletedAt()
  const libraryDir = libraryOriginalsDirPosixFile()?.nativePath

  return arr.map(info => ({
    path: info.nativePath,
    library: libraryDir === info.nativePath,
    status: uptToStatus(info, staleCompletedAt),
    isNext: info.nativePath === best?.nativePath,
    lastStartedISO: tsToISO(info.lastStartedAt),
    lastStarted: tsToAgo(info.lastStartedAt),
    lastCompletedISO: tsToISO(info.lastCompletedAt),
    lastCompleted: tsToAgo(info.lastCompletedAt)
  }))
}
