import { Tags } from "exiftool-vendored"
import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import { minuteMs } from "../../fe/Date"
import { Dimensions, isDimensions, maybeDimSwap } from "../../fe/Dimensions"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { first } from "../Array"
import { mkLogger } from "../Logger"
import { FileCache } from "../fs/FileCache"
import { PosixFile } from "../fs/PosixFile"
import { SimpleFileOrString } from "../fs/SimpleFile"
import { aspectRatio } from "../img/Dimensions"
import { RawInfo, rawInfo } from "../img/RawInfo"
import { extractVideoFrame_ } from "../img/Video"
import { readRawTags } from "./ExifTool"
import { isLibrawMimeType, isVideoMimeType } from "./MimeTypes"
import { ImageDimensions, extractRotation } from "./Orientation"

const logger = lazy(() => mkLogger("tags.SizeInfo"))

export interface SizeInfo extends ImageDimensions {
  /**
   * Dimensions after EXIF orientation has been applied
   */
  dimensions: Dimensions

  /**
   * Aspect ratio (width/height) after EXIF orientation has been applied
   */
  aspectRatio: number

  /**
   * These dimensions do not take EXIF orientation into account
   */
  fileDimensions: Dimensions

  /**
   * Degrees clockwise that the image should be rotated. `undefined` if the
   * tag is missing. 0 if the image is already in correct orientation.
   * Undefined if `Orientation` involves mirroring (which should be extremely
   * rare)
   */
  rotation: Maybe<number>

  /**
   * EXIF Orientation
   */
  orientation?: number
}

const sizeInfoCache = lazy(
  () =>
    new FileCache<Maybe<SizeInfo>>({
      name: "tags.sizeInfo",
      maxSize: 256,
      timeoutMs: minuteMs
    })
)

/**
 * Handles extracting dimensions from both image and video files
 */
export async function extractSizeInfoFromFile(
  pathOrFile: Maybe<SimpleFileOrString>,
  defaultTags?: Tags
): PromiseMaybe<SizeInfo> {
  if (pathOrFile == null) return
  const f = PosixFile.for(pathOrFile)
  return sizeInfoCache().getOrSetAsync(f.nativePath, () =>
    _extractSizeInfoFromFile(f, defaultTags)
  )
}

async function _extractSizeInfoFromFile(
  f: Maybe<PosixFile>,
  defaultTags?: Tags
): PromiseMaybe<SizeInfo> {
  if (f == null) return
  // defaultTags is set by tests and by videos after we extract a frame:
  const t =
    defaultTags == null || blank(defaultTags.ImageHeight)
      ? await readRawTags(f)
      : defaultTags
  if (t == null) return

  // If the video has a rotation value (or a sidecar with a rotation value), use
  // that:
  const rot = extractRotation(defaultTags) ?? extractRotation(t)

  if (isLibrawMimeType(t.MIMEType)) {
    return extractSizeInfoFromTags(t, rot, await rawInfo(f))
  }

  const si = extractSizeInfoFromTags(t, rot)
  if (si != null) return si

  if (isVideoMimeType(t.MIMEType)) {
    return _extractSizeInfoFromFile(await extractVideoFrame_(f), t)
  } else {
    return
  }
}

/**
 * Attempts to extract ImageHeight and ImageWidth from the given tags.
 *
 * You probably want the higher-level method, extractSizeInfoFromFile_().
 *
 * This is only exposed for use by __extractVideoFrame_
 */
export function extractSizeInfoFromTags(
  tags: Maybe<
    Partial<
      ImageDimensions &
        Pick<Tags, "MIMEType" | "ImageSize" | "FileName" | "Orientation">
    >
  >,
  rotation?: Maybe<number>,
  _rawInfo?: Maybe<Partial<RawInfo>>
): Maybe<SizeInfo> {
  rotation ??= extractRotation(tags)
  const fileDimensions = first(
    [
      _rawInfo?.ImageSize,
      // DON'T pull in ExifImageWidth/ExifImageHeight or
      // SourceImageWidth/SourceImageHeight: those tags don't necessarily
      // represent the actual image size.
      { width: tags?.ImageWidth, height: tags?.ImageHeight }
    ],
    d => (isDimensions(d) ? d : undefined)
  )

  // Sometimes this is "11mm" (for .svg)
  if (fileDimensions == null) {
    return logger().tap({
      msg: "extractSizeInfo(" + tags?.FileName + ") (undefined fileDimensions)",
      result: undefined,
      meta: {
        mimetype: tags?.MIMEType,
        rawInfo,
        fileDimensions
      }
    })
  }

  const dimensions = maybeDimSwap(fileDimensions, rotation)
  return logger().tap({
    msg: "extractSizeInfo(" + tags?.FileName + ")",
    result: {
      ImageHeight: dimensions.height,
      ImageWidth: dimensions.width,
      aspectRatio: aspectRatio(dimensions)!,
      dimensions,
      rotation,
      fileDimensions,
      orientation: tags?.Orientation
    },
    meta: {
      mimetype: tags?.MIMEType,
      rawInfo
    }
  })
}
