import { lazy } from "../../core/Lazy"
import { toA } from "../../fe/toA"
import { AppName } from "../AppName"
import { configDir } from "../dir/ConfigDir"
import { PosixFile } from "../fs/PosixFile"
import { mkLogger } from "../Logger"
import { settingsToObj } from "./Setting"
import { persistedSystemSettings } from "./Settings"
import { readSystemSettings, writeSystemSettings_ } from "./SettingsIO"
import { SettingsToml } from "./SettingsToml"

const logger = lazy(() => mkLogger("settings.MergeConfigDirs"))

export async function mergeUserDataDirs_() {
  const cfgDir = configDir()
  if (cfgDir == null) return

  const current = PosixFile.for(cfgDir)

  for (const prior of [
    current.join(AppName()),
    current.join(AppName().toLowerCase()),
    current.parent().join(AppName().toLowerCase())
  ]) {
    try {
      await mergeAndArchiveSystemSettingsDir_(prior, current)
    } catch (err) {
      logger().error("Failed to merge " + prior + " and " + current, err)
    }
  }
  logger().info("done")
}

export async function mergeAndArchiveSystemSettingsDir_(
  priorConfigDir_: PosixFile,
  currentConfigDir_: PosixFile
) {
  let log = mkLogger(
    "mergeAndArchiveSystemSettingsDir_(" +
      priorConfigDir_.baseWithGrandparent +
      " => " +
      currentConfigDir_.baseWithGrandparent +
      ")"
  )
  const priorResolvedConfigDir = await priorConfigDir_.resolve_()
  const currentResolvedConfigDir = await currentConfigDir_.resolve_()
  if (
    priorResolvedConfigDir == null ||
    currentResolvedConfigDir == null ||
    currentResolvedConfigDir.nativePath === priorResolvedConfigDir.nativePath
  ) {
    log.info("no-op, missing or same directory", {
      priorResolvedConfigDir: priorResolvedConfigDir?.nativePath,
      currentResolvedConfigDir: currentResolvedConfigDir?.nativePath
    })
    return
  }
  log = mkLogger(
    "mergeAndArchiveSystemSettingsDir_(" +
      priorResolvedConfigDir.baseWithGrandparent +
      " => " +
      currentResolvedConfigDir.baseWithGrandparent +
      ")"
  )

  if (!(await priorResolvedConfigDir.isDirectory())) {
    log.info("no-op, prior directory doesn't exist")
    return
  }
  {
    const prior = priorResolvedConfigDir.join(SettingsToml)
    if (await prior.isNonEmptyFile()) {
      const curr = currentResolvedConfigDir.join(SettingsToml)
      // layer in any prior settings, but last one in wins:
      await readSystemSettings(prior)
      log.info(
        "read prior settings.toml",
        settingsToObj(persistedSystemSettings())
      )
      await readSystemSettings(curr)
      log.info(
        "read current settings.toml",
        settingsToObj(persistedSystemSettings())
      )
      await writeSystemSettings_(curr)
    }
  }

  {
    // Copy any licenses over...
    const srcDir = priorResolvedConfigDir.join("licenses")
    const destDir = currentResolvedConfigDir.join("licenses")
    for (const src of toA(await srcDir.childFiles())) {
      const dest = destDir.join(src.base)
      if (await dest.notExists()) {
        try {
          await src.copyFile_(dest)
          log.info("Copied license " + src.base)
        } catch (err) {
          log.warn("Failed to copy license " + src.base, err)
        }
      }
    }
  }
  // Archive the directory so we don't migrate it again:
  const old = currentResolvedConfigDir.join("old")
  try {
    const dest = await priorResolvedConfigDir.renameYMDHMS_({
      subdir: old.nativePath
    })
    log.info("merged and archived to " + dest)
  } catch (err) {
    log.warn("Failed to archive to " + old, err)
  }
  return
}
