import { BinaryField, Tags } from "exiftool-vendored"
import { XMLParser } from "fast-xml-parser"
import { lazy } from "../../core/Lazy"
import { compact, compactBlankish, compactBlanks, uniq } from "../../fe/Array"
import { blank, mapNotBlank } from "../../fe/Blank"
import { flatten } from "../../fe/Flatten"
import { Maybe } from "../../fe/MaybeTypes"
import { pick } from "../../fe/Object"
import { toA } from "../../fe/toA"
import { remove } from "../Array"
import { mkLogger } from "../Logger"
import { escapeRegExp } from "../RegExp"
import { isString } from "../String"
import { Settings } from "../settings/Settings"

const logger = lazy(() => mkLogger("tags.KeywordTags"))

// "delimiters" separate individual tags:
export const delimRe = lazy(() =>
  mapNotBlank(
    Settings.keywordDelimiters.valueOrDefault,
    ea => new RegExp(`\\s*[${escapeRegExp(ea)}]\\s*`)
  )
)
Settings.keywordDelimiters.watchLater(() => delimRe.unset())

export function splitKeyword(s: string) {
  const re = delimRe()
  return re == null ? [s] : compactBlanks(s.split(re))
}

export function splitKeywords(s: string | string[] | BinaryField): string[] {
  if (s == null || s instanceof BinaryField) {
    return []
  }
  return compactBlanks(
    Array.isArray(s) ? flatten(s.map(splitKeywords)) : splitKeyword(s)
  )
}
// "path separators" separate tags hierarchical levels. If this is blank,
// hierarchical paths are disabled (unless encoded as a hierarchy in the EXIF as
// XML)
export const pathSepRe = lazy(() =>
  mapNotBlank(
    Settings.keywordPathSeparators.valueOrDefault,
    ea => new RegExp(`\\s*[${escapeRegExp(ea)}]\\s*`)
  )
)
Settings.keywordPathSeparators.watch(() => pathSepRe.unset())
interface Category {
  Category?: Category[]
  _: string
}
function catToS(c: Maybe<string | Category>): string[][] {
  if (c == null) {
    return []
  } else if (isString(c)) {
    return blank(c) ? [] : [[c]]
  } else {
    return flatten(
      toA(c.Category)
        .map(catToS)
        .map(paths => paths.map(path => compact([c._, ...path])))
    )
  }
}
const BlankCategoryRE = /^\(?none\)?$/i
// only exposed for tests

export function parseCategories(s: Maybe<string>): Maybe<string[][]> {
  if (blank(s) || BlankCategoryRE.exec(s) != null) return
  try {
    const xml = new XMLParser({ textNodeName: "_" }).parse(s)
    return flatten(toA(xml?.Categories?.Category).map(catToS))
  } catch (err) {
    logger().warn("parseCategories() failed:", { err, s })
    return
  }
}

export function rawTagKeywords(t: Maybe<Tags>): (string | string[])[] {
  const result: (string | string[])[] = []
  if (t != null) {
    const kwtags = [...Settings.keywordTags.values]
    if (kwtags.includes("Categories")) {
      remove(kwtags, "Categories")
      const ea = parseCategories(t.Categories)
      if (ea != null) result.push(...ea)
    }
    for (const key of kwtags) {
      const ea = (t as any)[key]
      // If it's already an array, don't split again:
      result.push(...(Array.isArray(ea) ? ea : splitKeywords(ea)))
    }
  }
  return logger().tap({
    msg: "rawTagKeywords()",
    result: compactBlankish(uniq(result)),
    meta: {
      t:
        t == null ? undefined : pick(t, ...(Settings.keywordTags.values as any))
    }
  })
}
