import { isEmpty } from "../Array"
import { lazy } from "../Lazy"
import { Maybe } from "../MaybeTypes"
import { isDigits } from "../Number"
import { prngSeed } from "../PRNG"
import {
  assembleFullPath,
  toURLSearchParams,
  URLSearchParamable
} from "../URLSearchParams"

// for tests:
export const urlSeed = lazy(prngSeed, 100)

function ensureSeed(query: Maybe<URLSearchParamable>, forceNewSeed: boolean) {
  const usp = toURLSearchParams(query)
  if (forceNewSeed || !isDigits(usp?.get("seed"))) {
    const seed = urlSeed().toString()
    usp?.set("seed", seed)
    return usp ?? { seed }
  }
  // we already have a seed, yay:
  return usp
}

/**
 * @return fullPath for home page (NO PROTOCOL OR HOSTNAME!)
 */
export function mkHomeFullPath(query?: URLSearchParamable) {
  return mkTagFullPath([], query, true)
}

export function mkTagFullPath(
  tagPath: string[],
  query?: URLSearchParamable,
  forceNewSeed: boolean = false
) {
  const o = mkTagUri(tagPath, query, forceNewSeed)
  return assembleFullPath(o.path, o.query)
}

export function mkTagUri(
  tagPath: string[],
  query?: URLSearchParamable,
  forceNewSeed: boolean = false
) {
  return {
    path:
      "/tag/" +
      (isEmpty(tagPath) ? "" : tagPath.map(encodeURIComponent).join("/")),
    query: ensureSeed(query, forceNewSeed)
  }
}
