import { mkLogger } from "../../core/Logger"
import { mapAsyncSerial } from "../../core/async/Promise"
import { HealthCheck } from "../../core/health/HealthCheck"
import { Settings } from "../../core/settings/Settings"
import { libraryHasSettings } from "../../core/settings/SettingsIO"
import { volsha } from "../../core/uri/volsha"
import { Volume } from "../../core/volumes/Volume"
import { commandTimeoutMs } from "../../core/volumes/VolumeTtls"
import { volumeUuidNotExpected } from "../../core/volumes/VolumeUUID"
import { bestVolumeForPath, volumes } from "../../core/volumes/Volumes"
import { compactBlanks, isEmpty, uniqBy } from "../../fe/Array"
import { li, tt } from "../../fe/BabyMarkdown"
import { defer } from "../../fe/Defer"
import { andList } from "../../fe/FmtList"
import { groupBy } from "../../fe/MultiMap"
import { pick } from "../../fe/Object"
import { scanPaths } from "../sync/PathsToSync"

const logger = defer(() => mkLogger("health.VolumeUUIDHealthCheck"))

export const volumeUuidHealthCheck = defer(() =>
  HealthCheck.for({
    section: "Library",
    id: "volume-uuids",
    pendingMsg: "Checking volumes",
    settings: ["scanAllDrives"],
    rejectIsWarn: true,
    links: [
      {
        text: `What's a volume?`,
        url: "https://photostructure.com/faq/what-is-a-volume/",
        icon: "docs"
      }
    ],
    later: async () => {
      // If we don't have a library open right now, short-circuit.
      if (!libraryHasSettings()) {
        return {
          level: "disabled",
          msg: `Volume UUID test disabled: no library is open`
        }
      }

      const vols: Volume[] = []
      const noUriPaths: string[] = []
      if (Settings.scanAllDrives.valueOrDefault) {
        vols.push(...(await volumes()))
      } else {
        const obj = await scanPaths()
        const arr = await mapAsyncSerial({
          name: "scannedVolumes",
          arr: obj.result.map(ea => ea.nativePath),
          f: path => bestVolumeForPath(path),
          timeoutMs: commandTimeoutMs()
        })
        vols.push(...uniqBy(arr, ea => ea.mountpoint))
        noUriPaths.push(...obj.noUriPaths)
      }

      const duplicateUUIDVolumes = [
        ...groupBy(vols, ea => ea.uuid).values()
      ].filter(ea => ea.length > 1)

      // We only care about uuids for scanned directories.
      const missingUUIDs = vols
        .filter(ea => ea.uuid == null && !volumeUuidNotExpected(ea))
        .map(ea => ea.mountpoint)

      const unhealthyVolumes = (await volumes())
        .filter(ea => ea.ok === false)
        .map(ea => compactBlanks([ea.mountpoint, ea.status]).join(": "))

      logger().debug("volumeHealthCheck result", {
        missingUUIDs,
        noUriPaths,
        unhealthyVolumes,
        scannedVolumes: vols.map(ea => pick(ea, "uuid", "mountpoint"))
      })

      if (
        isEmpty(missingUUIDs) &&
        isEmpty(duplicateUUIDVolumes) &&
        isEmpty(noUriPaths) &&
        isEmpty(unhealthyVolumes)
      ) {
        if (isEmpty(vols)) {
          return {
            msg: [
              "No volumes need UUIDs",
              tt(Settings.scanAllDrives.key) +
                " is false, and no volumes other than the library are configured to be scanned."
            ],
            level: "ok"
          }
        } else {
          return {
            msg: [
              `All scanned volume UUIDs are OK`,
              li(vols.map(ea => tt(volsha(ea.uuid) + "  " + ea.mountpoint)))
            ],
            level: "ok"
          }
        }
      } else {
        const msg: string[] = []
        if (!isEmpty(unhealthyVolumes)) {
          msg.push(
            "Some volumes are unhealthy",
            li(unhealthyVolumes.map(ea => tt(ea))) +
              "PhotoStructure may crash or hang when accessing unhealthy volumes."
          )
        }
        if (!isEmpty(missingUUIDs)) {
          msg.push(
            "Some volumes are missing UUIDs",
            li(missingUUIDs.map(ea => tt(ea))),
            "This can cause problems with deduplication and library portability."
          )
        }
        if (!isEmpty(duplicateUUIDVolumes)) {
          msg.push(
            "Some volumes have duplicate UUIDs",
            duplicateUUIDVolumes
              .map(arr =>
                li(
                  andList(arr.map(ea => tt(ea.mountpoint))) +
                    " share UUID " +
                    tt(arr[0].uuid)
                )
              )
              .join("\n---\n"),
            "This can cause problems with deduplication and library portability."
          )
        }
        if (!isEmpty(noUriPaths)) {
          msg.push(
            "Some paths failed to have URIs generated",
            li(noUriPaths.map(ea => tt(ea))),
            "This will prevent these paths from being imported."
          )
        }
        return {
          level: "warn",
          msg: msg.join("\n---\n"),
          links: [
            {
              text: "How to manually add a UUID",
              icon: "handyman",
              url: "https://photostructure.com/faq/what-is-a-volume/#add-uuid"
            },
            {
              text: "Rescan volumes",
              type: "button",
              method: "POST",
              url: "/admin/clear-caches",
              icon: "refresh"
            }
          ]
        }
      }
    }
  })
)
