import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { getLiveDbDir_ } from "../../core/db/CheckLocalDbReplica"
import { dbBackupCold_ } from "../../core/db/DbBackupCold"
import { assertDirSQLiteReadWrite_ } from "../../core/db/SQLiteReadWrite"
import { Schemas, pathToDb } from "../../core/db/Schemas"
import { libraryDataDirPosixFile } from "../../core/dir/LibraryDirs"
import { errorToS } from "../../core/error/Error"
import {
  DbSetupErrorFlag,
  InternalErrorFlag,
  NoLibraryErrorFlag
} from "../../core/error/ErrorFlags"
import { FsLock, withLock_ } from "../../core/fs/FsLock"
import { PosixFile } from "../../core/fs/PosixFile"
import { SimpleFileOrString, toNativePath_ } from "../../core/fs/SimpleFile"
import { HealthCheck } from "../../core/health/HealthCheck"
import { LogLevels } from "../../core/log/LogLevel"
import { Settings } from "../../core/settings/Settings"
import { _libraryHasSettings } from "../../core/settings/SettingsIO"
import { isNotEmpty } from "../../fe/Array"
import { blank, mapNotBlank } from "../../fe/Blank"
import { minuteMs } from "../../fe/Date"
import { Maybe } from "../../fe/MaybeTypes"
import { modelDb } from "../model/ModelDb"
import { Db } from "./Db"
import { addDbSetupError, clearDbSetupErrors } from "./DbSetupErrors"

const logger = lazy(() => mkLogger("db.DbModelSetup"))

export function libraryDbObjects_(
  libraryDir = Settings.libraryDir.valueOrDefault
) {
  if (blank(libraryDir)) {
    throw new Error("Library directory is not set" + NoLibraryErrorFlag)
  }
  const dataDir = libraryDataDirPosixFile(libraryDir)
  if (dataDir == null) {
    throw new Error("missing library dataDir" + NoLibraryErrorFlag)
  }

  if (!_libraryHasSettings(libraryDir)) {
    throw new Error(
      `Library at ${libraryDir} does not have a settings file` +
        NoLibraryErrorFlag
    )
  }
  return { dataDir, libraryDbFile: pathToDb(dataDir, "models") }
}

export function fsLockForDb(dbFile: PosixFile) {
  // using .for() here to ensure the _same instance_ gets returned for the
  // same path:
  return FsLock.for({
    file: dbFile,
    timeoutMs: 0,
    noopIfContested: false
  })
}

async function libraryDbLockObjects_(
  libraryDir = Settings.libraryDir.valueOrDefault
) {
  const result = libraryDbObjects_(libraryDir)
  return {
    ...result,
    fslock: fsLockForDb(result.libraryDbFile)
  }
}

export function libraryDbInfoJsonFile(
  libraryDir = Settings.libraryDir.valueOrDefault
): PosixFile {
  const dataDir = libraryDataDirPosixFile(libraryDir)
  if (dataDir == null) {
    throw new Error("missing library dir" + NoLibraryErrorFlag)
  } else {
    return libraryDbInfoFromDbFile(pathToDb(dataDir, "models"))
  }
}

export function libraryDbInfoFromDbFile(dbFile: PosixFile) {
  return dbFile.sibling("db-info.json")
}

export async function assertValidDbInfo_(
  infoJsonFile = libraryDbInfoJsonFile(),
  expectedLiveDbFile?: PosixFile
): Promise<{ liveDbFile: PosixFile }> {
  try {
    const priorInfo = await infoJsonFile.readJson<DbSetupInfo>(LogLevels.info)
    if (priorInfo == null) {
      throw new Error(infoJsonFile + " is missing" + DbSetupErrorFlag)
    }
    if (
      expectedLiveDbFile != null &&
      !(await expectedLiveDbFile.equalsUri_(priorInfo.uri))
    ) {
      throw new Error(
        infoJsonFile +
          " expects the live db to be " +
          priorInfo.uri +
          " but the live db is " +
          (await expectedLiveDbFile.uri_()) +
          DbSetupErrorFlag
      )
    }

    const liveDbFile = await PosixFile.forUri(priorInfo.uri)
    if (liveDbFile == null) {
      throw new Error(
        infoJsonFile +
          " expects the live db to be " +
          priorInfo.uri +
          " but that URI cannot be resolved." +
          DbSetupErrorFlag
      )
    }
    try {
      await assertDirSQLiteReadWrite_(liveDbFile.parent())
    } catch (error) {
      logger().error(
        "assertValidDbInfo_(): failed to assert live dir was r/w",
        { error, liveDbFile, infoJsonFile }
      )
      throw new Error(
        "Failed to validate that the live database directory, " +
          liveDbFile.dir +
          ", was read-writable by SQLite: " +
          errorToS(error) +
          DbSetupErrorFlag
      )
    }

    // Don't clear db errors here: we do that in dbModelSetup_() after
    // migration is successful:
    return {
      liveDbFile
    }
  } catch (error) {
    addDbSetupError(error)
    throw error
  }
}

export interface DbSetupInfo {
  uri: string
  useReplica: boolean
}

/**
 * Handles opening the SQLite model database.
 *
 * @throws if the library directory is missing, or if the library does not
 * have a settings file, or there is a problem opening the database.
 */
export async function dbModelSetup_(
  libraryDir: Maybe<SimpleFileOrString> = Settings.libraryDir.valueOrDefault
): Promise<{
  db: Db
  useReplica: boolean
  libraryDbFile: PosixFile
  fslock: FsLock
}> {
  const { libraryDbFile, fslock } = await libraryDbLockObjects_(
    mapNotBlank(libraryDir, toNativePath_)
  )

  // This isn't a lock as much as a way to coordinate who currently has the
  // library database open:
  await fslock.tryAcquire_()

  let useReplica: boolean | undefined
  let db: Db | undefined

  const infoJsonFile = libraryDbInfoFromDbFile(libraryDbFile)

  try {
    await withLock_(
      { file: infoJsonFile, timeoutMs: 5 * minuteMs },
      async () => {
        const priorInfo = await infoJsonFile.readJson<DbSetupInfo>(
          LogLevels.info
        )

        useReplica = priorInfo?.useReplica

        // We want to prevent "split brain" issues where multiple processes
        // read/write from different sqlite files.

        if (priorInfo != null) {
          const { liveDbFile } = await assertValidDbInfo_(
            infoJsonFile,
            libraryDbFile
          )

          db = new Db(Schemas.models, liveDbFile)
        }

        if (useReplica == null || db == null) {
          const liveDbInfo = await getLiveDbDir_(libraryDir)
          const uri = await liveDbInfo.db.uri_()
          useReplica = liveDbInfo.useReplica
          if (useReplica) {
            await HealthCheck.traceLater_(
              "Setting up local replica database",
              dbBackupCold_(libraryDbFile, liveDbInfo.db.parent())
            ).catch(error =>
              logger().throw("Failed to set up replica db" + DbSetupErrorFlag, {
                src: libraryDbFile,
                dest: liveDbInfo.db,
                error
              })
            )
          }
          db = new Db(Schemas.models, liveDbInfo.db)
          await infoJsonFile.writeJson_({ uri, useReplica })
        }
      }
    )

    if (db == null || useReplica == null) {
      return logger().throw(
        "Failed to open model db" + DbSetupErrorFlag + InternalErrorFlag,
        {
          libraryDir
        }
      )
    }

    if (await fslock.tryAcquire_()) {
      // migrate_() runs a repair if any checks fail.
      const result = await db.migrate_()
      if (
        useReplica &&
        (result.repaired || isNotEmpty(result.appliedMigrations))
      ) {
        // Only copy back if we repaired or applied migrations:
        await db.backup_(libraryDbFile.parent(), !useReplica)
      }
      // YAY EVERYTHING IS GREAT
      clearDbSetupErrors()
    }
    // ALL IS WELL let's go!
    modelDb.set(db)
  } catch (error) {
    logger().error("Failed to set up model db", { error })
    addDbSetupError(error)
    fslock.release()
    throw error
  }

  return logger().tap({
    msg: "dbModelSetup()",
    result: {
      db,
      useReplica,
      libraryDbFile,
      fslock
    }
  })
}
