import { Database } from "better-sqlite3"
import { lazy } from "../../core/Lazy"
import { eql } from "../../fe/Eql"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { randomChars } from "../../fe/Random"
import { mkLogger } from "../Logger"
import { PosixFile } from "../fs/PosixFile"
import { SimpleFileOrString } from "../fs/SimpleFile"
import { mkdb_ } from "./MkDb"
import { sqliteFiles } from "./SQLiteFiles"

const logger = lazy(() => mkLogger("db.SQLiteReadWrite"))

export async function firstDirSQLiteReadWrite<T extends SimpleFileOrString>(
  ...dirs: Maybe<T>[]
): PromiseMaybe<T> {
  for (const dir of dirs) {
    if (dir != null && (await isDirSQLiteReadWrite(dir))) {
      return dir
    }
  }
  return
}

export async function isDirSQLiteReadWrite(dirNativePath: SimpleFileOrString) {
  try {
    await assertDirSQLiteReadWrite_(dirNativePath)
    logger().info("isDirSQLiteReadWrite(): true", { dirNativePath })
    return true
  } catch (error) {
    logger().warn("isDirSQLiteReadWrite(): false", { dirNativePath, error })
    return false
  }
}

export async function assertDirSQLiteReadWrite_(
  dirNativePath: SimpleFileOrString
): Promise<void> {
  const dir = PosixFile.for(dirNativePath)
  const s = randomChars(8)
  const dbFile = dir.child(".test-" + s + ".sqlite")
  let db: Database | null = null
  try {
    await dir.mkdirp_()
    db = mkdb_({
      nativePath: dbFile.nativePath,
      timeoutMs: 1000
    })
    db.exec("CREATE TABLE t (s VARCHAR(16) NOT NULL)")
    db.exec(`INSERT INTO t (s) VALUES ('${s}')`)
    {
      const rows = db.prepare("SELECT s FROM t").all()
      if (!eql(rows, [{ s }])) {
        logger().throw("unexpected rows", { rows })
      }
    }
    db.exec("VACUUM")
    db.close()
    db = mkdb_({
      nativePath: dbFile.nativePath,
      timeoutMs: 1000
    })
    {
      const s2 = randomChars(8)
      db.exec(`INSERT INTO t (s) VALUES ('${s2}')`)
      const rows = db.prepare("SELECT s FROM t ORDER BY s").pluck().all()
      if (!eql(rows, [s, s2].sort())) {
        logger().throw("unexpected rows", { rows })
      }
    }
  } finally {
    try {
      db?.close()
    } catch {
      // ignore
    }
    await Promise.all(sqliteFiles(dbFile).map(ea => ea.rm()))
  }
}
