import sharp from "sharp"
import { flatten } from "../../fe/Flatten"
import { toA } from "../../fe/toA"
import { rgb2labArray } from "../color/Colorspace"
import { avg } from "../math/Vector"
import { HashDim } from "./ImageHash"

export type Binary = 0 | 1

export function mhash(arr: Buffer | number[]): Binary[] {
  const m = avg(arr)
  // Buffer.map() DOES NOT DO WHAT YOU WANT. toA() is REQUIRED.
  return m == null ? [] : toA(arr).map(ea => (ea >= m ? 1 : 0))
}

export function dhash(arr: Buffer | number[]): (0 | 1)[] {
  // Buffer.map() DOES NOT DO WHAT YOU WANT. toA() is REQUIRED.
  const pixels = toA(arr)
  const dim = Math.round(Math.sqrt(pixels.length))
  return pixels.map((ea, idx) => {
    // first pixel looks down. first column looks up. all others look left.
    const p =
      idx === 0 ? arr[dim] : idx % dim === 0 ? arr[idx - dim] : arr[idx - 1]
    return ea >= p ? 1 : 0
  })
}

export async function meanDiffHash(img: sharp.Sharp, hashSize = HashDim) {
  const pixels = await img
    .greyscale()
    .resize({
      // We want to resize to a small square. "fill" ignores the aspect ratio:
      fit: sharp.fit.fill,
      width: hashSize,
      height: hashSize
    })
    .raw()
    .toBuffer()

  return {
    meanHash: mhash(pixels),
    diffHash: dhash(pixels)
  }
}

export async function meanDiffHashCIELAB(img: sharp.Sharp, hashSize = HashDim) {
  const rgb = await img
    .resize({
      // We want to resize to a small square. "fill" ignores the aspect ratio:
      fit: sharp.fit.fill,
      width: hashSize,
      height: hashSize
    })
    .raw()
    .toBuffer()

  // [[L* values], [a* values], [b* values]]:
  const lab = rgb2labArray(rgb)
  return {
    meanHash: flatten(lab.map(mhash)),
    diffHash: flatten(lab.map(dhash))
  }
}
