import { mkLogger } from "../../core/Logger"
import { includes, uniq } from "../../fe/Array"
import { notBlank } from "../../fe/Blank"
import { isTrue } from "../../fe/Boolean"
import { Maybe } from "../../fe/MaybeTypes"
import { opt } from "../../fe/Opt"
import { stripPrefixes } from "../../fe/String"
import { Model, ModelClass } from "./Model"

const ctor2class = new Map<string, ModelClass<any>>()

export function addModelClass(mc: ModelClass<any>) {
  ctor2class.set(mc.$ctor, mc)
}

/**
 * Supports both db and rpc JSON representations
 */
export function fromJSON<M extends Model>(caller: ModelClass<M>, json: any): M {
  if (json == null) {
    throw new Error("json is null")
  }
  if (
    caller != null &&
    notBlank(caller.$ctor) &&
    !ctor2class.has(caller.$ctor)
  ) {
    addModelClass(caller)
  }
  if (json instanceof caller) {
    return json as M
  }

  const mc = opt(json["$ctor"])
    .flatMap(ea => ctor2class.get(ea))
    .getOrElse(() => caller)
  if (json instanceof mc) return json
  const m = new mc()
  if (json == null) return m
  return assignFromJSON(
    m,
    json,
    uniq([caller.$tableName + ".", mc.$tableName + "."])
  )
}

export function assignFromJSON<M extends Model>(
  m: M,
  json: any,
  prefixes: string[] = []
): M {
  const mc = m.class()
  for (const [k, v] of Object.entries(json)) {
    if (k === "$ctor") continue
    if (v === undefined) continue
    const key = stripPrefixes(k, prefixes)
    // If v is NULL, let it be null
    ;(m as any)[key] =
      v == null
        ? v
        : // if field is boolean, convert to boolean
        includes(mc.$booleanFields, key)
        ? isTrue(v)
        : // otherwise pass through value from db:
          v
  }
  return m
}

export function toJSON(
  m: Maybe<Model | Model[]>,
  mc: ModelClass<any>,
  key: string
): any | any[] {
  if (m == null) return
  if (Array.isArray(m))
    return m.map((ea, idx) => toJSON(ea, mc, `${key}[${idx}]`))
  if (typeof m.toJSON === "function") return m.toJSON()
  mkLogger("ModelJSON.toJSON()").warn("Failed", { m, key, mc: mc.$ctor })
  return undefined
}
