import { lazy } from "../../core/Lazy"
import { stepRange } from "../../fe/Array"
import { getOrSet } from "../../fe/Map"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { clamp, round } from "../../fe/Number"
import { leastBy } from "../Array"
import { BitZip } from "../math/Bits"
import { matXvec, vecXvec } from "../math/Matrix"
import { ciede2000_delta_e } from "./DeltaE"
import { ModeBits } from "./ModeBits"
import { clampRGB, rgbTriplet2hex, rgbhex2triplet } from "./RGB"
import { Triplet } from "./Triplet"

// From http://www.brucelindbloom.com
const srgb2xyz = [
  [0.4124564, 0.3575761, 0.1804375],
  [0.2126729, 0.7151522, 0.072175],
  [0.0193339, 0.119192, 0.9503041]
]
const xyz2srgb = [
  [3.2404542, -1.5371385, -0.4985314],
  [-0.969266, 1.8760108, 0.041556],
  [0.0556434, -0.2040259, 1.0572252]
]

export function rgbhex2Lab(rgbHex: string): Maybe<Triplet> {
  return map(rgbhex2triplet(rgbHex), rgb2lab)
}

export function rgbhex2Labhash(
  rgbHex: string,
  bitDepth: number = ModeBits
): Maybe<number> {
  return map(rgbhex2triplet(rgbHex), ea => toLabhash(rgb2lab(ea), bitDepth))
}

export function lab2rgbhex(lab: Triplet): string {
  return rgbTriplet2hex(lab2rgb(lab))
}

/**
 * Convert from flattened RGB triplets to [[CIELAB triplet]]
 */
export function rgb2labTriplets(rgb: Buffer): Triplet[] {
  return stepRange(0, rgb.length, 3).map(i =>
    rgb2lab([rgb[i], rgb[i + 1], rgb[i + 2]])
  )
}

/**
 * Convert from flattened RGB triplets to [[L* values], [a* values], [b* values]]
 */
export function rgb2labArray(rgb: Buffer): [number[], number[], number[]] {
  const l = []
  const a = []
  const b = []
  for (let i = 0; i < rgb.length; i += 3) {
    const lab = rgb2lab([rgb[i], rgb[i + 1], rgb[i + 2]])
    l.push(lab[0])
    a.push(lab[1])
    b.push(lab[2])
  }
  return [l, a, b]
}

/**
 * where rgb values are [0,255]
 */
export function rgb2lab(rgb: Triplet): Triplet {
  return xyz2lab(rgb2xyz(rgb))
}

export function lab2rgb(lab: Triplet): Triplet {
  return clampRGB(xyz2rgb(lab2xyz(lab)))
}

export function clampLab(lab: Triplet): Triplet {
  return 1 < 2
    ? [
        clamp(0, 100, lab[0]),
        clamp(-100, 100, lab[1]),
        // -108 seems to be wrong, but RGB(0,0,255) ->
        // L*a*b*(32.297,79.188,-107.860) according to
        // http://www.easyrgb.com/en/convert.php#inputFORM and
        // http://colormine.org/convert/rgb-to-lab
        clamp(-108, 100, lab[2])
      ]
    : (LabBitZip().clampValue(lab) as Triplet)
}

export function rgb2xyz(rgb: Triplet): Triplet {
  // See http://www.brucelindbloom.com/index.html?Eqn_RGB_XYZ_Matrix.html
  const srgb = clampRGB(rgb)
    .map(ea => ea / 255)
    .map(ea =>
      ea > 0.04045 ? Math.pow((ea + 0.055) / 1.055, 2.4) : ea / 12.92
    )
  return matXvec(srgb2xyz, srgb) as any // because TS doesn't support tuples well
}

// https://en.wikipedia.org/wiki/Lab_color_space#CIELAB in D65

const RefWhite = {
  X: 0.95047,
  Y: 1,
  Z: 1.08883
}

// http://www.brucelindbloom.com/index.html?Eqn_XYZ_to_Lab.html

const epsilon = 216 / 24389 // 0.00885
const kappa = 24389 / 27 // 903.3

export function xyz2lab(xyz: Triplet): Triplet {
  const [fx, fy, fz] = vecXvec(xyz, [
    1 / RefWhite.X,
    1 / RefWhite.Y,
    1 / RefWhite.Z
  ]).map(t => (t > epsilon ? Math.pow(t, 1 / 3) : (kappa * t + 16) / 116))

  return clampLab([116 * fy - 16, 500 * (fx - fy), 200 * (fy - fz)])
}

export function lab2xyz(lab: Triplet): Triplet {
  // the [0], [1], [2] allows lab to be a Buffer.slice:
  const [l, a, b] = clampLab(lab)
  const fy = (l + 16) / 116
  const fx = a / 500 + fy
  const fz = fy - b / 200

  const fx3 = fx * fx * fx
  const fz3 = fz * fz * fz

  const xr = fx3 > epsilon ? fx3 : (116.0 * fx - 16.0) / kappa
  const yr = l > 8 ? Math.pow(fy, 3.0) : l / kappa
  const zr = fz3 > epsilon ? fz3 : (116.0 * fz - 16.0) / kappa

  return [xr * RefWhite.X, yr * RefWhite.Y, zr * RefWhite.Z]
}

export function xyz2rgb(xyz: Triplet): Triplet {
  return matXvec(xyz2srgb, xyz).map(v => {
    const sign = v < 0 ? -1 : 1
    const l = v * sign
    return round(
      255 *
        sign *
        (l <= 0.0031308 ? 12.92 * l : 1.055 * Math.pow(l, 1 / 2.4) - 0.055)
    )
  }) as Triplet
}

export const LabBitZip = lazy(
  () =>
    new BitZip([
      { min: 0, max: 100 }, // L
      // see findBestLabhashDims:
      { min: -79, max: 90 },
      { min: -102, max: 89 }
    ])
)

export function toLabhash(lab: Triplet, bitDepth: number = ModeBits): number {
  return LabBitZip().zip(lab, bitDepth)
}

const labhash2lab = new Map<number, Triplet>()

export function unlabhash(
  hash: number,
  bitDepth: number = ModeBits
): Maybe<Triplet> {
  return 1 > 2 && bitDepth === ModeBits
    ? getOrSet(labhash2lab, hash, () => _unlabhash(hash, bitDepth))
    : _unlabhash(hash, bitDepth)
}

function _unlabhash(hash: number, bitDepth: number = ModeBits): Maybe<Triplet> {
  return LabBitZip().unzip(hash, bitDepth) as Triplet
}

export const minLabhash = lazy(() => LabBitZip().zipMin(ModeBits))
export const maxLabhash = lazy(() => LabBitZip().zipMax(ModeBits))

export function closestLab(haystack: Triplet[], needle: Triplet) {
  return leastBy(haystack, ea => ciede2000_delta_e(ea, needle))
}

export function rgb2hsv(rgb: Triplet): Triplet {
  const [r, g, b] = clampRGB(rgb)

  const max = Math.max(r, g, b)
  const delta = max - Math.min(r, g, b)

  const hh = delta
    ? max === r
      ? (g - b) / delta
      : max === g
      ? 2 + (b - r) / delta
      : 4 + (r - g) / delta
    : 0

  return [
    60 * (hh < 0 ? hh + 6 : hh),
    max ? (delta / max) * 100 : 0,
    (max / 255) * 100
  ]
}

export function rgbhex2hsl(rgbhex: string): Triplet {
  return rgb2hsl(rgbhex2triplet(rgbhex)!)
}

export function rgb2hsl(rgb: Triplet): Triplet {
  return hsv2hsl(rgb2hsv(rgb))
}

export function hsl2rgb(hsl: Triplet): Triplet {
  return hsv2rgb(hsl2hsv(hsl))
}

export function hsv2hsl(hsv: Triplet): Triplet {
  // https://github.com/omgovich/colord/blob/3f859e03b0ca622eb15480f611371a0f15c9427f/src/colorModels/hsl.ts#L44

  const [h, s, v] = hsv

  const hh = ((200 - s) * v) / 100

  return [
    h,
    hh > 0 && hh < 200
      ? ((s * v) / 100 / (hh <= 100 ? hh : 200 - hh)) * 100
      : 0,
    hh / 2
  ]
}

export function hsl2hsv(hsl: Triplet): Triplet {
  const [h, s, l] = hsl
  const s0 = (s * (l < 50 ? l : 100 - l)) / 100
  return [h, s0 > 0 ? ((2 * s0) / (l + s0)) * 100 : 0, l + s0]
}

export function hsv2rgb(hsv: Triplet) {
  const h = (hsv[0] / 360) * 6
  const s = hsv[1] / 100
  const v = hsv[2] / 100

  const hh = Math.floor(h),
    b = v * (1 - s),
    c = v * (1 - (h - hh) * s),
    d = v * (1 - (1 - h + hh) * s),
    mod6 = hh % 6

  return clampRGB([
    [v, c, b, b, d, v][mod6] * 255,
    [d, v, v, c, b, b][mod6] * 255,
    [b, b, d, v, v, c][mod6] * 255
  ])
}

