import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { mapGt0 } from "../../core/Number"
import { TagRoots } from "../../fe/api/Tag"
import { monthTagRef, ordinalToMonth } from "../curators/DateTagger"
import { Tag } from "./Tag"

const whenQuery = `'${TagRoots.When}' || char(31)`
const yearQuery = whenQuery + ` || '%' || char(31)`
const monthQuery = yearQuery + ` || '%' || char(31)`
const dayQuery = monthQuery + ` || '%' || char(31)`

const logger = lazy(() => mkLogger("model.DateTagNormalizer"))

export function getMonthTags() {
  return Tag.ops().allf(
    q =>
      q
        .whereNotNull("ordinal")
        .andWhereRaw(
          "_path LIKE " + monthQuery + " AND _path NOT LIKE " + dayQuery
        )
        .limit(200 * 12) // 200 years of month tags. We can fetch all 2400 into RAM, no need to batch.
  )
}

export async function normalizeDateTags() {
  for (const ea of getMonthTags()) {
    normalizeMonthTag(ea)
  }
}

export function normalizeMonthTag(tag: Tag) {
  const tagRef = mapGt0(tag.ordinal, ord => monthTagRef(ordinalToMonth(ord)))
  if (tagRef == null) {
    logger().warn("failed to fix tag (no monthTagRef)", { t: tag })
  } else if (tagRef.name !== tag.name) {
    logger().info("Fixing month name", { tagRef, tag })
    tag.changeName(tagRef.name)
  }
  tag.maybeUpsertDisplayName(tagRef?.displayName)
}
