import { defer } from "../../fe/Defer"
import { osFullName, whyOsNotSupported } from "../OperatingSystem"
import { HealthCheck } from "./HealthCheck"

export const osHealthCheck = defer(() =>
  HealthCheck.for({
    section: "System",
    id: "system-version",
    pendingMsg: "Checking operating system",
    settings: [],
    // no ttlMs: this won't change at runtime
    links: [
      {
        text: "What do I need to run PhotoStructure?",
        icon: "docs",
        url: "https://forum.photostructure.com/t/what-do-i-need-to-run-photostructure/44"
      }
    ],
    later: async () => {
      const why = whyOsNotSupported()
      return why == null
        ? {
            level: "ok",
            msg: [
              "Operating system is OK",
              "PhotoStructure supports " + osFullName()
            ]
          }
        : {
            level: "warn",
            msg: [
              "Operating system is not supported",
              osFullName() + " was detected, but only " + why + "."
            ]
          }
    }
  })
)
