import { lazy } from "../../core/Lazy"
import { weekMs } from "../../fe/Date"
import { isBillingService } from "../ServiceNames"
import { FileCleanup } from "../fs/FileCleanup"
import { isExt } from "../fs/FileExt"
import { Settings } from "../settings/Settings"

// Don't run logdir cleanup on account.photostructure.com!

export const LogDirCleanup = lazy(() =>
  isBillingService()
    ? undefined
    : new FileCleanup(
        "logDirCleanup",
        () => Settings.logDir.valueOrDefault,
        weekMs,
        f => isExt(f.ext, ".log", ".log.gz")
      )
)
