// HEURISTICS AHOY!

function replaceAll(s: string, re: RegExp, replacement = ""): string {
  const str = s.replace(re, replacement)
  return str === s ? s : replaceAll(str, re, replacement)
}

const CamelCasedCos = [
  "BenQ",
  "GoPro",
  "HTC",
  "LG",
  "NEC",
  "OnePlus",
  "Sony",
  "TrueHDR"
]
const Lower2Camel = new Map(CamelCasedCos.map(ea => [ea.toLowerCase(), ea]))

export function companyCased(str: string): string {
  const s = str.trim()
  const m = Lower2Camel.get(s.toLowerCase())
  return m != null
    ? m
    : s.length < 4
    ? s
    : s.toLowerCase().replace(/(?:^|\s|-)\S/g, c => c.toUpperCase())
}

