// TODO: does this need to be lazy'ed? I think I did it to avoid regex parsing
// at startup, but that's probably not a big deal.
const i = lazy(() => new Constants())

const GoProRe = /\bgopro\b/i

function goProMakeMaybe(tags: RareTags) {
  return GoProRe.test(toS(tags.HandlerDescription)) ||
    GoProRe.test(toS(tags.CompressorName))
    ? "GoPro"
    : undefined
}

export function extractMakeAndModel(tags: Maybe<RareTags>): {
  Make?: string
  Model?: string
} {
  if (tags == null) return {}
  const Make =
    make(tags.Make) ??
    makeFromSoftware(tags.Software) ??
    makeFromSoftware(tags.CreatorTool) ??
    goProMakeMaybe(tags)

  const Model = model(
    Make,
    // DON'T USE DeviceModel or DeviceManufacturer: those are from the ICC profile!
    toExifDefined(tags.Model) ??
      // Really old cameras, like QuickTake 200, use "CameraModelName" instead
      // of Model.
      toExifDefined(tags.CameraModelName) ??
      toExifDefined(tags.CameraModel)
  )

  return compactValues({ Make, Model }) ?? {}
}

export function make(rawMake: Maybe<string>): Maybe<string> {
  if (isExifUnset(rawMake)) return
  let s = stripQuotes(rawMake)
  // https://en.wikipedia.org/wiki/HP_Inc.
  if (/Hewlett[- ]Packard/i.test(s)) return "HP"
  s = replaceAll(s, i().IgnorableMakePatterns)
  s = s.replace(/\s+app on Apple iDevice$/i, "")
  s = s.replace(/\bLGE\b/, "LG")
  s = s.replace(/\bEastman Kodak\b/i, "Kodak")
  s = companyCased(s)
  s = s.replace(/^OM$/i, "OM System")
  return s
}

// Only emit files that have a .Software value:

// exiftool -r -if '$Software' -Software ~/src/test-images

const SoftwarePatterns = {
  Acer: /^Acer\b/i,
  Canon: /^(Canon|CanoScan)\b/i,
  Daisy: /^Exif Daisy\b/i,
  Fujifilm: /Digital Camera FinePix\b/i,
  GE: /Digital Camera GE\b/i,
  Kodak: /\bKodak\b/i,
  Kyocera: /^Kyocera|SAMURAI\b/i,
  LG: /^(LGE_SW|LG Electronics)\b/i,
  Minolta: /^(Minolta|DiMAGE|DYNAX|MAXXUM|ALPHA SWEET)\b/i,
  Nikon: /^(Nikon|COOLPIX)\b/i,
  Olympus: /^OLYMPUS\b/i,
  OnePlus: /^OnePlus/i,
  Pentax: /^(Optio|PENTAX)\b/i,
  Ricoh: /^RICOH\b/i,
  RIM: /^(Rim Exif Version|BlackBerry)/i
}

/**
 * @param rawSoftware the value from `.Software`. Some (ancient) devices that
 * don't emit a `.Make` value will emit a `.Software` value that lets us infer
 * the Make.
 */
export function makeFromSoftware(rawSoftware: Maybe<string>): Maybe<string> {
  if (blank(rawSoftware)) return
  for (const [k, v] of entries(SoftwarePatterns)) {
    if (v.test(rawSoftware)) return k
  }
  return
}

// < Digimax U-CA 5, Kenox U-CA 5 / Kenox U-CA 50 >

const AncientSamsungModel = /<\s*(\S[^,/]+)[,/]/

export function model(
  cleanMake?: string,
  rawModel?: string
): string | undefined {
  if (isExifUnset(cleanMake) || isExifUnset(rawModel)) {
    return
  }
  // Some Canon will put quotes around the make and model (!!)
  let m = stripQuotes(rawModel)

  if (cleanMake === "Samsung") {
    const match = AncientSamsungModel.exec(m)
    if (match != null) {
      m = match[1].trim()
    }
  }

  const commonName = map(
    i().CommonNamesByMake[cleanMake],
    (arr: NamedPattern[]) => arr.find(([re]) => m!.match(re) != null)
  )

  if (commonName != null) {
    return commonName[1].trim()
  }

  // If the make prefixes the model, remove that:
  m = m.replace(new RegExp(`^${escapeRegExp(cleanMake)}\\s+`, "i"), "")

  if (cleanMake === "Sony") {
    m = m.replace(/^(?:DSLR-A|SLT-A|ILCA-|ILCE-)/, "").replace(/7CL?/i, "7c") // ILCE-7CL is the 7c

    const match = /M(\d+)$/.exec(m)
    if (match != null) {
      m = m.slice(0, match.index) + " " + intToRoman(match[1])
    }
  }

  if (cleanMake === "Kodak") {
    m = m
      .replace(/Kodak|digital|science|zoom|camera/gi, "")
      .replace(/\s+/g, " ")
  }

  if (cleanMake === "Olympus" || cleanMake === "OM System") {
    // Handle "E-M5MarkIII"
    m = mapOr(
      /^(.+?\d)mark(i.+)$/i.exec(m),
      match => `${match[1]} Mark ${match[2]}`,
      () => m
    )
  }

  if (cleanMake === "Panasonic") {
    // Only remove "DC-" prefix if it leaves 2+ characters:
    m = m.replace(/^(?:DC|DMC)-(.{2,})$/i, "$1")
  }

  if (cleanMake === "Motorola") {
    // Handle "DROID2"
    m = m.replace(/DROID/i, "Droid ")
  }

  if (cleanMake === "Canon") {
    // Handle "EOS DIGITAL REBEL"
    m = m
      .replace(/\bDIGITAL\b/i, "")
      .trim()
      .replace(/EOS\s+REBEL/i, "EOS Rebel") // as per Canon's website

    const match = m.match(/^EOS R(\d+)\s*m(\d+)$/i)
    if (match != null) {
      m = `EOS R${match[1]} Mark ${intToRoman(match[2])}`
    }
    // Render "EOS R6m2" as "EOS R6 Mark II"
  }

  for (const re of i().IgnorableModelPatterns) {
    m = replaceAll(m!, re).trim()
  }

  // Even if it's "HP":
  if (
    cleanMake!.match(/^HP|Hewlett.Packard$/i) != null &&
    m.match(/^hp\b/i) != null
  ) {
    // Remove "HP" prefix:
    m = m.slice(2).trim()
  }
  return m
}
