import { ExifDateTime, ResourceEvent, Tags, WriteTags } from "exiftool-vendored"
import { lazy } from "../../core/Lazy"
import { isEmpty, isNotEmpty } from "../../fe/Array"
import { sortBy } from "../../fe/ArraySort"
import { blank, mapNotBlank, notBlank } from "../../fe/Blank"
import { eql } from "../../fe/Eql"
import { stringify } from "../../fe/JSON"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { isNumber } from "../../fe/Number"
import {
  assignFields,
  entries,
  isEmptyObj,
  maybeCall,
  pick
} from "../../fe/Object"
import { StrEnumKeys, strEnum } from "../../fe/StrEnum"
import { isString } from "../../fe/String"
import { toA } from "../../fe/toA"
import { toS } from "../../fe/toS"
import { AppNameVersion } from "../AppName"
import { parseJSON } from "../JSON"
import { mkLogger } from "../Logger"
import { isFirstCharAZ, startsWithIgnoreCase } from "../String"
import { datedToMillis } from "../date/DatedToMillis"
import { luxonTzOffsetToOffsetMinutes } from "../date/Timezone"
import { PosixFile } from "../fs/PosixFile"
import { toNativePath_ } from "../fs/SimpleFile"
import { Settings } from "../settings/Settings"
import { ExifTags } from "./ExifTags"
import { _readRawTags, overwriteTags_ } from "./ExifTool"

export const Actions = strEnum("infer", "set", "add", "delete")
export type Action = StrEnumKeys<typeof Actions>

export function mkHistoryRecords(
  action: Action,
  tags: Maybe<Tags | WriteTags>
): ResourceEvent[] {
  return entries(tags ?? {}).map(([key, value]) =>
    mkHistoryRecord({ action, key, value })
  )
}

export function mkHistoryRecord({
  action,
  key,
  value,
  ts
}: {
  action: Action
  key: string
  value: any
  ts?: number
}): ResourceEvent {
  return {
    Action: action,
    Changed: toS(key).replace(/[#+]$/, ""), // < strip any "#" or "+" suffix
    Parameters: stringify(maybeCall(value, "toJSON") ?? value),
    When: ExifDateTime.fromMillis(ts ?? Date.now()),
    SoftwareAgent: AppNameVersion()
  }
}

export const InferAction = "infer"

const logger = lazy(() => mkLogger("tags.History"))

export function tagsContainHistory(
  t: Maybe<Tags>,
  predicate: (ea: ResourceEvent) => boolean
): boolean {
  if (t == null || t.History == null || isString(t.History)) return false

  const h = t.History
  if (Array.isArray(h)) {
    return h.some(predicate)
  } else {
    return predicate(h)
  }
}

export function getInferredHistoricValues(
  history: Maybe<ResourceEvent | ResourceEvent[]>
): Partial<Record<keyof ExifTags, any>> {
  if (history == null) return {}
  const arr = toA(history)
  if (isEmpty(arr)) return {}
  const oldestFirst = sortBy(
    arr.filter(
      ea =>
        startsWithIgnoreCase(ea.SoftwareAgent, "PhotoStructure") &&
        ea.Action === Actions.infer
    ),
    ea =>
      datedToMillis(mapNotBlank(ea.When, str => ExifDateTime.from(str))) ??
      Date.now()
  )
  const result: any = {}
  for (const ea of oldestFirst.reverse()) {
    const k = ea.Changed
    if (notBlank(k) && !(k in result)) {
      result[k] = valueFromResourceEvent(ea)
    }
  }
  return result
}

export function valueFromResourceEvent(re: ResourceEvent) {
  if (re?.Parameters == null) return
  if (isNumber(re?.Parameters)) return re.Parameters
  const s = toS(re)
  if (blank(s)) return
  return parseJSON(re.Parameters) ?? re.Parameters
}

export function inferredToTags(
  inferred: Maybe<Partial<ExifTags>>
): Maybe<Tags> {
  if (inferred == null) return
  const result: Tags = {}
  for (const [k, v] of entries(inferred)) {
    if (isFirstCharAZ(k)) {
      ;(result as any)[k] = v
    }
  }
  if ("duration" in inferred) result.Duration = inferred.duration
  if ("title" in inferred) result.Title = inferred.title
  if ("description" in inferred) result.Description = inferred.description
  if ("rating" in inferred) result.Rating = inferred.rating

  assignFields(result, inferred.capturedAt?.asExifTag())

  const tz = luxonTzOffsetToOffsetMinutes(
    inferred.tz ?? inferred.capturedAt?.zoneName,
    inferred.capturedAt?.toMillis()
  )
  if (tz != null) {
    result.OffsetTime = tz
  }
  return result
}

/**
 * @param inferred the result from readTags().inferred
 */
export async function ensureInferredHistoryRecords(
  src: PosixFile,
  sidecar: PosixFile | string,
  inferred: Maybe<Tags>
): PromiseMaybe<ResourceEvent[]> {
  const historyRecords = mkHistoryRecords(Actions.infer, inferred)
  const additionalTags =
    (Settings.writeInferredTagsToLibraryCopies.valueOrDefault
      ? inferredToTags(inferred)
      : undefined) ?? {}

  if (Settings.writeSourceTagToLibraryCopies.valueOrDefault) {
    additionalTags.Source = src.nativePath
  }
  logger().debug("ensureInferredHistoryRecords()", {
    sidecar,
    historyRecords,
    additionalTags
  })

  return ensureHistoryRecords(sidecar, historyRecords, additionalTags)
}

export async function ensureHistoryRecords(
  sidecar: Maybe<PosixFile | string>,
  historyRecords: ResourceEvent[],
  additionalTags?: Maybe<Tags | WriteTags>
): PromiseMaybe<ResourceEvent[]> {
  if (sidecar == null || blank(sidecar)) {
    logger().warn("ensureHistoryRecords(): no-op: empty path", { sidecar })
    return
  }
  const sidecarPath = toNativePath_(sidecar)
  const prior = await _readRawTags(sidecarPath)
  const newHistoryRecords = toA(historyRecords).filter(hr => {
    if (hr.Action !== Actions.infer) return true
    const p1 = pick(hr, "Changed", "Parameters")
    return !tagsContainHistory(prior, ea =>
      eql(p1, pick(ea, "Changed", "Parameters"))
    )
  })
  const tagsToWrite = { ...(additionalTags ?? {}) }
  if (isNotEmpty(newHistoryRecords)) {
    ;(tagsToWrite as any)["History+"] = newHistoryRecords
  }
  if (isEmptyObj(tagsToWrite)) {
    logger().debug("ensureHistoryRecords(): no-op: nothing to write", {
      sidecar,
      historyRecords,
      newHistoryRecords,
      additionalTags,
      tagsToWrite
    })
    return
  }

  logger().debug("ensureHistoryRecords(): write", {
    sidecar,
    tagsToWrite,
    historyRecords,
    additionalTags
  })

  const sidecarFile = PosixFile.for(sidecar)

  await overwriteTags_(sidecarFile, tagsToWrite)

  // clear filesystem caches:
  sidecarFile.clearThisAndParent()

  return newHistoryRecords
}
