import { AssetTag } from "./AssetTag"
import { TableName } from "./TableName"
import { TaggedAssetStream } from "./TaggedAssetStream"
import { TimestampedModel } from "./TimestampedModel"

function orderBy(builder: Knex.QueryBuilder) {
  return builder.orderByRaw("COALESCE(ordinal, _path) COLLATE NOCASE")
}

const logger = lazy(() => mkLogger("model.Tag"))

later(() => {
  ee().on("clearCache", () => Tag.clear())
  ee().on("clearDbCache", () => Tag.clear())
})

const AssetCountForTag = compressWhitespace(
  "WITH RECURSIVE descendants(id) AS (",
  "  VALUES (:tagId)",
  "  UNION",
  "  SELECT Tag.id",
  "  FROM Tag, descendants",
  "  WHERE Tag.parentId = descendants.id",
  ")",
  "SELECT",
  "  count(DISTINCT Asset.id) AS assetCount",
  // "  count(DISTINCT AssetFile.id) AS assetFileCount",
  "FROM",
  "  descendants",
  "  JOIN AssetTag ON AssetTag.tagId = descendants.id",
  "  JOIN Asset ON Asset.id = AssetTag.assetId",
  // "  JOIN AssetFile ON AssetFile.assetId = Asset.id",
  "WHERE",
  "  Asset.shown = 1",
  "  AND Asset.hidden = 0",
  "  AND Asset.excludedAt IS NULL",
  "  AND Asset.deletedAt IS NULL"
)

export class Tag extends TimestampedModel implements TagRef {
  static override readonly $tableName: TableName = "Tag"
  static override readonly $uniqueColumnName = "_path"
  // Attempt to fix foreign key constraints failures error from @avdp:
  // "sql":"insert into Tag (_displayName, _path, assetCount, createdAt, id,
  // ordinal, parentId, updatedAt) values (?, ?, ?, ?, ?, ?, ?, ?) ON CONFLICT
  // (_path) DO UPDATE SET
  // _displayName=excluded._displayName,assetCountId,updatedAt=excluded.updatedAt","bindings":["Jan","fs\u001f",0,1691671043786,3,12,2,1691671999051]}}
  static override readonly $useCache = false

  static readonly cmp = (a: Tag, b: Tag) =>
    cmp(
      [a.ordinal ?? Number.MAX_SAFE_INTEGER, ...a.path],
      [b.ordinal ?? Number.MAX_SAFE_INTEGER, ...b.path]
    )

  static clear() {
    this.root.unset()
    this.roots.unset()
    this._upsertDefaultRoots.unset()
  }

  static validate_() {
    for (const bad of this.ops().allf(q => q.whereRaw("id = parentId"))) {
      logger().warn(
        `Tag ${bad.id} has itself as parentId. Attempting to fix...`
      )
      const parentPath = bad.path.slice(0, -1)
      const parent = isEmpty(parentPath)
        ? undefined
        : this.findOrCreate(parentPath)
      bad.upsert({ parentId: parent?.id ?? null })
    }
    for (const ea of Tag.roots()) {
      ea.validate_([])
    }
  }

  validate_(expectedParentPath: string[]) {
    const path = this.path
    if (!eql(path.slice(0, -1), expectedParentPath)) {
      throw new Error(
        `Tag:${this.id} path ${path.join(
          "/"
        )} does not match expected parent ${expectedParentPath.join("/")}`
      )
    }
    for (const ea of this.getChildren()) {
      ea.validate_(path)
    }
  }

  static cacheTags(arr: Tag[]) {
    return arr.map(ea => this.cacheTag(ea))
  }

  static cacheTag(tag: Maybe<Tag>): Maybe<Tag> {
    // if (tag != null) this.recentTagsByPath.set(tag._path, tag)
    if (tag?.parentId === 0) {
      this.root.unset()
    }
    return tag
  }

  static readonly root = lazy(() => {
    const root = new Tag()
    root.id = 0
    // root.name = "" // DON'T SET .name, it's always derived from _path.
    root._path = TagSep
    root.parentId = undefined
    root.children = Tag.roots().filter(
      ea =>
        !includesIgnoreCase(Settings.hiddenHomeTags.valueOrDefault, ea.name) &&
        !includesIgnoreCase(
          Settings.hiddenHomeTags.valueOrDefault,
          ea.description ?? ""
        )
    )
    root.ancestors = []
    root.assetCount = Asset.shownCount()
    root.upsert = () => {
      throw new Error("upsert not supported on root")
    }
    return root
  }, ShortCommandTimeoutMs) // so Tag.roots() gets refreshed

  private static readonly _upsertDefaultRoots = lazy(() =>
    Tag.ops().upsert(
      Roots.map(
        ea =>
          ({
            _path: joinTagPath([ea.name]),
            ordinal: ea.ordinal
          }) as PartialPojo<Tag>
      )
    )
  )

  static withNullAssetCount(): number[] {
    return Tag.dbl.pluckAll({
      sql: compressWhitespace(
        "WITH RECURSIVE ancestors(id) AS (",
        "  SELECT id FROM Tag where assetCount IS NULL",
        "  UNION ALL",
        "  SELECT Tag.parentId as id",
        "  FROM Tag, ancestors",
        "  WHERE Tag.id = ancestors.id",
        ")",
        "SELECT distinct(id)",
        "FROM ancestors"
      )
    })
  }

  static selfAndAncestorIds(ids: number[]): number[] {
    return Tag.dbl.pluckAll({
      sql: compressWhitespace(
        "WITH RECURSIVE ancestors(id) AS (",
        `  SELECT id FROM Tag where assetCount in (${ids
          .filter(gt0)
          .join(",")})`,
        "  UNION ALL",
        "  SELECT Tag.parentId as id",
        "  FROM Tag, ancestors",
        "  WHERE Tag.id = ancestors.id",
        ")",
        "SELECT distinct(id)",
        "FROM ancestors"
      )
    })
  }

  static updateAssetCount(tagId: number) {
    if (!gt0(tagId)) return
    const assetCount = Tag.dbl.pluckFirst({
      sql: AssetCountForTag,
      bindings: { tagId }
    })
    Tag.dbl.runf(q =>
      q.update({ assetCount, updatedAt: Date.now() }).where({ id: tagId })
    )
    logger().debug("Updated asset counts for tag ", { tagId, assetCount })
  }

  // afterUpsert will only clear this if new root tags are added
  static readonly roots = lazy<Tag[]>(() => {
    Tag._upsertDefaultRoots() // < this is lazy, so
    const tags = Tag.ops().all(orderBy(Tag.query().whereNull("parentId")))
    tags.forEach(ea => {
      ea.parentId = undefined
      ea.ancestors = []
      Tag.cacheTag(ea)
    })
    return tags
  }, ShortCommandTimeoutMs) // < TTL is important! Without this, tag counts don't update!

  static #newTag(tagPath: TagPath): Tag {
    const t = new Tag()
    t._path = joinTagPath(tagPath)
    const d = tagPath[tagPath.length - 1] as any
    if (typeof d === "object") {
      mapNotBlank(d.displayName, ea => (t._displayName = ea))
      mapNumeric(d.ordinal, ea => (t.ordinal = ea))
      mapNotBlank(d.description, ea => (t.description = ea))
      mapNumeric(d.releasedAt, ea => (t.releasedAt = ea))
    }
    return t
  }

  static findByIdOrPath(tagId: Maybe<number>, tagPath: string[]) {
    return tagId === 0
      ? Tag.root()
      : gt0(tagId)
      ? this.findById(tagId)
      : this.findByPath(tagPath)
  }

  static findById(tagId: number) {
    return tagId === 0 ? Tag.root() : this.ops().findById(tagId)
  }

  static findByPath(tagPath: TagPath): Maybe<Tag> {
    if (isEmpty(tagPath)) return Tag.root()
    const _path = joinTagPath(tagPath)
    return this.ops().firstf(q => q.whereLike("_path", _path))
    // return Tag.recentTagsByPath.getOrSet(_path, () =>
    //   map(
    //     // LIKE gives us case insensitivity:
    //     this.ops().firstf(q => q.whereLike("_path", _path)),
    //     ea => Tag.tagById.set(ea.id!, ea)
    //   )
    // )
  }

  static getPagedAssets(tagId: number, crit: TagAssetsCriteria) {
    const t = new Tag()
    t.id = tagId
    return t.getPagedAssetIds(crit)
  }

  static findOrCreate(tagPath: TagPath): Tag {
    if (isEmpty(tagPath)) {
      throw new Error("empty tagPath")
    }
    // const path = joinTagPath(tagPath, "/")
    const prior = this.findByPath(tagPath) as Tag
    // this.logger().debug("_findOrCreate(" + path + ")", { prior })
    if (prior != null) {
      return prior
    }

    const newTag = this.#newTag(tagPath)
    const parent =
      newTag.depth <= 1 ? undefined : this.findOrCreate(tagPath.slice(0, -1))
    map(parent, p => (newTag.parentId = p.id!))

    const result = Tag.cacheTag(this.ops().upsertOne(newTag))!
    // this.logger().debug("_findOrCreate(" + path + ") upserted", {
    //   newTag,
    //   result,
    //   parent
    // })
    return result
  }

  id?: number
  parentId?: number | null
  ordinal?: number

  // The _path is required and must be unique. It's used for navigation.
  _path!: string

  // "_displayName" is optional, and used when the "canonical" name differs from
  // the "display" name.

  // "_displayName" allows for more friendly tag names in the UI (for
  // month name localization or to replace the "volsha" in filesystem paths with the label
  // of the device).

  _displayName?: string
  description?: string
  releasedAt?: number
  assetCount?: number

  parent?: Tag
  root?: Tag
  children?: Tag[]
  ancestors?: Tag[]
  assets?: Asset[]

  /**
   * Holds the last-fetched related assets from `getRelatedAssets`
   */
  relatedAssets?: Asset[]
  nextBatchUrl?: string

  clear() {
    this.parent = undefined
    this.root = undefined
    this.children = undefined
    this.ancestors = undefined
    this.assets = undefined
    this.relatedAssets = undefined
    this.ancestorIds.unset()
    return this
  }

  $afterUpsert() {
    Tag.cacheTag(this)
  }

  siblingPath(name: string) {
    return joinTagPath([...this.parentPath, name])
  }

  changeName(name: string) {
    return this.changePath(this.siblingPath(name))
  }

  maybeUpsertDisplayName(displayName: Maybe<string>) {
    return blank(displayName) || displayName === this._displayName
      ? undefined
      : this.upsert({ _displayName: displayName } as any)
  }

  changePath(newPath: string, finallyClear = true) {
    const priorPath = this._path

    // If a sibling already exists, they win.
    const existingSibling = Tag.findByPath(splitTagPath(newPath))

    this.logger().info("changePath(): ", {
      priorPath,
      newPath,
      existingSibling
    })

    try {
      if (existingSibling == null) {
        Tag.dbl.runf(q =>
          q.whereLike("_path", priorPath + "%").update({
            updatedAt: Date.now(),
            _path: knex().raw("REPLACE(_path, ?, ?)", [priorPath, newPath])
          })
        )
        this._path = newPath
      } else {
        // OH NOES. I'm about to get deleted. Give my children to existingSibling.
        newPath = existingSibling._path // < deal with case insensitivity
        this.logger().info("changePath(): replacing with sibling", newPath)

        // Make sure my assets are still tagged:
        AssetTag.dbl.upsert(q =>
          q.where({ tagId: this.id }).update({ tagId: existingSibling.id })
        )
        AssetTag.dbl.runf(q => q.where({ tagId: this.id }).delete())

        // TODO: make this one big batch command instead of doing it in node:
        for (const ea of this.getChildren()) {
          ea.changePath(joinTagPath([...existingSibling.path, ea.name]), false)
        }

        // At this point my child tags will have been either deleted, if they were duplicated by `existingSibling`, or they need to be re-parented:
        Tag.dbl.runf(q =>
          q
            .where({ parentId: this.id })
            .update({ parentId: existingSibling.id })
        )
        this.delete()
      }
    } finally {
      if (finallyClear) Tag.clear()
    }
  }

  get name(): string {
    const p = this.path
    return p[p.length - 1]
  }

  get path(): string[] {
    return splitTagPath(this._path)
  }

  get displayName() {
    return toNotBlank(this._displayName) ?? this.name
  }

  // Watch out for the root tag: it should be [], not [""]!
  readonly displayPath: MemoizedThunk<string[]> = lazy(() => {
    const p = this.getParent()
    return compactBlanks([
      ...(p == null ? [] : p.displayPath()),
      this.displayName
    ])
  })

  get isRoot(): boolean {
    return this.id === 0
  }

  get depth(): number {
    return this.path.length
  }

  get parentPath(): string[] {
    return this.path.slice(0, -1)
  }

  get sortBy() {
    return [
      this.depth,
      this.ordinal == null ? 2 ** 51 : this.ordinal,
      this.path.map(ea => ea.toLowerCase())
    ]
  }

  $childrenQuery() {
    return orderBy(Tag.query().where({ parentId: this.id }))
  }

  $assetsQuery() {
    return Asset.shownUnhidden()
      .columns("Asset.*")
      .join("AssetTag", "AssetTag.assetId", "Asset.id")
      .where({ tagId: this.id! })
  }

  $selectAssetIdColumns(qb: Knex.QueryBuilder): Knex.QueryBuilder {
    return qb.clearSelect().select({
      assetId: "Asset.id",
      capturedAtLocal: "Asset.capturedAtLocal",
      updateCount: "Asset.updateCount",
      durationMs: "Asset.durationMs"
    })
  }

  $assetIdsQuery() {
    return this.$selectAssetIdColumns(this.$assetsQuery())
  }

  setAncestors(a: Tag[]) {
    this.ancestors = a
    this.parent = a[a.length - 1]
    map(this.parent, p => p.setAncestors(a.slice(0, -1)))
  }

  async toApiPathElements(): Promise<PathTag[]> {
    return thenCollect(this.getAncestorsAndSelf(), ea => ea.toPathTag())
  }

  toPathTag(): PathTag {
    return {
      tagPath: this.path,
      displayName: this.displayName
    }
  }

  toApiTag(): ApiTag {
    return {
      tagId: this.id!,
      tagPath: this.path,
      displayPath: this.displayPath(),
      description: this.description,
      assetCount: this.assetCount!
      // assetFileCount: this.assetFileCount!
    }
  }

  toStringId() {
    return "Tag(" + this.path.join("/") + ")"
  }

  getParent() {
    if (this.isRoot) return undefined
    if (this.parentId != null && this.parent == null) {
      return (this.parent = Tag.findById(this.parentId))
    } else {
      return this.parent
    }
  }

  getPagedChildren(crit: ChildTagCriteria): Tag[] {
    if (this.id === 0) {
      // Root tag:
      const begin = crit.offset ?? 0
      const end = begin + (crit.limit ?? this.children!.length)
      return [...this.children!].slice(begin, end)
    }
    const q = this.$childrenQuery()
    mapGt0(crit.offset, offset => {
      void q.offset(offset)
    })
    mapGt0(crit.limit, limit => void q.limit(limit))
    const result = Tag.ops().all(q)
    for (const ea of result) Tag.cacheTag(ea)
    return result
  }

  getChildrenCount(): number {
    return this.children != null
      ? this.children.length
      : (Tag.dbl.pluckFirstf(q =>
          (this.isRoot
            ? q.whereNull("parentId")
            : q.where({ parentId: this.id })
          ).count()
        ) as number)
  }

  getChildren() {
    if (!this.isRoot) {
      this.children = Tag.ops().all(this.$childrenQuery())
      this.children.forEach(childTag => {
        childTag.parent = this
      })
    }
    return this.children!
  }

  link(): string {
    return "/tag/" + this.path.map(encodeURIComponent).join("/")
  }

  get rootName() {
    return this.path[0]
  }

  get ancestorsAndSelf(): Tag[] {
    return [...this.ancestors!, this]
  }

  /**
   * @return nearest parent last
   */
  getAncestors() {
    return (this.ancestors ??= this._getAncestors())
    //   const result = this._getAncestors()
    //   const
    //   const ancestorPaths = ancestry(this.parentPath).map(ea => joinTagPath(ea))
    //   const cachedAncestors = ancestorPaths.map(ea =>
    //     Tag.recentTagsByPath.get(ea)
    //   )
    //   if (cachedAncestors.every(ea => ea != null)) {
    //     return (this.ancestors = cachedAncestors as Tag[])
    //   }
    //   const q = Tag.query().whereIn("_path", ancestorPaths).orderBy("_path")
    //   this.ancestors = Tag.ops().all(q)
    //   Tag.cacheTags(this.ancestors)

    //   this.parent = orElse(
    //     this.parent,
    //     this.ancestors[this.ancestors.length - 1]
    //   )
    // }
    // return this.ancestors
  }

  getAncestorsAndSelf() {
    return [...this.getAncestors(), this]
  }

  /**
   * Doesn't cache ancestors.
   * @return nearest parent last: [..., grandParent, parent]
   */
  _getAncestors() {
    if (this.parentId === 0 || this.parentId == null) return []
    return Tag.ops().all({
      sql: compressWhitespace(
        "WITH RECURSIVE cte(id, level) AS (",
        "  VALUES (:tagId, 0)",
        "  UNION ALL",
        "  SELECT Tag.parentId as id, cte.level + 1 as level",
        "  FROM Tag, cte",
        "  WHERE Tag.id = cte.id",
        ")",
        "SELECT Tag.*",
        "FROM Tag JOIN cte on cte.id = Tag.id",
        "WHERE Tag.id <> :tagId",
        "ORDER BY cte.level DESC"
      ),
      bindings: { tagId: this.id }
    })
  }

  /**
   * @return nearest parentId last: [..., grandParentId, parentId]
   */
  readonly ancestorIds = lazy(() => {
    if (this.parentId === 0 || this.parentId == null) return []
    return Tag.dbl.pluckAll<number>({
      sql: compressWhitespace(
        "WITH RECURSIVE cte(id, level) AS (",
        "  VALUES (:tagId, 0)",
        "  UNION ALL",
        "  SELECT Tag.parentId as id, cte.level + 1 as level",
        "  FROM Tag, cte",
        "  WHERE Tag.id = cte.id",
        ")",
        "SELECT Tag.id",
        "FROM Tag JOIN cte on cte.id = Tag.id",
        "WHERE Tag.id <> :tagId",
        "ORDER BY cte.level DESC"
      ),
      bindings: { tagId: this.id }
    })
  })

  getPagedAssetIds(crit: TagAssetsCriteria): AssetId[] {
    let q = this.$assetIdsQuery()

    q = q.limit(ThumbsPerSample)
    if (gt0(crit.offset)) {
      q = q.offset(crit.offset)
    }
    q = q.orderBy([
      { column: "capturedAtLocal", order: "desc" },
      { column: "assetId" }
    ])
    return compact(Asset.dbl.all(q).map(toAssetId))
  }

  getAssets() {
    // reverse chron:
    const q: any = this.$assetsQuery().orderBy("capturedAtLocal", "desc")
    return Asset.ops().all(q)
  }

  async assetCountDesc(assets?: Asset[]) {
    const assetCount = this.assetCount
    return (
      (assetCount == null ||
      assets == null ||
      assets.length === assetCount ||
      assets.length === 0
        ? ""
        : fmt(assets.length) + " of ") + plur(assetCount!, "asset")
    )
  }

  readonly firstNonInterstitialTagId: MemoizedThunk<number> = lazy(() => {
    if (
      !this.isRoot &&
      0 === this.directAssetCount() &&
      1 === this.getChildrenCount()
    ) {
      const arr = this.getChildren()
      if (arr?.length === 1) return arr[0].firstNonInterstitialTagId()
    }
    return this.id!
  })

  readonly directAssetCount = lazy(() =>
    this.id === 0
      ? 0
      : Asset.dbl.pluckFirst<number>(
          Asset.shownUnhidden()
            .join("AssetTag", "AssetTag.assetId", "Asset.id")
            .where("AssetTag.tagId", this.id)
            .countDistinct("Asset.id")
        )
  )

  getAssetCount(): Promise<number> {
    // fetching asset file counts more than doubles calculation time:
    return this.id === 0
      ? (Tag.dbl.pluckFirst({
          sql: compressWhitespace(
            "SELECT",
            "  count(DISTINCT Asset.id) AS assetCount",
            // "  count(DISTINCT AssetFile.id) AS assetFileCount",
            "FROM",
            "  Asset",
            // "  JOIN AssetFile ON AssetFile.assetId = Asset.id",
            "WHERE",
            "  Asset.shown = 1",
            "  AND Asset.excluded = 0",
            "  AND Asset.hidden = 0",
            "  AND Asset.deletedAt IS NULL"
          )
        }) as any)
      : Tag.dbl.pluckFirst({
          sql: AssetCountForTag,
          bindings: { tagId: this.id }
        })
  }

  $assetStreamQuery(asset: Asset, limit: number, op: "<" | "=" | ">") {
    let qb =
      this.path[0] === TagRoots.When
        ? Asset.shownUnhidden()
        : this.$assetsQuery()
    qb = qb
      .distinct()
      .where("capturedAtLocal", op, asset.capturedAtLocal)
      .andWhereNot("Asset.id", asset.id)
      .limit(op === "=" ? limit * 2 : limit) // in case both sides of the stream are all from the same time (!!!)

    if (op === "=") {
      return qb.orderByRaw(`ABS(Asset.id-${asset.id})`)
    } else {
      return qb.orderBy([
        { column: "capturedAtLocal", order: op === ">" ? "asc" : "desc" },
        "Asset.id"
      ])
    }
  }

  getAssetStream(asset: Asset, limit: number): TaggedAssetStream {
    limit = gt0(limit) ? limit : BeforeAfterStreamLimit
    const [before, same, after] = (["<", "=", ">"] as const).map(op =>
      compact(
        Asset.ops()
          .all(this.$assetStreamQuery(asset, limit, op))
          .map(toAssetId)
      )
    )
    for (const ea of same) {
      if (ea.assetId! < asset.id!) {
        before.push(ea)
      } else {
        after.push(ea)
      }
    }
    return new TaggedAssetStream([this], before, after.reverse(), limit)
  }

  getRelatedAssetIds({
    seed,
    limit = ThumbsPerSample
  }: {
    seed: number
    limit?: number
  }): AssetId[] {
    const max = Math.max(256, assetCount())
    const results = compact(
      AssetTag.dbl
        .allf(q =>
          Asset.shownUnhidden(this.$selectAssetIdColumns(q))
            .distinct()
            .join("Asset", "Asset.id", "AssetTag.assetId")
            .join("Tag", "Tag.id", "AssetTag.tagId")
            // TODO: is this faster?
            // .whereIn("AssetTag.assetId", await this.selfAndDescendantIds(txn))
            .whereLike("_path", this._path + "%")
            .orderByRaw(
              prngOrderByClause(seed + orElse(this.id, 0), "Asset.id", max)
            )
            .limit(limit)
        )
        .map(toAssetId)
    )

    this.logger().debug(
      this.path + ": Found " + results.length + " related assets"
    )
    return results
  }

  get attrs(): Record<string, string> {
    return { href: this.link(), title: this.name }
  }
}

const assetCount = lazy(
  () => Asset.dbl.pluckFirstf<number>(q => q.count()),
  minuteMs
)
