import { lazy } from "../../core/Lazy"
import { compactBlanks, isEmpty } from "../../fe/Array"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { mkTagUri } from "../../fe/app/FullPaths"
import { hostname } from "../net/Hostname"
import { myExternalIp4Addresses } from "../net/ip"
import { Settings } from "../settings/Settings"
import { tagPathToStringArray } from "../tags/TagPaths"
import { nativePath2uri } from "../uri/FileURI"
import { URI, toURI } from "../uri/URI"
import { uriToTagPath } from "./UriToTagPath"

export default function myWebUrls(
  port = Settings.httpPort.valueOrDefault
): URI[] {
  const hostnames = [Settings.localhost.valueOrDefault]
  if (Settings.exposeNetworkWithoutAuth.valueOrDefault) {
    hostnames.push(...compactBlanks([myExternalIp4Addresses()[0], hostname()]))
  }
  const base = toURI("http://a")

  return hostnames.map(ea => base.with({ authority: ea + ":" + port }))
}

export const myWebUrl = lazy(() => myWebUrls()[0])

export async function mkFolderUrl(nativePath: string) {
  const uri = await nativePath2uri(nativePath)
  const tagPath = uriToTagPath({ uri, isFile: false })
  if (isEmpty(tagPath)) return
  const change = mkTagUri(tagPathToStringArray(tagPath))
  return myWebUrl()?.with(change)
}

export function mkAssetUrl(assetId: Maybe<number>): Maybe<URI> {
  return gt0(assetId)
    ? myWebUrl()?.with({ path: "/asset/" + assetId })
    : undefined
}
