import { durationHMS } from "../../core/date/Date"
import { AssetId } from "../../fe/api/Asset"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0, isNumber, toGt0 } from "../../fe/Number"
import { DbValued } from "../db/DbValued"

export function toAssetId(
  obj:
    | Maybe<{
        id?: number
        assetId?: number
        capturedAtLocal: number
        updatedAt?: number
        updateCount?: number
        v?: number
        durationMs?: number
      }>
    | DbValued
): Maybe<AssetId> {
  const assetId = toGt0(obj?.assetId) ?? toGt0(obj?.id)
  if (obj == null || assetId == null || !isNumber(obj?.capturedAtLocal)) return
  const v =
    (toGt0(obj.v) ?? toGt0(obj.updateCount) ?? toGt0(obj.updatedAt) ?? 0) % 1e6
  const result: AssetId = { assetId, capturedAtLocal: obj.capturedAtLocal, v }
  if (gt0(obj.durationMs)) {
    result.durationHMS = durationHMS(obj.durationMs)
  }
  return result
}
