import { ExifDateTime } from "exiftool-vendored"
import { DateTime } from "luxon"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { isObject } from "../../fe/Object"
import { DateInterval } from "./DateInterval"
import { Dated } from "./Dated"

/**
 * @return true iff `d` is a class type that supports time resolution
 */
export function hasTime(
  d: Maybe<Dated>
): d is Date | ExifDateTime | DateTime | DateInterval {
  if (!isObject(d)) return false

  // We can't do any safe heuristics here:
  if (d instanceof Date) return true

  const a = d as any
  return gt0(a.hour) || gt0(a.minute) || gt0(a.second) || gt0(a.millisecond)
}

export function isExifDateTime(d: any): d is ExifDateTime {
  return d instanceof ExifDateTime
}
