import { errorToS } from "../../core/error/Error"
import { blank } from "../../fe/Blank"
import { isError } from "../../fe/isError"

export const _dbSetupErrors = new Set<string>()
const listeners: (() => void)[] = []

export function dbSetupErrors() {
  return [..._dbSetupErrors.values()]
}
/**
 * Allow out-of-caller-stack errors to be added to the modelDbHealthCheck
 */

export function addDbSetupError(error: Error | any) {
  if (isError(error) || !blank(error)) {
    _dbSetupErrors.add(errorToS(error))
    for (const ea of listeners) ea()
  }
}

export function clearDbSetupErrors({
  notifyListeners = true
}: { notifyListeners?: boolean } = {}) {
  _dbSetupErrors.clear()
  if (notifyListeners) for (const ea of listeners) ea()
}

export function hasDbSetupErrors() {
  return _dbSetupErrors.size > 0
}

export function addDbSetupErrorListener(listener: () => void) {
  listeners.push(listener)
}
