import { Database } from "better-sqlite3"
import { blank } from "../../fe/Blank"
import { MultiMap } from "../../fe/MultiMap"

export interface TableInfo {
  readonly tableName: string
  readonly columns: ColumnInfo[]
}

export interface ColumnInfo {
  readonly name: string
  readonly type: string
  readonly notnull: boolean
  readonly pk: boolean
}

export function parseTableInfo(
  arr: {
    tableName: string
    name: string
    type: string
    notnull: number
    pk: number
  }[]
): TableInfo[] {
  const mm = new MultiMap<string, ColumnInfo>()
  for (const row of arr) {
    if (
      !row.tableName.includes("fts") &&
      !row.tableName.includes("sqlite_") &&
      !blank(row.name) &&
      !blank(row.type)
    ) {
      mm.add(row.tableName, {
        name: row.name,
        type: row.type.toUpperCase().trim().replace(/\s+/g, ""),
        notnull: row.notnull === 1,
        pk: row.pk === 1
      })
    }
  }
  return mm
    .entriesArray()
    .map(([tableName, columns]) => ({ tableName, columns }))
}

const sql = `SELECT 
m.tbl_name as tableName,
p.*
FROM sqlite_master AS m
JOIN pragma_table_info(m.name) AS p
ORDER BY m.tbl_name, p.cid`

export function localTableInfo(db: Database): TableInfo[] {
  return parseTableInfo(db.prepare(sql).all() as any[])
}
