import { ExifDate, ExifDateTime } from "exiftool-vendored"
import { DateTime } from "luxon"
import { lazy } from "../../core/Lazy"
import { compact } from "../../fe/Array"
import { dayMs, hourMs } from "../../fe/Date"
import { later } from "../../fe/Delay"
import { Maybe } from "../../fe/MaybeTypes"
import { gt0 } from "../../fe/Number"
import { gt, gte } from "../../fe/Primitive"
import { mkLogger } from "../Logger"
import { within } from "../Number"
import { errorToS } from "../error/Error"
import { ee } from "../event/EventEmitter"
import { Settings } from "../settings/Settings"
import { DateInterval } from "./DateInterval"
import { Dated, isDated, isoToDated } from "./Dated"
import { datedToMillis } from "./DatedToMillis"
import { FuzzyDate, datedToISO } from "./FuzzyDate"
import { datedToLocal } from "./Localtime"

const logger = lazy(() => mkLogger("date.ValidDate"))

const badDates = lazy(() =>
  compact(Settings.badDates.values.map(ea => isoToDated(ea)))
)
const badDatesISO = lazy(() => compact(badDates().map(ea => datedToISO(ea))))
const badDatesLocal = lazy(() =>
  compact(badDates().map(ea => datedToLocal(ea)))
)
const badDatesTs = lazy(() => compact(badDates().map(ea => datedToMillis(ea))))

export function isValidDate(d: Dated | any): d is Dated {
  if (d == null) return false
  const why = whyNotValidDate(d)
  return logger().tap({
    msg: "isValidDate()",
    result: why == null,
    meta: { d, why }
  })
}

export function whyNotValidDate(d: Dated) {
  try {
    if (!isDated(d)) return "not Dated"
    else if (
      d instanceof ExifDateTime ||
      d instanceof DateInterval ||
      d instanceof DateTime ||
      d instanceof FuzzyDate
    ) {
      if (!d.isValid)
        return compact(["not valid", (d as any)?.invalidExplanation]).join(": ")
    } else if (d instanceof Date) {
      if (isNaN(d.getTime())) return "not valid js Date"
    } else if (d instanceof ExifDate) {
      if (!validYMD(d.year, d.month, d.day)) {
        return "not valid YMD"
      }
    } else {
      const dt = DateTime.fromObject(d)
      if (!dt.isValid) {
        return "not valid: " + dt.invalidExplanation
      }
    }

    if (badDatesTs().includes(datedToMillis(d)!)) {
      return "bad date (via millis)"
    } else if (badDatesLocal().includes(datedToLocal(d))) {
      return "bad date (via localtime)"
    } else if (badDatesISO().includes(datedToISO(d)!)) {
      return "bad date (via ISO)"
    } else {
      return
    }
  } catch (error) {
    logger().warn("whyNotValidDate() caught error", { input: d, error })
    return errorToS(error)
  }
}

export function mapValidDate<D extends Dated, T>(
  obj: Maybe<D>,
  f: (date: D) => T
): Maybe<T> {
  return isValidDate(obj) ? f(obj) : undefined
}

const minYear = lazy(() => Settings.minValidYear.valueOrDefault, hourMs)
const maxYear = lazy(() => new Date(Date.now() + dayMs).getFullYear(), hourMs)
const maxMonth = lazy(() => new Date(Date.now() + dayMs).getMonth() + 1, hourMs)
later(() => {
  Settings.minValidYear.watchLater(() => minYear.unset())
  ee().on("clearCache", () => {
    minYear.unset()
    maxYear.unset()
    maxMonth.unset()
  })
})

export function validYear(year: Maybe<number>) {
  return within(minYear(), maxYear(), year)
}

export function validMonth(month: Maybe<number>, year?: number) {
  return gte(year, maxYear()) && gt(month, maxMonth())
    ? false
    : within(1, 12, month)
}
/**
 * @return true iff the given month and day values are valid for the given year
 */

export function validDay(
  year: Maybe<number>,
  month: Maybe<number>,
  day: Maybe<number>
) {
  // If Date doesn't change the day, that month and day exist for that year.
  return gt0(day) && DateTime.fromObject({ year, month, day }).isValid
}

export function validYMD(year?: number, month?: number, day?: number): boolean {
  return (
    validYear(year) &&
    (gt0(month) ? validMonth(month, year) : true) &&
    (gt0(day) ? validDay(year, month, day) : true)
  )
}
