import { lazy } from "../../core/Lazy"
import { blank } from "../../fe/Blank"
import { PromiseMaybe } from "../../fe/MaybeTypes"
import { mkLogger } from "../Logger"
import { ShortCommandTimeoutMs } from "../ShortCommandTimeoutMs"
import { stdout_ } from "../child/ChildProcess"
import { isReadableDirectory } from "../fs/Stat"
import { LogLevels } from "../log/LogLevel"
import { isMac } from "../platform/Platform"

const logger = lazy(() => mkLogger("dir.DefaultApplePhotosLibrary"))

const ReadPhotoLibraryDir = [
  "read",
  "com.apple.photolibraryd",
  "SystemLibraryPath"
]

export async function defaultApplePhotosLibrary(): PromiseMaybe<string> {
  if (!isMac) return
  try {
    const result = await stdout_("defaults", ReadPhotoLibraryDir, {
      timeoutMs: ShortCommandTimeoutMs
    })
    if (blank(result)) {
      logger().warn(
        `"defaults ${ReadPhotoLibraryDir.join(" ")}" returned blank (!!)`
      )
      return
    } else {
      const isReadable = await isReadableDirectory(result)
      return logger().tap({
        msg: "defaultApplePhotosLibrary()",
        level: LogLevels.info,
        result: isReadable ? result : undefined,
        meta: { isReadable }
      })
    }
  } catch (error) {
    logger().warn("defaultApplePhotosLibrary()", error)
    return
  }
}
