import { Maybe } from "../../fe/MaybeTypes"
import { entries, Valued } from "../../fe/Object"
import { negateFilterName } from "./PredicateNames"

export interface SyncPredicate<A> {
  (a: A): boolean | null
}

export type SyncPredicateBundle<A> = Valued<SyncPredicate<A>>[]

export class SyncPredicates {
  static firstFalse<A>(
    a: A,
    ...predicates: SyncPredicateBundle<A>
  ): Maybe<string> {
    for (const obj of predicates) {
      for (const [k, v] of entries(obj)) {
        const result = v(a)
        if (false === result) {
          return k
        }
      }
    }
    return
  }

  static firstTrue<A>(
    a: A,
    ...predicates: SyncPredicateBundle<A>
  ): Maybe<string> {
    for (const obj of predicates) {
      for (const [name, ea] of entries(obj)) {
        if (true === ea(a)) return name
      }
    }
    return
  }

  static firstDefined<A>(
    a: A,
    ...predicates: SyncPredicateBundle<A>
  ): Maybe<{ name: string; result: boolean }> {
    for (const obj of predicates) {
      for (const [name, ea] of entries(obj)) {
        const result = ea(a)
        if (result != null) return { name, result }
      }
    }
    return
  }

  static accepted<A>(a: A, ...predicates: SyncPredicateBundle<A>): boolean {
    return null == SyncPredicates.firstFalse(a, ...predicates)
  }

  static whyRejected<A>(
    a: A,
    ...predicates: SyncPredicateBundle<A>
  ): string | undefined {
    return negateFilterName(SyncPredicates.firstFalse(a, ...predicates))
  }
}
