import { Maybe } from "../../fe/MaybeTypes"
import { wrap } from "../../fe/String"
import { cacheDir_ } from "../dir/CacheDir"
import { NoLibraryErrorFlag } from "../error/ErrorFlags"
import { PosixFile } from "../fs/PosixFile"
import { SimpleFileOrString } from "../fs/SimpleFile"
import { libraryUidStore } from "../fs/UIDStore"
import { writeTextfile_ } from "../fs/WriteFile"
import { Settings } from "../settings/Settings"
import { Schema, Schemas } from "./Schemas"

/**
 * The "cache" db is the local replica of the library database
 */
export async function cacheDbDir_(
  libraryDir: Maybe<SimpleFileOrString> = Settings.libraryDir.valueOrDefault,
  schema: Schema = Schemas.models
): Promise<PosixFile> {
  const store = libraryUidStore(libraryDir)
  if (store == null) {
    throw new Error("Library directory is not set" + NoLibraryErrorFlag)
  }

  // Adding the UUID ensures that other libraries don't overwrite eachother:
  const libraryUid = (await store.readUid_())!
  const result = PosixFile.for(cacheDir_()).join(
    schema + "-live-db",
    libraryUid
  )
  await result.mkdirp_()
  const readme = result.join("README.txt")
  await writeTextfile_(
    readme.nativePath,
    ...wrap(
      [
        "This folder is used by PhotoStructure for your library stored in",
        libraryDir,
        "You will corrupt your library if you remove this directory while PhotoStructure is running.",
        "See https://forum.photostructure.com/t/whats-ps-force-local-db-replica/837 for details."
      ].join("\n\n")
    )
  )
  return result
}
