import { EndableRanks } from "../../core/async/Endable"
import { EndableInterval } from "../../core/async/EndableInterval"
import { Schemas, SqliteBase } from "../../core/db/Schemas"
import { PosixFile } from "../../core/fs/PosixFile"
import { isTest } from "../../core/NodeEnv"
import { isStatsDbMigrator } from "../../core/ServiceNames"
import { minuteMs, secondMs } from "../../fe/Date"
import { Db } from "../db/Db"
import { statsDb } from "./StatsDb"

export async function statsDbJanitor(dbDir: PosixFile): Promise<Db> {
  const db = new Db(Schemas.stats, dbDir.join(SqliteBase))
  // stats are only opened by sync:
  if (isStatsDbMigrator()) {
    await db.migrate_()
    new EndableInterval({
      name: "statsDbJanitor vacuum",
      callback: () => db.vacuum(),
      intervalMs: isTest ? 20 * secondMs : 5 * minuteMs,
      rank: EndableRanks.service
    })
  }
  statsDb.set(db)
  return db
}
