import { ExifDateTime } from "exiftool-vendored"
import { ToISOTimeOptions, Zone, ZoneOptions } from "luxon"
import { lazy } from "../../core/Lazy"
import { map } from "../../fe/Maybe"
import { Maybe } from "../../fe/MaybeTypes"
import { clamp, toInt } from "../../fe/Number"
import { toS } from "../../fe/toS"
import { RegExpOptional, concatRegexp } from "../RegExp"
import { Dated, datedOverlap, datedToPrecisionMs } from "./Dated"
import { parseExifDateTime } from "./ExifDateTime"

// See https://en.wikipedia.org/wiki/ISO_8601#Time_intervals

const dateIntervalRE = lazy(() => {
  const startRE = /(?<start>\d{2,}[-+\dTZ:.,]+?)/i
  const endRE = /(?<end>\d{2}[-+\dTZ:.,]+)/
  const solidusRE = /(?<solidus>:--|\/)/
  const splitInfoRE = /\/(?<index>\d+)\/(?<splits>\d+)/
  return concatRegexp([
    /^/,
    startRE,
    solidusRE,
    endRE,
    RegExpOptional.from(splitInfoRE),
    /$/
  ])
})

function edtField<F extends keyof ExifDateTime>({
  start,
  end,
  middle,
  field
}: {
  start: ExifDateTime
  end: ExifDateTime
  middle: ExifDateTime
  field: F
}): Maybe<ExifDateTime[F]> {
  return start[field] != null && end[field] != null ? middle[field] : undefined
}

const Fields = [
  "year",
  "month",
  "day",
  "hour",
  "minute",
  "second",
  "millisecond"
] as const satisfies Readonly<(keyof ExifDateTime)[]>

export class DateInterval {
  static fromISO(s: string, defaultZone?: Maybe<string>): Maybe<DateInterval> {
    const m = dateIntervalRE().exec(toS(s).trim())
    const g = m?.groups

    return g == null
      ? undefined
      : DateInterval.for(
          parseExifDateTime(g.start, defaultZone),
          parseExifDateTime(g.end, defaultZone),
          toInt(g.index),
          toInt(g.splits)
        )
  }

  toJSON() {
    return {
      _ctor: "DateInterval",
      start: this.start?.toJSON(),
      end: this.end?.toJSON(),
      index: this.index,
      splits: this.splits
    }
  }

  static fromJSON(obj: ReturnType<DateInterval["toJSON"]>) {
    return this.for(
      map(obj.start, ea => ExifDateTime.fromJSON(ea)),
      map(obj.end, ea => ExifDateTime.fromJSON(ea)),
      obj.index,
      obj.splits
    )
  }

  static for(
    start: Maybe<ExifDateTime>,
    end: Maybe<ExifDateTime>,
    index = 0,
    splits = 1
  ): Maybe<DateInterval> {
    if (start == null || !start.isValid || end == null || !end.isValid) return
    splits = clamp(1, 1000, toInt(splits, { defaultValue: 1 })!)
    index = clamp(0, splits - 1, toInt(index, { defaultValue: 0 })!)

    if (start.hasZone && !end.hasZone && start.zone != null) {
      end = end.setZone(start.zone) ?? end
    } else if (!start.hasZone && end.hasZone && end.zone != null) {
      start = start.setZone(end.zone) ?? start
    }

    const startTs = start.toMillis()
    const durationMs = end.toMillis() - startTs
    const deltaMs = (durationMs / (splits + 1)) * (index + 1)
    const middle = start.plus({ milliseconds: deltaMs })

    // console.log("DateInterval.for", {
    //   start,
    //   end,
    //   index,
    //   splits,
    //   durationMs,
    //   duration: fmtDuration(durationMs),
    //   deltaMs,
    //   delta: fmtDuration(deltaMs),
    //   middle,
    //   middle_ISO: middle?.toISOString()
    // })

    return middle == null
      ? undefined
      : new DateInterval(start, middle, end, index, splits)
  }

  readonly year: Maybe<number>
  readonly month: Maybe<number>
  readonly day: Maybe<number>
  readonly hour: Maybe<number>
  readonly minute: Maybe<number>
  readonly second: Maybe<number>
  readonly millisecond: Maybe<number>

  private constructor(
    readonly start: ExifDateTime,
    readonly middle: ExifDateTime,
    readonly end: ExifDateTime,
    readonly index = 0,
    readonly splits = 1
  ) {
    ;[
      this.year,
      this.month,
      this.day,
      this.hour,
      this.minute,
      this.second,
      this.millisecond
    ] = Fields.map(field => edtField({ start, middle, end, field }))
  }

  get intervalMs() {
    return this.end.toMillis() - this.start.toMillis()
  }

  get precisionMs() {
    return (
      this.end.toMillis() -
      this.start.toMillis() +
      Math.max(datedToPrecisionMs(this.end), datedToPrecisionMs(this.start))
    )
  }

  toDate() {
    return this.middle.toDate()
  }

  toMillis() {
    return this.middle.toMillis()
  }

  get isValid(): boolean {
    // always validated prior to construction:
    return true
  }

  get zone() {
    return this.start.zone ?? this.end.zone
  }

  get hasZone() {
    return this.start.hasZone || this.end.hasZone
  }

  /**
   * This only complies with the ISO standard if the index and slots are 1.
   */
  toString(options: ToISOTimeOptions = {}): string {
    return (
      `${this.start.toISOString(options)}/${this.end.toISOString(options)}` +
      (this.index === 0 && this.splits === 1
        ? ""
        : `/${this.index}/${this.splits}`)
    )
  }

  toISOString(options: ToISOTimeOptions = {}) {
    return this.toString(options)
  }

  contains(d: Maybe<Dated>): boolean {
    return true === datedOverlap({ a: this, b: d })?.aContainsB
  }

  overlaps(d: Maybe<Dated>): boolean {
    return true === datedOverlap({ a: this, b: d })?.aOverlapsB
  }

  setZone(zone: string | Zone, opts?: ZoneOptions): DateInterval {
    return new DateInterval(
      this.start.setZone(zone, opts) ?? this.start,
      this.middle.setZone(zone, opts) ?? this.middle,
      this.end.setZone(zone, opts) ?? this.end,
      this.index,
      this.splits
    )
  }
}
