import { submitTask_ } from "../../core/event/SharedState"
import { PosixFile } from "../../core/fs/PosixFile"
import { SyncFileStates, syncReport } from "../../core/fs/SyncReport"
import { l, p } from "../../core/licensing/Licensing"
import { LogLevels } from "../../core/log/LogLevel"
import { Logger, mkLogger } from "../../core/Logger"
import { RequiredForceContext } from "../../core/settings/ForceContext"
import { pathToLibraryAsset } from "../../core/settings/PathToLibraryAsset"
import { Settings } from "../../core/settings/Settings"
import { readCapturedAt, readTags, sidecarEql } from "../../core/tags/ExifTool"
import { ensureInferredHistoryRecords } from "../../core/tags/History"
import { isMimeTypeIncluded } from "../../core/tags/MimeTypes"
import { readMimeType } from "../../core/tags/ReadMimeType"
import { mkAssetUrl } from "../../core/web/WebUrls"
import { compact, isEmpty, uniqBy } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { stringify } from "../../fe/JSON"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { toA } from "../../fe/toA"
import { PS_LIBRARY_PROTOCOL } from "../../fe/URI"
import { AssetFile } from "../model/AssetFile"

export async function mayCopyAssetsToLibrary() {
  return Settings.copyAssetsToLibrary.valueOrDefault && (await p())
}

/**
 * Stores all found asset files in date-prefixed folder hierarchies.
 */
export class AssetFileRepository {
  private readonly logger: Logger = mkLogger("AssetFileRepository")

  constructor(readonly originalsDir: PosixFile) {}

  async shouldCopy(file: Maybe<PosixFile>) {
    const whyNot = await this.whyNotCopyFile(file)
    return this.logger.tap({
      level: "info",
      msg: "shouldCopy()",
      result: whyNot == null,
      meta: {
        file: file?.nativePath,
        whyNot
      }
    })
  }

  async whyNotCopyFile(src: Maybe<PosixFile>) {
    if (src == null || blank(src.nativePath)) {
      return "blank nativePath"
    } else if (!Settings.copyAssetsToLibrary.valueOrDefault || (await l())) {
      return "copyAssetsToLibrary is false"
    } else if (await src.notExists({ refresh: true })) {
      return "file doesn't exist"
    } else if (src.isDescendantOf(this.originalsDir)) {
      return src + " already contained by " + this.originalsDir
    } else if (src.isDescendantOf(Settings.libraryDir.valueOrDefault)) {
      return src + " already contained by " + Settings.libraryDir.valueOrDefault
    }

    const mt = await readMimeType(src)
    if (mt == null) {
      return "readMimeType(" + src + ") was null"
      // And we don't need to worry about the sidecar, either.
    } else if (
      !isMimeTypeIncluded(mt, Settings.copyToLibraryMimeTypes.values)
    ) {
      return "mimetype " + mt + " is not included in copyToLibraryMimeTypes"
    }

    return null
  }

  async existingLibraryAssetFile(af: AssetFile) {
    if (af.uri.startsWith(PS_LIBRARY_PROTOCOL)) return af

    const candidates = uniqBy(
      toA(af.librarySiblings()?.filter(ea => ea.sha === af.sha)).concat(
        AssetFile.sameAssetFileInLibrary(af)
      ),
      ea => ea.id
    )
    for (const prior of candidates) {
      if (await prior.exists()) {
        if (prior.assetId !== af.assetId) {
          this.logger.warn(
            "Existing library asset file with a matching SHA is associated to a different asset. Requesting repair of the two impacted assets.",
            { af, prior }
          )
          const priorAssetId = prior.assetId
          af.getAsset()?.addAssetFile(prior)
          void submitTask_("repairAsset", { assetId: priorAssetId! })
        }
        this.logger.info(
          "whyNotCopyAssetFile(" + af.uri + "): found existing db record",
          {
            af,
            prior
          }
        )
        return prior
      }
    }
    return null
  }

  // Try to find a file with the same contents
  async findLibraryFileWithSameContents(src: PosixFile, dest: PosixFile) {
    if (await src.matchesContent(dest)) return dest
    let destDir = dest.parent()
    while (destDir.isSelfOrDescendantOf(this.originalsDir)) {
      const child = destDir.childWithSameContents(src)
      if (child != null) return child
      else destDir = destDir.parent()
    }
    return
  }

  /**
   * @throws if there are any errors
   */
  async importFile_(src: PosixFile): Promise<PosixFile> {
    // initial sanity check:

    const dest = await this.#importFile_(src)
    if (dest != null) {
      await this.#handleSidecars(src, dest)
      await this.#handleInferred(src, dest)
    }
    return dest
  }
  async #importFile_(src: PosixFile): Promise<PosixFile> {
    const ca = await readCapturedAt(src)
    const assetPath = pathToLibraryAsset(ca?.date, src)

    this.logger.debug("importFile(" + src + ")", {
      capturedAt: ca,
      assetPath: assetPath
    })

    if (assetPath == null) {
      throw new Error(src.nativePath + " failed to create a library path")
    }
    const dest = this.originalsDir.join(...assetPath)

    // Does a file in that subdirectory already have the same filesize and SHA?
    // This would only happen if the library db is not in sync with the library
    // directory.
    const existing = await this.findLibraryFileWithSameContents(src, dest)
    if (existing != null) {
      this.logger.debug("#importFile_(" + src + "): found existing file", {
        src,
        existing
      })
      return existing
    }

    const uniqDest = await dest.ensureNew_()
    this.logger.info("#importFile_(" + src + "): copying...", {
      uniqDest,
      dest
    })
    return src.copyFile_(uniqDest)
  }

  async #handleSidecars(src: PosixFile, dest: PosixFile): Promise<PosixFile[]> {
    const sidecars = await src.existingSidecars()
    const results = []
    for (const srcSidecar of sidecars) {
      results.push(await this.#handleSidecar(srcSidecar, dest))
    }
    return compact(results)
  }

  async #handleSidecar(srcSidecar: PosixFile, dest: PosixFile) {
    for (const destSidecar of await dest.existingSidecars()) {
      if (await sidecarEql(srcSidecar, destSidecar)) {
        this.logger.info("handleSidecar(): metadata already exists.", {
          srcSidecar,
          destSidecar,
          dest
        })
        // We don't touch to the same mtime, as they may be different file types.
        return
      }
    }
    const copyTo = await dest
      .parent()
      .join(dest.name + srcSidecar.ext)
      .ensureNew_({ emptyIsNew: true })
    return srcSidecar.copyFile_(copyTo)
  }

  async #handleInferred(src: PosixFile, dest: PosixFile) {
    if (src == null || dest == null || src.eql(dest)) return
    const t = await readTags(src)
    if (t == null) {
      this.logger.warn("#handleInferred(): Failed to read tags from " + src, {
        dest
      })
      return
    }
    // At least for now, only store inferred tags in the sidecar, so we know
    // in the future that the tag source is not necessarily trustworthy.
    return ensureInferredHistoryRecords(src, await dest.sidecar(), t.inferred)
  }

  /**
   * @return undefined for no-op, rejection for internal error, or new upserted AssetFile if copied
   */
  async maybeCopyToLibrary_(
    srcAssetFile: AssetFile,
    ctx: RequiredForceContext
  ): PromiseMaybe<AssetFile> {
    const start = Date.now()
    const src = await srcAssetFile.posixFile_()
    if (src == null) {
      this.logger.throw(
        "maybeCopyToLibrary_(" + srcAssetFile.uri + "): posixFile is null",
        {
          ignorable: false,
          retriable: false
        }
      )
    }
    {
      const why = await this.whyNotCopyFile(src)
      if (!blank(why)) {
        return this.logger.tap({
          level: LogLevels.info,
          msg: "maybeCopyToLibrary_(): not copying: " + why,
          result: undefined
        })
      }
    }

    const asset = srcAssetFile.getAsset()
    if (asset == null) {
      this.logger.throw(
        "maybeCopyToLibrary_(" + srcAssetFile.uri + "): null asset"
      )
    }

    {
      const prior = await this.existingLibraryAssetFile(srcAssetFile)
      if (prior != null) {
        const dest = await prior.posixFile_()
        if (dest == null) {
          this.logger.warn(
            "existingLibraryAssetFile() returned an assetFile that could not be resolved to a PosixFile",
            { prior }
          )
        } else {
          const copiedSidecars = await this.#handleSidecars(src, dest)
          const copiedInferredMetadata =
            null != (await this.#handleInferred(src, dest))
          const upsertNeeded =
            null != (await prior.upsertIfNeeded_(undefined, ctx.forceSync))
          const noop =
            !copiedInferredMetadata && isEmpty(copiedSidecars) && !upsertNeeded
          if (!noop) {
            syncReport().onProgress({
              path: dest.nativePath,
              from: "maybeCopyToLibrary()",
              state: SyncFileStates.synced,
              details:
                "Found and synced prior replica already in your library " +
                stringify({
                  src: src.nativePath,
                  upsertNeeded,
                  copiedSidecars,
                  copiedInferredMetadata
                }),
              elapsedMs: Date.now() - start,
              url: mkAssetUrl(asset.id)?.toString()
            })
          }
          return prior
        }
      }
    }

    const dest = await this.importFile_(src)
    if (dest == null) {
      this.logger.info("importFile() returned null", { srcAssetFile })
      return
    }
    if (dest.nativePath === src.nativePath) {
      this.logger.info("importFile() returned the same nativePath as src", {
        srcAssetFile,
        dest
      })
      return
    }

    const destAF = await asset.assetFileForFile_(dest, { skipUpsert: true })
    await destAF.updateFromShaSibling_(srcAssetFile)
    const result = destAF.upsert()
    syncReport().onProgress({
      path: dest.nativePath,
      from: "maybeCopyToLibrary()",
      state: SyncFileStates.copied,
      details: "Copied into your library from " + src.nativePath,
      elapsedMs: Date.now() - start,
      url: mkAssetUrl(asset.id)?.toString()
    })
    return result
  }
}
