import { map } from "../fe/Maybe"
import { Maybe } from "../fe/MaybeTypes"
import { BitZip } from "./math/Bits"
import { GeoRadix } from "./math/Radix"
import { within } from "./Number"

// See https://en.wikipedia.org/wiki/Geographic_coordinate_system
// and https://en.wikipedia.org/wiki/Decimal_degrees
// https://www.movable-type.co.uk/scripts/geohash.html

export interface GeoLocation {
  lat: number
  lon: number
}

const DefaultBitDepth = 30

function toNearestBitDepth(bitDepth: number) {
  return Math.floor(bitDepth / 5) * 5
}

export function validLat(latitude: Maybe<number>): latitude is number {
  return within(-90, 90, latitude) && latitude !== 0
}

export function validLon(longitude: Maybe<number>): longitude is number {
  return within(-180, 180, longitude) && longitude !== 0
}

export function validLatLon(
  latitude: Maybe<number>,
  longitude: Maybe<number>
): latitude is number {
  return validLat(latitude) && validLon(longitude)
}

export function locationToGeohash(
  loc?: Maybe<Partial<GeoLocation>>,
  bitDepth = DefaultBitDepth
): Maybe<string> {
  return geohash(loc?.lat, loc?.lon, bitDepth)
}

export function geohash(
  latitude?: number,
  longitude?: number,
  bitDepth = DefaultBitDepth
): Maybe<string> {
  return map(
    geohashNumeric(latitude, longitude, toNearestBitDepth(bitDepth)),
    i => GeoRadix.encode(i)
  )
}

export function geohashNumericShort(latitude?: number, longitude?: number) {
  return geohashNumeric(latitude, longitude, 30)
}

const GeoHashBitZip = new BitZip([
  {
    min: -180,
    max: 180
  },
  {
    min: -90,
    max: 90
  }
])

export function geohashNumeric(
  latitude?: number,
  longitude?: number,
  bitDepth = DefaultBitDepth
): Maybe<number> {
  return !validLatLon(latitude, longitude)
    ? undefined
    : // The geohash spec does bitmixing oddly, requiring this to be lon, lat :(
      GeoHashBitZip.zip([longitude!, latitude!], toNearestBitDepth(bitDepth))
}

export function ungeohash(
  geohashString: string,
  bitDepth: number = DefaultBitDepth
): Maybe<[number, number]> {
  return map(GeoRadix.decode(geohashString), ea => ungeohash_num(ea, bitDepth))
}

/**
 * @return [latitude, longitude]
 */
export function ungeohash_num(
  geohashNum: number,
  bitDepth: number = DefaultBitDepth
): [number, number] {
  return GeoHashBitZip.unzip(
    geohashNum,
    toNearestBitDepth(bitDepth)
  )?.reverse() as [number, number]
}

export function geoHashToLocation(
  geohashNum: number,
  bitDepth: number = DefaultBitDepth
): GeoLocation {
  const [lat, lon] = ungeohash_num(geohashNum, bitDepth)
  return { lat, lon }
}

const EarthDiaMeters = 12742000

export function geohashDistanceMeters(
  geohash1: Maybe<number>,
  geohash2: Maybe<number>
): Maybe<number> {
  if (geohash1 == null || geohash2 == null) return
  if (geohash1 === geohash2) return 0
  return distanceMeters(
    geoHashToLocation(geohash1),
    geoHashToLocation(geohash2)
  )
}

// http://www.movable-type.co.uk/scripts/latlong.html
export function distanceMeters(a: GeoLocation, b: GeoLocation) {
  const phi1 = (a.lat * Math.PI) / 180 // ,  in radians
  const phi2 = (b.lat * Math.PI) / 180
  const deltaPhi = ((b.lat - a.lat) * Math.PI) / 180
  const deltaLambda = ((b.lon - a.lon) * Math.PI) / 180
  const c =
    Math.sin(deltaPhi / 2) * Math.sin(deltaPhi / 2) +
    Math.cos(phi1) *
      Math.cos(phi2) *
      Math.sin(deltaLambda / 2) *
      Math.sin(deltaLambda / 2)
  const d = Math.atan2(Math.sqrt(c), Math.sqrt(1 - c))
  return EarthDiaMeters * d
}
