import { lazy } from "../../core/Lazy"
import { isNotEmpty } from "../../fe/Array"
import { minuteMs } from "../../fe/Date"
import { dmegapixels } from "../../fe/Dimensions"
import { MB } from "../../fe/Units"
import { isError } from "../../fe/isError"
import { toS } from "../../fe/toS"
import { mkLogger } from "../Logger"
import { endProcess, execFile } from "../child/ChildProcess"
import { errorToS } from "../error/Error"
import { DoNotSendErrorFlag, NonRetriableErrorFlag } from "../error/ErrorFlags"
import { WrappedError, toWrappedError } from "../error/WrappedError"
import { PosixFile } from "../fs/PosixFile"
import { dcrawEmuNativePath_ } from "../fs/Tools"
import { PullProgressObserver } from "../progress/PullProgressObserver"
import { Settings } from "../settings/Settings"
import { isLibrawMimeType } from "../tags/MimeTypes"
import { readMimeType } from "../tags/ReadMimeType"
import { dimensions } from "./FileDimensions"
import { readableToFile_ } from "./ImageCache"
import { ImageSize } from "./ImageSize"
import { dcrawTimeout } from "./SyncFileTimeout"
import { isIgnorableValidationError } from "./ValidationErrors"

const logger = lazy(() => mkLogger("img.libraw"))

export async function librawSupported(src: PosixFile): Promise<boolean> {
  // THIS SHOULD NOT CALL mimetype(), because we may already be trying to
  // service a readTags request for src.
  try {
    return isLibrawMimeType(await readMimeType(src.nativePath))
  } catch (err) {
    logger().warn("librawSupported(" + src + "): failed to read filetype", err)
    return false
  }
}

export async function raw2tiff_(src: PosixFile) {
  return readableToFile_({
    src,
    desc: "dcraw", // < this becomes the basename
    suffix: ".tiff",
    f: dest => dcraw_emu_(src, dest)
  })
}

const TiffOutput = ["-T"]
const WriteToStdout = ["-Z", "-"]
// -o [0-6]  Output colorspace (raw,sRGB,...):
const Colorspace = ["-o", "1"]
// disable auto-flip/auto-rotate, because libraw is sometimes wrong:
const IgnoreOrientation = ["-t", "0", "-j"]

/**
 * Tell dcraw to convert src to dest (as a TIFF)
 */
export async function dcraw_emu_(
  src: PosixFile,
  dest: PosixFile
): Promise<void> {
  // NOTE: this is already time()d by sharpReadable
  const start = Date.now()

  const srcDim = await dimensions(src)
  if (srcDim == null) {
    return logger().throw(
      "Cannot decode RAW " +
        src +
        ": no EXIF dimensions." +
        DoNotSendErrorFlag +
        NonRetriableErrorFlag
    )
  }

  const fitDim = ImageSize.largestFit().outputSize(srcDim)

  const maybeHalfSize: string[] = []

  if (fitDim != null && 4 * dmegapixels(fitDim) < dmegapixels(srcDim)) {
    logger().debug("Large original source: using -h")
    maybeHalfSize.push("-h")
  }

  const cmd = await dcrawEmuNativePath_()
  const args = [
    ...TiffOutput,
    ...WriteToStdout,
    ...Colorspace,
    ...maybeHalfSize,
    ...IgnoreOrientation,
    ...Settings.dcrawEmuArgs.values,
    src.nativePath
  ]
  const timeout = 5 * minuteMs
  const opts = {
    encoding: "buffer",
    timeout,
    maxBuffer: 250 * MB
  }
  logger().debug("dcraw_emu()", { cmd, args, opts })
  const childProc = execFile(cmd, args, timeout, opts)
  const errors: Error[] = []

  function onError(err: Error | string | Buffer) {
    const msg = isError(err) ? errorToS(err) : toS(err)
    if (isIgnorableValidationError(msg)) {
      logger().info("dcraw_emu():  warning", { src, msg })
    } else {
      logger().warn("dcraw_emu(): error", { src, msg })
      const e = isError(err) ? err : toWrappedError(msg, { fatal: false })
      errors.push(e)
    }
  }
  childProc.on("error", onError)
  childProc.stderr!.on("data", onError)

  // The timeout is very pessimistic:
  const expectedElapsedMs = dcrawTimeout(await src.size()) / 7

  const obs = new PullProgressObserver(
    { path: src.nativePath, op: "Converting raw image" },
    expectedElapsedMs,
    () => Date.now() - start
  )

  childProc.on("close", () => obs.end())
  await dest.writeStream_(childProc.stdout!)
  if (isNotEmpty(errors)) {
    throw new WrappedError("Failed to convert RAW image " + src, {
      causes: errors,
      path: src.nativePath
    })
  }

  void endProcess(childProc)

  return
}
