import { lazy } from "../../core/Lazy"
import { localeSync } from "../../core/Locale"
import { mapGt0 } from "../../core/Number"
import { stripSuffix } from "../../core/String"
import { Dated } from "../../core/date/Dated"
import { getDay, getMonth, getYear } from "../../core/date/GetDateField"
import { ee } from "../../core/event/EventEmitter"
import { PosixFile } from "../../core/fs/PosixFile"
import { Settings } from "../../core/settings/Settings"
import {
  CapturedAt,
  bestCapturedAt,
  capturedAtSrcFromStat
} from "../../core/tags/CapturedAt"
import { readTags } from "../../core/tags/ExifTool"
import { isEmpty, range } from "../../fe/Array"
import { map } from "../../fe/Maybe"
import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { within } from "../../fe/Number"
import { TagPath, TagRef, TagRoots } from "../../fe/api/Tag"
import { toS } from "../../fe/toS"

const dtf = lazy(
  () =>
    new Intl.DateTimeFormat(localeSync(), {
      month: "short"
    })
)

const months = lazy(() => {
  const f = dtf()
  // There are rendering bugs (!!) for 1998-4-1, 2003-4-1, 1998-11-1, 2003-11-1,
  // ... so we just capture the month names once for a known-correct year.
  return range(0, 12, i => f.format(new Date(2016, i, 1))).map(ea =>
    // Some locales (like en-CA) end with period:
    stripSuffix(ea, ".")
  )
})

ee().on("clearCache", () => {
  dtf.unset()
  months.unset()
})

export function yearToOrdinal(year: number): number {
  // Y10K COME AT ME BRO
  return 10000 - year
}

export function monthToOrdinal(oneIndexedMonth: number): number {
  return 13 - oneIndexedMonth
}

export function ordinalToMonth(monthTagOrdinal: number): number {
  return 13 - monthTagOrdinal
}

export function dayToOrdinal(oneIndexedMonth: number): number {
  return 33 - oneIndexedMonth
}

export function yearTagRef(year: number): Maybe<TagRef> {
  return mapGt0(year, ea => ({ name: toS(ea), ordinal: yearToOrdinal(ea) }))
}

export function monthTagRef(oneIndexedMonth: Maybe<number>): Maybe<TagRef> {
  if (!within(1, 12, oneIndexedMonth)) return
  return map(months()[oneIndexedMonth - 1], monthName =>
    // ordinal makes months order in reverse chron:
    ({
      name: String(oneIndexedMonth),
      displayName: monthName,
      ordinal: monthToOrdinal(oneIndexedMonth)
    })
  )
}

export function dayTagRef(day: Maybe<number>): Maybe<TagRef> {
  return mapGt0(day, ea => ({ name: toS(ea), ordinal: dayToOrdinal(ea) }))
}

export function dateTag(date: Maybe<Dated>): Maybe<TagPath> {
  const s = toS(Settings.tagYMD.valueOrDefault).toLowerCase()

  if (date == null || s === "" || s === "off" || s.startsWith("disable")) return // date tagging is disabled

  // i18n happens in a layer above this:
  const result: TagPath = [TagRoots.When]

  if (s.startsWith("y")) {
    const y = map(getYear(date), yearTagRef)
    if (y == null) return
    result.push(y)
  }
  if (s.startsWith("ym")) {
    const m = map(getMonth(date), monthTagRef)
    if (m == null) return result // just take the year.
    result.push(m)
  }
  if (s.startsWith("ymd")) {
    const m = map(getDay(date), dayTagRef)
    if (m == null) return result // just take the month.
    result.push(m)
  }
  return result
}

export async function dateTagFile(
  file: PosixFile,
  capturedAts: Maybe<CapturedAt>[]
): PromiseMaybe<TagPath> {
  if (Settings.tagYMD.valueOrDefault === "") return

  const arr = [...capturedAts]

  if (isEmpty(capturedAts)) {
    arr.push((await readTags(file))?.capturedAt)
  }

  const best = bestCapturedAt(arr)

  if (
    best == null ||
    (capturedAtSrcFromStat(best.src) &&
      !Settings.tagDateFromStat.valueOrDefault)
  ) {
    return
  }

  return dateTag(best.date)
}
