import { blank, notBlank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { toS } from "../../fe/toS"
import { isChrome, isFirefox } from "../../fe/UserAgents"
import { SimpleFile } from "../fs/SimpleFile"
import { isHeifSupported } from "../img/Heif"
import { isHeifMimeType } from "../img/HeifFilter"
import { isVideoSupported } from "../img/Video"
import {
  AssetFiletypes,
  CurrentBrowserMimetypes,
  OldBrowserMimetypes,
  RawImageFiletypes,
  SharpImageFiletypes
} from "./FileTypes"
import { readMimeType } from "./ReadMimeType"

const MimetypeAliases = {
  // ExifTool renders m2ts, but it's supposed to be mp2t: https://www.iana.org/assignments/media-types/video/MP2T
  "video/m2ts": "video/mp2t"
} as const

export function isMimeTypeSupportedByBrowser(
  mt: Maybe<string>,
  userAgent: Maybe<string> = "" // < default to old browsers
) {
  return (
    isChrome(userAgent) || isFirefox(userAgent)
      ? CurrentBrowserMimetypes
      : OldBrowserMimetypes
  ).includes(toS(mt) as any)
}

export function isImageMimeType(mimetype: Maybe<string>) {
  return toS(mimetype).startsWith("image/")
}

export function isVideoMimeType(mimetype: Maybe<string>): boolean {
  return (
    notBlank(mimetype) &&
    (mimetype.startsWith("video/") ||
      mimetype === "application/mp4" ||
      mimetype === "application/ogg")
  )
}

export function normalizeMimetype(mimetype: string): string {
  const s = toS(mimetype).toLowerCase()
  return (MimetypeAliases as any)[s] ?? s
}

export function isSharpMimeType(mimetype: Maybe<string>): boolean {
  return !blank(mimetype) && normalizeMimetype(mimetype) in SharpImageFiletypes
}

export function isLibrawMimeType(mimetype: Maybe<string>): boolean {
  return !blank(mimetype) && normalizeMimetype(mimetype) in RawImageFiletypes
}

export async function isFileMimeTypeIncluded(
  f: SimpleFile | string,
  patterns: string[]
) {
  return isMimeTypeIncluded(await readMimeType(f), patterns)
}

export async function isAssetFileMimeType(f: SimpleFile | string) {
  const mt = await readMimeType(f)
  return blank(mt)
    ? false
    : isVideoMimeType(mt)
    ? await isVideoSupported()
    : isHeifMimeType(mt)
    ? await isHeifSupported()
    : mt in AssetFiletypes
}

/**
 * Supports "image/*"-style glob-matches
 */
export function isMimeTypeIncluded(mt: Maybe<string>, patterns: string[]) {
  if (mt == null) return false
  for (const ea of patterns) {
    if (ea.includes("*")) {
      const re = new RegExp("^" + ea.replace(/\*/g, ".*") + "$", "i")
      if (re.exec(mt) != null) return true
    } else {
      if (ea.toLowerCase() === mt) return true
    }
  }
  return false
}
