// Pacific/Kiritimati is +14:00 TIL
// https://en.wikipedia.org/wiki/List_of_tz_database_time_zones
export const MaxTzOffsetHours = 14

// Unique values from https://en.wikipedia.org/wiki/List_of_tz_database_time_zones
export const ValidTimezoneOffsets = [
  "-12:00",
  "-11:00",
  "-10:30",
  "-10:00",
  "-09:30",
  "-09:00",
  "-08:30",
  "-08:00",
  "-07:30",
  "-07:00",
  "-06:00",
  "-05:00",
  "-04:30",
  "-04:00",
  "-03:30",
  "-03:00",
  "-02:30",
  "-02:00",
  "-01:00",
  "-00:44",
  // "-00:25:21", // LOL https://en.wikipedia.org/wiki/UTC%E2%88%9200:25:21
  "+00:00",
  "+00:20",
  "+00:30",
  "+01:00",
  "+01:24",
  "+01:30",
  "+02:00",
  "+02:30",
  "+03:00",
  "+03:30",
  "+04:00",
  "+04:30",
  "+04:51", // LOL https://en.wikipedia.org/wiki/UTC%2B04:51
  "+05:00",
  "+05:30",
  "+05:40",
  "+05:45",
  "+06:00",
  "+06:30",
  "+07:00",
  "+07:20",
  "+07:30",
  "+08:00",
  "+08:30",
  "+08:45",
  "+09:00",
  "+09:30",
  "+09:45",
  "+10:00",
  "+10:30",
  "+11:00",
  "+11:30",
  "+12:00",
  "+12:45",
  "+13:00",
  "+13:45",
  "+14:00"
] as const

export type TimezoneOffset = (typeof ValidTimezoneOffsets)[number]

export function offsetToMinutes(offset: TimezoneOffset): number {
  const [h, m] = offset.split(":").map(Number)
  // we can't just return `h * 60 + m`: that doesn't work with negative offsets!
  const sign = offset.startsWith("-") ? -1 : 1
  return sign * (Math.abs(h) * 60 + m)
}

// export const localTzOffsetMinutes = lazy(() => DateTime.local().offset, hourMs)

const AllZoneOffsetMinutes = lazy(() =>
  ValidTimezoneOffsets.map(offsetToMinutes)
)

export function normalizeZoneOffsetMinutes(
  tzoffsetMinutes: Maybe<number>,
  allowedSlopMinutes = 14
): Maybe<number> {
  if (
    !isNumber(tzoffsetMinutes) ||
    Math.abs(tzoffsetMinutes) > MaxTzOffsetHours * 60
  ) {
    return
  }

  const closest = leastBy(AllZoneOffsetMinutes(), ea =>
    Math.abs(ea - tzoffsetMinutes)
  )
  return closest != null &&
    Math.abs(closest - tzoffsetMinutes) < allowedSlopMinutes
    ? closest
    : undefined
}

/**
 * @param s if number, it's assumed to be a zone offset in minutes
 */
export function isValidZone(s: Maybe<number | string | Zone>): boolean {
  return normalizeZone(s) != null
}

const ZuluPrimitives = [
  0,
  "0",
  "Z",
  "GMT",
  "UTC",
  "GMT+0",
  "Etc/UTC",
  "GMT+00:00"
]

function isUTCPrimitive(s: any): boolean {
  return ZuluPrimitives.includes(s as any)
}

export function isUTCZone(s: Maybe<number | string | Zone>): boolean {
  return isUTCPrimitive(s) || normalizeZone(s)?.offset(Date.now()) === 0
}

/**
 * Attempt to avoid https://github.com/moment/luxon/issues/923
 *
 * @param s if number, it's assumed to be a zone offset in minutes
 *
 * @return a valid Zone, or undefined
 */
export function normalizeZone(s: Maybe<number | string | Zone>): Maybe<Zone> {
  if (s == null || blank(s) || isInvalidNumber(s)) return

  if (isUTCPrimitive(s)) return FixedOffsetZone.utcInstance

  const input = isNumber(s)
    ? normalizeZoneOffsetMinutes(s)
    : (isString(s)
        ? isoToOffsetMinutes(s) ?? ianaZoneToOffsetMinutes(s)
        : undefined) ?? s
  if (
    input == null ||
    input === UnsetZoneOffsetMinutes ||
    input === UnsetZoneName
  ) {
    return undefined
  }
  // CAREFUL! If this gets null/undefined, it returns LocalTimeZone!
  const z = Info.normalizeZone(input)
  return !z.isValid || z.offset(Date.now()) === UnsetZoneOffsetMinutes
    ? undefined
    : z
}

interface HasZone {
  zone?: Maybe<Zone>
  tzoffsetMinutes?: Maybe<number>
  offset?: Maybe<number>
  toMillis(): number
}

export function zoneOffsetMinutes(d: HasZone) {
  return (
    normalizeZoneOffsetMinutes(d.tzoffsetMinutes) ??
    normalizeZoneOffsetMinutes(d.offset) ??
    map(d.zone, z => z.offset(d.toMillis()))
  )
}

/**
 * @param ts timestamp
 */
export function zoneToTzOffsetMinutes(
  ts: Maybe<number>,
  zone: Maybe<number | string | Zone>
): Maybe<number> {
  if (ts == null || zone == null) return
  const z = normalizeZone(zone)
  return true === z?.isValid ? (z as Zone).offset(ts) : undefined
}

export function fmtOffsetMinutes(minutes: number): string {
  if (!isValidZone(minutes)) return ""
  const abs = Math.abs(Math.round(minutes))
  const h = Math.floor(abs / 60)
  const m = abs - h * 60
  return (minutes >= 0 ? "+" : "-") + pad2(h) + ":" + pad2(m)
}

export function zoneToExifOffsetMinutes(
  ts: Maybe<number>,
  zone: Maybe<number | string | Zone>
): Maybe<string> {
  const mins = zoneToTzOffsetMinutes(ts, zone)
  return mins == null ? undefined : fmtOffsetMinutes(mins)
}

// these group names can't collide with FuzzyDate: this RegExp gets concatted
// with the other patterns!
const utcRE = /\b(?<utc>Z|UTC|GMT)\b/ // case sensitive, as per ISO

// https://en.wikipedia.org/wiki/ISO_8601#Time_offsets_from_UTC
// [hh]:[mm], [hh][mm], or [hh]

// (PESKY: both hyphen-minus and minus sign are valid)
const signRE = /(?<tzSign>[-+])/
const negativeRE = /[-]/

// HOUR OFFSET REQUIRES PADDING! DateInterval parsing will fail otherwise
const paddedHourRE = /(?<tzHour>[01]\d)/
const hourRE = /(?<tzHour>[01]?\d)/
// colons are optional (!!)
const minRE = /(?::(?<tzMinutes>\d\d))/

export const TimezoneOffsetRE = lazy(() =>
  concatRegexp([
    new RegExpEscaped("(?:"),
    utcRE,
    new RegExpEscaped("|"),
    signRE,
    paddedHourRE,
    RegExpOptional.from(minRE),
    new RegExpEscaped(")"),
    // Either match the end of the string, or NOT a bit of an ISO timestamp
    // (like "T" or "/"),
    /(?:$|[^-+:_\d.T/])/
  ])
)

export function isoToOffsetMinutes(iso: string): Maybe<number> {
  return isUTCPrimitive(iso)
    ? 0
    : timezoneOffsetFromRegExpMatch(TimezoneOffsetRE().exec(iso))
}

export function timezoneOffsetFromRegExpMatch(
  m: RegExpExecArray | null
): Maybe<number> {
  if (m == null || isEmptyObj(m.groups)) return
  if (!blank(m.groups.utc)) return 0
  const hours = toInt(m.groups.tzHour)
  const sign = negativeRE.test(toS(m.groups.tzSign)) ? -1 : 1
  const minutes = toInt(m.groups.tzMinutes) ?? 0
  const result = hours == null ? undefined : sign * (hours * 60 + minutes)
  return isValidZone(result) ? result : undefined
}

const IanaZonePrefixRE = /\b(?<ianaPrefix>(?:Etc\/)?GMT)\b/

const IanaTimezoneOffsetRE = lazy(() =>
  concatRegexp([IanaZonePrefixRE, signRE, hourRE, RegExpOptional.from(minRE)])
)

export function ianaZoneToOffsetMinutes(iana: string): Maybe<number> {
  return timezoneOffsetFromRegExpMatch(IanaTimezoneOffsetRE().exec(iana))
}

export function luxonTzOffsetToOffsetMinutes(
  tz: Maybe<string>,
  ts: Maybe<number>
): Maybe<string> {
  if (blank(tz)) return
  // Is this a specific offset, like "Etc/GMT+3" or "+03:00"?
  const offsetMinutes = ianaZoneToOffsetMinutes(tz) ?? isoToOffsetMinutes(tz)
  if (offsetMinutes != null) {
    return fmtOffsetMinutes(offsetMinutes)
  }
  //
  if (ts != null) {
    const z = normalizeZone(tz)
    if (z != null) {
      return fmtOffsetMinutes(z.offset(ts))
    }
  }
  return
}
