// https://datascience.stackexchange.com/questions/48642/how-to-measure-the-similarity-between-two-images

// Research later: https://erdogant.github.io/undouble/pages/html/hash_functions.html

export interface ImageHash {
  mimetype: string

  /**
   * b64 8x8 L*a*b* triplet ahash
   */
  meanHash: string
  dctHash: string
  diffHash: string

  dominantColors: DominantColor[]
  dominantColorsDescription?: string
  dominantColorScore?: number

  /** width of original image */
  width: number
  /** height of original image */
  height: number
  /** is the original image greyscale? */
  isGreyscale: boolean // asset files don't have this

  /** is the image on disk rotated by metadata? */
  rotation: number
}

/**
 * How many bits-wide values can be bit-concatted into a single value?
 */
export function maxPerBits(bits: number): number {
  // We don't want to overflow Javascript numbers, hence the 52:
  return Math.floor(52 / bits)
}

const logger = lazy(() => mkLogger("img.ImageHash"))

/**
 * Image fingerprint to find duplicate images (so strive for no false
 * positives).
 *
 * May match against global exposure changes.
 *
 * Does not need to match cropping or free rotations.
 *
 * Moving the pixels to L*a*b* means we can pick bin to perceptual ranges.
 */
export async function imageHash(
  f: string | SimpleFile
): Promise<Maybe<ImageHash>> {
  try {
    return await imageHash_(toNativePath_(f))
  } catch (err) {
    logger().warn("imageHash() failed for " + f, err)
    return
  }
}

const cache = lazy(
  () =>
    new FileCache<Maybe<ImageHash>>({
      name: "img.imageHash",
      timeoutMs: minuteMs
    })
)

export const imageHash_ = shim1({
  name: "img.imageHash",
  cache,
  impl: _imageHash_
})

export async function _imageHash_(nativePath: string): Promise<ImageHash> {
  const file = PosixFile.for(nativePath)

  const sizeInfo = await extractSizeInfoFromFile(file)
  if (sizeInfo == null) {
    throw new Error(
      "Failed to read dimensions for " +
        nativePath +
        NonFatalErrorFlag +
        DoNotSendErrorFlag
    )
  }

  const minDim = fitToResolution(
    sizeInfo.dimensions,
    Math.round(Settings.dominantColorPixels.valueOrDefault)
  )

  // Don't count sharp-readable time inside the image-hash time!
  const readable = await sharpReadable_({ src: file, minDim })
  return time(`img.imageHash`, () => __imageHash_({ file, readable, sizeInfo }))
}

export function isImageHash(o: any): o is ImageHash {
  return (
    o != null &&
    notBlank(o.meanHash) &&
    notBlank(o.mimetype) &&
    isNotEmpty(o.dominantColors) // < prevents empty dominant color array from "matching"
    // isGreyscale is optional
  )
}

export type ImageHashish =
  | ImageHash
  | (Omit<ImageHash, "dominantColors"> & WithModeData)

export function toImageHash(ih: Maybe<ImageHashish>): Maybe<ImageHash> {
  if (ih == null) return
  if (isImageHash(ih)) {
    return ih
  }
  // The AssetFile model is stores the colors in columns:
  if (isModeData(ih)) {
    const result = {
      ...omitModeData(ih),
      dominantColors: DominantColor.fromModeData(ih)
    }
    if (isImageHash(result)) return result
  }
  return
}

// If this is 4, SyncService tests fail to validate against preview images:
export const HashDim = 8
// larger values don't seem to impact accuracy:
// meanHamm 3: { k: 18, avg: 0.962, min: 0.86, max: 1, sd: 0.05 }
// meanHamm 4: { k: 18, avg: 0.968, min: 0.89, max: 1, sd: 0.04 }
// meanHamm 5: { k: 18, avg: 0.963, min: 0.87, max: 1, sd: 0.03 },
// meanHamm 6: { k: 18, avg: 0.958, min: 0.88, max: 1, sd: 0.04 },
// meanHamm 7: { k: 18, avg: 0.945, min: 0.86, max: 1, sd: 0.04 }
// meanHamm 8: { k: 18, avg: 0.959, min: 0.85, max: 1, sd: 0.04 },

// Most EXIF thumbprints are 120x160, so less than that should be fine.
// NOTE: smaller dimensions (like 48) cause panoramas to fail tests.

async function __imageHash_({
  file,
  readable,
  sizeInfo
}: {
  file: PosixFile
  readable: Maybe<DescribedSharpReadable>
  sizeInfo: SizeInfo
}): Promise<ImageHash> {
  const l = mkLogger("ImageHash(" + file + ")")
  if (readable == null) {
    return l.throw("Cannot build readable stream")
  }

  const s1dims = fitToResolution(
    sizeInfo.dimensions,
    Settings.dominantColorPixels.valueOrDefault
  )

  if (isEnvTrue("DEBUG_IMAGE_HASH" as any)) {
    const out = "/tmp/image-hash-" + file.base + "-orig.jpg"
    await toSharp(readable).jpeg().toFile(out)
    console.log({ out, readable })
  }

  let s1 = toSharp(readable)
    // PNGs and TIFFs can have an alpha channel. We don't want that.
    .removeAlpha()
  // Some TIFFs and HEIFs are in a different colorspace. Normalize to sRGB.
  // .toColorspace("srgb")
  // NOTE: DON'T NORMALIZE s1, it mucks up what the actual dominant colors are!

  // Video frames will have been rotated for us already by ffmpeg.
  if (!isVideoMimeType(readable.mimetype)) {
    // Sharp doesn't do rotation correctly with raw images, so we have to be
    // explicit with the rotation.
    s1 = s1.rotate(sizeInfo.rotation)
  }

  if (dmegapixels(sizeInfo.dimensions) < 0.1) {
    s1 = s1
      // Some thumbnails have "letterboxing" (black bars) on the top and bottom. This won't be found on larger previews or original images.
      .trim({ background: "#000", threshold: 0 })
  }

  s1 = s1.resize({
    fit: _sharp.fit.outside,
    ...s1dims,
    withoutEnlargement: true
  })

  // _We_ have to do the RGB to L*a*b* conversion because raw() doesn't work:
  // https://github.com/lovell/sharp/issues/681
  const { data: rgb, info: rgbInfo } = await s1
    .raw()
    .toBuffer({ resolveWithObject: true })

  if (isEnvTrue("DEBUG_IMAGE_HASH" as any)) {
    const out = "/tmp/image-hash-" + file.base + "-" + rgbInfo.width + "w.jpg"
    await _sharp(rgb, { raw: { ...rgbInfo, channels: 3 } })
      .jpeg()
      .toFile(out)
    console.log({ out, rgbInfo, readable })
  }

  const labTriplets = rgb2labTriplets(rgb)

  const dc = extractDominantColors(labTriplets)

  const isGreyscale = labsAreGreyscale(labTriplets)

  const img = () => _sharp(rgb, { raw: { ...rgbInfo, channels: 3 } })

  const meanDiff = await (isGreyscale ? meanDiffHash : meanDiffHashCIELAB)(
    img()
  )
  const phashBits = await (isGreyscale ? dctHash : dctHashCIELAB)(img())

  return {
    meanHash: b64encodeBits(meanDiff.meanHash),
    diffHash: b64encodeBits(meanDiff.diffHash),
    dctHash: b64encodeBits(phashBits),
    dominantColors: dc.dominantColors,
    dominantColorsDescription: dc.description,
    dominantColorScore: dc.dominantColorScore,

    isGreyscale,
    mimetype: normalizeMimetype(readable.mimetype),
    ...sizeInfo.dimensions,
    rotation: sizeInfo.rotation ?? 0
  }
}
