import { range, stepRange } from "../../fe/Array"
import { mapFinite } from "../../fe/Number"
import { concat, greatestIndex, leastIndex } from "../Array"
import { Average } from "./Average"
import { mode, sumf } from "./Vector"

export function vecXvec(a: number[], b: number[]): number[] {
  return a.map((v, idx) => v * b[idx])
}

export function matXvec(matrix: number[][], vector: number[]): number[] {
  return matrix.map((row, rowIdx) => {
    if (row.length !== vector.length) {
      throw new Error("misshaped matrix on row " + rowIdx)
    }
    return sumf(row, (col, colIdx) => col * vector[colIdx])
  })
}

export interface Index2d {
  col: number
  row: number
}

/**
 * @param to is exclusive, like slice
 */
export function slice(m: number[][], from: Index2d, to: Index2d): number[][] {
  const rowFrom = Math.max(0, from.row)
  const rowTo = Math.min(m.length, to.row) // range is exclusive
  const colFrom = Math.max(0, from.col)
  const colTo = Math.min(m[0].length, to.col)
  return range(rowFrom, rowTo, row => m[row].slice(colFrom, colTo))
}

export function index1D(dim: Index2d, row: number, col: number) {
  return row * dim.col + col
}

/**
 * Returns the index values for a 1d matrix that correspond with the given
 * from and to.
 * @param dim holds the number of rows and columns in the 2d matrix
 * @param from inclusive
 * @param to exclusive
 */
export function submatrixForEach(
  dim: Index2d,
  from: Index2d,
  to: Index2d,
  f: (i: number) => any
): void {
  const rowFrom = Math.floor(Math.max(0, from.row))
  const rowTo = Math.ceil(Math.min(dim.row, to.row)) // range is exclusive
  const colFrom = Math.floor(Math.max(0, from.col))
  const colTo = Math.ceil(Math.min(dim.col, to.col))
  for (let row = rowFrom; row < rowTo; row++) {
    for (let col = colFrom; col < colTo; col++) {
      f(row * dim.col + col)
    }
  }
}

export function submatrixMagnitude(
  m: number[],
  dim: Index2d,
  from: Index2d,
  to: Index2d
): number {
  let agg = 0
  submatrixForEach(dim, from, to, i => mapFinite(m[i], v => (agg += v * v)))
  return Math.sqrt(agg)
}

export function submatrixStdDev(
  m: number[],
  dim: Index2d,
  from: Index2d,
  to: Index2d
): number {
  const a = new Average()
  submatrixForEach(dim, from, to, i => mapFinite(m[i], v => a.push(v)))
  return a.stdDev!
}

export function submatrixMode(
  m: number[],
  dim: Index2d,
  from: Index2d,
  to: Index2d
): number {
  const arr: number[] = []
  submatrixForEach(dim, from, to, i => mapFinite(m[i], v => arr.push(v)))
  return mode(arr)!
}

/**
 * Returns the quarter submatrix with the largest magnitude, in quadrant order:
 *
 * 1 | 0
 * - + -
 * 2 | 3
 */
export function leastMagQuarter(m: number[], dim: Index2d): number {
  const c = dim.col
  const r = dim.row
  const quarterMagnitudes = [
    submatrixMagnitude(m, dim, { col: c / 2, row: 0 }, { col: c, row: r / 2 }),
    submatrixMagnitude(m, dim, { col: 0, row: 0 }, { col: c / 2, row: r / 2 }),
    submatrixMagnitude(m, dim, { col: 0, row: r / 2 }, { col: c / 2, row: r }),
    submatrixMagnitude(m, dim, { col: c / 2, row: r / 2 }, { col: c, row: r })
  ]
  return leastIndex(quarterMagnitudes)
}

export function leastVariantQuarter(m: number[], dim: Index2d): number {
  const c = dim.col
  const r = dim.row
  return leastIndex([
    submatrixStdDev(m, dim, { col: c / 2, row: 0 }, { col: c, row: r / 2 }),
    submatrixStdDev(m, dim, { col: 0, row: 0 }, { col: c / 2, row: r / 2 }),
    submatrixStdDev(m, dim, { col: 0, row: r / 2 }, { col: c / 2, row: r }),
    submatrixStdDev(m, dim, { col: c / 2, row: r / 2 }, { col: c, row: r })
  ])
}

/**
 * Returns the quarter submatrix with the largest magnitude, in quadrant order:
 *
 *   0
 * 1 + 3
 *   2
 */
export function greatestHalf(m: number[], dim: Index2d): number {
  const c = dim.col
  const r = dim.row
  const quarterMagnitudes = [
    submatrixMode(m, dim, { col: 0, row: 0 }, { col: c, row: r / 2 }),
    submatrixMode(m, dim, { col: 0, row: 0 }, { col: c / 2, row: r }),
    submatrixMode(m, dim, { col: 0, row: r / 2 }, { col: c, row: r }),
    submatrixMode(m, dim, { col: c / 2, row: 0 }, { col: c, row: r })
  ]
  return greatestIndex(quarterMagnitudes)
}

export function submatrixCollect(
  dim: Index2d,
  from: Index2d,
  to: Index2d
): number[] {
  const r: number[] = []
  submatrixForEach(dim, from, to, i => r.push(i))
  return r
}

/**
 * Reduce to dimXdim matrix, using the mode of surrounding values in the
 * matrix
 */
export function modeReduce(
  m: number[],
  fromDim: Index2d,
  toDim: Index2d
): number[] {
  const colDelta = Math.floor(fromDim.col / toDim.col)
  const rowDelta = Math.floor(fromDim.row / toDim.row)
  const avg = new Average(Math.ceil(colDelta * rowDelta))
  return concat(
    ...stepRange(0, fromDim.row, rowDelta, rowStart =>
      stepRange(0, fromDim.col, colDelta, colStart => {
        avg.clear()
        const from = { col: colStart, row: rowStart }
        const to = { col: colStart + colDelta, row: rowStart + rowDelta }
        submatrixForEach(fromDim, from, to, i =>
          mapFinite(m[i], ea => avg.push(ea))
        )
        return avg.sampleMode
      })
    )
  )
}
