import { Maybe, PromiseMaybe } from "../../fe/MaybeTypes"
import { toGt0f } from "../../fe/Number"
import { PosixFile } from "../fs/PosixFile"
import { readRawTags } from "./ExifTool"
import { isVideoMimeType } from "./MimeTypes"
import { WithDuration } from "./WithDuration"

export function extractDurationSec(t: Maybe<WithDuration>): Maybe<number> {
  // We only extract duration for videos. ExifTool sometimes returns
  // `Duration: 1s` for Canon RAW CR3 images, which is odd.

  // TODO: when we support LivePhotos, we'll need to add a check for HEIC/HEIF
  // here:
  return isVideoMimeType(t?.MIMEType)
    ? [t?.duration, t?.Duration, t?.MediaDuration, t?.TrackDuration].find(
        toGt0f
      )
    : undefined
}

export async function extactDuration(
  pathOrFile: Maybe<string | PosixFile>
): PromiseMaybe<number> {
  if (pathOrFile == null) return
  const pf = PosixFile.for(pathOrFile)
  return extractDurationSec(await readRawTags(pf, false))
}
