import { lazy } from "../../core/Lazy"
import { mkLogger } from "../../core/Logger"
import { ee } from "../../core/event/EventEmitter"
import { PosixFile } from "../../core/fs/PosixFile"
import { SimpleFile } from "../../core/fs/SimpleFile"
import { isBadSha } from "../../core/img/AssetPreviewBuilder"
import { isEmpty, uniq } from "../../fe/Array"
import { blank } from "../../fe/Blank"
import { Maybe } from "../../fe/MaybeTypes"
import { DbRequest } from "../db/DbRequest"
import { modelDb } from "./ModelDb"

// This is implementation doesn't extend from Model as it doesn't have an ID or
// timestamps (or anything, really)

const logger = lazy(() => mkLogger("model.ShaBlocklist"))

const dbr = lazy(() => new DbRequest(modelDb, "ShaBlocklist"))

export async function notBlocklistedSha(file: string | SimpleFile) {
  if (modelDb() == null) return null
  const result = isShaBlockslisted(await PosixFile.for(file).sha())
  return result == null ? null : !result
}
export function isShaBlockslisted(sha: Maybe<string>) {
  if (modelDb() == null || blank(sha)) return false
  return logger().tap({
    msg: "isShaBlockslisted",
    result: null != dbr().pluckFirstf(q => q.where({ sha }).limit(1)),
    meta: { sha }
  })
}

export async function upsertFileToShaBlockslist(file: string | SimpleFile) {
  if (modelDb() == null) return
  const sha = await PosixFile.for(file).sha()
  return sha == null ? undefined : upsertToShaBlockslist(sha)
}

export function upsertToShaBlockslist(...shas: string[]) {
  if (modelDb.prior() == null) return
  const data = uniq(shas).map(sha => ({ sha }))
  return isEmpty(data) ? undefined : dbr().upsert(q => q.insert(data))
}

export const listenForInvalidFiles = lazy(() => {
  ee().on("invalidFile", upsertFileToShaBlockslist)
})

export const installBadShaShim = lazy(() => {
  isBadSha.setShim(isShaBlockslisted)
})
