import { Maybe } from "../../fe/MaybeTypes"
import { clamp } from "../../fe/Number"
import { sqrt } from "../Number"
import { Triplet } from "./Triplet"

export function cie76_delta_e(lab1: Triplet, lab2: Triplet): number {
  return Math.sqrt(
    (lab2[0] - lab1[0]) ** 2 +
      (lab2[1] - lab1[1]) ** 2 +
      (lab2[2] - lab1[2]) ** 2
  )
}

/**
 * @see https://en.wikipedia.org/wiki/Color_difference#CIE94
 * @return the perceptual difference. <= 2 is not perceptable.
 */
export function cie94_delta_e(lab1: Triplet, lab2: Triplet) {
  const [L1, a1, b1] = lab1
  const [L2, a2, b2] = lab2

  // http://www.brucelindbloom.com/index.html?ColorDifferenceCalc.html

  const _Kl = 1.0
  const _K1 = 0.045
  const _K2 = 0.015

  const dL = L1 - L2
  const dA = a1 - a2
  const dB = b1 - b2
  const c1 = sqrt(a1 ** 2 + b1 ** 2)
  const c2 = sqrt(a2 ** 2 + b2 ** 2)
  const dC = c1 - c2

  const deltaH = sqrt(dA ** 2 + dB ** 2 - dC ** 2)

  return sqrt(
    (dL / _Kl) ** 2 +
      (dC / (1.0 + _K1 * c1)) ** 2 +
      (deltaH / (1.0 + _K2 * c1)) ** 2
  )
}

export const MinCie = 2 // barely perceptable
export const MaxCie = 20 // very perceptable

/**
 * Use `diffCIE94` to return a correlation value between 1 (imperceptible
 * difference) and 0 (largest possible difference)).
 */
export function diffCIECorr(
  lab1: Maybe<Triplet>,
  lab2: Maybe<Triplet>
): number {
  if (lab1 == null || lab2 == null) return 1
  return clamp(0, MaxCie, ciede2000_delta_e(lab1, lab2) - MinCie) / MaxCie
}

const kL = 1 // 1 for graphic arts, 2 for textiles
const kC = 1 // unity factor
const kH = 1 // weighting factor

// https://github.com/omgovich/colord/blob/master/src/get/getPerceivedDifference.ts failed tests

// This is from https://github.com/Evercoder/d3-color-difference/blob/master/src/ciede2000.js

export function ciede2000_delta_e(std: Triplet, smp: Triplet): number {
  const [lStd, aStd, bStd] = std
  const cStd = sqrt(aStd ** 2 + bStd ** 2)

  const [lSmp, aSmp, bSmp] = smp
  const cSmp = sqrt(aSmp ** 2 + bSmp ** 2)

  const cAvg = (cStd + cSmp) / 2

  const G =
    0.5 * (1 - sqrt(Math.pow(cAvg, 7) / (Math.pow(cAvg, 7) + Math.pow(25, 7))))

  const apStd = aStd * (1 + G)
  const apSmp = aSmp * (1 + G)

  const cpStd = sqrt(apStd * apStd + bStd * bStd)
  const cpSmp = sqrt(apSmp * apSmp + bSmp * bSmp)

  let hpStd: number =
    Math.abs(apStd) + Math.abs(bStd) === 0 ? 0 : Math.atan2(bStd, apStd)
  hpStd += (hpStd < 0 ? 1 : 0) * 2 * Math.PI

  let hpSmp =
    Math.abs(apSmp) + Math.abs(bSmp) === 0 ? 0 : Math.atan2(bSmp, apSmp)
  hpSmp += (hpSmp < 0 ? 1 : 0) * 2 * Math.PI

  const dL = lSmp - lStd
  const dC = cpSmp - cpStd

  let dhp = cpStd * cpSmp === 0 ? 0 : hpSmp - hpStd
  dhp -= (dhp > Math.PI ? 1 : 0) * 2 * Math.PI
  dhp += (dhp < -Math.PI ? 1 : 0) * 2 * Math.PI

  const dH = 2 * sqrt(cpStd * cpSmp) * Math.sin(dhp / 2)

  const Lp = (lStd + lSmp) / 2
  const Cp = (cpStd + cpSmp) / 2

  let hp
  if (cpStd * cpSmp === 0) {
    hp = hpStd + hpSmp
  } else {
    hp = (hpStd + hpSmp) / 2
    hp -= (Math.abs(hpStd - hpSmp) > Math.PI ? 1 : 0) * Math.PI
    hp += (hp < 0 ? 1 : 0) * 2 * Math.PI
  }

  const Lpm50 = (Lp - 50) ** 2
  const T =
    1 -
    0.17 * Math.cos(hp - Math.PI / 6) +
    0.24 * Math.cos(2 * hp) +
    0.32 * Math.cos(3 * hp + Math.PI / 30) -
    0.2 * Math.cos(4 * hp - (63 * Math.PI) / 180)

  const Sl = 1 + (0.015 * Lpm50) / sqrt(20 + Lpm50)
  const Sc = 1 + 0.045 * Cp
  const Sh = 1 + 0.015 * Cp * T

  const deltaTheta =
    ((30 * Math.PI) / 180) *
    Math.exp(-1 * (((180 / Math.PI) * hp - 275) / 25) ** 2)
  const Rc = 2 * sqrt(Math.pow(Cp, 7) / (Math.pow(Cp, 7) + Math.pow(25, 7)))

  const Rt = -1 * Math.sin(2 * deltaTheta) * Rc

  return sqrt(
    Math.pow(dL / (kL * Sl), 2) +
      Math.pow(dC / (kC * Sc), 2) +
      Math.pow(dH / (kH * Sh), 2) +
      (((Rt * dC) / (kC * Sc)) * dH) / (kH * Sh)
  )
}
