import { Command } from "commander"
import { isMainService, isSyncService } from "../../core/ServiceNames"
import { CommandPlugin } from "../../core/cli/CLI"
import { isDaemon } from "../../core/cli/IsDaemon"
import { Settings } from "../../core/settings/Settings"
import { isTrue } from "../../fe/Boolean"
import { Obj } from "../../fe/Object"

function supportsTail() {
  return isMainService() || isSyncService()
}

export const LogArgs: CommandPlugin = {
  beforeParse: (cmd: Command) => {
    const maybeLogStdout =
      (supportsTail() ? `, "PS_TAIL_LOGS=true"` : "") +
      ` and "PS_LOG_STDOUT=true".`

    if (supportsTail()) {
      cmd.option(
        "-v, --verbose",
        `Verbose logging from all processes. Shortcut for "--info --tail". Caution: noisy during imports!`
      )
    } else {
      cmd.option("-v, --verbose", `Verbose logging. Equivalent to --info.`)
    }
    cmd.option(
      "--error",
      `Emit "error" messages from this process to stdout. Sets "PS_LOG_LEVEL=error"${maybeLogStdout}`
    )
    cmd.option(
      "--warn",
      `Emit "warn" and "error" messages from this process to stdout. Sets "PS_LOG_LEVEL=warn"${maybeLogStdout}`
    )
    cmd.option(
      "--info",
      `Emit "info", "warn", and "error" messages from this process to stdout. Sets "PS_LOG_LEVEL=info"${maybeLogStdout}`
    )
    cmd.option(
      "--debug",
      `Emit "debug", "info", "warn", and "error" messages from this process to stdout. CAUTION: VERY NOISY. Sets "PS_LOG_LEVEL=debug"${maybeLogStdout}}`
    )
    cmd.option(
      "--trace",
      `Emit "trace", "debug", "info", "warn", and "error" messages from this process to stdout. CAUTION: VERY VERY NOISY. Sets "PS_LOG_LEVEL=trace"${maybeLogStdout}}`
    )
    if (supportsTail()) {
      cmd.option(
        "--tail",
        `Emit log messages from both this process and all other concurrently running PhotoStructure processes on this host to stdout. This can be really helpful in seeing how PhotoStructure's processes are coordinating work. Caution: very noisy especially during imports. Sets "PS_TAIL_LOGS=true".`
      )
    }

    return cmd
  },

  afterParse: (opts: Obj) => {
    const error = isTrue(opts.error)
    const warn = isTrue(opts.warn)
    const info = isTrue(opts.info) || isTrue(opts.verbose) || isTrue(opts.v)
    const debug = isTrue(opts.debug)
    const trace = isTrue(opts.trace)

    // these should win: use envValue
    if (trace) {
      Settings.logLevel.envValue = "trace"
    } else if (debug) {
      Settings.logLevel.envValue = "debug"
    } else if (info) {
      Settings.logLevel.envValue = "info"
    } else if (warn) {
      Settings.logLevel.envValue = "warn"
    } else if (error) {
      Settings.logLevel.envValue = "error"
    }

    if (isDaemon(opts as any)) {
      // don't log to stdout if we're a daemon. Use .envValue to set to false but not persist
      Settings.tailLogs.envValue = false
      Settings.logStdout.envValue = false
    } else {
      if (isTrue(opts.tail)) {
        // they asked for it: do it.
        Settings.tailLogs.envValue = true
      }
      if (error || warn || info || debug || trace) {
        // not envValue, as it's not being asked for directly.
        Settings.logStdout.tmpValue = true

        if (supportsTail()) {
          Settings.tailLogs.tmpValue = true
        }
      }

      if (
        (Settings.tailLogs.valueOrDefault ||
          Settings.logStdout.valueOrDefault) &&
        Settings.logLevel.isUnset()
      ) {
        Settings.logLevel.envValue = "info"
      }
    }
  }
}
