import { isNotEmpty } from "../../fe/Array"
import { hourMs, minuteMs, secondMs } from "../../fe/Date"
import { delay } from "../../fe/Delay"
import { flatten } from "../../fe/Flatten"
import { fromEntries, values } from "../../fe/Object"
import { cmp, gte, lt } from "../../fe/Primitive"
import { thenMap } from "../../fe/Promise"
import { BoundedGreatestSet } from "../BoundedGreatestSet"
import { mkLogger } from "../Logger"
import { withBoundedConcurrency } from "../async/Promises"
import { untilTrue } from "../async/until"
import { errorToS } from "../error/Error"
import { DirectoryEntry } from "../fs/DirectoryEntry"
import { Settings } from "../settings/Settings"
import { LogEntry, logEntrySorter } from "./LogEntry"
import { LogLevels } from "./LogLevel"
import { LogReader } from "./LogReader"
import { writeRecentLogEntries } from "./WriteRecentLogEntries"

export function recentLogFiles(ttlMs = 10 * minuteMs) {
  const minMtime = Date.now() - ttlMs
  return thenMap(DirectoryEntry.for(Settings.logDir.valueOrDefault), dir =>
    dir.filterDescendantFiles(
      async ea =>
        [".log", ".log.gz"].includes(ea.ext) &&
        gte(await ea.mtimeMs(), minMtime)
    )
  )
}

export async function allRecentLogEntries(maxEntriesPerLevel = 50) {
  writeRecentLogEntries()

  const entriesByLevel: {
    [level: string]: BoundedGreatestSet<LogEntry>
  } = fromEntries(
    LogLevels.values.map(logLevel => [
      logLevel,
      new BoundedGreatestSet(maxEntriesPerLevel, logEntrySorter)
    ])
  )

  const oldestTs = Date.now() - hourMs
  const logger = mkLogger("allRecentLogEntries()")

  await thenMap(recentLogFiles(), files =>
    withBoundedConcurrency({
      name: "allRecentLogEntries()",
      laters: files.map(f => async () => {
        try {
          const errors: Error[] = []
          const lr = new LogReader(f, err => errors.push(err))
          await untilTrue(() => lr.ready(), { timeoutMs: 10 * secondMs })
          while (!lr.complete() && !lr.hasErrors()) {
            const le = lr.shift()
            if (le == null) {
              // We exhausted the buffer. Wait for it to fill again.
              await delay(5)
            } else if (le.ts > oldestTs) {
              entriesByLevel[le.l]?.add(le)
            }
          }
          if (isNotEmpty(errors)) {
            logger.warn("Read error(s) for " + f, errors)
            if (
              errors.some(
                (ea: any) =>
                  ea.code === "Z_BUF_ERROR" ||
                  errorToS(ea).includes("unexpected end of file")
              ) &&
              lt(await f.mtimeMs(), Date.now() - minuteMs)
            ) {
              logger.warn("Unlinking corrupt logfile " + f)
              await f.unlink_()
            }
          }
        } catch (err) {
          logger.warn("Failed to read entries from " + f, err)
        }
      })
    })
  )

  const entries = flatten(values(entriesByLevel).map(ea => ea.vacuum()))
  return entries.sort((a, b) => cmp(a.ts, b.ts))
}
